/**
 * This enum defines all the data types that are supported by workflow context browser
 */
export enum WorkflowContextBrowserItemTypes {

    /**
     * Represents a String Data Type
     */
    string = 1,

    /**
     * Represents a Numeric (int/decimal/double,etc) Data Type
     */
    number = 2,

    /**
     * Represents a Boolean Data Type
     */
    yesNo = 3,

    /**
     * Represents a DateTime Data Type
     */
    dateTime = 4,

    /**
     * Represents an object Data Type
     */
    object = 5,

    /**
     * Represents an object Data Type
     */
    identityUser = 6,

    /**
     * Represents an object Data Type
     */
    identityGroup = 7,

    /**
     * Represents an object Data Type
     */
    identityRole = 8,

    /**
     * Represents a Decimal Data Type
     */
    decimal = 9,

    /**
     * Represents a Xml Data Type
     */
    xml = 10,

}
