declare const $: any;

/**
 * initiate the date picker on a given container element
 *
 * @param root the element onto which the date picker initiates
 * @param target the element which initiates the date picker
 * @param changeCallback a callback to run when the date selection changes
 */
export function showDatePicker(target: any, changeCallback: Function): void {
    $(target).fdatepicker({
        // Initiates the date picker
        language: sourceCode.k2Designer.core.environmentService.resourceService.info.locale
    }).on('hide', () => {
        // Destroys the date picker
        $(target).off('show changeDate hide').fdatepicker('remove');
    }).on('show', onDatePickerShow.bind(null, target)) // Placement of date picker
        .on('changeDate', changeCallback.bind(this)) // Handles the change in date
        .triggerHandler('show'); // Display the date picker
}

/**
 * represents the date picker pop-up relative to the element which initiated it
 *
 * @param target the element that initiated the date picker control
 * @param date the date to render the date picker with
 */
export function onDatePickerShow(target: any, date: Date): void {
    const container: JQuery = $(target).first();
    const picker: JQuery = $('.datepicker').first();

    let css = {
        left: 0,
        top: 0
    };
    picker.css(css);

    css = getDropdownPlacementOffset(container, picker, $(window), 5);
    picker.css(css);

    if (picker.data('rendered') !== 'true') {
        picker.data('rendered', 'true');
    }
}

/**
 * Gets the appropriate placement of a dropdown element, relative to a specified element in a viewport
 *
 * @param relativeTo The element from which the dropdown should be relatively placed
 * @param dropdown The dropdown element
 * @param viewport The view port element
 * @param margin The margin/space between the dropdown and relative element
 */
export function getDropdownPlacementOffset(relativeTo: JQuery, dropdown: JQuery, viewport: JQuery, margin: number = 5): any {

    const result: JQueryCoordinates = {
        left: 0,
        top: 0
    };

    const relativeToOffset = relativeTo.offset();
    const relativeToWidth = relativeTo.outerWidth(); // includes padding & borders, excludes margins
    const relativeToHeight = relativeTo.outerHeight(); // includes padding & borders, excludes margins

    const dropdownWidth = dropdown.outerWidth(); // includes padding & borders, excludes margins
    const dropdownHeight = dropdown.outerHeight(); // includes padding & borders, excludes margins

    const viewportHeight = viewport.height();

    // Calculate horizontal placement
    result.left = relativeToOffset.left + relativeToWidth - dropdownWidth;

    // Calculate vertical placement
    if ((viewportHeight - relativeToOffset.top + relativeToHeight + margin) > dropdownHeight) {
        // Place below
        result.top = relativeToOffset.top + relativeToHeight + margin;
    }
    else {
        // Place above
        result.top = relativeToOffset.top - dropdownHeight - margin;
    }

    return result;
}
