import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockProgressBarService } from '../../../testing/mock-services/progress-bar-service';
import { IProgressBarService } from '../../../types/services/progress-bar/i-progress-bar-service';

const progressBarServiceFactory = () => environment.devHarness === true
    ? new MockProgressBarService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.ProgressBarService);

export const ProgressBarServiceToken = new InjectionToken<IProgressBarService>('IProgressBarService');

export const ProgressBarServiceFactory = {
    provide: ProgressBarServiceToken,
    useFactory: progressBarServiceFactory,
    deps: [

    ]
};
