/**
 * Enum to define what action should be taken on an escalation UI.
 */
export enum ReminderActionType {

    /**
     * Represents the ON dropdown item.
     */
    On = 1,

    /**
     * Represents the AFTER dropdown item.
     */
    After = 2,

    /**
     * Represents the BEFORE dropdown item.
     */
    Before = 3,

}
