import { Component, ElementRef, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { ListItemComponent } from 'src/app/components/common/list-item/list-item.component';
import { TimeSpanComponent } from 'src/app/components/common/time-span/time-span.component';
import { ConfigPanelServiceToken } from 'src/app/services/config-panel/config-panel-service-factory';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from 'src/app/services/validation/validation-service-factory';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';
import { DeadlineOptions } from 'src/app/shared/enums/deadline/deadline-options';
import { TextBoxConstraint } from 'src/app/shared/enums/text-box/text-box-constraint';
import { showDatePicker } from 'src/app/shared/utils/date-picker';
import { IConfigPanelTab } from 'src/types/models/config-panel/i-config-panel-tab';
import { IActivityConfiguration } from 'src/types/models/process/activities/configuration/i-activity-configuration';
import { IActivity } from 'src/types/models/process/activities/i-activity';
import { IConfigPanelService } from 'src/types/services/config-panel/i-config-panel-service';
import { IListItemIconData } from 'src/types/uiComponents/list-item/i-list-item-icon-data';
import { IValidationService } from '../../../../../../../types/services/validation/ivalidation.service';
import { PluginBaseComponent } from '../../../base/plugin-base/plugin-base.component';

@Component({
    selector: 'k2-timer-step',
    templateUrl: './timer-step.component.html',
    styleUrls: ['./timer-step.component.less']
})
export class TimerStepComponent extends PluginBaseComponent implements OnInit {

    @Input() activity: IActivity;

    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    get configuration(): IActivityConfiguration {
        return this.activity?.configuration as IActivityConfiguration;
    }

    get dateChecked(): boolean {
        return this.configuration.deadline.dateChecked;
    }
    set dateChecked(value: boolean) {
        this.configuration.deadline.dateChecked = value;
    }

    get timeChecked(): boolean {
        return this.configuration.deadline.timeChecked;
    }
    set timeChecked(value: boolean) {
        this.configuration.deadline.timeChecked = value;
    }

    rightIconData: IListItemIconData = {
        customClassFlexRow: 'list-item-icons',
        iconClass: 'icon icon-set-datatypes icon-size16 ic-datetime fdatepicker',
        isVertical: true,
        isWrappedInContentDiv: true
    };

    @ViewChild(ListItemComponent) datePicker: ListItemComponent;
    @ViewChild(TimeSpanComponent) afterTimeSpan: TimeSpanComponent;

    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    textBoxConstraint: typeof TextBoxConstraint = TextBoxConstraint;
    deadlineOptions: typeof DeadlineOptions = DeadlineOptions;

    constructor(protected resourceService: ResourceService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnInit(): void {
        //Set activity configuration as tab validation object (this cannot be done in the 'super', because inputs are not accessible yet)
        this.setTabValidationObject(this.activity.configuration, this.configPanelTab);
    }

    /**
     * Changes the timer step configuration according to deadline option provided
     *
     * @param deadlineOption deadline option of either a specific date or a period
     */
    changeTimerConfig(deadlineOption: DeadlineOptions): void {
        this.undoRedoService.addUndoMarker('--timer step: change timer step configuration--');

        //Date configuration
        if (deadlineOption === DeadlineOptions.specificDate && !this.configuration.deadline.dateChecked) {
            this.configuration.deadline.timeChecked = false;
            this.configuration.deadline.dateChecked = true;
            this.clearTimeConfiguration();
        }

        //Time configuration
        if (deadlineOption === DeadlineOptions.afterTime && !this.configuration.deadline.timeChecked) {
            this.configuration.deadline.timeChecked = true;
            this.configuration.deadline.dateChecked = false;
            this.configuration.deadline.specificDate.smartFields.clear();
            this.datePicker.isTextBoxFocused = false; // Removing focus from datepicker if set
        }
    }

    onTextBoxFocus() {
        this.changeTimerConfig(DeadlineOptions.specificDate);
    }

    openCalendarControl(eventTarget: EventTarget): void {
        showDatePicker(eventTarget, (ev) => {
            this.undoRedoService.addUndoMarker('--timer step: change timer date--');
            this.configuration.deadline.specificDate.smartFields.clear();
            this.configuration.deadline.specificDate.smartFields.push(
                sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.DateTimeField.createInstance({
                    title: ev.formatted,
                    dateTime: ev.formatted
                })
            );
        });
    }

    private clearTimeConfiguration() {
        this.configuration.deadline.expressDays.smartFields.clear();
        this.configuration.deadline.expressHours.smartFields.clear();
        this.configuration.deadline.expressMinutes.smartFields.clear();
        this.configuration.deadline.expressSeconds.smartFields.clear();
    }

}
