import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IConfigurationService } from '../../../types/services/configuration/i-configuration-service';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';

@Injectable({
    providedIn: 'root'
})
export class ConfigurationService implements IConfigurationService {
    private _legacyConfigurationService: sourceCode.k2Designer.services.studio.IConfigurationService;
    get legacyConfigurationService() {
        if (!this._legacyConfigurationService) {
            this._legacyConfigurationService = this.environmentService.getService(sourceCode.k2Designer.services.studio.ConfigurationService);
        }
        return this._legacyConfigurationService;
    }

    constructor(@Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService) { }

    getConfigValue(key: string): string {
        return this._legacyConfigurationService.getConfigValue(key);
    };

    getWorkingHours(): Observable<string[] | string> {
        return new Observable(subscriber => {
            this.environmentService.dataService.httpGet('api/configuration/workinghours',
                {
                    host: this.environmentService.currentEnvironment.hostId
                }, (result: string[] | string, error: boolean) => {
                    if (error) {
                        subscriber.error(result);
                    } else {
                        subscriber.next(result);
                    }
                }, this.environmentService, false);
        });
    };

    getWorkingHoursUrl(): string {
        let urlStr: string = this.getSmartFormsBaseUrl();

        // All workflows
        if (urlStr[urlStr.length - 1] !== '/') {
            urlStr += '/Management/#WorkflowServer/WorkingHours';
        }
        else {
            urlStr += 'Management/#WorkflowServer/WorkingHours';
        }

        return urlStr;
    }

    getSmartFormsBaseUrl(): string {

        //Get the base url from web.config for environments that have splitted workspace, runtime and webservice applications in IIS.
        const smartFormsRuntimeBaseUrl = this.environmentService.appSettings.SmartFormsRuntimeBaseUrl;
        if (smartFormsRuntimeBaseUrl?.length > 0) {
            if (smartFormsRuntimeBaseUrl[smartFormsRuntimeBaseUrl.length - 1] === '/') {
                return smartFormsRuntimeBaseUrl.substring(0, smartFormsRuntimeBaseUrl.length - 1);
            }
            else {
                return smartFormsRuntimeBaseUrl;
            }
        }
        else {
            //Use autodiscover webserviceUrl
            return this.environmentService.autoDiscoverResult.webService.Value;
        }
    }

    get installType(): string {
        return this.environmentService.appSettings['InstallType'] || '';
    }
}
