import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockValidationService } from '../../../testing/mock-services/validation-service';
import { IValidationService } from '../../../types/services/validation/ivalidation.service';

const dataServiceFactory = () => environment.devHarness === true
    ? new MockValidationService()
    : sourceCode.k2Designer.core.environmentService.validationService;

export const ValidationServiceToken = new InjectionToken<IValidationService>('IValidationService');

export const ValidationServiceFactory = {
    provide: ValidationServiceToken,
    useFactory: dataServiceFactory,
    deps: [

    ]
};
