import { Component, ElementRef, EventEmitter, HostListener, Input, OnChanges, Output, SimpleChanges, ViewChild } from '@angular/core';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuComponent } from './drop-down-menu/drop-down-menu.component';

@Component({
    selector: 'k2-drop-down',
    templateUrl: './drop-down.component.html',
    styleUrls: ['./drop-down.component.less']
})
export class DropDownComponent implements OnChanges {

    @ViewChild(DropDownMenuComponent) dropdownMenu: DropDownMenuComponent;

    public isMenuExpanded = false;
    @Input() menuItems: IDropDownMenuItem[] | (() => IDropDownMenuItem[]) = [];
    @Input() isMultiSelect = false;
    @Input() footerItems: IDropDownMenuItem[] = [];
    @Input() customMenuClass: string;
    @Input() emptyStateTemplate = '';
    @Input() menuAlignElement: ElementRef;

    /**
     * Formerly called dropDownType: number;
     * Used to determine how the dropdown is displayed. DropDownMenuAlignment.centered (formerly 0) for button and
     * centered dropdown and DropDownMenuAlignment.leftEdge (formerly 1) for a dropdown which aligns itself to the parent button's parent
     * Use this in the ts to use enum in template: public dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
     */
    @Input() menuAlignment: DropDownMenuAlignment = DropDownMenuAlignment.centered;
    @Input() trianglePosition: DropDownMenuTrianglePosition = null;
    @Input() innerItemComponent: DropdownMenuItemTemplates;
    @Input() selectedItemKey: string;

    @Output() onItemSelected: EventEmitter<IDropDownMenuItem> = new EventEmitter<IDropDownMenuItem>();
    @Output() menuOpened = new EventEmitter<any>();
    @Output() menuClosed = new EventEmitter<any>();

    public dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    private _menuElementMoved = false;

    constructor(public el: ElementRef) {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.isMenuExpanded) {
            if (changes.isMenuExpanded.currentValue && changes.isMenuExpanded.currentValue !== changes.isMenuExpanded.previousValue) {
                if (this.isMenuExpanded) {
                    this.menuOpened.emit();
                } else {
                    this.menuClosed.emit();
                }
            }
        }
    }

    @HostListener('mouseup', ['$event'])
    toggleMenu(evt): void {
        evt.stopPropagation();
        evt.preventDefault();
        this.dropdownMenu.toggleMenu();
    }

    selectItem(item: IDropDownMenuItem): void {
        this.onItemSelected.emit(item);
    }

    collapseMenu(){
        this.dropdownMenu.collapseMenu();
    }

    public static get separator(): IDropDownMenuItem {
        return {
            key: 'separator',
            isSeparator: true
        } as IDropDownMenuItem;
    }
}
