import { Component, ElementRef, EventEmitter, Input, OnChanges, Output, SimpleChanges } from '@angular/core';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { ResourceService } from '../../../services/resource/resource.service';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';

@Component({
    selector: 'k2-time-span',
    templateUrl: './time-span.component.html',
    styleUrls: ['./time-span.component.less']
})
export class TimeSpanComponent implements OnChanges {
    @Input() daysExpression: IExpression;
    @Input() hoursExpression: IExpression;
    @Input() minutesExpression: IExpression;
    @Input() secondsExpression: IExpression;
    @Input() isDisabled: boolean;
    @Input() hideLabels: boolean;
    @Input() noBorder: boolean;

    @Output() k2OnFocus: EventEmitter<void> = new EventEmitter<void>();

    //Public properties
    showLeft = false;
    showRight = true;

    //Enums
    public textBoxConstraint = TextBoxConstraint;

    //Private properties
    private root: HTMLElement;

    constructor(private resourceService: ResourceService,
        private element: ElementRef) {
        this.root = element.nativeElement;
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.isDisabled?.currentValue) { // should clear the values if disabled.
            this.daysExpression.smartFields.clear();
            this.hoursExpression.smartFields.clear();
            this.minutesExpression.smartFields.clear();
            this.secondsExpression.smartFields.clear();
        }
    }

    /**
     * Returns resource string from .resx file using name and key
     *
     * @param resourceName name of resx file
     * @param key resource lookup key
     */
    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    onTextBoxFocus(event): void {
        const focusedTextBox = event.target.closest('.inner-carousel-control');
        const allTextBoxes: HTMLCollection = this.root.getElementsByTagName('k2-text-box');
        let textBoxIndex = -1;

        for (let a = 0; a < allTextBoxes.length; a++) {
            if (allTextBoxes[a].id === focusedTextBox.id) {
                textBoxIndex = a;
                break;
            }
        }

        if (this.showLeft && textBoxIndex < 1) {
            this.moveNav('left');
        } else if (this.showRight && textBoxIndex > 2) {
            this.moveNav('right');
        }

        this.k2OnFocus.emit();
    }

    moveNav(way: string): void {
        if (way === 'right') {
            this.showRight = false;
            this.showLeft = true;
        }

        if (way === 'left') {
            this.showLeft = false;
            this.showRight = true;
        }
    }

    onKeyDown(e: KeyboardEvent, index: number) {
        if (e.keyCode === 9) {
            switch (index) {
                case 1:
                    this.moveNav('left');
                    break;
                case 2:
                    this.moveNav('right');
                    break;
                default:
                    break;
            }
        }
    }

}
