import { EventEmitter, Input, OnDestroy, Output, TemplateRef, ViewChild } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Observable, subscribeOn, Subscriber, Subscription } from 'rxjs';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-page',
    templateUrl: './page.component.html',
    styleUrls: ['./page.component.less']
})
export abstract class PageComponent extends BaseResourceComponent implements OnDestroy {

    @ViewChild('pageTemplate')
    template: TemplateRef<void>;

    /** Only used if the popup host has the isPagesIndexed set */
    @Input() pageIndex: number;

    /** Hide all page indicators when on this page?
     * Will default to true on the last page. Can be overridden to false
     */
    @Input() isAllPageIndicatorsHidden: boolean;

    /** Don't show the page indicator for this page
     * Defaults to true on the last page. Can be overridden
     */
    @Input() isHiddenPage: boolean;

    private onPageConfigChangedSubscribers: Subscriber<void>[] = [];
    private onPageStateChangedSubscribers: Subscriber<PopupState>[] = [];

    onPageConfigChanged: Observable<void>;
    onPageStateChanged: Observable<PopupState>;

    state: PopupState;

    abstract buttonConfig: IPopupButtonsConfig;

    constructor(protected resourceService: ResourceService) {

        super(resourceService);

        this.onPageConfigChanged = new Observable<void>(subscriber => {
            this.onPageConfigChangedSubscribers.push(subscriber);
            return () => {
                this.onPageConfigChangedSubscribers.slice(this.onPageConfigChangedSubscribers.indexOf(subscriber), 1);
            };
        });

        this.onPageStateChanged = new Observable<PopupState>(subscriber => {
            this.onPageStateChangedSubscribers.push(subscriber);
            return () => {
                this.onPageStateChangedSubscribers.slice(this.onPageStateChangedSubscribers.indexOf(subscriber), 1);
            };
        });
    }

    notifyConfigChanged(){
        this.onPageConfigChangedSubscribers.forEach( s => s.next() );
    }

    notifyPageStateChanged(state: PopupState){
        this.state = state;
        this.onPageStateChangedSubscribers.forEach( s => s.next(state) );
    }

    ngOnDestroy(): void {
        this.onPageConfigChangedSubscribers.forEach( s => s.unsubscribe() );
        this.onPageStateChangedSubscribers.forEach( s => s.unsubscribe() );
    }

    abstract beforePageDisplayed();

    abstract beforeNavigateNext();

    abstract beforeNavigatePrevious();
}
