import { Directive, InjectionToken } from '@angular/core';

/**
 * Injection token that can be used to reference instances of `InfoPopupTemplate`. It serves as
 * alternative token to the actual `InfoPopupTemplate` class which could cause unnecessary
 * retention of the class and its directive metadata.
 */
export const INFO_POPUP_TEMPLATE = new InjectionToken<InfoPopupTemplate>('K2InfoPopupTemplate');

/** Used to flag tab labels for use with the portal directive */
@Directive({
    selector: '[k2-info-popup-template], [K2InfoPopupTemplate]',
    providers: [{ provide: INFO_POPUP_TEMPLATE, useExisting: InfoPopupTemplate }],
})
export class InfoPopupTemplate { }
