import { Component, OnInit, Inject, ElementRef, OnDestroy, ViewChild, HostBinding } from '@angular/core';
import { Subscription } from 'rxjs';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { IFilterPanelService } from '../../../../types/services/filter-panel/i-filter-panel';
import { IPluginService } from '../../../../types/services/plugin/i-plugin-service';
import { IInlineSmartField } from '../../../../types/uiComponents/smartFieldComposer/i-inline-smart-field';
import { ILearnSmartFieldComposer } from '../../../../types/uiComponents/smartFieldComposer/ILearnSmartFieldComposer';
import { ISmartFieldComposer } from '../../../../types/uiComponents/smartFieldComposer/ISmartFieldComposer';
import { ISmartFieldComposerOptions } from '../../../../types/uiComponents/smartFieldComposer/ISmartFieldComposerOptions';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { FilterPanelServiceToken } from '../../../services/filter-panel/filter-panel-service-factory';
import { PluginServiceToken } from '../../../services/plugin/plugin-service-factory';
import { DataTypeResolverService } from '../../../services/plugins/data-type-resolver/data-type-resolver.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UserOptionsServiceToken } from '../../../services/user-options/user-options-service-factory';
import { OpenSmartFieldComposerEvent } from '../../../shared/constants/events';
import { plugins } from '../../../shared/constants/plugins';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { SmartFieldContainerComponent } from '../smart-field-container/smart-field-container.component';

@Component({
    selector: 'k2-smart-field-composer',
    templateUrl: './smart-field-composer.component.html',
    styleUrls: ['./smart-field-composer.component.less']
})
export class SmartFieldComposerComponent implements OnInit, OnDestroy, ISmartFieldComposer {
    selectedFunctionFieldIcon: string;
    composerHeading: string;
    selectedItem: IDropDownMenuItem;
    commandDrawerOpen: boolean;
    learnComposer: ILearnSmartFieldComposer;
    composers: ISmartFieldComposerOptions[];
    isActive: boolean;
    isHideFlex: boolean;
    linkto: string;

    private _isOpened: boolean;
    private _wheelEventHandler: EventListenerObject;
    private openSmartFieldComposerSub: Subscription;

    @ViewChild('sfcContainer') sfcContainer: ElementRef<HTMLElement>;
    @ViewChild('target') targetElement: ElementRef<HTMLElement>;
    @ViewChild('targetContent') targetContentElement: ElementRef<HTMLElement>;
    @ViewChild('mainContent') mainContent: ElementRef<HTMLElement>;
    @ViewChild('container') containerElement: ElementRef<HTMLElement>;
    @ViewChild('overlay') overlayElement: ElementRef<HTMLElement>;
    @ViewChild('arrow') arrowElement: ElementRef<HTMLElement>;

    private _config: {
        COMPOSER_WINDOW_PADDING: number;
        animationClassName: string;
        top: number;
        left: number;
        transitionDurationInMillisecond: string;
        position: ClientRect;
        inlineSmartField: IInlineSmartField;
    };

    constructor(private _element: ElementRef<HTMLElement>,
        private resourceService: ResourceService,
        @Inject(UserOptionsServiceToken) private _userOptionsService,
        @Inject(FilterPanelServiceToken) private _filterPanelService: IFilterPanelService,
        _eventService: EventNotifierService,
        private _dataTypeResolverService: DataTypeResolverService,
        @Inject(PluginServiceToken) private _pluginService: IPluginService) {
        this.composers = [];
        this.learnComposer = {
            hidden: false
        };
        this.openSmartFieldComposerSub = _eventService.subscribe(OpenSmartFieldComposerEvent, (options: ISmartFieldComposerOptions) => {
            if (this._element.nativeElement.contains(options.smartField.element.nativeElement)) { // only open if the event comes from a child
                this.open(options);
            }
        });
    }

    // Set the smart-field-composer element to be invisible to css
    @HostBinding('class.contents')
    ngOnInit(): void {
        this._config = {
            top: 0,
            left: 0,
            transitionDurationInMillisecond: '0ms',
            animationClassName: 'k2-SFC-Progress',
            position: this.getPosition(this._element.nativeElement),
            COMPOSER_WINDOW_PADDING: 10,
            inlineSmartField: <any>{},
        };
    }

    ngOnDestroy(): void {
        this.openSmartFieldComposerSub.unsubscribe();
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    public closeLearnComposer() {
        this._userOptionsService.setTipValue(sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypes.SmartFieldEditorTips, false);
        this.learnComposer.hidden = true;
    }

    //#region 'Inline Composer UI events'
    private getTypes(): IDropDownMenuItem[] {
        const types: sourceCode.k2Designer.objectModel.core.model.IEnumLookupPair[] = sourceCode.k2Designer.objectModel.core.configuredSmartFields.GetConfiguredSmartFieldTypesList();
        const dropDownItems: IDropDownMenuItem[] = [];

        if (types !== null && types !== undefined) {
            let newdropDownItem: IDropDownMenuItem;
            for (let i = 0; i < types.length; i++) {
                const spriteClass: string = this._dataTypeResolverService.getSpriteClassFromDataType(types[i].key.toString());

                newdropDownItem = {
                    key: types[i].key.toString(),
                    icon: 'icon-set-DataTypes ic-' + spriteClass,
                    title: types[i].title
                };
                //do not add the 'email address' and 'system name' properties to the dropdown
                //cannot remove this from generated files. 'email address' for instance is being used in the email wizard.
                if (newdropDownItem.key != '6' && newdropDownItem.key != '7') {
                    dropDownItems.push(newdropDownItem);
                }
            }
            this.selectedItem = newdropDownItem;

            return dropDownItems;
        } else {
            return [];
        }
    }

    private changeComposerIcon(item: IDropDownMenuItem): void {
        this.selectedFunctionFieldIcon = this._dataTypeResolverService.getIconBySmartFieldType(parseInt(item.key, 10));
    }

    public collapseFilterPanel(index: number): void {
        //If the filter panel originated from a composer that has now been closed, call toggleCollapsed to close the filter panel
        if (this.composers[index]) {
            for (let i = index; i < this.composers.length; i++) {
                if (this._filterPanelService.isFilterPanelOriginatedFrom(this.composers[i].data)) {
                    const filterPanelPlugIn: sourceCode.k2Designer.objectModel.plugin.IDefinition = this._pluginService.getPlugin(plugins.panels.filterPanel).plgnDef;
                    this._pluginService.toggleCollapsed(filterPanelPlugIn.name);
                    break;
                }
            }
        }
    }

    public getLeftArrowPosition(position: number): number {
        return (!!position && position < 0) ? 0 : position;
    }

    //#endregion 'Inline Composer UI events'


    public open(newComposer: ISmartFieldComposerOptions, duration?: string): void {
        if (!this.isSmartFieldExist(newComposer.data)) {
            newComposer.container.isOwnedByComposer = false;
            newComposer.smartField.smartFieldComposer = this;
            newComposer.container.smartFieldContainers.forEach((sf) => {
                const index = this.composers.findIndex((comp) => comp.smartField === sf);
                if (index > 0) {
                    this.close(index);
                }
            });

            if (this.composers.length < 1 && !this._isOpened) {
                const $smartfieldElement = $(newComposer.smartField.element.nativeElement);
                if ($smartfieldElement.width() > 0) {
                    // reset the positions
                    this._config.top = 0;
                    this._config.left = 0;
                    this._openNewComposer(newComposer, duration);
                }
                else {
                    this.waitForSmartFieldRendered($smartfieldElement, (context: any) => {
                        context._openNewComposer(newComposer, duration);
                    }, this);
                }
            }
            else {
                const popUp = $(newComposer.smartField.element.nativeElement).closest('.composer-container');
                if (!popUp.length) {
                    this.close(0, '0');
                    this._openNewComposer(newComposer, '0');
                } else {
                    newComposer.container.isOwnedByComposer = true;
                    this._addToExistingComposer(newComposer, popUp);
                }
            }
        }

        if (!newComposer.container.isOwnedByComposer) {
            //disable the container while the composer is open
            newComposer.container.disableForComposer();
        }

        if (!newComposer.eventHandlers) {
            return;
        }

        for (let i = 0; i < newComposer.eventHandlers.length; i++) {
            if (newComposer.eventHandlers[i].onOpen) {
                newComposer.eventHandlers[i].onOpen();
            }
        }

    }

    public closeComposer(composer: ISmartFieldComposerOptions, duration?: string): void {
        const ind: number = this.composers?.indexOf(composer) || 0;
        this.close(ind < 0 ? null : ind, duration);
    }

    public close(index: number = 0, duration: string = null): void {
        const composer = this.composers[index];

        if (composer?.smartField?.smartFieldComposer) {
            composer.smartField.isConfiguring = false;
            composer.smartField.smartFieldComposer = undefined;
        }

        if (!composer) {
            return;
        }

        if (index < 1) {
            this.resetPosition(duration);
        }
        else {
            //set animate to immediate
            if (duration) {
                const composerParent = $(this._element.nativeElement).find('.composer-container:eq(' + index + ')').closest('.smartfield-composer');
                composerParent.addClass('no-animate');
            }
            this.composers.splice(index);
        }

        if (!composer.container.isOwnedByComposer) {
            //enable the container now that the composer is closed
            composer.container.enableForComposer();
        }

        if (!composer.eventHandlers) {
            return;
        }

        for (let i = 0; i < composer.eventHandlers.length; i++) {
            if (composer.eventHandlers[i].onClose) {
                composer.eventHandlers[i].onClose();
            }
        }
    }

    public closeForSmartField(smartField: SmartFieldContainerComponent) {
        const index = this.composers.findIndex((composer) => composer.smartField === smartField);
        if (index > -1) {
            this.close(index);
        }
    }

    public adjustArrowPosition(isNewComposer: boolean, index?: number): void {
        const composerIndex = index || this.composers.length - 1;
        let SFParent = this.composers[composerIndex].container.htmlElement;
        if (this.composers.length === 1 && $(SFParent).outerWidth() > (this.mainContent.nativeElement.clientWidth - (2 * this._config.COMPOSER_WINDOW_PADDING))) {
            SFParent = $(this.overlayElement.nativeElement).find('.main')[0];
        }

        this.calculateArrowLeftPosition(isNewComposer, composerIndex, SFParent);
        this.calculateArrowsTopPosition(isNewComposer, composerIndex, SFParent);
    }

    public toggleCommandDrawer(index?: number): void {
        //remove parent pointer
        const ind = index || 0;

        this.composers[ind].commandDrawerOpen = !this.composers[ind].commandDrawerOpen;
        if (this.composers[ind].commandDrawerOpen) {
            this.composers[ind].commandDrawerHeight = $(this._element.nativeElement).find('.composer-container:eq(' + index + ') .commandDrawer .commandDrawerContent').height();
        } else {
            this.composers[ind].commandDrawerHeight = 0;
        }

        //close learn composer
        this.learnComposer.hidden = true;
    }
    //endregion 'Public'

    //#region 'Open Support functions'
    private waitForSmartFieldRendered($smartfieldElement: JQuery, callback: Function, context: any) {
        const timerId = setInterval(() => {
            if ($smartfieldElement.width() > 0) {
                clearInterval(timerId);
                callback(context);
            }
        }, 10);
    }

    private _addToExistingComposer(newComposer: ISmartFieldComposerOptions, popUp: JQuery) {
        const index = parseInt(popUp.find('.content').attr('index'), 10);
        if ($(this._element.nativeElement).find('.smartfield-composer:not(.ng-leave) .composer-container').length - 1 > index) {
            this.close(index + 1);
        }

        this.composers.push(newComposer);
        this.adjustArrowPosition(false);
    }

    private _openNewComposer(newComposer: ISmartFieldComposerOptions, duration?: string) {
        this.composers.push(newComposer);
        setTimeout(() => {
            this._adjustComposerPosition(newComposer, duration);
        }, 0);
    }

    private _adjustComposerPosition(newComposer: ISmartFieldComposerOptions, duration?: string) {
        const inlineSmartField = this.getInlineSmartFieldOf(newComposer);
        const composerContainer = this.containerElement.nativeElement;

        this._config.inlineSmartField = inlineSmartField;

        this.setupPosition(inlineSmartField);
        this.setupTransitionDuration(this._config.top, this._config.left);
        this.prepForAnimation(composerContainer, this._config.top);

        this.adjustArrowPosition(true);

        const transitionDuration = duration || this._config.transitionDurationInMillisecond;
        this.animate('show', transitionDuration);
    }

    private setupTransitionDuration(top: number, left: number): void {
        const length = Math.sqrt(Math.pow(top, 2) + Math.pow(left, 2));
        this._config.transitionDurationInMillisecond = sourceCode.k2Designer.utilities.ui.getTransitionVelocityFor(length);
    }

    private getInlineSmartFieldOf(originalComposer: ISmartFieldComposerOptions): IInlineSmartField {
        let inlineSmartField: IInlineSmartField;
        const inlineSFPos = this.getPosition(originalComposer.smartField.element.nativeElement);

        inlineSmartField = {
            htmlElement: originalComposer.smartField.element.nativeElement,
            position: inlineSFPos,
            textbox: originalComposer.container
        };

        return inlineSmartField;
    }

    private getTopPosition(inlineSmartField: IInlineSmartField, composerParentPosition: ClientRect): number {
        const top = inlineSmartField.position.top - composerParentPosition.top - this.overlayElement.nativeElement.clientHeight - 5 - (inlineSmartField.position.height / 2);
        return top;
    }

    private getLeftPosition(inlineSmartField: IInlineSmartField): number {
        const overlayPadding = this._config.COMPOSER_WINDOW_PADDING;
        let left = this.mainContent.nativeElement.getBoundingClientRect().left;
        let right = 0;

        const mainContentLeft = this.mainContent.nativeElement.getBoundingClientRect().left;
        const mainContentRight = this.mainContent.nativeElement.getBoundingClientRect().right;
        left = inlineSmartField.position.left - mainContentLeft;
        right = inlineSmartField.position.right - mainContentRight;

        if (left < 0) {
            return (-1) * (left - this._config.COMPOSER_WINDOW_PADDING - overlayPadding);
        } else if (right > 0) {
            return (-1) * (right + this._config.COMPOSER_WINDOW_PADDING + overlayPadding);
        }

        return 0;
    }

    private setupPosition(inlineSmartField: IInlineSmartField): void {
        if (this.sfcContainer !== null) {
            const position = this.getPosition(this.sfcContainer.nativeElement);
            this._config.position = this.getPosition($(this._element.nativeElement).find('.inlineComposersContainer-content')[0]);

            let existingTop = 0;
            let existingLeft = 0;

            if (this.composers.length === 1) {
                existingTop = this._config.top;
                existingLeft = this._config.left;
            }

            const top = this.getTopPosition(inlineSmartField, position);
            this._config.top = existingTop + top;

            const left = this.getLeftPosition(inlineSmartField);
            this._config.left = existingLeft + left;
        }
    }

    private getTransform(x: number, y: number) {
        return 'translate3d(' + x + 'px,' + y + 'px, 0px)';
    }

    private prepForAnimation(composerContainer: HTMLElement, top: number): void {
        $(composerContainer).css({ top: top + 'px' });
        const width = $(this.sfcContainer.nativeElement).outerWidth();
        const targetContent = this.targetContentElement;
        $(targetContent).css({ width: width + 'px', 'margin-left': ((-1) * width / 2) + 'px', position: 'absolute', left: '50%' });
        $(this.sfcContainer.nativeElement).css({ minWidth: this.mainContent.nativeElement.clientWidth + 'px' });
    }

    private animate(type: string, durationInMillisecond: string, onAnimated_callback?: Function) {
        const target = this.targetElement.nativeElement;
        const composerContainer = this.containerElement.nativeElement;

        const pos_X = this._config.left;
        const pos_Y = this._config.top;

        if (typeof onAnimated_callback === 'function') {
            if (parseInt(durationInMillisecond, 10) > 1) {
                this.onInlineComposerContainerAnimated(onAnimated_callback);
            } else {
                onAnimated_callback();
            }
        }

        switch (type.toUpperCase()) {
            case 'SHOW':
                $(composerContainer).css({
                    transitionDuration: durationInMillisecond
                    , opacity: '1'
                    , transform: this.getTransform((-1) * pos_X, 0)
                });
                $(target).css({
                    transitionDuration: durationInMillisecond
                    , transform: this.getTransform(pos_X, (-1) * pos_Y)
                });
                this._isOpened = true;
                break;
            default:
                $(composerContainer).css({
                    transitionDuration: durationInMillisecond
                    , opacity: '0'
                    , transform: this.getTransform(0, 0)
                });
                $(target).css({
                    transitionDuration: durationInMillisecond
                    , transform: this.getTransform(0, 0)
                });
        }
    }

    //#endregion 'Open Support functions'

    //#region 'Close Support functions'
    private resetPosition(duration?: string): void {
        const clearStyle = () => {
            this.composers.splice(0);
            this._isOpened = false;
        };
        const transitionDuration: string = duration || this._config.transitionDurationInMillisecond;
        this.animate('hide', transitionDuration, clearStyle);
    }
    //#endregion 'Close Support functions'

    //#region 'Helper'
    private getPosition(html: HTMLElement): ClientRect {
        return html?.getBoundingClientRect() || null;
    }

    private onInlineComposerContainerAnimated(callback): void {
        const composerContainer = $(this.containerElement.nativeElement);
        const animationClassName = this._config.animationClassName + (new Date()).getTime();
        composerContainer.one('transitionend webkitTransitionEnd oTransitionEnd MSTransitionEnd', function() {

            if ($(this).hasClass(animationClassName)) {
                $(this).removeClass(animationClassName);
                $(this).off('transitionend webkitTransitionEnd oTransitionEnd MSTransitionEnd');

                callback();
            }
        });
        composerContainer.addClass(animationClassName);
    }

    private isSmartFieldExist(smartFieldData: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField): boolean {
        return this.composers.filter((currValue: ISmartFieldComposerOptions) => currValue.data === smartFieldData).length > 0;
    }

    private calculateArrowLeftPosition(isNewComposer: boolean, composerIndex: number, SFParent: HTMLElement) {
        //ensure containing element is not hidden for correct calculations
        const arrowWidth = $(this.arrowElement.nativeElement).outerWidth();
        const smartfieldElement: HTMLElement = this.composers[composerIndex].smartField.element.nativeElement;
        const mainContent = this.mainContent.nativeElement.getBoundingClientRect();
        const composeArrowElement = $(this._element.nativeElement).find('.composer-arrowAnimate').eq(composerIndex - 1).find('.composer-arrow');
        let composerArrowLeft = 0;

        if (composeArrowElement.length > 0) {
            const composerArrow = $(composeArrowElement).closest('.composer-arrowAnimate')[0];
            composerArrowLeft = composerArrow.getBoundingClientRect().left - mainContent.left;
        }

        let arrowLeft = (smartfieldElement.getBoundingClientRect().left - mainContent.left + (smartfieldElement.getBoundingClientRect().width / 2)) - (arrowWidth / 2) - composerArrowLeft;

        //Adjustment
        if (isNewComposer) {
            //Composer full width in Email and Rule designer panel on load
            arrowLeft += this._config.left;

        } else {
            //Adjust arrow left position for left and right edge compare to it's parent (Textbox).
            const SFParentRect = SFParent.getBoundingClientRect();
            const leftEdge = SFParentRect.left - mainContent.left - composerArrowLeft;
            const rightEdge = Math.min(leftEdge + SFParentRect.width, (mainContent.right - mainContent.left)) - arrowWidth;
            if (arrowLeft <= leftEdge) {
                arrowLeft = leftEdge;
            } else if (arrowLeft >= rightEdge) {
                arrowLeft = rightEdge;
            }
        }
        //end of adjustment

        this.composers[composerIndex].arrow.left = arrowLeft;
    }
    private calculateArrowsTopPosition(isNewComposer: boolean, composerIndex: number, SFParent: HTMLElement) {
        const preComposerContentBlock = $(this._element.nativeElement).find('.composer-arrowAnimate')[composerIndex - 1];
        const preComposerContentInnerBlock = $(this._element.nativeElement).find('.composer-container')[composerIndex - 1];
        if (preComposerContentBlock) {
            const textboxConnectorTop = (preComposerContentBlock.getBoundingClientRect().bottom + parseInt($(preComposerContentBlock).css('marginBottom'), 10) - (SFParent.getBoundingClientRect().bottom - parseInt($(SFParent).css('borderBottomWidth'), 10)));
            const composerContainerConnectorTop = preComposerContentInnerBlock.getBoundingClientRect().bottom - (SFParent.getBoundingClientRect().bottom - parseInt($(SFParent).css('borderBottomWidth'), 10));
            this.composers[composerIndex].arrow.container.top = (textboxConnectorTop - 1) * (-1);
            this.composers[composerIndex].arrow.top = (textboxConnectorTop - composerContainerConnectorTop) * (-1);
        }
    }
    //#endregion 'Helper'
}
