import { Component, ElementRef, HostListener, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { IConfiguredExpression } from '../../../../../types/models/rules/i-configured-expression';
import { IConfiguredStatement } from '../../../../../types/models/rules/i-configured-statement';
import { IRuleDesignerItem } from '../../../../../types/models/rules/i-rule-designer-item';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { RuleDesignerSelectionBaseComponent } from '../rule-designer-selection-base/rule-designer-selection-base.component';

@Component({
    selector: 'k2-rule-designer-item-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export class RuleDesignerItemBaseComponent extends RuleDesignerSelectionBaseComponent implements OnInit, OnDestroy, IRuleDesignerItem {

    @Input() public expressionModel: IConfiguredExpression;
    @Input() public statementModel: IConfiguredStatement;
    @Input() public isParent = false;

    constructor(@Inject(SelectionServiceToken) selService: ISelectionService,
        protected rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) private _commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) private _configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        _eventService: EventNotifierService,
        protected rootElement: ElementRef<HTMLElement>) {

        super(selService, _eventService, rootElement);

    }

    ngOnInit(): void {
        super.ngOnInit();

        //Add to the collection in plugin service
        this.rulePluginService.addRuleDesignerItem(this);
    }

    ngOnDestroy(): void {
        this.rulePluginService.removeRuleDesignerItem(this);
    }

    /**
     *DESC: This method must be implemented in the subclass scope for the choice of its deletion algorithm.
     *
     * @param callback: The callback function will be called once deletion successfully executed.
     */
    public /*virtual*/ delete = (): Promise<boolean> => {
        throw 'Implementation must be in the subclass';
    };

    /**
     * Creates drag ghost element for element
     *
     * @param item
     */
    protected /*virtual*/ getDragImage(): HTMLElement {
        const result: HTMLDivElement = document.createElement('div');

        result.className = 'rules dragGhost';
        result.innerHTML = this.rootElement.nativeElement.outerHTML;

        return result;
    }

    /**
     * Updates state to reflect drag start of rule designer item
     * */
    protected addDraggingState() {
        this.states.add(RuleControlStates.dragging);
    }

    /**
     * Updates state to reflect drag end of trule designer item
     * */
    protected removeDraggingState() {
        this.states.remove(RuleControlStates.dragging);
    }

    /**
     * DESC: This method is used to bind key event to the specific functionality. E.g. Delete/Backspace key is binded to delete the expression/statement directive.
     *
     * @param keyDownData: contains key event and current rule item object
     */
    @HostListener('keydown', ['$event'])
    public onKeyPress(event: KeyboardEvent): void {
        if (!this.states.isExist(RuleControlStates.textboxFocus)) {
            //TODO: Test that the default key events are not interfering (Removed preventDefault)
            event.stopPropagation(); // prevent k2 text box from stealing focus

            switch (event.keyCode) {
                case 90: // ctrl-z for undo
                //Only allow one undo/redo operation at a time.
                    if (event.ctrlKey && this._commandService.keyHaveBeenReleased()) {

                        //Set flag on command service to keep track that key is pressed down.
                        this._commandService.keyDown();
                        this._configPanelService.undo();
                    }
                    break;
                case 89: // ctrl-y for redo
                //Only allow one undo/redo operation at a time.
                    if (event.ctrlKey && this._commandService.keyHaveBeenReleased()) {

                        //Set flag on command service to keep track that key is pressed down.
                        this._commandService.keyDown();
                        this._configPanelService.redo();
                    }
                    break;
                case 46: case 8: //Delete Expression/Statement
                    this.undoRedoService.addUndoMarker('-- delete rules expression --');
                    this.delete().then(() => this.exit());
                    break;

                case 27: //ESC:
                    this.exit();
                    break;

                case 13:
                    this.enter();
                    break;

                case 9: //Tab
                    if (!event.shiftKey) {
                        this.focusNext();
                    } else {
                        this.focusPrevious();
                    }
                    break;

                default:
                    break;
            }
        }
    }

    /**
     * Implementation should be done in child class
     * */
    public /*virtual*/ focusNext = (): boolean =>

    //TODO: Test that the correct next element is focused.
    //(In AngularJS this was done by manually finding the next focusable element by tabIndex and calling jQuery.focus  on it)

        false;


    /**
     * Implementation should be done in child class
     * */
    public /*virtual*/ focusPrevious = (): boolean =>
    //TODO: Test that the correct previous element is focused.
    //(In AngularJS this was done by manually finding the previous focusable element by tabIndex and calling jQuery.focus  on it)

        false;



    private exit = () => {
        //TODO: focus parent if in group
    };

    private enter = () => {

        //TODO: focus first focusable element

    };

}
