import { AfterViewInit, ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, Input, NgZone, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { fromEvent, Observable, Subject, Subscription, throttleTime } from 'rxjs';
import { ScrollEvent } from 'src/types/events/scrollbar/scroll.event';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { ScrollbarEventType } from 'src/app/shared/enums/scroll-bar/scroll-bar-event-type';
import { isUndefinedOrNull, debounce } from 'src/app/shared/utils/general';
import { scrollbar } from '../../../shared/constants/scroll-bar';
import { debounceTime } from 'rxjs';
import { inputDebounceTime } from '../../../shared/constants/general';

/**
 * Constant for minimum scroll dragger size (34px)
 */
const MINSCROLLSIZE = 34;

/**
 * Constant for the scroll event name
 */
const SCROLLEVENTNAME = scrollbar.events.scrollEvent;
const MOUSESCROLLEVENTNAME = scrollbar.events.mouseScrollEvent;

@Component({
    selector: '[k2-scrollbar]',
    templateUrl: './scrollbar.component.html',
    styleUrls: ['./scrollbar.component.less']
})
export class ScrollbarComponent implements OnInit, AfterViewInit, OnDestroy {
    public static SCROLL_SPEED = 35;
    public static THROTTLE_TIME = 200;
    public static WHEEL_DIRECTION_UP = 'UP';
    public static WHEEL_DIRECTION_DOWN = 'DOWN';

    @Input() public scrollbarVisible: boolean;
    @Input() public scrollbarVisibleHorizontal: boolean;
    /*A boolean value indicating whether or not a horizonatal scrollbar should be present. Defaults to false*/
    @Input() public horizontal = false;
    /*A boolean value indicating whether or not a vertical scrollbar should be present. Defaults to true*/
    @Input() public vertical = true;
    /*Either a string value of dark or light which represents the shade of the scrollbar*/
    @Input() public shade: string;
    /*A value that adds an extra amount of space in pixels to the height of the inner scroll container*/
    @Input() public extraHeight: number;
    /*A value that adds an extra amount of space in pixels to the width of the inner scroll container*/
    @Input() public extraWidth: number;

    @Output() public scrollBarInit = new EventEmitter();
    /**
     * Flag to show that the scroller is set to minimum (34px), because of too many items in the containter.
     */
    isMinimumScrollSize: boolean;
    isMinimumScrollSizeHorizontal: boolean;

    @ViewChild('transcludeContainer') _transcludedContainer: ElementRef<HTMLElement>;
    @ViewChild('scroller') _scroller: ElementRef;
    @ViewChild('scrollcontainer') _scrollcontainer: ElementRef;
    @ViewChild('scrollerHorizontal') _scrollerHorizontal: ElementRef;
    @ViewChild('scrollerContainerHorizontal') _scrollerContainerHorizontal: ElementRef;

    private _dragger: IScrollBarDragger;
    private _draggerHorizontal: IScrollBarDraggerHorizontal;
    private _page;

    public scrollboxStyle;
    public draggerStyle;
    public draggerLineStyle;
    public pageStyle;

    private _scrollboxStyleHorizontal;
    public draggerStyleHorizontal;
    public draggerLineStyleHorizontal;

    private _lastOffsetY: number;
    private _lastOffsetX: number;
    private _rebuildTimer: number;
    //used to determine if bar is being scroll up or down
    private _previousScrollTop = 0;
    private _scrollDisplayTimeOut = 0;
    private _immobilize = false;
    private _domMutationObserver;
    private _horizontalDrag = false;
    private _verticalDrag = false;

    private _mouseMoveEvent: Subscription;
    private scrollEvent: EventEmitter<ScrollEvent>;
    private mouseScrollEvent: EventEmitter<ScrollEvent>;
    private scrollEventSub: Subscription;
    private mouseWheelSub: Subscription;

    constructor(
        private el: ElementRef<HTMLElement>,
        private eventService: EventNotifierService,
        private zone: NgZone,
        private changeDetectorRef: ChangeDetectorRef) { }

    ngOnInit(): void {
        this.initializeVariables();
    }

    ngOnDestroy(): void {
        this._domMutationObserver?.disconnect();
        this.scrollEventSub?.unsubscribe();
        this.mouseWheelSub?.unsubscribe();
        this._dragOverEventSub?.unsubscribe();
    }

    ngAfterViewInit(): void {
        // Wait a tick to avoid one-time devMode
        // unidirectional-data-flow-violation error
        setTimeout(() => {
            this.initializeScrollbar();
            //Bind events
            this.bind();
        }, 0);
    }

    private initializeVariables(): void {
        if (this.shade === null || this.shade === undefined) {
            this.shade = 'dark';
        }

        if (this.vertical === null || this.vertical === undefined) {
            this.vertical = true;
        }

        this._dragger = {
            top: 0,
            height: 0,
            preAdjustedHeight: 0,
            trackHeight: 0
        };
        this._draggerHorizontal = {
            left: 0,
            width: 0,
            preAdjustedWidth: 0,
            trackWidth: 0
        };
        this._page = {
            height: 0,
            innerWidth: 0,
            position: 'relative'
        };

        this.scrollboxStyle = {};
        this.draggerStyle = {};
        this.draggerLineStyle = {};
        this.pageStyle = {};

        this._scrollboxStyleHorizontal = {};
        this.draggerStyleHorizontal = {};
        this.draggerLineStyleHorizontal = {};

        this._lastOffsetY = 0;
        this._lastOffsetX = 0;
        this._rebuildTimer = 0;

        this.scrollEvent = this.eventService.addEvent(SCROLLEVENTNAME);
        this.mouseScrollEvent = this.eventService.addEvent(MOUSESCROLLEVENTNAME);

    }

    private bind(): void {


        $(window).on('resize', debounce(() => {
            this.reinitializeScrollbar();
        }, ScrollbarComponent.THROTTLE_TIME));

        //listens to the child emit and scrolls parent scrollbar if the child scrollbar hits the top or bottom
        this.scrollEventSub = this.eventService.subscribe(SCROLLEVENTNAME, (event: ScrollEvent) => {
            this.delegateScrollToParent(event);
        });

        this._domMutationObserver = new MutationObserver((mutations) => {
            this.reinitializeScrollbar();
            for (let i = 0; i < mutations.length; i++) {
                if (mutations[i].addedNodes && mutations[i].addedNodes.length > 0) {
                    this.mutationObserved(mutations[i].addedNodes);
                }
            }
        });

        this.zone.runOutsideAngular(() => {
            this.mouseWheelSub = fromEvent(this._transcludedContainer.nativeElement, 'mousewheel')
                .pipe(throttleTime(scrollbar.autoScrollDelay)) // only trigger the scrolling every 100ms
                .subscribe((event: DragEvent) => this.mouseScrollHandler(event));
        });

        this._domMutationObserver.observe(this._transcludedContainer.nativeElement, { childList: true, subtree: true });
    }

    public onScrollTranscludedContainer(event: any): void {

        event.preventDefault();
        event.stopPropagation();

        //Calculate the new position of the dragger
        //For vertical, use scrollTop (top position it is currently scrolled to), divided by scrollHeight (entire height of scrollable area),
        //multiplied by container height (visible height of container)
        //Top for vertical scrolling, Left for horizontal scrolling
        let containerHeight = $(this._transcludedContainer.nativeElement).height();
        let containerWidth = $(this._transcludedContainer.nativeElement).width();
        //When minimum scrolling size is used, take into account that the scroll handler size is no longer
        //an exact percentage of the container size and adjust container size
        if (this.isMinimumScrollSize) {
            containerHeight = containerHeight - MINSCROLLSIZE - this._dragger.preAdjustedHeight;
        }
        if (this.isMinimumScrollSizeHorizontal) {
            containerWidth = containerWidth - MINSCROLLSIZE - this._draggerHorizontal.preAdjustedWidth;
        }
        const top = ($(this._transcludedContainer.nativeElement).scrollTop() / $(this._transcludedContainer.nativeElement)[0].scrollHeight) * containerHeight;
        const left = ($(this._transcludedContainer.nativeElement).scrollLeft() / $(this._transcludedContainer.nativeElement)[0].scrollWidth) * containerWidth;

        if (top < this._previousScrollTop) {
            //scroll up
            this._dragger.top = Math.min(this._dragger.top, top);
        }
        else {
            //scroll down
            this._dragger.top = Math.max(this._dragger.top, top);
        }

        this._draggerHorizontal.left = Math.max(this._draggerHorizontal.left, Math.round(left));

        this.scrollTo(this._draggerHorizontal.left, this._dragger.top);

        //update previous scroll top
        this._previousScrollTop = this._dragger.top;

        if (this._scrollDisplayTimeOut > 0) {
            clearTimeout(this._scrollDisplayTimeOut);
        }
        $(this.el.nativeElement).find('.k2-scrollbar-wrap').addClass('scrolling');
        this._scrollDisplayTimeOut = window.setTimeout(() => {
            $(this.el.nativeElement).find('.k2-scrollbar-wrap').removeClass('scrolling');
        }, 400);

    }

    private mutationObserved(addedNodes: NodeList) {
        for (let i = 0; i < addedNodes.length; i++) {
            if (addedNodes[i] instanceof HTMLElement) {
                const newElement: HTMLElement = addedNodes[i] as HTMLElement;
                const isFocusOnInserted: Boolean = (!isUndefinedOrNull($(newElement).attr('k2-scrollbar-focus'))
                    || !isUndefinedOrNull($(newElement).attr('data-k2-scrollbar-focus')));
                const k2ScrollbarFocus: ng.IAugmentedJQuery = <ng.IAugmentedJQuery>($(newElement).find('[k2-scrollbar-focus]')
                    || $(newElement).find('[data-k2-scrollbar-focus]'));

                if (k2ScrollbarFocus.length > 0) {
                    for (let i = 0; i < k2ScrollbarFocus.length; i++) {
                        this.scrollToElement(k2ScrollbarFocus[i]);
                        break;
                    }
                }
                else if (isFocusOnInserted) {
                    this.scrollToElement(newElement);
                    break;
                }
            }
        }
    }

    private delegateScrollToParent(event: ScrollEvent): boolean {
        if (this.el === event.source || !this.el.nativeElement.contains(event.source?.nativeElement)) {
            //do not scroll self (we only want to scroll parent)
            return;
        }
        else {
            switch (event.type) {
                case (ScrollbarEventType.up):
                //scroll up
                    this.scrollTo(this._draggerHorizontal.left, this._dragger.top - ScrollbarComponent.SCROLL_SPEED);
                    break;
                case (ScrollbarEventType.down):
                //scroll down
                    this.scrollTo(this._draggerHorizontal.left, this._dragger.top + ScrollbarComponent.SCROLL_SPEED);
                    break;
                case (ScrollbarEventType.immobilize):
                //listen to the child mouseover and prevent scroll together
                    this._immobilize = true;
                    break;
            }
            return false;
        }
    }

    public doResize(): void {
        this.reinitializeScrollbar();
    }

    private calculateStyles(): void {

        this.scrollboxStyle = {
            overflow: 'hidden',
            'max-width': '100%',
            height: '100%',
            width: '100%'
        };

        if (this._page.height) {
            this.scrollboxStyle.height = this._page.height + 'px';
        }

        //Minimum scroll size
        if (this.vertical) {
            if (this._dragger.height < MINSCROLLSIZE) {
                //Keep a backup of what actual calculated height of dragger should be
                this._dragger.preAdjustedHeight = this._dragger.height;
                this._dragger.height = MINSCROLLSIZE;
                this.isMinimumScrollSize = true;
            }
        }
        if (this.horizontal) {
            if (this._draggerHorizontal.width < MINSCROLLSIZE) {
                //Keep a backup of what actual calculated width of dragger should be
                this._draggerHorizontal.preAdjustedWidth = this._draggerHorizontal.width;
                this._draggerHorizontal.width = MINSCROLLSIZE;
                this.isMinimumScrollSizeHorizontal = true;
            }
        }

        this.draggerStyle = {
            position: 'absolute',
            height: this._dragger.height + 'px',
            top: this._dragger.top + 'px'
        };

        this.draggerLineStyle = {
            position: 'relative',
            'line-height': this._dragger.height + 'px'
        };

        if (this._page.width) {
            this.scrollboxStyle.width = this._page.width + 'px';
        }

        this.draggerStyleHorizontal = {
            position: 'absolute',
            width: this._draggerHorizontal.width + 'px',
            left: this._draggerHorizontal.left + 'px'
        };

        this.draggerLineStyleHorizontal = {
            position: 'relative',
            'line-width': this._draggerHorizontal.width + 'px'
        };

        this.pageStyle = {
            position: 'relative',
            overflow: 'hidden'
        };
    }

    private trackClick(event): void {

        const offsetY = !isUndefinedOrNull(event.offsetY) ? event.offsetY : event.layerY;
        const newTop = Math.max(0, Math.min(this._dragger.trackHeight - this._dragger.height, offsetY));
        this._dragger.top = newTop;
        this.scrollTo(this._draggerHorizontal.left, this._dragger.top);

        event.stopPropagation();
    }

    private trackClickHorizontal(event): void {

        const offsetX = !isUndefinedOrNull(event.offsetX) ? event.offsetX : event.layerX;
        const newLeft = Math.max(0, Math.min(this._draggerHorizontal.trackWidth - this._draggerHorizontal.width, offsetX));

        this._draggerHorizontal.left = newLeft;
        this.scrollTo(this._draggerHorizontal.left, this._dragger.top);

        event.stopPropagation();
    }

    //type guard to check that object is of Interface IK2DropDownScope
    private instanceOfDropDown(object: any): object is sourceCode.k2Designer.directives.k2DropDown.IK2DropDownScope {
        return 'k2DropdownPositioning' in object;
    }

    private _f;
    public mouseScrollHandler = (event) => {
        if (this._immobilize) {
            return;
        }
        if (this.scrollbarVisible || this.scrollbarVisibleHorizontal) {
            this.scrollEvent.emit({ source: this.el, type: ScrollbarEventType.immobilize });
        }

        this.mouseScrollEvent.emit({ source: this.el, type: ScrollbarEventType.mouseWheel});

        if (event.target.nodeName.toLowerCase() === 'textarea' || jQuery(event.target).closest('.k2-text-box-ex.multi-line').length > 0) {
            let scrolltarget;

            if (event.target.nodeName.toLowerCase() === 'textarea') {
                scrolltarget = event.target;
            }
            else {
                scrolltarget = jQuery(event.target).closest('.k2-text-box-ex').find('.k2-text-editor-container')[0];
            }

            if ((event.wheelDelta < 0 && scrolltarget.scrollTop < (scrolltarget.scrollHeight - scrolltarget.clientHeight)) // Downwards scroll when textarea is not scrolled to the very bottom
                || (event.wheelDelta > 0 && scrolltarget.scrollTop > 0)) { // Upwards scroll when the textarea is not scrolled to the very top
                // Exit function allowing the native behaviour
                return true;
            }
        }

        $('.datepicker').remove();

        this.reinitializeScrollbar();

        const o = event; let d = o.detail; const w = o.wheelDelta; const n = 225; const n1 = n - 1;

        // Handles mouse scroll speed/movement (This works for firefox, where the event.wheelDelta does not.
        d = d ? w && (this._f = w / d) ? d / this._f : -d / 1.35 : w / 120;
        d = d < 1 ? d < -1 ? (-Math.pow(d, 2) - n1) / n : d : (Math.pow(d, 2) + n1) / n;
        event.delta = Math.min(Math.max(d / 2, -1), 1);

        event.delta = event.delta * ScrollbarComponent.SCROLL_SPEED;
        if (this.vertical && this.scrollbarVisible) {
            this._dragger.top = Math.max(0, Math.min(parseInt(this._page.height, 10) - this._dragger.height, this._dragger.top - event.delta));
        } else if (this.horizontal && this.scrollbarVisibleHorizontal) {
            this._draggerHorizontal.left = Math.max(0, Math.min(parseInt(this._page.width, 10) - this._draggerHorizontal.width, this._draggerHorizontal.left - event.delta));
        }
        this.scrollTo(this._draggerHorizontal.left, this._dragger.top);

        this.checkScrollbarPosition();

        if (event.preventDefault) {
            event.preventDefault();
        } else {
            return false;
        }
    };

    private checkScrollbarPosition(): void {
        //if the scrollbar is all the way down
        if (this._dragger.trackHeight === (this._dragger.height + this._dragger.top)) {
            //then try scroll down the parent scrollbar
            this.scrollEvent.emit({ source: this.el, type: ScrollbarEventType.down });
        }
        else if (this._dragger.top === 0) { //scrollbar is all the way to the top
            //then try scroll up the parent scrollbar
            this.scrollEvent.emit({ source: this.el, type: ScrollbarEventType.up });
        }
    }

    private scrollHandleDragHandler = (event: MouseEvent) => {
        if (this._verticalDrag) {
            const newOffsetY = event.pageY - this._scroller.nativeElement.scrollTop - this._lastOffsetY;
            this._dragger.top = Math.max(0, Math.min(this._dragger.trackHeight - this._dragger.height, newOffsetY));
        } else
        if (this._horizontalDrag) {
            const newOffsetX = event.pageX - this._scrollerHorizontal.nativeElement.scrollLeft - this._lastOffsetX;
            this._draggerHorizontal.left = Math.max(0, Math.min(this._draggerHorizontal.trackWidth - this._draggerHorizontal.width, newOffsetX));
        }
        if (this._horizontalDrag || this._verticalDrag) {
            this.scrollTo(this._draggerHorizontal.left, this._dragger.top);
            event.stopPropagation();
        }
    };

    @HostListener('window:mouseup', ['$event'])
    @HostListener('window:losecapture', ['$event'])
    public onMouseUp = (event) => {
        if (this._mouseMoveEvent) {
            this._mouseMoveEvent.unsubscribe();

            if (this._verticalDrag || this._horizontalDrag) {
                this._verticalDrag = this._horizontalDrag = false;
                event.stopPropagation();
            }
        }
    };

    private initializeScrollbar(): void {
        this._page.height = this.el.nativeElement.offsetHeight;

        if (this.extraHeight && this.extraHeight > 0) {
            this._page.scrollHeight = this._transcludedContainer.nativeElement.scrollHeight + this.extraHeight;
        } else {
            this._page.scrollHeight = this._transcludedContainer.nativeElement.scrollHeight;
        }

        ///////////////////////////////////////////////////////////////////////
        this._page.width = this.el.nativeElement.offsetWidth;

        if (this.extraWidth && this.extraWidth > 0) {
            this._page.scrollWidth = this._transcludedContainer.nativeElement.scrollWidth + this.extraWidth;
        } else {
            this._page.scrollWidth = this._transcludedContainer.nativeElement.scrollWidth;
        }
        ////////////////////////////////////////////////////////////////////////

        if ((this._page.height < this._page.scrollHeight) && this.vertical) {
            this.scrollbarVisible = true;
            // Calculate the dragger height
            const calculatedDraggerHeight = this._page.height / this._page.scrollHeight * this._page.height;

            const previousDraggerHeight = this._dragger.height - calculatedDraggerHeight;

            this._dragger.height = calculatedDraggerHeight;
            this._dragger.trackHeight = this._page.height;

            // update the transcluded content style and clear the parent's
            this.calculateStyles();

            if (!this.isMinimumScrollSize) {
                const adjustedDraggerHeight = previousDraggerHeight - this._dragger.height;
                const top_1 = Math.min(this._page.height - this._dragger.height, this._dragger.top);
                const top_2 = top_1 + adjustedDraggerHeight;

                this._dragger.top = Math.max(0, top_1, top_2);
                this.scrollTo(this._draggerHorizontal.left, this._dragger.top);
            }

        } else {
            this.scrollbarVisible = false;
        }
        // Horizontal scrollbar
        if ((this._page.width < this._page.scrollWidth) && this.horizontal) {
            this.scrollbarVisibleHorizontal = true;

            // Calculate the dragger width
            this._draggerHorizontal.width = Math.round(this._page.width / this._page.scrollWidth * this._page.width);
            this._draggerHorizontal.trackWidth = this._page.width;

            // update the transcluded content style and clear the parent's
            this.calculateStyles();


            this._draggerHorizontal.left = Math.max(0, Math.min(parseInt(this._page.width, 10) - this._draggerHorizontal.width, this._draggerHorizontal.left));
            this.scrollTo(this._draggerHorizontal.left, this._dragger.top);

        } else {
            this.scrollbarVisibleHorizontal = false;
        }
    }

    public reinitializeScrollbar(): void {
        this._immobilize = false;
        /* jshint -W116 */
        if (this._rebuildTimer != null) {
            clearTimeout(this._rebuildTimer);
        }

        const that = this;

        this._rebuildTimer = window.setTimeout(function() {
            that.initializeScrollbar();
        }, 72);
    }

    public scrollTo(x: number, y: number, callback?: Function): void {
        this.cleanUpScroller(x, y);

        //If Horizontal scrollbar is enabled, scroll to x position
        if (this.horizontal && this._page.scrollWidth > this._page.width) {
            this.draggerStyleHorizontal.left = `${x}px`;
            let draggerOffsetX = x / this._page.width;
            if (this.isMinimumScrollSizeHorizontal) {
                draggerOffsetX = x / (this._page.width - MINSCROLLSIZE - this._draggerHorizontal.preAdjustedWidth);
            }
            this._page.left = -Math.round(this._page.scrollWidth * draggerOffsetX);
        }

        //If Vertical scrollbar is enabled, scroll to y position
        if (this.vertical && this._page.scrollHeight > this._page.height) {
            this.draggerStyle.top = `${y}px`;
            let draggerOffsetY = y / this._page.height;
            if (this.isMinimumScrollSize) {
                draggerOffsetY = y / (this._page.height - MINSCROLLSIZE - this._dragger.preAdjustedHeight);
            }

            this._page.top = -(this._page.scrollHeight * draggerOffsetY);
        }

        if (callback) {
            callback();
        }
    }
    private cleanUpScroller(x: number, y: number) {
        y = isUndefinedOrNull(y) ? 0 : y;
        x = isUndefinedOrNull(x) ? 0 : x;

        this.pageStyle.overflow = 'hidden';
        this.pageStyle.height = '100%';
        this.pageStyle.width = '100%';

        let containerHeight = $(this._transcludedContainer.nativeElement).height();
        let containerWidth = $(this._transcludedContainer.nativeElement).width();
        if (this.isMinimumScrollSize) {
            //The dragger has a minimum size, so take that in account when calculating where to scroll to
            containerHeight = containerHeight - MINSCROLLSIZE - this._dragger.preAdjustedHeight;
        }
        if (this.isMinimumScrollSizeHorizontal) {
            containerWidth = containerWidth - MINSCROLLSIZE - this._draggerHorizontal.preAdjustedWidth;
        }
        const sTop = (y / containerHeight) * $(this._transcludedContainer.nativeElement)[0].scrollHeight;
        const sLeft = (x / containerWidth) * $(this._transcludedContainer.nativeElement)[0].scrollWidth;
        if (this.vertical) $(this._transcludedContainer.nativeElement).scrollTop(sTop);
        if (this.horizontal) $(this._transcludedContainer.nativeElement).scrollLeft(sLeft);
    }

    public scrollToElement(element: HTMLElement, callback?: Function): void {
        const timeoutId = setTimeout(() => {
            clearTimeout(timeoutId);
            const scrollWrapper = this.el.nativeElement;
            const isExist = !isUndefinedOrNull(element) && $.contains(document.body, element);

            //Check if element is ready to be scroll to.
            if (isExist && element.getBoundingClientRect().width > 0) {
                this.initializeScrollbar();

                let x = 0;
                if (scrollWrapper.getBoundingClientRect().width > element.getBoundingClientRect().width) {
                    x = element.getBoundingClientRect().right - scrollWrapper.getBoundingClientRect().right - 10 > 0
                        ? element.getBoundingClientRect().right - scrollWrapper.getBoundingClientRect().right - 10 : x;
                    x = element.getBoundingClientRect().left - scrollWrapper.getBoundingClientRect().left < 0
                        ? element.getBoundingClientRect().left - scrollWrapper.getBoundingClientRect().left : x;
                }

                let y = 0;
                if (scrollWrapper.getBoundingClientRect().height > element.getBoundingClientRect().height) {
                    y = element.getBoundingClientRect().bottom - scrollWrapper.getBoundingClientRect().bottom > 0
                        ? element.getBoundingClientRect().bottom - scrollWrapper.getBoundingClientRect().bottom : y;
                    y = element.getBoundingClientRect().top - scrollWrapper.getBoundingClientRect().top < 0
                        ? element.getBoundingClientRect().top - scrollWrapper.getBoundingClientRect().top : y;
                }

                this._draggerHorizontal.left += x;
                this._dragger.top += y;

                this.scrollTo(this._draggerHorizontal.left, this._dragger.top, callback);
            }
            else if (callback) {
                callback();
            }

        }, 0);

    }

    public scrollerMouseDown(event: MouseEvent) {
        this._mouseMoveEvent = fromEvent(window, 'mousemove').subscribe((event: MouseEvent) => { this.scrollHandleDragHandler(event); });
        this._lastOffsetY = event.pageY - this._scroller.nativeElement.offsetTop;
        this._verticalDrag = true;
        event.preventDefault();
    }

    public scrollerMouseDownHorizontal(event: MouseEvent) {
        this._mouseMoveEvent = fromEvent(window, 'mousemove').subscribe((event: MouseEvent) => { this.scrollHandleDragHandler(event); });
        this._lastOffsetX = event.pageX - this._scrollerHorizontal.nativeElement.offsetLeft;
        this._horizontalDrag = true;
        event.preventDefault();
    }

    public mouseEnter(event) {
        //immobilize the scrollbar only if it is active
        if (this.scrollbarVisible === true) {
            this.scrollEvent.emit({ source: this.el, type: ScrollbarEventType.immobilize });
        }

        this.reinitializeScrollbar();

        return false;
    }

    onDragEnter() {
        this.zone.runOutsideAngular(() => {
            this._dragOverEventSub = fromEvent(this._transcludedContainer.nativeElement, 'dragover')
                .pipe(throttleTime(scrollbar.autoScrollDelay)) // only trigger the scrolling every 100ms
                .subscribe((event: DragEvent) => this.onDragOver(event));
        });
    }

    onDragLeave() {
        this._dragOverEventSub?.unsubscribe();
        this._dragOverEventSub = null;
    }

    private _dragOverEventSub: Subscription;
    onDragOver(event: DragEvent) {
        let delta;
        const bounds = this._transcludedContainer.nativeElement.getBoundingClientRect();
        const mouseRelativeX = event.x - bounds.x;
        const mouseRelativeY = event.y - bounds.y;
        if (this.vertical && this.scrollbarVisible) {
            if (mouseRelativeY < scrollbar.autoScrollRegionSize) {
                delta = ScrollbarComponent.SCROLL_SPEED;
            } else if (mouseRelativeY > (this._transcludedContainer.nativeElement.clientHeight - scrollbar.autoScrollRegionSize)) {
                delta = -ScrollbarComponent.SCROLL_SPEED;
            } else {
                return true;
            }
            this._dragger.top = Math.max(0, Math.min(parseInt(this._page.height, 10) - this._dragger.height, this._dragger.top - delta));
        }
        if (this.horizontal && this.scrollbarVisibleHorizontal) {
            if (mouseRelativeX < scrollbar.autoScrollRegionSize) {
                delta = ScrollbarComponent.SCROLL_SPEED;
            } else if (mouseRelativeX > (this._transcludedContainer.nativeElement.clientWidth - scrollbar.autoScrollRegionSize)) {
                delta = -ScrollbarComponent.SCROLL_SPEED;
            } else {
                return true;
            }
            this._draggerHorizontal.left = Math.max(0, Math.min(parseInt(this._page.width, 10) - this._draggerHorizontal.width, this._draggerHorizontal.left - delta));
        }
        if (delta) {
            this.scrollTo(this._draggerHorizontal.left, this._dragger.top);
            this.changeDetectorRef.detectChanges(); // since the entire dragover event is running outside of angular, we have to tell angular that we have changed something
        }
    }
}
