import { Injectable } from '@angular/core';
import { ICacheContent } from '../../../types/services/cache/i-cache-content';

const DEFAULT_MAX_AGE = 300000;

/**
 * A simple cache mechanism to store expirable values of any type indexed by a string key
 */
@Injectable({
    providedIn: 'root'
})
export class CacheService {

    private cache: {
        [key: string]: ICacheContent;
    } = {};

    constructor() { }

    /**
     * Gets the cached value by key value
     *
     * @param key
     */
    get(key: string): any {

        // Find the current entry
        const entry = this.cache[key];

        if (entry) {
            // If the entry has expired, remove from cache
            if (entry.expiry && entry.expiry < Date.now()) {
                delete this.cache[key];
            }

            return entry.value;
        }

        return null;
    }

    /**
     * Adds or updates the value to the cache indexed by the provided key
     *
     * @param key Indexing key
     * @param value Value to be cached
     * @param maxAge Maximum age in milliseconds, NULL / 0 (zero) / false will set no expiry
     */
    set(key: string, value: any, maxAge: number = DEFAULT_MAX_AGE): void {

        // Find the current entry
        const entry = this.get(key);

        // Remove entry
        if (!!entry) {
            delete this.cache[key];
        }

        // New entry added
        this.cache[key] = {
            value, expiry: (!!maxAge ? Date.now() + maxAge : null)
        };
    }

    /**
     * Removes a value from the cache
     *
     * @param key
     */
    public remove(key: string) {
        delete this.cache[key];
    }

    /**
     * Returns a collection of keys of values that have expired
     */
    public get expiredKeys(): string[] {

        const keys: string[] = [];

        for (const key in this.cache) {
            if (this.cache.hasOwnProperty(key)) {

                // Access entry
                const entry = this.cache[key];

                // If the entry has expired, add to keys collection
                if (entry.expiry && entry.expiry < Date.now()) {
                    keys.push(key);
                }
            }
        }

        return keys;
    }

    /**
     * Returns a collection of all the keys regardless of expiry
     */
    public get allKeys(): string[] {

        const keys: string[] = [];

        for (const key in this.cache) {
            if (this.cache.hasOwnProperty(key)) {
                keys.push(key);
            }
        }

        return keys;
    }

    /**
     * Returns a collection of keys of valid (non-expired) items
     */
    public get validKeys(): string[] {

        const keys: string[] = [];

        for (const key in this.cache) {
            if (this.cache.hasOwnProperty(key)) {

                // Access entry
                const entry = this.cache[key];

                // If the entry has not expired, add to keys collection
                if (!entry.expiry || (entry.expiry && entry.expiry >= Date.now())) {
                    keys.push(key);
                }
            }
        }

        return keys;

    }

    /**
     * Removes expired values from the cache, returning the keys removed
     */
    public purge(): string[] {

        const keys: string[] = [];

        for (const key in this.cache) {
            if (this.cache.hasOwnProperty(key)) {

                // Access entry
                const entry = this.cache[key];

                // If the entry has expired, remove from cache
                if (entry.expiry && entry.expiry < Date.now()) {
                    keys.push(key);
                    delete this.cache[key];
                }
            }
        }

        return keys;
    }

    /**
     * Removes all entries from the cache
     */
    public clear(): void {
        for (const key in this.cache) {
            if (this.cache.hasOwnProperty(key)) {
                delete this.cache[key];
            }
        }
    }
}
