/**
 * Deferred object to be used to resolve/reject a promise outside of promise scope
 */
export class Deferred<T> {
    resolve: (val: T) => void;
    reject: (err?: any) => void;
    private _promise: Promise<T>;
    constructor() {
        this._promise = new Promise<T>((resolve, reject) => {
            this.reject = reject;
            this.resolve = resolve;
        });
    }

    destroy() {
        this.reject('object got destroyed before promise fulfilled');
    }

    toPromise(): Promise<T> {
        return this._promise;
    }
}
