import { Component, ElementRef, Inject, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { IMergeExpressionConfiguration } from '../../../../../types/config/rules/expressions/IMergeExpressionConfiguration';
import { IMergeDropDownContext } from '../../../../../types/models/rules-designer/expression/IMergeDropDownContext';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { rules } from '../../../../shared/constants/rules';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { isUndefinedOrNull } from '../../../../shared/utils/general';
import { DropDownComponent } from '../../../common/drop-down/drop-down.component';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';
import { IConfiguredMergeExpression } from '../../../../../types/models/rules/i-configured-merge-expression';

@Component({
    selector: 'k2-merge-expression',
    templateUrl: './merge-expression.component.html',
    styleUrls: ['./merge-expression.component.less']
})
export class MergeExpressionComponent extends ExpressionBaseComponent implements OnInit, OnDestroy {
    public /*override*/ expressionModel: IConfiguredMergeExpression;

    public mergePathsDropDownName = 'path-menu';
    public mergePathsItems: IDropDownMenuItem[];
    public mergePathsFooterItems: IDropDownMenuItem[];

    public mergeStateDropDownName = 'state-menu';
    public mergeStateItems: IDropDownMenuItem[];

    // Enum imports for use in templates
    public dropdownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate = DropdownMenuItemTemplates;

    public pathsDropDownTemplate: DropdownMenuItemTemplates;
    public stateDropDownTemplate: DropdownMenuItemTemplates;

    @ViewChild('mergePathsDropDown')
    public mergePathsDropDownRef: DropDownComponent;
    @ViewChild('mergeStateDropDown')
    public mergeStateDropDownRef: DropDownComponent;

    private _allPaths: any[];
    private selectedPathItem;
    private selectedStateItem;
    private activityConfListener;
    private _isPathConnected = false;

    constructor(@Inject(SelectionServiceToken) _selService: ISelectionService,
        rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        resourceService: ResourceService,
        eventService: EventNotifierService,
        private _el: ElementRef) {
        super(_selService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, _el, resourceService);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this._allPaths = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as IMergeExpressionConfiguration).paths;

        this.setDefaultSelectedItem();
        this.mergePathsItems = this.getPathsDropdownItems();
        this.mergePathsFooterItems = this.getPathsDropdownFooterItems();
        this.mergeStateItems = this.getStateDropDownItems();

        const activityCnf = this.rulePluginService.contextModel.getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration>();
        this.activityConfListener = activityCnf?.listen(sourceCode.k2Designer.objectModel.core.model.EventTypes.anythingChanged, () => {
            this.mergePathsItems = this.getPathsDropdownItems();
            return true;
        });
    }

    /**
     * Returns the operator classes for the given drop down instance
     *
     * @param dropdownInstance drop down instance to retrieve classes for
     */
    public getOperatorIcon(dropdownName: string): string {
        if (!dropdownName) {
            return '';
        }
        let iconClass: string = this.expressionModel.logicalOperatorIcon + ' selected';
        const isOnHover = this.states.isExist(dropdownName + '-hover');
        const isMenuActive = this.states.isExist(dropdownName + '-active');

        if (isOnHover || isMenuActive) {
            iconClass = rules.icons.operatorIconHoverOrActive;
        }
        return iconClass;
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this.activityConfListener) {
            this.activityConfListener();
        }
    }

    /**
     * Event handler for drop down menu open
     *
     * @param dropdownInstance drop down which menu opened
     */
    public onMenuOpen(dropDownName: string) {
        const actionMenuName = dropDownName + '-active';
        this.states.add(actionMenuName);
        this.states.add(RuleControlStates.edit);
    }

    /**
     * Event handler for drop down menu close
     *
     * @param dropdownInstance drop down which menu closed
     */
    public onMenuClose(dropDownName: string, dropdownInstance: DropDownComponent) {
        if (this.states.isExist(dropDownName + '-active')) {
            this.states.remove(dropDownName + '-active');
            dropdownInstance.el.nativeElement.focus();
            this.states.remove(RuleControlStates.edit);
        }
    }

    public addDraggingState() {
        this.states.add(RuleControlStates.dragging);
    }

    public removeDraggingState() {
        this.states.remove(RuleControlStates.dragging);
    }

    public operatorOnMouseEnter(dropDownName: string) {
        const hoverMenuName = dropDownName + '-hover';
        this.states.add(RuleControlStates.hover);
        if (!isUndefinedOrNull(hoverMenuName)) {
            this.states.add(hoverMenuName);
        }
    }

    public operatorOnMouseLeave(dropDownName: string) {
        const hoverMenuName = dropDownName + '-hover';
        this.states.remove(RuleControlStates.hover);
        if (!isUndefinedOrNull(hoverMenuName)) {
            this.states.remove(hoverMenuName);
        }
    }


    private setDefaultSelectedItem() {
        const outcomes = this.getPaths();
        const pathTitle = this.expressionModel?.pathTitle || '';

        if (outcomes && outcomes.length > 0 && !pathTitle) {
            this.expressionModel.pathTitle = this.resourceService.getString(this._allPaths[0].titleToken);
            this.expressionModel.pathReference = this._allPaths[0].name;
        }
        if (!this.expressionModel.state) {
            this.expressionModel.state = rules.mergeStates.completed;
        }
    }

    //Paths related operations
    private getPaths(): sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcome[] {
        let outcomes = [];
        if (this.rulePluginService.contextModel) {
            const activityCnf = this.rulePluginService.contextModel.getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration>();
            const split: any = activityCnf.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelObject>().findItem(
                (activityCnf as sourceCode.k2Designer.objectModel.workflow.process.k2Process.activities.mergeActivity.IMergeActivityConfiguration).selectedSplitStepReference);
            if (!split) {
                this._isPathConnected = false;
            } else {
                this._isPathConnected = true;
                outcomes = split.configuration.outcomes;
            }
        }
        return outcomes;
    }

    protected getPathsDropdownItems(): IDropDownMenuItem[] {
        const outcomes = this.getPaths();
        const items = [];
        if (outcomes?.length > 0) {
            for (let i = 0; i < outcomes.length; i++) {
                if (i >= 1) {
                    items.push(DropDownComponent.separator);
                }

                const isSelected: boolean = (!!this.expressionModel.pathReference && this.expressionModel.pathReference.length > 0)
                    ? outcomes[i].getReference() === this.expressionModel.pathReference
                    : false;

                items.push({
                    title: outcomes[i].title,
                    key: outcomes[i].getReference(),
                    icon: this.getIconFor(outcomes[i].getReference()),
                    isSelected
                });
                this.pathsDropDownTemplate = DropdownMenuItemTemplates.SimpleExpressionOperator;
            }
        }

        if (!this.isSelectedPathExist && this.expressionModel?.pathTitle?.trim().length > 0) {
            if (items.length > 0) {
                items.splice(0, 0, DropDownComponent.separator);
            }

            items.splice(0, 0, {
                title: this.expressionModel.pathTitle,
                key: this.expressionModel.pathReference,
                icon: this.getIconFor(this.expressionModel.pathReference),
                isSelected: true
            });
            this.pathsDropDownTemplate = DropdownMenuItemTemplates.Disabled;
        }
        return items;
    }

    protected getPathsDropdownFooterItems(): IDropDownMenuItem[] {
        const items = [];
        const paths = this._allPaths;

        if (paths) {
            for (let i = 0; i < paths.length; i++) {
                if (i >= 1) {
                    items.push(DropDownComponent.separator);
                }
                const isSelected: boolean = this.expressionModel.pathReference?.length && paths[i].name === this.expressionModel.pathReference;

                items.push({
                    title: this.resourceService.getString(paths[i].titleToken),
                    key: paths[i].name,
                    icon: paths[i].icon,
                    isSelected,
                    innerItemComponent: DropdownMenuItemTemplates.SimpleExpressionOperator
                });

                this.pathsDropDownTemplate = DropdownMenuItemTemplates.SimpleExpressionOperator;
            }
        }
        return items;
    }

    private getIconFor(pathRef: string): string {
        const icon = pathRef.toLowerCase() === this._allPaths[0].name
            ? this._allPaths[0].icon
            : 'icon icon-set-picker icon-size24 ic-outcomedecision';
        return icon;
    }

    public get isSelectedPathExist() {
        const pathRefs = this.getPaths().filter((item: any) => {
            const title = item.title || '';
            const k2ModelTitle = this.expressionModel.pathTitle || '';
            return title.toLowerCase() === k2ModelTitle.toLowerCase()
                || item.getReference() === this.expressionModel.pathReference;
        });

        const isPreConfiguredPath = this._allPaths.filter((item: any) => item.name === this.expressionModel.pathReference).length > 0;

        return pathRefs?.length || isPreConfiguredPath;
    }

    public getEmptyPathsTemplate(): string {
        let html: string;
        let message = '';

        if (this._isPathConnected) {
            message = this.resourceService.getString('ruledesigner_merge_expression_mergePath_NoOutcome');
        } else {
            message = this.resourceService.getString('ruledesigner_merge_expression_mergePath_NotConnected');
        }

        html = '<span class="icon icon-set-picker icon-size24 ic-splittask"></span><div class="empty-state-message">' + message + '</div>';
        return html;
    }
    // End of paths related ops

    // State related operations
    private getStateDropDownItems(): IDropDownMenuItem[] {
        const operators = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as IMergeExpressionConfiguration).mergeStates;
        const commands = (<sourceCode.k2Designer.objectModel.rulePlugins.IExpressionConfiguration>this.rulePluginService.getExpressionConfiguration(this.expressionModel)).commands;
        const config = {
            logicalOperators: operators,
            commands
        };

        const selectedOperator = this.expressionModel.state;
        const items: IDropDownMenuItem[] = [];

        for (let i = 0; i < config.logicalOperators.length; i++) {
            if (i >= 1) {
                items.push(DropDownComponent.separator);
            }

            items.push(
                <IDropDownMenuItem>{
                    key: config.logicalOperators[i].name,
                    title: this.resourceService.getString(config.logicalOperators[i].titleToken),
                    icon: config.logicalOperators[i].icon,
                    isSeparator: false,
                    isSelected: selectedOperator === config.logicalOperators[i].name,
                    innerItemComponent: DropdownMenuItemTemplates.SimpleExpressionOperator
                });

            this.stateDropDownTemplate = DropdownMenuItemTemplates.SimpleExpressionOperator;
        }

        super.populateOperatorCommands(config.commands, items);

        return items;
    }

    // End of state related ops
    public focusMe(event: Event): void {
        this._el.nativeElement.focus();
    }

    /**
     * Event handler for a selection being made on a drop down
     *
     * @param dropDownInstance drop down where the selection was made
     * @param item the selected item
     */
    onMergePathSelected = (item: IDropDownMenuItem) => {
        this.expressionModel.pathTitle = item.title;
        this.expressionModel.pathReference = item.key;
        this.selectedPathItem = item;

        this.onMenuClose(this.mergePathsDropDownName, this.mergePathsDropDownRef);
    };

    /**
     * Event handler for a selection being made on a drop down
     *
     * @param dropDownInstance drop down where the selection was made
     * @param item the selected item
     */
    onMergeStateSelected = (item: IDropDownMenuItem) => {
        this.expressionModel.state = item.key;
        this.selectedStateItem = item;

        this.onMenuClose(this.mergeStateDropDownName, this.mergeStateDropDownRef);
    };

    public isPathChosen(): boolean {
        return this.expressionModel.pathTitle?.trim().length > 0;
    }

    public getStateText(mergeState: string): string {
        return this.resourceService.getString('ruledesigner_merge_expression_' + mergeState);
    }

    public getDragImage(): HTMLElement {
        const result = super.getDragImage();
        $(result).addClass('condition-block-dragging');
        return result;
    }
}
