import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockLegacyResourceService } from 'src/testing/mock-legacy-services/resource/mock-legacy-resource.service';
import { ILegacyResourceService } from 'src/types/services/resources/i-legacy-resource.service';

const legacyResourceServiceFactory = () => environment.devHarness === true
    ? new MockLegacyResourceService()
    : sourceCode.k2Designer.core.environmentService.resourceService;

export const LegacyResourceServiceToken = new InjectionToken<ILegacyResourceService>('ILegacyResourceService');

export const LegacyResourceServiceFactory = {
    provide: LegacyResourceServiceToken,
    useFactory: legacyResourceServiceFactory,
    deps: [

    ]
};
