/**
 * This enum defines all the priority types that are on the activity settings screen
 */
export enum ActivityPriorityTypes {

    /**
     * Represents a High Priority Type
     */
    high = 0,

    /**
     * Represents a Medium Priority Type
     */
    med = 1,

    /**
     * Represents a Low Priority Type
     */
    low = 2,

}
