import { Directive, ElementRef, EventEmitter, Inject, Input, Output } from '@angular/core';
import { DragDropService } from '../../services/drag-drop/drag-drop.service';
import { UndoRedoServiceToken } from '../../services/undo-redo/undo-redo-service-factory';

@Directive({
    selector: '[K2Droppable]'
})
export class DroppableDirective {
    @Input() public k2AcceptsDrop: (a, b) => any;
    @Input() public k2Droptypes: string;
    @Input() public k2WillBubbleToGrandparent = false;

    @Output() public k2Dropped = new EventEmitter<IDragDropData>();
    @Output() public k2DragOver = new EventEmitter<IDragDropData>();
    @Output() public k2DragEnter = new EventEmitter<IDragDropData>();
    @Output() public k2DragLeave = new EventEmitter<IDragDropData>();

    private _element: HTMLElement;
    private _allowDrop: boolean;
    private _dropTypes: string[];

    constructor(
        el: ElementRef<HTMLElement>,
        private _dragDropService: DragDropService,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService) {

        this._element = el.nativeElement;

        this._element.addEventListener('dragover', this.onDragOver.bind(this), false);
        this._element.addEventListener('dragenter', this.onDragEnter.bind(this), false);
        this._element.addEventListener('dragleave', this.onDragLeave.bind(this), false);
        this._element.addEventListener('drop', this.onDrop.bind(this), false);
    }

    onDragEnter(e: DragEvent): boolean {
        this._element.classList.add('over');
        if (this.k2DragEnter) {
            this.k2DragEnter.emit();
        }

        this.setDropTypes();

        this._allowDrop = this.allowDrop(this._dropTypes);

        if (!this._allowDrop) {
            e.dataTransfer.dropEffect = 'none';
            e.preventDefault();
        }

        return false;
    }

    onDragLeave(e: DragEvent): boolean {
        this._element.classList.remove('over');
        if (this.k2DragLeave) {
            this.k2DragLeave.emit();
        }

        return false;
    }

    onDrop(e: DragEvent): boolean {
        this._undoRedoService.addUndoMarker('-- k2Droppable: on drop --');

        // stops some browsers from redirecting.
        e.preventDefault();
        if (e.stopPropagation) {
            e.stopPropagation();
        }

        this._element.classList.remove('over');

        this.setDropTypes();

        for (let i = 0; i < this._dropTypes.length; i++) {
            if (this._dragDropService.hasItem(this._dropTypes[i])) {

                // peekItem reads the value withtout removing it.
                // getItem will read the value and remove it from the dictionary.
                // If we we pass the drop on to the grandparent component and the item has been removed by the getItem, the drop will be disallowed
                const data: any = this.k2WillBubbleToGrandparent
                    ? this._dragDropService.peekItem(this._dropTypes[i])
                    : this._dragDropService.getItem(this._dropTypes[i]);
                this.k2Dropped.emit(data);
            }
        }

        return false;
    }

    onDragOver(e: DragEvent): boolean {

        if (this.k2DragOver) {
            this.k2DragOver.emit();
        }

        if (this._allowDrop) {
            e.dataTransfer.dropEffect = 'move';
            // allows us to drop
            if (e.preventDefault) {
                e.preventDefault();
            }
            this._element.classList.add('over');
        } else {
            e.dataTransfer.dropEffect = 'none';
            this._element.classList.remove('over');
            //IE11 won't change the icon if prevent default is called, but other browsers will not change the drop effect unless it is called
            if (!sourceCode.k2Designer.core.isIE11) {
                e.preventDefault();
            }
        }

        return false;
    }

    private allowDrop(types: string[]) {
        let allowDrop = false;

        // check if there is a drag/drop validation function
        if (typeof this.k2AcceptsDrop === 'function') {
            // check if we have any acceptable types that can be dropped
            for (var i = 0; i < types.length; i++) {
                // check if we have data associated with this type
                if (this._dragDropService.hasItem(types[i])) {
                    // we have data - now execute the validation function
                    if (this.k2AcceptsDrop(types[i], this._dragDropService.peekItem(types[i]))) {
                        allowDrop = true;
                        break;
                    }
                } // else we have no data assocated with the type, don't do anything
            }
        } else {
            // we have no drag/drop validation function
            // if we have any data associated with the type, we will allow the drop, otherwise not
            for (var i = 0; i < types.length; i++) {
                if (this._dragDropService.hasItem(types[i])) {
                    allowDrop = true;
                    break;
                }
            }
        }

        return allowDrop;
    }

    private setDropTypes() {
        if (this.k2Droptypes) {
            if (typeof this.k2Droptypes === 'string') {
                this._dropTypes = (<string>this.k2Droptypes).split(' ');
            } else {
                this._dropTypes = this.k2Droptypes as string[];
            }
        } else {
            this._dropTypes = [];
        }
    }
}
