import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { SmartFormsIntegrationService } from 'src/app/services/plugins/smart-forms-integration/smart-forms-integration.service';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IProcessPluginDefinitionContext } from 'src/types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';

@Component({
    selector: 'k2-workflow-integration-forms-popup',
    templateUrl: './workflow-integration-forms-popup.component.html',
    styleUrls: ['./workflow-integration-forms-popup.component.less']
})
export class WorkflowIntegrationFormsPopupComponent implements OnInit {

    @Input() formsPopupConfig: sourceCode.k2Designer.objectModel.popup.IContextWizardPopupConfiguration;
    @Input() isPopupWizardActive: boolean;

    @Output() close: EventEmitter<PopupState> = new EventEmitter<PopupState>();

    pluginContext: IProcessPluginDefinitionContext;

    constructor(private _smartFormsIntegrationService: SmartFormsIntegrationService ) { }

    ngOnInit(): void {
        this.pluginContext = this.formsPopupConfig.context as IProcessPluginDefinitionContext;
    }

    closeDialogPopup(state: PopupState){
        this.close.emit(state);
    }

    onSavedAndFinished(){
        this._smartFormsIntegrationService.updateForm(this.pluginContext);
    }
}
