import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IDataField } from '../../../../types/models/data-field/i-data-field';
import { INewDataFieldContext } from '../../../../types/models/popup/i-new-datafield-context';
import { IPopupButton } from '../../../../types/models/popup/i-popup-button';
import { IListItemIconData } from '../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { IContextPopupConfiguration } from '../../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { DataTypeResolverService } from '../../../services/plugins/data-type-resolver/data-type-resolver.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { popup } from '../../../shared/constants/wizard-popup';
import { ChooseControlTypes } from '../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { DataFieldTypes } from '../../../shared/enums/model/workflow/process/data-field-types';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';
import { showDatePicker } from '../../../shared/utils/date-picker';
import { shortUid } from '../../../shared/utils/general';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';


@Component({
    selector: 'k2-new-data-field',
    templateUrl: './new-data-field.component.html',
    styleUrls: ['./new-data-field.component.less']
})
export class NewDataFieldComponent extends BaseResourceComponent implements OnInit, OnDestroy {
    @Input() config: IContextPopupConfiguration;

    //Enums
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    public textBoxConstraint: typeof TextBoxConstraint = TextBoxConstraint;
    public dataFieldTypes: typeof DataFieldTypes = DataFieldTypes;

    //Generated unique ids for the choose controls
    public hiddenId: string = shortUid();
    public onDemandId: string = shortUid();
    public auditId: string = shortUid();
    public sharedId: string = shortUid();
    public rdbTrueId: string = shortUid();
    public rdbFalseId: string = shortUid();
    public emptyValueId: string = shortUid();

    public dropDownItems: IDropDownMenuItem[];
    public hover = false;
    public rightIconData: IListItemIconData = {
        customClassFlexRow: 'list-item-icons',
        iconClass: 'icon icon-set-datatypes icon-size16 ic-datetime fdatepicker',
        isVertical: true,
        isWrappedInContentDiv: true
    };

    //Javascript root element (the main taskConditionBlock element)
    private closingEventSubscription: Subscription;
    private rePositionSubscription: Subscription;

    constructor(@Inject(UndoRedoServiceToken) private undoRedoService,
        protected resourceService: ResourceService,
        private dataTypeResolver: DataTypeResolverService,
        private eventService: EventNotifierService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.dataField.title = this.dataField.title || '';
        this.dataField.description = this.dataField.description || '';
        this.config.returnData = this.dataField;

        /*  Older builds would determine if the 'Empty Value' checkbox should be checked based on whether or not the Intial Value field was empty.
            New builds use a specific isEmptyValue property and persist the checkbox selection regardless of whether or not the initial value field has a value.
            See Bug 824517
        */
        this.dataField.manuallySetEmptyCheckbox = true;

        this.buildTypeDropDownItems();
    }

    ngOnDestroy(): void {
        this.rePositionSubscription?.unsubscribe();
    }

    get dataField(): IDataField {
        return (this.config.context as INewDataFieldContext).existingDataField;
    }

    set dataField(val) {
        (this.config.context as INewDataFieldContext).existingDataField = val;
        this.config.returnData = val;
    }

    getTypeTitle(): string {
        let typeTitle = '';
        switch (this.dataField.type) {
            case DataFieldTypes.yesNo:
                typeTitle = this.resourceService.getString('workflow_dataFieldType_yesNo'); break;
            case DataFieldTypes.dateTime:
                typeTitle = this.resourceService.getString('workflow_dataFieldType_dateTime'); break;
            case DataFieldTypes.decimal:
                typeTitle = this.resourceService.getString('workflow_dataFieldType_decimal'); break;
            case DataFieldTypes.number:
                typeTitle = this.resourceService.getString('workflow_dataFieldType_number'); break;
            default:
                typeTitle = this.resourceService.getString('workflow_dataFieldType_string');
        }
        return typeTitle;
    }

    getIconFromType(): string {
        return this.dataTypeResolver.getSpriteClassFromDataType(this.dataField.type.toString());
    }

    onTypeDropDownItemSelected(item: IDropDownMenuItem): void {
        if (this.dataField.type !== parseInt(item.key)) {
            this.dataField.type = parseInt(item.key);

            this.dataField.isEmptyValue = true;
            this.dataField.isXMLDataField = false;

            //Set default values for dataField type-values:
            this.dataField.stringValue = undefined;
            this.dataField.numberValue = undefined;
            this.dataField.boolValue = undefined;
            this.dataField.dateTimeValue = undefined;
            this.dataField.schema = undefined;

            switch (item.key) {
                case DataFieldTypes.number.toString():
                case DataFieldTypes.decimal.toString():
                    this.dataField.numberValue = 0;
                    break;
                case DataFieldTypes.yesNo.toString():
                    this.dataField.boolValue = false;
                    break;
            }
        }
    }

    private buildTypeDropDownItems(): void {
        const types = this.dataTypeResolver.getDataFieldTypesList();
        this.dropDownItems = [];

        if (types) {
            for (let i = 0; i < types.length; i++) {
                const sprite = this.dataTypeResolver.getSpriteClassFromDataType(types[i].key.toString());
                const dropDownItem: IDropDownMenuItem = {
                    key: types[i].key.toString(),
                    title: types[i].title,
                    icon: 'icon-set-datatypes ic-' + sprite,
                    isSelected: this.dataField.type == types[i].key
                };

                this.dropDownItems.push(dropDownItem);
            }
        }
    }

    //Sets the values to default.
    public OnEmptyValueSelect(val: boolean): void {
        if (!val) return;
        switch (this.dataField.type.toString()) {
            case DataFieldTypes.string.toString():
                this.dataField.stringValue = '';
                break;
            case DataFieldTypes.number.toString():
                this.dataField.numberValue = 0;
                break;
            case DataFieldTypes.decimal.toString():
                this.dataField.numberValue = 0;
                break;
            case DataFieldTypes.yesNo.toString():
                this.dataField.boolValue = false;
                break;
            case DataFieldTypes.dateTime.toString():
                this.dataField.dateTimeValue = undefined;
                break;
        }
    }

    openCalendarControl(eventTarget: EventTarget): void {
        showDatePicker(eventTarget, (ev) => {
            this.undoRedoService.enabledAndBeginUndoGroupWithMarkerAndFinish('--newDataFieldController: change timer date--');

            this.dataField.dateTimeValue = ev.formatted;
        });
    }
}
