import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { ResourceService } from '../../../../services/resource/resource.service';
import { ValidationServiceToken } from '../../../../services/validation/validation-service-factory';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { BaseComponent } from '../../../base/base.component';
import { States } from '../../../../shared/control-helpers/states';
import { IStates } from '../../../../../types/models/rules/i-states';
import { IValidationService } from '../../../../../types/services/validation/ivalidation.service';

@Component({
    selector: 'k2-end-point',
    templateUrl: './end-point.component.html',
    styleUrls: ['./end-point.component.less']
})
export class EndPointComponent extends BaseComponent implements OnInit {
    @Input() public expressionModel: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredEndPoint;
    @Input() public value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpression;
    @Input() public k2ValidateWhen: any;
    @Input() public k2Validation: any;
    @Input() public states: IStates;

    @Output() public addParentState: EventEmitter<string> = new EventEmitter<string>();
    @Output() public removeParentState: EventEmitter<string> = new EventEmitter<string>();

    constructor(@Inject(ValidationServiceToken) private _validationService: IValidationService,
        private _resourceService: ResourceService) {
        super(_validationService);
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    ngOnInit(): void {
        this.setApplicableValidationTypes(this.k2Validation);
        super.validateArrayOnChange(this.expressionModel.value, 'smartFields', this.k2ValidateWhen);

        if (!this.states) {
            this.states = new States();
        }
    }

    public onDragEnter() {
        this.updateParentState(RuleControlStates.selected);
        this.states.add(RuleControlStates.focus);
    }

    public onDragLeave() {
        this.updateParentState(RuleControlStates.selected, true);
        this.states.remove(RuleControlStates.focus);
    }

    public onBlur() {
        this.states.remove(RuleControlStates.focus);
        this.updateParentState(RuleControlStates.textboxFocus, true);
        this.updateParentState(RuleControlStates.selected, true);
    }

    public onFocus() {
        this.states.add(RuleControlStates.focus);
        this.updateParentState(RuleControlStates.textboxFocus);
        this.updateParentState(RuleControlStates.selected);
    }

    private updateParentState(state: string, remove: boolean = false) {
        if (remove) {
            this.removeParentState.emit(state);
        } else {
            this.addParentState.emit(state);
        }
    }
}
