import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import IVotingRuleExpressionConfiguration from 'src/types/models/rules/i-voting-rule-expression-configuration';
import { rules } from 'src/app/shared/constants/rules';
import { DropDownComponent } from 'src/app/components/common/drop-down/drop-down.component';

@Component({
    selector: 'k2-voting-response-expression',
    templateUrl: './voting-response-expression.component.html',
    styleUrls: ['./voting-response-expression.component.less']
})
export class VotingResponseExpressionComponent extends ExpressionBaseComponent implements OnInit {
    @Input() index: number;

    @Output() onDropped: EventEmitter<number> = new EventEmitter<number>();
    @Output() onLogicalOperatorItemSelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();
    @Output() onLeftOperandItemSelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();
    @Output() onSpecialOperatorItemSelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();

    // Dropdown Menu Items
    public logicalOperatorDropdownItems: IDropDownMenuItem[] = [];
    public leftOperandDropDownItems: IDropDownMenuItem[] = [];
    public specialOperatorDropDownItems: IDropDownMenuItem[] = [];

    // Enum imports for use in templates
    public dropdownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate = DropdownMenuItemTemplates;

    constructor(@Inject(SelectionServiceToken) selectionService: ISelectionService,
        protected rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        resourceService: ResourceService,
        protected element: ElementRef<HTMLElement>,
        eventService: EventNotifierService) {
        super(selectionService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, element, resourceService);
    }

    public get votingResponseExpression(): sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredVotingResponseExpression {
        return this.expressionModel as sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredVotingResponseExpression;
    }

    public get votingResponseExpressionExtended(): sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredVotingResponseExpressionExtension {
        return this.votingResponseExpression as sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredVotingResponseExpressionExtension;
    }

    ngOnInit(): void {
        super.ngOnInit();

        // Initialize if not set
        if (!this.votingResponseExpression.logicalOperator) {
            this.votingResponseExpression.logicalOperator = 'of';
        }
        if (!this.votingResponseExpression.leftOperandType) {
            this.votingResponseExpression.leftOperandType = 'all';
        }
        if (!this.votingResponseExpression.spetialOperator) {
            this.votingResponseExpression.spetialOperator = 'equals';
        }

        const config = this.rulePluginService.getExpressionConfiguration(this.expressionModel) as IVotingRuleExpressionConfiguration;

        // Populate the logical operator dropdown
        config.operators.forEach((operator: sourceCode.k2Designer.objectModel.rulePlugins.operator, index: number) => {
            if (index !== 0) {
                this.logicalOperatorDropdownItems.push(DropDownComponent.separator);
            }

            this.logicalOperatorDropdownItems.push({
                key: operator.name,
                icon: operator.icon === 'useresources' ? this.resourceService.getString('ruledesigner_response_expression_icon_' + operator.name) : operator.icon,
                title: this.resourceService.getString(operator.titleToken),
                innerItemComponent: DropdownMenuItemTemplates.SimpleExpressionOperator,
                isSelected: this.votingResponseExpression.logicalOperator === operator.name
            });

            if (operator.name === this.votingResponseExpression.logicalOperator) {
                this.votingResponseExpressionExtended.logicalOperatorIcon = this.logicalOperatorDropdownItems[this.logicalOperatorDropdownItems.length - 1].icon;
            }
        });

        super.populateOperatorCommands(config.commands, this.logicalOperatorDropdownItems);

        super.populateLogicalOperators(config.leftOperandType, this.leftOperandDropDownItems, this.votingResponseExpression.leftOperandType);

        super.populateLogicalOperators(config.specialOperators, this.specialOperatorDropDownItems, this.votingResponseExpression.spetialOperator);
        super.populateOperatorCommands(config.commands, this.specialOperatorDropDownItems);

        this.votingResponseExpressionExtended.leftOperandTypeIcon = config
            .leftOperandType
            .find(x => x.name === this.votingResponseExpression.leftOperandType)
            ?.icon;

        this.votingResponseExpressionExtended.spetialOperatorIcon = config
            .specialOperators
            .find(x => x.name === this.votingResponseExpression.spetialOperator)
            ?.icon;
    }

    ngOnDestroy() {
        super.ngOnDestroy();
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    getOperatorIcon(menuPrefix: string, originalIconClass: string): string {
        let iconClass = originalIconClass + ' selected';

        const isOnHover = this.states.isExist(menuPrefix + '-operator-hover');
        const isMenuActive = this.states.isExist(menuPrefix + '-menu-active');
        const isSpecialOperatorIgnoreCase = (menuPrefix === 'special' && this.votingResponseExpression.spetialOperator === 'equals');

        if (isOnHover || isMenuActive) {
            iconClass = rules.icons.popoverArrow;
        }
        else if (isSpecialOperatorIgnoreCase) {
            iconClass = rules.icons.popoverArrow + ' inactive';
        }

        return iconClass;
    }

    public addDraggingState = super.addDraggingState.bind(this);

    public removeDraggingState = super.removeDraggingState.bind(this);

    getExpressionBlock(item: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression): HTMLElement {
        const result: HTMLElement = super.getDragImage();
        result.classList.add('condition-block-dragging');
        return result;
    }

    onDrop(): void {
        //Emit drop event to parent (GroupExpression)
        this.onDropped.emit(this.index);
    }

    /**
     * Handles the selection of the logical operator dropdown
     *
     * @param item selected dropdown menu item
     */
    onLogicalOperatorDropDownSelected(item: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--k2VotingResponse Expression: item selected--');

        this.votingResponseExpression.logicalOperator = item.key;
        this.votingResponseExpressionExtended.logicalOperatorIcon = item.icon;

        this.onLogicalOperatorItemSelected.emit(item);
    }

    /**
     * Handles the selection of the left operand dropdown
     *
     * @param item selected dropdown menu item
     */
    onLeftOperandDropDownSelected(item: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--k2VotingResponse Expression: item selected--');

        this.votingResponseExpression.leftOperandType = item.key;
        this.votingResponseExpressionExtended.leftOperandTypeIcon = item.icon;

        this.onLeftOperandItemSelected.emit(item);
    }

    /**
     * Handles the selection of the left operand dropdown
     *
     * @param item selected dropdown menu item
     */
    onSpecialOperatorDropDownSelected(item: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--k2VotingResponse Expression: item selected--');

        this.votingResponseExpression.spetialOperator = item.key;
        this.votingResponseExpressionExtended.spetialOperatorIcon = item.icon;

        this.onSpecialOperatorItemSelected.emit(item);
    }
}
