type IAssetItem = sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItem;

type IFormProcessDefinition = sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.IFormProcessDefinition;

@Component({
    selector: 'k2-start-step',
    templateUrl: './start-step.component.html',
    styleUrls: ['./start-step.component.less']
})
export class StartStepComponent extends RulePluginBaseComponent implements OnInit, OnDestroy {

    @ViewChild('popupTarget') popupTarget: ElementRef<HTMLDivElement>;

    /**
     * Property to determine if configured event plugins is loaded.
     */
    configuredPluginsLoaded: boolean;

    /**
     * Collection configured plugin definitions.
     */
    configuredPluginDefinitions: IConfiguredPluginDefinition[] = [];

    /**
     * Selected configured plugin definition
     */
    selectedConfiguredPluginDefinition: IConfiguredPluginDefinition;

    /**
     *  Model used for badging the event platform list items on the start steps config panel
     */
    eventPlatformModel: sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.IEventPlatformConfiguration;

    /**
     * Used for when we want to cancel what happens in the popup.
     */
    tempOriginalConfiguration: sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.IEventPlatformConfiguration;

    /**
     * Subscription index used when configuring and resetting the event platfor integration
     */
    subscriptionIndex: number;

    /**
     * The selected process definition plugin from drop down.
     */
    processDefinitionPluginsItems: IDropDownMenuItem[];

    chooseControlTypes = ChooseControlTypes;
    integrationType = IntegrationType;
    positioning = Positioning;
    infoPopupGotItType = UserOptionsTipTypes.HelpStartWorkflowTips;
    contextHandlerName = RuleContextHandlerNames.StartRule;
    sharePointAutoConfigureCompletedkey = 'SharePointAutoConfigureCompleted';
    configurePluginsMethodsLoadedCount = 0;
    spConfiguredPluginExists = false;
    busyLoading = false;
    otherStartEventExists = false;
    containsSharePointIntegrationEvents = false;
    isToggleBtnHover = false;

    isSharepointPopupWizardActive: boolean;

    isFormsPopupWizardActive: boolean;

    isEventPlatformPopupWizardActive: boolean;

    isDeleteFormsPopupWizardActive: boolean;

    isDeleteSharePointPopupWizardActive: boolean;

    isDeleteEventPlatformPopupWizardActive: boolean;

    formsPopupConfig: IContextWizardPopupConfiguration;

    sharepointPopupConfig: IContextWizardPopupConfiguration;

    eventPlatformPopupConfig: IContextWizardPopupConfiguration;

    deleteFormPopupConfig: IContextWizardPopupConfiguration;

    deleteSharePointPopupConfig: IContextWizardPopupConfiguration;

    deleteEventPlatformPopupConfig: IContextWizardPopupConfiguration;

    sharePointPopupHeading: string;

    formsPopupHeading: string;

    eventPlatformHeading: string;

    deleteEventPlatformHeading: string;

    private tabChangedCallback: IPanelTabChangedCallback;
    private activeItemChangedCallback: IPluginStateChangedCallback;

    /**
     * Object that will hold logic to control what outcome will be followed on workflow start.
     */
    get startRule(): IConfiguredRule {
        return this.processService.root?.configuration?.startRule;
    }

    private infoPopupInternal: InfoPopupComponent;
    get infoPopup(): InfoPopupComponent {
        return this.infoPopupInternal;
    }
    @ViewChild(InfoPopupComponent) set infoPopup(val: InfoPopupComponent) {
        if (val) {
            this.infoPopupInternal = val;
            if (this.shouldOpenPopup(this.configPanelService.currentPnl.currentItem.name)) {
                setTimeout(() => {
                    this.infoPopupInternal.open(this.popupTarget?.nativeElement?.getBoundingClientRect() || null);
                }, 500); // give time for the animation
            }
        }
    }

    get isExpanded() {
        return this.environmentService.activeStudioItem.pluginStates.configurationPanel === 'expanded';
    }

    constructor(protected resourceService: ResourceService,
        private processService: ProcessService,
        private smartFormsIntegrationService: SmartFormsIntegrationService,
        private workflowIntegrationService: WorkflowIntegrationService,
        protected rulePluginService: RulePluginService,
        @Inject(ValidationServiceToken)
            protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken)
            protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken)
            protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken)
            protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken)
            protected environmentService: IEnvironmentService,
        @Inject(ObjectModelServiceToken)
            private objectModelService: IObjectModelService<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>,
        @Inject(RecentsServiceToken)
            private recentsService: IRecentsService
    ) {

        super(validationService, configPanelService, rulePluginService, undoRedoService,
            pluginService, environmentService, resourceService, RuleContextHandlerNames.StartRule);

        this.sharePointPopupHeading = this.resourceService.getString('sharePoint_integration_found_references');
    }

    ngOnInit(): void {
        this.setupConditionalStartRule();
        this.smartFormsIntegrationAutoConfigure();
        this.configureTabValidation();
        this.tabChangedCallback = this.configPanelService.listenTabChanged(() => { this.checkPopupClose(); });
        this.activeItemChangedCallback = this.environmentService.listenActiveStudioItemPluginStateChanged(() => {
            this.checkPopupClose();
        });
    }

    ngOnDestroy() {
        this.configPanelService.removeTabChangedCallback(this.tabChangedCallback);
        this.environmentService.removeActiveStudioItemPluginStateChangedListener(this.activeItemChangedCallback);
    }

    /**
     * Method to set selected states for a list item in a collection.
     */
    selectConfiguredPluginDefinition(item: IConfiguredPluginDefinition): void {
        //Set the directive property to be able to see on the UI which item is selected.
        for (const configuredPlugin of this.configuredPluginDefinitions) {
            if (configuredPlugin.integrationType === IntegrationType.SmartForms) {
                configuredPlugin.selected = item.id === configuredPlugin.id &&
                    (item.metaData as IPluginDefinitionMetaData).ruleId === (configuredPlugin.metaData as IPluginDefinitionMetaData).ruleId;
            }
            else if (configuredPlugin.integrationType === IntegrationType.SharePoint) {
                configuredPlugin.selected = item.id === configuredPlugin.id;
            }
            else if (configuredPlugin.integrationType === IntegrationType.WorkflowError) {
                configuredPlugin.selected = item.id === configuredPlugin.id;
            }
            else {
                //Custom start workflow integrations framework
            }
        }

        //Set the scope selected item for re-use like in the delete method.
        this.selectedConfiguredPluginDefinition = item;
    }

    /**
     * Handle keybord events
     */
    onKeyDown(e: KeyboardEvent): void {
        switch (e.keyCode) {
            case keyCodes.DELETE:
            //Some browser go to the previous page.
                e.preventDefault();
                this.removeConfiguredPluginDefinition();
                break;
            case keyCodes.ENTER:
                this.onPluginTitleChanged();
                break;
        }
    }

    onPluginTitleChanged(target: EventTarget = null): void {
        //Only workflow error integration type title can be changed
        if (this.selectedConfiguredPluginDefinition.integrationType === IntegrationType.WorkflowError) {
            const selectedProcessSubscription = this.processService.root.configuration.eventPlatformConfiguration.subscriptions
                .find(s => s.identifier === this.selectedConfiguredPluginDefinition.id);

            if (selectedProcessSubscription) {
                selectedProcessSubscription.description = this.selectedConfiguredPluginDefinition.title;
            }
        }
    }

    /**
     * Remove the integration used to start the workflow based on the currently selected plugin definition
     * */
    removeConfiguredPluginDefinition() {
        if (this.selectedConfiguredPluginDefinition) {
            switch (this.selectedConfiguredPluginDefinition.integrationType) {
                case IntegrationType.SmartForms:
                    this.removeFormsIntegration(this.selectedConfiguredPluginDefinition);
                    break;
                case IntegrationType.SharePoint:
                    this.removeSharePointIntegration(this.selectedConfiguredPluginDefinition);
                    break;
                case IntegrationType.WorkflowError:
                    this.removeEventPlatformIntegration(this.selectedConfiguredPluginDefinition);
                    break;
                default:
                    console.error(`Trying to delete a plugin definition of an unknown type: `);
            }
        }
    }

    removeEventPlatformIntegration(pluginDef: IConfiguredPluginDefinition) {

        const resourceObject = {
            pageTitle: this.resourceService.getString('event_platform_when_any_workflow_errors'),
            deleteEventHeading: this.resourceService.getString('event_platform_delete_event_heading'),
            deleteEventUpdateHeading: this.resourceService.getString('event_platform_delete_event_update_heading_workflow_error_event'),
            processing: this.resourceService.getString('event_platform_delete_event_processing'),
        };

        this.deleteEventPlatformPopupConfig = {
            context: {
                selectedSubscriptionId: pluginDef.id,
                resourceObject
            } as unknown as IRemoveEventPluginContext,
        } as IContextWizardPopupConfiguration;

        this.isDeleteEventPlatformPopupWizardActive = true;
    }

    /**
     * Remove the Forms Integration used to start the workflow by taking the user through a wizard
     * */
    removeFormsIntegration(pluginDef: IConfiguredPluginDefinition) {

        this.deleteFormPopupConfig = {
            context: {
                actionId: (pluginDef.metaData as IPluginDefinitionMetaData).actionId,
                itemReferences: (pluginDef.metaData as IPluginDefinitionMetaData).itemReferences,
                state: (pluginDef.metaData as IPluginDefinitionMetaData).state,
                formName: pluginDef.title,
                formSystemName: pluginDef.name,
                eventName: pluginDef.subTitle,
                deleteState: true,
                deleteAction: true,
                deleteItemReferences: true,
                checkOut: true,
                success: true
            } as IRemovePluginContext,
        } as IContextWizardPopupConfiguration;

        this.isDeleteFormsPopupWizardActive = true;
    }

    /**
     * Remove the Sharepoint Integration used to start the workflow by taking the user through a wizard
     * */
    removeSharePointIntegration(pluginDef: IConfiguredPluginDefinition) {

        const plugin: IWorkflowData = pluginDef.metaData;

        const deleteDescription: string = this.resourceService
            .getString('sharepoint_integration_error_description')
            .replace('{0}', pluginDef.title)
            .replace('{1}', plugin.DocumentLibrary
                ? this.resourceService.getString('sharePoint_integration_found_references_library')
                : this.resourceService.getString('sharePoint_integration_found_references_list')
            );

        const itemReferences = this.workflowIntegrationService.getSharePointItemReferencesBySmartObject(plugin.SmartObjectName);

        const deleteReferencesDescription: string = this.resourceService
            .getString('sharepoint_integration_delete_references_message')
            .replace('{0}', itemReferences.length.toString());

        const unregisterEventsDescription: string = this.resourceService
            .getString('sharepoint_integration_unregister_events_message')
            .replace('{0}', plugin.EventData.length.toString());

        this.deleteSharePointPopupConfig = {
            hideInfoButton: true,
            context: {
                deleteDesription: deleteDescription,
                deleteItemReferences: true,
                deleteReferencesMessage: deleteReferencesDescription,
                errorMessage: '',
                itemReferences,
                unregisterEventsMessage: unregisterEventsDescription,
                selectedPlugin: pluginDef,
                deleteListDescription: '',
                deleteListHeading: ''
            } as IRemoveSharePointPluginContext
        } as IContextWizardPopupConfiguration;

        this.isDeleteSharePointPopupWizardActive = true;
    }

    /**
     * Open the wizard popups to guide the user through integrations
     *
     * @param plugin IProcessDefinitionPluginConfiguration
     */
    showProcessDefinitionPlugin(plugin: IProcessDefinitionPluginConfiguration) {
        switch (plugin.name) {
            case 'SharePointProcessDefinition':
                this.showSharepointPopups();
                break;
            case 'SmartFormsProcessDefinition':
                this.showFormsPopups(plugin);
                break;
            case 'WorkflowErrorEventProcessDefinition':
                this.showEventPlatformPopup(plugin as unknown as IConfiguredPluginDefinition);
                break;
            default:
                throw new Error('Unknown process definition for integration: ' + plugin.name);
        }
    }

    /**
     * Show the hierarchy dialog and then wizard to integrate with forms
     */
    showFormsPopups = (plugin: IProcessDefinitionPluginConfiguration) => {
        this.workflowIntegrationService
            .showFormsHierarchyControl()
            .then((item: IAssetItem) => {
                this.showFormsWizardPopup(item, plugin);
            });
    };
    /**
     * Show the hierarchy dialog and then wizard to guide the user through forms integration
     */

    showSharepointPopups = () => {

        this.workflowIntegrationService
            .showSharePointHierarchyControl()
            .then((item: IAssetItem) => {
                this.showSharepointWizardPopup(item, false);
            });
    };

    showEventPlatformPopup = (pluginDef: IConfiguredPluginDefinition, editMode: boolean = false) => {

        this.tempOriginalConfiguration = null;
        let subscriptionModel: ISubscription = null;
        this.subscriptionIndex = 0;

        if (!this.processService.root.configuration.eventPlatformConfiguration) {
            subscriptionModel = this.workflowIntegrationService.createNewSubscription(0);

            this.processService.root.configuration.eventPlatformConfiguration =
                sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.EventPlatformConfiguration.createInstance({
                    subscriptions: [subscriptionModel]
                });
        } else if (!editMode) {

            this.tempOriginalConfiguration = this.processService.root.configuration.eventPlatformConfiguration.clone();

            const subscriptions = this.processService.root.configuration.eventPlatformConfiguration.subscriptions;

            // We will need to revisit this once we implement support for more events
            if (subscriptions.length === 0) {
                subscriptionModel = this.workflowIntegrationService.createNewSubscription(subscriptions.length);
                subscriptions.push(subscriptionModel);
            }
            else {
                subscriptionModel = subscriptions[0];
                subscriptionModel.state = sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.State.Enabled;
            }

        } else {
            const subscriptions = this.processService.root.configuration.eventPlatformConfiguration.subscriptions;

            for (let i = 0; i < subscriptions.length; i++) {
                if (pluginDef.id === subscriptions[i].identifier) {
                    this.subscriptionIndex = i;
                    subscriptionModel = this.processService
                        .root
                        .configuration
                        .eventPlatformConfiguration
                        .subscriptions[this.subscriptionIndex];

                    this.tempOriginalConfiguration = this.processService.root.configuration.eventPlatformConfiguration.clone();
                }
            }
        }

        this.eventPlatformHeading = this
            .resourceService
            .getString('event_platform_on_any_error_heading')
            .replace('{0}', this.environmentService.contextProviderService.itemInstance.title);

        this.eventPlatformPopupConfig = {
            hideInfoButton: true,
            heading: '',
            headingIcon: 'workflowerrorstartevent',
            modal: true,
            width: 400,
            customClass: 'event-platform',
            context: {
                mapperTitle: this.environmentService.contextProviderService.itemInstance.title,
                title: this.environmentService.contextProviderService.itemInstance.title,
                icon: 'ic-callsubworkflow',
                model: subscriptionModel,
                hideIndicators: true,
            } as unknown as IDataMapperContext,
        } as IContextWizardPopupConfiguration;

        this.isEventPlatformPopupWizardActive = true;
    };

    onEventPlatformIntegrationClosed(closureReason: PopupState) {
        if (closureReason === PopupState.Cancelled) {
            this.revertToPreviousErrorEventConfiguration();
        }

        this.onWizardPopupClosed();
    }

    /**
     * Show the wizard to integrate with forms
     *
     * @param item IAssetItem | IConfiguredPluginDefinition
     * @param plugin IProcessDefinitionPluginConfiguration
     */
    showFormsWizardPopup = (item: IAssetItem | IConfiguredPluginDefinition, plugin?: IProcessDefinitionPluginConfiguration): void => {

        let newProcessDefinition;
        //Create a new instance of the plugin and add it to the process definition.
        if (plugin?.cls.length > 0) {
            const lookup = sourceCode.k2Designer.objectModel.core.model.getImplementorExporter(plugin.cls);

            newProcessDefinition = sourceCode.k2Designer.objectModel.core.model.ModelObject.createFrom(
                lookup.implementor,
                lookup.exporter,
                null,
                null
            );
        }

        this.formsPopupHeading = this.resourceService.getString('smartforms_integration_create_smartfield');

        this.formsPopupConfig = {
            context: {
                form: {
                    title: item.title,
                    key: (item as IConfiguredPluginDefinition).id ?? (item as IAssetItem).key,
                    name: item.name
                } as IAssetItem,
                formItemReferences: {
                    collection: []
                } as ISmartFormItemReferences,
                existingItemReferences: {
                    collection: []
                } as ISmartFormItemReferences,
                newItemReferences: {
                    collection: []
                } as ISmartFormItemReferences,
                processDefinition: newProcessDefinition,
                cotainMatchingReferences: false,
                createItemReferencesHeading: this.resourceService.getString('smartforms_integration_found_references'),
                unmappedFieldsHeading: '',
                configureFormRuleDescription: '',
                makeStateDefault: true,
                foundWorkflowActionOnForm: false,
                ruleConfigureLaterDescription: '',
                ruleConfigureNowDescription: '',
                folio: '',
                newStateName: '',
                states: [],
                createNewState: true,
                isUserForm: false,
                updatedFormSuccesfully: true,
                formUpdateDone: false,
                pageIndex: 0,
                checkedOutBy: '',
                checkOutForm: false,
                addItemReferences: false,
                addActionToRule: false,
                showSuccessSummary: true,
                editMode: !!item?.metaData?.actionId,
                ruleId: item?.metaData?.ruleId,
                fqn: this.environmentService.workflowFullname,
            } as IProcessPluginDefinitionContext
        } as IContextWizardPopupConfiguration;

        this.isFormsPopupWizardActive = true;
    };

    /**
     * Show the wizard to integrate with sharepoint
     *
     * @param item
     * @param isExistingIntegration
     * @returns
     */
    showSharepointWizardPopup = (item: IAssetItem | IConfiguredPluginDefinition, isExistingIntegration: boolean): void => {

        if (!item) {
            // Probably got here by closing the hierarchy dialog with the X
            return;
        }

        let metaData: IWorkflowData;
        let itemParts = [];

        if (typeof item.metaData === 'string') {
            // assetType: itemParts[0],
            // serviceInstance: itemParts[1],
            // relativeUrl: itemParts[2]
            itemParts = item?.metaData?.split('|');

            if (!itemParts || itemParts.length < 3) {
                throw new Error('Sharepoint response unexpected');
            }
        } else { // assume object
            metaData = item.metaData;
        }

        // First check the metaData object (as in the case of an edit), then
        // fall back to the item and metaData as array (AssetItem returned from server for new integration)
        // But why are the metaData properties capital letters? It could have been nice and easy destructoring, but no....
        this.sharepointPopupConfig = {
            context: {
                sourceItemReferences: [],
                existingItemReferences: [],
                newItemReferences: [],
                isDocLib: metaData?.DocumentLibrary ?? itemParts[0] === SharePointAssetType.library.toString(),
                itemName: metaData?.ListName ?? item.title,
                listId: metaData?.ListId ?? (item as IAssetItem).key,
                serviceInstance: metaData?.ServiceInstance ?? itemParts[1],
                subSiteRelativeUrl: metaData?.RelativeUrl ?? itemParts[2],
                status: SharePointEventsRegisteredStatus.inProgress,
                existingIntegration: isExistingIntegration,
                savedEvents: metaData?.EventData ?? null,
                settingId: metaData?.SettingId ?? null,
                siteUrl: metaData?.SiteURL ?? null,
                smartObjectName: metaData?.SmartObjectName ?? null
            } as ISharePointContext,
            validationModel: {}
        } as IContextWizardPopupConfiguration;

        this.isSharepointPopupWizardActive = true;
    };

    onWizardPopupClosed = (): void => {
        this.loadSavedPlugins();
    };

    /**
     * Method that gets called when the user select a plugin from the add definition plugin drop down.
     */
    processDefinitionPluginSelected(item: IDropDownMenuItem): void {

        switch (item.metaData.name) {
            case 'SharePointProcessDefinition':
                this.showSharepointPopups();
                break;
            case 'SmartFormsProcessDefinition':
                this.showFormsPopups(item.metaData);
                break;
            case 'WorkflowErrorEventProcessDefinition':
                this.showEventPlatformPopup(item.metaData);
                break;
            default:
                throw new Error('Unknown process definition for integration: ' + item.metaData.name);
                break;
        }

        //Deselect all selected items
        for (const processDefinitionPlugin of this.processDefinitionPluginsItems) {
            processDefinitionPlugin.isSelected = false;
        }
    }

    /**
     * This method handles the logic of switching between the always start and conditional start options for the conditional start rule,
     * This also closes the rules designer when the conditional rule is removed,
     * This also creates an in-memory copy of the statements to re-add it once the custom rule is reselected.
     */
    selectRuleType(ruleType: number): void {
        this.undoRedoService.addUndoMarker('--workflow integration: change start activity configuration--');

        switch (ruleType) {
            case DecisionOptionTypeStartRule.alwaysStart:
                this.processService.root.configuration.decisionOptionTypeStartRule = DecisionOptionTypeStartRule.alwaysStart;
                this.pluginService.toggleCollapsed(plugins.panels.rulesDesigner);

                this.rulePluginService.setCustomRuleStatements(
                    this.contextHandlerName,
                    this.processService.root.configuration.startRule.statements
                );

                if (this.processService.root.configuration.startRule.statements) {
                    for (const statement of this.processService.root.configuration.startRule.statements) {
                        this.validationService.forceAllPassValidation(statement);
                    }
                }

                this.processService.root.configuration.startRule.statements = null;

                this.validationService.forceAllPassValidation(this.processService.root.configuration.startRule);

                break;
            case DecisionOptionTypeStartRule.conditionalStart:
                this.processService.root.configuration.decisionOptionTypeStartRule = DecisionOptionTypeStartRule.conditionalStart;
                break;
        };
        //Reconfigure tab validation for the startRule
        this.configureTabValidation();

        this.onRuleTypeSelected();
    };

    /**
     * This Method should determine if the process has a Conditional Start Rule.  It sets the Radio buttons' states.
     * The decisionOptionTypeStartRule needs to be replaced by the CSR object to determine its state.
     */
    hasConditionalStartRule(): boolean {
        return this.processService.root?.configuration?.decisionOptionTypeStartRule ===
            DecisionOptionTypeStartRule.conditionalStart;
    }

    editPlugin(selectedPluginDefinition: IConfiguredPluginDefinition) {

        const { integrationType, name } = { ...selectedPluginDefinition };

        // No need to loop through this.processService.definitionPlugins anymore, because we already know the pages

        switch (integrationType) {
            case IntegrationType.SmartForms:
            // The old code first checked if we already had an integration and then if could integrate.
            // This is an integration only path, so that check shouldn't be necessary.
                this.showFormsWizardPopup(selectedPluginDefinition);
                break;
            case IntegrationType.SharePoint:
                this.showSharepointWizardPopup(selectedPluginDefinition, true);
                break;
            case IntegrationType.WorkflowError:
                this.showEventPlatformPopup(selectedPluginDefinition, true);
                break;
            default:
                throw new Error(`Attempting to edit unknown plugin with integrationType ${integrationType} and name ${name}`);
                break;
        }
    }

    /**
     * This Method should provide the CSR object, its configuration and the localized title for the Rules designer.
     */
    protected loadAndPopulateRuleDesigner() {
        this.setSavedCustomStatementsById(this.contextHandlerName, this.processService.root.configuration.startRule);

        if (!this.processService.root.configuration.startRule?.statements?.length) {
            this.processService.root.configuration.startRule = this.rulePluginService.createDefaultRule(this.contextHandlerName);
            //Reconfigure tab validation for the startRule
            this.configureTabValidation();
        }

        this.rulePluginService.setDesignerContext(this.contextHandlerName, this.processService.root.configuration.startRule);
        this.rulePluginService.renderCanvas();
    }

    protected refreshTabIndexes(): void { };

    /**
     * Depending on the rule selected, will either clear the rules designer, or populate the rules designer with default values
     */
    protected renderRuleDesigner(): void {
        if (this.processService.root.configuration.decisionOptionTypeStartRule ===
            DecisionOptionTypeStartRule.alwaysStart) {
            this.loadEmptyRuleDesigner();
        } else if (this.processService.root.configuration.decisionOptionTypeStartRule ===
            DecisionOptionTypeStartRule.conditionalStart) {
            this.loadAndPopulateRuleDesigner();
        }
    };

    private revertToPreviousErrorEventConfiguration() {

        this.processService.root.configuration.eventPlatformConfiguration = this.tempOriginalConfiguration;
        this.eventPlatformModel = this.processService.root.configuration.eventPlatformConfiguration;

        if (this.eventPlatformModel) {
            const fields = this.eventPlatformModel.subscriptions[this.subscriptionIndex].mapping.data.fields;

            for (const field of fields) {
                const validatorItem = {
                    modelObject: field,
                    propertyName: 'dataFieldReference',
                    validationFailedMessage: 'resource_service_removed_consumed_reference',
                    validationResourceTokens: [],
                    errorSeverity: ErrorSeverity.Error
                } as IValidatorItem;

                this.validationService.evaluate([ValidationTypes.Reference], validatorItem, true);
            }
        }
    }

    private checkPopupClose(): void {
        if (this.busyLoading) return;

        this.busyLoading = true;

        if (!this.shouldOpenPopup(this.configPanelService.currentPnl.currentItem.name)) {
            this.infoPopup?.close();
            this.busyLoading = false;
        }
    }

    private setupConditionalStartRule(): void {
        if (this.objectModelService.isInitialized() !== true) {
            const modelCreatedCallback = {
                subscriberId: 'StartRule_WaitForReady',
                notify: (data) => {
                    this.objectModelService.unRegisterEventListener(
                        sourceCode.k2Designer.services.studioItem.ObjectModelService.ReadyForChangesEvent,
                        modelCreatedCallback.subscriberId
                    );

                    this.setupConditionalStartRule();
                }
            };

            this.objectModelService.registerEventListener(
                sourceCode.k2Designer.services.studioItem.ObjectModelService.ReadyForChangesEvent,
                modelCreatedCallback
            );

            return;
        }

        if (this.processService.root.configuration.outcomes && this.processService.root.configuration.outcomes.length === 0) {
            const startOutcome = sourceCode.k2Designer.objectModel.workflow.process.k2Process.StartOutcome.createInstance({
                title: this.resourceService.getString('smartforms_integration_start_rule_start_outcome'),
                originalTitle: this.resourceService.getString('smartforms_integration_start_rule_start_outcome'),
                startWorkflow: true
            });

            const dontStartOutcome = sourceCode.k2Designer.objectModel.workflow.process.k2Process.StartOutcome.createInstance({
                title: this.resourceService.getString('smartforms_integration_start_rule_dont_start_outcome'),
                originalTitle: this.resourceService.getString('smartforms_integration_start_rule_dont_start_outcome'),
                startWorkflow: false
            });

            this.processService.root.configuration.outcomes.push(startOutcome);
            this.processService.root.configuration.outcomes.push(dontStartOutcome);
        }

        //Check if the start rule exists and create a default start rule if not
        if (!this.processService.root.configuration.startRule?.statements?.length) {
            this.processService.root.configuration.startRule = this.rulePluginService.createDefaultRule(this.contextHandlerName);
            this.processService.root.configuration.decisionOptionTypeStartRule = DecisionOptionTypeStartRule.alwaysStart;
            //Reconfigure tab validation for the startRule
            this.configureTabValidation();
        }

        this.renderRuleDesigner();
    }

    private smartFormsIntegrationAutoConfigure(): void {

        //Special case because this is a step that can render immediately when making a new WF
        //In release mode the JS files are combined and result in many fewer requests.
        //It's fairly easy to end up with a case where this code gets executed and the async call finishes before
        //the object model service is initialized and listening to events, because of this
        //the configuration and references created here may never get sent to the server
        //we MUST wait until the object model service is ready to send instructions to the server before this initialization code happens

        //THIS IS A WORKAROUND: the real solution is to eliminate/mitigate or to queue instructions during
        //the gap that can happen between ProcessService.createInitialModel end and ObjectModelService.setupInstructionsProcessor begin
        //During this gap any instructions do not get sent to the server and are lost forever.
        if (this.objectModelService.isInitialized() !== true) {
            const modelCreatedCallback = {
                subscriberId: 'Integration_WaitForReady',
                notify: (data) => {
                    this.objectModelService.unRegisterEventListener(
                        sourceCode.k2Designer.services.studioItem.ObjectModelService.ReadyForChangesEvent,
                        modelCreatedCallback.subscriberId);
                    this.smartFormsIntegrationAutoConfigure();
                }
            };

            this.objectModelService.registerEventListener(
                sourceCode.k2Designer.services.studioItem.ObjectModelService.ReadyForChangesEvent,
                modelCreatedCallback
            );

            return;
        }

        if (this.environmentService.query.objectid) {
            this.workflowIntegrationService.GetFormCategoryAndName()
                .then(result => this.getFormInformationLoaded(result))
                .catch(error => this.sharePointIntegrationAutoConfigure());
        }
        else {
            //No auto configure for SmartForms, check if it must autoconfigure for SharePoint.
            this.sharePointIntegrationAutoConfigure();
        }
    }

    /**
     * Callback from rest call.  This will sync the item references and populate the model.
     */
    private getFormInformationLoaded(result: sourceCode.k2Designer.objectModel.smartForms.IFormContent): void {
        const formContent: sourceCode.k2Designer.objectModel.smartForms.IFormContent = result;
        //Check recent service if service exists
        if (!this.recentsService.hasContext('form')) {
            this.recentsService.registerContext('form', 25);
        }

        const form: IAssetItem = {
            name: formContent.systemName,
            key: formContent.id,
            title: formContent.displayName,
            spriteClass: 'icon icon-set-steps icon-size16 ic-form',
            metaData: formContent.id
        };
        this.recentsService.addItem('form', form, 'key');

        //Automatically add integration on new processes.
        const processId = this.environmentService.query.guid || this.environmentService.query.id;
        if (processId == null) {

            const definition = sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.FormProcessDefinition.createInstance();

            const pluginIntentAlreadyExist = this.smartFormsIntegrationService
                .setupFormDefinitionsOnPluginIntend(form.name, form.title, definition);

            if (!pluginIntentAlreadyExist) {
                const pluginHelper = new sourceCode.k2Designer.uiComponents.configPanel.plugins.pluginWizards.Helper(
                    this.environmentService as any
                );

                pluginHelper.createItemReferences(formContent.itemReferences,
                    true,
                    sourceCode.k2Designer.uiComponents.configPanel.plugins.pluginWizards.StartMode.smartForms
                );
            }
        }
        this.sharePointIntegrationAutoConfigure();
    }

    /**
     * When it is a new process and the required properties is in querystring, auto configure the item references and
     * start event on the process.
     */
    private sharePointIntegrationAutoConfigure(): void {
        //Automatically add integration on new processes.
        const processId = this.environmentService.query.guid || this.environmentService.query.id;
        if (!processId && !this.environmentService.query.objectid) {
            if (!this.environmentService.referrer.smosysname &&
                this.environmentService.referrer.settingid &&
                this.environmentService.isNewWorkflow &&
                !this.environmentService.getGlobalAttributeValue(this.sharePointAutoConfigureCompletedkey)) {
                this.workflowIntegrationService.GetSharepointListSmartObjectName().then((smartObjectSystemName: string) => {
                    this.workflowIntegrationService.integrateWithSharePoint(smartObjectSystemName)
                        .then(result => this.autoConfigureSharePointEventsCompleted(result))
                        .catch(error => this.loadSavedPlugins());
                });
            } else if (this.environmentService.referrer.smosysname &&
                this.environmentService.isNewWorkflow &&
                !this.environmentService.getGlobalAttributeValue(this.sharePointAutoConfigureCompletedkey)) {
                this.workflowIntegrationService.integrateWithSharePoint(this.environmentService.referrer.smosysname)
                    .then(result => this.autoConfigureSharePointEventsCompleted(result))
                    .catch(error => this.loadSavedPlugins());
            }
            else {
                this.loadSavedPlugins();
            }
        }
        else {
            this.loadSavedPlugins();
        }
    }

    /**
     * Set the collection of configured process definitions on the process.
     */
    private loadSavedPlugins = (shouldFireRecursionValidation: boolean = false): void => {
        if (this.processService.root.configuration?.processDefinitions) {
            this.configuredPluginDefinitions = [];
            this.configurePluginsMethodsLoadedCount = 0;
            this.configuredPluginsLoaded = false;

            this.integrateWithSmartForms();

            this.workflowIntegrationService.GetSharepointIntegration()
                .then((result) => this.getEventsForWorkflowSettingCompleted(shouldFireRecursionValidation, result))
                .catch((err) => {
                    if (err) { console.error(err); }
                    const firstLoad: boolean = isUndefinedOrNull(this.environmentService.
                        getGlobalAttributeValue(globalVariableNames.sharepointIntegratonWorkflowData));

                    //This is for delete/adding an integration.
                    if (shouldFireRecursionValidation || firstLoad) {
                        sourceCode.k2Designer.objectModel.validationService.plugins.WorkflowRecursionValidator.validateAll(
                            this.environmentService as any
                        );
                    }

                    this.populateProcessDefinitionPlugins();

                    this.configurePluginsMethodsLoadedCount += 1;
                    if (this.configurePluginsMethodsLoadedCount === 2) {
                        this.configuredPluginsLoaded = true;
                        this.updateStartWorkflowIntegrationStatus();
                        this.integrationsChanged();
                    }
                });
            this.checkOtherStartEvents();
        }
        else {
            setTimeout(this.loadSavedPlugins.bind(this), 100);
        }
    };

    private checkOtherStartEvents(): void {
        if (this.processService?.root?.configuration?.eventPlatformConfiguration) {
            const subscriptions = this.processService.root.configuration.eventPlatformConfiguration.subscriptions;
            for (let i = 0; i < subscriptions.length; i++) {
                if (subscriptions[i].state === EventPlatformState.Enabled as any) {
                    this.configuredPluginDefinitions.push({
                        id: this.processService.root.configuration.eventPlatformConfiguration.subscriptions[i].identifier,
                        name: this.processService.root.configuration.eventPlatformConfiguration.subscriptions[i].description,
                        title: this.processService.root.configuration.eventPlatformConfiguration.subscriptions[i].description,
                        subTitle: '',
                        leftIconClass: 'workflowerrorstartevent',
                        integrationType: IntegrationType.WorkflowError
                    } as IConfiguredPluginDefinition);
                }
            }
        }
    }

    /**
     * Get all the integration points to SmartForms. This will retrieve a collection of forms that this workflow have been integrated with.
     */
    private integrateWithSmartForms(): void {
        const intendedForms: string[] = [];
        for (const definition of  this.processService.root.configuration.processDefinitions) {
            if ((definition as IFormProcessDefinition)?.formSystemName?.indexOf('_removed') < 0) {
                intendedForms.push((definition as IFormProcessDefinition).formSystemName);
            }
        }

        this.workflowIntegrationService.GetStartWorkflowForms(intendedForms).then((result) => {
            this.smartFormsIntegrationLoaded(result);
        }).catch(() => {
            this.configurePluginsMethodsLoadedCount += 1;
            if (this.configurePluginsMethodsLoadedCount === 2) {
                this.configuredPluginsLoaded = true;
                this.updateStartWorkflowIntegrationStatus();
            }
        });
    }


    /**
     * Callback from rules rest call.
     */
    private smartFormsIntegrationLoaded(result: sourceCode.k2Designer.objectModel.smartForms.IStartWorkflow[]): void {
        const integrationPoints: sourceCode.k2Designer.objectModel.smartForms.IStartWorkflow[] = result;

        for (const integrationPoint of integrationPoints) {

            //RuleId will be empty if the user selected to configure later and only create references.  Thus it would not
            //add an start workflow action but only create the references.
            if (integrationPoint.ruleId) {
                this.configuredPluginDefinitions.push({
                    id: integrationPoint.formId,
                    name: integrationPoint.formSystemName,
                    title: integrationPoint.formDisplayName,
                    subTitle: integrationPoint.ruleName,
                    metaData: {
                        actionId: integrationPoint.actionId,
                        ruleId: integrationPoint.ruleId,
                        state: integrationPoint.state,
                        itemReferences: integrationPoint.itemReferences.collection
                    } as unknown as IPluginDefinitionMetaData,
                    leftIconClass: 'formstartevent',
                    integrationType: IntegrationType.SmartForms
                } as unknown as IConfiguredPluginDefinition);
            }
            else {
                //Only get the item references found on the workflow.
                const itemReferences: IModelArray<ISmartFormItemReference> = [];

                for (const itemReferenceCollection of integrationPoint.itemReferences.collection) {

                    for (const itemReference of this.processService.root.configuration.itemReferences) {
                        if (itemReference.systemName === itemReferenceCollection.name) {
                            itemReferences.push(itemReferenceCollection);
                            break;
                        }
                    }

                }

                this.configuredPluginDefinitions.push({
                    id: integrationPoint.formId,
                    name: integrationPoint.formSystemName,
                    title: integrationPoint.formDisplayName,
                    subTitle: this.resourceService.getString('wizard_workflow_integration_no_start_workflow_configured'),
                    metaData: {
                        itemReferences,
                        isIntendedPlugin: true
                    } as IPluginDefinitionMetaData,
                    leftIconClass: 'formstartevent',
                    integrationType: IntegrationType.SmartForms
                } as IConfiguredPluginDefinition);
            }
        }

        this.configurePluginsMethodsLoadedCount += 1;
        if (this.configurePluginsMethodsLoadedCount === 2) {
            this.configuredPluginsLoaded = true;
            this.updateStartWorkflowIntegrationStatus();
        }

        if (this.configurePluginsMethodsLoadedCount === 2) this.integrationsChanged();
    }

    private integrationsChanged(): void {
        if (this.busyLoading) return;
        this.busyLoading = true;

        if (!this.shouldOpenPopup(this.configPanelService.currentPnl.currentItem.name)) {
            this.infoPopup?.close(false);
            this.busyLoading = false;
        }
        this.eventPlatformModel = this.processService.root.configuration.eventPlatformConfiguration;

        // Now that the event platform configuration exists we need to make sure the listener for the tab badging is set up
        this.configureTabValidation();
    }

    private shouldOpenPopup(currentTabName: string): boolean {
        return this.configuredPluginsLoaded &&
            !this.infoPopup?.isOpen &&
            !(this.containsStartWorkflowIntegration() || this.spConfiguredPluginExists) &&
            currentTabName === 'WorkflowIntegrationTab';
    }

    private containsStartWorkflowIntegration(): boolean {
        if (!this.configuredPluginDefinitions?.length) return false;

        for (const plugin of this.configuredPluginDefinitions) {
            if (plugin.metaData) {
                if (!(plugin.metaData as IPluginDefinitionMetaData).isIntendedPlugin) {
                    return true;
                }
            }
        }

        return this.otherStartEventExists;
    }

    private updateStartWorkflowIntegrationStatus(): void {

        this.environmentService.removeGlobalAttribute<IWorkflowData>('StartWorkflowIntegrationExists');
        this.environmentService.globalVariables.push({
            // eslint-disable-next-line @typescript-eslint/naming-convention
            StartWorkflowIntegrationExists: this.spConfiguredPluginExists || this.containsStartWorkflowIntegration()
        } as sourceCode.k2Designer.services.core.IGlobalVariable<boolean>);

        this.legacyBadgingCleanup();
    }

    private legacyBadgingCleanup(): void {
        //Cleanup legacy workflow badging on start step when
        const startStep = this.processService.root.nodes[0];
        if (startStep.errorInformation?.length || startStep.childErrorInformation?.length) {
            const validationData = new sourceCode.k2Designer.objectModel.validationService.plugins.ValidatorItem();
            validationData.modelObject = this.processService.root.nodes[0];
            validationData.propertyName = 'isStartActvity';
            validationData.validationFailedMessage = 'no_start_workflow_integration_warning';
            validationData.validationResourceTokens = [];
            validationData.errorSeverity = sourceCode.k2Designer.objectModel.core.model.ErrorSeverity.Warning;
            this.validationService.forcePassValidation(validationData);
        }
    }

    /**
     * Callback method to retrieve the events saved for the workflow.
     */
    private getEventsForWorkflowSettingCompleted(shouldFireRecursionValidation: boolean, result: IWorkflowData): void {
        this.spConfiguredPluginExists = false;

        const firstLoad: boolean = isUndefinedOrNull(this.environmentService.
            getGlobalAttributeValue(globalVariableNames.sharepointIntegratonWorkflowData));

        if (result) {
            const workflowData: IWorkflowData = result;
            this.configuredPluginDefinitions.push({
                id: workflowData.SettingId,
                name: workflowData.Id,
                title: workflowData.ListName,
                subTitle: this.getStartEventDescription(workflowData.EventData),
                leftIconClass: 'sharepointstartevent',
                integrationType: IntegrationType.SharePoint,
                metaData: workflowData
            } as IConfiguredPluginDefinition);

            this.spConfiguredPluginExists = true;

            //add item for validation
            this.environmentService.removeGlobalAttribute<IWorkflowData>(globalVariableNames.sharepointIntegratonWorkflowData);
            this.environmentService.globalVariables.push({
                // eslint-disable-next-line @typescript-eslint/naming-convention
                SharePointIntegrationWorkflowData: workflowData
            } as sourceCode.k2Designer.services.core.IGlobalVariable<IWorkflowData>);
        }

        //This is for delete/adding an integration.
        if (shouldFireRecursionValidation || firstLoad) {
            sourceCode.k2Designer.objectModel.validationService.plugins.WorkflowRecursionValidator.validateAll(
                this.environmentService as any
            );
        }

        this.populateProcessDefinitionPlugins();

        this.configurePluginsMethodsLoadedCount += 1;
        if (this.configurePluginsMethodsLoadedCount === 2) {
            this.configuredPluginsLoaded = true;
            this.updateStartWorkflowIntegrationStatus();
            this.integrationsChanged();
        }
    }

    private getStartEventDescription(eventReceivers: IRemoteEventReceiver[]): string {
        let selectedEvents = '';
        if (eventReceivers?.length) {
            this.containsSharePointIntegrationEvents = true;

            for (let i = 0; i < eventReceivers.length; i++) {

                switch (eventReceivers[i].Id.toString()) {
                    case '1111':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_manually_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_manually_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_manually_more_than_two_events');
                            }
                        }
                        break;
                    case '10001':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_added_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_added_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_added_more_than_two_events');
                            }
                        }
                        break;
                    case '10002':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_updated_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_updated_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_updated_more_than_two_events');
                            }
                        }
                        break;
                    case '10003':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_deleted_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_deleted_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_deleted_more_than_two_events');
                            }
                        }
                        break;
                    case '10004':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_checked_in_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_in_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_in_more_than_two_events');
                            }
                        }
                        break;
                    case '10005':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_checked_out_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_out_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_out_more_than_two_events');
                            }
                        }
                        break;
                    case '10006':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_checked_out_discarded_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_out_discarded_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_checked_out_discarded_more_than_two_events');
                            }
                        }
                        break;
                    case '10007':
                        if (selectedEvents.length < 2) {
                            selectedEvents = this.resourceService.getString('sharePoint_integration_attachment_added_one_event');
                        }
                        else {
                            if (eventReceivers.length === 2) {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_attachment_added_two_events');
                            }
                            else {
                                selectedEvents += this.resourceService.getString('sharePoint_integration_attachment_added_more_than_two_events');
                            }
                        }
                        break;
                }

                if (eventReceivers.length > 2 && i < eventReceivers.length - 1) selectedEvents += ',';
            }
        } else {
            selectedEvents += this.resourceService.getString('sharepoint_integration_no_events_integration');
            this.containsSharePointIntegrationEvents = false;
        }

        return selectedEvents;
    }

    /**
     * Method that load plugins from config into the collection for the drop down.
     */
    private populateProcessDefinitionPlugins(): void {
        this.processDefinitionPluginsItems = [];
        this.otherStartEventExists = false;
        if (sourceCode.k2Designer.core.EVENTPLATFORMENABLED) {
            const eventPlatformConfig = this.processService.root.configuration.eventPlatformConfiguration;
            if (eventPlatformConfig?.subscriptions?.length) {
                // for now we only support one event, we will have to revisit the logic
                // once we support more
                if (eventPlatformConfig.subscriptions[0].state === EventPlatformState.Enabled as any) {
                    this.otherStartEventExists = true;
                }
            }
        }

        for (let i = 0; i < this.processService.definitionPlugins.length; i++) {
            const processNameDefinition = this.processService.definitionPlugins[i].name.toLowerCase();
            if (processNameDefinition === 'sharepointprocessdefinition') {
                if (!(this.spConfiguredPluginExists)) {
                    const resourceText = 'wizard_workflow_integration_start_text_template';

                    this.pushProcessDefinitionPlugin(i, resourceText);
                }
            } else if (processNameDefinition === 'workflowerroreventprocessdefinition') {
                if (sourceCode.k2Designer.core.EVENTPLATFORMENABLED && !this.otherStartEventExists) {
                    const resourceText = 'wizard_workflow_integration_start_text_template_event';
                    this.pushProcessDefinitionPlugin(i, resourceText);
                }
            } else {
                const resourceText = 'wizard_workflow_integration_start_text_template';

                this.pushProcessDefinitionPlugin(i, resourceText);
            }
        }
    }

    /**
     * Helper method that adds a processDefinitionPluginItem to the array on the scope
     */
    private pushProcessDefinitionPlugin(i: number, resourceText: string): void {

        this.processDefinitionPluginsItems.push({
            additionalValueProperty: this.resourceService
                .getString(resourceText)
                .replace('{0}',
                    this.resourceService
                        .getString(this.processService.definitionPlugins[i].titleToken)
                ),
            title: this.resourceService.getString(this.processService.definitionPlugins[i].linkTitleToken),
            key: this.processService.definitionPlugins[i].cls,
            icon: this.processService.definitionPlugins[i].svgIconId,
            isIconSvg: true,
            isSelected: false,
            metaData: this.processService.definitionPlugins[i]
        } as IDropDownMenuItem);
    }

    /**
     * Callback method when autoconfigure SharePoint events.
     */
    private autoConfigureSharePointEventsCompleted(result: string): void {
        if (result !== '') {
            const itemReferences: IModelArray<ISmartFormItemReference> = result as any;
            if (this.processService.root.configuration.itemReferences.length === 0) {
                const pluginHelper = new sourceCode.k2Designer.uiComponents.configPanel.plugins.pluginWizards.Helper(
                    this.environmentService as any
                );

                pluginHelper.createItemReferences(itemReferences as any,
                    true,
                    sourceCode.k2Designer.uiComponents.configPanel.plugins.pluginWizards.StartMode.sharePointEvent
                );
            }

            // Save a global variable 'SharePointAutoConfigureCompleted', so that we don't we
            // auto integrate again when constructor fire on a new process
            // and the user did delete the SharePoint integration.  This means that the
            // integration will only happen on a New process && when there is no
            // global entry 'SharePointAutoConfigureCompleted' = true
            this.environmentService.globalVariables.push({
                // eslint-disable-next-line @typescript-eslint/naming-convention
                SharePointAutoConfigureCompleted: true
            } as IGlobalVariable<boolean>);
        }
        this.loadSavedPlugins();
    }

    /**
     * Whenever the start rule is reset, this function should be called to rebind the listeners
     * on the startRule's error information that will be used for tab validation.
     */
    private configureTabValidation(): void {
        //Clear predefined watches on startRule
        this.item.componentsToWatch = [];

        if (this.processService?.root?.configuration?.startRule) {
            //Objects which should cause tab to be badged when in error:
            const objToValidateConfigTab: IConfigTabValidatedComponent = {
                componentObj: this.processService.root.configuration.startRule,
                compareFullReference: false
            };
            this.addObjectsToValidateConfigTab([objToValidateConfigTab], this.item);
        }

        if (this.processService?.root?.configuration?.eventPlatformConfiguration) {
            //Objects which should cause tab to be badged when in error:
            const objToValidateConfigTab: IConfigTabValidatedComponent = {
                componentObj: this.processService.root.configuration.eventPlatformConfiguration,
                compareFullReference: false
            };
            this.addObjectsToValidateConfigTab([objToValidateConfigTab], this.item);
        }
    }
}
