import { Component, ElementRef, HostListener, Inject, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { ICategoryDialogContext } from '../../../../types/models/popup/i-category-dialog-context';
import { ICategoryDialogData } from '../../../../types/models/popup/i-category-dialog-data';
import { ICategoryItem } from '../../../../types/models/popup/i-category-item';
import { IPopupButton } from '../../../../types/models/popup/i-popup-button';
import { IPopupConfiguration } from '../../../../types/models/popup/i-popup-configuration';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { PopupService } from '../../../services/popup/popup.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { popup } from '../../../shared/constants/wizard-popup';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';
import { ScrollbarComponent } from '../../common/scrollbar/scrollbar.component';

@Component({
    selector: 'k2-category-dialog',
    templateUrl: './category-dialog.component.html',
    styleUrls: ['./category-dialog.component.less']
})
export class CategoryDialogComponent extends BaseResourceComponent implements OnInit, OnDestroy {
    @Input() config: IPopupConfiguration;

    loading: boolean;
    selectedItem: sourceCode.k2Designer.objectModel.core.categories.ICategoryItem;
    addItemToCrumb: IBreadCrumbItem;
    busyEditing = false;
    newFolderName: string;


    @ViewChild('editItem', { read: ElementRef, static: false }) set editItem(item: ElementRef<HTMLElement>) {
        if (!item) return;
        if (this.scrollBar?.scrollbarVisible) {
            this.scrollBar.scrollToElement(item.nativeElement);
        }
        this._editItem = item;
    };
    @ViewChild('scrollBar') scrollBar: ScrollbarComponent;

    private createButtonSub: Subscription;
    private closeSub: Subscription;
    private _editItem: ElementRef<HTMLElement>;

    get context(): ICategoryDialogContext {
        return this.config?.context as ICategoryDialogContext;
    }

    constructor(resourceService: ResourceService,
        @Inject(EnvironmentServiceToken) private _env: IEnvironmentService,
        private _popupService: PopupService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (this.config?.buttons?.createButton) {
            this.createButtonSub = this.config?.buttons?.createButton.onClick.subscribe((button: IPopupButton) => {
                this.addNewFolderItem(true);
            });
        }
        this.closeSub = this.config.closeEvent.subscribe((button: IPopupButton) => {
            if (this.busyEditing) {
                this.removeTemporaryItem();
            }
        });
    }

    ngOnDestroy() {
        this.createButtonSub?.unsubscribe();
        this.closeSub?.unsubscribe();
    }

    onCrumbItemSelected(event: IBreadCrumbItemEvent): void {
        let id = 1;

        if (event.item !== null) {
            id = Number(event.item.key);
        }

        this.loading = true;

        this._env.dataService.httpGet('api/category/category', {
            systemId: this._env.currentEnvironment.defaultConfig.category.defaultSystemId,
            category: id,
            host: this._env.currentEnvironment.hostId
        }, this.onCollectionComplete, this._env, false, this.onProgress.bind(this));
        (<ICategoryDialogContext>this.config.context).breadCrumbItems = event.newCrumbs;
    }

    itemClicked(item: ICategoryItem): void {
        if (!this.busyEditing) {
            for (const item of (<ICategoryDialogContext>this.config.context).listItems) {
                item.selected = false;
            }

            (<any>this.config.buttons).okButton.enabled = true;

            item.selected = true;
            this.selectedItem = item;
            this.config.returnData = <ICategoryDialogData>{
                selectedItem: this.selectedItem
            };
        }
    }

    itemDoubleClicked(item: ICategoryItem): void {
        if (!this.busyEditing) {
            this.config.buttons[popup.buttons.create].enabled = true;

            const crumbItem = <IBreadCrumbItem>{
                key: item.id.toString(),
                title: item.name
            };

            this.addItemToCrumb = crumbItem;

            const newBreadCrumbItems = (<ICategoryDialogContext>this.config.context).breadCrumbItems.concat();
            newBreadCrumbItems.push(crumbItem);
            (<ICategoryDialogContext>this.config.context).breadCrumbItems = newBreadCrumbItems;

            this.loading = true;

            if (item.childCount === 0) {
                (<ICategoryDialogContext>this.config.context).listItems = [];
                this.loading = false;
            }
            else {
                this._env.dataService.httpGet('api/category/category', {
                    systemId: this._env.currentEnvironment.defaultConfig.category.defaultSystemId,
                    category: item.id,
                    host: this._env.currentEnvironment.hostId
                }, this.onCollectionComplete, this._env, false, this.onProgress.bind(this));
            }
        }
    }

    private onCollectionComplete = (result: any): void => {
        (<ICategoryDialogContext>this.config.context).listItems = result.items;
        this.config.buttons.okButton.enabled = false; // using popup.buttons.ok here causes the callback to not be able to reference the const 'popup'
        this.loading = false;
    };

    private onProgress(key: number): void {

    }

    cancelNew() {
        if ((<ICategoryDialogContext>this.config.context).listItems.length > 0 &&
            (<ICategoryDialogContext>this.config.context).listItems[(<ICategoryDialogContext>this.config.context).listItems.length - 1].id === -1) {
            this.removeTemporaryItem();
            this.doneEditing();
        }
    }

    createNewFolder = (oldValue: string, newValue: string) => {
        if (newValue.length > 0) {
            //Try to create the new category
            this._env.dataService.httpGet('api/category/createCategory', {
                name: newValue,
                systemId: this._env.currentEnvironment.defaultConfig.category.defaultSystemId,
                parentCategoryId: this.addItemToCrumb ? this.addItemToCrumb.key : '',
                host: this._env.currentEnvironment.hostId
            }, this.createNewFolderCompleted.bind(this), this._env, false, this.onProgress.bind(this), false);

        }
        else {
            this._popupService.showActionErrorDialog(this.getString('category_popup_empty_folder'),
                this.getString('category_popup_empty_folder_error'),
                this.errorOkClicked);
        }
        return true;
    };

    private doneEditing(): void {
        this.config.buttons[popup.buttons.create].enabled = true;
        this.busyEditing = false;
    }

    private addNewFolderItem(newFolder: boolean): void {
        //Placeholder in collection for new item.
        this.busyEditing = true;
        this.config.buttons[popup.buttons.create].enabled = false;

        (<ICategoryDialogContext>this.config.context).listItems.push(sourceCode.k2Designer.objectModel.core.categories.CategoryItem.createInstance({
            id: -1,
            name: this.getString('category_popup_new_folder')
        }));
    }

    private removeTemporaryItem(): void {
        //if the user cancelled by pressing escape, remove the temporary item. Check if it is the temporary item before deleting.
        if ((<ICategoryDialogContext>this.config.context).listItems.length > 0 &&
            (<ICategoryDialogContext>this.config.context).listItems[(<ICategoryDialogContext>this.config.context).listItems.length - 1].id === -1) {
            (<ICategoryDialogContext>this.config.context).listItems.pop();
        }
    }

    private createNewFolderCompleted(result: any, error: boolean): void {
        if (!error) {
            if (result.Success) {
                (<ICategoryDialogContext>this.config.context).listItems[(<ICategoryDialogContext>this.config.context).listItems.length - 1].id = JSON.parse(result.Success).Id;
                (<ICategoryDialogContext>this.config.context).listItems[(<ICategoryDialogContext>this.config.context).listItems.length - 1].path = JSON.parse(result.Success).Path;
            }
            else if (result.Error) {
                //Remove must be before errorOkClicked callback.
                this._popupService.showActionErrorDialog(this.getString('category_popup_duplicate_folder'),
                    result.Error,
                    this.errorOkClicked);

            }
        } else if (JSON.parse(result).Message) {
            //Server error
            this._popupService.showActionErrorDialog(JSON.parse(result).Message,
                JSON.parse(result).ExceptionMessage,
                this.errorOkClicked);
        }

        this.doneEditing();
    }

    private errorOkClicked = (): void => {
        this.busyEditing = false;
        setTimeout(() => {
            this.busyEditing = true;
        }, 0);
    };

    @HostListener('document:keydown.escape', ['$event']) onKeydownHandler(event: KeyboardEvent) {
        this._popupService.hide('', this.config);
    }
}
