import { Component, Inject, Input, OnInit } from '@angular/core';
import { BaseResourceComponent } from 'src/app/components/base-resource/base-resource.component';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { EndConfig } from 'src/app/shared/enums/activities/end-config';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';

@Component({
    selector: 'k2-end-step',
    templateUrl: './end-step.component.html',
    styleUrls: ['./end-step.component.less']
})
export class EndStepComponent extends BaseResourceComponent implements OnInit {
    @Input() activity: sourceCode.k2Designer.objectModel.workflow.process.k2Process.activities.endActivity.IEndActivity;

    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    endConfig: typeof EndConfig = EndConfig;

    endConfiguration: sourceCode.k2Designer.objectModel.workflow.process.k2Process.activities.endActivity.IEndActivityConfiguration;

    constructor(protected resourceService: ResourceService,
        @Inject(UndoRedoServiceToken) private undoRedoService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.endConfiguration = this.activity.configuration as sourceCode.k2Designer.objectModel.workflow.process.k2Process.activities.endActivity.IEndActivityConfiguration;

        if (!this.endConfiguration.endConfig) {
            this.endConfiguration.endConfig = EndConfig.end;
        }
    }

    setConfiguration(configType: EndConfig, val: boolean): void {
        if (!val) return;
        this.undoRedoService.addUndoMarker('--change end step configuration--');
        this.endConfiguration.endConfig = configType;
    }
}
