import { Injectable } from '@angular/core';
import { ISelectionChangedCallback } from '../../types/services/selection/i-selection-changed-callback';
import { ISelectionService } from '../../types/services/selection/i-selection-service';

@Injectable({
    providedIn: 'root'
})
export class MockSelectionService implements ISelectionService {

    constructor() { }
    setSelection = <T>(selection: T[], context?: string) => void (0);

    getSelection = <T>(context?: string) => [];

    hasSelection = (context?: string) => false;

    listen = <T>(callback: ISelectionChangedCallback<T>, context?: string) => void (0);

    isSelected = <T>(item: T, context?: string) => false;
}
