import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IEnvironmentField } from '../../../types/models/core/external-reference-definitions/i-environment-field';
import { IExternalReferenceDefinition } from '../../../types/models/core/external-reference-definitions/i-external-reference-definition';
import { ISmartForm } from '../../../types/models/core/external-reference-definitions/i-smart-form';
import { ISmartObjectField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-field';
import { ISmartObjectMethodField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-method-field';
import { ISmartObjectPropertyField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-property-field';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { INotificationService } from '../../../types/services/notification/i-notification.service';
import { IValidatedComponent } from '../../../types/validation/i-validated-component';
import { ComponentIdMapping } from '../../shared/constants/components';
import { ExternalReferenceState } from '../../shared/enums/external-references/external-references';
import { CacheService } from '../cache/cache.service';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';
import { NotificationServiceToken } from '../notification/notification-service-factory';
import { ProcessService } from '../process/process.service';
import { ResourceService } from '../resource/resource.service';

/**
 * Service to manage references to external components
 */
@Injectable({
    providedIn: 'root'
})
export class ExternalReferenceService {
    private enableFeature: boolean;

    constructor(@Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        private processService: ProcessService,
        private cache: CacheService,
        private resourceService: ResourceService,
        private http: HttpClient,
        @Inject(NotificationServiceToken) private notificationService: INotificationService) {

        this.enableFeature = this.environmentService.currentEnvironment.defaultConfig.externalReferenceService.enable;
    }

    /**
     * Adds an external reference to the service's cache
     *
     * @param externalReference
     */
    addExternalReferenceToCache(externalReference: IExternalReferenceDefinition): void {
        this.cache.set(externalReference.getReference(), (externalReference.implementor as any).name);
    }

    /**
     * Loads all external references of the process to the service's cache and set them ready for immediate update
     */
    loadAllExternalReferencesToCache(): void {

        for (const exRef of this.processService.root.externalReferenceDefinitions) {
            // Add to cache but expire immediately to ensure that each reference will be refreshed
            this.cache.set(exRef.getReference(), (exRef.implementor as any).name, -1);
        }

    }

    /**
     * Fetches updated details of all expired external references and updates them accordingly
     */
    updateExternalReferences(): Observable<any> {

        const refreshReferences: string[] = this.cache.purge();

        return this.validateExternalReferences(refreshReferences);

    }

    /**
     * Validates and updates all external references
     *
     * @param paths Collection of reference paths to external references to be validated, if none are passed,
     *              all external references will be validated
     */
    validateExternalReferences(paths: string[] = []): Observable<any> {

        return new Observable<any>(subscriber => {

            if (!this.enableFeature) {
                // Feature is disabled, resolving validation as successful
                subscriber.complete();
            }
            else {

                const sfFields: ISmartForm[] = [];
                const envFields: IEnvironmentField[] = [];
                const smoFields: ISmartObjectField[] = [];

                this.processService.root.externalReferenceDefinitions.filter((extRef: IExternalReferenceDefinition): boolean =>
                    paths.length === 0 || paths.indexOf(extRef.getReference()) > -1
                ).forEach((extRef: IExternalReferenceDefinition): void => {

                    // Only validate an external reference if it has dependencies
                    if (this.processService.trackedReferenceHasChildren(extRef.getReference())) {

                        switch (extRef.componentId) {

                            case ComponentIdMapping.SmartFormsExternalReference:
                                sfFields.push(extRef as ISmartForm);

                                break;

                            case ComponentIdMapping.EnvironmentFieldExternalReference:
                                envFields.push(extRef as IEnvironmentField);

                                break;

                            case ComponentIdMapping.SmartObjectFieldExternalReference:
                                smoFields.push(extRef as ISmartObjectField);

                                break;
                        }

                    }

                });

                // Validate SmartObject external references
                if (smoFields.length > 0) {

                    this.getSmartObjectFieldData(smoFields).subscribe({
                        next: (responseData: any) => {
                            if (!this.validateAndUpdateSmartObjectFields(smoFields, responseData)) {
                                subscriber.error(smoFields);
                            }
                            else {
                                subscriber.next(smoFields);
                            }
                        }, error: (err: string) => {
                            console.error(err);
                            subscriber.error(err);
                        }
                    });
                }

                // Validate Environment Field external references
                if (envFields.length) {
                    /* TODO - Validation for EnvironmentField references  to be added here - */
                }

                // Validate SmartForms external references
                if (sfFields.length) {
                    /* TODO - Validation for SmartForm references  to be added here - */
                }

                // Resolve the subscription with the outcomes of the validation
                subscriber.complete();
            };
        });
    }

    /**
     * Performs a clean-up of unused external references, to reduce dependencies
     */
    removeUnusedExternalReferences(): void {

        if (this.enableFeature) {
            let index = 0;

            while (index < this.processService.root.externalReferenceDefinitions.length) {

                const extRef = this.processService.root.externalReferenceDefinitions[index];

                if (extRef.componentId === ComponentIdMapping.SmartObjectFieldExternalReference) {

                    let methodIndex = 0;

                    while (methodIndex < (extRef as ISmartObjectField).methods.length) {

                        const method: ISmartObjectMethodField = (extRef as ISmartObjectField).methods[methodIndex];

                        let propIndex = 0;

                        while (propIndex < method.inputs.length) {

                            if (method.inputs[propIndex].state === ExternalReferenceState.Missing &&
                                !this.processService.trackedReferenceHasChildren(method.inputs[propIndex].getReference())) {
                                method.inputs[propIndex].removeFromModelArray();
                            }

                            propIndex++;

                        }

                        propIndex = 0;

                        while (propIndex < method.parameters.length) {

                            if (method.parameters[propIndex].state === ExternalReferenceState.Missing &&
                                !this.processService.trackedReferenceHasChildren(method.parameters[propIndex].getReference())) {
                                method.parameters[propIndex].removeFromModelArray();
                            }

                            propIndex++;

                        }

                        propIndex = 0;

                        while (propIndex < method.returns.length) {

                            if (method.returns[propIndex].state === ExternalReferenceState.Missing &&
                                !this.processService.trackedReferenceHasChildren(method.returns[propIndex].getReference())) {
                                method.returns[propIndex].removeFromModelArray();
                            }

                            propIndex++;

                        }

                        if (method.state === ExternalReferenceState.Missing &&
                            !this.processService.trackedReferenceHasChildren(method.getReference())) {
                            method.removeFromModelArray();
                        }

                        methodIndex++;
                    }

                }

                if (extRef.state === ExternalReferenceState.Missing &&
                    !this.processService.trackedReferenceHasChildren(extRef.getReference())) {
                    extRef.removeFromModelArray();
                }

                index++;
            }

        }
    }

    private getSmartObjectFieldData(smoFields: ISmartObjectField[]): Observable<any> {

        return new Observable<any>(subscriber => {

            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService?.currentEnvironment.hostId)
                    .set('source', JSON.stringify(smoFields.map((smoField: ISmartObjectField): any => smoField.getJson())))
            };

            this.http
                .post<string>('api/smartobject/getsmartobjectreferences', options)
                .subscribe({
                    next: (responseData: any) => {
                        subscriber.next(responseData);
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });

    }

    /**
     * Validates the original SmartObject external reference definitions against the returned server response
     *
     * @param originalSmoFields Original SmartObject external reference definitions to be validated
     * @param smoFields SmartObject external reference definitions to validate against
     * @param cascade Cascade the validation state of a parent field to child fields
     */
    private validateAndUpdateSmartObjectFields(originalSmoFields: ISmartObjectField[],
        smoFields: ISmartObjectField[], cascade: boolean = false): boolean {

        let hasErrors = false;

        originalSmoFields.forEach((originalSmoField: ISmartObjectField): boolean => {

            // Find the existing field in the returned collection
            let search = smoFields.filter((field: ISmartObjectField) =>
                field.systemName === originalSmoField.systemName
            );

            // If found, it still exists
            const smoField: ISmartObjectField = search.length === 1 ? search[0] : null;

            if (!!smoField) {

                // External Reference is valid
                originalSmoField.state = ExternalReferenceState.Valid;

                // Update the details of the SmartObject
                if (originalSmoField.displayName !== smoField.displayName) {
                    originalSmoField.displayName = smoField.displayName;
                }

                if (originalSmoField.description !== smoField.description) {
                    originalSmoField.description = smoField.description;
                }

                // Update each method
                originalSmoField.methods.forEach((originalMethod: ISmartObjectMethodField): boolean => {

                    // Find the existing method in the returned SmartObject
                    search = smoField.methods.filter((method: ISmartObjectMethodField): boolean =>
                        method.systemName === originalMethod.systemName);

                    const updatedMethod: ISmartObjectMethodField = search.length === 1 ? search[0] : null;

                    if (!!updatedMethod) {
                        // Method still exists
                        originalMethod.state = ExternalReferenceState.Valid;

                        // Update the details of the method
                        if (originalMethod.displayName !== updatedMethod.displayName) {
                            originalMethod.displayName = updatedMethod.displayName;
                        }

                        if (originalMethod.description !== updatedMethod.description) {
                            originalMethod.description = updatedMethod.description;
                        }

                        if (originalMethod.type !== updatedMethod.type) {
                            originalMethod.type = updatedMethod.type;
                        }

                        // Update parameters
                        originalMethod.parameters.forEach((originalParameter: ISmartObjectPropertyField): boolean => {

                            // Find the existing parameter in the returned method
                            search = updatedMethod.parameters.filter((param: ISmartObjectPropertyField): boolean => {
                                switch (param.type) {
                                    case 'file':
                                    case 'image':
                                    case 'hyperlink':
                                        return param.systemName === originalParameter.systemName ||
                                            (param.systemName + '.XML') === originalParameter.systemName;
                                    default:
                                        return param.systemName === originalParameter.systemName;
                                }
                            });

                            const updatedParameter = search.length === 1 ? search[0] as ISmartObjectPropertyField : null;

                            if (!!updatedParameter) {
                                // Parameter still exists
                                originalParameter.state = ExternalReferenceState.Valid;

                                // Update the details of the parameter
                                if (originalParameter.displayName !== updatedParameter.displayName) {
                                    originalParameter.displayName = updatedParameter.displayName;
                                }

                                if (originalParameter.description !== updatedParameter.description) {
                                    originalParameter.description = updatedParameter.description;
                                }

                                if (originalParameter.type !== updatedParameter.type) {
                                    originalParameter.type = updatedParameter.type;
                                }

                                if (originalParameter.isRequired !== updatedParameter.isRequired) {
                                    originalParameter.isRequired = updatedParameter.isRequired;
                                }
                            }
                            else {
                                // Parameter no longer exists, removing it
                                hasErrors = this.raiseExternalReferenceError(originalParameter);
                            }

                            return true;

                        });

                        // Update input parameters
                        originalMethod.inputs.forEach((originalParameter: ISmartObjectPropertyField): boolean => {

                            // Find the existing parameter in the returned method
                            search = updatedMethod.inputs.filter((param: ISmartObjectPropertyField): boolean => {
                                switch (param.type) {
                                    case 'file':
                                    case 'image':
                                    case 'hyperlink':
                                        return param.systemName === originalParameter.systemName ||
                                            (param.systemName + '.XML') === originalParameter.systemName;
                                    default:
                                        return param.systemName === originalParameter.systemName;
                                }
                            });

                            const updatedParameter = search.length === 1 ? search[0] as ISmartObjectPropertyField : null;

                            if (!!updatedParameter) {
                                // Parameter still exists
                                originalParameter.state = ExternalReferenceState.Valid;

                                // Update the details of the parameter
                                if (originalParameter.displayName !== updatedParameter.displayName) {
                                    originalParameter.displayName = updatedParameter.displayName;
                                }

                                if (originalParameter.description !== updatedParameter.description) {
                                    originalParameter.description = updatedParameter.description;
                                }

                                if (originalParameter.type !== updatedParameter.type) {
                                    originalParameter.type = updatedParameter.type;
                                }

                                if (originalParameter.isRequired !== updatedParameter.isRequired) {
                                    originalParameter.isRequired = updatedParameter.isRequired;
                                }
                            }
                            else {
                                // Parameter no longer exists, removing it
                                hasErrors = this.raiseExternalReferenceError(originalParameter);
                            }

                            return true;

                        });

                        // Update return parameters
                        originalMethod.returns.forEach((originalParameter: ISmartObjectPropertyField): boolean => {

                            // Find the existing parameter in the returned method
                            search = updatedMethod.returns.filter((param: ISmartObjectPropertyField): boolean => {
                                switch (param.type) {
                                    case 'file':
                                    case 'image':
                                    case 'hyperlink':
                                        return param.systemName === originalParameter.systemName
                                            || (param.systemName + '.XML') === originalParameter.systemName
                                            || (param.systemName + '.FileName') === originalParameter.systemName
                                            || (param.systemName + '.Content') === originalParameter.systemName;
                                    default:
                                        return param.systemName === originalParameter.systemName;
                                }
                            });

                            const updatedParameter = search.length === 1 ? search[0] as ISmartObjectPropertyField : null;

                            if (!!updatedParameter) {
                                // Parameter still exists
                                originalParameter.state = ExternalReferenceState.Valid;

                                // Update the details of the parameter
                                if (originalParameter.displayName !== updatedParameter.displayName) {
                                    originalParameter.displayName = updatedParameter.displayName;
                                }

                                if (originalParameter.description !== updatedParameter.description) {
                                    originalParameter.description = updatedParameter.description;
                                }

                                if (originalParameter.type !== updatedParameter.type) {
                                    originalParameter.type = updatedParameter.type;
                                }
                            }
                            else {
                                // Parameter no longer exists, removing it
                                hasErrors = this.raiseExternalReferenceError(originalParameter);
                            }

                            return true;
                        });

                    }
                    else {
                        // Method no longer exists, need to remove it
                        hasErrors = this.raiseExternalReferenceError(originalMethod);

                        if (cascade) {
                            // Cascade validation states to child fields
                            originalMethod.parameters.forEach((param: ISmartObjectPropertyField): void => {
                                this.raiseExternalReferenceError(param);
                            });

                            originalMethod.inputs.forEach((param: ISmartObjectPropertyField): void => {
                                this.raiseExternalReferenceError(param);
                            });

                            originalMethod.returns.forEach((param: ISmartObjectPropertyField): void => {
                                this.raiseExternalReferenceError(param);
                            });
                        }

                    }

                    return true;
                });

            }
            else {
                // SmartObject no longer exists, need to remove it
                hasErrors = this.raiseExternalReferenceError(originalSmoField);

                if (cascade) {
                    // Cascade validation states to child fields
                    originalSmoField.methods.forEach((method: ISmartObjectMethodField): void => {
                        this.raiseExternalReferenceError(method);

                        method.parameters.forEach((param: ISmartObjectPropertyField): void => {
                            this.raiseExternalReferenceError(param);
                        });

                        method.inputs.forEach((param: ISmartObjectPropertyField): void => {
                            this.raiseExternalReferenceError(param);
                        });

                        method.returns.forEach((param: ISmartObjectPropertyField): void => {
                            this.raiseExternalReferenceError(param);
                        });
                    });
                }

            }

            return true;
        });

        return !hasErrors;

    }

    /**
     * Raise an error on all of its dependencies to the external reference definition
     *
     * @param externalReference External Reference Definition in error
     * @returns Returns true if any errors was raised else false
     */
    private raiseExternalReferenceError(externalReference: IExternalReferenceDefinition): boolean {

        // Set State Flag on External Reference
        externalReference.state = ExternalReferenceState.Missing;

        // Raise validation errors on all references to the affected external reference
        const referenceItems = this.processService.getTrackedReferencesForNode(externalReference.getReference());

        for (const item of referenceItems) {

            const objectPath: string = item.substr(0, item.lastIndexOf('.'));
            const objectProperty: string = item.substr(item.lastIndexOf('.') + 1);

            const modelObject = this.processService.root.findItem<IValidatedComponent>(objectPath);

            // A collection should not have a property that can reference an external reference definition
            if (modelObject && !Array.isArray(modelObject[objectProperty])) {
                this.processService.raiseReferenceError(modelObject, objectProperty, externalReference.getReference());
            }
        }

        return referenceItems.length > 0;

    }
}
