import { enableProdMode } from '@angular/core';
import { loader } from './app/loader';
import { environment } from './environments/environment';

if (environment.devHarness) {
    import('./app/app.module').then((AppModule) => {
        import('@angular/platform-browser-dynamic').then(
            (PlatformBrowserDynamic) => {
                PlatformBrowserDynamic.platformBrowserDynamic()
                    .bootstrapModule(AppModule.AppModule)
                    .catch((err) => console.error(err))
                    .then(() => {
                        console.log('app module bootstrapped');
                    });
            }
        );
    });
} else {
    if (environment.production) {
        enableProdMode();
    }

    const loaderInstance = new loader();
    window.addEventListener(
        'message',
        loaderInstance.messageReceivedHandler.bind(loaderInstance),
        false
    );

    // Wait for the document to be ready before sending a message to the parent frame
    $(document).ready(loaderInstance.documentReadyHandler.bind(loaderInstance));
}
