import { Injectable } from '@angular/core';
import { IModelArray } from '../../../types/models/base/i-model-array';
import { IConfiguredSmartField } from '../../../types/models/core/configured-smart-fields/i-configured-smart-field';
import { IExpression } from '../../../types/models/core/configured-smart-fields/i-expression';
import { ISmartObjectMethodField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-method-field';
import { ISmartObjectPropertyField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-property-field';
import { ISmartObjectField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-field';
import { ComponentIdMapping } from '../../shared/constants/components';
import { ALLITEMSFILTER, DESCRIPTION, DISPLAYNAME, EMAIL, FQN, GETEMAILFORGROUPUSERS, GROUPFQN, LABEL, MANAGER, NAME, SIPACCOUNT, USERANDGROUPS, USERNAME } from '../../shared/constants/smart-objects';
import { isUndefinedOrNull } from '../../shared/utils/general';
import { ProcessService } from '../process/process.service';
import { ResourceService } from '../resource/resource.service';
import { WorkflowService } from '../workflow/workflow.service';
import { IConfiguredSmartObjectField } from '../../../types/models/core/configured-smart-fields/i-smart-object-field';
import { ISmartObjectFieldProperty } from '../../../types/models/core/configured-smart-fields/i-smart-object-field-property';

@Injectable({
  providedIn: 'root'
})
export class SmartObjectService {

    constructor(private workflowService: WorkflowService,
        private processService: ProcessService,
        private resourceService: ResourceService) { }

    /**
     *  Returns icon for destination expression
     *  
     * @param destinationExpression
     */
    getDestinationIcon(destinationExpression: IExpression): string {
        let icon = `icon icon-size24 icon-set-picker ic-recipientnondynamic`;
        let isUserIcon = false;
        let isGroupIcon = false;
        if (destinationExpression?.smartFields) {
            isUserIcon = destinationExpression.smartFields.length &&
                !(ComponentIdMapping.k2WorkflowContextField === destinationExpression.smartFields[0].componentId
                    || ComponentIdMapping.k2EnvironmentField === destinationExpression.smartFields[0].componentId);

            isGroupIcon = destinationExpression.smartFields.length === 1
                ? this.isEmailForGroupUsersField(destinationExpression.smartFields[0]) :
                false;
        }

        if (!destinationExpression?.isDynamic && isGroupIcon) {
            icon = `icon icon-size24 icon-set-steps ic-group`;
        }
        else if (!destinationExpression?.isDynamic && isUserIcon) {
            icon = `icon icon-size24 icon-set-steps ic-user`;
        }

        return icon;
    }

    /**
     * Method to check if field is the specific 'Get_E_mail_For_Group_Users' property
     * @param processService
     * @param smartField
     */
    isEmailForGroupUsersField(smartField: IConfiguredSmartField): boolean {

        if (smartField.instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectField.implementor)) {

            let smartObjectField = smartField as IConfiguredSmartObjectField;
            if (smartObjectField.methodReference && smartObjectField.propertyReference) {

                let externalSmoMethod = this.workflowService.findItem<ISmartObjectMethodField>(smartObjectField.methodReference, smartObjectField);

                if (externalSmoMethod) {

                    let exteralSmoProperty = this.workflowService.findItem<ISmartObjectPropertyField>(smartObjectField.propertyReference, smartObjectField);

                    return exteralSmoProperty.systemName === EMAIL && externalSmoMethod.systemName === GETEMAILFORGROUPUSERS;
                }
            }
        }
        return false;
    }


    /**
     * Method to wrap the selected group into a SmartObject field so that it can resolve at runtime.
     * @param environmentService
     * @param processService
     * @param userName
     * @param label
     */
    wrapEmailForGroupUsersField(groupDisplayName: string, groupFqn: string, label: string): ISmartObjectField {

        //Get the 'Users and Groups' SmartObject external reference definition
        let usersAndGroupsSmoExternalRef = this.processService.root.externalReferenceDefinitions
            .single<ISmartObjectField>
            ({
                systemName: USERANDGROUPS,
                componentId: ComponentIdMapping.SmartObjectFieldExternalReference
            });

        //Create the 'Get E-Mail for Group Users' Method on the 'Users and Groups' SmartObject external reference definition.
        let usersAndGroupsSmoMethodExternalRef = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectMethodField.createInstance({
            systemName: GETEMAILFORGROUPUSERS,
            type: 'list',
            displayName: this.resourceService.getString('smartObject_method_display_name'),
            description: this.resourceService.getString('smartObject_method_display_name'),
            parameters: this.getExternalMethodParameters() as any, //Old and new types of IModelArray<ISmartObjectPropertyField> not compatible
            inputs: this.getExternalMethodInputs() as any,
            returns: this.getExternalMethodReturns() as any
        });

        //Check if the 'Users and Groups' external reference definition exists, if not add it.
        if (isUndefinedOrNull(usersAndGroupsSmoExternalRef)) {

            //Create new external reference smartObjectField.
            usersAndGroupsSmoExternalRef = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectField.createInstance({
                systemName: USERANDGROUPS,
                displayName: this.resourceService.getString('smartObject_display_name'),
                description: this.resourceService.getString('smartObject_description'),
                methods: [usersAndGroupsSmoMethodExternalRef]
            }) as unknown as ISmartObjectField;

            this.processService.root.externalReferenceDefinitions.push(usersAndGroupsSmoExternalRef);
        }

        //Get the 'Get E-Mail for Group Users' Method external reference definition.
        let smoExternalRefMethod = usersAndGroupsSmoExternalRef.methods.single<ISmartObjectMethodField>({ systemName: GETEMAILFORGROUPUSERS });

        //Check if it exists in external reference definition, if not add it.
        if (isUndefinedOrNull(smoExternalRefMethod)) {
            usersAndGroupsSmoExternalRef.methods.push(usersAndGroupsSmoMethodExternalRef as unknown as ISmartObjectMethodField);
        }

        return this.createGetEmailForGroupUserSmartField(usersAndGroupsSmoExternalRef, groupDisplayName, groupFqn, label);
    }

    /**
     * Method to get the 'Get_E_mail_For_Group_Users' external reference parameters.
     * @param resourceService
     */
    private getExternalMethodParameters(): ISmartObjectPropertyField[] {
        return [
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: GROUPFQN,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_fqn'),
                description: this.resourceService.getString('smartObject_method_property_fqn')
            }) as unknown as ISmartObjectPropertyField,
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: LABEL,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_label'),
                description: this.resourceService.getString('smartObject_method_property_label')
            }) as unknown as ISmartObjectPropertyField
        ];
    }

    /**
      * Method to get the 'Get_E_mail_For_Group_Users' external reference inputs.
      * @param resourceService
      */
    private getExternalMethodInputs(): ISmartObjectPropertyField[] {
        return [
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: NAME,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_name')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: DESCRIPTION,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_description')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: EMAIL,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_mail')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: MANAGER,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_manager')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                isRequired: true,
                systemName: GROUPFQN,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_fqn'),
                description: this.resourceService.getString('smartObject_method_property_fqn')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                isRequired: true,
                systemName: LABEL,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_label'),
                description: this.resourceService.getString('smartObject_method_property_label')
            })
        ];
    }

    /**
     * Method to get the 'Get_E_mail_For_Group_Users' external reference method returns.
     * @param resourceService
     */
    private getExternalMethodReturns(): ISmartObjectPropertyField[] {
        return [
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: FQN,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_fqn'),
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: USERNAME,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_user_name'),
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: EMAIL,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_mail'),
                description: this.resourceService.getString('smartObject_method_property_mail')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: NAME,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_name')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: DESCRIPTION,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_description')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: MANAGER,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_manager')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: SIPACCOUNT,
                type: 'text',
                displayName: this.resourceService.getString('SIP_Account')
            }),
            sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                systemName: DISPLAYNAME,
                type: 'text',
                displayName: this.resourceService.getString('smartObject_method_property_display_name')
            })
        ];
    }

    /**
     * Method to create the configured SmartObject field for textbox.
     * @param usersAndGroupsSmoExternalRef
     * @param groupDisplayName
     * @param groupFqn
     * @param label
     */
    private createGetEmailForGroupUserSmartField(usersAndGroupsSmoExternalRef: ISmartObjectField,
        groupDisplayName: string, groupFqn: string, label: string): ISmartObjectField {

        let smoExternalRefMethod = usersAndGroupsSmoExternalRef.methods.single<ISmartObjectMethodField>({ systemName: GETEMAILFORGROUPUSERS });
        let smoExternalEmailReturnProp = smoExternalRefMethod.returns.single<ISmartObjectPropertyField>({ systemName: EMAIL });

        //Create the SmartObject method field.
        let smoMethod = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldMethod.createInstance({
            externalReference: smoExternalRefMethod.getReference(),
            returnProperty: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldProperty.createInstance({
                externalReference: smoExternalEmailReturnProp.getReference()
            }),
            parameters: [sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldParameter.createInstance({
                externalReference: smoExternalRefMethod.parameters.single<sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectFieldParameter>(
                    { systemName: GROUPFQN }).getReference(),
                value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance(
                    {
                        richText: false, smartFields: [
                            sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({ text: groupFqn })
                        ]
                    }),
            }),
            sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldParameter.createInstance({
                externalReference: smoExternalRefMethod.parameters.single<sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectFieldParameter>({ systemName: LABEL }).getReference(),
                value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({ richText: false, smartFields: [sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({ text: label })] }),
            })],
            inputProperties: this.getInputProperties(true, smoExternalRefMethod),
            methodFilter: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldFilter.createInstance({
                filterType: ALLITEMSFILTER,
                logicalOperator: 'equal',
                leftProperty: smoExternalRefMethod.returns[0].getReference(),
                rightProperty: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({ richText: false, smartFields: [] }),
            })
        });
        //Create the actual SmartObject property field.
        return sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectField.createInstance({
            method: smoMethod,
            propertyReference: smoExternalEmailReturnProp.getReference(),
            methodReference: smoExternalRefMethod.getReference(),
            externalReference: usersAndGroupsSmoExternalRef.getReference(),
            customTitle: groupDisplayName
        });
    }

    /**
     * Method to build the input properties
     * @param isListMethod
     * @param smoMethod
     */
    getInputProperties(isListMethod: boolean, smoMethod: ISmartObjectMethodField):
        ISmartObjectFieldProperty[] {

        let properties: ISmartObjectFieldProperty[] = [];

        for (let i: number = 0; i < smoMethod.inputs.length; i++) {
            let property: ISmartObjectPropertyField = smoMethod.inputs[i];

            let isBindToParamerter = smoMethod.parameters.filter(function (item: ISmartObjectPropertyField) {
                return item.systemName === smoMethod.inputs[i].systemName;
            }).length > 0;

            if (!isBindToParamerter && ((isListMethod && property.isRequired) || !isListMethod)) {
                let newConfiguredProp: ISmartObjectFieldProperty = {
                    externalReference: property.getReference(),
                    value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({ richText: false, smartFields: [] }),
                };
                properties.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.SmartObjectFieldProperty.createInstance(newConfiguredProp));
            }
        }
        return properties;
    }
}
