import { Component, Inject, Input, OnInit, OnDestroy } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { IProcessPluginDefinitionContext } from 'src/types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { debounceTime } from 'rxjs/operators';
import { defaultDebounceTime } from 'src/app/shared/constants/general';
import { IContextWizardPopupConfiguration } from 'src/types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { ResourceService } from 'src/app/services/resource/resource.service';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { PopupService } from '../../../../services/popup/popup.service';
import { PageComponent } from '../../page/page.component';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';

@Component({
    selector: 'k2-configure-start-rule',
    templateUrl: './configure-start-rule.component.html',
    styleUrls: ['./configure-start-rule.component.less'],
    providers: [{ provide: PageComponent, useExisting: ConfigureStartRuleComponent }]
})
export class ConfigureStartRuleComponent extends PageComponent implements OnInit, OnDestroy {

    /**
     * Wizard popup configuration
     */
    @Input() config: IContextWizardPopupConfiguration;

    get pluginContext(): IProcessPluginDefinitionContext {
        return this.config?.context as IProcessPluginDefinitionContext;
    };

    shouldScrollPopup: boolean;
    menuAlignment = DropDownMenuAlignment;
    formLoaded = false;

    buttonConfig: IPopupButtonsConfig = {
        previous: {
            isVisible: true
        }
    };

    //Private members
    private windowResizeSubscription: Subscription;
    private getFormSub: Subscription;
    private refreshing = false;

    get formRules() {
        if (!this.refreshing && !this.pluginContext?.rules?.length) {
            this.refreshing = true;
            this.refreshRules();
        }
        return this.pluginContext?.rules || [];
    }

    constructor(private popupService: PopupService,
        resourceService: ResourceService,
        private smartFormsIntegrationService: SmartFormsIntegrationService) {

        super(resourceService);

        this.windowResizeSubscription = fromEvent(window, 'resize')
            .pipe(debounceTime(defaultDebounceTime))
            .subscribe(() => {
                this.shouldScrollPopup = this.isPopupHeightLargerThan(this.windowHeight);
            });

        this.shouldScrollPopup = this.isPopupHeightLargerThan(this.windowHeight);
    }

    ngOnInit(): void {
        this.getFormSub = this.smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formLoaded = true;
            });
    }

    ngOnDestroy(): void {
        this.windowResizeSubscription?.unsubscribe();
        this.getFormSub?.unsubscribe();
    }

    beforePageDisplayed() {
    }

    beforeNavigateNext() {}

    beforeNavigatePrevious() {}

    /**
     * Method when item is selected from the dropdown.
     */
    itemSelected(item: IDropDownMenuItem): void {
        if (!this.pluginContext || !this.smartFormsIntegrationService || !this.pluginContext.rules) {
            console.error('pluginContext is not set in ConfigureStartRuleComponent');
            return;
        }

        this.pluginContext.selectedRule = item;
        this.smartFormsIntegrationService
            .refreshRuleConditionsAndActions(this.pluginContext)
            .subscribe();

        this.smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    onRulePlacementChanged(hasChanged: boolean) {
        if (!this.pluginContext || !this.smartFormsIntegrationService) {
            console.error('pluginContext is not set in ConfigureStartRuleComponent');
            return;
        }

        if (hasChanged) {
            this.pluginContext.addActionToRule = true;
        } else {
            this.pluginContext.addActionToRule = false;
            this.smartFormsIntegrationService.determinePages(this.pluginContext);
        }
    }

    /**
     * Method to refresh the rules in the dropdown.
     */
    refreshRules(): void {
        this.smartFormsIntegrationService.refreshRules(this.pluginContext).subscribe(
            {
                next:  () => { this.refreshing = false; },
                error:  () => { this.refreshing = false; }
            }
        );
    }

    private isPopupHeightLargerThan(height: number): boolean {
        if (this.popupService.getInitialHeight() > height) {
            return false;
        }
        else {
            return true;
        }
    }

    private get windowHeight(): number {
        return window.innerHeight;
    }
}
