import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class MockUndoRedoService implements IUndoRedoService {
    constructor() { }
    enabledAndBeginUndoGroupWithMarkerAndFinish(title: string): IComplexUndoRedoTransaction {
        return {} as any;
    }
    undoRedoEnabled = true;
    registerStudioItem(studioItem: sourceCode.k2Designer.objectModel.environment.ItemInstance): void { };
    undo(): boolean { return true; };
    redo(): boolean { return true; };
    getUndoRedoStack(): sourceCode.k2Designer.objectModel.undoRedoService.IUndoRedoStack { return {} as any; };
    canUndo(): boolean { return true; };
    canRedo(): boolean { return true; };
    disable(): void { };
    enable(): void { };
    isEnabled(): boolean { return true; };
    beginUndoGroup(title: string): void { };
    finishUndoGroup(): void { };
    cancelUndoGroup(): void { };
    listen(callback: sourceCode.k2Designer.objectModel.undoRedoService.IUndoRedoOperationCallback): void { };
    listenStateChanged(callback: sourceCode.k2Designer.objectModel.undoRedoService.IUndoRedoStateChangedCallback): void { };
    addCustomUndoEvent(callback: sourceCode.k2Designer.services.studio.ICustomUndoEventCallback, params: any, title?: string): void { };
    addCustomObjectUndoEvent: (object: any, propertyNames: string[], title?: string) => boolean;
    forceDisable(): void { };
    forceDisableAsync(id: string): string { return ''; };
    resetForceDisable(): void { };
    forceDisableWhile(executingFn: Function, shouldDisable: boolean, title?: string): void { };
    forceDisableWhilePromise(when: JQueryPromise<any>, then: Function, shouldDisable: boolean, title?: string): void { };
    resetForceDisableAsync(uniqueId: string): void { };
    shouldKeepDisabledState(): boolean { return true; };
    undoRedoSafeSetTimeout(fn: Function, timeout?: number): void { };
    onUndoingRedoing(callback: sourceCode.k2Designer.objectModel.undoRedoService.IUndoingRedoingCallback): void { };
    addUndoInfo(title: string): void { };

    /**
     * Places a marker in the undo stack that indicates a position in the undo stack where a continuous
     * undo operation will stop
     */
    addUndoMarker(title?: string): void {
        console.log('adding undo marker - ', title);
    }
}
