import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockNotificationService } from '../../../testing/mock-services/notification-service';
import { INotificationService } from '../../../types/services/notification/i-notification.service';

const notificationServiceFactory = () => environment.devHarness === true
    ? new MockNotificationService()
    : sourceCode.k2Designer.core.environmentService.notificationService;

export const NotificationServiceToken = new InjectionToken<INotificationService>('INotificationService');

export const NotificationServiceFactory = {
    provide: NotificationServiceToken,
    useFactory: notificationServiceFactory,
    deps: [

    ]
};
