    /**
     * This ensures the namespace and registers it for disposal
     *
     * @param ns
     * @param config
     */
    private ensureNamespace(ns: string, config: any): any {
        Mocks.ensureNamespace(ns, config);
        this.registeredNamespaces.push(ns);
    }

    /**
     * This will dispose of any namespaces generated
     * By default it will destroy all 'sourceCode' namespaces
     * See https://developer.mozilla.org/en-US/docs/Web/JavaScript/Memory_Management#garbage_collection
     *
     * @param whole dispose all 'sourceCode' namespaces
     */
    public dispose(whole: boolean = true): void {
        // Dispose of namespaces
        if (whole) {
            Mocks.destroyNamespace('sourceCode', true);
        } else {
            this.registeredNamespaces.forEach((ns) => {
                Mocks.destroyNamespace(ns, true);
            });
        }
        MockEnvironmentService.ensureBaseNameSpaces();
    }

    public static ensureBaseNameSpaces() {
        //Just ensure environment service definition stays
        Mocks.ensureNamespace('sourceCode.k2Designer.services.core', null);
    }

