import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { IProcessPluginDefinitionContext } from '../../../../../types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { shortUid } from '../../../../shared/utils/general';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-smartforms-configure-state',
    templateUrl: './configure-state.component.html',
    styleUrls: ['./configure-state.component.less'],
    providers: [{ provide: PageComponent, useExisting: SmartFormsConfigureStateComponent }]
})
export class SmartFormsConfigureStateComponent extends PageComponent implements OnInit, OnDestroy {
    buttonConfig: IPopupButtonsConfig;

    @Input() public config: IPopupConfiguration;

    /**
     * Unique identifier for create new state radio button
     */
    public createNewUid: string;

    /**
     * Unique identifier for use existint state radio button
     */
    public useExistingUid: string;

    /**
     * Unique identifier for default checkbox
     */
    public defaultCheckboxUid: string;

    /**
     * Unique identifier for default checkbox
     */
    public handleAllocationUid: string;

    /**
     * Context of the plugin wizard.
     */
    get pluginContext(): IProcessPluginDefinitionContext {
        return this.config?.context as IProcessPluginDefinitionContext;
    };

    // Enum imports for use in templates
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    formLoaded = false;

    private _getFormSub: Subscription;

    constructor(protected resourceService: ResourceService,
        private _smartFormsIntegrationService: SmartFormsIntegrationService
    ) {
        super(resourceService);
        this.createNewUid = shortUid();
        this.useExistingUid = shortUid();
        this.defaultCheckboxUid = shortUid();
        this.handleAllocationUid = shortUid();
    }

    ngOnInit(): void {
        this._getFormSub = this._smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formLoaded = true;
            });
    }

    ngOnDestroy() {
        this._getFormSub.unsubscribe();
    }

    beforePageDisplayed() {
    }

    beforeNavigateNext() {}

    beforeNavigatePrevious() {}

    /**
     * Method when the user check/uncheck allocate to user checkbox.
     */
    public allocateTaskToUserChange(): void {
        this._smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    /**
     * Method when location is selected from the dropdown.
     */
    public locationSelected(item: IDropDownMenuItem): void {
        this.pluginContext.selectedLocation = item;
        for (let i = 0; i < this.pluginContext.workflowViewLocations.length; i++) {
            if (this.pluginContext.workflowViewLocations[i].key != item.key) {
                this.pluginContext.workflowViewLocations[i].isSelected = false;
            }
        }
        this._smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    /**
     * Method when item is selected from the dropdown.
     */
    public itemSelected(item: IDropDownMenuItem): void {
        this.pluginContext.selectedState = item;
        for (let i = 0; i < this.pluginContext.states.length; i++) {
            if (this.pluginContext.states[i].key != item.key) {
                this.pluginContext.states[i].isSelected = false;
            }
        }
        this._smartFormsIntegrationService.determinePages(this.pluginContext);
        this._smartFormsIntegrationService.refreshRules(this.pluginContext);
    }

    /**
     * Method to update form rules
     */
    public updateFormRules(): void {
        this._smartFormsIntegrationService.refreshRules(this.pluginContext);
    }

    /**
     * Method to refresh the states.
     */
    public refreshStates(): void {
        this._smartFormsIntegrationService.refreshStates(this.pluginContext);
    }

}
