import { Component, Inject, Input, OnInit, QueryList, ViewChild, ViewChildren } from '@angular/core';
import { DropDownComponent } from 'src/app/components/common/drop-down/drop-down.component';
import { TextboxComponent } from 'src/app/components/common/text-box/text-box.component';
import { ConfigPanelServiceToken } from 'src/app/services/config-panel/config-panel-service-factory';
import { DataTypeResolverService } from 'src/app/services/plugins/data-type-resolver/data-type-resolver.service';
import { PopupService } from 'src/app/services/popup/popup.service';
import { ProcessService } from 'src/app/services/process/process.service';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from 'src/app/services/validation/validation-service-factory';
import IDropDownMenuItem from 'src/app/shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { ErrorSeverity } from 'src/app/shared/enums/model/error-severity';
import { ValidationTypes } from 'src/app/shared/enums/model/validation-types';
import { DataFieldTypes } from 'src/app/shared/enums/model/workflow/process/data-field-types';
import { findUniqueName } from 'src/app/shared/utils/name-helper';
import { IModelObject } from 'src/types/models/base/i-model-object';
import { IDataFieldMappingRows } from 'src/types/models/config-panel/plugins/plugin-wizards/custom-wizards/set-data-fields-wizard/i-data-field-mapping-rows';
import { IDataField } from 'src/types/models/data-field/i-data-field';
import { ISetDataFieldMapping } from 'src/types/models/process/events/configuration/i-set-data-field-mapping';
import { ISetDataFieldsConfiguration } from 'src/types/models/process/events/configuration/i-set-data-fields-configuration';
import { IConfigPanelService } from 'src/types/services/config-panel/i-config-panel-service';
import { IValidationService } from 'src/types/services/validation/ivalidation.service';
import { PluginBaseComponent } from '../../../base/plugin-base/plugin-base.component';

@Component({
    selector: 'k2-set-data-fields-wizard',
    templateUrl: './set-data-fields-wizard.component.html',
    styleUrls: ['./set-data-fields-wizard.component.less']
})
export class SetDataFieldsWizardComponent extends PluginBaseComponent implements OnInit {

    // Input properties
    @Input() configuration: ISetDataFieldsConfiguration;

    // Public properties
    hoverClass = false;
    rowDropdownFocus = false;
    rowValueControlFocus = false;
    selectedMappingRowIndex: number;

    selectedRowItem: IDataFieldMappingRows;
    selectedProperties: IDataFieldMappingRows[] = [];

    selectedItem: IDropDownMenuItem;
    datafieldsDropdownFooterArray: IDropDownMenuItem[] = [];
    datafieldsDropdownRowFooterArray: IDropDownMenuItem[] = [];

    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    @ViewChildren(TextboxComponent) private textboxes: QueryList<TextboxComponent>;
    @ViewChild('newRowDropdown') private newRowDropdown: DropDownComponent;

    get process(): sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process {
        return this.configuration.getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>();
    }

    constructor(protected resourceService: ResourceService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        private popupService: PopupService,
        private processService: ProcessService,
        private dataTypeResolverService: DataTypeResolverService) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnInit(): void {
        // We disable the undo redo service when adding the mapping, so that it will not be undone when undoing the drop.
        // Undoing the mapping add will remove the error, and then there will be nothing to reevaluate when you redo
        this.undoRedoService.forceDisable();
        if (this.configuration.mappings.length === 0) {
            this.configuration.mappings.push({
                dataFieldReference: '',
                value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    smartFields: []
                })
            });
            this.selectedMappingRowIndex = 0;
            this.rowDropdownFocus = false;
            this.rowValueControlFocus = false;
        }
        this.undoRedoService.resetForceDisable();

        // add separator and 'create new' item
        this.datafieldsDropdownFooterArray.push(DropDownComponent.separator);

        //dropdown menu popup when click (+) footer icon
        this.datafieldsDropdownFooterArray.push(
            {
                key: 'createNew',
                icon: 'icon-set-steps ic-dataevent',
                title: this.resourceService.getString('workflow_dataFields_createNew'),
                ignoreUpdateSelection: true,
                hideCheckbox: true,
                onSelected: this.showNewDataFieldPopup.bind(this)
            }
        );

        this.datafieldsDropdownRowFooterArray.push(DropDownComponent.separator);

        //dropdown menu for data field (left cell) of data field row
        this.datafieldsDropdownRowFooterArray.push(
            {
                key: 'createNew',
                icon: 'icon-set-steps ic-dataevent',
                title: this.resourceService.getString('workflow_dataFields_createNew'),
                ignoreUpdateSelection: true,
                onSelected: this.showNewDataFieldRowPopup.bind(this)
            }
        );

        if (!this.selectedProperties) {
            this.selectedProperties = [];
        }

        for (let i = 0; i < this.configuration.mappings.length; i++) {
            this.setValidationInfoAndValidate(this.configuration.mappings[i], i.toString());
        }
    }

    private setValidationInfoAndValidate(mapping: IModelObject, uniqueIdentifier: string): void {
        const bindingName = 'datafieldDropdown' + uniqueIdentifier;

        const validationData = super.createValidationData(
            'resource_service_empty_field',
            ValidationTypes.Required,
            mapping,
            'dataFieldReference',
            ErrorSeverity.Error,
            [
                'resource_service_field_name_data_field',
                'configpanel_tabs_task_tooltips_title_setdatafields'
            ]
        );

        //Listen for change of datafield reference, that will trigger validation
        const propertyObjectsToListenTo = [{
            object: mapping,
            properties: ['dataFieldReference', 'value']
        }];

        super.bindValidationToFieldAndListen(bindingName, validationData, propertyObjectsToListenTo);

        super.validate(bindingName);
    }

    getDataFields(): IDropDownMenuItem[] {
        const dataFields: IDropDownMenuItem[] = [];

        // populate all process-level data fields
        for (let i = 0; i < this.process.configuration.dataFields.length; i++) {
            if (!this.process.configuration.dataFields[i].systemField) {
                const reference: string = this.process.configuration.dataFields[i].getReference();

                //if it is already mapped, it should be selected state (isSelected = true)
                dataFields.push({
                    icon: this.getIconFromType(this.process.configuration.dataFields[i].type.toString()),
                    key: reference,
                    title: this.process.configuration.dataFields[i].title,
                    onSelected: this.onSelectedDdItem.bind(this),
                    isSelected: !this.showDataFieldInDropdown(reference)
                });
            }
        }

        return dataFields;
    }

    getRowDataFields(selectedMappingsIndex: number): IDropDownMenuItem[] {
        const dataFields: IDropDownMenuItem[] = [];

        // populate all process-level data fields
        const process: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process = this.process;
        const selectedDataFieldReference: string = this.configuration.mappings[selectedMappingsIndex]?.dataFieldReference;

        for (let i = 0; i < process.configuration.dataFields.length; i++) {
            if (!process.configuration.dataFields[i].systemField) {
                const reference: string = process.configuration.dataFields[i].getReference();
                if (this.showDataFieldInDropdown(reference, selectedMappingsIndex)) {
                    dataFields.push({
                        icon: this.getIconFromType(process.configuration.dataFields[i].type.toString()),
                        key: reference,
                        title: process.configuration.dataFields[i].title,
                        onSelected: this.onSelectedSwapItem.bind(this),
                        isSelected: selectedDataFieldReference === reference
                    });
                }
            }
        }

        return dataFields;
    }

    private getIconFromType(typeOfIcon: string): string {
        const iconType: string = this.dataTypeResolverService.getSpriteClassFromDataType(typeOfIcon);
        return `icon-set-datatypes ic-${iconType}`;
    }

    getIconByRef(ref: string): string {
        const process: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process = this.process;
        for (let i = 0; i < process.configuration.dataFields.length; i++) {
            const reference: string = process.configuration.dataFields[i].getReference();

            if (reference === ref) {
                return this.getIconFromType(process.configuration.dataFields[i].type.toString());
            }
        }

        return '';
    }

    private onSelectedDdItem(item: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--setDataFieldController: add data field mapping--');

        if (item.isSelected === false) {
            if (this.configuration.mappings && this.configuration.mappings.length === 1 &&
                (!this.configuration.mappings[0].dataFieldReference || this.configuration.mappings[0].dataFieldReference === '')
                && !this.configuration.mappings[0].title) {
                this.configuration.mappings[0].dataFieldReference = item.key;
            }
            else {
                this.configuration.mappings.push({
                    dataFieldReference: item.key,
                    value: {
                        smartFields: []
                    }
                });
            }
        }
        else {
            for (let i = 0; i < this.configuration.mappings.length; i++) {
                if (this.configuration.mappings[i].dataFieldReference === item.key) {
                    this.configuration.mappings[i].removeFromModelArray();
                    break;
                }
            }
        }

        //Select Last item
        this.selectedMappingRowIndex = this.configuration.mappings.length - 1;

        this.focusRowTextbox();
    }

    private onSelectedSwapItem(item: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--setDataFieldController: swap data field mapping--');

        this.configuration.mappings[this.selectedMappingRowIndex].dataFieldReference = item.key;

        this.focusRowTextbox();
    }

    private showDataFieldInDropdown(dataFieldReference: string, rowIndex: number = -1): boolean {
        const mappings: ISetDataFieldMapping[] =
            (<ISetDataFieldsConfiguration>this.configuration).mappings;
        for (let i = 0; i < mappings.length; i++) {
            //Exclude datafield if already in mappings but, don't exclude the selected datafield for current mapper row.
            if (mappings[i].dataFieldReference === dataFieldReference && i !== rowIndex) return false;
        }
        return true;
    }

    private showNewDataFieldPopup(mapping: ISetDataFieldMapping): void {

        this.processService.root.configuration.dataFields.push(sourceCode.k2Designer.objectModel.workflow.process.k2Process.DataField.createInstance({
            title: this.getNewDataFieldTitle(),
            type: DataFieldTypes.string,
            isEmptyValue: true
        }));

        this.newRowDropdown.collapseMenu();

        this.popupService.showDataFieldPopup(
            this.process.configuration,
            this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1],
            this.onNewDataFieldCreated.bind(this, mapping, this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1]),
            this.onCancel.bind(this, this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1]),
            this
        );
    }

    private showNewDataFieldRowPopup(mapping: ISetDataFieldMapping): void {

        this.processService.root.configuration.dataFields.push(sourceCode.k2Designer.objectModel.workflow.process.k2Process.DataField.createInstance({
            title: this.getNewDataFieldTitle(),
            type: DataFieldTypes.string,
            isEmptyValue: true
        }));

        this.popupService.showDataFieldPopup(
            this.process.configuration,
            this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1],
            this.onNewDataFieldRowCreated.bind(this, mapping, this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1]),
            this.onCancel.bind(this, this.processService.root.configuration.dataFields[this.processService.root.configuration.dataFields.length - 1]),
            this
        );
    }

    private onCancel(dataField: IDataField) {
        dataField.removeFromModelArray();
    }

    private onNewDataFieldCreated(mapping: ISetDataFieldMapping, item: IDataField): void {
        this.undoRedoService.addUndoMarker('--setDataFieldController: new datafield created--');

        if (!!mapping) {
            const reference = item.getReference();

            (<ISetDataFieldsConfiguration>this.configuration).mappings.push({
                dataFieldReference: reference,
                value: {
                    smartFields: []
                }
            });

            //Select Last item
            this.selectedMappingRowIndex = this.configuration.mappings.length - 1;

            this.setValidationInfoAndValidate(this.configuration.mappings[this.selectedMappingRowIndex], this.selectedMappingRowIndex.toString() + reference);

            this.focusRowTextbox();
        }
    }

    private onNewDataFieldRowCreated(mapping: ISetDataFieldMapping, item: IDataField): void {
        this.undoRedoService.addUndoMarker('--setDataFieldController: new data field row created--');

        (<ISetDataFieldsConfiguration>this.configuration).mappings[this.selectedMappingRowIndex].dataFieldReference = item.getReference();

        this.focusRowTextbox();
    }

    /**
     * After a new field is chosen or create, focus is set on the current row textbox
     */
    private focusRowTextbox() {
        setTimeout(() => {
            this.textboxes.last?.doFocus();
        }, 100);
    };

    /**
     * Methods to delete a mapping row
     */
    mappingRowSelected(index: number): void {
        this.selectedRowItem = this.selectedProperties[index];
        this.rowDropdownFocus = false;
        this.rowValueControlFocus = false;
        this.selectedMappingRowIndex = index;
    }

    deleteDataFieldMappingRow() {
        this.undoRedoService.addUndoMarker('--setDataFieldController: delete data field mapping--');

        this.configuration.mappings[this.selectedMappingRowIndex].removeFromModelArray();
        this.selectedMappingRowIndex = -1;
    }

    private getNewDataFieldTitle(): string {
        const defaultName: string = this.resourceService.getString('workflow_dataFields_defaultFieldName');

        return findUniqueName(this.processService.root.configuration.dataFields, 'title', defaultName);
    }

}
