import { HttpClientModule } from '@angular/common/http';
import { ApplicationRef, NgModule } from '@angular/core';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { setAngularJSGlobal, UpgradeModule } from '@angular/upgrade/static';
import { environment } from 'src/environments/environment';
import { downgradeComponents } from './ajs.downgrade';
import {
    rootElementServiceProvider,
    rootScopeServiceProvider,
    sceServiceProvider,
    scopeServiceProvider,
    stateServiceProvider
} from './ajs.upgrade';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { TextboxComponent } from './components/common/text-box/text-box.component';
import { ComponentsModule } from './components/components.module';
import { ErrorBarComponent } from './components/error-bar/error-bar.component';
import { UserDialogComponent } from './components/popup/user-dialog/user-dialog.component';
import { RibbonComponent } from './components/ribbon/ribbon.component';
import { GroupBooleanOperatorExpressionComponent } from './components/rules-designer/expressions/group-boolean-operator-expression/group-boolean-operator-expression.component';
import { ThisResponseExpressionComponent } from './components/rules-designer/expressions/this-response-expression/this-response-expression.component';
import { ToolboxComponent } from './components/toolbox/toolbox.component';
import { CreateSmartFieldsComponent } from './components/wizard-popup/smartforms/create-smart-fields/create-smart-fields.component';
import { DirectivesModule } from './directives/directives.module';
import { LegacyResourceServiceFactory } from './legacy-service-factories/resource-service-factory';
import { PipesModule } from './pipes/pipes.module';
import { CanvasServiceFactory } from './services/canvas/canvas-service-factory';
import { ClipboardServiceFactory } from './services/clipboard/clipboard-service-factory';
import { CommandServiceFactory } from './services/command/command-service-factory';
import { ConfigPanelServiceFactory } from './services/config-panel/config-panel-service-factory';
import { ContextProviderFactory } from './services/context-provider/context-provider-service-factory';
import { DataServiceFactory } from './services/data/data-service-factory';
import { DesignerServiceFactory } from './services/designer/designer-service-factory';
import { DropDownService } from './services/drop-down/drop-down.service';
import { EnvironmentServiceFactory } from './services/environment/environment-service-factory';
import { EventNotifierService } from './services/event-notifier/event-notifier.service';
import { HelpServiceFactory } from './services/help/help-service-factory';
import { IdentityService } from './services/identity/identity.service';
import { ItemProviderServiceFactory } from './services/item-provider/item-provider-service-factory';
import { NotificationServiceFactory } from './services/notification/notification-service-factory';
import { ObjectModelServiceFactory } from './services/object-model/object-model-service-factory';
import { PluginServiceFactory } from './services/plugin/plugin-service-factory';
import { PluginItemReferencesService } from './services/plugins/item-references/item-references.service';
import { SmartFormsIntegrationService } from './services/plugins/smart-forms-integration/smart-forms-integration.service';
import { ProgressBarServiceFactory } from './services/progress-bar/progress-bar-service-factory';
import { RecentsServiceFactory } from './services/recents/recents-service-factory';
import { RedirectServiceFactory } from './services/redirect/redirect-service-factory';
import { ResourceService } from './services/resource/resource.service';
import { RibbonBarServiceFactory } from './services/ribbon-bar/ribbon-bar-service-factory';
import { RulePluginService } from './services/ruleplugin/rule-plugin-service';
import { SelectionServiceFactory } from './services/selection/selection-service-factory';
import { SmartFieldComposerServiceFactory } from './services/smart-field-composer/smart-field-composer-service-factory';
import { SmartFieldPluginServiceFactory } from './services/smart-field-plugin/smart-field-plugin-service-factory';
import { ToolBoxServiceFactory } from './services/tool-box/tool-box-service-factory';
import { UndoRedoServiceFactory } from './services/undo-redo/undo-redo-service-factory';
import { UserOptionsServiceFactory } from './services/user-options/user-options-service-factory';
import { ValidationServiceFactory } from './services/validation/validation-service-factory';
import { LegacyRulePluginServiceFactory } from './legacy-service-factories/rule-plugin-service-factory';
import { LegacyDragDropServiceFactory } from './legacy-service-factories/drag-drop-service-factory';
import { DragDropService } from './services/drag-drop/drag-drop.service';
import { FilterPanelServiceFactory } from './services/filter-panel/filter-panel-service-factory';
import { LegacyPopupServiceFactory } from './services/popup/legacy-popup-service-factory';
import { PopupService } from './services/popup/popup.service';
import { ProcessService } from './services/process/process.service';
import { WorkflowService } from './services/workflow/workflow.service';
import { ConfigurationService } from './services/configuration/configuration-service';
import { EVENT_MANAGER_PLUGINS } from '@angular/platform-browser';
import { ZonelessEventPluginService } from './custom-providers/zoneless-event-plugin-service';

declare global {
    interface Window {
        angular: any;
    }
}

@NgModule({
    declarations: [AppComponent],
    exports: [],
    imports: [
        AppRoutingModule,
        ComponentsModule,
        DirectivesModule,
        BrowserAnimationsModule,
        PipesModule,
        HttpClientModule,
    ],
    // Put global services here.
    providers: [
        {
            provide: EVENT_MANAGER_PLUGINS,
            useClass: ZonelessEventPluginService,
            multi: true
        },
        scopeServiceProvider,
        rootScopeServiceProvider,
        rootElementServiceProvider,
        sceServiceProvider,
        stateServiceProvider,
        LegacyRulePluginServiceFactory,
        RulePluginService,
        EnvironmentServiceFactory,
        ValidationServiceFactory,
        LegacyResourceServiceFactory,
        LegacyDragDropServiceFactory,
        DragDropService,
        ValidationServiceFactory,
        DataServiceFactory,
        LegacyPopupServiceFactory,
        PopupService,
        RecentsServiceFactory,
        IdentityService,
        DropDownService,
        EventNotifierService,
        CanvasServiceFactory,
        ConfigPanelServiceFactory,
        NotificationServiceFactory,
        ProcessService,
        ObjectModelServiceFactory,
        UndoRedoServiceFactory,
        SmartFieldPluginServiceFactory,
        SmartFieldComposerServiceFactory,
        ClipboardServiceFactory,
        ContextProviderFactory,
        DesignerServiceFactory,
        ItemProviderServiceFactory,
        PluginServiceFactory,
        ProgressBarServiceFactory,
        RedirectServiceFactory,
        RibbonBarServiceFactory,
        ToolBoxServiceFactory,
        UserOptionsServiceFactory,
        SelectionServiceFactory,
        CommandServiceFactory,
        HelpServiceFactory,
        SmartFormsIntegrationService,
        ResourceService,
        PluginItemReferencesService,
        FilterPanelServiceFactory,
        WorkflowService,
        ConfigurationService,
    ],
    bootstrap: [],
    schemas: [],
    //This should contain the components that are referenced by angularjs templates
    entryComponents: [
        ToolboxComponent,
        RibbonComponent,
        GroupBooleanOperatorExpressionComponent,
        ThisResponseExpressionComponent,
        UserDialogComponent,
        ErrorBarComponent,
        TextboxComponent,
        CreateSmartFieldsComponent,
    ]
})
export class AppModule {
    constructor(private upgrade: UpgradeModule) { }

    ngDoBootstrap(app: ApplicationRef) {
        setAngularJSGlobal(window.angular);
        if (environment.devHarness) {
            app.bootstrap(AppComponent);
        } else {
            downgradeComponents();

            const appName = document.location.pathname.match(/(viewflow.aspx)/i)
                ? 'ViewflowApp'
                : 'K2DesignerApp';

            this.upgrade.bootstrap(document.body, [appName], {
                strictDi: true
            });
        }
    }
}
