import { ThrowStmt } from '@angular/compiler';
import { OnDestroy } from '@angular/core';
import { AfterContentInit, Component, ContentChildren, EventEmitter, Input, OnInit, Output, QueryList } from '@angular/core';
import { last, Subscription } from 'rxjs';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IButtonConfig } from 'src/types/config/i-button-config';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';
import { PageComponent } from '../page/page.component';

@Component({
    selector: 'k2-wizard-popup-host',
    templateUrl: './wizard-popup-host.component.html',
    styleUrls: ['./wizard-popup-host.component.less']
})
export class WizardPopupHostComponent extends BaseResourceComponent implements OnInit, OnDestroy, AfterContentInit {

    @ContentChildren(PageComponent) pages: QueryList<PageComponent>;

    /** Heading of the popup window */
    @Input() heading: string;

    /** whether or not to display a heading icon */
    @Input() headingIcon: string;

    /** When this is emitted, the caller should close the dialog by setting the ngIf to false. */
    @Output() closeDialogReason: EventEmitter<PopupState> = new EventEmitter<PopupState>();

    /** Custom class to style the popup body. */
    @Input() customClass: string;

    @Input() height = 638;
    @Input() width = 530;
    @Input() bodyHeight = 530;
    @Input() bodyWidth = 530;

    @Input() isPopupWizardActive = false;
    @Output() isPopupWizardActiveChange = new EventEmitter<boolean>();

    /** Hide the current and number of page indicators? */
    @Input() isPageIndicatorsHidden = false;

    /** Whether or not to display the info popup.
     * TODO: (LATER) NOT CURRENTLY IMPLEMENTED
     * */
    @Input() isInfoPopupHidden = true;

    private pagesGenerator: Generator;
    private numberOfPages: number;
    private currentIndex: number;
    private defaultButtonConfig: IButtonConfig = {
        buttonText: null,
        classOverride: null,
        isDisabled: false,
        isVisible: false
    };
    private pageSubs: Subscription;

    currentPage: PageComponent;

    buttonConfig: IPopupButtonsConfig = {
        previous: {},
        next: {},
        finish: {}
    };

    popupState: typeof PopupState = PopupState;
    currentPopupState: PopupState = PopupState.Normal;

    constructor(protected resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
    }

    ngOnDestroy() {
        this.pageSubs?.unsubscribe();
    }

    ngAfterContentInit(): void {
        this.numberOfPages = this.pages.length;

        this.pagesGenerator = this.getPageGenerator(this.pages);

        this.pages.changes.subscribe((x) => {
            // Recalculate the index, in case the number of pages or their positions changed
            this.currentIndex = this
                .pages
                .toArray()
                .indexOf(this.currentPage);

            this.numberOfPages = this.pages.length;
            this.configureButtons();
            this.configurePageIndicators();
            this.redoSubscriptions(); // new pages need to be subscribed to and old unsubsribed
        });

        this.subscribeToPages();

        // setTimeout to combat ExpressionChangedAfterItHasBeenCheckedError errors.
        // We need to wait until afterContentInit, because that is when the pages are populated,
        // but if we make changes to the view after this, then we get the ExpressionChangedAfterItHasBeenCheckedError error. Unless we wait longer...
        setTimeout(() => {
            this.currentPage = this.pagesGenerator.next().value;
        });
    }

    private redoSubscriptions() {
        this.pageSubs?.unsubscribe();
        this.subscribeToPages();
    }

    private subscribeToPages() {
        this.pageSubs = new Subscription();
        this.pages.forEach(p => {
            this.pageSubs.add(p.onPageConfigChanged.subscribe(this.configureButtons));
            this.pageSubs.add(p.onPageStateChanged.subscribe(this.updateState));
        });
    }

    updateState = (state?: PopupState) => {
        this.currentPopupState = state ?? this.currentPage.state;

        if (this.currentPopupState === PopupState.Cancelled) this.closeDialogWindow();
    };

    next() {
        const generatorData = this.pagesGenerator.next();
        this.currentPage = generatorData.value;
    }

    previous() {
        if (this.currentIndex === 0) {
            // If the previous button is visible on the start page, it will always be used as close.
            this.closeDialogWindow();
        } else {
            const generatorData = this.pagesGenerator.next(true);

            this.currentPage = generatorData.value;
        }
    }

    configurePageIndicators() {
        const lastPage = this.pages.get(this.numberOfPages - 1);

        if (lastPage) {
            lastPage.isHiddenPage = lastPage.isHiddenPage ?? true;
        }

        // For the last page, default to hidden if not explicitly false.
        if (this.currentIndex === this.numberOfPages - 1) {
            this.isPageIndicatorsHidden = this.currentPage.isAllPageIndicatorsHidden !== false;
        } else { // For the rest, default to false if not true
            this.isPageIndicatorsHidden = this.currentPage.isAllPageIndicatorsHidden === true;
        }

    }

    private *getPageGenerator(pages: QueryList<PageComponent>) {
        for (this.currentIndex = 0; this.currentIndex < pages.length; this.currentIndex++) {

            this.currentPage = pages.get(this.currentIndex);

            this.currentPage.beforePageDisplayed();
            this.configureButtons();
            this.updateState();
            this.configurePageIndicators();

            const goBack: boolean = yield this.currentPage ?? false;

            // If the pages changed since the last page action,
            // the current index would have been updated in the pages QueryList change event.
            // See the ngAfterContentInit for that code.

            if (goBack) {
                this.currentIndex -= 2;
                this.currentPage.beforeNavigatePrevious();
            } else {
                this.currentPage.beforeNavigateNext();
            }
        }
    }

    private configureButtons = () => {

        this.buttonConfig.next = { ...this.buttonConfig.next, ...this.defaultButtonConfig };
        this.buttonConfig.previous = { ...this.buttonConfig.previous, ...this.defaultButtonConfig };
        this.buttonConfig.finish = { ...this.buttonConfig.finish, ...this.defaultButtonConfig };

        // First cofnigure the default, then override with the specific page config
        switch (this.currentIndex) {
            case 0:
                this.buttonConfig.previous.isVisible = false;
                this.buttonConfig.next.isVisible = true;
                this.buttonConfig.finish.isVisible = false;
                break;
            case this.numberOfPages - 1:
                this.buttonConfig.previous.isVisible = false;
                this.buttonConfig.next.isVisible = false;
                this.buttonConfig.finish.isVisible = true;
                break;
            default:
                this.buttonConfig.previous.isVisible = true;
                this.buttonConfig.next.isVisible = true;
                this.buttonConfig.finish.isVisible = false;
        }

        this.buttonConfig.next = { ...this.buttonConfig.next, ...this.currentPage.buttonConfig?.next };
        this.buttonConfig.previous = { ...this.buttonConfig.previous, ...this.currentPage.buttonConfig?.previous };
        this.buttonConfig.finish = { ...this.buttonConfig.finish, ...this.currentPage.buttonConfig?.finish };
    };

    closeDialogWindow() {
        this.closeDialogReason.emit(PopupState.Cancelled);
        this.currentPage?.beforeNavigatePrevious();
        this.close();
    }

    finish() {
        this.closeDialogReason.emit(PopupState.Completed);
        this.close();
    }

    private close() {
        this.isPopupWizardActive = false;
        this.isPopupWizardActiveChange.emit(false);
    }

}
