import { AfterViewInit, Component, EventEmitter, Inject, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { ISmartFormItemReference } from '../../../types/models/smart-forms/i-smart-form-item-reference';
import { IProcessService } from '../../../types/services/process/i-process-service';
import { IListItemIconData } from '../../../types/uiComponents/list-item/i-list-item-icon-data';
import { ProcessService } from '../../services/process/process.service';
import { ResourceService } from '../../services/resource/resource.service';
import { listItemIcons } from '../../shared/constants/list-item';
import { findUniqueName } from '../../shared/utils/name-helper';
import { BaseResourceComponent } from '../base-resource/base-resource.component';

@Component({
    selector: 'k2-item-references',
    templateUrl: './item-references.component.html'
})
export class ItemReferencesComponent extends BaseResourceComponent implements OnInit, OnChanges {

    @Input() heading: string;
    @Input() subHeading: string;
    @Input() createHeading: string;
    @Input() illustration: string;
    @Input() noSuggestionText: string;
    @Input() noSuggestionIcon: string;
    @Input() learnMoreLink: string;
    @Input() isLoading: boolean;
    @Input() referenceToCreateCount: number;
    @Input() learnMoreId: number;
    @Input() sourceItemReferences: ISmartFormItemReference[] = [];
    @Input() newItemReferences: ISmartFormItemReference[] = [];
    @Input() existingItemReferences: ISmartFormItemReference[] = [];

    @Output() itemReferenceUpdated: EventEmitter<ISmartFormItemReference[]> = new EventEmitter<ISmartFormItemReference[]>();
    @Output() componentLoaded: EventEmitter<void> = new EventEmitter<void>();
    @Output() updateCreateCount: EventEmitter<number> = new EventEmitter<number>();

    loadingText: string;
    existingReferenceText: string;
    formReferenceText: string;
    formSourcesHeadingText: string;
    formSourcesSubHeadingText: string;
    newRefListLeftIcon: IListItemIconData = listItemIcons.itemRefIcon;
    startProcRightIcon: IListItemIconData = listItemIcons.startProcessIcon;
    /**
     * Property to determine if existing references is expanded or not.
     */
    isExistingReferencesExpanded = false;

    constructor(private processService: ProcessService,
        protected resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.setResources();
        this.componentLoaded.emit();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.sourceItemReferences) {
            this.updateReferencesFromForm();
        }
    }

    /**
     * Remove or restore item reference (disabe/enable)
     *
     * @param itemReference
     */
    toggleItemReference(itemReference: ISmartFormItemReference): void {

        itemReference.disabled = !itemReference.disabled;

        this.checkIfItemReferenceNeedsToBeCreated();

    }

    /**
     * Ensures item reference name is unique.
     */
    setUniqueItemReferenceName(selectedItemReference: ISmartFormItemReference): void {

        selectedItemReference.displayName = findUniqueName(this.processService.root.configuration.itemReferences,
            'title', selectedItemReference.displayName, null, false);
    }

    /**
     * Restore a disabled item reference
     *
     * @param itemReference
     */
    restoreItemReference(itemReference: ISmartFormItemReference): void {

        //Set item reference state to enabled.
        itemReference.disabled = false;

        this.checkIfItemReferenceNeedsToBeCreated();
    }

    private setResources() {
        this.existingReferenceText = this.getString('wizard_workflow_integration_existing_references');
        this.formReferenceText = this.getString('wizard_wfi_create_form_references');
        this.formSourcesHeadingText = this.getString('wizard_wfi_create_form_no_sources_heading');
        this.formSourcesSubHeadingText = this.getString('wizard_wfi_create_form_no_sources_sub_heading');
        this.loadingText = this.getString('popup_state_loading');
    }

    /**
     * Method to check for any new item reference to be created.
     */
    private checkIfItemReferenceNeedsToBeCreated(): void {

        this.referenceToCreateCount = this.newItemReferences.filter(i => !i.disabled).length;

        this.onItemReferenceUpdated();
    }

    /**
     * Method to call when an item references was updated in the directive.
     */
    private onItemReferenceUpdated(): void {

        this.itemReferenceUpdated.emit(this.newItemReferences);

        this.updateCreateCount.emit(this.referenceToCreateCount);
    }

    /**
     * Method to match and assign item references to new & existing collections according to item references found in the process vs form sources.
     * Ensures unique names for references
     */
    private updateReferencesFromForm(): void {
        this.newItemReferences = [];

        if (this.sourceItemReferences.length === 0) {
            return;
        }

        //Get the primary item reference from the source item references (there can be only 1 primary)
        const primarySourceItemRef = this.sourceItemReferences.find(i => i.primary);
        if (!primarySourceItemRef) {
            console.error('No primary item reference found in source');
            return;
        }

        const process = this.processService.root as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessExtended;

        //Build up new and existing itemreferences collections
        this.sourceItemReferences.forEach((sourceItemRef: ISmartFormItemReference) => {
            //First try to find by name
            let existingRef = process
                .findWorkflowItemReferenceByNameAndStartMode(sourceItemRef.name, sourceItemRef.startMode);
            //Then try to get item reference on process by smo name and method name, this is when the field have been renamed or already exists
            if (!existingRef) {
                existingRef = process
                    .findWorkflowItemReference(sourceItemRef.smartObjectName,
                        sourceItemRef.smartObjectMethodName,
                        //have to cast to unknown because findWorkflowItemReference method takes ProcessService instead of IProcessService
                        this.processService as any,
                        sourceItemRef.primary,
                        sourceItemRef.startMode,
                        sourceItemRef.primaryReference);
            }

            if (existingRef) {
                if (this.existingItemReferences.indexOf(sourceItemRef) === -1) {

                    const matchingExistingReferences = this.existingItemReferences
                        .filter(x => {
                            for (const [k, v] of Object.entries(x)) {
                                // Only check strings, but remove entries which do not match every field
                                if (typeof sourceItemRef[k] === 'string' && sourceItemRef[k] !== v) {
                                    return false;
                                }
                            }

                            return true;
                        });

                    if (matchingExistingReferences.length === 0) {
                        sourceItemRef.displayName = existingRef.title;
                        this.existingItemReferences.push(sourceItemRef);
                    }
                }
            } else {
                //New
                const uniqueItemReferenceName = findUniqueName(this.processService.root.configuration.itemReferences,
                    'systemName', sourceItemRef.name, null, false, '_');

                const uniqueItemReferenceTitle = findUniqueName(this.processService.root.configuration.itemReferences,
                    'title',
                    sourceItemRef.displayName,
                    null,
                    false
                );

                sourceItemRef.name = uniqueItemReferenceName;
                sourceItemRef.displayName = uniqueItemReferenceTitle;

                this.newItemReferences.push(sourceItemRef);
            }
        });

        //Get updated primary name:
        const primaryItemReferenceName = primarySourceItemRef.name;

        //Update all non-primary references to have a correct reference to the primary
        this.sourceItemReferences.filter(i => !i.primary).forEach((nonPrimaryRef: ISmartFormItemReference) => {
            nonPrimaryRef.primaryReference = primaryItemReferenceName;
        });

        this.checkIfItemReferenceNeedsToBeCreated();
    }
}
