import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';
import { IStates } from '../../../../../../types/models/rules/i-states';
import { States } from '../../../../../shared/control-helpers/states';

@Component({
    selector: 'k2-choose-control-default-label',
    templateUrl: './choose-control-default-label.component.html',
    styleUrls: ['./choose-control-default-label.component.less']
})
export class ChooseControlDefaultLabelComponent implements OnInit {
    @Input() id: string;
    @Input() displayText: string;
    @Input() isChecked: boolean;
    @Input() isDynamicMode: boolean;
    @Input() isDisabled: boolean;
    @Input() isToggled: boolean;
    @Input() modelValue: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpression;
    @Input() isEnabledValue: boolean;
    @Input() customLabelClass: string;
    @Input() type: ChooseControlTypes;
    //Control states for styling
    @Input() states: IStates = new States();

    @Output() onModeToggled = new EventEmitter<boolean>();
    @Output() onBlurred = new EventEmitter<any>();
    @Output() onClick = new EventEmitter<void>();

    widgetClass = '';
    nonWidgetClass = '';
    hovered = false;
    types: typeof ChooseControlTypes = ChooseControlTypes;

    constructor() {
    }

    ngOnInit(): void {
    }

    /**
     * Label button click event (dynamic  mode)
     * */
    onToggle(): void {
        this.onModeToggled.emit(this.isToggled);
    }

    onBlur(): void {
        if (this.isEnabledValue) {
            this.onBlurred.emit();
        }
    }

    onClicked(): void {
        this.onClick.emit();
    }

    /**
     * TODO: dynamic mode not yet fully implemented for upgraded component
     * No components currently use this.
     **/
}
