import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output, ViewChild, OnChanges, SimpleChanges } from '@angular/core';
import { DomSanitizer, SafeHtml } from '@angular/platform-browser';
import { DragDropService } from '../../../services/drag-drop/drag-drop.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { keyCodes } from '../../../shared/constants/general';

@Component({
    selector: 'k2-click-to-edit',
    templateUrl: './click-to-edit.component.html',
    styleUrls: ['./click-to-edit.component.less']
})
export class ClickToEditComponent implements OnChanges {
    @Input() public highlight: string;
    @Input() public model: string;
    @Input() public onEditFinished: Function;
    @Input() public edit: boolean;

    @Output() public modelChange = new EventEmitter<string>();
    @Output() public cancelled = new EventEmitter<void>();
    @Output() public editChange = new EventEmitter<boolean>();

    @ViewChild('input') input: ElementRef<HTMLInputElement>;

    tempValue: string;

    constructor(protected sanitizer: DomSanitizer,
        private _dragDropService: DragDropService,
        @Inject(UndoRedoServiceToken) private _undoRedoService) { }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.highlight) {
            this.onHighlight();
        }
        if (changes.edit?.currentValue) { // if it is in the changes list and true
            this.tempValue = this.model; // set the temp value
            this._dragDropService.disable();
            setTimeout(() => {
                this.input.nativeElement.select(); // select entire text
            });
        }
    }

    /**
     * highlights the string to display, if highlight search string has been supplied
     */
    public onHighlight(): string | SafeHtml {
        const val = this.model;
        if (!this.highlight) return this.model;
        const unsafe: string = val.replace(new RegExp(this.highlight, 'gi'), '<span class=\'search-highlight\'>$&</span>');
        return this.sanitizer.bypassSecurityTrustHtml(unsafe); // angular dom sanitizer would remove/convert all the html unless it is bypassed.
    }

    public onClicked(): void {
        this.tempValue = this.model;
        this.edit = true;
        this.editChange.emit(true);
        this._dragDropService.disable();

        this.input?.nativeElement?.focus();
        setTimeout(() => {
            this.input?.nativeElement?.focus();
        });
    }

    public persist(): void {
        this.edit = false;
        this.editChange.emit(false);
        this._dragDropService.enable();
        if (typeof this.onEditFinished === 'function') {
            const old = this.model;
            this.model = this.tempValue;

            const ok = this.onEditFinished(old, this.model);
            if (!ok) {
                this._undoRedoService.undo();
                this.model = old;
            }
            this._undoRedoService.addUndoMarker();
            this.modelChange.emit(this.model);
        }
        else {
            this._undoRedoService.addUndoMarker();
            this.model = this.tempValue;
            this.modelChange.emit(this.model);
        }
    }

    onKeyDown($event: KeyboardEvent): boolean {
        switch ($event.keyCode) {
            case keyCodes.ESCAPE:
                $event.stopPropagation();
                this.tempValue = this.model;
                this.cancelled.emit();
                return false;
            case keyCodes.TAB:
            case keyCodes.ENTER: this.persist();
                break;
        }

        return true;
    }
}
