import { Component, Inject, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { ISmartObjectMethodField } from '../../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-method-field';
import { ISelectedOrderByProperty } from '../../../../types/models/list-options/i-selected-order-by-property';
import { ISmartObjectListOptions } from '../../../../types/models/list-options/i-smartobject-list-options';
import { IUIValueCollection } from '../../../../types/models/ui-values/i-ui-values';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { ChooseControlTypes } from '../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { ExternalReferenceState } from '../../../shared/enums/external-references/external-references';
import { ListFilter } from '../../../shared/enums/list/filter';
import { ListOrder } from '../../../shared/enums/list/order';
import { ExternalReferenceUtilities } from '../../../shared/utils/external-references';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-list-options',
    templateUrl: './list-options.component.html',
    styleUrls: ['./list-options.component.less']
})
export class ListOptionsComponent extends BaseResourceComponent implements OnInit, OnChanges {
    @Input() properties: IDropDownMenuItem[];
    @Input() configuredListOptions: ISmartObjectListOptions;
    @Input() savedUIValues: sourceCode.k2Designer.objectModel.core.model.IModelDictionary<IUIValueCollection>;
    @Input() externalDefinitionParent: ISmartObjectMethodField;

    resources: { [key: string]: string } = {};
    selectedFilterItem: IDropDownMenuItem;
    isOpen: boolean;
    filterItems: IDropDownMenuItem[];
    selectedProperty: ISelectedOrderByProperty;
    listOrder = ListOrder;

    dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;

    private filterItemTitles: { [key: string]: string } = {
        all: this.getString('list_option_all'),
        first: this.getString('list_option_first'),
        last: this.getString('list_option_last'),
        index: this.getString('list_option_index')
    };
    private textTimeOut = null;
    private textSetDelay = 200;

    constructor(resourceService: ResourceService,
        private processService: ProcessService,
        @Inject(ValidationServiceToken) private validationService: IValidationService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.resources = {
            listOptionsHeader: this.getString('list_option_header'),
            listOptionsSubheader: this.getString('list_option_subheader'),
            listOptionsOrderedBy: this.getString('list_option_ordered_by'),
            listOptionsAscending: this.getString('list_option_ascending'),
            listOptionsDescending: this.getString('list_option_descending'),
        };

        if (!this.selectedFilterItem) {
            const filterItemKey: string = this.configuredListOptions?.filterOption ? this.configuredListOptions.filterOption : 'all';

            this.selectedFilterItem = {
                key: filterItemKey,
                title: this.filterItemTitles[filterItemKey],
                isSelected: true
            };
        }

        if (this.savedUIValues) {
            if (this.savedUIValues.isOpen) {
                this.isOpen = this.savedUIValues.isOpen.smartFields[0].text === 'true';
            } else {
                this.isOpen = false;
                this.savedUIValues.add('isOpen', sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    smartFields: [
                        sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                            text: this.isOpen?.toString()
                        })
                    ]
                }));
            }
        }

        this.filterItems = [
            {
                key: ListFilter.All,
                title: this.getString('list_option_all'),
                isSelected: this.selectedFilterItem?.key === ListFilter.All
            },
            {
                key: ListFilter.First,
                title: this.getString('list_option_first'),
                isSelected: this.selectedFilterItem?.key === ListFilter.First
            },
            {
                key: ListFilter.Last,
                title: this.getString('list_option_last'),
                isSelected: this.selectedFilterItem?.key === ListFilter.Last
            },
            {
                key: ListFilter.Index,
                title: this.getString('list_option_index'),
                isSelected: this.selectedFilterItem?.key === ListFilter.Index
            }
        ];
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.properties) {
            this.onPropertiesChanged();
        }
    }

    /**
     * Event that fires when the accordian is clicked
     */
    onAccordionClicked(): void {
        this.isOpen = !this.isOpen;
        this.savedUIValues.isOpen.smartFields[0].text = this.isOpen.toString();
    }

    /**
     * Event fired when a filter item is selected
     */
    onFilterItemSelected = (item: IDropDownMenuItem): void => {
        this.undoRedoService.addCustomObjectUndoEvent(this, ['selectedFilterItem'], '-- onFilterItemSelected: reverting scope selected filter item');
        this.undoRedoService.addCustomObjectUndoEvent(this.configuredListOptions,
            ['filterOption', 'index'], '-- onFilterItemSelected: reverting scope configuredListOptions (filterOption and index)');

        this.selectedFilterItem = item;
        this.configuredListOptions.filterOption = item.key;

        if (item.key === 'index') {
            this.configuredListOptions.index = 1;
        } else {
            this.configuredListOptions.index = null;
        }
    };

    /**
     * Ensures index is not empty if index is enabled (defaults to 1)
     */
    onIndexBlur(): void {
        if (this.selectedFilterItem.key === 'index' &&
            (!this.configuredListOptions.index ||
                isNaN(parseInt(this.configuredListOptions.index.toString(), 10)))) {
            this.configuredListOptions.index = 1;
        }
    }

    /**
     * Event fired when a property is selected
     */
    onPropertySelected = (item: IDropDownMenuItem): void => {
        const wasInError: boolean = this.selectedProperty?.hasError;

        this.selectedProperty = item as ISelectedOrderByProperty;
        this.configuredListOptions.orderedByProperty = ExternalReferenceUtilities.getExternalDefinitionToken(this.externalDefinitionParent, item.key, true);

        //Validation:
        this.selectedProperty.hasError = false;
        this.selectedProperty.errorMessage = '';

        if (item.metaData &&
            item.metaData.state === ExternalReferenceState.Missing &&
            this.configuredListOptions.errorInformation?.length &&
            this.configuredListOptions.errorInformation[0].errorMessage) {
            //Get and set error information
            this.selectedProperty.hasError = true;
            this.selectedProperty.errorMessage = this.resourceService.resolveTokenResourceString(
                this.configuredListOptions.errorInformation[0].errorMessage,
                this.configuredListOptions.errorInformation[0].errorResourceTokens,
                this.configuredListOptions
            );
            //Remove missing property from collection
            for (let i: number = this.properties.length - 1; i >= 0; i--) {
                if (this.properties[i].key === this.selectedProperty.key) {
                    this.properties.splice(i, 1);
                    break;
                }
            }
        } else if (wasInError) {
            //Current selected property is valid, but previous selected property was a missing value in error
            const reEvalRef = this.configuredListOptions.getReference();
            const objToReEval =
                this.configuredListOptions.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelObject>().findItem<IValidatedComponent>(reEvalRef);
            this.validationService.reevaluateErrors(objToReEval.errorInformation, true);
        }
    };

    /**
     * Allows only number to be types in the textbox
     *
     * @param event
     */
    allowOnlyNumbers(event: KeyboardEvent): void {
        // only numeric input accepted
        if (event.keyCode < 48 || event.keyCode > 57) {
            event.preventDefault();
        }

        //add undo/redo functionality to input control on k2ListItem
        if (!this.textTimeOut) {
            //add marker for initial value of itemtext - used to bring undo stack back to default value
            this.undoRedoService.addUndoMarker('-----change text value normal-----');
        }
        else {
            //clear timeout if set is hit again this ensures only 1 timeout that will eventually occur to add undo marker
            clearTimeout(this.textTimeOut);
            this.textTimeOut = null;
        }

        //add a marker after a certain amount of time of no user interaction - default 200ms
        this.textTimeOut = setTimeout(() => {
            if (this.configuredListOptions.index !== (event.target as any).value) {
                this.undoRedoService.addUndoMarker('-----change text value async-----');
            }
            else {
                clearTimeout(this.textTimeOut);
                this.textTimeOut = null;
            }
        }, this.textSetDelay);
    }

    /**
     * Called when the orderber by checkbox is checked to update model value
     *
     * @param value
     */
    orderByChecked(value: boolean): void {
        this.configuredListOptions.orderedBy = value;
    }

    /**
     * Called when a radio button is clicked to set the sort direction
     *
     * @param direction
     */
    setDirection(direction: string): void {
        this.configuredListOptions.direction = direction;
    }

    /**
     * Called to update the properties collection when input is changed
     */
    private onPropertiesChanged() {
        if (this.properties?.length && this.configuredListOptions) {
            if (!this.configuredListOptions.orderedByProperty?.length) {
                this.onPropertySelected(this.properties[0]);
            } else {
                const resolvedToken: string =
                    ExternalReferenceUtilities.resolveExternalReferenceToken(this.configuredListOptions.orderedByProperty, this.processService);

                for (const property of this.properties) {
                    //Either it equals the resolve value or it equals the original reference value
                    //The key can be a reference or a propertyName, need to account for both
                    if (property.key === resolvedToken
                        || property.key === this.configuredListOptions.orderedByProperty) {
                        this.onPropertySelected(property);
                        break;
                    }
                }
            }
        }
    }

}
