export const scopeServiceProvider = {
    provide: '$scope',
    useFactory: ($injector: any) => $injector.get('$scope'),
    deps: ['$injector'],
};

export const rootScopeServiceProvider = {
    provide: '$rootScope',
    useFactory: ($injector: any) => $injector.get('$rootScope'),
    deps: ['$injector'],
};

export const stateServiceProvider = {
    provide: '$state',
    useFactory: ($injector: any) => $injector.get('$state'),
    deps: ['$injector'],
};

export const rootElementServiceProvider = {
    provide: '$rootElement',
    useFactory: ($injector: any) => $injector.get('$rootElement'),
    deps: ['$injector'],
};

export const sceServiceProvider = {
    provide: '$sce',
    useFactory: ($injector: any) => $injector.get('$sce'),
    deps: ['$injector'],
};

export const compileServiceProvider = {
    provide: '$compile',
    useFactory: ($injector: any) => $injector.get('$compile'),
    deps: ['$compile'],
};
