import { Component, ElementRef, Inject, OnInit } from '@angular/core';
import { CommandServiceToken } from 'src/app/services/command/command-service-factory';
import { ConfigPanelServiceToken } from 'src/app/services/config-panel/config-panel-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { RulePluginService } from 'src/app/services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from 'src/app/services/selection/selection-service-factory';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { isUndefinedOrNull } from 'src/app/shared/utils/general';
import { IConfiguredSimpleExpression } from 'src/types/models/rules/i-configured-simple-expression';
import { IConfigPanelService } from 'src/types/services/config-panel/i-config-panel-service';
import { ISelectionService } from 'src/types/services/selection/i-selection-service';
import { RuleDesignerItemBaseComponent } from '../rule-designer-item-base/rule-designer-item-base.component';

@Component({
    selector: 'k2-statement-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export class StatementBaseComponent extends RuleDesignerItemBaseComponent implements OnInit {

    constructor(@Inject(SelectionServiceToken) selService: ISelectionService,
      protected rulePluginService: RulePluginService,
      @Inject(CommandServiceToken) commandService: ICommandService,
      @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
      @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
      eventService: EventNotifierService,
      protected rootElement: ElementRef<HTMLElement>) {
        super(selService, rulePluginService, commandService, configPanelService,
            undoRedoService, eventService, rootElement);
    }

    ngOnInit(): void {
        super.ngOnInit();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
    }

  public /*override*/ delete = (): Promise<boolean> => {

      let isDeleted = false;

      if (this.isStatementRemovable()) {
          this.statementModel.removeFromModelArray();
          isDeleted = true;
      }

      const promise = new Promise<boolean>((resolve) => {
          resolve(isDeleted);
      });

      return promise;
  };

    /**
     * Return an object of data that is associated with the current element
     */
    getDragItem = (item: IConfiguredSimpleExpression, context: any): sourceCode.k2Designer.objectModel.dragDropService.IDragDropData => new sourceCode.k2Designer.objectModel.dragDropService.DragDropData<any, any, any>(item, context, this.getDragData.bind(this), this.getDropData.bind(this), null);

    getDragData(source: any, context: any, extraData: any): any {
        return source || extraData || context;
    }

    getDropData(source: any, context: any, dragData: any, extraData: any): any {
        return source || extraData || dragData || context;
    }

    private isStatementRemovable() {
        const parent: sourceCode.k2Designer.objectModel.core.model.IModelBase = <sourceCode.k2Designer.objectModel.core.model.IModelBase>this.statementModel.getLogicalParentModel();

        return isUndefinedOrNull(parent)
            ? false : parent.instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor);
    }

}
