import { DOCUMENT } from '@angular/common';
import { Directive, ElementRef, EventEmitter, Inject, Input, NgZone, Output } from '@angular/core';
import { Subscription, fromEvent } from 'rxjs';
import { DragDropService } from '../../services/drag-drop/drag-drop.service';
import { isUndefinedOrNull } from '../../shared/utils/general';

declare let TWEEN: any;

@Directive({
    selector: '[K2Draggable]'
})
export class DraggableDirective {
    @Input() public DragEnable = true;
    @Input() public k2DragIndex: number;
    @Input() public k2Dragcontext: any;
    @Input() public k2Dragtype: string;
    @Input() public k2Dragitem: (item: any) => any;
    @Input() public k2Dragghost: (item: any, HTMLSourceElement?: HTMLElement) => HTMLElement;
    @Input() public k2Dropped: () => (type: string, data: any) => boolean;
    @Output() public k2Dragstart = new EventEmitter<DragStartEvent>();
    @Output() public k2Dragend = new EventEmitter<DragEvent>();
    @Output() public k2Drag = new EventEmitter<DragEvent>();

    private _el: HTMLElement;
    private _isIE: boolean;
    private _window = window;
    private _dragSubscription: Subscription;

    constructor(el: ElementRef<HTMLElement>,
        private _dragDropService: DragDropService,
        @Inject(DOCUMENT) private doc,
        private _zone: NgZone
    ) {
        this._el = el.nativeElement; // Element being dragged
        this._el.draggable = true;
        this._isIE = (typeof (<any>doc.createElement('span')).dragDrop === 'function'); // cant remove the any due to the weird checking type
    }

    ngOnInit(): void {
        this._el.addEventListener('dragstart', this.onDragStart.bind(this), false);
        this._el.addEventListener('dragend', this.onDragEnd.bind(this), false);
        this._zone.runOutsideAngular(() => {
            this._dragSubscription = fromEvent(this._el, 'drag').subscribe((event: DragEvent) => this.onDrag(event));
            this._el.addEventListener('mousedown', this.onMouseDown.bind(this), false);
        });
    }

    ngOnDestroy() {
        this._dragSubscription.unsubscribe();
    }

    public static changeDragGhost(ghost: HTMLElement): void {
        if (ghost) {
            ghost.style.position = 'absolute';
            ghost.style.opacity = '0';

            if (sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv && sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstChild) {
                new TWEEN.Tween((<HTMLElement>sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstChild).style).to({ opacity: 0 }, 250).start().onComplete(function() {
                    sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.removeChild(sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstChild);
                });
            }

            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.appendChild(ghost);
            new TWEEN.Tween(ghost.style).to({ opacity: 1 }, 250).start();
        }
    }

    private static _keepDragGhost: boolean;
    /**
     * When drag/drop finishes, the drag ghost will not be removed from the UI
     */
    public static keepDragGhost(): void {
        DraggableDirective._keepDragGhost = true;
    }

    public static dontKeepDragGhost(): void {
        DraggableDirective._keepDragGhost = false;
    }
    /**
     * Removes drag ghost from the ui
     */
    public static removeDragGhost(): void {
        //Only remove the drag div if it's not already been removed
        //IE11 / Edge can trigger multiple transitionend events
        //causing this to be called more than once in some cases and throwing an error
        if (sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.parentNode !== null) {
            document.body.removeChild(sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv);
        }
    }

    onDragEnd(event: DragEvent): boolean {
        this._dragDropService.isBusyDragging = false;

        if (this.k2Dragend !== null && this.k2Dragend !== undefined) {
            this.k2Dragend.emit(event);
        }

        if (DraggableDirective._keepDragGhost !== true) DraggableDirective.removeDragGhost();
        this._el.classList.remove('drag');

        if (!isUndefinedOrNull((<HTMLElement>event.target).style)) {
            (<HTMLElement>event.target).style.opacity = '1';
        }

        if (this._isIE) {
            this._el.classList.remove('drag-ie');
            $('.drag-ie-ghost').remove();
        }

        this._dragDropService.notifyDragStopped();
        return false;
    }

    onDrag(e: DragEvent): boolean {
        if (this.k2Drag !== null && this.k2Drag !== undefined) {
            this.k2Drag.emit(e);
        }

        this._window.requestAnimationFrame(() => {
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.top = (e.clientY || e.y || e.offsetY || 0) - sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset.y + 'px';
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.left = (e.clientX || e.x || e.offsetX || 0) - sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset.x + 'px';
        });

        return false;
    }

    onDragStart(event: DragEvent): boolean {
        if (!this._dragDropService.isEnabled) {
            event.preventDefault();
            return true;
        }

        if (!this.DragEnable) {
            return true;
        }
        this._dragDropService.isBusyDragging = true;

        this.k2Dragstart.emit({ dragevent: event, k2DragIndex: this.k2DragIndex });

        event.dataTransfer.setData('text', this.k2Dragtype);
        event.dataTransfer.effectAllowed = 'copyMove';

        // Clear the drag div (container for the ghost 'image')
        while (sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstChild) {
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.removeChild(sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstChild);
        }

        const sourceElement = $(event.srcElement || event.target).clone(true)[0];
        let ghost: HTMLElement = this.k2Dragghost != null ? this.k2Dragghost(this.k2Dragcontext, <any>sourceElement) : null;
        if (ghost != null) {
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.appendChild(ghost);
        } else {
            // Getting the outer draggable element
            let target: HTMLElement = <HTMLElement>event.target;

            if (!target.draggable) {
                const draggbleTarget = $(target).closest('[draggable]');
                if (draggbleTarget.length === 1) target = draggbleTarget[0];
            }
            else if (target.classList.contains('ie-drag-handler')) {
                const parent = $(target).closest('.ie-drag-wrapper');
                if (parent.length > 0) {
                    target = parent[0];
                }
            }

            target.style.opacity = '0.3';
            ghost = <HTMLElement>target.cloneNode(true);
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.appendChild(ghost);
        }

        if (this._isIE) {
            sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.classList.add('dragdrop-ie');
        }

        this.doc.body.appendChild(sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv);
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.opacity = '1';

        const type: string = this.k2Dragtype || this._el.getAttribute('k2Dragtype');

        let data: any = null;
        if (typeof this.k2Dragitem === 'function') {
            data = this.k2Dragitem(this.k2Dragcontext);
        } else {
            data = this.k2Dragitem;
        }

        this._dragDropService.setItem(type, data);

        if (typeof this.k2Dropped === 'function') {
            const onDroppedFn: (type: string, data: any) => boolean = this.k2Dropped();
            if (onDroppedFn != null) {
                this._dragDropService.setNotifyOnDropped(onDroppedFn);
            }
        }

        this._dragDropService.notifyDragStarted(type, data);

        const dragImage = this._isIE ? this._el : sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragGhost;
        (<any>event.dataTransfer).setDragImage(dragImage, 0, 0);

        this._el.classList.add('drag');
        return false;
    }

    onMouseDown(event: MouseEvent): boolean {
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset = this._isIE ? { x: event.offsetX - 50, y: event.offsetY } : { x: event.offsetX, y: event.offsetY };
        // this is for custom step svg icons to ensure event is firing from parent element this._el
        const svgElements = this._el.getElementsByTagName('svg');
        if (svgElements.length > 0) {
            svgElements[0].style.pointerEvents = 'none';
        }
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.opacity = '0';
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.top = (event.clientY || event.y || event.offsetY || 0) - sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset.y + 'px';
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.style.left = (event.clientX || event.x || event.offsetX || 0) - sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset.x + 'px';

        return false;
    }

    public static get currentGhost(): HTMLElement {
        return <HTMLElement>sourceCode.k2Designer.directives.k2DragDrop.K2Draggable._dragDiv.firstElementChild;
    }
}
