import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockUserOptionsService } from '../../../testing/mock-services/user-options-service';
import { IUserOptions } from '../../../types/services/user-options/i-user-options-service';

const userOptionsServiceFactory = () => environment.devHarness === true
    ? new MockUserOptionsService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.UserOptionsService);

export const UserOptionsServiceToken = new InjectionToken<IUserOptions>('IUserOptions');

export const UserOptionsServiceFactory = {
    provide: UserOptionsServiceToken,
    useFactory: userOptionsServiceFactory,
    deps: [

    ]
};
