import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockHelpService } from '../../../testing/mock-services/help-service';
import { IHelpService } from '../../../types/services/help/i-help-service';

const helpServiceFactory = () => environment.devHarness === true
    ? new MockHelpService()
    : sourceCode.k2Designer.core.environmentService.helpService;

export const HelpServiceToken = new InjectionToken<IHelpService>('IHelpService');

export const HelpServiceFactory = {
    provide: HelpServiceToken,
    useFactory: helpServiceFactory,
    deps: [

    ]
};
