import { Component, Inject, OnInit } from '@angular/core';
import { ICanvasService } from '../../../types/services/canvas/i-canvas.service';
import { IConfigPanelService } from '../../../types/services/config-panel/i-config-panel-service';
import { IObjectModelService } from '../../../types/services/object-model/i-object-model-service';
import { IValidationService } from '../../../types/services/validation/ivalidation.service';
import { CanvasServiceToken } from '../../services/canvas/canvas-service-factory';
import { ConfigPanelServiceToken } from '../../services/config-panel/config-panel-service-factory';
import { ObjectModelServiceToken } from '../../services/object-model/object-model-service-factory';
import { ProcessService } from '../../services/process/process.service';
import { ValidationServiceToken } from '../../services/validation/validation-service-factory';
import { ErrorSeverity } from '../../shared/enums/model/error-severity';
import { isUndefinedOrNull } from '../../shared/utils/general';

@Component({
    selector: 'k2-error-bar',
    templateUrl: './error-bar.component.html',
    styleUrls: ['./error-bar.component.less']
})
export class ErrorBarComponent implements OnInit {
    public clientErrorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>;
    public serverErrorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>;
    public signalRErrorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>;
    public state: string;
    public errorSeverity: ErrorSeverity;

    constructor(@Inject(ValidationServiceToken) private _validationService: IValidationService,
        private _processService: ProcessService,
        @Inject(ConfigPanelServiceToken) private _configPanelService: IConfigPanelService,
        @Inject(CanvasServiceToken) private _canvasService: ICanvasService,
        @Inject(ObjectModelServiceToken) private _objectModelService: IObjectModelService<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>) {
    }

    ngOnInit(): void {
        this.errorSeverity = ErrorSeverity.Error;

        this.state = this.state ? this.state : 'collapsed';

        this.clientErrorList = this._validationService.getErrorList();
        this.serverErrorList = this._processService.getErrorList();
        this.signalRErrorList = this._objectModelService.getErrorList();

        this._validationService.listenForErrorList(this.setClientErrorList.bind(this));
        this._processService.listenForErrorList(this.setServerErrorList.bind(this));
        this._objectModelService.listenForSRErrorList(this.setSignalRErrorList.bind(this));
    }

    private setClientErrorList(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        const list: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation> = [];

        errorList.forEach((error: sourceCode.k2Designer.objectModel.core.model.IErrorInformation): void => {

            // Finding related items
            const related: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation> = list.filter((err: sourceCode.k2Designer.objectModel.core.model.IErrorInformation): boolean => {
                let isRelated = true;

                // Continue if the validation type, severity, & message match
                if (!(isRelated = error.validationType === err.validationType && error.errorSeverity === err.errorSeverity && error.errorMessage === err.errorMessage)) {
                    return isRelated;
                }

                // Comparing resource tokens with potential match
                if (isRelated = error.errorResourceTokens.length === err.errorResourceTokens.length) {
                    isRelated = error.errorResourceTokens.every((token: string, index: number): boolean => err.errorResourceTokens.indexOf(token) === index);

                    if (!isRelated) {
                        return isRelated;
                    }

                    // Do the items relate by reference path (one a descendent of the other)
                    isRelated = (error.reference.indexOf(err.reference) > -1 || err.reference.indexOf(error.reference) > -1);
                }
                else {
                    return isRelated;
                }

                return isRelated;
            });

            // Only if the error appears to be unique, continue
            if (related.length === 0) {
                list.push(error);
            }

        });

        // If the list was filtered, use the filtered list instead
        if (errorList.length !== list.length) {
            errorList = list;
        }

        if (this.clientErrorList !== errorList) {
            this.clientErrorList = errorList;
        }

        // Reset to default state when error list is empty.
        if (this.clientErrorList.length === 0 && this.serverErrorList.length === 0 && this.signalRErrorList.length === 0) {
            this.state = 'collapsed';
        }

        this.setErrorSeverity();
    }

    private setServerErrorList(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        //Check that signalR has not already added the same error
        this.addDeploymentErrorInfo(errorList);

        // Reset to default state when error list is empty.
        if (this.clientErrorList.length === 0 && this.serverErrorList.length === 0 && this.signalRErrorList.length === 0) {
            this.state = 'collapsed';
        }

        this.setErrorSeverity();
    }

    private setSignalRErrorList(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        //Check that deployment has not already added the same error, and replace if it has
        this.addSignalRErrorInfo(errorList);

        // Reset to default state when error list is empty.
        if (this.clientErrorList.length === 0 && this.serverErrorList.length === 0 && this.signalRErrorList.length === 0) {
            this.state = 'collapsed';
        }

        this.setErrorSeverity();
    }

    /**
     * Method to set the error bar severity, the errorBar Notifier errorSeverity is bound to this scope errorSeverity property.
     * It will determine if the error bar severity is 'Warning', 'Error'. If any errors including server errors, the state will be
     * error otherwise it will be warning.
     */
    private setErrorSeverity(): void {

        let clientErrorListContainsErrors = false;
        //Only do this if no signalR or server errors to determine if it contains errors with error severity.
        if (this.serverErrorList.length === 0 || this.signalRErrorList.length === 0) {
            for (let i = 0; i < this.clientErrorList.length; i++) {
                if (isUndefinedOrNull(this.clientErrorList[i].errorSeverity) ||
                    (!isUndefinedOrNull(this.clientErrorList[i].errorSeverity) &&
                        this.clientErrorList[i].errorSeverity === ErrorSeverity.Error)) {
                    clientErrorListContainsErrors = true;
                    break;
                }
            }
        }

        if (clientErrorListContainsErrors || this.serverErrorList.length > 0 || this.signalRErrorList.length > 0) {
            this.errorSeverity = ErrorSeverity.Error;
        } else if (this.clientErrorList.length > 0) {
            this.errorSeverity = ErrorSeverity.Warning;
        }
    }

    private addDeploymentErrorInfo(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {
        this.serverErrorList = [];
        for (let i = 0; i < errorList.length; i++) {
            const deployError = errorList[i];
            const index = this.findIndexByExceptionMessage(this.signalRErrorList, deployError.errorMessage);
            if (index > -1) {
                //remove duplicate from signalR error list since we will have the error in server error list that should persist till next deployment
                this.signalRErrorList.splice(index);
                this.serverErrorList.push(deployError);
            }
            else {
                this.serverErrorList.push(deployError);
            }
        }
    }

    private addSignalRErrorInfo(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        this.signalRErrorList = [];
        for (let i = 0; i < errorList.length; i++) {
            const srError = errorList[i];
            const index = this.findIndexByExceptionMessage(this.serverErrorList, srError.errorMessage);
            if (index > -1) {
                //remove duplicate error from signal R error list and add to the server error list so that the error persist until next deployment
                this.serverErrorList.push(srError);
                this.signalRErrorList.splice(index);
            }
            else {
                //add signal R error such as 'K2 server down' to the list
                this.signalRErrorList.push(srError);
            }
        }
    }

    private findIndexByExceptionMessage(targetArray: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>, errorMessage: string): number {
        for (let i = 0; i < targetArray.length; i++) {
            const tException: sourceCode.k2Designer.objectModel.core.model.IErrorInformation = targetArray[i];
            if (errorMessage === tException.errorMessage) return i;
        }
        return -1;
    }

    /**
     * When called, will set the state to expanded, showing the console
     */
    public expand(): void {
        this.state = 'expanded';
    }

    /**
     * When caleld, will the state to collapsed, hiding the console
     */
    public collapse(): void {
        this.state = 'collapsed';
    }

    /**
     * Simulates a click on canvas for step in error. Opens config panel and selects correct tab when 'SHOW ME' is clicked in error bar.
     *
     * @param errorObj
     */
    public focusDesignerNode(errorObj: sourceCode.k2Designer.objectModel.core.model.IErrorInformation): void {
        if (!isUndefinedOrNull(errorObj)) {
            let reference = errorObj.reference;

            if (this._canvasService && reference) {
                const currentTab = this._configPanelService.currentPnl.currentItem.descriptionToken;

                const modelObj: sourceCode.k2Designer.objectModel.core.model.IModelObject = this._processService.root.findItem(reference);

                if (isUndefinedOrNull(modelObj)) {
                    // remove item valiation
                    const deepestExisting: sourceCode.k2Designer.objectModel.core.model.IModelObject = this._processService.root.findDeepestExisting(reference);
                    this._validationService.removeErrorsFrom(deepestExisting, reference);
                } else {
                    if (!isUndefinedOrNull(errorObj.metadata) && !isUndefinedOrNull(errorObj.metadata.referencingNode)) {
                        const nodeUsingItemReference: sourceCode.k2Designer.objectModel.core.model.IModelObject = this._processService.root.findItem(errorObj.metadata.referencingNode);
                        if (!isUndefinedOrNull(nodeUsingItemReference)) {
                            reference = nodeUsingItemReference.getReference();
                        }
                    }
                    this._validationService.reevaluateErrors([errorObj]);
                }

                if (modelObj.getReference().indexOf(this._processService.root.configuration.startRule.getReference()) >= 0 ||
                    (this._processService.root.configuration.eventPlatformConfiguration &&
                        modelObj.getReference().indexOf(this._processService.root.configuration.eventPlatformConfiguration.getReference()) >= 0)) {

                    //set the reference to the Start Activity
                    reference = this._processService.root.nodes[0].getReference();
                }

                this._canvasService.selectStepByReference(reference);

                // If it's a tab on the same step, just immediately go to the tab, else only focus it when they are loaded
                if (currentTab !== this._configPanelService.currentPnl.currentItem.descriptionToken) {
                    this._configPanelService.configTabErrorDataSetListener = this.onConfigPanelTabErrorDataLoaded.bind(this, reference);
                } else {
                    const tabs = this._configPanelService.currentPnl.items;
                    tabs.forEach((tab) => {
                        this.onConfigPanelTabErrorDataLoaded(reference, tab);
                    });
                }
            }
        }
    }

    /**
     * This event fires when config panel is opened and controller was loaded for config panel, meaning it is ready
     * to select the correct tab after 'SHOW ME' was clicked.
     * It will go through all components in error for this tab and if it matches the reference of the error information, select tab.
     *
     * @param errorReference reference configured in error information on error on which 'SHOW ME' was clicked
     * @param loadedTab tab which fired the loaded event
     */
    private onConfigPanelTabErrorDataLoaded(errorReference: string, loadedTab: sourceCode.k2Designer.objectModel.configPanel.IConfigPanelTab): void {
        const tempTab = new sourceCode.k2Designer.objectModel.configPanel.ConfigPanelTab(null, [], '', '', '', '', [], '', '', '', []);
        let tabToSelect: sourceCode.k2Designer.objectModel.configPanel.IConfigPanelTab = null;

        if (!isUndefinedOrNull(loadedTab) && loadedTab.containsErrors) {
            for (const comp of loadedTab.componentsToWatch) {
                //See if this component causes tab to be in error
                if (tempTab.componentInError(comp)) {
                    //See if component error matches error console error
                    const obj = comp.componentObj;
                    const matches = (comp.compareFullReference && errorReference === obj.getReference()) ||
                        (!comp.compareFullReference && errorReference.indexOf(obj.getReference()) === 0);
                    if (matches) {
                        tabToSelect = loadedTab;
                        break;
                    }
                }
            }
        }
        if (!isUndefinedOrNull(tabToSelect)) {
            this._configPanelService.selectConfigTab(tabToSelect);
        }
    }

}
