/**
 * Indicates the type of constraint that is applied to the textbox when the user is typing
 * */
export enum TextBoxConstraint {
    Number = 'number', //Only allow digits (whole numbers)
    DropSelect = 'drop-select', //Only allow drop or select of drop-down
    DropOnly = 'drop-only', //Only allow drop and ignore typing
    None = 'none'
}
