import { Component, ContentChild, ElementRef, Inject, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { IPosition } from '../../../../types/models/positioning/i-position';
import { IUserOptions } from '../../../../types/services/user-options/i-user-options-service';
import { UserOptionsServiceToken } from '../../../services/user-options/user-options-service-factory';
import { Positioning } from '../../../shared/enums/positioning/positioning';
import { UserOptionsTipTypes } from '../../../shared/enums/user-options/user-options-tip-types';
import { InfoPopupTemplate } from './info-popup-template';

@Component({
    selector: 'k2-info-popup',
    templateUrl: './info-popup.component.html',
    styleUrls: ['./info-popup.component.less']
})
export class InfoPopupComponent implements OnInit {
    @Input() popupClass: string;
    @Input() position: Positioning = Positioning.left;
    @Input() showOnCreate = false;
    @Input() forceHideWhenNoPosition = false;
    @Input() target: HTMLElement;
    @Input() location: ClientRect;
    @Input() gotItType: UserOptionsTipTypes;

    isOpen = false;
    hasBeenOpened: boolean;
    imageResolution = window.devicePixelRatio < 2 ? '1x' : '2x';

    @ContentChild(InfoPopupTemplate, { read: TemplateRef }) template: TemplateRef<any>;

    private _popupEl: ElementRef;
    get popupEl(): ElementRef<HTMLElement> {
        return this._popupEl;
    }
    @ViewChild('popup') set popupEl(val: ElementRef<HTMLElement>) {
        if (val) {
            this._popupEl = val;
            this.refreshPosition();
        }
    }

    constructor(protected _el: ElementRef<HTMLElement>, @Inject(UserOptionsServiceToken) private userOptionsService: IUserOptions) { }

    ngOnInit(): void {
        if (this.showOnCreate) {
            this.isOpen = true;
        }
    }

    refreshPosition() {
        const popupHtml = this.popupEl ? this.popupEl.nativeElement : this._el.nativeElement;
        let targetPosition: any;

        if (this.location) {
            targetPosition = this.location;
        }
        else if (popupHtml) {
            targetPosition = this.target ? this.target.getBoundingClientRect() : this._el.nativeElement.getBoundingClientRect();
        }

        if (targetPosition.height === 0 && targetPosition.width === 0 && this.forceHideWhenNoPosition) {
            this.close(false);
            return;
        }

        if (targetPosition && popupHtml) {
            let arrowPosition: string;
            const pos = {
                top: 0,
                right: 0,
                bottom: 0,
                left: 0
            } as IPosition;

            switch (this.position) {
                case Positioning.top:
                    arrowPosition = 'arrow-bottom';
                    pos.top = targetPosition.top - popupHtml.getBoundingClientRect().height - 25;
                    pos.left; targetPosition.left + (targetPosition.width - popupHtml.getBoundingClientRect().width) / 2;
                    break;
                case Positioning.bottom:
                    arrowPosition = 'arrow-top';
                    pos.top = targetPosition.bottom + 25;
                    pos.left; targetPosition.left + (targetPosition.width - popupHtml.getBoundingClientRect().width) / 2;
                    break;
                case Positioning.left:
                    arrowPosition = 'arrow-right';
                    pos.top = (targetPosition.top + (targetPosition.height / 2) - (popupHtml.getBoundingClientRect().height / 2)) - 18; // 18 less to align the triangle position
                    pos.left = (targetPosition.left - popupHtml.getBoundingClientRect().width - 25);
                    break;
                case Positioning.right:
                    arrowPosition = 'arrow-left';
                    pos.top = (targetPosition.top + (targetPosition.height / 2) - (popupHtml.getBoundingClientRect().height / 2)) - 18; // 18 less to align the triangle position
                    pos.left = targetPosition.right + 25;
                    break;
                default:
                    break;
            }

            popupHtml.style.top = pos.top + 'px';
            popupHtml.style.left = pos.left + 'px';
            popupHtml.classList.add(arrowPosition);
        }
    }

    close(neverShowAgain?: boolean): void {
        this.isOpen = false;
        if (neverShowAgain && this.gotItType) {
            this.userOptionsService.setTipValue(this.gotItType, false);
        }
    }

    open(targetPosition?: ClientRect) {
        if (this.userOptionsService.userOptions.tips[this.gotItType]) {
            if (targetPosition) {
                this.location = targetPosition;
            }
            this.isOpen = true;
        }
    }
}
