import { Component } from '@angular/core';
import { ResourceService } from '../../services/resource/resource.service';

@Component({
    selector: 'k2-base-resource',
    template: '<p> Empty Template since it wont be used </p>'
})
export class BaseResourceComponent {

    constructor(protected resourceService: ResourceService) { }

    /**
     * Returns resource string from .resx file using name and key
     *
     * @param resourceName name of resx file
     * @param key resource lookup key
     */
    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    /**
     * Returns resource string from JSON
     *
     * @param key
     */
    getString = (key: string) => this.resourceService.getString(key);
}
