import { Component, ElementRef, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { ClipboardServiceToken } from '../../../services/clipboard/clipboard-service-factory';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { inputDebounceTime } from '../../../shared/constants/general';
import { BaseComponent } from '../../base/base.component';

@Component({
    selector: 'k2-text-area',
    templateUrl: './text-area.component.html',
    styleUrls: ['./text-area.component.less']
})
export class TextAreaComponent extends BaseComponent implements OnInit, OnDestroy {
    @Input() validationTypes: any;
    @Input() value: any;
    @Input() rows: number;
    @Input() validationModel: any;
    @Input() validationProperty: string;
    @Input() isValidateError: boolean;
    @Input() placeholder = '';
    @Input() maxLength: number;
    @Input() parentObject: sourceCode.k2Designer.objectModel.core.model.IModelObject;
    @Input() watermark: string;

    @Output() k2Change: EventEmitter<string> = new EventEmitter<string>();
    @Output() k2Focus: EventEmitter<any> = new EventEmitter<any>();
    @Output() k2Blur: EventEmitter<any> = new EventEmitter<any>();

    textValueChanged: Subject<string> = new Subject<string>();
    isFocus = false;

    private textValueChangeSub: Subscription;

    constructor(@Inject(ValidationServiceToken) validationService: IValidationService,
        @Inject(ClipboardServiceToken) private clipboardService: IClipboardService,
        @Inject(CommandServiceToken) private commandService: ICommandService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService) {
        super(validationService);

        this.textValueChangeSub = this.textValueChanged.pipe(
            debounceTime(inputDebounceTime),
            distinctUntilChanged()
        ).subscribe((val) => {
            this.value = val;
            this.modelChanged(val);
        });
    }

    ngOnInit() {
        if (!this.validationTypes) {
            this.validationTypes = [];
        }
        if (this.validationModel && this.validationProperty) {
            this.validatePropertyOnChange(this.validationModel, this.validationProperty);
        }
        this.setApplicableValidationTypes(this.validationTypes);
    }

    ngOnDestroy() {
        this.textValueChangeSub.unsubscribe();
    }

    onFocus(): void {
        this.clipboardService.setCurrentContext('TextArea');
        this.commandService.setCurrentContext();
        this.k2Focus.emit();
        this.isFocus = true;
    }

    onBlur(): void {
        this.isFocus = false;
        this.k2Blur.emit();
    }

    onEditorDrop(event: DragEvent): void {
        if (event.stopPropagation) event.stopPropagation();
        if (event.preventDefault) event.preventDefault();
    }

    modelChanged(val): void {
        if (this.undoRedoService.isEnabled()) {
            this.undoRedoService.addUndoMarker('--k2TextArea: changed text--');
        }

        this.k2Change.emit(val);
    }
}
