import { Component, Inject, Input, OnInit } from '@angular/core';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { DataTypeResolverService } from '../../../../services/plugins/data-type-resolver/data-type-resolver.service';
import { ProcessService } from '../../../../services/process/process.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { PageComponent } from '../../page/page.component';
import { DataMapperComponent } from '../data-mapper/data-mapper.component';

@Component({
    selector: 'k2-event-platform-add',
    templateUrl: './event-platform-add.component.html',
    styleUrls: ['./event-platform-add.component.less'],
    providers: [{ provide: PageComponent, useExisting: EventPlatFormAddComponent }]
})
export class EventPlatFormAddComponent extends DataMapperComponent implements OnInit {

    constructor(protected resourceService: ResourceService,
        processService: ProcessService,
        eventService: EventNotifierService,
        private dataTypeResolver: DataTypeResolverService) {
        //Initialize parent DataMapperComponent:
        super(resourceService, processService, eventService);
    }

    ngOnInit(): void {
        super.ngOnInit();
        this.setValues();
    }

    buttonConfig: IPopupButtonsConfig = {
        finish: {
            isVisible: true,
            buttonText: this.getString('workflow_contextPopup_addButton'),
        },
        next: {
            isVisible: false
        }
    };


    protected setValues(): void {
        const result = [
            {
                type: 'Integer',
                extendedType: null,
                required: false,
                systemName: 'inst_id',
                metadata: {
                    displayName: 'Workflow ID',
                    description: 'Workflow ID'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'set_name',
                metadata: {
                    displayName: 'Workflow Name',
                    description: 'Workflow Name'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'set_folder',
                metadata: {
                    displayName: 'Workflow Path',
                    description: 'Workflow Path'
                }
            },
            {
                type: 'DateTime',
                extendedType: null,
                required: false,
                systemName: 'inst_start',
                metadata: {
                    displayName: 'Start Date',
                    description: 'Start Date'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'inst_originator_name',
                metadata: {
                    displayName: 'Originator',
                    description: 'Originator'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'inst_priority',
                metadata: {
                    displayName: 'Priority',
                    description: 'Priority'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'inst_folio',
                metadata: {
                    displayName: 'Folio',
                    description: 'Folio'
                }
            },
            {
                type: 'String',
                extendedType: null,
                required: false,
                systemName: 'error_message',
                metadata: {
                    displayName: 'Error Message',
                    description: 'Error Message'
                }
            },
            {
                type: 'DateTime',
                extendedType: null,
                required: false,
                systemName: 'error_date',
                metadata: {
                    displayName: 'Error Date',
                    description: 'Error Date'
                }
            },
        ];

        for (let i = 0; i < result.length; i++) {
            this.values.push({
                icon: this.getIconFromType(result[i].type),
                key: result[i].systemName,
                title: result[i].metadata.displayName,
                metaData: this.dataTypeResolver.getTypeFromDotNetType(result[i].type)
            });
        }
    }

    protected getIconFromType(dotNetType: string): string {
        const iconType: string = this.dataTypeResolver.getSpriteClassFromDotNetType(dotNetType);
        return `icon-set-datatypes ic-${iconType}`;
    }
}
