import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { IRulePluginService } from '../../../../types/services/rules/i-rule-plugin.service';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { FilterControlStates } from '../../../shared/enums/filter-control/filter-control-states';
import { EventTypes } from '../../../shared/enums/model/event-types';
import { isUndefinedOrNull } from '../../../shared/utils/general';
import { RulePluginService } from '../../../services/ruleplugin/rule-plugin-service';

@Component({
    selector: 'k2-filter-control',
    templateUrl: './filter-control.component.html',
    styleUrls: ['./filter-control.component.less']
})

export class FilterControlComponent implements OnInit {
    @Input() public k2DeleteStep: boolean;
    @Input() public configuredFilter: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilter;
    @Input() public properties: IDropDownMenuItem[];
    @Input() public externalDefinitionParent: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField;
    @Input() public initialStateDescription: string;

    @Output() public openConditionalPanel: EventEmitter<any> = new EventEmitter();
    @Output() public closeConditionalPanel: EventEmitter<any> = new EventEmitter();

    public state: FilterControlStates;
    public filterControlStates: typeof FilterControlStates = FilterControlStates;
    public hoverClass: string;
    public selectedModel: {
        item: any;
        index: number;
    };

    private _expressionsChangedListener: sourceCode.k2Designer.objectModel.core.model.IEventRemover;

    constructor(protected rootElement: ElementRef,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        protected rulePluginService: RulePluginService,
        protected resourceService: ResourceService) { }

    ngOnInit(): void {
        this.hoverClass = '';

        this.configuredFilter = this.configuredFilter || sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredFilter.createInstance({
            expressions: []
        });

        this.state = this.configuredFilter && this.configuredFilter.expressions.length > 0 ? FilterControlStates.Normal : FilterControlStates.Initial;

        if (this.state === FilterControlStates.Normal) {
            for (let e = 0; e < this.configuredFilter.expressions.length; e++) {
                if (this.configuredFilter.expressions[e].instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredComplexLogicalGroupExpression.implementor)) {
                    this.state = FilterControlStates.Complex;
                    break;
                }
            }
        }

        this.properties = this.properties || [];

        this.selectedModel = {
            item: null,
            index: null
        };

        this._expressionsChangedListener =
            this.configuredFilter.expressions.listen(EventTypes.anyArrayChanged, this.onExpressionsChanged.bind(this));
    }

    ngOnDestroy(): void {
        if (this._expressionsChangedListener) {
            this._expressionsChangedListener();
        }
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    /**
     * Function called when the add filter link is clicked
     */
    public addFilterClicked(): void {
        this.state = FilterControlStates.Normal;

        this.undoRedoService.addUndoMarker('--Filter Control: add filter clicked--');

        if (this.configuredFilter.expressions.length > 0) {

            const newOperator = this.rulePluginService.createOperator('GroupBooleanOperator') as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredGroupOperator;

            newOperator.configureForFilterControl = true;

            this.configuredFilter.expressions.push(newOperator);
        }

        this.configuredFilter.expressions.push(<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression>this.rulePluginService.createExpression('FilterExpression'));

        this.selectedModel.item = this.configuredFilter.expressions[this.configuredFilter.expressions.length - 1];
        this.selectedModel.index = this.configuredFilter.expressions.length - 1;

        this.focusTextbox();
    }

    /**
     * This function sets focus on textbox as soon as a new row is added
     */
    protected focusTextbox() {
        // The reason this is done, is in order to set the execution of the method to after the scope was updated by angular, since there is no
        // callback on agular to subcribe to.
        setTimeout((): void => {
            const selectedRow = this.rootElement.nativeElement.getElementsByClassName('.expression-repeat')[this.selectedModel.index];

            if (selectedRow) {
                selectedRow.getElementByClassName('.k2-text-box-watermark').click();
            }
        });
    }

    /**
     * Function called when the add filter link is clicked
     */
    public removeFilterRow(model?: any): void {

        const k2Model = isUndefinedOrNull(model)
            ? this.selectedModel.item
            : model;

        if (k2Model) {
            this.undoRedoService.addUndoMarker('--k2FilterControl: delete filter expression--');

            const parent = (<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression>k2Model)
                .getParentModel<sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression>>();
            const index = parent.indexOf(k2Model);

            if (parent.length < 2) {
                k2Model.removeFromModelArray();
            }
            else {

                //Remove this section only after the operator has been removed
                k2Model.removeFromModelArray();

                const groupOpertorOf = function(index: number,
                    expressions: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression>) {

                    if (index > expressions.length - 1) {
                        index -= 1;
                    }
                    return expressions[index].directive === 'k2-group-boolean-operator-expression' ? expressions[index] : undefined;
                };

                const operatorToBeRemove: any = groupOpertorOf(index, parent);
                if (operatorToBeRemove) {
                    operatorToBeRemove.removeFromModelArray();
                }
            }
        }
    }


    /**
     * Function called when advanced condition is clicked
     */
    public setContext(): void {
        this.state = this.state !== FilterControlStates.Condition ? FilterControlStates.Condition : FilterControlStates.Complex;

        if (this.state === 'condition') {
            if (this.openConditionalPanel) {
                this.openConditionalPanel.emit();
            }
        } else {
            if (this.closeConditionalPanel) {
                this.closeConditionalPanel.emit();
            }

            let groupingFound = false;

            for (let e = 0; e < this.configuredFilter.expressions.length; e++) {
                if (this.configuredFilter.expressions[e].instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredComplexLogicalGroupExpression.implementor)) {
                    groupingFound = true;
                    break;
                }
            }

            if (!groupingFound) {
                this.state = FilterControlStates.Normal;
            }
        }
    }

    /**
     * Function called to reset the filter once it was in complex mode
     */
    public resetFilter(): void {
        this.state = FilterControlStates.Initial;
        this.configuredFilter.expressions.length = 0;
    }

    /**
     * Used to determine if the filter is open
     */
    public isPanelOpen(): boolean {
        const isPanelOpen: boolean = this.environmentService.activeStudioItem.pluginStates.filterPanel === 'expanded';

        if (!isPanelOpen) {
            this.updateState();
        }

        return isPanelOpen;
    }

    /**
     * Update state of the control based on the expressions object
     */
    private updateState(): void {
        let groupingFound = false;

        for (let e = 0; e < this.configuredFilter.expressions.length; e++) {
            if (this.configuredFilter.expressions[e].instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredComplexLogicalGroupExpression.implementor)) {
                groupingFound = true;
                break;
            }
        }

        if (!groupingFound) {
            this.state = FilterControlStates.Normal;
        } else {
            this.state = FilterControlStates.Complex;
        }
    }

    public onExpressionsChanged() {
        if (this.configuredFilter.expressions.length === 0) {
            this.state = FilterControlStates.Initial;
        }
        else {
            //re-assign state of the scope.
            this.updateState();

            if (!isUndefinedOrNull(this.selectedModel.item)) {
                if (this.selectedModel.item.getReference() === 'root') {
                    this.selectedModel.index = null;
                    this.selectedModel.item = null;

                }
            }

        }
    }

    public addHoverClass(): void {
        this.hoverClass = 'disabled';
    }

    public removeHoverClass(): void {
        this.hoverClass = '';
    }
}
