import { Component, EventEmitter, Inject, Input, OnInit } from '@angular/core';
import { IActivityConfiguration } from '../../../../../../../../types/models/process/activities/configuration/i-activity-configuration';
import { IActivity } from '../../../../../../../../types/models/process/activities/i-activity';
import { IClientEventConfiguration } from '../../../../../../../../types/models/process/events/configuration/i-client-event-configuration';
import { IEvent } from '../../../../../../../../types/models/process/events/i-event';
import { IConfigPanelService } from '../../../../../../../../types/services/config-panel/i-config-panel-service';
import { IEnvironmentService } from '../../../../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../../../../types/services/plugin/i-plugin-service';
import { IValidationService } from '../../../../../../../../types/services/validation/ivalidation.service';
import { ConfigPanelServiceToken } from '../../../../../../../services/config-panel/config-panel-service-factory';
import { EnvironmentServiceToken } from '../../../../../../../services/environment/environment-service-factory';
import { EventNotifierService } from '../../../../../../../services/event-notifier/event-notifier.service';
import { PluginServiceToken } from '../../../../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../../../../services/ruleplugin/rule-plugin-service';
import { UndoRedoServiceToken } from '../../../../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../../../../services/validation/validation-service-factory';
import { WorkflowService } from '../../../../../../../services/workflow/workflow.service';
import { ComponentIdMapping } from '../../../../../../../shared/constants/components';
import { DecisionActivityChangedEvent } from '../../../../../../../shared/constants/events';
import { rules } from '../../../../../../../shared/constants/rules';
import { StepTemplates } from '../../../../../../../shared/enums/activities/step-template-names';
import { ChooseControlTypes } from '../../../../../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../../../../../shared/enums/drop-down/drop-down-triangle-position';
import { DecisionOptionType } from '../../../../../../../shared/enums/model/workflow/process/decision-option-types';
import { RuleContextHandlerNames } from '../../../../../../../shared/enums/rules/rule-context-handler-names';
import { isUndefinedOrNull, shortUid } from '../../../../../../../shared/utils/general';
import { RulePluginBaseComponent } from '../../../../base/rule-plugin-base/rule-plugin-base.component';

@Component({
    selector: 'k2-decision-step-footer',
    templateUrl: './decision-step-footer.component.html',
    styleUrls: ['./decision-step-footer.component.less']
})
export class DecisionStepFooterComponent extends RulePluginBaseComponent implements OnInit {

    @Input() activity: IActivity;

    isDropDownActive = false;
    checkBoxId = shortUid();
    defaultActions: IDropDownMenuItem[] = [];

    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;

    private decisionActionChangedEvent: EventEmitter<IActivityConfiguration>;

    constructor(protected resourceService: ResourceService,
        protected rulePluginService: RulePluginService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken) protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        private workflowService: WorkflowService,
        private eventNotifierService: EventNotifierService) {
        super(validationService, configPanelService, rulePluginService, undoRedoService,
            pluginService, environmentService, resourceService, RuleContextHandlerNames.DecisionRule);

        this.decisionActionChangedEvent = this.eventNotifierService.addEvent(DecisionActivityChangedEvent);
    }

    ngOnInit(): void {
        this.defaultActions = this.getDefaultActions();
    }

    /**
     * Decision activity configuration
     */
    get activityConfig(): IActivityConfiguration {
        //Have to cast to unknown, because of 'createdFromIfDrop' property which does not exist in WFD
        return this.activity.configuration as unknown as IActivityConfiguration;
    }

    /**
     * Event configured for the decision (referenced in outcomesEventReference)
     */
    get selectedEvent(): IEvent {
        return this.activityConfig.outcomesEventReference ?
            this.workflowService.findItem<IEvent>(this.activityConfig.outcomesEventReference, this.activity) : null;
    }

    activitySelected(actionItem: IDropDownMenuItem) {
        this.undoRedoService.addUndoMarker('--outcomes tab: link to event--');

        const decisionStep = this.activity;

        this.activityConfig.outcomesEventReference = actionItem.key;

        if (decisionStep.configuration.isDecisionOutcomeCheckBoxChecked) {
            //Ensure that automatic rule is generated for selected task outcomes, only if no custom rule has been configured
            if (sourceCode.k2Designer.objectModel.rulePlugins.rules.DecisionRule.checkIfRuleIsDefault(decisionStep.configuration.outcomeRule)) {
                this.selectRuleType(DecisionOptionType.auto);
            }
        }

        this.decisionActionChangedEvent.emit(this.activityConfig);
    }

    /**
     * Get title from event, if event has no title, get from parent activity
     * */
    getSelectedEventTitle(): string {
        if (this.selectedEvent?.title) {
            return this.selectedEvent.title;
        } else if (this.selectedEvent) {
            return this.selectedEvent.getLogicalParentModel<IActivity>().title;
        }
        return null;
    }

    autoAddDecisionsChanged(isAutoAdd: boolean): void {

        if (!isAutoAdd) {
            //Unlink
            this.undoRedoService.addUndoMarker('--outcomes tab: unlink from user task--');
            this.activityConfig.outcomesEventReference = '';

        } else {
            this.undoRedoService.addUndoMarker('--outcomes tab: link to user task--');
            const selectedAction = this.defaultActions.find(a => a.isSelected);
            if (selectedAction) {
                this.activityConfig.outcomesEventReference = selectedAction.key;
            }
        }

        this.decisionActionChangedEvent.emit(this.activityConfig);
    }

    protected loadAndPopulateRuleDesigner(): void {
        //Do nothing
    }

    protected refreshTabIndexes(): void {
        //Do nothing
    }

    protected selectRuleType(ruleType: DecisionOptionType): void {
        if (ruleType === DecisionOptionType.auto) {
            this.workflowService.createAutoRuleForDecisionOrSplit(this.activity);
            this.loadEmptyRuleDesigner();
        }
    }

    protected renderRuleDesigner(): void {
        //Do nothing
    }

    /**
     * This method loops through all the activities on the canvas, adds it to an array.
     * The dropdown will display all items in the array.
     */
    private getDefaultActions(): IDropDownMenuItem[] {
        const actions = [];
        const process = this.activity.getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>();
        if (process && process.nodes) {
            for (let i = 0; i < process.nodes.length; i++) {
                const template = process.nodes[i].ui.template;
                if (template === StepTemplates.TaskStep || template === StepTemplates.MultiStep) {
                    const isMultiStep = template === StepTemplates.MultiStep;
                    const step = process.nodes[i] as unknown as IActivity;
                    this.addAction(actions, step, isMultiStep);
                }
            }
        }
        return actions;
    }

    private addAction(actions: IDropDownMenuItem[], foundActivity: IActivity, isMultiStep: boolean): void {
        for (let i: number = foundActivity.children.length - 1; i >= 0; i--) {
            const event = foundActivity.children[i];
            const key = event.getReference();
            const isSelected = this.selectedEvent === event;
            let title = event.title;
            const eventParent = event.getLogicalParentModel<IActivity>();

            if (!isMultiStep && !isUndefinedOrNull(eventParent)) {
                title = eventParent.title;
            }

            const eventConf = event.configuration as IClientEventConfiguration;

            if ((eventConf.componentId === ComponentIdMapping.ClientEventConfiguration) ||
                (this.workflowService.canHaveOutcomes(event) && eventConf.actions.length > 0)) {
                actions.push({
                    icon: rules.icons.outcomeTask,
                    isSelected,
                    key,
                    title
                });
            }
        }
    }
}
