import { Component, OnInit, OnDestroy, Input, Inject } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { combineLatestWith } from 'rxjs/operators';
import { EnvironmentServiceToken } from 'src/app/services/environment/environment-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { RemoteEventReceiversPopulatedEvent } from 'src/app/shared/constants/events';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IModelArray } from 'src/types/models/base/i-model-array';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { ISmartFormItemReference } from '../../../../../types/models/smart-forms/i-smart-form-item-reference';
import { IHelpService } from '../../../../../types/services/help/i-help-service';
import { IRemoteEventReceiver } from '../../../../../types/uiComponents/pluginWizards/IRemoteEventReceiver';
import { IRemoteEventReceivers } from '../../../../../types/uiComponents/pluginWizards/IRemoteEventReceivers';
import { ISharePointContext } from '../../../../../types/uiComponents/pluginWizards/ISharePointContext';
import { ISharePointSettingResult } from '../../../../../types/uiComponents/pluginWizards/ISharePointSettingsResult';
import { HelpServiceToken } from '../../../../services/help/help-service-factory';
import { SharePointService } from '../../../../services/plugins/share-point/share-point.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-create-references',
    templateUrl: './create-references.component.html',
    styleUrls: ['./create-references.component.less'],
    providers: [{ provide: PageComponent, useExisting: CreateReferencesComponent }]
})
export class CreateReferencesComponent extends PageComponent implements OnInit, OnDestroy {
    @Input() public config: IPopupConfiguration;

    buttonConfig: IPopupButtonsConfig = {
        next: {
            isDisabled: true
        }
    };

    /**
     * Context of the plugin wizard.
     */
    pluginContext: ISharePointContext;

    /**
     * Help link to open.
     */
    helpLink: string;

    /**
     * Flag to indictate if loading.
     */
    isLoading: boolean;

    private subscriptionSink = new Subscription();

    constructor(@Inject(EnvironmentServiceToken) private envService: IEnvironmentService,
        protected resourceService: ResourceService,
        @Inject(HelpServiceToken) private helpService: IHelpService,
        private sharePointService: SharePointService,
        private eventNotifier: EventNotifierService) {

        super(resourceService);

        this.isLoading = true;
        this.helpLink = this.helpService.getHelpUrl('createreferences_sp');
    }

    ngOnInit(): void {
        this.pluginContext = this.config.context as ISharePointContext;

        this.loadData();
    }

    ngOnDestroy(): void {
        this.subscriptionSink.unsubscribe();
    }

    // Methods required by the abstract class.
    beforePageDisplayed = () => { };
    beforeNavigateNext = () => { };
    beforeNavigatePrevious = () => { };

    /**
     * Method that will invoke once the item references component is done loading.
     */
    public loadData(): void {

        //Set the workflow name
        this.pluginContext.workflowFqn = this.envService.workflowFullname;
        this.pluginContext.createItemReferencesHeading = this.resourceService.getString('sharePoint_integration_found_references');

        const saveSharePointSettingsSub = this.saveSharePointSettings(
            this.pluginContext.serviceInstance,
            this.pluginContext.listId,
            this.pluginContext.subSiteRelativeUrl
        )
            .subscribe(([settingId, smartObjectName]) => {
                this.sharePointService
                    .getItemReferences(smartObjectName, this.pluginContext.isDocLib)
                    .subscribe((itemReferences: IModelArray<ISmartFormItemReference>) => {
                        this.pluginContext.sourceItemReferences = itemReferences;
                    });

                const fetchAvailableSharePointEvents$ = this.fetchAvailableSharePointEvents();
                const getEventsForWorkflowSettingSub = this.sharePointService
                    .getEventsForWorkflowSetting(settingId, this.pluginContext.workflowFqn)
                    .pipe(combineLatestWith(fetchAvailableSharePointEvents$))
                    .subscribe(([workflowSettingsEvents]) => {
                        if (this.pluginContext.existingIntegration) {
                            //Populate saved items that was previously selected. Loaded plugin information.
                            this.populateEvents(this.pluginContext.savedEvents);
                        } else if (workflowSettingsEvents.length === 0 && this.envService.referrer.settingid) {
                            // Check if entry point from SharePoint/settingId provided in the referrer.
                            // If it is load it from there, otherwise load it from previous saved values.
                            // This is after we requested the events with the other settingId? Is this necessary?
                            // Will they perhaps always be the same ?
                            this.pluginContext.settingId = this.envService.referrer.settingid;

                            const getSharePointSettingEventsSub = this.sharePointService
                                .getSharePointSettingEvents(this.envService.referrer.settingid)
                                .subscribe((eventReceivers: IRemoteEventReceivers) => {
                                    this.populateEvents(eventReceivers.EventDataCollection);
                                });

                            this.subscriptionSink.add(getSharePointSettingEventsSub);
                        } else {
                            //Populate saved items that was previously selected.
                            this.populateEvents(workflowSettingsEvents);
                        }

                        this.buttonConfig.next.isDisabled = false;
                        this.notifyConfigChanged();
                    });

                this.subscriptionSink.add(getEventsForWorkflowSettingSub);
            });

        this.subscriptionSink.add(saveSharePointSettingsSub);
    }

    /**
     * Method that will execute if an item reference on the directive was removed.
     */
    public updateHeading(): void {
        this.pluginContext.unmappedFieldsHeading = this
            .resourceService
            .getString('sharePoint_integration_existing_references')
            .replace('{0}', this.pluginContext.newItemReferences.length.toString())
            .replace('{1}', this.pluginContext.itemName)
            .replace('{2}', this.pluginContext.isDocLib
                ? this.resourceService.getString('sharePoint_integration_found_references_library')
                : this.resourceService.getString('sharePoint_integration_found_references_list')
            );

        this.isLoading = false;
    }

    public setNewItemReferences(newItemReferences: ISmartFormItemReference[]): void {
        this.pluginContext.newItemReferences = newItemReferences;
        this.updateHeading();
    }

    public updateReferenceToCreateCount(count: number): void {
        if (count >= 0) {
            this.pluginContext.newReferenceCount = count;
        }
    }

    /**
     * Needs the serviceInstance, listId, subSiteRelativeUrl to exist on plugin context.
     *
     * Saves the settingId, siteUrl, smartObjectName
     *
     * @returns
     */
    private saveSharePointSettings(serviceInstance: string, listId: string, subSiteRelativeUrl: string): Observable<[string, string]> {
        return new Observable<[string, string]>(subscriber => {

            //Don't save a new integration when edit, this will cause multiple entries with new settingId
            if (this.pluginContext.existingIntegration) {
                // The plugin context already has the correct values, and there are no changes yet
                subscriber.next([
                    this.pluginContext.settingId,
                    this.pluginContext.smartObjectName
                ]);
            } else {
                this.sharePointService
                    .saveSharePointSettings(
                        serviceInstance,
                        listId,
                        subSiteRelativeUrl)
                    .subscribe((settingResult: ISharePointSettingResult) => {
                        //Set the necessary results
                        this.pluginContext.settingId = settingResult.SettingId;
                        this.pluginContext.siteUrl = settingResult.SharePointSiteUrl;
                        this.pluginContext.smartObjectName = settingResult.ListSmartObject;

                        subscriber.next([
                            this.pluginContext.settingId,
                            this.pluginContext.smartObjectName
                        ]);
                    });
            }
        });
    }

    /**
     * Needs nothing.
     *
     * Sets remoteEventReceivers.
     *
     * @returns
     */
    private fetchAvailableSharePointEvents = (): Observable<void> => new Observable<void>(subscriber => {
        this.sharePointService
            .getSharePointAvailableEvents()
            .subscribe((events: IRemoteEventReceivers) => {
                //Set the events & default the selected stat to false.
                this.pluginContext.remoteEventReceivers = events;
                this.pluginContext.remoteEventReceivers.EventDataCollection.map((item) => {
                    item.Selected = false;
                });

                // The StartWorkflowEvents component shouldn't make it's requests before we populate the events.
                this.eventNotifier.addEvent(RemoteEventReceiversPopulatedEvent).next(events);
                subscriber.next();
            });
    });

    /**
     * Method to populate the events based on the collection returned.
     */
    private populateEvents(events: IRemoteEventReceiver[]) {

        //Populate UI with settings values that was set on the artifact page.
        this.pluginContext.originalReceiverString = '';

        for (const event of events) {
            for (const eventDataCollection of this.pluginContext.remoteEventReceivers.EventDataCollection) {
                if (event.Id === eventDataCollection.Id) {
                    eventDataCollection.Selected = true;
                    this.pluginContext.originalReceiverString = this.pluginContext.originalReceiverString +
                        eventDataCollection.Id + ';';
                }
            }
        }

        //Remove the last ;
        if (this.pluginContext.originalReceiverString.length > 0) {
            this.pluginContext.originalReceiverString = this
                .pluginContext
                .originalReceiverString
                .substring(0, this.pluginContext.originalReceiverString.length - 1);
        }

        this.pluginContext.eventReceiverString = this.pluginContext.originalReceiverString;

        this.sharePointService.detectChanges(this.pluginContext);
    }
}
