import { Directive, ElementRef, Inject, Input, OnInit } from '@angular/core';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { EnvironmentServiceToken } from '../../services/environment/environment-service-factory';

@Directive({
    selector: '[k2-tab-index]'
})
export class TabIndexDirective implements OnInit {
    @Input('k2-tab-index') public tabIndex?: string;

    private onPluginStateChangedCallback: sourceCode.k2Designer.services.core.IPluginStateChangedCallback;

    constructor(private element: ElementRef<HTMLElement>,
        @Inject(EnvironmentServiceToken) private _envService: IEnvironmentService) {
        // Register a handler for this element to update the tabindex attribute when the pluginstates changes
        this.onPluginStateChangedCallback = this._envService.listenActiveStudioItemPluginStateChanged(this.onPluginStateChanged.bind(this));
    }

    ngOnInit(): void {
        // Read the value from the tabindex attribute if not set in the directive attribute
        if (!this.tabIndex && this.element.nativeElement.hasAttribute('tabindex')) {
            this.tabIndex = this.element.nativeElement.getAttribute('tabindex');
        }

        this.setElementTabIndex();
    }

    ngOnDestroy() {
        this._envService.removeActiveStudioItemPluginStateChangedListener(this.onPluginStateChangedCallback);
        this.onPluginStateChangedCallback = null;
    }

    /**
     * Indicates if the targeted element is currently displayed in the viewport
     * Take note that the logic is flakey, it merely checks to see if any plugin is in an expanded state.
     */
    private isElementInViewport(): boolean {
        const rez = this._envService.activeStudioItem.pluginStates.configurationPanel === 'expanded' ||
            this._envService.activeStudioItem.pluginStates.rulesDesigner === 'expanded' ||
            this._envService.activeStudioItem.pluginStates.emailBody === 'expanded' ||
            this._envService.activeStudioItem.pluginStates.recipientPanel === 'expanded' ||
            this._envService.activeStudioItem.pluginStates.filterPanel === 'expanded' ||
            this._envService.activeStudioItem.pluginStates.contextBrowser === 'expanded';

        return rez;
    }

    private onPluginStateChanged(): void {
        this.setElementTabIndex();
    }

    private setElementTabIndex(): void {
        if (!this.isElementInViewport()) {
            this.element.nativeElement.setAttribute('tabIndex', '-1');
        }
        else {
            if (this.tabIndex === null || this.tabIndex === undefined) {
                this.element.nativeElement.removeAttribute('tabIndex');
            } else {
                this.element.nativeElement.setAttribute('tabIndex', this.tabIndex);
            }
        }
    }
}
