import { Component, EventEmitter, Inject, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { IDeleteProcessContext } from '../../../../../types/models/popup/i-delete-process-context';
import { ICanvasService } from '../../../../../types/services/canvas/i-canvas.service';
import { IDataService } from '../../../../../types/services/data/i-data-service';
import { IEnvironmentService } from '../../../../../types/services/environment/i-environment-service';
import { INotificationService } from '../../../../../types/services/notification/i-notification.service';
import { ILegacyResourceService } from '../../../../../types/services/resources/i-legacy-resource.service';
import { IUserOptions } from '../../../../../types/services/user-options/i-user-options-service';
import { IValidationService } from '../../../../../types/services/validation/ivalidation.service';
import { ICloseWFData } from '../../../../../types/uiComponents/ribbon/ICloseWFData';
import { IFileInfoModel } from '../../../../../types/uiComponents/ribbon/IFileInfoModel';
import { LegacyResourceServiceToken } from '../../../../legacy-service-factories/resource-service-factory';
import { CanvasServiceToken } from '../../../../services/canvas/canvas-service-factory';
import { DataServiceToken } from '../../../../services/data/data-service-factory';
import { EnvironmentServiceToken } from '../../../../services/environment/environment-service-factory';
import { NotificationServiceToken } from '../../../../services/notification/notification-service-factory';
import { PopupService } from '../../../../services/popup/popup.service';
import { ProcessService } from '../../../../services/process/process.service';
import { UserOptionsServiceToken } from '../../../../services/user-options/user-options-service-factory';
import { ValidationServiceToken } from '../../../../services/validation/validation-service-factory';
import { ErrorSeverity } from '../../../../shared/enums/model/error-severity';
import { ToastrTypes } from '../../../../shared/enums/model/toastr-types';

@Component({
    selector: 'app-tab-item-info',
    templateUrl: './tab-item-info.component.html',
    styleUrls: ['./tab-item-info.component.less']
})
export class TabItemInfoComponent implements OnInit, OnDestroy {

    @Input() public getString: (resourceId: string, collectionId?: string) => string;
    @Output() public isBusyDeploying = new EventEmitter<boolean>();
    @Output() public isBusyClosing = new EventEmitter<ICloseWFData>();
    @Input() public showNoStartEventsMessage: boolean;
    @Input() fileItemConfig: sourceCode.k2Designer.objectModel.plugin.IConfiguration;
    @Input() public fileItemInfo: IFileInfoModel;

    public isDeployingProcess: boolean;
    public containsWarnings: boolean;
    public containsErrors: boolean;
    public validationGroups: sourceCode.k2Designer.objectModel.ribbon.IValidationGroup[];
    public workflowTitle: string;
    public workflowDesc: string;

    private _errorListener: Function;
    private _warningListener: Function;

    constructor(@Inject(EnvironmentServiceToken) private _envService: IEnvironmentService,
        private _processService: ProcessService,
        @Inject(UserOptionsServiceToken) private _userOptionsService: IUserOptions,
        @Inject(CanvasServiceToken) private _canvasService: ICanvasService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        @Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        @Inject(DataServiceToken) private _dataService: IDataService,
        @Inject(NotificationServiceToken) private _notificationService: INotificationService,
        private _popupService: PopupService) {

        //TODO: This flag should be set in the process service where deployment is done to ensure it does not reset itself when ribbon unloads
        this.isDeployingProcess = false;
        this.containsWarnings = false;
        this.validationGroups = [];
    }

    ngOnInit(): void {
        //Show start events warning messages:
        this.setupGotItButtons();

        const errorList = this._validationService.getErrorList(ErrorSeverity.Error);
        this.containsErrors = errorList.length > 0;
        const warningList = this._validationService.getErrorList(ErrorSeverity.Warning);
        this.containsWarnings = warningList.length > 0;

        //Listen for changes on validation service, update the containsError flag if there is any errors with error ErrorSeverity
        this._errorListener = this._validationService.listenForErrorList(this.errorListUpdated.bind(this),
            ErrorSeverity.Error);
        if (this.containsErrors) {
            this.errorListUpdated(errorList);
        }

        //Listen for changes on validation service, update the containsWarning flag if there is any errors with warning ErrorSeverity
        this._warningListener = this._validationService.listenForErrorList(this.warningListUpdated.bind(this),
            ErrorSeverity.Warning);
        if (this.containsWarnings) {
            this.warningListUpdated(warningList);
        }

        //Workflow Information:
        this.workflowTitle = this._envService.workflowFullname;
        this.workflowDesc = (this._processService.root.description)
            ? this._processService.root.description
            : this.getString('concierge_workflow_no_results');
    }

    ngOnDestroy(): void {
        this._errorListener();
        this._warningListener();
    }

    public getDeploymentStatus(): string {
        let result = '';

        if (this.isDeployingProcess) {
            result = this._resourceService.getString('workflow_deployment_in_progress');
        }
        else if (this.containsErrors) {
            result = this._resourceService.getString('workflow_deployment_disabled_errors');
        }
        else if (this._dataService.getOpenRequestCount() > 0
            || this._processService.getInstructionCount() > 0
            || this._processService.allowProcessing() === false) {
            result = this._resourceService.getString('workflow_deployment_disabled_saving_workflow_to_server');
        }
        return result;
    }

    //Deploy
    private deploymentToastr: PNotify;
    public deployProcess(): void {
        if (this.isDeployingProcess === false && this.isDeploymentEnabled()) {
            this.isDeployingProcess = true;

            const onEventRaisedCallBack = () => {
                try {

                    this._processService.deployProcess(this.onDeploymentCompleted.bind(this));
                }
                catch (e) {
                    this._processService.enableProcessing();
                    this.isDeployingProcess = false;
                    if (this.deploymentToastr) {
                        this.deploymentToastr.remove();
                        this.deploymentToastr = null;
                    }
                    console.error('deployProcess error: ' + e.toString());
                }
            };

            this.deploymentToastr = this._notificationService.toastr(this.getString('workflow_deployment_started_title'),
                this.getString('workflow_deployment_started'), ToastrTypes.deployment);
            this._processService.requestProcessingCompleteNotification(true, false, onEventRaisedCallBack);

            this.isBusyDeploying.emit(this.isDeployingProcess);
        }
    }

    public isDeploymentEnabled(): boolean {
        let result = false;

        if (this._validationService) {
            result = !this.containsErrors;
        }
        if (this._dataService.getOpenRequestCount() > 0) {
            return false;
        }
        if (this._processService.getInstructionCount() > 0) {
            return false;
        }
        if (this._processService.allowProcessing() === false) {
            return false;
        }
        return result;
    }

    private setupGotItButtons(): void {
        if (!this._userOptionsService || !this._envService) {
            this.showNoStartEventsMessage = false;
            return;
        }

        this.showNoStartEventsMessage =
            this._userOptionsService.userOptions.tips[<number>sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypes.HelpStartWorkflowTips] &&
            !this._envService.getGlobalAttributeValue('StartWorkflowIntegrationExists');
    }

    public btnGotItClick(): void {
        this.showNoStartEventsMessage = false;

        this._userOptionsService.setTipValue(sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypes.HelpStartWorkflowTips, false);
    }

    public btnShowStartStepClick(): void {
        this._canvasService.selectStepByReference(this._processService.root.nodes[0].getReference());
    }

    /**
     * Invokes the delete confirmation popup
     */
    public deleteProcess(): void {
        const context = <IDeleteProcessContext>{
            processToDeleteFQN: this._envService.workflowFullname,
            onDeleteCompletedCallback: this.onDeleteComplete.bind(this)
        };

        this._popupService.showDeleteProcessDialog(this.getString('delete_process_heading_text'), context);
    }

    /**
     * This function is the callback that will fire after workflow has been deleted from server.
     * It will close and return to exit url (designer)
     *
     * @param deleteSuccess - indicates if delete was successful. if delete was not succesful, error toast would have displayed and popup
     * and workflow designer will remain open.
     */
    private onDeleteComplete(deleteSuccess: boolean): void {
        if (deleteSuccess) {
            //add parameters to exit url that will let exit page know that workflow was deleted
            const processId = this._envService.query.guid || this._envService.query.id;
            const params = {
                wfaction: 'delete',
                wfid: processId
            };
            //close without flushing (nothing to flush)
            this.isBusyClosing.emit({
                close: true,
                item: null,
                params,
                categoryId: this.fileItemInfo?.category?.id
            });
        }
    }

    private onDeploymentCompleted(title: string, message: string, type: ToastrTypes): void {
        this._processService.enableProcessing();
        this.isDeployingProcess = false;

        if (type === ToastrTypes.success) {
            const adr = this._envService.autoDiscoverResult;
            if (adr && adr.webService && adr.webService.Value && adr.webService.Value.length > 0) {
                let urlStr: string = sourceCode.k2Designer.utilities.getsmartFormsBaseUrl(this._envService);

                // All workflows
                if (urlStr[urlStr.length - 1] !== '/') {
                    urlStr += '/Management/#WorkflowServer/Workflows';
                }
                else {
                    urlStr += 'Management/#WorkflowServer/Workflows';
                }

                // Attempt Workflow Category
                if (this._envService.workflowPrefixedCategory) {
                    urlStr += '/' + this._envService.workflowPrefixedCategory;
                }

                message += ` ${this.getString('workflow_deployment_management_pre_link')}
<a href="${urlStr}" target="_blank" class="">${this.getString('workflow_deployment_management_link')}</a>`;

            }

            this.isBusyDeploying.emit(this.isDeployingProcess);
        }

        if (this.deploymentToastr && (this.deploymentToastr.state === 'open' || this.deploymentToastr.state === 'opening')) {
            this._notificationService.toastr(title, message, type, 0, null, this.deploymentToastr);
        }
        else {
            this._notificationService.toastr(title, message, type, 0, null, null);
        }
        this.deploymentToastr = null;

        //if (!this._scope.$$phase)
        //    this._scope.$apply();
    }

    /**
     * Listen for changes on validation service, update the containsWarning flag if there is any errors with warning severity
     */
    private warningListUpdated(warningList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        this.containsWarnings = warningList.length > 0;

        const deployWarningCnf = <sourceCode.k2Designer.objectModel.ribbon.IDeploymentWarningsConfiguration>this.fileItemConfig;

        const validationGroupsDictionary = {};

        const warningsDictionary = {};

        this.validationGroups = [];

        for (const warning of warningList) {
            warningsDictionary[warning.validationType] = true;
        }

        //go through each warning and filter each validationGroup object down to objects that have validationTypes that match for that warning's validation type
        this.validationGroups = deployWarningCnf.validationGroups.filter((validationGroup) => {
            const valTypes = validationGroup.validationTypes.filter((valType) => {

                if (warningsDictionary[sourceCode.k2Designer.objectModel.core.model.ValidationTypes[valType]]
                    && !validationGroupsDictionary[validationGroup.validationGroupName]) {
                    validationGroupsDictionary[validationGroup.validationGroupName] = true;
                    return true;
                }
                return false;

            });
            return valTypes.length > 0;
        });

    }

    /**
     * Listen for changes on validation service, update the containsError flag if there is any errors with error severity
     */
    private errorListUpdated(errorList: Array<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>): void {

        this.containsErrors = errorList.length > 0;
    }
}
