import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { IStates } from '../../../../../../types/models/rules/i-states';
import { IValidatedComponent } from '../../../../../../types/validation/i-validated-component';
import { ConfigurationService } from '../../../../../services/configuration/configuration-service';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../../../services/undo-redo/undo-redo-service-factory';
import { States } from '../../../../../shared/control-helpers/states';
import IDropDownMenuItem from '../../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../../shared/enums/drop-down/drop-down-triangle-position';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-choose-control-within-working-hours-label',
    templateUrl: './choose-control-within-working-hours-label.component.html',
    styleUrls: ['./choose-control-within-working-hours-label.component.less']
})
export class ChooseControlWithinWorkingHoursLabelComponent extends BaseResourceComponent implements OnInit {
    @Input() isChecked: boolean;
    @Input() isDisabled: boolean;
    @Input() isToggled: boolean;
    @Input() isDynamicMode: boolean;
    @Input() selectedValue: string;
    @Input() additionalDropdownItems: IDropDownMenuItem[];
    @Input() footerItems: IDropDownMenuItem[];
    @Input() isDarkStyle: boolean;
    @Input() modelValue: IValidatedComponent;
    @Input() customLabelClass: string;
    //Control states for styling
    @Input() states: IStates = new States();

    @Output() onClick = new EventEmitter<void>();

    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropdownMenuItemTemplates: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    workingHoursUrl: string;

    constructor(resourceService: ResourceService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        private configurationService: ConfigurationService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.workingHoursUrl = this.configurationService.getWorkingHoursUrl();
    }

    onClicked(): void {
        this.onClick.emit();
    }

    itemSelected(): void {
        this.undoRedoService.addUndoMarker();
    }
}
