import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockSmartFieldComposerService } from '../../../testing/mock-services/smart-field-composer-service';

const smartFieldComposerServiceFactory = () => environment.devHarness === true
    ? new MockSmartFieldComposerService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.SmartFieldComposerService);

export const SmartFieldComposerServiceToken = new InjectionToken<ISmartFieldComposerService>('ISmartFieldComposerService');

export const SmartFieldComposerServiceFactory = {
    provide: SmartFieldComposerServiceToken,
    useFactory: smartFieldComposerServiceFactory,
    deps: [

    ]
};
