import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { IDataService } from 'src/types/services/data/i-data-service';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IIdentityService } from 'src/types/services/identity/i-identity-service';
import { DataServiceToken } from '../data/data-service-factory';

@Injectable({
    providedIn: 'root'
})
export class IdentityService implements IIdentityService{

    private _executingHash: number;

    constructor(
        @Inject(DataServiceToken) private _dataService: IDataService,
        @Inject(EnvironmentServiceToken) private _environmentService: IEnvironmentService
    ) {
    }

    /**
     * Retrive the identities by group.
     *
     * @param label Name of the group
     * @param identityFqName identity Fully Qualified Name
     * @returns
     */
    getIdentitiesByGroup(label: string, identityFqName: string): Observable<any> {
        return new Observable(subscriber => {
            this._dataService.httpGet('api/identity/identitiesbygroup',
                {
                    host: this._environmentService.currentEnvironment.hostId,
                    label,
                    identityFQName: identityFqName,
                    page: 0,
                    pageSize: 20
                }, (result: any, error: boolean) => {
                    if (error) {
                        subscriber.error(result);
                    } else {
                        subscriber.next(result);
                    }
                });
        });
    }


    /**
     * Retrieves all the identity labels/groups
     *
     * @returns
     */
    getLabels(): Observable<any> {
        // any is used in the WFD as well. :,(
        return new Observable(subscriber => {
            this._dataService.httpGet('api/identity/getlabels',
                {
                    host: this._environmentService.currentEnvironment.hostId
                }, (result: any, error: boolean) => {
                    if (error) {
                        subscriber.error(result);
                    } else {
                        subscriber.next(result);
                    }
                });
        });
    }

    /**
     * Retrieves all the identities
     *
     * @param label label name
     * @param filter partial name / filter
     * @param returnTypes Types to include
     * @returns
     */
    getIdentities(label: string, filter: string, returnTypes: string[]): Observable<any> {

        if (this._executingHash) {
            this._dataService.abortRequest(this._executingHash);
        }

        return new Observable(subscriber => {
            this._executingHash = this._dataService.httpPost('api/identity/getidentities', {
                host: this._environmentService.currentEnvironment.hostId,
                label,
                partialName: filter,
                page: 0,
                pageSize: 20,
                typesToReturn: JSON.stringify(returnTypes)
            }, (result: any, error: boolean) => {
                if (error) {
                    subscriber.error(result);
                } else {
                    subscriber.next(result);
                }
            },
                this._environmentService,
                true,
                null,
                false);
        });
    }

    /**
     * Aborts the current getIdentities request, if one is in progress
     * */
    abortGetIdentities(): void {
        if(this._executingHash) {
            this._dataService.abortRequest(this._executingHash);
        }
    }
}
