/**
 * Type of toastr that should display
 * */
export enum ToastrTypes {
    /**
     * When process is deploying
     * */
    deployment,
    /**
     * General informative text
     * */
    information,
    /**
     * When an error occured
     * */
    error,
    /**
     * COnfirmation required from user
     * */
    confirm,
    /**
     * Deployment was a success
     * */
    success,
    /**
     * Copying text to clipboard was a success
     * */
    copySuccess,
    /**
     * Copying text to clipboard failed
     * */
    copyFail
}
