import { AfterViewChecked, ChangeDetectorRef, Component, ElementRef, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { IConfiguredDestinationSetStatement } from '../../../../../types/models/rules/i-configured-destination-set-statement';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { rules } from '../../../../shared/constants/rules';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { EventTypes } from '../../../../shared/enums/model/event-types';
import { RecipientTypes } from '../../../../shared/enums/rules/recipient-types';
import { StatementBaseComponent } from '../../base/statement-base/statement-base.component';

@Component({
    selector: 'k2-destination-set-statement',
    templateUrl: './destination-set.component.html',
    styleUrls: ['./destination-set.component.less']
})
export class DestinationSetComponent extends StatementBaseComponent implements OnInit, AfterViewChecked {

    @Input() public index: number;

    public isDropdownButtonSelected = false;
    //Enums:
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    public destinationSets: IDropDownMenuItem[] = [];
    private _recipientsArrayChangedRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover;
    private _recipientPropertyChangedRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover;

    @ViewChild('recipientDropDownArrow') ddArrow: ElementRef<HTMLElement>;

    constructor(@Inject(SelectionServiceToken) _selService: ISelectionService,
        private _rulePluginSvc: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        eventService: EventNotifierService,
        private _el: ElementRef,
        private _changeDetector: ChangeDetectorRef) {
        super(_selService, _rulePluginSvc, commandService, configPanelService, undoRedoService, eventService, _el);
    }

    ngOnInit(): void {
        super.ngOnInit();

        this.undoRedoService.forceDisable();

        this._setupDestinationSets();

        this._recipientsArrayChangedRemover = this._clientEventConfig.destinationSets.listen(EventTypes.arrayChanged, this._onDestinationSetsArrayChanged.bind(this));
        this._recipientPropertyChangedRemover = this._clientEventConfig.destinationSets.listen(EventTypes.anyPropertyChanged, this._onDestinationSetsPropertyChanged.bind(this));

        this.undoRedoService.resetForceDisable();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this._recipientsArrayChangedRemover) {
            this._recipientsArrayChangedRemover();
        }
        if (this._recipientPropertyChangedRemover) {
            this._recipientPropertyChangedRemover();
        }
    }

    menuAlignElem: ElementRef;
    ngAfterViewChecked(): void {
        if (this.menuAlignElem != this.ddArrow) { // this is to fix : Expression has changed after it was checked.
            this.menuAlignElem = this.ddArrow;
            this._changeDetector.detectChanges();
        }
    }

    /**
     * Get statement model as type ConfiguredDestinationSetStatement
     *
     * @param item
     */
    public get configuredDestinationSetStatement(): IConfiguredDestinationSetStatement {
        return this.statementModel as IConfiguredDestinationSetStatement
            || {} as IConfiguredDestinationSetStatement;
    }

    public onDestinationItemSelected = (item: IDropDownMenuItem): void => {
        //The destination set dropdown is a multiselect, must update on select and deselect
        const configuredSelectedItem = this.configuredDestinationSetStatement.selectedItems.find(d => d.key === item.key);
        const linkDestinationSetReferenceIndex = this.configuredDestinationSetStatement.linkedDestinationSetReferences.indexOf(item.key);

        if (item.isSelected) {
            if (!configuredSelectedItem) {
                //Selected item must be added to the list of selected items in configuredDestinationSetStatement.selectedItem
                this.configuredDestinationSetStatement.selectedItems.push(
                    sourceCode.k2Designer.objectModel.workflow.configuredRules.ConfiguredRuleDisplayItem.createInstance({
                        title: item.title,
                        icon: item.icon,
                        key: item.key
                    })
                );
            }
            if (linkDestinationSetReferenceIndex < 0) {
                //Selected item must be added to the list of linked destinationSetReferences
                this.configuredDestinationSetStatement.linkedDestinationSetReferences.push(item.key);
            }
        }
        else {
            if (configuredSelectedItem) {
                //Deselected item must be removed from the list of selected items in configuredDestinationSetStatement.selectedItem
                configuredSelectedItem.removeFromModelArray();
            }

            if (linkDestinationSetReferenceIndex > -1) {
                //Selected item must be removed from the list of linked destinationSetReferences
                this.configuredDestinationSetStatement.linkedDestinationSetReferences.splice(linkDestinationSetReferenceIndex, 1);
            }

            //If unselected all items in the destination list, delete the statement:
            if (this.configuredDestinationSetStatement.selectedItems.length === 0) {
                this.delete();
            }
        }
    };

    public addDraggingState = super.addDraggingState.bind(this);

    public removeDraggingState = super.removeDraggingState.bind(this);

    /**
     * Creates drag ghost image
     */
    public getDragImageFn = super.getDragImage.bind(this);

    private get _clientEventConfig(): sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfiguration {
        return this._rulePluginSvc.contextModel.getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfiguration>();
    }

    private _setupDestinationSets(): void {
        //clear dropdown menu items
        this.destinationSets.length = 0;

        this._clientEventConfig.destinationSets.forEach(destinationSet => {
            const reference = destinationSet.getReference();
            let isSelected = this.configuredDestinationSetStatement.linkedDestinationSetReferences.indexOf(reference) > -1;
            if (!isSelected) {
                //If it was not found in the linkedDestinationSetReferences, check in selected items
                isSelected = !!this.configuredDestinationSetStatement.selectedItems.find(d => d.key === reference);
            }

            this.destinationSets.push({
                title: destinationSet.title,
                icon: this._getDestinationSetIcon(destinationSet),
                titleBinding: {
                    objectReference: destinationSet,
                    propertyName: 'title'
                },
                key: reference,
                isSelected
            });
        });

        //Select the first item by default if the selected item was not found
        if (this.configuredDestinationSetStatement.selectedItems.length === 0 &&
            this.destinationSets.length > 0) {
            const selectedDestinationSet = this.destinationSets[0];
            selectedDestinationSet.isSelected = true;

            //Selected item must be added to the list of selected items in configuredDestinationSetStatement.selectedItem
            this.configuredDestinationSetStatement.selectedItems.push(
                sourceCode.k2Designer.objectModel.workflow.configuredRules.ConfiguredRuleDisplayItem.createInstance({
                    title: selectedDestinationSet.title,
                    icon: selectedDestinationSet.icon,
                    key: selectedDestinationSet.key
                })
            );
            if (this.configuredDestinationSetStatement.linkedDestinationSetReferences.indexOf(selectedDestinationSet.key) < 0) {
                //Selected item must be added to the list of linked destinationSetReferences
                this.configuredDestinationSetStatement.linkedDestinationSetReferences.push(selectedDestinationSet.key);
            }
        }
    }

    private static _destSetImplementor = 'IDestinationSetImplementor';

    private _onDestinationSetsPropertyChanged(object: sourceCode.k2Designer.objectModel.core.model.IModelObject, name: string): void {
        //To prevent too many refreshes on unimportant changes, for now we only refresh on title changed
        if (object?.implementor?.name === DestinationSetComponent._destSetImplementor && name === 'title') {
            this._setupDestinationSets();
            this._refreshSelectedItems();
        }
    }

    private _onDestinationSetsArrayChanged(array: sourceCode.k2Designer.objectModel.core.model.IModelArray<any>): void {
        //To prevent too many refreshes on unimportant changes, for now we only refresh on destination set array changed (not other internal arrays)
        if (array?.implementor?.name === DestinationSetComponent._destSetImplementor) {
            this._setupDestinationSets();
            this._refreshSelectedItems();
        }
    }

    private _refreshSelectedItems(): void {
        this.configuredDestinationSetStatement.selectedItems.splice(0, this.configuredDestinationSetStatement.selectedItems.length);

        for (let i = 0; i < this.destinationSets.length; i++) {
            if (this.destinationSets[i].isSelected) {
                this.configuredDestinationSetStatement.selectedItems.push(
                    sourceCode.k2Designer.objectModel.workflow.configuredRules.ConfiguredRuleDisplayItem.createInstance({
                        title: this.destinationSets[i].title,
                        icon: this.destinationSets[i].icon,
                        key: this.destinationSets[i].key
                    })
                );
            }
        }
    }

    private _getDestinationSetIcon(destinationSet: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDestinationSet): string {

        let icon: string = rules.icons.recipientGroup;

        if (destinationSet.isRecipient && destinationSet.destinationItems?.length > 0) {

            //is a recipient but may have a group as its type
            switch (destinationSet.destinationItems[0].type) {

                case RecipientTypes.group:
                case RecipientTypes.queue:
                    icon = rules.icons.recipientGroup;
                    break;
                case RecipientTypes.user:
                default:
                    icon = rules.icons.recipient;
                    break;
            }
        }

        return icon;
    }
}
