export class DragDropData<sourceType, extraType, contextType> implements IDragDropData {

    public extraData: extraType;

    private _source: sourceType;
    private _context: contextType;
    private _getDragData: <dragType>(source: sourceType, context: any, extraData: extraType) => dragType;
    private _getDropData: <dragType, dropType>(source: sourceType, context: any, dragData: dragType, extraData: extraType) => dropType;

    constructor(
        source: sourceType,
        context: contextType,
        getDragData: <dragType>(source: sourceType) => dragType,
        getDropData: <dragType, dropType>(source: sourceType, dragData: dragType) => dropType,
        extraData: extraType = null) {

        this.extraData = extraData;
        this._context = context;
        this._source = source;
        this._getDragData = getDragData;
        this._getDropData = getDropData;
    }

    //Stricter compilation in ts 3.2. Return type should be sourceType. For now return any
    public getSource(): any {
        return this._source;
    }

    //Stricter compilation in ts 3.2. Return type should be sourceType. For now return any
    public getContext(): any {
        return this._context;
    }

    public getDragData<dragType>(): dragType {
        return this._getDragData<dragType>(this._source, this._context, this.extraData);
    }

    public getDropData<dragType, dropType>(dragData: dragType = null): dropType {
        return this._getDropData<dragType, dropType>(this._source, this._context, dragData, this.extraData);
    }

    /**
     * Requests that the callback be invoked when the item is dropped
     */
    public ntfOnDrop(callback: () => void): void {
    }

}
