import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockRibbonBarService } from '../../../testing/mock-services/ribbon-bar-service';
import { IRibbonService } from '../../../types/services/ribbon-bar/i-ribbon-bar-service';

const ribbonBarServiceFactory = () => environment.devHarness === true
    ? new MockRibbonBarService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.RibbonService);

export const RibbonBarServiceToken = new InjectionToken<IRibbonService>('IRibbonService');

export const RibbonBarServiceFactory = {
    provide: RibbonBarServiceToken,
    useFactory: ribbonBarServiceFactory,
    deps: [

    ]
};
