import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { IPopupService } from '../../../../../types/services/popup/i-popup-service';
import { IProcessPluginDefinitionContext } from '../../../../../types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { PopupService } from '../../../../services/popup/popup.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { popup } from '../../../../shared/constants/wizard-popup';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-summary-result',
    templateUrl: './summary-result.component.html',
    styleUrls: ['./summary-result.component.less'],
    providers: [{ provide: PageComponent, useExisting: SummaryResultComponent }]
})
export class SummaryResultComponent extends PageComponent implements OnInit, OnDestroy {
    buttonConfig: IPopupButtonsConfig;

    @Input() public config: IPopupConfiguration;

    /**
     * Heading property used to bind on form.
     */
    @Input() public updateFormHeading: string;

    /**
     * Context of the plugin wizard.
     */
    get pluginContext(): IProcessPluginDefinitionContext {
        return this.config?.context as IProcessPluginDefinitionContext;
    };

    formLoaded = false;

    private _closingEventSubscription: Subscription;
    private _getFormSub: Subscription;

    constructor(private popupService: PopupService,
        protected resourceService: ResourceService,
        private eventService: EventNotifierService,
        private _smartFormsIntegrationService: SmartFormsIntegrationService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.updateFormHeading = this.resourceService.getString('wizard_workflow_integration_update_form').replace('{0}',
            this.pluginContext.form.title);

        //Subscribes to an event emited by the parent PopupHost Component on closing
        //TODO: With User Story 935966: <Angular> Convert PopupHostController to a Component using Angular Latest
        //emit event popup.events.onClosing when clicking the popup close button
        this._closingEventSubscription = this.eventService.subscribe(popup.events.onClosing, this.onClosing);

        this._getFormSub = this._smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formLoaded = true;
            });
    }

    ngOnDestroy(): void {
        this._closingEventSubscription.unsubscribe();
        this._getFormSub.unsubscribe();
    }

    getFromResx = (key: string) => this.resourceService.getFromResx('Wizards', key);

    beforePageDisplayed() {
        this.notifyPageStateChanged(PopupState.Busy);
        this._smartFormsIntegrationService
            .updateForm(this.pluginContext)
            .then(() => {
                this._smartFormsIntegrationService
                    .createItemReferencesAndPlugins(this.pluginContext);
                this.pluginContext.updatedFormSuccesfully = true;

                // Copied from the SF integration service, but I don't want to mess with scopes now, so it stays.
                this._smartFormsIntegrationService.clearCache(); // this should be removed once the popuphost is migrated. ensure that the service scope is on the host

                this.notifyPageStateChanged(PopupState.Normal);
            })
            .catch(error => {
                this.notifyPageStateChanged(PopupState.Error);

                if(error){
                    let errorResult;
                    try {
                        errorResult = JSON.parse(error);
                    } catch {
                        console.error('Error trying to parse the error result: ', error);

                    }
                    if (errorResult?.responseJSON.InnerException) {
                        this.pluginContext.errorMessage = errorResult.responseJSON.InnerException.ExceptionMessage;
                    } else {
                        this.pluginContext.errorMessage = errorResult?.responseJSON.Message || '';
                    }
                } else {
                    this.pluginContext.errorMessage = this.getString('smartforms_integration_summary_error');
                }

                this.pluginContext.updatedFormSuccesfully = false;
                this.pluginContext.showSuccessSummary = false;

            })
            .finally(()=>{
                this.pluginContext.formUpdateDone = true;
            });
    }

    beforeNavigateNext() {}
    beforeNavigatePrevious() {}

    /**
     * Method to create item references if form update failed.
     */
    public createItemReferences(): void {
        this._smartFormsIntegrationService.createItemReferencesAndPlugins(this.pluginContext);
        this.popupService.hide('', this.config);
    }

    /**
     * Called when dialog closes.
     */
    private onClosing = (key: string, result: string): void => {
        //TODO: Investigate if this is really necessary
        //this._smartFormsIntegrationService.resetPageIndicators(this.pluginContext);
        this._smartFormsIntegrationService.clearCache();
    };
}
