import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockDesignerService } from '../../../testing/mock-services/designer-service';
import { IDesignerService } from '../../../types/services/designer/i-designer-service';

const designerServiceFactory = () => environment.devHarness === true
    ? new MockDesignerService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.DesignerService);

export const DesignerServiceToken = new InjectionToken<IDesignerService>('IDesignerService');

export const DesignerServiceFactory = {
    provide: DesignerServiceToken,
    useFactory: designerServiceFactory,
    deps: [

    ]
};
