import { ApplicationRef, ComponentFactoryResolver, ComponentRef, EmbeddedViewRef, Inject, Injectable, Injector, Type } from '@angular/core';
import { IDataField } from '../../../types/models/data-field/i-data-field';
import { IActionPopupConfiguration } from '../../../types/models/popup/i-action-popup-configuration';
import { ICategoryDialogContext } from '../../../types/models/popup/i-category-dialog-context';
import { ICategoryDialogData } from '../../../types/models/popup/i-category-dialog-data';
import { ICategoryItem } from '../../../types/models/popup/i-category-item';
import { IDeleteProcessContext } from '../../../types/models/popup/i-delete-process-context';
import { IPopup } from '../../../types/models/popup/i-popup';
import { IPopupButton } from '../../../types/models/popup/i-popup-button';
import { IPopupConfiguration } from '../../../types/models/popup/i-popup-configuration';
import { IUserDialogContext } from '../../../types/models/popup/i-user-dialog-context';
import { IOutcome } from '../../../types/models/process/outcomes/i-outcome';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { ILegacyPopupService } from '../../../types/services/popup/i-legacy-popup-service';
import { IContextPopupConfiguration } from '../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { IContextWizardPopupConfiguration } from '../../../types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { IHierarchyDialogContext } from '../../../types/uiComponents/pluginWizards/IHierarchyDialogContext';
import { IStateChangedListener } from '../../../types/uiComponents/pluginWizards/IStateChangedListener';
import { PopupComponent } from '../../components/popup/popup.component';
import { popup } from '../../shared/constants/wizard-popup';
import { Deferred } from '../../shared/deferred';
import { PopupButtonStyleType } from '../../shared/enums/plugin-wizard/popup-button-style-type';
import { ActionPopupTemplate } from '../../shared/enums/popup/action-popup-template';
import { PopupHeaderTemplate } from '../../shared/enums/popup/popup-header-template';
import { PopupTemplate } from '../../shared/enums/popup/popup-template';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';
import { ProcessService } from '../process/process.service';
import { ResourceService } from '../resource/resource.service';
import { LegacyPopupServiceToken } from './legacy-popup-service-factory';

@Injectable({
    providedIn: 'root'
})
export class PopupService {
    popups: IPopup[] = [];

    constructor(@Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        @Inject(LegacyPopupServiceToken) private _legacyPopupService: ILegacyPopupService,
        private resourceService: ResourceService,
        private componentFactoryResolver: ComponentFactoryResolver,
        private appRef: ApplicationRef,
        private injector: Injector) { }

    /**
     * Show a popup with the specified config
     * resolves the value which the popup closed with
     *
     * @param config config for the popup to use
     */
    show<T>(config: IPopupConfiguration): Promise<T> {
        const result = new Deferred<T>();
        const popupRef = this.appendComponentToDOM(PopupComponent);
        popupRef.instance.config = config;
        this.popups.push({ config, componentRef: popupRef, result });
        return result.toPromise();
    };

    /**
     * Hide the popup with the config specified. if not specified, hides the last popup shown
     *
     * @param result the result of the popup
     * @param config the config which the popup used
     */
    hide(result: any, config?: IPopupConfiguration) {
        if (!this.popups?.length) { // legacy/wizard popup
            this._legacyPopupService.hide('okButton', result);
            return;
        }
        let popup: IPopup;
        if (config) {
            popup = this.popups.find((popup) => popup.config === config);
        } else {
            popup = this.popups[this.popups.length - 1]; // hide the last one if none specified
            config = popup?.config;
        }
        if (!popup) {
            console.error('no active popups found with the config given');
            return;
        }
        this.popups.splice(this.popups.indexOf(popup), 1);
        popup.result.resolve(result);
        this.removeComponentFromBody(popup.componentRef);
        if (config?.onClosed) {
            config.onClosed('', result);
        }
    };

    listen(callback: IStateChangedListener) {
        this._legacyPopupService.listen(callback);
    };

    /**
     * Forces the top-most popup to be dismissed with the specified result
     *
     * @param key The key of the button that invoked the popup dismissal
     * @param result A result value that indicates the reason for the dismissal
     */
    hideContextPopup(key: string, result: string) {
        if (!this.popups?.length) { // legacy/wizard popup
            this._legacyPopupService.hideContextPopup(key, result);
            return;
        }

        this.hide(result);
    };

    getInitialHeight() {
        return this._legacyPopupService.getInitialHeight();
    };

    /**
     *
     * @param actionButtons
     * @param actionTemplate
     * @param actionType
     */
    showActionDialog<T>(actionButtons: IPopupButton[], actionTemplate: ActionPopupTemplate, actionType: string): Promise<T> {
        return new Promise<T>((resolve, reject) => {
            const popupConfig = <IActionPopupConfiguration>{
                buttons: {},
                heading: this.resourceService.getString('workflow_authentication_error'),
                style: {
                    minHeight: '345px',
                    width: '265px'
                },
                template: PopupTemplate.actionPopup,
                actionButtons,
                actionTemplate,
                type: actionType,
                showFooter: false
            };

            this.show<T>(popupConfig).then((result) => resolve(result)).catch((err) => reject(err));
        });
    }

    /**
     *
     * @param heading
     * @param errorMessage
     * @param onOkClicked
     * @param onClosed
     */
    showActionErrorDialog<T>(heading: string, errorMessage: string, onClosed: () => void): Promise<T> {
        return new Promise<T>((resolve, reject) => {
            const popupConfig = <IActionPopupConfiguration>{
                buttons: {
                    closeButton: {
                        key: popup.buttons.ok,
                        text: this.resourceService.getString('OK'),
                        visible: true,
                        dismissResult: popup.buttons.okResult,
                        buttonStyleType: PopupButtonStyleType.tertiary
                    }
                },
                heading,
                template: PopupTemplate.actionPopup,
                actionButtons: [],
                actionTemplate: ActionPopupTemplate.custom,
                errorMessage,
                //type is the style override in the popup.
                type: 'custom-error',
                showFooter: true,
                bodyHeight: 92,
                onClosed
            };

            this.show<T>(popupConfig).then((result) => resolve(result)).catch(err => reject(err));
        });
    }

    /**
     *
     * @param heading
     * @param context
     * @param onCompleted
     */
    showCategoryDialog(heading: string, context: ICategoryDialogContext, onCompleted: (item: ICategoryItem, cancelled: boolean) => void): void {
        const config = {
            buttons: {
                createButton: {
                    key: popup.buttons.create,
                    text: this.resourceService.getString('category_popup_create_folder'),
                    visible: true,
                    enabled: true,
                    invokeValidation: false,
                    showLeft: true,
                    buttonStyleType: PopupButtonStyleType.tertiary
                },
                okButton: {
                    key: popup.buttons.ok,
                    text: this.resourceService.getString('workflow_contextPopup_okButton'),
                    visible: true,
                    enabled: false,
                    dismissResult: popup.buttons.okResult,
                    invokeValidation: false
                }
            },
            heading,
            context,
            template: PopupTemplate.categoryDialog,
            onClosed: (key: string, dismissResult: string) => {
                if (dismissResult === popup.buttons.okResult) {
                    if (config.returnData) {
                        const selectedItem = (<ICategoryDialogData>config.returnData).selectedItem as ICategoryItem;
                        onCompleted(selectedItem, false);
                    }
                }
                else {
                    onCompleted(null, true);
                }
            },
            footerCssClass: 'multiple-buttons'
        } as IContextPopupConfiguration;

        this.show(config);
    };

    /**
     *
     * @param config
     * @param context
     */
    showDeleteProcessDialog(heading: string, context: IDeleteProcessContext): void {
        //On OK clicked, the popup does not need to be dismissed, because when delete is done, designer will close
        const okButton: IPopupButton = {
            key: popup.buttons.ok,
            text: this.resourceService.getString('delete_process_ok_button_text'),
            visible: true,
            enabled: true,
            invokeValidation: true
        };
        const cancelButton: IPopupButton = {
            key: popup.buttons.cancel,
            text: this.resourceService.getString('delete_process_cancel_button_text'),
            visible: true,
            enabled: true,
            dismissResult: popup.buttons.cancelResult,
            invokeValidation: false,
            buttonStyleType: PopupButtonStyleType.tertiary,
            showLeft: true
        };

        const popupConfig: IPopupConfiguration = {
            buttons: { cancelButton, okButton },
            heading,
            template: PopupTemplate.deleteProcess,
            height: popup.deleteProcessDialog.defaultHeight,
            bodyHeight: popup.deleteProcessDialog.defaultBodyHeight,
            type: 'delete-process',
            context
        };

        this.show(popupConfig);
    }

    /**
     * Show the hierarchy dialog
     *
     * @param heading popup heading
     * @param disableUndoService should undo-redo service be disabled
     * @param context popup context
     * @param onCompleted callback function on completed
     */
    public showHierarchyDialog(heading: string,
        disableUndoService: boolean = false,
        context: IHierarchyDialogContext,
        onCompleted: (item: sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItem, cancelled: boolean, selectedFromRecent?: boolean) => void
    ): void {
        const popupConfig = <IContextPopupConfiguration>{
            buttons: {
                okButton: {
                    key: popup.buttons.ok,
                    text: this.resourceService.getString('workflow_contextPopup_okButton'),
                    visible: true,
                    enabled: false,
                    dismissResult: popup.buttons.okResult,
                    invokeValidation: true
                }
            },
            heading,
            context,
            template: PopupTemplate.hierarchyDialog,
            onClosed: (key: string, dismissResult: string) => {
                if (dismissResult === popup.buttons.okResult) {
                    if (popupConfig.returnData) {
                        const selectedItem: sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItemExtension = (popupConfig.returnData as any).selectedItem;

                        //Add item to recent server
                        this.environmentService.getService(sourceCode.k2Designer.services.studio.RecentsService).addItem(
                            (popupConfig.returnData as any).recentContextString,
                            selectedItem, 'key');

                        onCompleted(selectedItem, false, (popupConfig.returnData as any).selectedFromRecent);
                    }
                } else {
                    onCompleted(null, true);
                }
            },
            type: 'hierarchy',
            width: popup.hierarchyDialog.defaultWidth,
            height: popup.hierarchyDialog.defaultHeight,
            disableUndoService
        };

        this.show(popupConfig);
    }

    /**
     * Open New DataField popup (migrated NewDataField.component)
     *
     * @param processConfiguration Represents the process on which the datafield can be added.
     * @param existingDataField Datafield to configure
     * @param onFinished Callback on finish of configuration
     * @param onCancelled Callback on cancel of configuration
     * @param scope caller scope (passed on to cancel callback)
     * @param originalItem original datafield item (used by cancel callback to revert changes)
     */
    public showDataFieldPopup(
        processConfiguration: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessConfiguration,
        existingDataField: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDataField,
        onFinished: (mapping: any, item: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDataField) => void,
        onCancelled: (scope: any, originalItem: any) => void = null,
        scope?: any,
        originalItem?: any
    ): void {

        var popupConfig: IContextPopupConfiguration = {
            buttons: {
                okButton: {
                    key: popup.buttons.ok,
                    text: this.resourceService.getString('workflow_contextPopup_okButton'),
                    visible: true,
                    enabled: true,
                    dismissResult: popup.buttons.okResult,
                    invokeValidation: false
                }
            },
            context: {
                processConfiguration,
                existingDataField
            },
            headerTemplate: PopupHeaderTemplate.dataField,
            heading: this.resourceService.getString('workflow_contextPopup_newDataFieldHeading'),
            modal: true,
            headingColor: 'settings',
            template: PopupTemplate.newDataField,
            validationContext: sourceCode.k2Designer.objectModel.core.model.ValidationTypes.DataField,
            validationModel: [scope.item],
            onClosed: (key: string, dismissResult: string) => {
                if (dismissResult === popup.buttons.okResult) {
                    onFinished(null, <IDataField>popupConfig.returnData);
                } else if (onCancelled) {
                    onCancelled(scope, originalItem);
                }
            },
            width: popup.dataFieldDialog.defaultWidth
        };

        this.show(popupConfig);
    }

    showUserDialog(onCompleted: (item: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem, cancelled: boolean) => void, types: string[]): void {
        var popupConfig = {
            buttons: {
                okButton: {
                    key: popup.buttons.ok,
                    text: this.resourceService.getString('workflow_contextPopup_okButton'),
                    visible: true,
                    enabled: false,
                    dismissResult: popup.buttons.okResult,
                    invokeValidation: true
                }
            },
            heading: this.resourceService.getString('workflow_contextPopup_select_user'),
            context: {
                allowBrowseMode: true,
                assetType: types,
                createReference: true,
                defaultFilter: 'k2',
                outputProperty: 'email',
                showReference: true
            } as IUserDialogContext,
            modal: false,
            template: PopupTemplate.userDialog,
            style: {
                minHeight: '460px',
                width: '530px'
            },
            body: {
                minHeight: '350px'
            },
            onClosed(key: string, dismissResult: string) {
                if (dismissResult === popup.buttons.okResult) {
                    if (popupConfig.returnData) {
                        onCompleted(<any>popupConfig.returnData, false);
                    }
                } else {
                    onCompleted(null, true);
                }
            }
        } as IPopupConfiguration;

        this.show(popupConfig);
    }

    public showContextWizardPopup(config: IContextWizardPopupConfiguration): void {
        this._legacyPopupService.showContextWizardPopup(config as any);
    }

    appendComponentToDOM<T>(component: Type<T>): ComponentRef<T> {
        // 1. Create a component reference from the component
        const componentRef = this.componentFactoryResolver
            .resolveComponentFactory(component)
            .create(this.injector);

        // 2. Attach component to the appRef so that it's inside the ng component tree
        this.appRef.attachView(componentRef.hostView);

        // 3. Get DOM element from component
        const domElem = (componentRef.hostView as EmbeddedViewRef<any>)
            .rootNodes[0] as HTMLElement;

        // 4. Append DOM element to the body
        document.body.appendChild(domElem);

        return componentRef;
    }

    removeComponentFromBody(componentRef: ComponentRef<unknown>) {
        this.appRef.detachView(componentRef.hostView);
        componentRef.destroy();
    }
}
