import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { AppModule } from './app.module';
import { IToolsetBinding } from './shared/models/i-toolset-binding';
import { IToolsetMessage } from './shared/models/i-toolset-message';
import { IClientAppConfig } from './shared/models/iClientAppConfig';
import { messageTypes } from './shared/enums/toolset/message-types';
import { HttpInterceptor } from './shared/utils/httpInterceptor';
declare let $: any;

export class loader {
    public readonly headerName = 'K2AppsContext';
    public $window = window; // make reference to window for unit testing
    public $console = console; // make reference to console for unit testing
    public $document = document; // make reference to document for unit testing
    public progress: number; // in case we want to show progress during this phase
    public total: number; // to know how many we need to load during this phase
    public phase: string; // current phase of the loading
    public appContext: IToolsetBinding = null; // the app context associated with this client instance if any
    public configuration: IClientAppConfig; // configuration received from the config provider backend
    private _httpInterceptor: HttpInterceptor; // the interceptor instance that will attach headers

    constructor() {
        this._httpInterceptor = new HttpInterceptor(null);
    }

    /**
     * Message handler for responding to iframe message.
     *
     * @param event message parameters
     */
    public messageReceivedHandler(event) {
        if (event.source !== this.$window.parent) {
            this.$console.error(
                'Message received from another source! :' + event.data
            );
            return;
        }
        if (!event.data) {
            this.$console.error('No data received from window message');
            return;
        }
        const message: IToolsetMessage = event.data;

        switch (message.messageType) {
            case messageTypes.setData:
                this.appContext = message.binding;
                const headers = <{ [name: string]: string }>{};
                headers[this.headerName] = this.b64EncodeUnicode(
                    JSON.stringify(this.appContext)
                );
                // TODO: decide whether we load the entire appcontext in one header or split among props
                this._httpInterceptor.headers = headers;
                this._httpInterceptor.Intercept();
                break;
            default:
            // Other messageTypes will be handled by messaging service
                return;
        }
        this.loadTemplates();
    }

    /**
     * Handler to be attached to document ready.
     * Sends message to parent frame to get apps context.
     * Starts app loading if no parent frame present or if parent frame hasn't responded.
     * */
    public documentReadyHandler() {
        if ((<any>this.$window).define) (<any>this.$window).define.amd = null; // disable require from here on out since some libraries try to init using it
        if (
            this.$window.parent && // check if the parent exists
            this.$window.parent !== this.$window && //and that it is not itself
            $('#DesignerIframe') &&
            $('#DesignerIframe').length === 0
        ) {
            //check that it is not loaded into the SF designer
            const message: IToolsetMessage = <IToolsetMessage>{
                messageType: messageTypes.getData,
                binding: null
            };
            this.$window.parent.postMessage(message, '*');
            const that = this;
            this.$window.setTimeout(function() {
                if (!that.phase) {
                    // hasn't started loading already
                    that.loadTemplates();
                }
            }, 1000); // assume we dont have an apps context if the frame doesnt respond within 1s
        } else {
            this.loadTemplates();
        }
    }

    /**
     * Load templates.
     * Load libraries when done.
     */
    loadTemplates() {
        this.phase = 'Loading templates';
        const url =
            'api/templates/gettemplates?language=' + window.navigator.language;
        $.get(url)
            .done((result) => {
                (<any>window).K2_ANGULAR_TEMPLATES = result;
                this.loadConfig();
            })
            .fail((error) => {
                console.error(error);
            });
    }

    loadConfig() {
        this.phase = 'Loading Configuration';
        const url = 'api/configuration/retrieveconfiguration';
        $.get(url)
            .done((result) => {
                this.configuration = result;

                //update global vars from the configuration
                this.$window.K2DESIGNERDEBUGGING =
                    this.configuration.k2DesignerDebugging;
                this.$window.ASSEMBLYFILEVERSION =
                    this.configuration.assemblyFileVersion;
                this.$window.APPVERSION = this.configuration.appVersion;
                this.$window.HELPLINK = this.configuration.helpLink;
                this.$window.__antiXsrfEnabled =
                    this.configuration.antiXsrfEnabled;
                this.$window.__xsrfTokenName = this.configuration.xsrfTokenName;
                this.$window.__xsrfCookieName =
                    this.configuration.xsrfCookieName;
                this.$window.EVENTPLATFORMENABLED =
                    this.configuration.eventPlatformEnabled;
                this.$window.TEMPLATE_CACHING_ENABLED =
                    this.configuration.templateCachingEnabled;

                const scripts: string[] = [];
                this.phase = 'Loading Scripts';
                this.progress = 0;

                // Order matters
                if (this.$window.K2DESIGNERDEBUGGING === true) {
                    scripts.push.apply(scripts, this.configuration.libUrls);
                    scripts.push.apply(scripts, this.configuration.coreAppUrls);
                    scripts.push.apply(scripts, this.configuration.serviceUrls);

                    this.total = scripts.length;
                    this.executeScripts(scripts, this.loadSignalR.bind(this));
                } else {
                    let allScripts = [];
                    this.total = 3;
                    $.get(
                        `webServices/GetScriptPackage.jspack?list=libs.json&_v=${this.$window.ASSEMBLYFILEVERSION}&cache=${this.$window.TEMPLATE_CACHING_ENABLED}`
                    ).done((s1) => {
                        allScripts = allScripts.concat(JSON.parse(s1));
                        this.progress++;
                        $.get(
                            `webServices/GetScriptPackage.jspack?list=core.json&_v=${this.$window.ASSEMBLYFILEVERSION}&cache=${this.$window.TEMPLATE_CACHING_ENABLED}`
                        ).done((s2) => {
                            allScripts = allScripts.concat(JSON.parse(s2));
                            this.progress++;
                            this.appendScripts(
                                allScripts,
                                this.loadSignalR.bind(this)
                            );
                        });
                    });
                }
            })
            .fail((error) => {
                console.error(error);
            });
    }

    appendScripts(scripts, cb) {
        if (scripts && scripts.length > 0) {
            const script = document.createElement('script');
            script.text = scripts.shift();
            document.head.appendChild(script).parentNode.removeChild(script);
            setTimeout(this.appendScripts.bind(this), 0, scripts, cb);
        } else {
            cb();
        }
    }

    /**
     * Load signalR client hub.
     * Bootstrap angular when done.
     */
    loadSignalR() {
        this.phase = 'Loading signalR Hub';
        const that = this;
        const script = document.createElement('script');
        script.type = 'text/javascript';
        script.src = 'signalr/hubs';
        script.onload = function() {
            that.$window.sourceCode.k2Designer.core.Prepare(
                that.appContext,
                that._httpInterceptor,
                that.configuration
            );
            platformBrowserDynamic().bootstrapModule(AppModule);
        };
        document.body.appendChild(script);
    }

    /**
     * Load and execute all the scripts recursively in order.
     *
     * @param urls urls of scripts to load and execute
     * @param callback call this when it is done with all the scripts
     */
    executeScripts(urls: string[], callback: any, i?: number) {
        if (i == undefined) i = 0;
        const that = this;
        if (urls[i] === undefined) return;
        if ((<any>window).K2DESIGNERDEBUGGING) {
            const script = this.$document.createElement('script');
            script.src = urls[i];
            script.async = false;
            script.onload = () => {
                that.progress++;
                that.executeScripts(urls, callback, ++i);
                if (that.progress >= that.total) {
                    callback();
                }
            };
            document.body.appendChild(script);
        } else {
            $.getScript(urls[i], () => {
                that.progress++;
                that.executeScripts(urls, callback, ++i);
                if (this.progress >= this.total) {
                    callback();
                }
            });
        }
    }

    // Encoding UTF8 ⇢ base64
    b64EncodeUnicode(str) {
        return btoa(
            encodeURIComponent(str).replace(
                /%([0-9A-F]{2})/g,
                function(match, p1) {
                    return String.fromCharCode(parseInt(p1, 16));
                }
            )
        );
    }

    // Decoding base64 ⇢ UTF8
    b64DecodeUnicode(str) {
        return decodeURIComponent(
            Array.prototype.map
                .call(atob(str), function(c) {
                    return (
                        '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2)
                    );
                })
                .join('')
        );
    }
}
