import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';

@Component({
    selector: 'k2-error-bar-notifier',
    templateUrl: './error-bar-notifier.component.html',
    styleUrls: ['./error-bar-notifier.component.less']
})
export class ErrorBarNotifierComponent implements OnInit {

    @Input() public clientErrorList: Array<any> = [];
    @Input() public serverErrorList: Array<any> = [];
    @Input() public signalRErrorList: Array<any> = [];
    @Input() public errorSeverity: number;

    @Output() public onClick: EventEmitter<any> = new EventEmitter();

    constructor() { }

    /**
     * When called, will emit the onClick event to show the console
     */
    public performClick(): void {
        this.onClick.emit();
    }

    ngOnInit(): void {
    }

}
