import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { IModelArray } from '../../../../../../../../types/models/base/i-model-array';
import { IConfigPanelTab } from '../../../../../../../../types/models/config-panel/i-config-panel-tab';
import { IConfigTabValidatedComponent } from '../../../../../../../../types/models/config-panel/i-config-tab-validated-component';
import { IExpression } from '../../../../../../../../types/models/core/configured-smart-fields/i-expression';
import { IEnvironmentField } from '../../../../../../../../types/models/core/external-reference-definitions/i-environment-field';
import { IClientEventConfiguration } from '../../../../../../../../types/models/process/events/configuration/i-client-event-configuration';
import { IEmailConfiguration } from '../../../../../../../../types/models/process/events/configuration/i-email-configuration';
import { IEvent } from '../../../../../../../../types/models/process/events/i-event';
import { IClientEventUI } from '../../../../../../../../types/models/process/events/ui/i-client-event-ui';
import { IConfigPanelService } from '../../../../../../../../types/services/config-panel/i-config-panel-service';
import { IPluginService } from '../../../../../../../../types/services/plugin/i-plugin-service';
import { IValidationService } from '../../../../../../../../types/services/validation/ivalidation.service';
import { IRecipientValidationOptions } from '../../../../../../../../types/validation/i-recipient-validation-options';
import { ConfigPanelServiceToken } from '../../../../../../../services/config-panel/config-panel-service-factory';
import { ConfigurationService } from '../../../../../../../services/configuration/configuration-service';
import { PluginServiceToken } from '../../../../../../../services/plugin/plugin-service-factory';
import { ProcessService } from '../../../../../../../services/process/process.service';
import { ResourceService } from '../../../../../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../../../../services/validation/validation-service-factory';
import { ComponentIdMapping } from '../../../../../../../shared/constants/components';
import { EmailConstants } from '../../../../../../../shared/constants/email';
import { installTypes } from '../../../../../../../shared/constants/general';
import { plugins } from '../../../../../../../shared/constants/plugins';
import { ChooseControlTypes } from '../../../../../../../shared/enums/choose-control/choose-control-types';
import { EmailParts } from '../../../../../../../shared/enums/email/email-parts';
import { PluginBaseComponent } from '../../../../base/plugin-base/plugin-base.component';

@Component({
    selector: 'k2-task-notification',
    templateUrl: './task-notification.component.html',
    styleUrls: ['./task-notification.component.less']
})
export class TaskNotificationComponent extends PluginBaseComponent implements OnInit, OnDestroy{

    @Input() event: IEvent;

    @Input() eventConfiguration: IClientEventConfiguration;
    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    partDisableList: EmailParts[] = [];
    partHideList: EmailParts[] = [];

    //Public enums:
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;

    //Validation Options per field:
    validation: IRecipientValidationOptions = {
        from: [
            {
                Required:
                {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_from',
                        'configpanel_tabs_task_tooltips_title_NotificationsTab'
                    ]
                }
            }],
        cc: [
            {
                Required:
                {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_cc',
                        'configpanel_tabs_task_tooltips_title_NotificationsTab'
                    ]
                }
            }
        ],
        bcc: [
            {
                Required:
                {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_bcc',
                        'configpanel_tabs_task_tooltips_title_NotificationsTab'
                    ]
                }
            }
        ]
    };

    constructor(resourceService: ResourceService,
        @Inject(PluginServiceToken) private pluginService: IPluginService,
        private configurationService: ConfigurationService,
        private processService: ProcessService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnInit(): void {
        if (!this.eventConfiguration && this.event) {
            //If for some reason the eventConfiguration is not passed on from the parent
            this.eventConfiguration = this.event.configuration as IClientEventConfiguration;
        }

        if (!this.emailConfig.body?.smartFields?.length) {
            this.resetBody();
        }

        //Objects which should cause tab to be badged when in error:
        let objToValidateConfigTab: IConfigTabValidatedComponent = {
            componentObj: this.emailConfig,
            compareFullReference: false
        };
        this.addObjectsToValidateConfigTab([objToValidateConfigTab], this.configPanelTab);

        //Task Notification tab does not display Priority / To and only displays From according to install type
        this.partDisableList.push(EmailParts.Priority);
        this.partDisableList.push(EmailParts.To);
        if (!this.shouldShowFrom) {
            this.partDisableList.push(EmailParts.From);
            this.resetFrom();
        }

        //Show/hide cc/bcc
        const hasCC: boolean = this.emailConfig?.cc?.some((cc: IExpression) => cc.smartFields.length > 0 || cc.errorInformation.length > 0);

        const hasBCC: boolean = this.emailConfig?.bcc?.some((bcc: IExpression) => bcc.smartFields.length > 0 || bcc.errorInformation.length > 0);

        if (!this.eventUi.showCC && !hasCC) {
            this.partHideList.push(EmailParts.Cc);
        }

        if (!this.eventUi.showBCC && !hasBCC) {
            this.partHideList.push(EmailParts.Bcc);
        }
    }

    get eventUi(): IClientEventUI {
        return this.event.ui as IClientEventUI;
    }

    get shouldSendNotification(): boolean {
        return this.eventConfiguration.sendNotification;
    }

    get shouldUseCustomTemplate(): boolean {
        return !(this.eventConfiguration.useStandardTemplate);
    }

    get emailConfig(): IEmailConfiguration {
        return this.eventConfiguration.emailConfiguration;
    }

    /**
     * Only allow From address for BlackPearl install types
     */
    get shouldShowFrom(): boolean {
        return this.configurationService.installType.toUpperCase() === installTypes.blackPearl;
    }

    toggleSendNotification(shouldSendNotification: boolean): void {
        this.eventConfiguration.sendNotification = shouldSendNotification;

        if (!shouldSendNotification) {
            this.loadBodyTemplate();
        } else {
            this.eventConfiguration.useStandardTemplate = true;
            this.reInitializeEmailConfig();
            this.pluginService.toggleCollapsed(plugins.panels.emailBody);
        }
    }

    toggleUseStandardTemplate(shouldUseCustomTemplate: boolean): void {
        this.eventConfiguration.useStandardTemplate = !shouldUseCustomTemplate;
        if (this.eventConfiguration.useStandardTemplate) {
            this.reInitializeEmailConfig();
            this.pluginService.toggleCollapsed(plugins.panels.emailBody);
        }
    }

    private reInitializeEmailConfig(): void {
        this.resetCc();

        this.resetBcc();

        this.emailConfig.attachments.clear();

        this.emailConfig.subject.smartFields.clear();

        this.resetFrom();

        this.loadBodyTemplate();

        this.eventUi.showFrom = this.shouldShowFrom;
        this.eventUi.showCC = false;
        this.eventUi.showBCC = false;
        if (this.partHideList.indexOf(EmailParts.Cc) === -1) {
            this.partHideList.push(EmailParts.Cc);
        }
        if (this.partHideList.indexOf(EmailParts.Bcc) === -1) {
            this.partHideList.push(EmailParts.Bcc);
        }
    }

    private resetCc(): IModelArray<IExpression> {
        this.emailConfig.cc.clear();
        this.undoRedoService.addUndoMarker('--Task Notification controller: add cc--');
        this.emailConfig.cc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
        return this.emailConfig.cc;
    }

    private resetBcc(): IModelArray<IExpression> {
        this.emailConfig.bcc.clear();
        this.undoRedoService.addUndoMarker('--Task Notification controller: add bcc--');
        this.emailConfig.bcc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
        return this.emailConfig.bcc;
    }

    private resetFrom(): IExpression {
        let def = sourceCode.k2Designer.objectModel.smartFieldPlugins.EnvironmentFieldPlugin.createExternalDefinition(this.processService.root, EmailConstants.FromAddress);

        this.emailConfig.from = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
            smartFields: [{
                title: def.name,
                environmentFieldReference: def.getReference(),
                componentId: ComponentIdMapping.k2EnvironmentField
            } as IEnvironmentField]
        });

        return this.emailConfig.from;
    }

    private resetBody(): IExpression {

        this.loadBodyTemplate();

        (this.emailConfig.body as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpressionExtended).notifyChange(null);

        return this.emailConfig.body;

    }

    private loadBodyTemplate(): void {
        if (this.emailConfig.body.smartFields) {
            this.emailConfig.body.smartFields.clear();
            this.emailConfig.body.richText = true;
            this.emailConfig.body.html = false;
        }

        if (this.eventConfiguration.sendNotification) {
            if (this.eventConfiguration.isSmartActionsEnabled) {
                let template = JSON.parse(this.resourceService.getString('wizard_task_notification_template_smartactions'));
                for (let i: number = 0; i < template.length; i++) {
                    this.emailConfig.body.smartFields.push(template[i]);
                }
            }
            else {
                let template = JSON.parse(this.resourceService.getString('wizard_task_notification_template'));
                for (let i: number = 0; i < template.length; i++) {
                    this.emailConfig.body.smartFields.push(template[i]);
                }
            }
        }
    }

}
