import { Injectable } from '@angular/core';
import { ISmartFormItemReference } from '../../../../types/models/smart-forms/i-smart-form-item-reference';
import { StartMode } from '../../../shared/enums/integration/start-mode';
import { ProcessService } from '../../process/process.service';

@Injectable({
    providedIn: 'root'
})
export class PluginItemReferencesService {

    constructor(private _processService: ProcessService) {
    }

    /**
     * Method to create item references on the process
     */
    public createItemReferences(
        itemReferences: sourceCode.k2Designer.objectModel.core.model.IModelArray<ISmartFormItemReference>,
        startMode: StartMode): void {

        const primaryItemReferenceName = this.getPrimaryItemReferenceName(itemReferences);

        for (let i = 0; i < itemReferences.length; i++) {

            if (!itemReferences[i].disabled) {


                //Create item reference instance from SMO find on form
                const itemReference = sourceCode.k2Designer.objectModel.workflow.process.k2Process.ItemReference.createInstance({
                    systemName: itemReferences[i].name,
                    title: itemReferences[i].displayName,
                    objectNames: itemReferences[i].objectNames,
                    objectTypes: itemReferences[i].objectTypes,
                    serviceTypes: itemReferences[i].serviceTypes,
                    type: sourceCode.k2Designer.objectModel.workflow.process.k2Process.ItemReferenceItemTypes.SmartObjectItem,
                    methodReference: '',
                    smartObjectReference: '',
                    primary: itemReferences[i].primary
                });

                if (itemReference.primary) {
                    itemReference.startMode = StartMode[startMode];
                }
                else {
                    itemReference.primaryReference = primaryItemReferenceName;
                }

                //Create external defintions
                let smartObjectExternalReference = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectField.createInstance({
                    systemName: itemReferences[i].smartObjectName,
                    isDynamic: false,
                    componentId: sourceCode.k2Designer.objectModel.core.deployment.LookupTypes.SmartObjectFieldExternalReference
                });

                let methodExternalReference = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectMethodField.createInstance({
                    systemName: itemReferences[i].smartObjectMethodName,
                    type: itemReferences[i].smartObjectMehtodType
                });

                //This is the SMO external reference definition
                if (this._processService.root.externalReferenceDefinitions.single<sourceCode.k2Designer.objectModel.core.model.IExternalReferenceDefinition>(
                    { systemName: itemReferences[i].smartObjectName, componentId: 70002 }) === undefined) {
                    this._processService.root.externalReferenceDefinitions.push(smartObjectExternalReference);
                } else {
                    smartObjectExternalReference =
                        <sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectField>
                    this._processService.root.externalReferenceDefinitions.single
                        <sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectField>
                        ({ systemName: itemReferences[i].smartObjectName, componentId: 70002 });
                }

                if (smartObjectExternalReference.methods.single<sourceCode.k2Designer.objectModel.core.model.IExternalReferenceDefinition>(
                    { systemName: methodExternalReference.systemName }) === undefined) {
                    smartObjectExternalReference.methods.push(methodExternalReference);
                }
                else {
                    methodExternalReference =
                        smartObjectExternalReference.methods.single
                            <sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField>
                            ({ systemName: methodExternalReference.systemName });
                }

                itemReference.methodReference = methodExternalReference.getReference();
                itemReference.smartObjectReference = smartObjectExternalReference.getReference();

                //Add the item reference to the process.
                this._processService.root.configuration.itemReferences.push(itemReference);
            }
        }
    }

    /**
     * Use this method to get the parent SMO, need to do this in the UI incase an item reference already exist with the same smo name.  In this case
     * the item reference will be incremented with 1 and we then don't want to use the value from the server which defaults to the smo name.
     */
    public getPrimaryItemReferenceName(
        itemReferences: sourceCode.k2Designer.objectModel.core.model.IModelArray<ISmartFormItemReference>): string {

        for (let i = 0; i < itemReferences.length; i++) {
            if (itemReferences[i].primary) {
                return itemReferences[i].name;
            }

        }

        return '';
    }
}
