import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IListItemIconData } from '../../../../../types/uiComponents/list-item/i-list-item-icon-data';

@Component({
    selector: 'k2-list-item-icon-template',
    templateUrl: './icon-template.component.html',
    styleUrls: ['./icon-template.component.less']
})
export class IconTemplateComponent implements OnInit {

    /**
     * Data to describe how the icon template should render
     * isVertical: if true, will add class 'vertical' to the flexRow div
     * isWrappedInContentDiv: if true, will wrap the flexRow div inside a fixed content div
     * customClassFlexRow: custom class to add to flexRow div (only adds if specified)
     * iconClass: specific css class for icon
     * backgroundColor: if specified, will add an inline background-color of specified value to flexRow div
     */
    @Input() listItemIconData: IListItemIconData = {
        isVertical: false,
        isWrappedInContentDiv: false,
        customClassFlexRow: '',
        iconClass: '',
        backgroundColor: ''
    };

    @Output() iconClicked: EventEmitter<EventTarget> = new EventEmitter<EventTarget>();

    get templateContext() {
        return { $implicit: this };
    }

    constructor() { }

    ngOnInit(): void {
    }

    onIconClicked(event: MouseEvent): void {
        this.iconClicked.emit(event.target);
    }
}
