import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { LegacyResourceServiceToken } from 'src/app/legacy-service-factories/resource-service-factory';
import { INotificationService } from '../../../../types/services/notification/i-notification.service';
import { ILegacyResourceService } from '../../../../types/services/resources/i-legacy-resource.service';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { NotificationServiceToken } from '../../../services/notification/notification-service-factory';
import { ProcessService } from '../../../services/process/process.service';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { ToastrTypes } from '../../../shared/enums/model/toastr-types';

@Component({
    selector: 'k2-error-bar-console',
    templateUrl: './error-bar-console.component.html',
    styleUrls: ['./error-bar-console.component.less']
})
export class ErrorBarConsoleComponent implements OnInit {

    @Input() public clientErrorList: Array<any> = [];
    @Input() public serverErrorList: Array<any> = [];
    @Input() public signalRErrorList: Array<any> = [];

    @Output() public onClose: EventEmitter<any> = new EventEmitter();
    @Output() public onFocusNode: EventEmitter<sourceCode.k2Designer.objectModel.core.model.IErrorInformation> =
        new EventEmitter<sourceCode.k2Designer.objectModel.core.model.IErrorInformation>();

    public errorBarLinkText: string;
    public errorBarTitle: string;
    public errorBarCopy: string;
    public errorBarSmalltext: string;
    public copyBtnHover: boolean;
    public closeBtnHover: boolean;

    private _isWaiting = false;

    constructor(@Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        @Inject(NotificationServiceToken) private _notificationService: INotificationService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        private _processService: ProcessService) {
    }

    ngOnInit() {
        this.errorBarLinkText = this._resourceService.getString('error_bar_console_link_text');
        this.errorBarTitle = this._resourceService.getString('error_bar_console_title');
        this.errorBarCopy = this._resourceService.getString('error_bar_copy_button_label');
        this.errorBarSmalltext = this._resourceService.getString('error_bar_console_small_text');
        this.copyBtnHover = false;

        //Initialize default button states for lists
        for (let c = 0; c < this.clientErrorList.length; c++) {
            this.initRowButtonExpandHoverState(this.clientErrorList[c]);
            this.initRowButtonCollapseState(this.clientErrorList[c]);
        }

        for (let s = 0; s < this.serverErrorList.length; s++) {
            this.initRowButtonExpandHoverState(this.serverErrorList[s]);
            this.initRowButtonCollapseState(this.serverErrorList[s]);
        }

        for (let sr = 0; sr < this.signalRErrorList.length; sr++) {
            this.initRowButtonExpandHoverState(this.signalRErrorList[sr]);
            this.initRowButtonCollapseState(this.signalRErrorList[sr]);
        }

    }

    /**
     * Returns a string error message compiled from the provided error object
     */
    public resolveErrorMsg(errorObj: sourceCode.k2Designer.objectModel.core.model.IErrorInformation): string {
        let result: string = null;

        if (errorObj) {
            const modelObj: sourceCode.k2Designer.objectModel.core.model.IModelObject = this._processService.root.findItem(errorObj.reference);

            if (modelObj) {
                result = this._resourceService.resolveTokenResourceString(errorObj.errorMessage, errorObj.errorResourceTokens, modelObj);
            } else {
                //Orphaned error, was removed before resolveErrorMsg called with scope digest, need to refresh list

                // Wait a while after updating error flat list before allowing additional updates.
                // This prevents spamming the update list method, the updateErrorFlatList method itself
                // has a deferred implementation internally
                if (!this._isWaiting) {

                    this._isWaiting = true;
                    setTimeout(() => {
                        this._isWaiting = false;
                    }, 110);

                    this._validationService.updateErrorFlatList(errorObj, this._processService);
                }
            }
        }

        if (!result) {
            result = this._resourceService.getString('resource_service_unresolved_msg');
        }

        return result;
    }

    /**
     * Closes the error console
     */
    public performClose(): void {
        this.onClose.emit();
    }

    /**
     * Opens the config panel and focusses the tab on which the error is present
     */
    public performFocusNode(reference: sourceCode.k2Designer.objectModel.core.model.IErrorInformation): void {
        this.onFocusNode.emit(reference);
    }

    /**
     * Ensures there are no trailing whitespace on message
     */
    public isMsgTruncated(evt: JQueryEventObject) {
        const elem: JQuery = $(evt.target).find('span').addBack('span');

        if (elem.length > 0) {
            return elem[0].offsetWidth < elem[0].scrollWidth;
        }
        else {
            return false;
        }
    }

    /**
     * Copy the currently displayed error messages to a user's clipboard
     */
    public copyErrorMessages(): void {

        const textToCopy = this.getErrorMessagesAsAString();

        //Create a temporary dom element, append it to the body
        const temp = $('<textarea>');
        $('body').append(temp);

        //Set and select the text we want to copy
        temp.html(textToCopy);
        temp.select();

        let success = false;
        //Save the selection to the clipboard
        try {
            //This returns true even if nothing is copied and/or clipboard permission is denied.
            success = document.execCommand('copy');
        } catch (error) {
            console.error(error);
            success = false;
        }

        //remove the element from the DOM
        temp.remove();

        //If the browser is IE, display a copy fail toast if the user does not allow clipboard access
        if (navigator.appVersion.indexOf('Trident') !== -1 && navigator.appVersion.indexOf('Chrome') === -1 && navigator.appVersion.indexOf('Edge') === -1) {

            const textOnClipboard = ((<any>window) as IEWindow).clipboardData.getData('Text');

            const filteredTextOnClipboard = textOnClipboard.replace(new RegExp('\n', 'g'), '').replace(new RegExp('\r', 'g'), '');
            const filteredTextToCopy = textToCopy.replace(new RegExp('\n', 'g'), '').replace(new RegExp('\r', 'g'), '');

            //Check that the text we wanted to copy matches what is currently on the clipboard
            if (filteredTextOnClipboard === filteredTextToCopy) {
                success = true;
            } else {
                success = false;
            }
        }

        //Display toast
        if (success === true) {
            this._notificationService.toastr(this._resourceService.getString('workflow_copy_error_button_success_toast_title'),
                this._resourceService.getString('workflow_copy_error_button_success_toast_description'),
                ToastrTypes.copySuccess, 2000);
        } else {
            this._notificationService.toastr(this._resourceService.getString('workflow_copy_error_button_failure_toast_title'),
                this._resourceService.getString('workflow_copy_error_button_failure_toast_description'),
                ToastrTypes.copyFail, 2000);
        }

        //Interface for allowing access to clipboard data for Internet Explorer Windows
        interface IEWindow extends Window {
            clipboardData: {
                getData: (key: string) => string;
            };
        }
    }

    /**
     * Gets the error messages and returns them as a single string
     */
    public getErrorMessagesAsAString() {
        let textToCopy = '';
        const errorList = [];
        const fullClientErrorList = this.clientErrorList;
        const serverErrorList = this.serverErrorList;
        const signalRErrorList = this.signalRErrorList;
        const clientWarningList = [];

        if (fullClientErrorList) {

            //Sort the client error list by reference
            fullClientErrorList.sort((a, b) => a.reference.localeCompare(b.reference));

            //Add only the client errors to the error list. The warnings are added to a new array to push at the end
            for (let i = 0; i < fullClientErrorList.length; i++) {
                if (fullClientErrorList[i].errorSeverity != 1) {
                    errorList.push(this.resolveErrorMsg(fullClientErrorList[i]));
                }
                else {
                    clientWarningList.push(this.resolveErrorMsg(fullClientErrorList[i]));
                }
            }
        }

        if (serverErrorList) {

            //Sort the server error list by reference
            serverErrorList.sort((a, b) => a.errorMessage.localeCompare(b.errorMessage));

            //Add the server errors to the error list
            for (let i = 0; i < serverErrorList.length; i++) {
                errorList.push(this.resolveErrorMsg(fullClientErrorList[i]));
            }
        }

        if (signalRErrorList) {

            //Sort the signalR error list by reference
            signalRErrorList.sort((a, b) => a.errorMessage.localeCompare(b.errorMessage));

            //Add the signalR errors to the error list
            for (let i = 0; i < signalRErrorList.length; i++) {
                errorList.push(this.resolveErrorMsg(fullClientErrorList[i]));
            }
        }

        if (clientWarningList) {

            //Add the client warnings to the error list
            for (let i = 0; i < clientWarningList.length; i++) {
                errorList.push(clientWarningList[i]);
            }
        }

        //Format the error list as a single string
        for (let i = 0; i < errorList.length; i++) {
            textToCopy += errorList[i] + '\n';
        }

        //Remove extra new line at the end of the string
        textToCopy = textToCopy.slice(0, -1);
        return textToCopy;
    }

    /**
     * Inverts current hover state for the copy button
     */
    public changeCopyButtonHoverState() {
        this.copyBtnHover = !this.copyBtnHover;
    }

    /**
     * Inverts current hover state for the close button
     */
    public changeCloseButtonHoverState() {
        this.closeBtnHover = !this.closeBtnHover;
    }

    /**
     * Inverts current hover state for the row expand button
     */
    public changeRowButtonExpandHoverState(row: any) {
        row.expandBtnHover = !row.expandBtnHover;
    }

    /**
     * Sets initial value for row expand button to not show hover state
     */
    public initRowButtonExpandHoverState(row: any) {
        row.expandBtnHover = false;
    }

    /**
     * Inverts current hover state for the row collapse button
     */
    public changeRowButtonCollapseHoverState(row: any) {
        row.expandBtnHover = !row.expandBtnHover;
    }

    /**
     * Sets initial value for row collaps button to not show hover state
     */
    public initRowButtonCollapseState(row: any) {
        row.expandBtnHover = false;
    }
}
