import { Component, Inject, Input, OnInit, OnDestroy } from '@angular/core';
import { IProcessPluginDefinitionContext } from 'src/types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { IHelpService } from '../../../../../types/services/help/i-help-service';
import { IContextWizardPopupConfiguration } from '../../../../../types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { HelpServiceToken } from '../../../../services/help/help-service-factory';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { BaseResourceComponent } from '../../../base-resource/base-resource.component';
import { ISmartFormItemReference } from '../../../../../types/models/smart-forms/i-smart-form-item-reference';
import { Subscription } from 'rxjs';
import { ResourceService } from '../../../../services/resource/resource.service';
import { PageComponent } from '../../page/page.component';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IModelArray } from 'src/types/models/base/i-model-array';

@Component({
    selector: 'k2-create-smart-fields',
    templateUrl: './create-smart-fields.component.html',
    styleUrls: ['./create-smart-fields.component.less'],
    providers: [{ provide: PageComponent, useExisting: CreateSmartFieldsComponent }]
})
export class CreateSmartFieldsComponent extends PageComponent implements OnInit, OnDestroy {

    /**
     * Wizard popup configuration
     */
    @Input() public config: IContextWizardPopupConfiguration;

    /**
     * Help link.
     */
    helpLink: string;

    /**
     * Flag to determine if the directive is loading
     */
    isLoading: boolean;

    get pluginContext(): IProcessPluginDefinitionContext {
        return this.config?.context as IProcessPluginDefinitionContext;
    };

    buttonConfig: IPopupButtonsConfig;

    private formContentSub: Subscription;

    constructor(protected resourceService: ResourceService,
        @Inject(HelpServiceToken) private helpService: IHelpService,
        private smartFormsIntegrationService: SmartFormsIntegrationService) {
        super(resourceService);
        this.isLoading = true;
    }

    ngOnInit(): void {
        this.pluginContext.directiveLoaded = this.itemReferencesComponentLoaded.bind(this);

        this.helpLink = this.helpService.getHelpUrl('createreferences_sf');
    }

    ngOnDestroy(): void {
        this.formContentSub?.unsubscribe();
    }

    beforePageDisplayed() {}
    beforeNavigateNext() {
        this.smartFormsIntegrationService.determinePages(this.pluginContext);
    }
    beforeNavigatePrevious() {}

    /**
     * Method that will execute if an item reference on the directive was removed.
     */
    updateHeading = (newItemReferences: IModelArray<ISmartFormItemReference>): void => {
        // Update dynamic headings.

        if (newItemReferences.length > 0) {
            this.pluginContext.containsNewReferences = true;
            if (this.pluginContext.containsNewReferences) {
                this.pluginContext.unmappedFieldsHeading = this.getString('smartForms_integration_references')
                    .replace('{0}', newItemReferences.length.toString())
                    .replace('{1}', this.pluginContext.form.title);
            }
            this.smartFormsIntegrationService.determinePages(this.pluginContext);

            this.pluginContext.newItemReferences.collection
                    = newItemReferences as sourceCode.k2Designer.objectModel.core.model.IModelArray<ISmartFormItemReference>;
        }
    };

    itemReferencesComponentLoaded(): void {
        // This call can only be made when the item references component is loaded.
        this.formContentSub = this.smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formContentLoaded();
            });
    }

    private formContentLoaded = (): void => {
        this.isLoading = false;
    };
}
