import { Component, Input, OnInit } from '@angular/core';
import { IChooseCreateChangeOutcomeContext } from '../../../../types/models/popup/i-choose-create-change-outcome-context';
import { IOutcome } from '../../../../types/models/process/outcomes/i-outcome';
import { ISaveReferencePopupConfiguration } from '../../../../types/uiComponents/pluginWizards/i-save-reference-popup-configuration';
import { PopupService } from '../../../services/popup/popup.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { popup } from '../../../shared/constants/wizard-popup';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-choose-create-change-outcome',
    templateUrl: './choose-create-change-outcome.component.html',
    styleUrls: ['./choose-create-change-outcome.component.less']
})
export class ChooseCreateChangeOutcomeComponent extends BaseResourceComponent implements OnInit {
    @Input() config: ISaveReferencePopupConfiguration;

    get context(): IChooseCreateChangeOutcomeContext {
        return this.config?.context as IChooseCreateChangeOutcomeContext;
    }

    selectedOutcomeObjectRef: string; //Used for legacy (not needed for migrated components using this popup)
    objectList: IOutcome[];
    expiredList: IOutcome[];
    //Default expired outcome reference
    DEFAULT_EXPIRED_REFERENCE = 'Expired';

    constructor(resourceService: ResourceService,
        private popupService: PopupService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (this.context.selectedItem) {
            this.selectedOutcomeObjectRef = this.context.selectedItem.getReference();
            this.selectedOutcome = this.context.selectedItem;
        }

        this.objectList = [];
        this.expiredList = [];
        if (this.context.contextObjectList) {
            for (const item of this.context.contextObjectList) {
                if (item.isExpiredOutcome !== true) {
                    this.objectList.push(item);
                }
                else {
                    this.expiredList.push(item);
                }
            }
        }
    }

    get selectedOutcome(): IOutcome {
        return (this.config.context as IChooseCreateChangeOutcomeContext).selectedItem;
    }

    set selectedOutcome(val) {
        (this.config.context as IChooseCreateChangeOutcomeContext).selectedItem = val;
        this.setReturnData();
    }

    /**
     * No outcome selected
     * */
    selectNone(): void {
        this.selectedOutcomeObjectRef = null;
        this.selectedOutcome = null;
        this.setReturnData();
    }

    /**
     * Expired outcome that needs to created
     * */
    selectNewExpired(): void {
        this.selectedOutcome = null;
        this.selectedOutcomeObjectRef = this.DEFAULT_EXPIRED_REFERENCE;
        this.setReturnData(true);
    }

    /**
     * Existing outcome selected from row (expired or normal)
     *
     * @param selectedRow
     */
    selectOutcomeRow(selectedRow: IOutcome): void {
        this.selectedOutcomeObjectRef = selectedRow.getReference();
        this.selectedOutcome = selectedRow;
        this.setReturnData();
    }

    /**
     * Binds to the method that executes after validation completed and before the popup closes.
     */
    private setReturnData(shouldCreateNewExpired: boolean = false): void {
        this.config.returnData =
        {
            selectedReference: this.selectedOutcome,
            isNewReference: shouldCreateNewExpired
        };
    }

    closeOnDoubleClick(): void {
        this.popupService.hideContextPopup(popup.buttons.ok, popup.buttons.okResult);
    }

    getLinkedToText(outcome: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcome): string {
        if (outcome.linkedActionReference && outcome.linkedActionReference.length > 0) {
            const action = outcome
                .getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>()
                .findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfigurationAction>(outcome.linkedActionReference);
            return this.getString('wizard_Task_Voting_Link_To_ActionName').format(action.actionTitle);
        } else {
            return '';
        }
    }
}
