import { Component, Inject, Input, OnInit } from '@angular/core';
import { IDeleteProcessContext } from '../../../../types/models/popup/i-delete-process-context';
import { IPopupButton } from '../../../../types/models/popup/i-popup-button';
import { IPopupConfiguration } from '../../../../types/models/popup/i-popup-configuration';
import { IDataService } from '../../../../types/services/data/i-data-service';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { DataServiceToken } from '../../../services/data/data-service-factory';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-delete-process',
    templateUrl: './delete-process.component.html',
    styleUrls: ['./delete-process.component.less']
})
export class DeleteProcessComponent extends BaseResourceComponent implements OnInit {
    @Input() config: IPopupConfiguration;

    get context(): IDeleteProcessContext {
        return this.config.context as IDeleteProcessContext;
    }

    constructor(
        @Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        resourceService: ResourceService,
        private processService: ProcessService,
        @Inject(DataServiceToken) private dataService: IDataService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (this.config.buttons?.okButton) {
            this.config.buttons.okButton.onClick.subscribe((button: IPopupButton) => {
                if (this.context.processToDeleteFQN && this.context.processToDeleteFQN.length) {
                    $(document).trigger('DesignerStateChange', ['Busy']);
                    this.processService.requestProcessingCompleteNotification(true, true, this.doDeleteProcess);
                }
            });
        }
    }

    doDeleteProcess = () => {
        let success = false;
        //Make delete service call:
        this.dataService.httpGet('api/process/deletebyname', {
            host: this.environmentService.currentEnvironment.hostId,
            processName: this.context.processToDeleteFQN,
            deleteDeployedVersion: false, //Do not delete deployed version
            deleteLogEntries: false
        }, (message: string, error: boolean, errorCode: number) => {
            if (!error) {
                success = true;
                this.context.onDeleteCompletedCallback(success);
            }
            $(document).trigger('DesignerStateChange', ['Normal']);
        }, this.environmentService, false);
        return success;
    };
}
