import { Component, Inject, Input, OnChanges, OnDestroy, OnInit, SimpleChanges } from '@angular/core';
import { IDataField } from '../../../../types/models/data-field/i-data-field';
import { IK2OutputMapper } from '../../../../types/models/output-mapper/i-output-mapper';
import { IK2OutputMapperRow } from '../../../../types/models/output-mapper/i-output-mapper-row';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { DataTypeResolverService } from '../../../services/plugins/data-type-resolver/data-type-resolver.service';
import { PopupService } from '../../../services/popup/popup.service';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { DataFieldTypes } from '../../../shared/enums/model/workflow/process/data-field-types';
import { findUniqueName } from '../../../shared/utils/name-helper';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';
import { DropDownComponent } from '../drop-down/drop-down.component';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { EventTypes } from '../../../shared/enums/model/event-types';
import { dataTypes } from '../../../shared/constants/data-types';
import { ITextSmartField } from '../../../../types/models/core/configured-smart-fields/i-text-field';

@Component({
    selector: 'k2-output-mapper',
    templateUrl: './output-mapper.component.html',
    styleUrls: ['./output-mapper.component.less']
})
export class OutputMapperComponent extends BaseResourceComponent implements OnInit, OnChanges, OnDestroy {
    @Input() public headingData: IK2OutputMapper;
    @Input() public savedProperties: sourceCode.k2Designer.objectModel.core.model.IModelDictionary<IExpression>;
    //All available properties for output mapper
    @Input() public availableProperties: IK2OutputMapperRow[];

    public resources: { [key: string]: string } = {};
    public workflowName: string;
    //Output mappings from available properties that were selected
    public selectedProperties: IK2OutputMapperRow[] = [];
    public rowDropdownFocus: boolean;
    public rowValueControlFocus: boolean;
    public outputMapperSingleDropdownRowFields: IDropDownMenuItem[];
    public selectedItemKey: string;
    public outputMapperSingleDropdownFields: IDropDownMenuItem[];
    public dataFieldItems: IDropDownMenuItem[];
    public selectedRowIndex: number;
    public rowSelected = false;
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public hoverClass = '';
    //Available output mappings per row, with key of selected row
    //Each row has to exclude all selected properties, except its own selected property
    public availableOutputMappingsForSelectedRow: { [key: string]: IDropDownMenuItem[] } = {};
    public availableDataFieldsForSelectedRow: {
        [key: string]: {
            items: IDropDownMenuItem[];
            footerItems: IDropDownMenuItem[];
        };
    } = {};
    //A list of all available properties for addding or removal (selected or unselected)
    public availableOutputMappings: IDropDownMenuItem[];

    private selectedRowItem: IK2OutputMapperRow;
    private eventRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover;

    constructor(public resourceService: ResourceService,
        @Inject(EnvironmentServiceToken) private envService: IEnvironmentService,
        @Inject(ValidationServiceToken) private validationService: IValidationService,
        private processService: ProcessService,
        private popupService: PopupService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        private dataTypeResolverService: DataTypeResolverService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.resources = {
            generalSubWorkflow: this.resourceService.getString('general_subworkflow'),
            generalThisWorkflow: this.resourceService.getString('general_this_workflow'),
            controlPropertyMapperNoProperties: this.resourceService.getString('control_property_mapper_no_properties'),
        };

        this.workflowName = this.envService.activeStudioItem.title;

        this.setupDataFieldDropdownItems();
        this.setupAvailableOutputMappingsForRows();

        //Update datafields collection when datafields are changed
        this.eventRemover =
            this.processService.root.configuration.dataFields.listen(EventTypes.anythingChanged, this.onDataFieldsChanged);
    }

    ngOnChanges(changes: SimpleChanges): void {
        //Available and selected properties are set with ajax calls on parent,
        //need to update the input mappings when these are changed externally
        if (changes.availableProperties || changes.savedProperties) {
            this.setupAvailableOutputMappingsForRows();
        }
    }

    ngOnDestroy(): void {
        if (typeof this.eventRemover === 'function') {
            this.eventRemover();
        }
    }

    /**
     * Build up the dictionary of output key-value mappings that is loaded from saved values
     * */
    setupAvailableOutputMappingsForRows(): void {
        this.availableOutputMappingsForSelectedRow = {};

        this.selectedProperties = [];

        this.availableProperties.forEach(availableProperty => {
            const key = availableProperty.key;
            const selectedProp = this.selectedProperties.find(s => s.key === key);
            //This property was previously configured and saved, but not in selected properties
            if (this.savedProperties && this.savedProperties[key] && !selectedProp) {
                this.selectedProperties.push(availableProperty);
            }

            //Add all available properties for this selected property row:
            this.availableProperties.forEach(prop => {
                //exclude system field such as loop step index/result
                //exclude other already selected properties (except current selected row)
                const isThisAvailableProperty = availableProperty.key === prop.key;
                const shouldAdd = !prop.systemField && !availableProperty.systemField &&
                    (isThisAvailableProperty || !this.selectedProperties.find(s => s.key === prop.key));

                if (shouldAdd) {
                    this.availableOutputMappingsForSelectedRow[key] = this.availableOutputMappingsForSelectedRow[key] || [];
                    this.availableOutputMappingsForSelectedRow[key].push(
                        {
                            key: prop.key,
                            icon: 'icon icon-set-datatypes icon-size16 ic-' + prop.typeIcon.toLowerCase(),
                            isSeparator: false,
                            isDisabled: false,
                            title: prop.rowTitle,
                            isSelected: isThisAvailableProperty
                        } as IDropDownMenuItem
                    );
                }
            });
        });

        //New row should contain all available output mappings that can be either added / removed:
        this.availableOutputMappings = [];
        this.availableProperties.forEach(availableProperty => {
            //Only list available properties that are not in selected properties and not system field
            if (!availableProperty.systemField) {
                const isSelected = !!this.selectedProperties.find(s => s.key === availableProperty.key);
                this.availableOutputMappings.push(
                    {
                        key: availableProperty.key,
                        icon: 'icon icon-set-datatypes icon-size16 ic-' + availableProperty.typeIcon.toLowerCase(),
                        title: availableProperty.rowTitle,
                        isSelected
                    } as IDropDownMenuItem
                );
            }
        });

        this.setupAvailableDataFieldsForSelectedRows();
    }

    private setupAvailableDataFieldsForSelectedRows(): void {
        this.availableDataFieldsForSelectedRow = {};

        if (!this.dataFieldItems) {
            this.setupDataFieldDropdownItems();
        }

        this.selectedProperties?.forEach(selectedProperty => {
            const key = selectedProperty.key;
            this.availableDataFieldsForSelectedRow[key] = { items: [], footerItems: [] };
            this.availableDataFieldsForSelectedRow[key].items = $.extend(true, [], this.dataFieldItems?.slice()); //deep clone array
            //If a datafield has been selected and saved, set item selected
            const savedExpression = this.savedProperties[key];
            if (savedExpression?.smartFields?.length > 0) {
                let dataFieldPath = savedExpression.smartFields[0].designTimeValue;
                //Get datafield from dataFieldItems by quickpath (to ensure title, type etc is latest)
                let dataField: IDropDownMenuItem = null;
                let itemToSelect: IDropDownMenuItem = null;
                if (dataFieldPath) {
                    itemToSelect = this.availableDataFieldsForSelectedRow[key].items.find(d => d.key === dataFieldPath);
                } else {
                    //Smartfield designTimeValue is not saved when KPRX loaded, need to find datafield by title
                    itemToSelect = this.availableDataFieldsForSelectedRow[key].items.find(d => d.title === savedExpression.smartFields[0].text);
                    dataFieldPath = itemToSelect? itemToSelect.key : '';
                }
                dataField = dataFieldPath ? this.dataFieldItems?.find(d => d.key === dataFieldPath) : null;
                selectedProperty.value = savedExpression;

                if (itemToSelect) {
                    itemToSelect.isSelected = true;
                    //update selected item:
                    selectedProperty.valueTitle = dataField?.title || itemToSelect.title;
                    selectedProperty.valueTypeIcon = dataField?.icon || itemToSelect.icon;
                }
                if (!dataField) {
                    //Datafield has been deleted, do validation
                    this.processService.raiseReferenceError(savedExpression.smartFields[0], savedExpression.smartFields[0].text, dataFieldPath);
                    //Use old saved title to display in dropdown
                    selectedProperty.valueTitle = savedExpression.smartFields[0].text;
                    selectedProperty.valueTypeIcon = dataTypes.icons.default; //we do not know the type, default to text
                }
            }
            this.availableDataFieldsForSelectedRow[key].footerItems = this.getDataFieldsDdFooterItems(key);
        });
    }

    /**
     * Adds or removes a row from selected output mappings
     *
     * @param item
     */
    addOrRemoveSelectedOutputMapping(item: IDropDownMenuItem) {
        let keyToRemove = '';
        if (!item.isSelected && this.availableOutputMappingsForSelectedRow[item.key]) {
            //Remove
            const removeIndex = this.selectedProperties?.indexOf(this.selectedProperties?.find(s => s.key === item.key));
            if (removeIndex >= 0) {
                this.selectedProperties.splice(removeIndex, 1);
                keyToRemove = item.key;
            }
        } else {
            //Add
            const property = this.availableProperties.find(a => a.key === item.key);

            //Ensure correct icon
            property.typeIcon = property.typeIcon?.toLocaleLowerCase() || dataTypes.icons.default;

            //Required properties should be added to the top of the list
            //TODO: Verify if this really matters (cannot find how properties/datafields could be required)
            if (property.required) {
                //get index of last required property
                const requiredProps = this.selectedProperties.filter(p => p.required);
                let lastRequiredPropIndex = 0;
                if (requiredProps?.length > 0) {
                    lastRequiredPropIndex = this.selectedProperties.indexOf(requiredProps[requiredProps.length - 1]);
                }
                //insert selected required property at top of list, but underneath other required properties
                this.selectedProperties.splice(lastRequiredPropIndex, 0, property);
            } else {
                this.selectedProperties.push(property);
            }
        }

        //Refresh saved properties
        this.updateSavedProperties(keyToRemove);

        //Refresh list of available and selected output mappings for rows:
        this.setupAvailableOutputMappingsForRows();
    }

    /**
     * Selected output mapping key has changed
     * Update all other dropdowns with available properties
     *
     * @param item
     */
    updateSelectedOutputMapping(item: IDropDownMenuItem, previousKey: string) {
        //Clear errors on the datafield smartfield
        if (this.savedProperties[previousKey]?.smartFields?.length > 0 &&
            this.savedProperties[previousKey].smartFields[0].errorInformation?.length > 0) {
            this.validationService.forceAllPassValidation(this.savedProperties[previousKey].smartFields[0]);
        }

        //Remove previously selected item from selected items collection
        const removeIndex = this.selectedProperties?.indexOf(this.selectedProperties?.find(s => s.key === previousKey));
        if (removeIndex >= 0) {
            this.selectedProperties.splice(removeIndex, 1);
        }

        //Add newly selected item to  selected items collection
        const property = this.availableProperties.find(a => a.key === item.key);
        this.selectedProperties.splice(removeIndex, 0, property);
        //Reset the value for that available property to not set
        property.value = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
            smartFields: []
        });

        //Refresh saved properties
        this.updateSavedProperties(previousKey);

        //Refresh list of available and selected output mappings for rows:
        this.setupAvailableOutputMappingsForRows();
    }


    /**
     * Method is called when a row is clicked
     */
    mapperRowSelected(index: number): void {
        this.selectedRowItem = this.selectedProperties[index];
        this.rowSelected = !this.selectedRowItem.locked;
        this.rowDropdownFocus = false;
        this.rowValueControlFocus = false;
        this.selectedRowIndex = index;
    }

    /**
     * Method is used to delete a mapper row
     */
    deleteMapperRow(): void {
        this.selectedProperties[this.selectedRowIndex].altered = false;
        const selectedKey = this.selectedRowItem.key;

        //Clear errors on the datafield smartfield
        if (this.savedProperties[selectedKey]?.smartFields?.length > 0 &&
            this.savedProperties[selectedKey].smartFields[0].errorInformation.length > 0) {
            this.validationService.forceAllPassValidation(this.savedProperties[selectedKey].smartFields[0]);
        }
        this.savedProperties.remove(selectedKey);

        this.selectedProperties.splice(this.selectedRowIndex, 1);

        if (this.selectedRowItem.value.errorInformation.length > 0) {
            const idx: number = this.availableProperties.indexOf(this.selectedRowItem);
            if (idx > -1) {
                this.availableProperties.splice(idx, 1);

                this.validationService.reevaluateErrors
                (this.savedProperties.getLogicalParentModel<IValidatedComponent>().childErrorInformation,
                    true);
            }
        }

        this.rowSelected = false;
        this.selectedRowIndex = -1;
        this.selectedItemKey = '';

        //Refresh saved properties
        this.updateSavedProperties(this.selectedRowItem.key);

        //Refresh list of available and selected output mappings for rows:
        this.setupAvailableOutputMappingsForRows();
    }

    /**
     * Populates the value dropdown items (<right> value of the <left> output mapping)
     * with all available datafields
     * */
    setupDataFieldDropdownItems(): IDropDownMenuItem[] {
        this.dataFieldItems = [];
        const dataFields = this.processService.root?.configuration?.dataFields || [];

        for (let d = 0; d < dataFields.length; d++) {
            if (!dataFields[d].systemField) {
                this.dataFieldItems.push({
                    icon: this.getIconFromType(dataFields[d].type.toString()),
                    key: dataFields[d].getReference(),
                    title: dataFields[d].title,
                    metaData: {
                        valueItemIcon: '',
                        valueItemTitle: ''
                    }
                });
            }
        }

        return this.dataFieldItems;
    }

    /**
     * Aligns saved output mappings and their values with the selection changes
     *
     * @param keyToRemove - optional, key to remove if saved property was updated to use another key
     */
    private updateSavedProperties(keyToRemove: string = ''): void {
        this.selectedProperties.forEach(selectedProperty => {
            if (this.savedProperties[selectedProperty.key]) {
                this.savedProperties[selectedProperty.key] = selectedProperty.value;
            } else {
                this.savedProperties.add(selectedProperty.key, selectedProperty.value);
            }
        });

        if (keyToRemove.length > 0 && this.savedProperties[keyToRemove]) {
            this.savedProperties.remove(keyToRemove);
        }
    }

    private setIconFromRowValue(selectedProperty: IK2OutputMapperRow): void {
        selectedProperty.valueTypeIcon = '';

        if (selectedProperty.value?.smartFields?.length) {
            this.setupDataFieldDropdownItems();
            for (let i = 0; i < this.dataFieldItems.length; i++) {
                if (this.dataFieldItems[i].key === selectedProperty.value.smartFields[0].designTimeValue
                    || this.dataFieldItems[i].title === (selectedProperty.value.smartFields[0] as ITextSmartField).text) {

                    selectedProperty.valueTypeIcon = this.dataFieldItems[i].icon;
                    break;
                }
            }
        }
    }

    private setSelectedItemTitleDropdown(item: IK2OutputMapperRow): void {
        if (this.selectedRowItem?.value?.smartFields?.length) {
            this.setupDataFieldDropdownItems();
            for (let i = 0; i < this.dataFieldItems.length; i++) {
                if (this.dataFieldItems[i].key === this.selectedRowItem.value.smartFields[0].designTimeValue) {
                    if (this.dataFieldItems[i].title !== (this.selectedRowItem.value.smartFields[0] as ITextSmartField).text) {
                        (this.selectedRowItem.value.smartFields[0] as ITextSmartField).text = this.dataFieldItems[i].title;
                    }

                    item.valueTitle = this.dataFieldItems[i].title;
                    return;
                }
            }

            if (this.selectedRowItem.value.smartFields.length === 1) {
                item.valueTitle = (this.selectedRowItem.value.smartFields[0] as ITextSmartField).text;
                return;
            }
        }

        item.valueTitle = '';
    }

    /**
     * When datafield is selected, update saved row
     *
     * @param item
     * @param selectedKey
     */
    onDataFieldSelected(item: IDropDownMenuItem, selectedKey: string) {
        if (item.ignoreUpdateSelection) return; //footer item (create new)

        //Clear errors on the datafield smartfield
        if (this.savedProperties[selectedKey]?.smartFields?.length > 0 &&
            this.savedProperties[selectedKey].smartFields[0].errorInformation.length > 0) {
            this.validationService.forceAllPassValidation(this.savedProperties[selectedKey].smartFields[0]);
        }

        const savedValue = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
            smartFields:
                [
                    sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                        text: item.title,
                        designTimeValue: item.key
                    })
                ]
        });

        //Only need to update the one changed saved property's value
        this.updateSavedRowWithDatafieldTitle(savedValue, selectedKey);
        this.setDataFieldIconAndTitle(selectedKey);

    }

    private setDataFieldIconAndTitle(selectedKey: string): void {
        const selectedProperty = this.selectedProperties.find(p => p.key === selectedKey);
        if (selectedProperty) {
            this.setIconFromRowValue(selectedProperty);
            this.setSelectedItemTitleDropdown(selectedProperty);
        }
    }

    private updateSavedRowWithDatafieldTitle(savedValue: IExpression, selectedKey: string) {
        if (this.savedProperties[selectedKey]) {
            this.savedProperties[selectedKey] = savedValue;
        } else {
            this.savedProperties.add(selectedKey, savedValue);
        }

        this.selectedRowItem.value = savedValue;
    }

    private getIconFromType(typeOfIcon: string): string {
        const iconType: string = this.dataTypeResolverService.getSpriteClassFromDataType(typeOfIcon);
        return `icon-set-datatypes ic-${iconType}`;
    }

    private getDataFieldsDdFooterItems(rowKey: string): IDropDownMenuItem[] {
        const dataFieldsFooterItems: IDropDownMenuItem[] = [];
        // add separator and 'create new' item
        dataFieldsFooterItems.push(DropDownComponent.separator);

        dataFieldsFooterItems.push({
            key: rowKey,
            icon: 'icon-set-steps ic-dataevent',
            title: this.resourceService.getString('workflow_dataFields_createNew'),
            ignoreUpdateSelection: true,
            onSelected: this.showNewDataFieldRowPopup.bind(this)
        });

        return dataFieldsFooterItems;
    }

    private showNewDataFieldRowPopup(item: IDropDownMenuItem): void {

        const rootConfig = this.processService.root.configuration;

        rootConfig.dataFields.push
        (sourceCode.k2Designer.objectModel.workflow.process.k2Process.DataField.createInstance({
            title: this.getNewDataFieldTitle(),
            type: DataFieldTypes.string,
            isEmptyValue: true
        }));

        item.isSelected = false;

        this.popupService.showDataFieldPopup(
            rootConfig,
            rootConfig.dataFields[rootConfig.dataFields.length - 1],
            this.onNewDataFieldCreated.bind(this, item.key),
            this.onCancelNewDataField.bind(this, rootConfig.dataFields[rootConfig.dataFields.length - 1], item.key),
            this);
    }

    private getNewDataFieldTitle(): string {
        const defaultName: string = this.resourceService.getString('workflow_dataFields_defaultFieldName');

        return findUniqueName(this.processService.root.configuration.dataFields, 'title', defaultName);
    }

    private onCancelNewDataField(dataField: IDataField, rowKey: string) {
        dataField.removeFromModelArray();
        //Set selected value back
        const oldDataFieldKey = this.savedProperties[rowKey]?.smartFields[0]?.designTimeValue;
        if (oldDataFieldKey) {
            const itemToSelect = this.dataFieldItems.find(d => d.key === oldDataFieldKey);
            if (itemToSelect) {
                this.onDataFieldSelected(itemToSelect, rowKey);
            }
        }
    }

    private onNewDataFieldCreated(rowKey: string): void {
        const rootConfig = this.processService.root.configuration;
        const item = rootConfig.dataFields[rootConfig.dataFields.length - 1];

        this.undoRedoService.addUndoMarker('--k2OutputMapper: on new datafield created--');

        const savedValue = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
            smartFields:
                [
                    sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                        text: item.title,
                        designTimeValue: item.getReference()
                    })
                ]
        });

        //Refresh datafield items collection:
        this.setupDataFieldDropdownItems();
        this.updateSavedRowWithDatafieldTitle(savedValue, rowKey);
        //Add new created datafield to all datafield dropdowns
        this.setupAvailableDataFieldsForSelectedRows();
    }

    private onDataFieldsChanged = (): boolean =>  {
        this.setupDataFieldDropdownItems();
        this.setupAvailableDataFieldsForSelectedRows();
        return true;
    };
}
