import { Component, Input, OnInit } from '@angular/core';
import { take } from 'rxjs';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { RemoteEventReceiversPopulatedEvent } from 'src/app/shared/constants/events';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { IRemoteEventReceiver } from '../../../../../types/uiComponents/pluginWizards/IRemoteEventReceiver';
import { ISharePointContext } from '../../../../../types/uiComponents/pluginWizards/ISharePointContext';
import { SharePointService } from '../../../../services/plugins/share-point/share-point.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-start-workflow-events',
    templateUrl: './start-workflow-events.component.html',
    styleUrls: ['./start-workflow-events.component.less'],
    providers: [{ provide: PageComponent, useExisting: StartWorkflowEventsComponent }]
})
export class StartWorkflowEventsComponent extends PageComponent implements OnInit {

    @Input() config: IPopupConfiguration;

    /**
     * Context of the plugin wizard.
     */
    pluginContext: ISharePointContext;
    /**
     * Start workflow events data collection
     */
    eventDataCollection: IRemoteEventReceiver[] = [];
    /**
     * Choose control types enumerator
     */
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    /**
     * Dynamic heading for the available events.
     */
    triggerHeading = '';
    /**
     * Main heading displayed above the trigger heading
     */
    heading = '';

    buttonConfig: IPopupButtonsConfig;

    constructor(protected resourceService: ResourceService,
        private sharePointHelperService: SharePointService,
        private eventNotifier: EventNotifierService) {
        super(resourceService);
    }

    beforePageDisplayed = () => {
        this.buttonConfig = {
            next: {
                isDisabled: this.pluginContext?.eventReceiverString?.length === 0
            }
        };
    };

    beforeNavigateNext = () => { };
    beforeNavigatePrevious = () => { };

    ngOnInit(): void {
        this.pluginContext = this.config.context as ISharePointContext;

        this.eventNotifier.addEvent(RemoteEventReceiversPopulatedEvent)
            .pipe(take(1))
            .subscribe({
                next: () => {
                    this.eventDataCollection = this.pluginContext.remoteEventReceivers?.EventDataCollection;
                }
            });

        this.heading = this.getString('sharePoint_integration_trigger_heading');

        this.triggerHeading = this.getString('sharePoint_integration_trigger_description')
            .replace('{0}', this.pluginContext.itemName)
            .replace('{1}', this.pluginContext.isDocLib ?
                this.getString('sharePoint_integration_found_references_library') :
                this.getString('sharePoint_integration_found_references_list'));
    }

    /**
     * Method when event is selected.
     */
    public eventSelected() {

        this.sharePointHelperService.detectChanges(this.pluginContext);
        this.buttonConfig.next.isDisabled = this.pluginContext.eventReceiverString.length === 0;
        this.notifyConfigChanged();

    }
}
