import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockContextProviderService } from '../../../testing/mock-services/context-provider-service';
import { IContextProviderService } from '../../../types/services/context-provider/i-context-provider-service';

const contextProviderServiceFactory = () => environment.devHarness === true
    ? new MockContextProviderService()
    : sourceCode.k2Designer.core.environmentService.activeStudioItem.getService(sourceCode.k2Designer.services.studioItem.ContextProviderService);

export const ContextProviderServiceToken = new InjectionToken<IContextProviderService>('IContextProviderService');

export const ContextProviderFactory = {
    provide: ContextProviderServiceToken,
    useFactory: contextProviderServiceFactory,
    deps: [

    ]
};
