import { Component, Inject, Input, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { EndConfig } from 'src/app/shared/enums/activities/end-config';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';
import { IConfigPanelTab } from '../../../../../../../types/models/config-panel/i-config-panel-tab';
import { IConfigTabValidatedComponent } from '../../../../../../../types/models/config-panel/i-config-tab-validated-component';
import { ISetFolioEventConfiguration } from '../../../../../../../types/models/process/events/configuration/i-set-folio-event-configuration';
import { IEvent } from '../../../../../../../types/models/process/events/i-event';
import { IConfigPanelService } from '../../../../../../../types/services/config-panel/i-config-panel-service';
import { IValidationService } from '../../../../../../../types/services/validation/ivalidation.service';
import { ConfigPanelServiceToken } from '../../../../../../services/config-panel/config-panel-service-factory';
import { ValidationServiceToken } from '../../../../../../services/validation/validation-service-factory';
import { ComponentIdMapping } from '../../../../../../shared/constants/components';
import { PluginBaseComponent } from '../../../base/plugin-base/plugin-base.component';

@Component({
    selector: 'k2-set-folio-step',
    templateUrl: './set-folio-step.component.html',
    styleUrls: ['./set-folio-step.component.less']
})
export class SetFolioStepComponent extends PluginBaseComponent implements OnInit {

    @Input() eventConfiguration: ISetFolioEventConfiguration;
    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    event: IEvent;

    constructor(protected resourceService: ResourceService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnInit(): void {

        //Ensure the object model is properly applied on first create of step
        if (this.eventConfiguration.componentId !== ComponentIdMapping.SetFolioEventConfiguration) {
            this.event = this.eventConfiguration.getParentModel<IEvent>();
            this.event.componentId = ComponentIdMapping.SetFolioEvent;

            this.eventConfiguration =
                sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.setFolio.SetFolioConfiguration.createInstance() as ISetFolioEventConfiguration;
            this.eventConfiguration.folioField = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                smartFields: []
            });

            this.event.configuration = this.eventConfiguration;
        }

        //Cannot use inherited tab validation, because scope object can be recreated.
        this.configureTabValidation();
    }

    private configureTabValidation(): void {
        //Clear predefined watches on formConfiguration
        this.configPanelTab.componentsToWatch = [];

        const objToValidateConfigTab: IConfigTabValidatedComponent = {
            componentObj: this.eventConfiguration,
            compareFullReference: false
        };
        this.addObjectsToValidateConfigTab([objToValidateConfigTab], this.configPanelTab);
    }
}
