import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { IConfigPanelTab } from '../../../../../../../types/models/config-panel/i-config-panel-tab';
import { IExpression } from '../../../../../../../types/models/core/configured-smart-fields/i-expression';
import { IEmailConfiguration } from '../../../../../../../types/models/process/events/configuration/i-email-configuration';
import { IConfigPanelService } from '../../../../../../../types/services/config-panel/i-config-panel-service';
import { IEnvironmentService } from '../../../../../../../types/services/environment/i-environment-service';
import { IRecentsService } from '../../../../../../../types/services/recents/i-recents-service';
import { IValidationService } from '../../../../../../../types/services/validation/ivalidation.service';
import { IRecipientValidationOptions } from '../../../../../../../types/validation/i-recipient-validation-options';
import { ConfigPanelServiceToken } from '../../../../../../services/config-panel/config-panel-service-factory';
import { ConfigurationService } from '../../../../../../services/configuration/configuration-service';
import { EnvironmentServiceToken } from '../../../../../../services/environment/environment-service-factory';
import { ProcessService } from '../../../../../../services/process/process.service';
import { RecentsServiceToken } from '../../../../../../services/recents/recents-service-factory';
import { ResourceService } from '../../../../../../services/resource/resource.service';
import { ValidationServiceToken } from '../../../../../../services/validation/validation-service-factory';
import { ComponentIdMapping } from '../../../../../../shared/constants/components';
import { EmailConstants } from '../../../../../../shared/constants/email';
import { installTypes } from '../../../../../../shared/constants/general';
import { plugins } from '../../../../../../shared/constants/plugins';
import IDropDownMenuItem from '../../../../../../shared/enums/drop-down/drop-down-menu-item';
import { EmailParts } from '../../../../../../shared/enums/email/email-parts';
import { PluginBaseComponent } from '../../../base/plugin-base/plugin-base.component';

@Component({
    selector: 'k2-send-email-step',
    templateUrl: './send-email-step.component.html',
    styleUrls: ['./send-email-step.component.less']
})
export class SendEmailStepComponent extends PluginBaseComponent implements OnInit {
    @Input() event: sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.email.IEmailEvent;

    @Input() eventConfiguration: IEmailConfiguration;

    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    @Output() registerThirdPanel = new EventEmitter<string>();

    partDisableList: EmailParts[] = [];
    partHideList: EmailParts[] = [];
    partReadonlyList: EmailParts[] = [];
    //Email component validation options:
    validation: IRecipientValidationOptions = {
        from: [
            {
                Required:
                {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_from',
                        'resource_service_tab_send_email'
                    ]
                }
            }],
        cc: [
            {
                Required:
                {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_cc',
                        'resource_service_tab_send_email'
                    ]
                }
            }
        ],
        bcc: [
            {
                Required: {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_bcc',
                        'resource_service_tab_send_email'
                    ]
                }
            }
        ],
        to: [
            {
                Required: {
                    validationFailedMessage: 'resource_service_empty_field_in_list',
                    validationResourceTokens: [
                        'resource_service_field_name_to',
                        'resource_service_tab_send_email'
                    ]
                }
            }
        ]
    };

    get ui(): sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.email.IEmailEventUI {
        return this.event?.ui;
    }

    constructor(resourceService: ResourceService,
        private _processService: ProcessService,
        @Inject(RecentsServiceToken) private _recentsService: IRecentsService,
        @Inject(EnvironmentServiceToken) private _env: IEnvironmentService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        private configurationService: ConfigurationService) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnInit(): void {
        const isFromEmailAddressVisible = this.configurationService.installType.toUpperCase() === installTypes.blackPearl;

        // Update K2 Cloud email configurations, resetting the 'from' address expression to system default (custom expressions not allowed)
        if (!isFromEmailAddressVisible) {
            const fromAddress = sourceCode.k2Designer.objectModel.smartFieldPlugins.EnvironmentFieldPlugin.createExternalDefinition(
                this._processService.root as unknown as sourceCode.k2Designer.services.studioItem.workflow.ProcessService,
                EmailConstants.FromAddress);

            // Replace existing expression with K2 Cloud system default
            this.eventConfiguration.from = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                smartFields: [
                    sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.EnvironmentField.createInstance({
                        title: fromAddress.name,
                        environmentFieldReference: fromAddress.getReference(),
                        componentId: ComponentIdMapping.k2EnvironmentField
                    })
                ]
            });

            // Reset the visibility of the From address
            this.partDisableList.push(EmailParts.From);
        }

        if (!this.eventConfiguration.showFrom) {
            this.partHideList.push(EmailParts.From);
        }

        const hasCC: boolean = this.eventConfiguration?.cc?.some((cc: IExpression) => cc.smartFields.length > 0 || cc.errorInformation.length > 0);

        const hasBCC: boolean = this.eventConfiguration?.bcc?.some((bcc: IExpression) => bcc.smartFields.length > 0 || bcc.errorInformation.length > 0);

        if (!this.ui.showCC && !hasCC) {
            this.partHideList.push(EmailParts.Cc);
        }

        if (!this.ui.showBCC && !hasBCC) {
            this.partHideList.push(EmailParts.Bcc);
        }

        this.registerThirdPanel.emit(plugins.panels.emailBody);
    }

    getUserDropDownList() {
        const items: IDropDownMenuItem[] = [];

        items.push(<IDropDownMenuItem>{
            title: this.resourceService.getString(`user_task_step_recipient_originator`),
            key: `Originator`,
            icon: `icon-set-picker ic-recipientnondynamic`,
        });

        items.push(<IDropDownMenuItem>{
            title: this.resourceService.getString(`user_task_step_recipient_originator_manager`),
            key: `ProcessOriginatorManager`,
            icon: `icon-set-picker ic-recipientnondynamic`,
        });

        const listOfRecentUser: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.IUserField[] = this.getRecentlySelectedUsers();

        for (const recentUser of listOfRecentUser) {
            items.push(<IDropDownMenuItem>{
                title: recentUser.displayName,
                key: recentUser.displayName,
                additionalValueProperty: recentUser
            });
        }

        return items;
    }

    private getRecentlySelectedUsers() {
        let items = [];
        if (this._recentsService.itemCount(`RecentlySelectedUsers`) > 0) {
            items = this._recentsService.getItems(`RecentlySelectedUsers`, 10);
        }
        return items;
    }

    updateUI(partHideList: EmailParts[]) {
        if (partHideList.includes(EmailParts.Cc) && this.ui.showCC) {
            this.ui.showCC = false;
        } else if (!partHideList.includes(EmailParts.Cc) && !this.ui.showCC) {
            this.ui.showCC = true;
        }
        if (partHideList.includes(EmailParts.Bcc) && this.ui.showBCC) {
            this.ui.showBCC = false;
        } else if (!partHideList.includes(EmailParts.Bcc) && !this.ui.showBCC) {
            this.ui.showBCC = true;
        }
        if (partHideList.includes(EmailParts.From) && this.eventConfiguration.showFrom) {
            this.eventConfiguration.showFrom = false;
        } else if (!partHideList.includes(EmailParts.From) && !this.eventConfiguration.showFrom) {
            this.eventConfiguration.showFrom = true;
        }
    }
}
