import { Inject, Injectable } from '@angular/core';
import { IModelArray } from '../../../types/models/base/i-model-array';
import { IActivityConfiguration } from '../../../types/models/process/activities/configuration/i-activity-configuration';
import { IActivity } from '../../../types/models/process/activities/i-activity';
import { IEvent } from '../../../types/models/process/events/i-event';
import { IOutcome } from '../../../types/models/process/outcomes/i-outcome';
import { IConfiguredComplexLogicalGroupExpression } from '../../../types/models/rules/i-configured-complex-logical-group-expression';
import { IConfiguredIfThenElseStatement } from '../../../types/models/rules/i-configured-if-then-else-statement';
import { IConfiguredOutcomeStatement } from '../../../types/models/rules/i-configured-outcome-statement';
import { IConfiguredStatement } from '../../../types/models/rules/i-configured-statement';
import { IValidationService } from '../../../types/services/validation/ivalidation.service';
import { ComponentIdMapping } from '../../shared/constants/components';
import { DecisionOptionType } from '../../shared/enums/model/workflow/process/decision-option-types';
import { isUndefinedOrNull } from '../../shared/utils/general';
import { RulePluginService } from '../ruleplugin/rule-plugin-service';
import { ValidationServiceToken } from '../validation/validation-service-factory';

/**
 * Workflow, nodes and process related service
 */
@Injectable({
    providedIn: 'root'
})
export class WorkflowService {

    constructor(@Inject(ValidationServiceToken) protected validationService: IValidationService,
        protected rulePluginService: RulePluginService)
    {
    }

    /**
     * Auto populates the rule for split or decision step, based on linked task's outcomes.
     * Resets all validation errors on rule before creating new rule.
     *
     * @param decisionOrSplitStep the decision or split step to auto generate outcome rule for.
     * @param forcePassValidation indicates whether or not to instruct validation to occur within the method
     */
    createAutoRuleForDecisionOrSplit(decisionOrSplitStep: IActivity,
        forcePassValidation: boolean = true): void {

        if (isUndefinedOrNull(decisionOrSplitStep) ||
            isUndefinedOrNull(decisionOrSplitStep.configuration)) {
            //Invalid data
            return;
        }

        if (decisionOrSplitStep.configuration.decisionOptionType !== DecisionOptionType.auto) {
            //Only execute if rule type is auto
            return;
        }

        //Reference of event that decision is linked to:
        const outcomesEventReference: string = decisionOrSplitStep.configuration.outcomesEventReference || '';

        if (outcomesEventReference.length === 0) {
            //Only execute if decision is linked to an activity
            return;
        }

        const linkedActivityEvent = this.findItem<IEvent>(outcomesEventReference, decisionOrSplitStep);
        if (!linkedActivityEvent) {
            //Not linked to a valid activity event
            return;
        }
        //Find activity that decision is linked to:
        const linkedActivity = linkedActivityEvent.getLogicalParentModel<IActivity>();

        if (isUndefinedOrNull(linkedActivity) || isUndefinedOrNull(linkedActivity.configuration)) {
            //Not linked to a valid activity
            return;
        }

        //Clear the errors in the outcome rule before creating the new one
        if (decisionOrSplitStep.configuration.outcomeRule && forcePassValidation) {
            this.validationService.forceAllPassValidation(decisionOrSplitStep.configuration.outcomeRule);
            if (decisionOrSplitStep.configuration.outcomeRule.statements) {
                for (let s = 0; s < decisionOrSplitStep.configuration.outcomeRule.statements.length; s++) {
                    this.validationService.forceAllPassValidation(decisionOrSplitStep.configuration.outcomeRule.statements[s]);
                }
            }
        }
        //Clear the errors in the outcomes
        const validationData = new sourceCode.k2Designer.objectModel.validationService.plugins.ValidatorItem();
        validationData.modelObject = decisionOrSplitStep.configuration;
        validationData.propertyName = 'outcomes';

        if (forcePassValidation) {
            this.validationService.forcePassValidation(validationData);
        }

        //Reset current rule:
        if (decisionOrSplitStep.configuration.outcomeRule && decisionOrSplitStep.configuration.outcomeRule.statements) {
            decisionOrSplitStep.configuration.outcomeRule.statements.clear();
        }

        const decisions = decisionOrSplitStep.configuration.outcomes;
        if (decisions.length === 0) {
            //Decision step has no outcomes (decisions) defined, there can be no rule
            return;
        }

        const outcomes = linkedActivity.configuration.outcomes;
        if (outcomes.length === 0) {
            //Linked activity has no outcomes, there can be no rule
            return;
        }

        //Start creating default rule recursively from linked activity:
        decisionOrSplitStep.configuration.outcomeRule = sourceCode.k2Designer.objectModel.workflow.configuredRules.ConfiguredDecisionRule.createInstance({
            statements: [this.createTaskExpression(linkedActivity as unknown as IActivity, 0, decisionOrSplitStep)]
        });
    }

    /**
     * Creates the default rule (if-else statement) based on the linked activity's outcomes configurations
     *
     * @param linkedActivity Activity that has actions that are used for configuring the outcomes based step
     * @param linkedActivityOutcomeIndex Outcome index to use for configuring the 'IF-THEN' part of the expression (ELSE part will use next outcome if exists)
     * @param outcomesStep Outcomes-based step on which the default rule needs to be added
     * @param environmentService Needed to access the rules plugin service
     * @returns ifStatement default rule
     */
    public createTaskExpression(
        linkedActivity: IActivity,
        linkedActivityOutcomeIndex: number,
        outcomesStep: IActivity): IConfiguredIfThenElseStatement {

        const nextOutcome = outcomesStep.configuration.outcomes[linkedActivityOutcomeIndex + 1];
        const elseStatements: IModelArray<IConfiguredStatement> = [];
        if (!isUndefinedOrNull(nextOutcome)) {
            elseStatements.push(this.createTaskExpression(linkedActivity, linkedActivityOutcomeIndex + 1, outcomesStep));
        }

        // Build up a rule using the rule service to retrieve the required components and assign relevant values

        const taskExpression = this.rulePluginService.createExpression('TaskExpression') as sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredTaskExpression;
        //Reference to the activity:
        taskExpression.activityReference = linkedActivity.getReference();

        const decisionOutcome = outcomesStep.configuration.outcomes[linkedActivityOutcomeIndex];

        //Reference to activity outcome that corresponds to current decision outcome:
        taskExpression.outcomeReference = '';
        for (var i = 0; i < linkedActivity.configuration.outcomes.length; i++) {
            if (linkedActivity.configuration.outcomes[i].getReference() === decisionOutcome.linkedOutcomeReference) {
                taskExpression.outcomeReference = decisionOutcome.linkedOutcomeReference;
                break;
            }
        }

        const groupExpression = this.rulePluginService.createExpression('GroupExpression') as IConfiguredComplexLogicalGroupExpression;
        groupExpression.expressions.push(taskExpression);

        const outcomeStatement = this.rulePluginService.createStatement('OutcomeStatement') as IConfiguredOutcomeStatement;

        if (taskExpression.outcomeReference !== '' && decisionOutcome) {
            outcomeStatement.linkedOutcomeReferences.push(decisionOutcome.getReference());
        }

        const ifStatement = this.rulePluginService.createStatement('IfThenElseStatement') as IConfiguredIfThenElseStatement;
        ifStatement.IfExpressions.push(groupExpression);
        ifStatement.thenStatements.push(outcomeStatement);
        for (var i = 0; i < elseStatements.length; i++) {
            ifStatement.elseStatements.push(elseStatements[i]);
        }

        return ifStatement;
    }

    /**
     * Returns true if all configured decision outcomes are connected to a correspoding task action
     *
     * @param decisionConf - decision configuration
     */
    public isAllOutcomesConnected(decisionConf: IActivityConfiguration): boolean {
        //If no outcomes defined, return false:
        if (decisionConf.outcomes.length === 0) {
            return false;
        }
        const allConnected = true;

        let prevActionInternalID = 0;
        for (let i = 0; i < decisionConf.outcomes.length; i++) {
            const outcome = decisionConf.outcomes[i];
            //If there is an outcome without a defined linked outcome reference, return false:
            if (!(outcome.linkedOutcomeReference && outcome.linkedOutcomeReference.length >= 0)) {
                return false;
            }
            else {
                //Get the linked action's outcome, that the decision is linked to:
                const linkedActionOutcome = this.findItem<IOutcome>(outcome.linkedOutcomeReference, outcome);

                //If it is not linked to an action's outcome, return false:
                if (isUndefinedOrNull(linkedActionOutcome)) {
                    return false;
                }
                else {

                    //Get the event action that is the parent of this action outcome:
                    let eventAction = null;
                    if (linkedActionOutcome.linkedActionReference && linkedActionOutcome.linkedActionReference.length > 0) {
                        //Normal action outcomes will have a linkedActionReference, pointing to the event/action object
                        eventAction = this.findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfigurationAction>
                        (linkedActionOutcome.linkedActionReference, linkedActionOutcome);

                    } else if (linkedActionOutcome.eventConfigurationReference && linkedActionOutcome.eventConfigurationReference.length > 0) {
                        //Voting outcomes will have an eventConfigurationReference, pointing to the event/action configuration object
                        const eventActionConf = this.findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfigurationAction>
                        (linkedActionOutcome.eventConfigurationReference, linkedActionOutcome);
                        eventAction = eventActionConf.getLogicalParentModel();
                    }

                    if (eventAction) {
                        //Get event configuration parent of this event action:
                        const eventConfiguration = <any>eventAction.getLogicalParentModel();
                        if (eventConfiguration) {
                            //Get the event (action child event) of this event configuration:
                            const event = eventConfiguration.getLogicalParentModel();
                            if (event) {
                                //Get the activity node parent of this event:
                                const activityParent = event.getLogicalParentModel();
                                //Check if the internal ids match:
                                if (activityParent) {
                                    if (prevActionInternalID === 0) {
                                        prevActionInternalID = activityParent.internalId;
                                    } else if (prevActionInternalID !== activityParent.internalId) {
                                        return false;
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }

        return allConnected;

    }

    /**
     * Finds item on process of type T, using startObject as starting point to find process root and path as lookup quickpath
     *
     * @param path quickpath to item / object on process
     * @param startObject any other item on process to use to find the process root
     */
    public findItem<T>(path: string, startObject: sourceCode.k2Designer.objectModel.core.model.IModelObject): T {
        return startObject
            .getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>()
            .findItem<T>(path);
    }

    /*
    * Only events of type Client Event can have outcomes
    */
    public canHaveOutcomes(event: IEvent): boolean {
        return event.componentId === ComponentIdMapping.ClientEvent;
    }

    /**
     * Method counts all incoming Ports
     *
     * @param activity activity ports that will be used to determine incoming lines
     */
    countAllIncomingPorts(activity: IActivity): number {
        let incomingLinksCount = 0;
        const ports = ['leftPorts', 'topPorts', 'rightPorts', 'bottomPorts'];

        for (const portSide of ports) {
            for (const port of activity.ui[portSide]) {
                incomingLinksCount += (port as sourceCode.k2Designer.objectModel.workflow.process.IArtefactNodePort).incomingLinkReferences.length;
            }
        }

        return incomingLinksCount;
    }
}
