import { Component, OnInit, Inject, Input } from '@angular/core';
import { IContextProviderService } from '../../../../types/services/context-provider/i-context-provider-service';
import { IObjectModelService } from '../../../../types/services/object-model/i-object-model-service';
import { ILegacyResourceService } from '../../../../types/services/resources/i-legacy-resource.service';
import { ISelectionService } from '../../../../types/services/selection/i-selection-service';
import { LegacyResourceServiceToken } from '../../../legacy-service-factories/resource-service-factory';
import { ClipboardServiceToken } from '../../../services/clipboard/clipboard-service-factory';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { ContextProviderServiceToken } from '../../../services/context-provider/context-provider-service-factory';
import { ObjectModelServiceToken } from '../../../services/object-model/object-model-service-factory';
import { SelectionServiceToken } from '../../../services/selection/selection-service-factory';

@Component({
    selector: 'tab-home',
    templateUrl: './tab-home.component.html',
    styleUrls: ['./tab-home.component.less']
})

export class TabHomeComponent implements OnInit {

    @Input() uiComp: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    @Input() public togglePin: () => void;
    @Input() public getString: (resourceId: string, collectionId?: string) => string;

    public resources?: any;
    public canCopy: boolean;
    public canPaste: boolean;
    public canCut: boolean;
    public clipboardEnabled: boolean;

    private _context: string;

    constructor(@Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        @Inject(ClipboardServiceToken) private _clipBoardService: IClipboardService,
        @Inject(CommandServiceToken) private _commandService: ICommandService,
        @Inject(SelectionServiceToken) private _selectionService: ISelectionService,
        @Inject(ObjectModelServiceToken) private _objectModelService: IObjectModelService<any>,
        @Inject(ContextProviderServiceToken) private _contextProviderService: IContextProviderService) {

        this.resources = {
            homeRibbon_paste: this._resourceService.getString('homeRibbon_paste'),
            homeRibbon_cut: this._resourceService.getString('homeRibbon_cut'),
            homeRibbon_copy: this._resourceService.getString('homeRibbon_copy'),
            homeRibbon_selectAll: this._resourceService.getString('homeRibbon_selectAll'),
            homeRibbon_alignTop: this._resourceService.getString('homeRibbon_alignTop'),
            homeRibbon_alignBottom: this._resourceService.getString('homeRibbon_alignBottom'),
            homeRibbon_alignLeft: this._resourceService.getString('homeRibbon_alignLeft'),
            homeRibbon_color: this._resourceService.getString('homeRibbon_color'),
            homeRibbon_rename: this._resourceService.getString('homeRibbon_rename'),
            ribbon_watermark_align: this._resourceService.getString('ribbon_watermark_align'),
            ribbon_watermark_step: this._resourceService.getString('ribbon_watermark_step'),
            homeRibbon_notes: this._resourceService.getString('homeRibbon_notes'),
            homeRibbon_properties: this._resourceService.getString('homeRibbon_properties'),
            homeRibbon_grid: this._resourceService.getString('homeRibbon_grid'),
            homeRibbon_1to1: this._resourceService.getString('homeRibbon_1to1'),
            homeRibbon_fittowindow: this._resourceService.getString('homeRibbon_fittowindow'),
            ribbon_watermark_view: this._resourceService.getString('ribbon_watermark_view'),
            homeRibbon_startProcess: this._resourceService.getString('homeRibbon_startProcess')
        };

        this._context = 'WorkflowDesigner';
    }

    ngOnInit(): void {
        this.updateVars();
    }

    public canPasteItem(): boolean {
        this.updateVars();
        return this.canPaste && this._clipBoardService.hasData();
    }

    public pasteItem(): void {
        this.updateVars();
        if (this.canPaste) {
            if (this._clipBoardService.paste()) {
                this.updateVars();
            }
        }
    }

    public copyItem(): void {
        if (this.canCopy) {
            if (this._clipBoardService.copy()) {
                this.updateVars();
            }
        }
    }

    public cutItem(): void {
        this.updateVars();
        if (this.canCut) {
            if (this._clipBoardService.cut()) {
                this.updateVars();
            }
        }
    }

    public canCutItem(): boolean {
        this.updateVars();
        const currentContext = this.getCurrentContext();

        return this.canCut && currentContext && currentContext.length !== 0;
    }

    public canCopyItem(): boolean {
        this.updateVars();
        const currentContext = this.getCurrentContext();

        return this.canCopy && currentContext && currentContext.length !== 0;
    }

    public selectAll(): void {
        this._commandService.invokeCommand('Select All', false, false, false, true);
        this.updateVars();
    }

    public canAlign(): boolean {
        return false;
    }

    public canRename(): boolean {
        const selection_count = this._selectionService.getSelection(this._context).length;

        if (selection_count === 1) {
            return this._selectionService.hasSelection(this._context);
        }

        return false;
    }

    public renameSelection(): void {
        this._commandService.setCurrentContext(this._context);
        this._commandService.invokeCommand('Rename', true, false, false, true);
    }

    private getCurrentContext(): any[] {
        return this._contextProviderService.getContextProviderFromModel(this._objectModelService.root).getCntx();
    }

    private updateVars(): void {
        this.canCopy = this._clipBoardService.canCopy();
        this.canPaste = this._clipBoardService.canPaste();
        this.canCut = this._clipBoardService.canCut();
        this.clipboardEnabled = this._clipBoardService.clipboardEnabled === true;
    }

}
