import { Component, OnInit, Inject, Input } from '@angular/core';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';

@Component({
    selector: 'app-tab-view',
    templateUrl: './tab-view.component.html',
    styleUrls: ['./tab-view.component.less']
})
export class TabViewComponent implements OnInit {

    @Input() public getString: (resourceId: string, collectionId?: string) => string;
    @Input() public togglePin: () => void;

    constructor(@Inject(CommandServiceToken) private _commandService: ICommandService) { }

    ngOnInit(): void {
    }

    public centerToStart(): void {
        this._commandService.invokeCommand('Center Start', false, false, true, true);
    }

    public scaleToDefault(): void {
        this._commandService.invokeCommand('Scale Default', false, false, true, true);
    }

    public scaleToCanvas(): void {
        this._commandService.invokeCommand('Fit To Window', false, false, true, true);
    }

    public zoomInCanvas(): void {
        this._commandService.invokeCommand('Zoom In', false, false, false, true);
    }

    public zoomOutCanvas(): void {
        this._commandService.invokeCommand('Zoom Out', false, false, false, true);
    }

}
