import { AfterViewInit, Component, EventEmitter, Input, OnInit, Output, QueryList, ViewChildren } from '@angular/core';
import { IDataField } from '../../../../types/models/data-field/i-data-field';
import { IChooseCreateChangeOutcomeContext } from '../../../../types/models/popup/i-choose-create-change-outcome-context';
import { IItemReference } from '../../../../types/models/process/item-references/i-item-reference';
import { IOutcome } from '../../../../types/models/process/outcomes/i-outcome';
import { IListItemIconData } from '../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { IContextPopupConfiguration } from '../../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { ISaveReferencePopupReturnData } from '../../../../types/uiComponents/pluginWizards/i-save-reference-popup-return-data';
import { ISmartFieldContext } from '../../../../types/uiComponents/pluginWizards/i-smart-field-context';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import { PopupService } from '../../../services/popup/popup.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { keyCodes } from '../../../shared/constants/general';
import { listItemIcons } from '../../../shared/constants/list-item';
import { popup } from '../../../shared/constants/wizard-popup';
import { PopupTemplate } from '../../../shared/enums/popup/popup-template';
import { ReferenceTypes } from '../../../shared/enums/references/reference-types';
import { isUndefinedOrNull } from '../../../shared/utils/general';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';
import { ListItemComponent } from '../list-item/list-item.component';

@Component({
    selector: 'k2-save-reference',
    templateUrl: './save-reference.component.html',
    styleUrls: ['./save-reference.component.less']
})
export class SaveReferenceComponent extends BaseResourceComponent implements OnInit, AfterViewInit {

    /**
     * The object representing the selected reference
     */
    @Input() selectedObject: IItemReference | IOutcome | IDataField | IValidatedComponent;

    /**
     * Property name for the title of selected object (defaults to 'title')
     */
    @Input() titleProperty = 'title';

    /**
     * Show 'choose existing option' link
     */
    @Input() shouldShowChooseOption = false;

    /**
     * Show 'edit' link
     */
    @Input() shouldShowEdit = false;

    /**
     * Disable component functionality
     */
    @Input() isDisabled = false;

    /**
     * Flag to indicate if a badge should be displayed (result is required)
     */
    @Input() isInError = false;

    /**
     * Tooltip text to display for badge that is shown (if isInError is true)
     */
    @Input() badgeTooltip: string;

    /**
     * Sub text to display underneath selected reference title
     */
    @Input() subText: string;

    /**
     * The original title value of the reference before any editing
     */
    @Input() originalValue: string;

    /**
     * Before the selected reference text is set, it will be validated against this model if specified
     */
    @Input() validationArray: sourceCode.k2Designer.objectModel.core.model.IModelArray<any>;

    /**
     * Template for popup that displays when selecting existing reference
     */
    @Input() referencePopupTemplate: PopupTemplate;

    /**
     * Popup heading for the choose-outcome dialog
     */
    @Input() dialogHeading: string;

    /**
     * List of outcomes to select from in the choose-outcome dialog
     */
    @Input() dialogObjectList: IOutcome[] | IItemReference[] | IDataField[] | IValidatedComponent[];

    /**
     * The type of reference saved (Outcome, ItemReference, DataField, Other)
     */
    @Input() referenceType: ReferenceTypes = ReferenceTypes.Other;

    @Input() customIconClass = '';

    /**
     * Event emitter for new reference creation
     */
    @Output() referenceCreated = new EventEmitter<void>();

    /**
     * Event emitter for value changed event
     * (emits true if value was reset to original)
     */
    @Output() valueChanged = new EventEmitter<{ wasReset: boolean; changedReference: IOutcome | IItemReference | IValidatedComponent}>();

    /**
     * Is reference in edit state?
     */
    isEditing: boolean;

    /**
     * Has the reference name been updated?
     * To determine if name can be reset.
     */
    hasValueChanged: boolean;

    /**
     * List item child component used to display reference
     * In ViewChildren QueryList, to subscribe and ensure it is loaded when used.
     */
    @ViewChildren('referenceListItem') listItems: QueryList<ListItemComponent>;
    listItem: ListItemComponent;
    leftIconData: IListItemIconData = listItemIcons.votingIcon;

    constructor(protected resourceService: ResourceService,
        private popupService: PopupService    ) {
        super(resourceService);
    }

    ngOnInit(): void {
        //Set list item icon-template according to reference type
        switch (this.referenceType) {
            case ReferenceTypes.Outcome:
                this.leftIconData = listItemIcons.votingIcon;
                break;
            case ReferenceTypes.ItemReference:
                this.leftIconData = listItemIcons.referenceIcon;
                break;
            case ReferenceTypes.DataField:
                this.leftIconData = listItemIcons.dataFieldIcon;
                //DataField icon changes depending on type
                this.leftIconData.iconClass = this.customIconClass;
                break;
            case ReferenceTypes.Other:
            default:
                this.leftIconData = listItemIcons.votingIcon;
        }
    }

    ngAfterViewInit() {
        this.listItem = this.listItems?.first;

        this.listItems?.changes.subscribe((components: QueryList<ListItemComponent>) => {
            this.listItem = components.first;
        });
    }

    ngOnChanges(changes) {
        if (changes.customIconClass && this.referenceType === ReferenceTypes.DataField) {
            this.leftIconData = {
                iconClass: this.customIconClass || 'header-dd datafield-icon icon icon-size16 icon-set-datatypes selected ic-text',
                isWrappedInContentDiv: true,
                isVertical: true,
                customClassFlexRow: 'reference-icon-background'
            };
        }
    }

    createNewReference(): void {
        //User selected to create new default expired reference
        this.referenceCreated.emit();
    }

    /**
     * Opens the save reference dialog.
     * Dialog displayed is determined by template input of save reference component.
     */
    showDialog(): void {
        if (this.isDisabled) {
            return;
        }

        const popupConfig: IContextPopupConfiguration = {
            buttons: {
                okButton: {
                    key: popup.buttons.ok,
                    text: this.getString('workflow_contextPopup_okButton'),
                    visible: true,
                    dismissResult: popup.buttons.okResult,
                    invokeValidation: true
                }
            },
            context: {
                contextObjectList: this.dialogObjectList,
                selectedItem: this.selectedObject,
                titleProperty: this.titleProperty
            } as IChooseCreateChangeOutcomeContext | ISmartFieldContext,
            heading: this.dialogHeading,
            modal: true,
            onClosed: (key: string, dismissResult: string) => {
                if (dismissResult === popup.buttons.okResult) {
                    this.changeReference(popupConfig.returnData);
                }
            },
            validationModel: [],
            template: this.referencePopupTemplate
        };

        this.popupService.show(popupConfig);
    }

    /**
     * Focus textbox to enable the user to edit the entered value
     */
    onEdit(): void {
        this.isEditing = true;
        this.listItem?.simpleTextInput?.doFocus();
    }

    /**
     * This method is used to reset name entered by the user
     */
    onReset(): void {
        this.isEditing = false;

        this.selectedObject[this.titleProperty] = this.originalValue;

        this.valueChanged.emit({
            wasReset: true,
            changedReference: this.selectedObject
        });
    }

    /**
     * Callback method from popup when reference is changed and popup dismissed
     *
     * @param selectedReference
     */
    changeReference(referenceReturnData: ISaveReferencePopupReturnData): void {

        if (referenceReturnData.isNewReference) {
            //Let parent know a new reference should be created
            this.referenceCreated.emit();
            return;
        }

        this.selectedObject = referenceReturnData.selectedReference;
        this.processValueChanged();
    }

    onInputKeyPressed(keyCode: number): void {
        if (keyCode === keyCodes.ENTER) {
            this.processValueChanged();
        }
    }

    onInputBlurred(target: EventTarget): void {
        this.processValueChanged();
    }

    /**
     * Checks if value differs from original value, to hide/show 'Reset Name'
     * Emits value changed event
     * */
    private processValueChanged(): void {
        if (!this.selectedObject) {
            //None selected (if original value also empty, emit true for reset to original)
            this.hasValueChanged = !isUndefinedOrNull(this.originalValue) || this.originalValue !== '';
        } else {
            if (this.selectedObject[this.titleProperty] !== this.originalValue) {
                this.selectedObject[this.titleProperty] = this.selectedObject[this.titleProperty]?.toString().trimEnd() || '';
                this.hasValueChanged = true;
            }
            else {
                //Reset back to original
                this.hasValueChanged = false;
            }
        }
        this.valueChanged.emit({
            wasReset: !this.hasValueChanged,
            changedReference: this.selectedObject
        });
    }

}
