import { Component, Input, OnInit } from '@angular/core';
import { DataTypeResolverService } from 'src/app/services/plugins/data-type-resolver/data-type-resolver.service';
import { popup } from 'src/app/shared/constants/wizard-popup';
import { IContextWizardPopupConfiguration } from 'src/types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { ISmartFieldContext } from 'src/types/uiComponents/pluginWizards/i-smart-field-context';
import { IDataField } from '../../../../types/models/data-field/i-data-field';
import { PopupService } from '../../../services/popup/popup.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-save-smart-field-choose-dialog',
    templateUrl: './save-smart-field-choose-dialog.component.html',
    styleUrls: ['./save-smart-field-choose-dialog.component.less']
})
export class SaveSmartFieldChooseDialogComponent extends BaseResourceComponent implements OnInit {
    @Input() public config: IContextWizardPopupConfiguration;

    public selectedItemTitle: string;

    public objectList: IDataField[];

    constructor(protected resourceService: ResourceService,
        private popupService: PopupService,
        private dataTypeResolverService: DataTypeResolverService) {
        super(resourceService);
    }

    ngOnInit(): void {
        const smartFieldContext = this.config.context as ISmartFieldContext;
        if (smartFieldContext.selectedItem) {
            this.selectedItemTitle = smartFieldContext.selectedItem[smartFieldContext.titleProperty || 'title'];
            this.setReturnData();
        } else {
            this.selectNone();
        }

        this.objectList = smartFieldContext.contextObjectList.filter((field) => !field.systemField); // dont include system fields in display list

        if (!this.objectList || this.objectList.length === 0) {
            this.config.returnData =
            {
                selectedReference: null,
                isNewReference: false
            };
        }
    }

    public selectNone(): void {
        this.selectedItemTitle = '';
        (<any>this.config.context).selectedItem = null;
        this.setReturnData();
    }

    public selectItem(selectedItem: any): void {
        const smartFieldContext = this.config.context as ISmartFieldContext;
        smartFieldContext.selectedItem = selectedItem;
        this.selectedItemTitle = '';
        if (smartFieldContext.selectedItem) {
            this.selectedItemTitle = smartFieldContext.selectedItem[smartFieldContext.titleProperty || 'title'];
        }
        this.setReturnData();
    }

    public closeOnDoubleClick(): void {
        this.popupService.hideContextPopup(popup.buttons.ok, popup.buttons.okResult);
    }

    public getItemIcon(item: IDataField): string {
        if (item) {
            return `icon icon-size16 icon-set-datatypes ic-${this.dataTypeResolverService.getSpriteClassFromDataType(item.type?.toString())} selected`;
        }
    }

    /**
     * Binds to the method that executes after validation completed and before the popup closes.
     */
    private setReturnData(): void {
        const smartFieldContext = this.config.context as ISmartFieldContext;
        this.config.returnData =
        {
            selectedReference: smartFieldContext.selectedItem,
            isNewReference: false
        };
    }
}
