import { Component, ElementRef, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, ViewChild, ViewEncapsulation } from '@angular/core';
import { IExpressionConfiguration } from '../../../../../types/models/rules/i-expression-configuration';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { IValidationService } from '../../../../../types/services/validation/ivalidation.service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../services/validation/validation-service-factory';
import { rules } from '../../../../shared/constants/rules';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';

@Component({
    selector: 'k2-task-expression',
    templateUrl: './task-expression.component.html',
    styleUrls: ['./task-expression.component.less'],
    encapsulation: ViewEncapsulation.None
})
export class TaskExpressionComponent extends ExpressionBaseComponent implements OnInit, OnDestroy {

    @Input('isParent') public isParentInput = false;
    @Input() public index: number;
    @Output() public onDropped: EventEmitter<number> = new EventEmitter<number>();
    @Output() public onOperatorItemSelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();
    @Output() public onActivitySelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();
    @Output() public onOutcomeSelected: EventEmitter<IDropDownMenuItem>
        = new EventEmitter<IDropDownMenuItem>();

    @ViewChild('taskConditionBlock', { static: false }) taskConditionBlock: ElementRef;

    public operatorHover = false;
    //Enums:
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;
    public outcomeFooterArray: IDropDownMenuItem[] = [];
    public selectedActivityKey = '';

    //Operator dropdown items
    public taskExpressionOperatorDropdownItems: IDropDownMenuItem[] = [];

    //Javascript root element (the main taskConditionBlock element)
    private _rootElement: HTMLElement;

    private _outcomes: IDropDownMenuItem[] = [];
    private _activityDropDownItems: IDropDownMenuItem[] = [];
    private _selectedActivity: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity;
    private _outcomesArrayChangedRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover;
    private _processRoot: sourceCode.k2Designer.objectModel.core.model.ModelBase;

    constructor(@Inject(SelectionServiceToken) _selService: ISelectionService,
        rulePluginSvc: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoSvc: IUndoRedoService,
        resourceService: ResourceService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        eventService: EventNotifierService,
        private _el: ElementRef) {

        super(_selService, rulePluginSvc, commandService, configPanelService,
            undoRedoSvc, eventService, _el, resourceService);

        this.isParent = this.isParentInput;
        this._rootElement = this._el.nativeElement;
    }

    ngOnInit(): void {
        this.undoRedoService.forceDisable();

        super.ngOnInit();

        this._processRoot = this.rulePluginService.contextModel.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelBase>();

        //Find the current activity that rules are being configured against:
        this._selectedActivity = this._processRoot.findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>
        (this.configuredTaskExpression?.activityReference);

        if (this.expressionModel.createdFromIfDrop !== true && this.expressionModel.isSelectable) {
            //Focus on drop
            this._rootElement.focus();
        }

        //Set default icons and operator:
        const operators = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as
            IExpressionConfiguration)?.operators;

        if (!this.configuredTaskExpression.logicalOperator) {
            this.configuredTaskExpression.logicalOperator = operators.find(o => o.name === rules.defaultOperator.operatorName)?.name;
            this.configuredTaskExpression.operatorIcon = operators.find(o => o.name === rules.defaultOperator.operatorName)?.icon;
        }
        else if (!this.configuredTaskExpression.operatorIcon) {
            this.configuredTaskExpression.operatorIcon = rules.icons.operatorIconBase + this.configuredTaskExpression.logicalOperator.toLowerCase();
        }

        //Populate activities dropdown for all activity nodes on workflow (excluding parent task)
        this.setupOutcomesAndActivities();

        this.setupOperators();

        if (this._selectedActivity) {
            //Populate outcomes dropdown for activity
            for (let i = 0; i < this._selectedActivity.configuration.outcomes.length; i++) {
                const outcome = this._selectedActivity.configuration.outcomes[i] as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcomeExtended;
                this._outcomes.push({
                    title: outcome.inferredTitle,
                    titleBinding: {
                        objectReference: outcome,
                        propertyName: 'inferredTitle'
                    },
                    key: this._selectedActivity.configuration.outcomes[i].getReference(),
                    icon: rules.icons.outcomeVoting
                });

            }
            //Ensure the activity reference is the selected activity's reference
            if (!this.configuredTaskExpression.activityReference) {
                this.configuredTaskExpression.activityReference = this._selectedActivity.getReference();
            }

            this.validateOutcomeReference();

            //Set selected activity in activity dropdown
            this.selectedActivityKey = this.configuredTaskExpression.activityReference;
            const currentActivityItem = this._activityDropDownItems.find((f) => f.key === this.selectedActivityKey);
            if (currentActivityItem) {
                currentActivityItem.isSelected = true;
            }

            //Set selected outcome in outcome dropdown
            const currentOutcomeItem = this._outcomes.find((f) => f.key === this.configuredTaskExpression.outcomeReference);
            if (currentOutcomeItem) {
                currentOutcomeItem.isSelected = true;
            }
        } else {
            this.validateOutcomeReference();
        }

        this.outcomeFooterArray = this.getOutcomeFooterItems();

        this.undoRedoService.resetForceDisable();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();

        if (this._outcomesArrayChangedRemover) {
            this._outcomesArrayChangedRemover();
        }
    }

    public onDrop(): void {
        //Emit drop event to parent (GroupExpression)
        this.onDropped.emit(this.index);
    }

    /**
     * Get expression model as type ConfiguredTaskExpression
     *
     * @param item
     */
    public get configuredTaskExpression(): sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredTaskExpression {
        return this.expressionModel
            || {} as sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredTaskExpression;
    }

    /**
     * Get k2Model as type IConfiguredSimpleExpression
     *
     * @param item
     */
    public get configuredSimpleExpression(): sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression {
        return this.configuredTaskExpression as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredSimpleExpression;
    }

    /**
     * Actioned on the selection of an operator item from dropdown
     *
     * @param item
     */
    public operatorItemSelected = (item: IDropDownMenuItem): void => {
        this.undoRedoService.addUndoMarker('-- group boolean operator selected --');

        if (item.key === 'delete') {
            this.delete();
        } else {
            this.configuredSimpleExpression.logicalOperator = item.key;
            this.configuredSimpleExpression.operatorIcon = item.icon;
        }

        //Emit operator item selected event to parent
        this.onOperatorItemSelected.emit(item);
    };

    /**
     * Returns list of outcome items for outcomes dropdown list
     */
    public get outcomeItems(): IDropDownMenuItem[] {

        return this._outcomes;
    }

    /**
     * Returns list of command items to add to footer of outcome dropdown
     * */
    getOutcomeFooterItems = (): IDropDownMenuItem[] => {
        //The command items in the outcomes dropdown (e.g. 'Delete')
        const ret: IDropDownMenuItem[] = [];
        const commands = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as
            IExpressionConfiguration).commands;

        commands.forEach(command => {
            ret.push({
                title: this.resourceService.getString(command.titleToken),
                key: command.name,
                icon: command.icon,
                innerItemComponent: DropdownMenuItemTemplates.Command
            });
        });

        return ret;
    };

    /**
     * Populate activities dropdown for all activity nodes on workflow (excluding parent task step)
     * */
    private setupOutcomesAndActivities(): void {

        this._outcomes.length = 0;

        //Get current selected activity from context
        const actCnf =
            this.rulePluginService.contextModel
                .getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfigurationExtension>();
        const activity = actCnf.getParentActivity();

        const activities: IDropDownMenuItem[] = [];

        const allTaskActivities:
            sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>>
            = [];
        const clientEvtConfigImplementor = sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.ClientEventConfiguration.implementor;
        const process = activity.getRoot() as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessExtended;
        //Find all activities with configuration type ClientEventConfiguration and output to allTaskActivities (array of array)
        process.findAllStepsWithEventConfigurationOfType(activity, 5, allTaskActivities,
            () => { }, clientEvtConfigImplementor, false);

        if (allTaskActivities.length === 0) {
            console.error('No task activities found when setting up outcomes and activities');
            return;
        }

        const expressionParentTaskStep = actCnf.getParentActivity();
        //Get all other task steps excluding current selected activity
        let otherTaskSteps = allTaskActivities[0].filter((item) => item != expressionParentTaskStep) as
            Array<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>;

        if (activity.ui) { //If x coordinate present

            //Re-sort per xy distance from current activity

            otherTaskSteps = otherTaskSteps.sort((a: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity,
                b: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity) => {
                const dx1 = Math.pow(a.ui.x - activity.ui.x, 2);
                const dx2 = Math.pow(b.ui.x - activity.ui.x, 2);
                const dy1 = Math.pow(a.ui.y - activity.ui.y, 2);
                const dy2 = Math.pow(b.ui.y - activity.ui.y, 2);

                const da = Math.sqrt(dx1 + dy1);
                const db = Math.sqrt(dx2 + dy2);

                return da > db ? 1 : -1;
            });

        }

        otherTaskSteps.forEach(taskStep => {
            activities.push({
                key: taskStep.getReference(),
                title: taskStep.title,
                titleBinding: {
                    objectReference: {
                        inferredTitle: taskStep.title
                    },
                    propertyName: 'inferredTitle'
                },
                icon: rules.icons.outcomeTask
            });
        });

        this._activityDropDownItems = activities;
    }

    /**
     * Returns collection of dropdown items containing activities on workflow
     */
    public get activityItems(): IDropDownMenuItem[] {
        return this._activityDropDownItems;
    }

    /**
     * Creates drag ghost image
     */
    public getDragImageFn = super.getDragImage.bind(this);

    public addDraggingState = super.addDraggingState.bind(this);

    public removeDraggingState = super.removeDraggingState.bind(this);

    /**
     * Acts on item selected from activity dropdown
     *
     * @param item dropdown item
     */
    public activitySelected = (item: IDropDownMenuItem): void => {

        this._selectedActivity =
            (this.rulePluginService.contextModel.getRoot() as sourceCode.k2Designer.objectModel.core.model.ModelObject).findItem(item.key);
        this.configuredTaskExpression.outcomeReference = '';
        this.configuredTaskExpression.activityReference = item.key;

        this._outcomes.length = 0;
        for (let i = 0; i < this._selectedActivity.configuration.outcomes.length; i++) {
            const outcome = this._selectedActivity.configuration.outcomes[i] as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcomeExtended;
            this._outcomes.push({
                title: outcome.inferredTitle,
                titleBinding: {  //dynamically update outcome titles in dropdown
                    objectReference: outcome,
                    propertyName: 'inferredTitle'
                },
                key: outcome.getReference(),
                icon: rules.icons.outcomeVoting
            });
        }
        this._outcomesArrayChangedRemover = this._selectedActivity.configuration.outcomes.listen(sourceCode.k2Designer.objectModel.core.model.EventTypes.anyArrayChanged,
            this.onOutcomesArrayChanged.bind(this));

        this.onActivitySelected.emit(item);

        this.validateOutcomeReference();
    };

    /**
     * Set up operator dropdown items
     * */
    private setupOperators(): void {

        const operators = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as
            IExpressionConfiguration)?.operators;

        //Add the command items to the operator dropdown (e.g. 'Delete')
        const commands = (this.rulePluginService.getExpressionConfiguration(this.expressionModel) as
            IExpressionConfiguration).commands;

        super.populateLogicalOperators(operators, this.taskExpressionOperatorDropdownItems, rules.operators.equals);
        super.populateOperatorCommands(commands, this.taskExpressionOperatorDropdownItems);
    }

    private onOutcomesArrayChanged(arr: sourceCode.k2Designer.objectModel.core.model.IModelArray<any>, addedIndex: number,
        added: any[], removedIndex: number, removed: any[]): boolean {

        //rebuild outcome dropdown list
        this._outcomes.length = 0;
        for (let i = 0; i < this._selectedActivity.configuration.outcomes.length; i++) {
            const outcome = this._selectedActivity.configuration.outcomes[i] as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcomeExtended;
            const isPrevSelectedOutcome = this.configuredTaskExpression.outcomeReference == outcome.getReference();

            this._outcomes.push({
                title: outcome.inferredTitle,
                titleBinding: {  //dynamically update outcome titles in dropdown
                    objectReference: outcome,
                    propertyName: 'inferredTitle'
                },
                key: outcome.getReference(),
                isSelected: isPrevSelectedOutcome,
                icon: rules.icons.outcomeVoting
            });
        }
        return true;
    }

    /**
     * Acts on item selected from outcomes dropdown
     *
     * @param item dropdown item
     */
    public outcomeSelected = (item: IDropDownMenuItem): void => {
        this.configuredTaskExpression.outcomeReference = item.key;
        this.validateOutcomeReference();
        //Emit outcome item selected event to parent
        this.onOutcomeSelected.emit(item);
    };

    /*
     * Returns the activity title
     */
    public getActivityTitle = (): string => {

        let activityTitle = this.resourceService.getString('ruledesigner_task_condition_choose_task');

        if (this._selectedActivity) {
            const root = this._selectedActivity.getRoot<sourceCode.k2Designer.objectModel.core.model.IModelBase>();
            if (root) {

                let activityReference = this.configuredTaskExpression.activityReference;
                if (!activityReference) {
                    //Get activity reference from the selected activity if not exist in expression model input
                    const selectedActivityRef = this._selectedActivity.getReference();
                    if (selectedActivityRef) {
                        activityReference = selectedActivityRef;
                        this.configuredTaskExpression.activityReference = activityReference;
                    }
                }

                if (activityReference && activityReference !== '') {
                    const activityFromRoot = root.findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>(activityReference);
                    if (activityFromRoot) {
                        activityTitle = activityFromRoot.title;
                    }
                }
            }
        }

        return activityTitle;
    };

    /**
     * Returns the outcome title
     * */
    getOutcomeTitle = (): string => {
        if (this._selectedActivity
            && this.configuredTaskExpression.outcomeReference) {
            const outcome = (this._selectedActivity.getRoot<sourceCode.k2Designer.objectModel.core.model.IModelObject>()
                .findItem(this.configuredTaskExpression.outcomeReference));
            //make sure default 'Choose...' selected when outcome is null
            if (outcome === null) {
                return this.resourceService.getString('ruledesigner_task_condition_choose_outcome');
            }

            return (outcome as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcomeExtended).inferredTitle;
        }
        else {
            return this.resourceService.getString('ruledesigner_task_condition_choose_outcome');
        }
    };

    /**
     * Template for outcomes dropdown with no outcomes
     *
     * @param templateOption
     */
    getEmptyDropDownTemplate = (templateOption: string): string => {
        if (templateOption === 'right') {
            return `<div class='empty-state-icon'>
    <span class='icon icon-set-picker icon-size24 ic-outcomevoting alt'></span>
</div>
<div class='empty-state-message'>
    <span>${this.resourceService.getString('ruledesigner_taskCondition_taskStep_status_noOutcomes')}</span>
</div>`;
        } else {
            return `<div class='empty-state-icon'>
    <span class='icon icon-set-picker icon-size24 ic-outcometask'></span>
</div>
<div class='empty-state-message'>
    <span>${this.resourceService.getString('ruledesigner_taskCondition_taskStep_status_noTaskSteps')}</span>
</div>`;
        }
    };

    private validateOutcomeReference(): void {
        this._validationService.evaluate([sourceCode.k2Designer.objectModel.core.model.ValidationTypes.Required],
            {
                modelObject: this.expressionModel,
                propertyName: 'outcomeReference',
                validationFailedMessage: 'resource_service_empty_field_rule_in_list',
                validationResourceTokens: ['resource_service_field_name_result']
            } as sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem);
    }
}
