import { Component, Inject, OnInit } from '@angular/core';
import { IPluginService } from '../../../../../types/services/plugin/i-plugin-service';
import { PluginServiceToken } from '../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { plugins } from '../../../../shared/constants/plugins';

@Component({
    selector: 'k2-empty-rule-designer',
    templateUrl: './empty-rule-designer.component.html',
    styleUrls: ['./empty-rule-designer.component.less']
})
export class EmptyRuleDesignerComponent implements OnInit {

    constructor(private _resourceService: ResourceService,
        private _rulePluginSvc: RulePluginService,
        @Inject(PluginServiceToken) private _pluginService: IPluginService) { }

    ngOnInit(): void {
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    public isNotValid(): boolean {
        return this._rulePluginSvc.isNewTab;
    }

    public toggleRulesDesigner(): void {
        this._pluginService.toggleState(plugins.panels.rulesDesigner);
    }

}
