import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockClipboardService } from '../../../testing/mock-services/clipboard-service';

const clipboardServiceFactory = () => environment.devHarness === true
    ? new MockClipboardService()
    : sourceCode.k2Designer.core.environmentService.clipboardService;

export const ClipboardServiceToken = new InjectionToken<IClipboardService>('IClipboardService');

export const ClipboardServiceFactory = {
    provide: ClipboardServiceToken,
    useFactory: clipboardServiceFactory,
    deps: [

    ]
};
