    getDisplayName(externalRef: string): string {
        let title = '';
        const propRef: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField = this._rootObj.findItem(externalRef);

        if (propRef) {
            title = propRef.displayName;
        }

        return title;
    }

    getSubTitle(item: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField): string {
        const externalRef = this.getExternalReferenceString(item);
        const propRef = this._rootObj.findItem(externalRef);
        let subTitle = '';

        if (propRef) {
            const dataType = sourceCode.k2Designer.objectModel.workflow.itemProvider.SmartObjectItemTypes[propRef.type] as any;
            subTitle = this._itemProviderService.getItemProviderByName(sourceCode.k2Designer.utilities.constants.PROVIDERNAMES.SMARTOBJECTS).getItemDisplayType(dataType);

            if (this.smoMethodExternalDefinition?.parameters?.find(param => param.systemName === propRef.systemName)) {
                subTitle = this._resourceService.getString('workflow_smartFieldComposer_SMO_Parameter');
            }
        }
        return subTitle;
    }

    /**
     * Gets the resourcified text for 'Open Configuration Panel' or 'Close Configuration Panel', depending
     * on whether the panel is open or closed.
     */
    getFilterButtonText(): string {
        let buttonText = '';

        if (this._filterPanelService.isPanelActive() === true) {
            buttonText = this._resourceService.getString('workflow_smartFieldComposer_SMO_filter_panel_close');
        }
        else {
            buttonText = this._resourceService.getString('workflow_smartFieldComposer_SMO_filter_panel_open');
        }

        return buttonText;
    }

    getStringOf(key: string): string {
        return this._resourceService.getString(key);
    }
