import { EventEmitter, Injectable } from '@angular/core';
import { Subscription } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class EventNotifierService {
    private events: EventEmitter<any>[];
    constructor() {
        this.events = [];
    }

    /**
     * subscribe to the event defined by the key
     * create if it does not yet exist
     *
     * @param key key of the event
     * @param cb function to call on event
     */
    public subscribe(key: string, cb: Function): Subscription {
        let event: EventEmitter<any> = this.events[key];
        if (!event) {
            event = new EventEmitter<any>();
            this.events[key] = event;
        }

        if (typeof (cb) === 'function') {
            return event.subscribe((data) => {
                cb(data);
            });
        } else {
            throw new Error('Subscription requires a callback function');
        }
    }
    /**
     * Add event to the event list
     * if it does not yet exist
     * otherwise return existing
     *
     * @param key key of event to add
     */
    public addEvent(key: string): EventEmitter<any> {
        let event: EventEmitter<any> = this.events[key];
        if (!event) {
            event = new EventEmitter<any>();
            this.events[key] = event;
        }
        return event;
    }
}
