import { ChangeDetectorRef, Component, ElementRef, EventEmitter, HostListener, Inject, Input, NgZone, OnChanges, OnDestroy, OnInit, Output, Renderer2, SimpleChanges, ViewChild } from '@angular/core';
import { DropDownService } from 'src/app/services/drop-down/drop-down.service';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { shortUid } from '../../../../shared/utils/general';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { fromEvent, Subject, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { defaultDebounceTime } from '../../../../shared/constants/general';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ScrollEvent } from '../../../../../types/events/scrollbar/scroll.event';
import { scrollbar } from '../../../../shared/constants/scroll-bar';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { EnvironmentServiceToken } from 'src/app/services/environment/environment-service-factory';
import { ResourceService } from '../../../../services/resource/resource.service';
import { BaseResourceComponent } from '../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-drop-down-menu',
    templateUrl: './drop-down-menu.component.html',
    styleUrls: ['./drop-down-menu.component.less']
})
export class DropDownMenuComponent extends BaseResourceComponent implements OnInit, OnChanges, OnDestroy {

    @Input() selectedItems: IDropDownMenuItem[] = [];
    @Input() customMenuClass: string;
    @Input() states: any[] = [];
    @Input() footerItems: IDropDownMenuItem[] = [];
    @Input() isMenuExpanded = false;
    @Input() isMultiSelect = false;
    @Input() parentDropdown: HTMLElement;
    @Input() menuAlignment: DropDownMenuAlignment = DropDownMenuAlignment.centered;
    @Input() trianglePosition: DropDownMenuTrianglePosition = null;
    @Input() innerItemComponent: DropdownMenuItemTemplates;
    @Input() emptyStateTemplate = '';
    @Input() searchText: string;
    @Input() menuAlignElement: HTMLElement;
    @Input() set menuItems(val: IDropDownMenuItem[] | (() => IDropDownMenuItem[])) {
        if (typeof (val) === 'function') {
            this._retrieveMenuItems = val;
        } else {
            this._menuItems = val;
        }
    };

    get menuItems(): IDropDownMenuItem[] {
        return this._menuItems;
    }

    @Output() onItemSelected: EventEmitter<IDropDownMenuItem> = new EventEmitter<IDropDownMenuItem>();
    @Output() isMenuExpandedChange = new EventEmitter<boolean>();

    @ViewChild('menu') menuElement: ElementRef;

    public dropdownHover: boolean;
    public k2DropdownPositioning: any = {
        'z-index': '10000000',
        position: 'absolute',
        top: '-5px',
        'min-width': '0px'
    };

    public menuIndex = -1;
    public menuId = '';

    private _menuItems: IDropDownMenuItem[] = [];
    private _retrieveMenuItems: () => IDropDownMenuItem[];
    private _outsideClickListener: () => void;
    private _menuPositionSubscription: Subscription;
    private _scrollEventSubscription: Subscription;
    private _menuElementMoved = false;
    //default top position for rendering menu off-screen
    private defaultTop = -10000;

    private get _triangleHeight() {
        return !!this.trianglePosition ? 11 : 1;
    }

    constructor(public element: ElementRef,
        private _renderer: Renderer2,
        private _changeRef: ChangeDetectorRef,
        private _dropDownService: DropDownService,
        private _eventService: EventNotifierService,
        private _zone: NgZone,
        @Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        protected resourceService: ResourceService) {
        super(resourceService);
    };

    ngOnInit(): void {
        this.menuIndex = this._dropDownService.registerMenu(this);
        this.menuId = `dd_${shortUid()}_${this.menuIndex}`;
        this.menuAlignElement = this.menuAlignElement || this.parentDropdown;

        // Wrap event listener outside angular zone to prevent angular change detection from causing performance hit
        this._zone.runOutsideAngular(() => {
            // Window resize event
            this._menuPositionSubscription = fromEvent(window, 'resize')
                .pipe(debounceTime(defaultDebounceTime))
                .subscribe(() => {
                    this._rePositionMenu();
                });
        });

        //Note - this event gets triggered from scrollbar.component and legacy k2Scrollbar
        this._scrollEventSubscription = this._eventService.subscribe(scrollbar.events.scrollEvent, (event: ScrollEvent) => {
            if (!event?.source?.nativeElement?.className ||
                event?.source?.nativeElement?.className?.indexOf('k2-dropdown') === -1) {
                //Only collapse if not the dropdown's scrollbar
                this.collapseMenu();
            }
        });

        //Set the event notifier service in legacy environment service, so that it can be used to notify from legacy scrollbar
        this.environmentService.eventNotifierService = this._eventService;
    }

    ngOnDestroy() {
        this._dropDownService.deRegisterMenu(this);
        if (this._menuElementMoved) {
            //Because this element has been moved outside container, it does not automatically get cleaned up on destroy
            document.getElementById(this.menuId)?.closest('k2-drop-down-menu').remove();
        }
        this._menuPositionSubscription?.unsubscribe();
        this._scrollEventSubscription?.unsubscribe();
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.menuAlignElement && this.isMenuExpanded) {
            this._rePositionMenu();
        }
    }

    selectItem(item: IDropDownMenuItem): void {
        if (!item.isSeparator && !item.isDisabled) {
            if (!this.isMultiSelect) {
                this.collapseMenu();
            } else {
                setTimeout(() => { // update position once the dom has been updated
                    this._rePositionMenu();
                }, 0);
            }

            this.setSelectedItems(item);
            this.onItemSelected.emit(item);
        }
    }

    /**
     * This method is called to execute when a Dropdown Item is selected to determine the selected item's key to change its altered state to the opposite
     */
    private setSelectedItems(selectedDropdownItem: IDropDownMenuItem): void {
        if (selectedDropdownItem?.ignoreUpdateSelection !== true) {

            if (this.isMultiSelect !== true) {
                for (let i = 0; i < this.selectedItems.length; i++) {
                    this.selectedItems[i].isSelected = false;
                }

                this.selectedItems.length = 0;
                selectedDropdownItem.isSelected = true;
                this.selectedItems.push(selectedDropdownItem);

                //Set the selection state of the items
                for (let i = 0; i < this.menuItems.length; i++) {
                    if (this.menuItems[i].key && this.menuItems[i].key !== 'browse' && this.menuItems[i].key !== 'seperator') {
                        this.menuItems[i].isSelected = this.menuItems[i].key === selectedDropdownItem.key;
                    }
                }

                this.footerItems.forEach((i: IDropDownMenuItem) => { i.isSelected = selectedDropdownItem.key === i.key; });
            } else {
                if (selectedDropdownItem.isSelected) {
                    selectedDropdownItem.isSelected = false;
                    this.selectedItems.splice(this.selectedItems.indexOf(selectedDropdownItem), 1);
                } else {
                    selectedDropdownItem.isSelected = true;
                    this.selectedItems.push(selectedDropdownItem);
                }
            }
        }
    }

    private _rePositionMenu = (): void => {
        if (!this.isMenuExpanded) return;

        this._updatePosition();
        this._checkPopupVerticalLocation();
    };

    private _checkPopupVerticalLocation = (): void => {

        const pageHeight: number = window.innerHeight;

        const menuElement = this.menuElement.nativeElement as HTMLElement;
        //Drop-down parent control
        const parentPosition = (this.menuAlignElement as HTMLElement).getBoundingClientRect();
        //Drop-down menu element that opens up
        const menuPosition = menuElement.getBoundingClientRect();
        //Top position of parent drop-down element
        const parentElementTop = parentPosition.top;
        //Height of drop-down menu element that opens up
        const menuElementHeight = menuPosition.height;
        //Height of parent drop-down element
        const parentHeight = parentPosition.height;

        if (parentElementTop + menuElementHeight + parentHeight < pageHeight) {
            //There is space, no need to flip
            return;
        }

        const spaceBottom = pageHeight - parentElementTop;
        const spaceTop = parentElementTop - this._triangleHeight - parentHeight;

        //Is there more space top or bottom of dropdown element?
        if (spaceTop > spaceBottom) {
            //Display above
            this.k2DropdownPositioning.top = (parentElementTop - this._triangleHeight - menuElementHeight - 2) + 'px';

            if (this.menuAlignment === DropDownMenuAlignment.centered) {
                this.flipTriangle(true, menuElement, this.trianglePosition);
            }
            else {
                menuElement.classList.add('with-border');
            }
        }
        else {
            this.flipTriangle(false, menuElement, this.trianglePosition);
        }

    };

    private _updatePosition = (): void => {
        const targetPosition = (this.menuAlignElement as HTMLElement).getBoundingClientRect();
        const menuElement = this.menuElement.nativeElement as HTMLElement;
        const menuPosition = menuElement.getBoundingClientRect();

        if (this.menuAlignment === DropDownMenuAlignment.centered) {
            const newOffsetTop = targetPosition.top + targetPosition.height;
            const width = menuPosition.width < targetPosition.width ? targetPosition.width : menuPosition.width;
            this.k2DropdownPositioning.top = newOffsetTop + 2 + this._triangleHeight + 'px';
            this.k2DropdownPositioning['min-width'] = targetPosition.width + 'px';

            switch (this.trianglePosition) {
                case 'left':
                    menuElement.classList.add('k2-dropdown-before-arrow-left');
                    this.k2DropdownPositioning.left = targetPosition.left + 'px';
                    break;

                case 'right':
                    menuElement.classList.add('k2-dropdown-before-arrow-right');
                    this.k2DropdownPositioning.left = (targetPosition.left - width + targetPosition.width + 1) + 'px';
                    break;

                case 'center':
                    menuElement.classList.add('k2-dropdown-before-arrow-center');
                default:
                    this.k2DropdownPositioning.left = (targetPosition.left + (targetPosition.width / 2) - (width / 2)) + 'px';
                    break;
            }

        } else if (this.menuAlignment === DropDownMenuAlignment.leftEdge) {
            this.k2DropdownPositioning.top = targetPosition.top + targetPosition.height + 1 + 'px';
            this.k2DropdownPositioning.left = targetPosition.left + 'px';
            this.k2DropdownPositioning['min-width'] = targetPosition.width + 'px';
        } else if (this.menuAlignment === DropDownMenuAlignment.rightEdge) {
            const newOffsetTop = targetPosition.top + targetPosition.height;
            this.k2DropdownPositioning.top = newOffsetTop + 2 + this._triangleHeight + 'px';
            const width = menuPosition.width < targetPosition.width ? targetPosition.width : menuPosition.width;
            this.k2DropdownPositioning.left = (targetPosition.left + targetPosition.width - width) + 'px';
            this.k2DropdownPositioning['min-width'] = targetPosition.width + 'px';
        }

        //Ensure triangle is pointing upward
        this.flipTriangle(false, menuElement, this.trianglePosition);
    };

    private setDefaultPosition(): void {
        const menuElement = this.menuElement.nativeElement;
        const targetElement = this.menuAlignElement;

        //Clear dropdown position css class
        menuElement?.classList.remove('k2-dropdown-before-arrow-left');
        menuElement?.classList.remove('k2-dropdown-before-arrow-right');
        menuElement?.classList.remove('k2-dropdown-before-arrow-center');
        menuElement?.classList.remove('k2-dropdown-after-arrow-left');
        menuElement?.classList.remove('k2-dropdown-after-arrow-right');
        menuElement?.classList.remove('k2-dropdown-after-arrow-center');

        this.k2DropdownPositioning.top = this.defaultTop + 'px';
        this.k2DropdownPositioning['min-width'] = targetElement.offsetWidth + 'px';
    }

    private flipTriangle = (flipToBottom: boolean, element: HTMLElement, trianglePosition: string): void => {

        if (flipToBottom) {
            if (trianglePosition === DropDownMenuTrianglePosition.left) {
                element.classList.remove('k2-dropdown-before-arrow-left');
                element.classList.add('k2-dropdown-after-arrow-left');
            } else if (trianglePosition === DropDownMenuTrianglePosition.right) {
                element.classList.remove('k2-dropdown-before-arrow-right');
                element.classList.add('k2-dropdown-after-arrow-right');
            } else if (trianglePosition === DropDownMenuTrianglePosition.center) {
                element.classList.remove('k2-dropdown-before-arrow-center');
                element.classList.add('k2-dropdown-after-arrow-center');
            }
        } else {
            if (trianglePosition === DropDownMenuTrianglePosition.left) {
                element.classList.remove('k2-dropdown-after-arrow-left');
                element.classList.add('k2-dropdown-before-arrow-left');
            } else if (trianglePosition === DropDownMenuTrianglePosition.right) {
                element.classList.remove('k2-dropdown-after-arrow-right');
                element.classList.add('k2-dropdown-before-arrow-right');
            } else if (trianglePosition === DropDownMenuTrianglePosition.center) {
                element.classList.remove('k2-dropdown-after-arrow-center');
                element.classList.add('k2-dropdown-before-arrow-center');
            }
        }
    };

    /**
     * MouseUp listener to just stop propagation. Otherwise it will bubble through to the closeMenu mouseup event on the document
     * Close all other dropdown menus
     *
     * @param evt mouseup event
     */
    @HostListener('mouseup', ['$event'])
    onMouseUp(evt) {
        evt.stopPropagation();
        evt.preventDefault();
    }

    toggleMenu(): void {
        if (!this.isMenuExpanded) {
            this.expandMenu();
        } else {
            this.collapseMenu();
        }
    }

    expandMenu(): void {
        if (!this.isMenuExpanded) {
            this._dropDownService.collapseAllExcept(this);
            if (this._retrieveMenuItems) this._menuItems = this._retrieveMenuItems();

            this.isMenuExpanded = true;
            this.isMenuExpandedChange.emit(this.isMenuExpanded);
            this.moveMenuElement();

            this.setDefaultPosition();
            this.whenDropdownRenderedExecute(() => {

                this._updatePosition();
                this._checkPopupVerticalLocation();

                // Listener on the document to close the menu when the user clicks outside it.
                this._outsideClickListener = this._renderer.listen('document', 'mouseup', (evt) => {
                    if (this.element.nativeElement !== evt?.target) {
                        this.collapseMenu();
                    }
                });
            }, this.menuElement.nativeElement);
        }
    }

    collapseMenu(): void {
        // remove the listener when the menu is collapsed to avoid eventListener pollution
        if (typeof this._outsideClickListener === 'function') {
            this._outsideClickListener();
        }
        if (this.isMenuExpanded) {
            //Do not emit change event for menu that was already collapsed
            this.isMenuExpanded = false;
            this.isMenuExpandedChange.emit(this.isMenuExpanded);
        }
    }

    private whenDropdownRenderedExecute(callback: Function, element: HTMLElement) {
        if (callback) {
            setTimeout(() => {
                if (!document.body.contains(element) || element.offsetWidth === 0) {
                    this.whenDropdownRenderedExecute(callback, element);
                    return;
                }
                callback();
                this._changeRef.markForCheck();
            });
        }
    }

    private moveMenuElement(): void {
        //Only do this with the first open of the dropdown
        if (!this._menuElementMoved) {
            //Move menu element to body to enable it to display over container elements
            document.body.appendChild(this.element.nativeElement);
            this._menuElementMoved = true;
        }
    }
}
