import { Injectable } from '@angular/core';
import { IProgressNotification } from 'src/types/services/data/i-progress-notification';
import { ISearchPagingOptions } from 'src/types/services/data/i-search-paging-options';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IDataService } from '../../types/services/data/i-data-service';

/**
 * NOTE: This is a service class with stub methods currently only used in the dev environment
 * (implementation of the interface is determined by the factory),
 * but the stubs will be replaced with true implementation when services are ported.
 * */

@Injectable({
    providedIn: 'root'
})
export class MockDataService implements IDataService {

    constructor() { }

    httpPost(url: string,
        parms: {},
        callback: any,
        environmentService?: IEnvironmentService,
        cache?: boolean, onProgress?:
            IProgressNotification,
        showNotification: boolean = true): number {

        setTimeout(() => {
            switch (url) {
                case 'api/identity/getlabels':
                    callback({ valueList: ['SP', 'K2', 'K2SQL', 'CE'], defaultLabel: 'K2SQL' });
                    break;
                case 'api/identity/getidentities':
                    callback([{ email: '', systemName: 'Package and Deployment', label: '', displayName: 'Package and Deployment', description: 'Built in K2 system role for Package and Deployment', type: 8, items: [] }]);
                    break;
                case 'api/identity/identitiesbygroup':
                    callback([{ email: '', systemName: 'Package and Deployment', label: '', displayName: 'Package and Deployment', description: 'Built in K2 system role for Package and Deployment', type: 8, items: [] }]);
                    break;
                default:
                    throw `API Mock not found: ${url}`;
            }

        }, 100);

        return 1;
    }
    getFile(url: string, callback: sourceCode.k2Designer.services.angular.IFileCallback, cache?: boolean, onProgress?: IProgressNotification): void { }
    getScriptFile(url: string, callback: sourceCode.k2Designer.services.angular.IScriptCallback, cache?: boolean, onProgress?: IProgressNotification): void { }
    getScriptPackage(type: string, url: string, callback: sourceCode.k2Designer.services.angular.IScriptCallback, cache?: boolean, onProgress?: IProgressNotification): void { }
    getCssFile(url: string, callback: sourceCode.k2Designer.services.angular.ICssCallback, cache?: boolean, onProgress?: IProgressNotification): void { }
    setAppendix(appdx: string): void { }
    preGet(url: string): void { }
    search: (url: string, parameters: any, onDataReceived: (data: any) => void, onCompleted?: () => void, onError?: (error: any) => void, onAborted?: () => void, paging?: ISearchPagingOptions) => void;
    abortRequest(hash: number): void {
        // todo when porting service
    }

    getOpenRequestCount = () => 0;

    httpGet(url: string,
        parms: {},
        callback: any,
        environmentService?: IEnvironmentService,
        cache?: boolean,
        onProgress?: any,
        showNotification?: boolean,
        eventLoadingData?: any) {

        setTimeout(() => {

            if (!this.callbackParameters[url]) {
                console.log('API Mock callback parameters not found for HttpGet of url: ', url);
                throw `API Mock not found: ${url}`;
            }

            callback(this.callbackParameters[url]);

        }, 100);
    };

    getDataUrl(key: string): string {
        return key;
    }

    /**
     * To enable test that uses mock to specify callback parameters for specific call.
     * Add callback as key as needed.
     */
    callbackParameters = {
        'api/identity/getlabels': { valueList: ['SP', 'K2', 'K2SQL', 'CE'], defaultLabel: 'K2SQL' },
        'api/identity/getidentities': [{ email: '', systemName: 'Package and Deployment', label: '', displayName: 'Package and Deployment', description: 'Built in K2 system role for Package and Deployment', type: 8, items: [] }],
        'api/identity/identitiesbygroup': [{ email: '', systemName: 'Package and Deployment', label: '', displayName: 'Package and Deployment', description: 'Built in K2 system role for Package and Deployment', type: 8, items: [] }],
        'api/smartobject/searchSmartObjects': [{}],
        'api/smartforms/getformcontent': {},
        'api/core/getresxfile': {},
        'api/smartforms/getstartworkflowforms': {},
        'api/sharePoint/getSharePointPluginDetails': {},
        'api/sharePoint/getListSmartObjectName': {},
        'api/sharePoint/autoConfingureSharePointEvents': {}
    };
}
