import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockFilterPanelService } from '../../../testing/mock-services/filter-panel-service';
import { IFilterPanelService } from '../../../types/services/filter-panel/i-filter-panel';


const filterPanelServiceFactory = () => environment.devHarness === true
    ? new MockFilterPanelService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.FilterPanelService);

export const FilterPanelServiceToken = new InjectionToken<IFilterPanelService>('IFilterPanelService');

export const FilterPanelServiceFactory = {
    provide: FilterPanelServiceToken,
    useFactory: filterPanelServiceFactory,
    deps: [

    ]
};
