import { Component, Inject, OnDestroy, OnInit } from '@angular/core';
import { IValidationService } from '../../../types/services/validation/ivalidation.service';
import { IValidatedComponent } from '../../../types/validation/i-validated-component';
import { ValidationServiceToken } from '../../services/validation/validation-service-factory';
import { ErrorSeverity } from '../../shared/enums/model/error-severity';
import { EventTypes } from '../../shared/enums/model/event-types';
import { ValidationTypes } from '../../shared/enums/model/validation-types';

@Component({
    selector: 'k2-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export class BaseComponent implements OnDestroy {
    protected applicableValidationTypes: ValidationTypes[] = [];
    protected validationOptions: {
        [validationType: number]: sourceCode.k2Designer.objectModel.validationService.plugins.IValidationTypeItem;
    } = {};

    private validateWhen: string;
    private validationListener: () => void;
    private condition: () => void;

    constructor(@Inject(ValidationServiceToken) private validationService: IValidationService) { }

    ngOnDestroy() {
        if (this.validationListener) {
            this.validationListener();
        }
    }

    /**
     * Gets the validation types and config from the attributes, and registers them for validation.
     * This should be called from the constructor.
     *
     * @param scope
     */
    protected setApplicableValidationTypes(validationTypesArray: any): void {

        // If there isn't any validation specified, just end.
        if (!validationTypesArray) {
            return;
        }

        if (!Array.isArray(validationTypesArray)) {
            throw new Error('The validation attribute should be an array.');
        }

        for (const validationType of validationTypesArray) {
            let validationName;
            if (typeof validationType === 'string') {
                validationName = validationType;
            } else if (typeof validationType === 'object') {
                //get the name from the key of the object
                validationName = Object.keys(validationType)[0]; // The first item will be name of the validation
                if (validationName === 'when') {
                    this.validateWhen = validationType[validationName];
                    continue;
                }
                this.validationOptions[ValidationTypes[validationName]] = validationType[validationName];
            } else { // get this type error out of the way. From here on we can assume if not string, then object
                throw new Error('The validation attribute array should be objects or strings.');
            }

            const validationTypeVal = ValidationTypes[validationName];

            if (!validationTypeVal) {
                throw new Error('Unknown validation type specified in Validation Attribute');
            }

            this.applicableValidationTypes.push(validationTypeVal as unknown as ValidationTypes);
        }
    }

    /**
     * Validate the model and property based on the validation types set in setApplicableValidationTypes
     *
     * @param model
     * @param property
     */
    protected validate(model: sourceCode.k2Designer.objectModel.core.model.IModelObject, property: string): boolean {
        if (this.applicableValidationTypes.length === 0) {
            // nothing to validate, exit
            return true;
        }

        const validationData = new sourceCode.k2Designer.objectModel.validationService.plugins.ValidatorItem();

        validationData.modelObject = model;
        validationData.propertyName = property;

        for (const applicableValidationType of this.applicableValidationTypes) {
            const validationOptions = this.validationOptions[applicableValidationType];

            const validationName = ValidationTypes[applicableValidationType];
            const validationItemTypeName = validationName + 'Item';
            let validationConfig: sourceCode.k2Designer.objectModel.validationService.plugins.ValidationTypeItem;

            if (typeof sourceCode.k2Designer.objectModel.validationService.plugins[validationItemTypeName] === 'function') {
                validationConfig = new sourceCode.k2Designer.objectModel.validationService.plugins[validationItemTypeName]();
            } else {
                validationConfig = new sourceCode.k2Designer.objectModel.validationService.plugins.ValidationTypeItem();
            }

            const validationFailedMessage = validationOptions
                && validationOptions.validationFailedMessage
                ? validationOptions.validationFailedMessage : '-- This field is configured incorrectly --';
            // replace with resource string when we get the text

            const validationResourceTokens = validationOptions
                && validationOptions.validationResourceTokens
                ? validationOptions.validationResourceTokens : [];

            //Gets the errorSeverity from validationOptions, if none specified default to error.
            const validationErrorSeverity = validationOptions
                && validationOptions.errorSeverity
                ? validationOptions.errorSeverity : ErrorSeverity.Error;

            const properties = Object.keys(validationConfig);
            const validationConfigOptions: any = {};
            for (const prop of properties) {
                switch (prop) {
                    case 'validationType':
                        validationConfigOptions.validationType = applicableValidationType;
                        break;
                    case 'validationFailedMessage':
                        validationConfigOptions.validationFailedMessage = validationFailedMessage;
                        break;
                    case 'validationResourceTokens':
                        validationConfigOptions.validationResourceTokens = validationResourceTokens;
                        break;
                    case 'errorSeverity':
                        validationConfigOptions.errorSeverity = validationErrorSeverity;
                        break;
                    default:
                        validationConfigOptions[prop] = validationOptions ? validationOptions[prop] : undefined;
                        break;
                }
            }

            validationData.validationConfigs[applicableValidationType] = validationConfigOptions;
        }

        this.validationService.evaluate(this.applicableValidationTypes, validationData);

        return true;
    }

    /**
     * Listens for property changes, and then validates the field
     *
     * @param model
     * @param property
     * @param condition
     */
    protected validatePropertyOnChange(model: IValidatedComponent, property: string, condition?: () => boolean) {
        if (this.applicableValidationTypes.length === 0) {
            // nothing to validate, exit
            return true;
        }
        if (this.condition) {
            throw new Error('only one validation watch allowed per directive');
        }

        if (typeof condition === 'function') {
            this.condition = () => {
                const result = condition();
                if (result && typeof this.validationListener !== 'function') {
                    this.applyPropertyListener(model, property);
                    this.validate(model, property);
                } else if (!result) {
                    this.destroyListener();
                }
            };
        } else {
            this.applyPropertyListener(model, property);
            this.validate(model, property);
        }
    }

    /**
     * Listens for array changes, and then validates the array
     *
     * @param model
     * @param property
     * @param condition
     */
    protected validateArrayOnChange(model: IValidatedComponent, property: string, condition?: () => boolean) {
        if (this.applicableValidationTypes.length === 0) {
            // nothing to validate, exit
            return true;
        }

        if (typeof condition === 'function') {
            this.condition = () => {
                const result = condition();
                if (result && typeof this.validationListener !== 'function') {
                    this.applyArrayListener(model, property);
                    this.validate(model, property);
                } else if (!result) {
                    this.destroyListener();
                    // no more validation. Pass it all.
                    this.validationService.forceAllPassValidation(model);
                }
            };
            this.condition();
        } else {
            this.applyArrayListener(model, property);
            this.validate(model, property);
        }
    }

    /**
     * Validates an array. Accepts the standard IArrayChangedEvent parameters.
     *
     * @param fieldName
     * @param arrayObj
     * @param name
     * @param oldVal
     * @param newVal
     */
    private onArrayListenerTriggered(property: string,
        parentObj: sourceCode.k2Designer.objectModel.core.model.IModelObject,
        arrayObj: sourceCode.k2Designer.objectModel.core.model.IModelObject,
        name: string,
        oldVal: any,
        newVal: any): boolean {
        // since we listened to the array, no need to compare the property to the name
        this.validate(parentObj, property);
        return true;
    }

    private applyPropertyListener(model: sourceCode.k2Designer.objectModel.core.model.IModelObject, property: string) {
        if (this.validationListener) {
            throw new Error('only one validation listener allowed per directive');
        }

        if (model && model.listen) {
            this.validationListener = model.listen(EventTypes.propertyChanged, this.onPropertyListenerTriggered.bind(this, property));
        }
    }

    private applyArrayListener(model: sourceCode.k2Designer.objectModel.core.model.IModelObject, property: string) {
        if (this.validationListener) {
            throw new Error('only one validation listener allowed per directive');
        }

        if (model && model[property] && model[property].listen) {
            this.validationListener =
                model[property].listen(EventTypes.arrayChanged, this.onArrayListenerTriggered.bind(this, property, model));
        }
    }

    private destroyListener(): void {
        if (typeof this.validationListener === 'function') {
            this.validationListener();
            this.validationListener = null;
        }
    }

    /**
     * Executes validate if the property that changed is the correct one. Accepts the standard IPropertyChangedEvent parameters.
     *
     * @param property
     * @param obj
     * @param name
     * @param oldVal
     * @param newVal
     */
    private onPropertyListenerTriggered(property: string,
        obj: sourceCode.k2Designer.objectModel.core.model.IModelObject,
        name: string,
        oldVal: any,
        newVal: any): boolean {
        if (property === name) {
            this.validate(obj, property);
        }
        return true;
    }
}
