import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockConfigPanelService } from '../../../testing/mock-services/config-panel-service';
import { IConfigPanelService } from '../../../types/services/config-panel/i-config-panel-service';


const configPanelServiceFactory = () => environment.devHarness === true
    ? new MockConfigPanelService()
    : sourceCode.k2Designer.core.environmentService.configPanelService;

export const ConfigPanelServiceToken = new InjectionToken<IConfigPanelService>('IConfigPanelService');

export const ConfigPanelServiceFactory = {
    provide: ConfigPanelServiceToken,
    useFactory: configPanelServiceFactory,
    deps: [

    ]
};
