import { AfterViewInit, Component, ElementRef, HostListener, Inject, Input, OnDestroy, OnInit, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { IActivityConfiguration } from '../../../../../../../types/models/process/activities/configuration/i-activity-configuration';
import { IActivity } from '../../../../../../../types/models/process/activities/i-activity';
import { IOutcome } from '../../../../../../../types/models/process/outcomes/i-outcome';
import { IEvent } from '../../../../../../../types/models/process/events/i-event';
import { IListItemIconData } from '../../../../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { ResourceService } from '../../../../../../services/resource/resource.service';
import { DropDownMenuAlignment } from '../../../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../../../shared/enums/drop-down/drop-down-triangle-position';
import { isUndefinedOrNull, shortUid } from '../../../../../../shared/utils/general';
import { DropDownComponent } from '../../../../../common/drop-down/drop-down.component';
import { IDecisionOutcome } from './i-decision-outcome';
import { UndoRedoServiceToken } from '../../../../../../services/undo-redo/undo-redo-service-factory';
import { EnvironmentServiceToken } from '../../../../../../services/environment/environment-service-factory';
import { IEnvironmentService } from '../../../../../../../types/services/environment/i-environment-service';
import { DecisionOptionType } from '../../../../../../shared/enums/model/workflow/process/decision-option-types';
import { RulePluginService } from '../../../../../../services/ruleplugin/rule-plugin-service';
import { RulePluginBaseComponent } from '../../../base/rule-plugin-base/rule-plugin-base.component';
import { ValidationServiceToken } from '../../../../../../services/validation/validation-service-factory';
import { ConfigPanelServiceToken } from '../../../../../../services/config-panel/config-panel-service-factory';
import { IConfigPanelService } from '../../../../../../../types/services/config-panel/i-config-panel-service';
import { PluginServiceToken } from '../../../../../../services/plugin/plugin-service-factory';
import { IPluginService } from '../../../../../../../types/services/plugin/i-plugin-service';
import { RuleContextHandlerNames } from '../../../../../../shared/enums/rules/rule-context-handler-names';
import { ChooseControlTemplates } from '../../../../../../shared/enums/choose-control/choose-control-templates';
import { ChooseControlTypes } from '../../../../../../shared/enums/choose-control/choose-control-types';
import { IValidatedComponent } from '../../../../../../../types/validation/i-validated-component';
import { IModelArray } from '../../../../../../../types/models/base/i-model-array';
import { WorkflowService } from '../../../../../../services/workflow/workflow.service';
import { EventNotifierService } from '../../../../../../services/event-notifier/event-notifier.service';
import { DecisionActivityChangedEvent } from '../../../../../../shared/constants/events';
import { Subscription } from 'rxjs';
import { ValidationTypes } from '../../../../../../shared/enums/model/validation-types';
import { ErrorSeverity } from '../../../../../../shared/enums/model/error-severity';
import { IValidationService } from '../../../../../../../types/services/validation/ivalidation.service';

@Component({
    selector: 'k2-decision-step',
    templateUrl: './decision-step.component.html',
    styleUrls: ['./decision-step.component.less']
})
export class DecisionStepComponent extends RulePluginBaseComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() activity: IActivity;

    //Left icon of outcomes list item
    outcomeIconData: IListItemIconData = {
        isWrappedInContentDiv: true,
        isVertical: true,
        customClassFlexRow: 'outcome-icon-background',
        iconClass: 'icon-set-outcomes icon-size24 ic-outcomeblock'
    };

    //Right icon of outcomes list item
    outcomeLinkIconData: IListItemIconData = null;

    //Hover styles:
    hoverClass = '';
    isToggleBtnHover = false;

    dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropdownMenuItemTemplates: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;
    decisionOptionType: typeof DecisionOptionType = DecisionOptionType;
    chooseControlTemplates: typeof ChooseControlTemplates = ChooseControlTemplates;
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;

    @ViewChild('outcomeList') outcomeList: ElementRef;

    selectedOutcome: IOutcome;
    isInEditMode = false;
    activityOutcomes: IDecisionOutcome[] = [];
    decisionRuleUseDefault = shortUid();
    decisionRuleUseCustom = shortUid();
    outcomesValidatedComponent: IValidatedComponent;
    private activityChangedSub: Subscription;

    constructor(protected resourceService: ResourceService,
        protected rulePluginService: RulePluginService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken) protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        private workflowService: WorkflowService,
        private eventNotifierService: EventNotifierService) {
        super(validationService, configPanelService, rulePluginService, undoRedoService,
            pluginService, environmentService, resourceService, RuleContextHandlerNames.DecisionRule);

        this.activityChangedSub = eventNotifierService.subscribe(DecisionActivityChangedEvent, () => {
            this.outcomesChanged();
        });
    }

    ngOnInit(): void {
        this.undoRedoService.forceDisable();
        this.activityConfig.isDecisionOutcomeCheckBoxChecked = !!this.activityConfig.isDecisionOutcomeCheckBoxChecked;

        //Default to auto type if rule is default and all outcomes are connected
        if (this.workflowService.isAllOutcomesConnected(this.activityConfig) &&
            sourceCode.k2Designer.objectModel.rulePlugins.rules.DecisionRule.checkIfRuleIsDefault(this.activityConfig.outcomeRule)) {
            this.activityConfig.decisionOptionType = DecisionOptionType.auto;
        }

        this.selectRuleType(this.activityConfig.decisionOptionType);

        this.undoRedoService.resetForceDisable();

        const validationData = super.createValidationData('resource_service_empty_list', ValidationTypes.Required, this.activityConfig,
            'outcomes', ErrorSeverity.Error,
            ['wizard_Task_Decision_DefaultOutcome', 'configpanel_tabs_tooltips_title_StepOutcomesTab']);

        //Listen for change of outcomes, that will trigger validation
        const propertyObjectsToListenTo = [{
            object: this.activityConfig,
            properties: ['outcomes']
        }];
        super.bindValidationToFieldAndListen('outcomesConfig', validationData, propertyObjectsToListenTo);
        super.validateField('outcomesConfig');
    }

    ngAfterViewInit(): void {
        //Have to assign this on afterViewInit to ensure template/viewchild exists
        this.activityOutcomes = this.getActivityOutcomes();
        this.outcomesValidatedComponent = this.getOutcomesValidatedComponent();
    }

    ngOnDestroy(): void {
        this.activityChangedSub?.unsubscribe();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.activity) {
            //Refresh activity outcome data (to ensure template only refreshes outcome templates on changes,
            //instead of binding to functions that gets evaluated constantly)
            this.activityOutcomes = this.getActivityOutcomes();
        }
    }

    /**
     * Decision activity configuration
     */
    get activityConfig(): IActivityConfiguration {
        //Have to cast to unknown, because of 'createdFromIfDrop' property which does not exist in WFD
        return this.activity.configuration as unknown as IActivityConfiguration;
    }

    /**
     * Event configured for the decision (referenced in outcomesEventReference)
     */
    get selectedEvent(): IEvent {
        return this.activityConfig.outcomesEventReference ?
            this.workflowService.findItem<IEvent>(this.activityConfig.outcomesEventReference, this.activity) : null;
    }

    /**
     * Populates list of actions for relinking / unlinking / autonaming outcomes
     *
     * @param outcome
     */
    getActionsForOutcome = (outcome: IOutcome): IDropDownMenuItem[] => {

        const actions: IDropDownMenuItem[] = [];

        if (this.selectedEvent) {
            const activityConfig: IActivityConfiguration =
                <IActivityConfiguration>this.selectedEvent.getLogicalParentModel<IActivity>().configuration;

            //Unlinked action
            actions.push({
                title: this.getString('wizard_Task_Voting_Outcomes_Unlinked'),
                key: 'allUnLinked',
                icon: 'icon-set-indicators icon-size16 ic-nohyperlink',
                isSelected: isUndefinedOrNull(outcome.linkedOutcomeReference) || outcome.linkedOutcomeReference === '',
                additionalValueProperty: 'grey-icon-background',
                onSelected: () => {
                    this.unlink(outcome);
                }
            } as IDropDownMenuItem);

            actions.push(DropDownComponent.separator);

            //All outcomes
            for (let i = 0; i < activityConfig.outcomes.length; i++) {
                const key: string = activityConfig.outcomes[i].getReference();
                actions.push({
                    title: activityConfig.outcomes[i].inferredTitle,
                    key,
                    isSelected: outcome.linkedOutcomeReference === key,
                    icon: 'icon-set-picker icon-size24 ic-outcomevoting',
                    onSelected: () => {
                        this.linkActionToOutcome(outcome, key);
                    }
                } as IDropDownMenuItem);

                actions.push(DropDownComponent.separator);
            }

            //Auto name
            actions.push({
                title: this.getString('wizard_Task_Voting_Outcomes_Autoname'),
                key: 'autoName',
                innerItemComponent: this.dropdownMenuItemTemplates.OutcomeActionAutoname,
                isTitleAutoUpdate: !outcome.isCustomTitle,
                blockPopUpFromClosing: false,
                isSelected: !outcome.isCustomTitle,
                onSelected: (item: IDropDownMenuItem) => {
                    outcome.isCustomTitle = !outcome.isCustomTitle;
                    item.additionalValueProperty = !outcome.isCustomTitle;
                    this.outcomesChanged();
                }
            } as IDropDownMenuItem);
        }
        return actions;
    };

    /**
     * Adds new decision outcome
     * */
    addNewOutcome(): void {
        this.undoRedoService.addUndoMarker('--outcomes tab: add outcome--');

        const newOutcome = this.activityConfig.addNewOutcome(this.activityConfig.defaultOutcomeTitle, true);

        if (isUndefinedOrNull(this.activityConfig.outcomeRule)) {
            this.activityConfig.outcomeRule = this.rulePluginService.createDefaultRule(this.ruleContextHandlerName);
        }

        newOutcome.outcomeRuleReference = this.activityConfig.outcomeRule.getReference();

        //Refresh outcomes array
        this.activityOutcomes = this.getActivityOutcomes();

        this.selectOutcomeByIndex(this.activityConfig.outcomes.length - 1);

        this.addUndoEventForAutoCreate();

        this.outcomesValidatedComponent = this.getOutcomesValidatedComponent();
        if (this.outcomesValidatedComponent.errorInformation.length > 0) {
            this.activityConfig.validateOutcomeRule(this.environmentService);
        }
    }

    /**
     * Remove decision outcome
     * */
    removeSelectedOutcome(): void {

        if (!this.selectedOutcome) {
            //no outcome selected to delete
            return;
        }

        this.undoRedoService.addUndoMarker('--outcomes tab: delete outcome--');

        this.activityConfig.removeOutcome(this.selectedOutcome);

        //if there is a non-custom rule, recreate it to match changes
        if (this.activityConfig.decisionOptionType === DecisionOptionType.auto) {
            this.workflowService.createAutoRuleForDecisionOrSplit(this.activity, false);
        }

        //Re-evaluate outcome rule validation:
        this.outcomesChanged();

        this.selectOutcomeByIndex(this.activityConfig.outcomes.length -1);
    }

    outcomeSelected = (target: EventTarget, outcome: IOutcome): void => {

        this.selectedOutcome = outcome;
        let textInputs = $(this.outcomeList.nativeElement).find('.k2-list-item-outcome .k2-input-with-sub-text');

        //Blur all inputs
        textInputs.blur();
        this.isInEditMode = false;

        //Highlight the selected outcome text input
        if ((target as HTMLElement)?.nodeName?.toUpperCase() === 'INPUT') {
            const textInput = target as HTMLInputElement;
            textInput.focus();
            textInput.select();
            this.isInEditMode = true;
        }
        else {
            // Reason for timeout: when a new outcome is added, the element is not yet available, so focus can't be set.
            setTimeout(() => {
                textInputs = $(this.outcomeList.nativeElement).find('.k2-list-item-outcome .k2-input-with-sub-text');
            });
        }
    };

    /**
     * Builds up the decision's linked-to text for the list item (from the outcome's linked reference)
     *
     * @param outcome
     */
    getLinkedToText(outcome: IOutcome): string {
        let linkedToText = '';
        if (outcome.linkedOutcomeReference && outcome.linkedOutcomeReference.length > 0) {
            const linkedOutcome =
                outcome.getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>()
                    .findItem<IOutcome>(outcome.linkedOutcomeReference);
            if (!!linkedOutcome) {
                linkedToText = this.resourceService.getString('wizard_outcomes_linked_to_outcome').format(linkedOutcome.inferredTitle);
            }
        }
        return linkedToText;
    }

    /**
     * Outcome is not in edit mode
     *
     * @param evt
     */
    @HostListener('mouseup', ['$event'])
    deSelectOutcome(evt): void {
        if ((evt.target as HTMLElement).nodeName?.toUpperCase() !== 'INPUT' ||
            !evt.target.closest('.k2-list-item-outcome')) {
            this.isInEditMode = false;
        }
    }

    protected loadAndPopulateRuleDesigner(): void {

        this.setSavedCustomStatements(this.activityConfig, this.activityConfig.outcomeRule);

        const errorList = this.rulePluginService.getCustomRuleStatementsErrors(this.ruleContextHandlerName + this.activityConfig.getId());

        if (errorList?.length > 0) {
            setTimeout(() => {
                this.validationService.reevaluateErrors(errorList);
            }, 100);
        }

        if (!this.activityConfig.outcomeRule?.statements || this.activityConfig.outcomeRule.statements.length === 0) {
            this.activityConfig.outcomeRule = this.rulePluginService.createDefaultRule(this.ruleContextHandlerName);
        }

        this.rulePluginService.setDesignerContext(this.ruleContextHandlerName, this.activityConfig.outcomeRule);
        this.rulePluginService.renderCanvas();
    }

    protected renderRuleDesigner(): void {
        if (this.activityConfig.decisionOptionType === DecisionOptionType.custom) {
            this.loadAndPopulateRuleDesigner();
        }
        else {
            this.loadEmptyRuleDesigner();
        }
    }

    ruleTypeChanged(selected: boolean, ruleType: DecisionOptionType): void {
        if (selected) {
            this.selectRuleType(ruleType);
        } else {
            //Clear validation
            this.validationService.removeAllErrorsFrom(this.activityConfig);
        }
    }

    selectRuleType(ruleType: DecisionOptionType): void {

        this.undoRedoService.addUndoMarker('--outcomes controller - change outcome type rule--');
        this.undoRedoService.beginUndoGroup('--outcomes controller - change outcome type rule--');

        const previousRuleType = this.activityConfig.decisionOptionType;

        switch (ruleType) {
            case DecisionOptionType.custom:
                this.activityConfig.decisionOptionType = DecisionOptionType.custom;
                this.loadAndPopulateRuleDesigner();
                break;
            default: case DecisionOptionType.auto:
                this.activityConfig.decisionOptionType = DecisionOptionType.auto;

                if (previousRuleType != ruleType) {
                    this.rulePluginService.setCustomRuleStatements(this.ruleContextHandlerName + this.activityConfig.getId(), this.activityConfig.outcomeRule.statements);
                }

                /*
                We will call this method with the forceValidate param set to false, ensuring that validation does not occur within this method.
                validateOutcomeRule will take care of validation after this method has been run.
                */
                this.workflowService.createAutoRuleForDecisionOrSplit(this.activity, false);
                this.loadEmptyRuleDesigner();

                break;
        }

        this.outcomesValidatedComponent = this.getOutcomesValidatedComponent();

        this.onRuleTypeSelected();

        //entire rule will be validated again here so validation should be taken care of here instead of in above method
        this.validateOutcomeRule(this.activityConfig);

        this.undoRedoService.finishUndoGroup();
    }

    protected refreshTabIndexes(): void {
        //do nothing; tab indexes set in template
    }

    private unlink(outcome: IOutcome): void {
        //get the current title before unlink (use original title if available, else the last linked inferred title)
        //TFS: 737589 - be sure to test this with any changes made to this and changes in IOutcomeExtension
        const currentTitle = outcome.title ? outcome.title : outcome.inferredTitle;
        outcome.linkedOutcomeReference = null;
        outcome.isCustomTitle = true;
        if (!outcome.title)
        {outcome.title = currentTitle;}

        this.outcomesChanged();
    }

    /**
     * Select action to link to outcome
     *
     * @param outcome
     */
    private linkActionToOutcome(outcome: IOutcome,  newKey: string): void {
        if (outcome.linkedOutcomeReference === newKey) {
            this.undoRedoService.addUndoMarker('--outcomes tab: unlink from action--');
            this.unlink(outcome);
        } else {
            this.undoRedoService.addUndoMarker('--outcomes tab: link to action--');

            outcome.linkedOutcomeReference = newKey;
            outcome.isCustomTitle = false;
        }

        if (this.activityConfig.decisionOptionType === DecisionOptionType.auto) {
            this.workflowService.createAutoRuleForDecisionOrSplit(this.activity);
        }

        this.outcomesChanged();
    }

    private getActivityOutcomes(): IDecisionOutcome[] {
        const outcomes = this.activityConfig.outcomes;
        outcomes.forEach((outcome: IDecisionOutcome) => {
            outcome.linkedToText = this.getLinkedToText(outcome);
            outcome.outcomeActionItems = this.getActionsForOutcome(outcome);
            outcome.isLinked = outcome.linkedOutcomeReference?.length > 0;
        });
        return outcomes as IDecisionOutcome[];
    }

    /**
     * Validates that all outcomes are connected when rule type is 'auto'
     *
     * @param decisionConfig
     */
    private validateOutcomeRule(decisionConfig: IActivityConfiguration): void {
        const decisionActivityConfig = decisionConfig as unknown as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfigurationExtension;
        decisionActivityConfig.validateOutcomeRule(this.environmentService as unknown as sourceCode.k2Designer.services.core.EnvironmentService);
    }

    /**
     * Performs outcome rule validation
     * Refreshes outcomes array
     * */
    private outcomesChanged(): void {
        //Re-evaluate outcome rule validation:
        this.undoRedoService.addCustomUndoEvent((isUndo): boolean => {
            this.validateOutcomeRule(this.activityConfig);
            return true;
        }, null, '-- evaluate outcome rule --');

        this.validateOutcomeRule(this.activityConfig);

        //Refresh outcomes array
        this.activityOutcomes = this.getActivityOutcomes();

        this.outcomesValidatedComponent = this.getOutcomesValidatedComponent();

        this.validationService.reevaluateErrors(this.activity.errorInformation, true);
        this.validationService.reevaluateErrors(this.activity.childErrorInformation, true);
    }

    private selectOutcomeByIndex(index: number) {

        const isIndexDefined = !isUndefinedOrNull(index);
        if (this.activityConfig.outcomes.length > 0 && isIndexDefined) {
            if (index < 0) {
                index = 0;
            }
            else if (index > this.activityConfig.outcomes.length - 1) {
                index = this.activityConfig.outcomes.length - 1;
            }

            //Select list item
            const inputs = (this.outcomeList.nativeElement as HTMLElement).getElementsByClassName('k2-input-with-sub-text');
            const target = inputs[index];
            this.outcomeSelected(target, this.activityConfig.outcomes[index]);
        }
    }

    /**
     * Adds a custom event to the undo stack if the decisionOptionType is auto to recreate the auto rule on undo
     */
    private addUndoEventForAutoCreate(): void {
        if (this.activityConfig.decisionOptionType === DecisionOptionType.auto) {
            this.undoRedoService.addCustomUndoEvent(this.refreshAutoRule, '-- recreate auto outcome rule --');
        }
    }

    /**
     * Refresh the configured rule if it's an auto rule type
     */
    private refreshAutoRule = (): boolean => {
        this.workflowService.createAutoRuleForDecisionOrSplit(this.activity, false);
        this.validateOutcomeRule(this.activityConfig);
        return true;
    };

    private getOutcomesValidatedComponent(): IValidatedComponent {
        const cmp = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance();
        if (this.activityConfig.decisionOptionType === DecisionOptionType.auto) {
            //Create error information on object if all outcomes NOT connected
            const allOutcomesConnected = this.workflowService.isAllOutcomesConnected(this.activityConfig);
            if (!allOutcomesConnected) {
                const errorMsg = this.resourceService.getFromResx('Wizards', 'wizard_Decision_Rule_UseTask_Error_Badge').value;
                cmp.errorInformation.push({
                    errorMessage: errorMsg,
                    errorResourceTokens: [],
                    errorSeverity: 1,
                    validationType: ValidationTypes.OutcomeLink,
                    propertyName: 'outcomes',
                    reference: this.activityConfig.getReference()
                });
            }
        }

        return cmp;

    }
}
