import { ChangeDetectorRef, Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output, QueryList, Renderer2, ViewChildren } from '@angular/core';
import { TextboxComponent } from 'src/app/components/common/text-box/text-box.component';
import { ValidationServiceToken } from 'src/app/services/validation/validation-service-factory';
import { ComponentIdMapping } from 'src/app/shared/constants/components';
import { dataTypes } from 'src/app/shared/constants/data-types';
import { rules } from 'src/app/shared/constants/rules';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from 'src/app/shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { ExternalReferenceState } from 'src/app/shared/enums/external-references/external-references';
import { EventTypes } from 'src/app/shared/enums/model/event-types';
import { ExternalReferenceUtilities } from 'src/app/shared/utils/external-references';
import { IConfiguredFilterExpression } from 'src/types/models/rules/i-configured-filter-expression';
import { IExpressionConfiguration } from 'src/types/models/rules/i-expression-configuration';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { IValidationService } from '../../../../../types/services/validation/ivalidation.service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ProcessService } from '../../../../services/process/process.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';

@Component({
    selector: 'k2-filter-expression',
    templateUrl: './filter-expression.component.html',
    styleUrls: ['./filter-expression.component.less']
})
export class FilterExpressionComponent extends ExpressionBaseComponent implements OnInit {

    // Used to get the textbox
    @ViewChildren('txtValue') textBoxes: QueryList<TextboxComponent>;
    private txtValue: TextboxComponent;

    @Input() selectedIndex = 0;

    @Input() selectedLeftItem;

    @Input() leftItems = [];

    @Input() simpleExpressionOperatorDropdownItems = [];

    @Input() externalDefinitionParent: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField;

    @Output() onGroupExpressionDropped: EventEmitter<number> = new EventEmitter<number>();

    @Input() expression: IConfiguredFilterExpression;

    operators: IDropDownMenuItem[] = [];
    selectedOperatorItem: IDropDownMenuItem;
    hasParentError = false;
    parentErrorMessage = '';
    rulesIcons = rules.icons;

    private _expressionConfiguration: IExpressionConfiguration;
    private _outsideClickListenerRemover: any;
    private _propertyChangedWatch: Function;
    private _isLeftItemsLoaded: boolean;
    private _leftItemCheckIntervalHandle: NodeJS.Timeout;

    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    constructor(@Inject(SelectionServiceToken) _selService: ISelectionService,
        rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        resourceService: ResourceService,
        eventService: EventNotifierService,
        private _renderer: Renderer2,
        private _el: ElementRef,
        private _processService: ProcessService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        private _changeDetector: ChangeDetectorRef) {

        super(_selService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, _el, resourceService);
    }

    ngOnInit(): void {

        super.ngOnInit();

        this.undoRedoService.forceDisable();

        this.expressionModel = this.expression;

        this.expression = this.expressionModel as IConfiguredFilterExpression;
        this._expressionConfiguration = this.rulePluginService.getExpressionConfiguration(this.expression) as IExpressionConfiguration;

        this.populateLogicalOperatorDropDown();

        this.setInitialValues();

        // clone, as they are passed in by reference
        this.leftItems = this.leftItems.map(leftItem =>
            typeof leftItem.clone === 'function' ? leftItem.clone() : {...leftItem}
        );

        this._propertyChangedWatch = this.expression.listen(EventTypes.propertyChanged, this.updateSelectedItem.bind(this));

        this.undoRedoService.resetForceDisable();
    }

    ngAfterViewInit(): void {

        // The textbox won't render when this control loads, so we have to create a query, and subscribe to result changes
        // Then we can get the first one to focus on when it is rendered.
        this.textBoxes.changes.subscribe((components: QueryList<TextboxComponent>) => {
            this.txtValue = components.first;
        });

        // Handle deselecting the condition
        this._outsideClickListenerRemover = this._renderer.listen('document', 'mouseup', (evt) => {
            if (this.rootElement.nativeElement !== evt?.target) {
                this.setSelected(false);
            }
        });

        /**
         * All this because the left-items are updated after afterviewinit.
         */
        this._leftItemCheckIntervalHandle = setInterval(() => {
            if (!this._isLeftItemsLoaded && this.leftItems.length > 0) {

                this._isLeftItemsLoaded = true;
                clearInterval(this._leftItemCheckIntervalHandle);

                if (this.expression.property) {
                    this.updateSelectedItem(null, 'property', null, null);
                    this._changeDetector.detectChanges();
                }
            }
        }, 50);
    }

    ngOnDestroy(): void {
        typeof this._outsideClickListenerRemover === 'function' && this._outsideClickListenerRemover();
        typeof this._propertyChangedWatch === 'function' && this._propertyChangedWatch();
        super.ngOnDestroy();
    }

    updateSelectedItem = (obj: sourceCode.k2Designer.objectModel.core.model.IModelObject, name: string, oldVal: any, newVal: any) => {

        if (name !== 'property') {
            return;
        }

        this.validateExternalReferenceParent();

        const selectedProperty = this._processService.root.findItem<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField>
        (this.expression.property);

        if (!selectedProperty) {
            this.selectedLeftItem = null;
            return;
        }

        const wasInError: boolean = this.expression.errorInformation?.length > 0;

        //Find selected item in item collection list
        const matches = this.leftItems.filter(leftItem => leftItem.key === selectedProperty.systemName);

        if (matches.length > 0) {
            //Set selected item from the match in left items collection list
            this.selectedLeftItem = matches[0];

            if (wasInError) {
                //Current selected property is valid, but previous selected property was a missing value in error
                this._validationService.reevaluateErrors(this.expression.errorInformation, true);
            }

            this.expression.property = ExternalReferenceUtilities.getExternalDefinitionToken(this.externalDefinitionParent, this.selectedLeftItem.key, true);

        } else if (selectedProperty.state !== ExternalReferenceState.Missing
            && !this.hasParentError) {

            //Property is not in a missing state but cannot be found in items collection, reset
            this.selectedLeftItem = null;

            return;
        }

        //Handle missing properties in error. We also need to ensure if the parent is in error we still display the property
        if (selectedProperty.state === ExternalReferenceState.Missing ||
            this.hasParentError) {

            if (matches.length === 0) {

                //Missing property has already been removed (in a previous configured filter), set selected property from external reference property
                // Default icon
                let propIcon = `${dataTypes.icons.prefix}${dataTypes.icons.default}`;

                if (!!selectedProperty.type) {
                    propIcon = `${dataTypes.icons.prefix}${selectedProperty.type.toLowerCase()}`;
                }

                // TODO before commit: See if we can refactor this?
                this.selectedLeftItem = <IDropDownMenuItem>{
                    key: selectedProperty.getReference(),
                    title: selectedProperty.displayName || selectedProperty.systemName,
                    icon: `${dataTypes.icons.base} ${propIcon}`,
                    metaData: {
                        type: selectedProperty.type,
                        state: selectedProperty.state
                    },
                    isSelected: true
                };

            } else {

                //Remove missing property from collection
                for (let i: number = this.leftItems.length - 1; i >= 0; i--) {
                    if (this.leftItems[i].key === this.selectedLeftItem.key) {
                        this.leftItems.splice(i, 1);
                        break;
                    }
                }
            }
        }
    };

    /**
     * Set default or existing values for the control
     */
    setInitialValues() {

        this.expression.configuredOperator = this.expression.configuredOperator || rules.operators.equals;

        this.expression.operatorIcon = this._expressionConfiguration
            .operators
            .find(o => o.name === this.expression.configuredOperator)
            ?.icon;
    }

    onLeftItemSelected(item: IDropDownMenuItem) {
        this.undoRedoService.addUndoMarker('--k2FilterExpression: left item selected--');

        this.selectedLeftItem = item;
        this.expression.propertyType = item.metaData.type;

        this.expression.property = ExternalReferenceUtilities.getExternalDefinitionToken(this.externalDefinitionParent, item.key, true);

        this.focusTextbox();
    }

    onOperatorSelected(item: IDropDownMenuItem) {
        this.expression.configuredOperator = item.key;
        this.expression.operatorIcon = item.icon;
        this.selectedOperatorItem = item;
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    isHovering(controlName: string) {
        return this.states.isExist(`${controlName}-hover`);
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    hoverOn(controlName: string) {
        this.states.add(`${controlName}-hover`);
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    hoverOff(controlName: string) {
        this.states.remove(`${controlName}-hover`);
    }

    private focusTextbox() {
        setTimeout(() => {
            this.txtValue?.doFocus();
        }, 100);
    }

    private populateLogicalOperatorDropDown() {
        this.populateLogicalOperators(this._expressionConfiguration.operators, this.operators, this.expression.configuredOperator);
        this.populateOperatorCommands(this._expressionConfiguration.commands, this.operators);
    }

    /**
     * Set validation data if property mapper's parent external reference is missing
     * */
    private validateExternalReferenceParent() {

        this.hasParentError = false;
        this.parentErrorMessage = '';

        if (this.externalDefinitionParent) {
            //If parent method or parent SMO is missing:
            const parent = this.externalDefinitionParent;
            if (parent.componentId === ComponentIdMapping.SmartObjectMethodFieldExternalReference) {
                let hasValidParent = this.externalDefinitionParent.state !== ExternalReferenceState.Missing;
                if (hasValidParent) {
                    const smartFieldParent = (this.externalDefinitionParent as sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodFieldExtension)
                        .parentSmartObjectField();
                    if (!!smartFieldParent) {
                        hasValidParent = smartFieldParent.state !== ExternalReferenceState.Missing;
                        if (!hasValidParent) {
                            const errorMessage = 'resource_service_removed_consumed_ext_reference_smartobject_by_property_mapper';
                            this.parentErrorMessage = this.resourceService.resolveTokenResourceString(
                                errorMessage, [smartFieldParent.displayName || smartFieldParent.systemName], smartFieldParent
                            );
                        }
                    }
                } else {
                    const errorMessage = 'resource_service_removed_consumed_ext_reference_smartobject_method_by_property_mapper';
                    this.parentErrorMessage = this.resourceService.resolveTokenResourceString(
                        errorMessage, [parent.displayName || parent.systemName], parent
                    );
                }
                this.hasParentError = !hasValidParent;
            }
        }
    }

    protected deleteExpression(expressionToDelete: IConfiguredFilterExpression): boolean {
        if (!expressionToDelete) return false;

        this.undoRedoService.addUndoMarker('-- delete expression--');

        const parentGroup = this.getExpressionParentGroup(expressionToDelete);

        if (parentGroup.expressions.length === 1) {
            // first set to null so that undo reverts it.
            expressionToDelete.configuredOperator = null;

            expressionToDelete.removeFromModelArray();
        } else {
            const expressionToRemoveIndex = parentGroup.expressions.indexOf(expressionToDelete);
            //If expression to delete is contained between 2 operators, remove 1 operator (to prevent double operators remaining)
            if (parentGroup.expressions.length > 2 && expressionToRemoveIndex > 0 &&
                expressionToRemoveIndex < parentGroup.expressions.length - 1 &&
                this.isOperatorType(parentGroup.expressions[expressionToRemoveIndex - 1]) &&
                this.isOperatorType(parentGroup.expressions[expressionToRemoveIndex + 1])) {
                parentGroup.expressions[expressionToRemoveIndex - 1].removeFromModelArray();
            }

            expressionToDelete.removeFromModelArray();
        }

        this.cleanupOperators(parentGroup);
        this.updateGroupExpressionOf(parentGroup);
        return true;
    }

    getDragImageFn = super.getDragImage.bind(this);

}
