import { Component, Input, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { IContextWizardPopupConfiguration } from '../../../../../types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { IEventPlatformRemovePluginContext } from '../../../../../types/uiComponents/pluginWizards/IEventPlatformRemovePluginContext';
import { PopupService } from '../../../../services/popup/popup.service';
import { ProcessService } from '../../../../services/process/process.service';
import { popup } from '../../../../shared/constants/wizard-popup';
import { PopupState } from '../../../../shared/enums/plugin-wizard/popup-state';
import { isUndefinedOrNull } from '../../../../shared/utils/general';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-event-platform-remove',
    templateUrl: './event-platform-remove.component.html',
    styleUrls: ['./event-platform-remove.component.less'],
    providers: [{provide: PageComponent, useExisting: EventPlatformRemoveComponent}]
})
export class EventPlatformRemoveComponent extends PageComponent implements OnInit {
    buttonConfig: IPopupButtonsConfig = {
        previous: {
            isVisible: true,
            buttonText: this.getString('workflow_contextPopup_cancelButton')
        },
        next: {
            buttonText: this.getString('workflow_contextPopup_delete_event'),
            classOverride: 'modal-button alert large'
        }
    };

    @Input() public config: IPopupConfiguration;

    public context: IEventPlatformRemovePluginContext;
    public resources: any;

    constructor(protected _processService: ProcessService,
        resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.context = this.config.context as IEventPlatformRemovePluginContext;
        this.resources = this.context.resourceObject;
    }

    beforePageDisplayed() {}
    beforeNavigateNext() {}
    beforeNavigatePrevious() {}
}
