import { Component, Input, OnInit } from '@angular/core';
import { SharePointEventsRegisteredStatus } from 'src/app/shared/enums/integration/share-point-events-registered-status';
import { StartMode } from 'src/app/shared/enums/integration/start-mode';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IContextWizardPopupConfiguration } from 'src/types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { ISharePointContext } from 'src/types/uiComponents/pluginWizards/ISharePointContext';
import { PluginItemReferencesService } from '../../../../services/plugins/item-references/item-references.service';
import { SharePointService } from '../../../../services/plugins/share-point/share-point.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-sp-summary',
    templateUrl: './summary.component.html',
    styleUrls: ['./summary.component.less'],
    providers: [{ provide: PageComponent, useExisting: SharePointSummaryComponent }]
})
export class SharePointSummaryComponent extends PageComponent implements OnInit {

    @Input() config: IContextWizardPopupConfiguration;

    @Input() isFinishPage = false;

    /**
     * Context of the plugin wizard.
     */
    pluginContext: ISharePointContext;

    /**
     * Share point events registered status enum
     */
    sharePointEventsRegisteredStatus: typeof SharePointEventsRegisteredStatus = SharePointEventsRegisteredStatus;

    buttonConfig: IPopupButtonsConfig = {
        previous:{},
        finish:{
            isDisabled: false,
            buttonText: 'workflow_contextPopup_okButton'
        },
        next: {}
    };

    constructor(protected resourceService: ResourceService,
        private itemReferenceService: PluginItemReferencesService,
        private sharePointService: SharePointService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.pluginContext = this.config.context as ISharePointContext;
    }

    beforePageDisplayed = () => {
        if(this.isFinishPage){
            this.notifyPageStateChanged(PopupState.Busy);

            //Save events
            this.saveSharePointEvents();
        } else {
            const assetType: string = this.pluginContext.isDocLib
                ? this.resourceService.getString('sharePoint_integration_found_references_library')
                : this.resourceService.getString('sharePoint_integration_found_references_list');

            this.buttonConfig.next.buttonText = 'workflow_contextPopup_saveAndFinish';

            //Set wizard dynamic headings.
            this.pluginContext.resultHeading = this.resourceService.getString('sharePoint_integration_summary_almost_done');

            this.pluginContext.updateListHeading = this
                .resourceService
                .getString('sharePoint_integration_summary_update_heading')
                .replace('{0}', this.pluginContext.itemName)
                .replace('{1}', assetType);

            this.pluginContext.changesHeading = this
                .resourceService
                .getString('sharePoint_integration_summary_changes_heading')
                .replace('{0}', this.pluginContext.isDocLib
                    ? this.resourceService.getString('sharePoint_integration_found_references_library')
                    : this.resourceService.getString('sharePoint_integration_found_references_list')
                );

            if (this.pluginContext.existingIntegration) {
                this.pluginContext.createStartEventsHeading = this
                    .resourceService
                    .getString('sharePoint_integration_summary_update_start_events')
                    .replace('{0}', this.getSelectedEventsCount().toString());
            }
            else {
                this.pluginContext.createStartEventsHeading = this
                    .resourceService
                    .getString('sharePoint_integration_summary_create_start_events')
                    .replace('{0}', this.getSelectedEventsCount().toString());
            }

            this.pluginContext.createReferencesHeading = this
                .resourceService
                .getString('sharePoint_integration_summary_create_references_description')
                .replace('{0}', assetType);

            this.pluginContext.createReferenceSubHeading = this
                .resourceService
                .getString('sharePoint_integration_summary_create_references_action')
                .replace('{0}', this.pluginContext.newReferenceCount.toString());
        }
    };

    beforeNavigateNext = () => {};
    beforeNavigatePrevious= () => {};

    /**
     * Method to save SharePoint events against the workflow.
     */
    private saveSharePointEvents(): void {

        //Save the workflow settings
        this.sharePointService.saveSharePointWorkflowSetting(
            this.pluginContext.workflowFqn,
            this.pluginContext.listId,
            this.pluginContext.siteUrl,
            this.pluginContext.settingId,
            this.pluginContext.smartObjectName,
            this.pluginContext.isDocLib,
            this.pluginContext.eventReceiverString)
            .subscribe({
                next: (result) => {
                    this.saveSharePointWorkflowSettingCompleted(result, false);
                }, error: (error: any) => {
                    this.setErrorState(error);
                }
            });
    }

    /**
     * Method to get the amount of events selected.
     */
    private getSelectedEventsCount(): number {

        let selectedEventCount = 0;
        for (let i = 0; i < this.pluginContext.remoteEventReceivers.EventDataCollection.length; i++) {
            if (this.pluginContext.remoteEventReceivers.EventDataCollection[i].Selected) {
                selectedEventCount += 1;
            }
        }
        return selectedEventCount;
    }

    /**
     * Callback method after SharePoint setting was saved & returned workflowId for further configuration of events.
     */
    private saveSharePointWorkflowSettingCompleted = (result: string, error: boolean): void => {

        this.sharePointService.manageEventReceivers(result,
            this.pluginContext.settingId,
            this.pluginContext.eventReceiverString,
            this.pluginContext.siteUrl,
            this.pluginContext.listId)
            .subscribe({
                next: () => {
                    this.setSuccessState();
                }, error: (error: string) => {
                    this.setErrorState(error);
                }
            });
    };

    /**
     * Method to set the error state.
     */
    private setErrorState(result: string): void {
        if(this.isFinishPage){
            this.buttonConfig.finish.isVisible = false;
            this.buttonConfig.previous.isVisible = false;
        }

        this.state = PopupState.Error;
        this.notifyPageStateChanged(PopupState.Error);
        const exception = JSON.parse(result);

        if (exception.ExceptionMessage) {
            this.pluginContext.errorMessage = exception.ExceptionMessage;
        } else {
            this.pluginContext.errorMessage = exception.Message;
        }

        this.pluginContext.status = SharePointEventsRegisteredStatus.failed;
        this.pluginContext.resultHeading = this.resourceService.getString('sharePoint_integration_summary_oops');
    }

    private setSuccessState(): void {
        this.pluginContext.status = SharePointEventsRegisteredStatus.success;
        this.pluginContext.resultHeading = this.resourceService.getString('sharePoint_integration_summary_success');

        if(this.isFinishPage){
            this.buttonConfig.finish.isVisible = true;
        }

        this.createReferences(false);

        this.notifyPageStateChanged(PopupState.Normal);
    }

    /**
     * Method to create references.
     */
    private createReferences(closeWizard: boolean) {
        if (this.pluginContext.newReferenceCount > 0) {
            //Create item references.
            this.itemReferenceService.createItemReferences(
                this.pluginContext.newItemReferences,
                StartMode.sharePointEvent);
        }
    }
}
