import { Directive, ElementRef, HostListener } from '@angular/core';

@Directive({
    selector: '[k2TooltipBasic]'
})
export class TooltipBasicDirective {

    private _element: HTMLElement;

    constructor(el: ElementRef) {
        this._element = el.nativeElement;
    }

    @HostListener('mouseenter', ['$event'])
    onMouseEnter(e: MouseEvent) {
        //Determine if the text overlow, which means that ellipses show and we should then set the title (tooltip) to the innertext of the element.
        if (this._element.offsetWidth < this._element.scrollWidth) {
            this._element.title = this._element.innerText;

        } else {
            // No overflow, don't show tooltip if it fits on element.
            this._element.title = '';
        }
    }
}
