import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { EventEmitter, Inject, Injectable, OnDestroy } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { ICache } from '../../../../types/models/data/i-cache';
import { ISmartFormItemReference } from '../../../../types/models/smart-forms/i-smart-form-item-reference';
import { INotificationService } from '../../../../types/services/notification/i-notification.service';
import { IGeneralItem } from '../../../../types/uiComponents/pluginWizards/IGeneralItem';
import { IProcessPluginDefinitionContext } from '../../../../types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { popup } from '../../../shared/constants/wizard-popup';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { StartMode } from '../../../shared/enums/integration/start-mode';
import { ToastrTypes } from '../../../shared/enums/model/toastr-types';
import { GeneralItemTypes } from '../../../shared/enums/plugin-wizard/general-item-types';
import { EnvironmentServiceToken } from '../../environment/environment-service-factory';
import { EventNotifierService } from '../../event-notifier/event-notifier.service';
import { NotificationServiceToken } from '../../notification/notification-service-factory';
import { ProcessService } from '../../process/process.service';
import { ResourceService } from '../../resource/resource.service';
import { PluginItemReferencesService } from '../item-references/item-references.service';

type IFormProcessDefinition = sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.IFormProcessDefinition;

@Injectable({
    providedIn: 'root'
})
export class SmartFormsIntegrationService implements OnDestroy {

    private formContent: sourceCode.k2Designer.objectModel.smartForms.IFormContent = null;

    private cache: ICache = {};
    private subs: ICache = {};

    constructor(@Inject(EnvironmentServiceToken) private environmentService,
        private processService: ProcessService,
        @Inject(NotificationServiceToken) private notificationService: INotificationService,
        private resourceService: ResourceService,
        private itemReferenceService: PluginItemReferencesService,
        private http: HttpClient) {
    }

    ngOnDestroy() {
        for (const [key, sub] of Object.entries(this.subs)) {
            (sub as Subscription)?.unsubscribe();
        }
    }

    /**
     * This method is used to unsubscribe and get rid of all observables inside the smart forms integration service.
     */
    clearCache() {
        this.cache = {};
        for (const [key, sub] of Object.entries(this.subs)) {
            (sub as Subscription)?.unsubscribe();
        }
        this.subs = {};
    }

    /**
     * Method to determine which pages should show.
     */
    determinePages(pluginContext: IProcessPluginDefinitionContext): void {
        this.detectChanges(pluginContext);
    }

    /**
     * Method to get the form content.
     */
    getFormContent(pluginContext: IProcessPluginDefinitionContext): Observable<number> {
        const formId = pluginContext.form.name || pluginContext.form.metaData;
        const isStartForm = !pluginContext.isUserForm;
        const ruleId = pluginContext.ruleId;

        this.resetToDefaults(pluginContext);

        const options =
        {
            params: new HttpParams()
                .set('host', this.environmentService.currentEnvironment.hostId)
                .set('hashes', '[]')
                .set('formId', formId)
                .set('fqn', pluginContext.fqn)
                .set('isStartForm', isStartForm ? 'true' : 'false')
                .set('ruleId', ruleId || '')
                .set('vers', Date.now().toString()) //no cache
        };

        return new Observable<number>(subscriber => {

            let observable;
            const key = `getformcontent_${formId}_${ruleId}`;
            if (this.cache[key]) {
                observable = this.cache[key];
            } else {
                observable = this.http.get<string>('api/smartforms/getformcontent', options)
                    .pipe(
                        shareReplay(1)
                    );
                this.cache[key] = observable;
            }

            this.subs[key] = observable.subscribe({
                next: (result: string) => { // parses to sourceCode.k2Designer.objectModel.smartForms.IFormContent
                    if (result) {
                        try {
                            this.formContent = JSON.parse(result) as sourceCode.k2Designer.objectModel.smartForms.IFormContent;
                        } catch {
                            this.notificationService.toastr(this.resourceService.getString('rest_message_500'),
                                this.getMessageFromResult(result),
                                ToastrTypes.error, 3000);

                            delete this.cache[key];
                            console.error('Error attempting to deserialize the result from getformcontent');
                            subscriber.error('Error attempting to deserialize the result from getformcontent');
                            return;
                        }

                        pluginContext.parameters = this.formContent.parameters;
                        this.configureStateModel(pluginContext);

                        this.refreshRules(pluginContext)
                            .subscribe(() => {
                                subscriber.next(pluginContext.pageIndex);
                            });
                    }
                },
                error: (error: string) => { // Assume all errors will be strings.
                    this.notificationService.toastr(this.resourceService.getString('rest_message_500'),
                        this.getMessageFromResult(error),
                        ToastrTypes.error, 3000);

                    delete this.cache[key];
                    console.error(`Error attempting to fetch the result from getformcontent: ${error}`);
                    subscriber.error(`Error attempting to fetch the result from getformcontent: ${error}`);
                },
                unsubscribe: () => {
                    (this.subs[key] as Subscription)?.unsubscribe();
                }
            });
        });
    }

    /**
     * Method to refresh the selected rule's actions and conditions.
     */
    refreshRuleConditionsAndActions(pluginContext: IProcessPluginDefinitionContext): Observable<void> {
        return new Observable<void>(subscriber => {

            if (pluginContext.selectedRule) {
                //Base state
                let stateId: string = pluginContext.states[0].key;

                //Only take the selected state if the use existing radio button is selected
                if (!pluginContext.createNewState) {
                    stateId = pluginContext.selectedState.key;
                }

                const rule = pluginContext.selectedRule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule;
                const options =
                {
                    params: new HttpParams()
                        .set('host', this.environmentService.currentEnvironment.hostId)
                        .set('hashes', JSON.stringify([]))
                        .set('formId', pluginContext.form?.key || '')
                        .set('ruleId', rule?.id || '')
                        .set('stateId', stateId || '')
                        .set('ruleName', rule?.name || '')
                        .set('instanceId', rule?.instanceGuid || '')
                        .set('fqn', pluginContext.fqn /*this._environmentService.workflowFullname*/)
                        .set('isStartEvent', !pluginContext.isUserForm ? 'true' : 'false')
                        .set('vers', Date.now().toString()) //no cache
                };

                let observable;
                const key = `getrulehandlersconditionsandactions_${pluginContext.form?.key}_${rule?.id}_${stateId}_${rule?.name}`;
                if (this.cache[key]) {
                    observable = this.cache[key];
                } else {
                    observable = this.http.get<string>('api/smartforms/getrulehandlersconditionsandactions', options)
                        .pipe(
                            shareReplay(1)
                        );
                    this.cache[key] = observable;
                }

                this.subs[key] = observable.subscribe({
                    next: (result: string) => { // to be parsed to sourceCode.k2Designer.objectModel.smartForms.IHandler[]
                        if (result) {
                            this.convertHandlers(pluginContext, result);
                            subscriber.next();
                            subscriber.complete();
                        } else {
                            delete this.cache[key];
                            console.error('Empty result calling getrulehandlersconditionsactions');
                            subscriber.error('Empty result calling getrulehandlersconditionsactions');
                        }
                    },
                    error: (error: string) => { // Assuming all errors are strings? Should be if the result is.
                        delete this.cache[key];
                        console.error(`Error calling getrulehandlersconditionsactions: ${error}`);
                        subscriber.error(`Error attempting to call getrulehandlersconditionsactions: ${error}`);
                    },
                    unsubscribe: () => {
                        (this.subs[key] as Subscription)?.unsubscribe();
                    }
                });
            } else {
                this.configureOptionModel(pluginContext);
                subscriber.next();
                subscriber.complete();
            }
        });
    }

    /**
     * Callback from actions and conditions rest call.
     */
    convertHandlers(pluginContext: IProcessPluginDefinitionContext, result: string): void {

        let handlers: sourceCode.k2Designer.objectModel.smartForms.IHandler[] = [];
        try {
            handlers = JSON.parse(result) as sourceCode.k2Designer.objectModel.smartForms.IHandler[];
        } catch { }

        if (handlers.length === 0) {
            return;
        }

        if (pluginContext.isUserForm) {
            pluginContext.selectedStatehandlers = this.processHandlersForUserTask(handlers, pluginContext);
        }
        else {
            pluginContext.selectedStatehandlers = this.processHandlersForStartRule(handlers, pluginContext);
        }

        pluginContext.formLoading = false;
        this.configureOptionModel(pluginContext);
    }

    /**
     * Get selected state rules if the use existing option is selected.
     */
    refreshRules = (pluginContext: IProcessPluginDefinitionContext): Observable<void> => {
        let stateId = '';

        //Only take the selected state if the use existing radio button is selected
        if (!pluginContext.createNewState) {
            stateId = pluginContext.selectedState.key;
        }

        pluginContext.rules = [];

        const options = {
            params: new HttpParams()
                .set('host', this.environmentService.currentEnvironment.hostId)
                .set('hashes', JSON.stringify([]))
                .set('formId', pluginContext.form?.key || '')
                .set('stateId', stateId)
                .set('isStartEvent', !pluginContext.isUserForm ? 'true' : 'false')
                .set('vers', Date.now().toString()) //no cache
        };

        return new Observable<void>(subscriber => {
            let observable;
            const key = `getformrules_${pluginContext.form?.key || ''}_${stateId}`;
            if (this.cache[key]) {
                observable = this.cache[key];
            } else {
                observable = this.http.get<string>('api/smartforms/getformrules', options)
                    .pipe(
                        shareReplay(1)
                    );
                this.cache[key] = observable;
            }
            this.subs[key] = observable.subscribe({
                next: (result: string) => {
                    if (result) {
                        this.rulesLoaded(pluginContext, result)
                            .subscribe(() => {
                                subscriber.next();
                            });
                    } else {
                        subscriber.next();
                        subscriber.complete();
                    }
                },
                error: (error: string) => { // Assume any errors will be strings
                    delete this.cache[key];
                    console.error(`Error retrieving form rules: ${error}`);
                    subscriber.error(`Error retrieving form rules: ${error}`);
                },
                unsubscribe: () => {
                    (this.subs[key] as Subscription)?.unsubscribe();
                }
            });
        });
    };

    /**
     * Processes a list of IHandler objects into a list of IGeneralItem objects for configuring a user task rule
     *
     * @param result
     */
    processHandlersForUserTask(result: sourceCode.k2Designer.objectModel.smartForms.IHandler[],
        pluginContext: IProcessPluginDefinitionContext): IGeneralItem[] {
        const handlers: sourceCode.k2Designer.objectModel.smartForms.IHandler[] = result;
        const generalItems: IGeneralItem[] = [];

        // for the User Task, because a condition/action is generated, we need to move between
        // handlers and not between actions.
        // An edge case for If with an Else exist, the first IF handler block and
        // the ELSE handler block must be created as one block.

        const conditionHandlerId = null;
        const previousHandlerId = null;
        for (let i = 0, len = result.length; i < len; i++) {
            const handler = handlers[i];
            let handlerItem: IGeneralItem = null;

            if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.ForEach) {
                handlerItem = handler;
                handlerItem.subItems = [];
                handlerItem.level = 0;
                handlerItem.itemType = GeneralItemTypes.forEach;

                this.addConditions(handlerItem, handler.conditions, 1);

                const indentLevel = handler.conditions.length > 0 ? 2 : 1;
                this.addActions(handlerItem, handler.actions, indentLevel);

                generalItems.push(handlerItem);
            }
            else if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.Else) {
                handlerItem = handler;
                handlerItem.subItems = [];
                handlerItem.level = 0;
                handlerItem.itemType = GeneralItemTypes.else;

                this.addConditions(handlerItem, handler.conditions, 1);

                const indentLevel = handler.conditions.length > 0 ? 2 : 1;
                this.addActions(handlerItem, handler.actions, indentLevel);

                generalItems.push(handlerItem);
            }
            else if (handler.conditions.length > 0) {
                handlerItem = handler.conditions[0];
                handlerItem.subItems = [];
                handlerItem.itemType = GeneralItemTypes.condition;
                // transfer handler id to the condition as the first condition will be
                // treated as the handler
                handlerItem.id = handler.id;
                handlerItem.level = 0;

                this.addConditions(handlerItem, handler.conditions, 0, 1);

                // check that this is the open task condition/action pair
                for (const action of handler.actions) {
                    if (action.isLoadProcessAction) {
                        handlerItem.isOpenTaskCondition = true;
                        break;
                    }
                }

                if (handlerItem.isOpenTaskCondition) {
                    if (i !== 0) {
                        handlerItem.previousHandlerId = handlers[i - 1].id;
                    }
                    else {
                        handlerItem.previousHandlerId = null;
                    }
                    pluginContext.workflowCondition = handlerItem;
                }

                const returnAction = this.addActions(handlerItem, handler.actions, 1);
                if (returnAction !== null) {
                    returnAction.handlerId = conditionHandlerId;
                    returnAction.previousHandlerId = previousHandlerId;
                    pluginContext.workflowAction = returnAction;
                }

                if (i + 1 < len && result[i + 1].type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.Else) {
                    const elseHandler = handlers[i + 1];
                    const elseItem: IGeneralItem = elseHandler;
                    elseItem.level = 0;
                    elseItem.itemType = GeneralItemTypes.else;

                    handlerItem.subItems.push(elseItem);

                    this.addConditions(handlerItem, elseHandler.conditions, 1);

                    const indentLevel = elseHandler.conditions.length > 0 ? 2 : 1;
                    this.addActions(handlerItem, elseHandler.actions, indentLevel);

                    i++;
                }
                generalItems.push(handlerItem);
            }
            else if (handler.actions.length > 0) {
                const handlerAction: IGeneralItem = handler.actions[0];
                // transfer handler id to the action as the first action will be
                // treated as the handler
                handlerAction.id = handler.id;
                handlerAction.level = 0;
                handlerAction.itemType = GeneralItemTypes.action;
                handlerAction.subItems = [];

                this.addActions(handlerAction, handler.actions, 0, 1);

                generalItems.push(handlerAction);
            }
        }

        return generalItems;
    }

    /**
     * Processes a list of IHandler objects into a list of IGeneralItem objects for configuring a start rule
     *
     * @param result
     */
    processHandlersForStartRule(result: sourceCode.k2Designer.objectModel.smartForms.IHandler[],
        pluginContext: IProcessPluginDefinitionContext): IGeneralItem[] {
        const handlers: sourceCode.k2Designer.objectModel.smartForms.IHandler[] = result;
        const generalItems: IGeneralItem[] = [];

        //Flattend the actions & conditions, because for the drag & drop we can't have repeats within repeats to work.
        for (const handler of handlers) {

            if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.ForEach) {
                const forEachItem: IGeneralItem = handler;
                forEachItem.level = 0;
                forEachItem.itemType = GeneralItemTypes.forEach;
                generalItems.push(forEachItem);
            }
            else if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.Else) {
                const elseItem: IGeneralItem = handler;
                elseItem.level = 0;
                elseItem.itemType = GeneralItemTypes.else;
                elseItem.subItems = [];

                for (const subItem of handler.conditions as IGeneralItem[]) {
                    subItem.level = 1;
                    subItem.itemType = GeneralItemTypes.condition;
                    elseItem.subItems.push(subItem);
                }

                generalItems.push(elseItem);
            }
            else if (handler.conditions.length > 0) {
                const conditionItem: IGeneralItem = handler.conditions[0];
                conditionItem.level = 0;
                conditionItem.itemType = GeneralItemTypes.condition;
                conditionItem.subItems = [];
                if (handler.conditions.length > 1) {
                    for (let j = 1; j < handler.conditions.length; j++) {
                        const subItem: IGeneralItem = handler.conditions[j];
                        subItem.level = 0;
                        subItem.itemType = GeneralItemTypes.condition;
                        conditionItem.subItems.push(subItem);
                    }
                }
                generalItems.push(conditionItem);
            }

            for (let k = 0; k < handler.actions.length; k++) {
                const actionItem: IGeneralItem = (handler.actions[k]);
                actionItem.itemType = GeneralItemTypes.action;
                actionItem.level = 0;
                actionItem.selected = false;
                actionItem.focused = false;
                actionItem.handlerId = handler.id;

                if (k > 0) {
                    actionItem.previousActionId = handler.actions[k - 1].id;
                }

                const containsConditions = handler.conditions.length > 0;

                if (handler.actions[k].isStartWorkflowAction || handler.actions[k].isLoadProcessAction) {
                    actionItem.isWorkflowAction = true;
                    pluginContext.workflowAction = actionItem;
                }

                // determine level
                if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.ForEach) {
                    actionItem.level = 1;
                }
                else if (handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.Else && containsConditions) {
                    actionItem.level = 2;
                } else if (containsConditions || handler.type === sourceCode.k2Designer.objectModel.smartForms.HandlerType.Else) {
                    actionItem.level = 1;
                }
                else {
                    actionItem.level = 0;
                }

                generalItems.push(actionItem);
            }
        }

        return generalItems;
    }

    /**
     * Method to create item references on the process and configured plugins in the config panel
     */
    createItemReferencesAndPlugins(pluginContext: IProcessPluginDefinitionContext): void {

        this.itemReferenceService.createItemReferences(pluginContext.newItemReferences.collection, StartMode.smartForms);

        if (!pluginContext.isUserForm) {
            if (pluginContext.configureRuleLater) {
                this.setupFormDefinitionsOnPluginIntend(pluginContext.form.name || pluginContext.form.metaData, pluginContext.form.title,
                    pluginContext.processDefinition as
                    sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.IFormProcessDefinition
                );
            }

            if (!pluginContext.configureRuleLater) {
                this.removeIntendedPluginWhenConfigured(pluginContext);
            }
        }
    }

    /**
     * Method to set the links to the form if the user intended to integrate to a form, but only selected to configure the item references.
     * Something needs to be saved in the definition so that it can be
     * be reconfigured at a later stage. It will return a boolean value to determine if the plugin already exist/not.
     */
    setupFormDefinitionsOnPluginIntend(
        formSystemName: string,
        formDisplayName: string,
        targetProcessDefinition: IFormProcessDefinition): boolean {

        for (const processDefinition of this.processService.root.configuration.processDefinitions as IFormProcessDefinition[] ) {

            // Needs to check for 'n formSystemName of empty as well,
            // this gets set when the user configure or remove the plugin, we need some kind of way to determine.
            if (processDefinition.formSystemName != null &&
                ((processDefinition.formSystemName.indexOf('_removed') > 0 &&
                    processDefinition.formSystemName.substring(0, processDefinition.formSystemName.indexOf('_removed')) === formSystemName) ||
                    processDefinition.formSystemName === formSystemName)) {
                return true;
            }
        }

        //Update the process definition external definition reference that points to the form.
        this.processService.root.configuration.processDefinitions.push(targetProcessDefinition);

        targetProcessDefinition.formSystemName = formSystemName;
        targetProcessDefinition.formDisplayName = formDisplayName;

        return false;
    }

    /**
     * Method to update the form
     */
    updateForm(pluginContext: IProcessPluginDefinitionContext): Promise<void> {

        return new Promise<void>((resolve, reject) => {
            try {

                if (!pluginContext.configureRuleLater) {

                    if (pluginContext.isUserForm) {

                        const navigateUrlSmartFields = pluginContext.navigateUrl?.smartFields || [];

                        const clientEventPublish = {
                            formSystemName: pluginContext.form.name || pluginContext.form.metaData,
                            afterSubmitNavigateToForm: pluginContext.navigateUrl.smartFields.length > 0 ?
                                (navigateUrlSmartFields[0] as sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartForm).name : '',
                            afterSubmitNavigateToUrl: pluginContext.navigateUrl.smartFields.length > 0 ?
                                (navigateUrlSmartFields[0] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text : '',
                            formName: pluginContext.form.title,
                            activityName: pluginContext.fqn,
                            activityDisplayName: pluginContext.activityDisplayName,
                            stateName: pluginContext.createNewState ? pluginContext.newStateName : '',
                            stateId: pluginContext.createNewState ? '' : pluginContext.selectedState.key,
                            rule: pluginContext.selectedRule.metaData,
                            itemReferences: this.getReferences(pluginContext),
                            actionType: 'workflowview',
                            viewAt: pluginContext.selectedLocation.key,
                            actionCommand: pluginContext.actionCommand,
                            actionMessage: pluginContext.submitMessage,
                            allocateWorkflow: pluginContext.allocateTaskToUser,
                            actionId: pluginContext.workflowAction.id,
                            previousActionGuid: pluginContext.workflowAction.previousActionId,
                            handlerGuid: pluginContext.workflowCondition.id,
                            previousHandlerGuid: pluginContext.workflowCondition.previousHandlerId,
                            afterSubmitRuleId: pluginContext.afterSubmitRule ? pluginContext.afterSubmitRule.definitionGuid : '',
                            afterSubmitRuleInstanceId: pluginContext.afterSubmitRule ? pluginContext.afterSubmitRule.instanceGuid : ''
                        } as sourceCode.k2Designer.objectModel.smartForms.IClientEventPublish;

                        this.publishClientEvent(clientEventPublish)
                            .subscribe({
                                next: () => {
                                    resolve();
                                },
                                error: (error: any) => {
                                    reject(error);
                                }
                            });
                    }
                    else {

                        const folioExpression = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                            smartFields:
                                [
                                    sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                                        text: pluginContext.folio,
                                        title: pluginContext.folio
                                    })
                                ]
                        });

                        const processPublish = {
                            formSystemName: pluginContext.form.name || pluginContext.form.metaData,
                            formName: pluginContext.form.title,
                            processName: pluginContext.fqn,
                            stateName: pluginContext.createNewState ? pluginContext.newStateName : '',
                            stateId: pluginContext.createNewState ? '' : pluginContext.selectedState.key,
                            makeDefaultState: pluginContext.makeStateDefault,
                            ruleId: (pluginContext.selectedRule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule).definitionGuid,
                            ruleInstanceId: (pluginContext.selectedRule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule).instanceGuid,
                            ruleSubFormId: (pluginContext.selectedRule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule).subFormGuid,
                            precedingActionId: pluginContext.workflowAction.previousActionId,
                            handlerId: pluginContext.workflowAction.handlerId,
                            itemReferences: this.getReferences(pluginContext),
                            actionId: pluginContext.editMode ? pluginContext.actionId : '',
                            folio: folioExpression.getJson(),
                            moveAction: pluginContext.editMode
                        } as sourceCode.k2Designer.objectModel.smartForms.IProcessPublish;

                        this.updateFormOnServer(processPublish)
                            .subscribe({
                                next: () => {
                                    resolve();
                                },
                                error: (error: any) => {
                                    reject(error);
                                }
                            });
                    }
                }
                else {
                    resolve();
                }

            } catch (ex) {
                console.error(ex);
                reject(null);
            }
        });
    }

    /**
     * Method to refresh the states.
     */
     refreshStates(pluginContext: IProcessPluginDefinitionContext): void {
        const options = {
            params: new HttpParams()
                .set('host', this.environmentService.currentEnvironment.hostId)
                .set('hashes', JSON.stringify([]))
                .set('formId', pluginContext.form?.key || '')
                .set('vers', Date.now().toString()) //no cache
        };

        this.http.get<sourceCode.k2Designer.objectModel.smartForms.IState[]>('api/smartforms/getformstates', options)
            .pipe(map(result => (typeof result) === 'string' ? JSON.parse(result as any) as sourceCode.k2Designer.objectModel.smartForms.IState[] : result))
            .subscribe((result) => {
                const states: sourceCode.k2Designer.objectModel.smartForms.IState[] = result;
                pluginContext.states = [];
                for (let i = 0; i < states.length; i++) {
                    const item: IDropDownMenuItem = <IDropDownMenuItem>{
                        key: states[i].id,
                        title: states[i].name,
                        isSelected: pluginContext.selectedState.key == states[i].id.toString(),
                        metaData: states[i].isBase
                    };
                    pluginContext.states.push(item);

                    if (item.isSelected) {
                        pluginContext.selectedState = item;
                    }
                }

                if (!pluginContext.selectedState && states.length > 0) {
                    pluginContext.selectedState = pluginContext.states[0];
                }
            });
    }

    /**
     * Server call to Update Form
     */
    private updateFormOnServer(processPublish: sourceCode.k2Designer.objectModel.smartForms.IProcessPublish): Observable<string> {

        const httpOptions = {
            headers: new HttpHeaders({
                'content-type': 'application/json'
            })
        };

        return this.http.post<string>('api/smartforms/updateform', {
            host: this.environmentService.currentEnvironment.hostId,
            toSerialize: JSON.stringify(processPublish)
        }, httpOptions);
    }

    /**
     * Server call to Publish Client Event
     */
    private publishClientEvent(clientEventPublish: sourceCode.k2Designer.objectModel.smartForms.IClientEventPublish): Observable<string> {

        const httpOptions = {
            headers: new HttpHeaders({
                'content-Type': 'application/json'
            })
        };

        return this.http.post<string>('api/smartforms/publishclientevent', {
            host: this.environmentService.currentEnvironment.hostId,
            toSerialize: JSON.stringify(clientEventPublish)
        }, httpOptions);
    }

    /**
     * Method to merge references
     */
    private getReferences(pluginContext: IProcessPluginDefinitionContext): ISmartFormItemReference[] {

        const references = [];

        for (const existingReference of pluginContext.existingItemReferences.collection) {
            if (!existingReference.disabled) {
                references.push(existingReference);
            }
        }

        for (const newReference of pluginContext.newItemReferences.collection) {
            if (!newReference.disabled) {
                references.push(newReference);
            }
        }

        return references;
    }

    /**
     * Method to remove the intended plugin when reconfigured.
     */
    private removeIntendedPluginWhenConfigured(pluginContext: IProcessPluginDefinitionContext): void {

        for (const definition of this.processService.root.configuration.processDefinitions as IFormProcessDefinition[]) {

            if (definition.formSystemName &&
                definition.formSystemName === (pluginContext.form.name || pluginContext.form.metaData)) {

                //Mark the name, don't remove the entry.  We need something to know that the intented plugin is already configured or
                //removed otherwise it will keep adding the inteded plugin
                //each time it hits the constructor.
                definition.formSystemName = definition.formSystemName + '_removed';
                break;
            }
        }
    }

    private addConditions(parent: IGeneralItem,
        conditions: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.smartForms.ICondition>,
        indentLevel: number = 0,
        startAt: number = 0) {
        for (let i = startAt, len = conditions.length; i < len; i++) {
            const conditionItem: IGeneralItem = conditions[i];
            conditionItem.level = indentLevel;
            conditionItem.itemType = GeneralItemTypes.condition;
            parent.subItems.push(conditionItem);
        }
    }

    private addActions(parent: IGeneralItem,
        actions: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.smartForms.IAction>,
        indentLevel: number = 0,
        startAt: number = 0): IGeneralItem {
        let returnItem: IGeneralItem = null;
        for (let i = startAt, len = actions.length; i < len; i++) {
            const actionItem: IGeneralItem = actions[i];
            actionItem.selected = false;
            actionItem.focused = false;
            actionItem.level = indentLevel;
            actionItem.itemType = GeneralItemTypes.action;
            if (actionItem.isLoadProcessAction) {
                returnItem = actionItem;
            }

            parent.subItems.push(actionItem);
        }
        return returnItem;
    }

    /**
     * Method to detemine what option to configure with.
     */
    private configureOptionModel(pluginContext: IProcessPluginDefinitionContext): void {
        //If the form contains a viable rule to use and the user did not select the option to configure (show settings)
        if (pluginContext.containsDefaultRule && !pluginContext.configureRuleNow) {
            pluginContext.configureRuleForMe = true;
            pluginContext.configureRuleLater = false;
            pluginContext.configureRuleNow = false;
            this.determinePages(pluginContext);
        }
        else {
            pluginContext.configureRuleNow = true;
            pluginContext.configureRuleLater = false;
            pluginContext.configureRuleForMe = false;
        }

        if (pluginContext.isUserForm) {
            if (pluginContext.foundWorkflowActionOnForm) {
                pluginContext.configureFormRuleDescription = this.resourceService
                    .getString('smartforms_integratio_user_taskn_rule_action_heading_found')
                    .replace('{0}', sourceCode.k2Designer.utilities.ui.htmlEncode(pluginContext.form.title));
                pluginContext.ruleConfigureNowDescription = this.resourceService
                    .getString('smartforms_integration_user_task_rule_action_confirm_configuration');
                pluginContext.ruleConfigureLaterDescription = this.resourceService.getString('smartforms_integration_user_task_rule_action_skip');
                pluginContext.ruleDoItForMeDescription = this.resourceService.getString('smartforms_integration_rule_action_exists_skip');
            }
            else {
                pluginContext.configureFormRuleDescription = this.resourceService
                    .getString('smartforms_integration_user_task_rule_action_heading_not_found')
                    .replace('{0}', sourceCode.k2Designer.utilities.ui.htmlEncode(pluginContext.form.title));
                pluginContext.ruleConfigureNowDescription = this.resourceService.getString('smartforms_integration_user_task_rule_action_add_now');
                pluginContext.ruleConfigureLaterDescription = this.resourceService.getString('smartforms_integration_user_task_rule_action_add_later');
                pluginContext.ruleDoItForMeDescription = this.resourceService.getString('smartforms_integration_do_it_for_me');
            }
        }
        else {
            //Apply resources depending on workflow action found or not.
            if (pluginContext.foundWorkflowActionOnForm) {
                pluginContext.configureFormRuleDescription = this.resourceService
                    .getString('smartforms_integration_rule_action_heading_found')
                    .replace('{0}', sourceCode.k2Designer.utilities.ui.htmlEncode(pluginContext.form.title));
                pluginContext.ruleConfigureNowDescription = this.resourceService.getString('smartforms_integration_rule_action_confirm_configuration');
                pluginContext.ruleConfigureLaterDescription = this.resourceService.getString('smartforms_integration_rule_action_exists_skip');
                pluginContext.ruleDoItForMeDescription = this.resourceService.getString('smartforms_integration_rule_action_exists_skip');
            }
            else {
                pluginContext.configureFormRuleDescription = this.resourceService
                    .getString('smartforms_integration_rule_action_heading_not_found')
                    .replace('{0}', sourceCode.k2Designer.utilities.ui.htmlEncode(pluginContext.form.title));
                pluginContext.ruleConfigureNowDescription = this.resourceService.getString('smartforms_integration_rule_action_add_now');
                pluginContext.ruleConfigureLaterDescription = this.resourceService.getString('smartforms_integration_rule_action_add_later');
                pluginContext.ruleDoItForMeDescription = this.resourceService.getString('smartforms_integration_do_it_for_me');
            }
        }

        //Enable the next button when done.
        //pluginContext.pages[0].nextButton.disabled = false;
        pluginContext.formLoading = false;

        pluginContext.formItemReferences.collection = this.formContent.itemReferences;
        pluginContext.folio = this.formContent.folio;

        pluginContext.hideIndicators = false;
    }

    /**
     * Callback from rules rest call.
     */
    private rulesLoaded(pluginContext: IProcessPluginDefinitionContext, result: string): Observable<void> {

        let rules: sourceCode.k2Designer.objectModel.smartForms.IRule[] = [];
        try {
            rules = JSON.parse(result) as sourceCode.k2Designer.objectModel.smartForms.IRule[];
        } catch { }
        pluginContext.rules = [];
        pluginContext.containsDefaultRule = false;

        let defaultRule: IDropDownMenuItem = null;

        if (rules.length === 0) {
            pluginContext.noRulesError = this.resourceService.getString('wizard_Task_smartform_select_state_with_rules');
        }
        else {
            pluginContext.noRulesError = '';
        }

        for (const rule of rules) {
            const item: IDropDownMenuItem = {
                key: rule.id + '|' + rule.instanceGuid,
                title: rule.displayName,
                isSelected: this.formContent.workflowActionRule && this.formContent.workflowActionRule.id === rule.id,
                metaData: rule
            };
            pluginContext.rules.push(item);

            if (item.isSelected) {
                pluginContext.selectedRule = item;
                pluginContext.containsDefaultRule = true;
            }

            if (rule.isDefault) defaultRule = item;
        }

        if (!pluginContext.containsDefaultRule && defaultRule !== null) {
            pluginContext.selectedRule = defaultRule;
            pluginContext.containsDefaultRule = true;

            for (const rule of pluginContext.rules) {
                rule.isSelected = rule.key === defaultRule.key;
            }
        }

        if (!pluginContext.containsDefaultRule && rules.length > 0) {
            pluginContext.selectedRule = pluginContext.rules[0];
        }

        if (pluginContext.isUserForm) {
            pluginContext.afterSubmitRules = $.extend(true, [], pluginContext.rules.slice());
            this.configureWorkflowStrip(pluginContext);
        }

        //Clear the conditions/rules & actions collection when there is nothing on the selected state.
        if (pluginContext.rules.length === 0) {
            pluginContext.selectedRule = null;
            pluginContext.selectedStatehandlers = [];
        }

        return this.refreshRuleConditionsAndActions(pluginContext);
    }

    /**
     * Method to configure the workflow strip
     */
    private configureWorkflowStrip(pluginContext: IProcessPluginDefinitionContext): void {
        pluginContext.workflowViewLocations = [];

        const topItem: IDropDownMenuItem = {
            key: 'top',
            title: this.resourceService.getString('smartforms_integration_user_task_workflowstrip_top'),
            isSelected: this.formContent.workflowStripLocation === 'top'
        };

        const bottomItem: IDropDownMenuItem = {
            key: 'bottom',
            title: this.resourceService.getString('smartforms_integration_user_task_workflowstrip_bottom'),
            isSelected: this.formContent.workflowStripLocation === 'bottom'
        };

        pluginContext.workflowViewLocations.push(topItem);
        pluginContext.workflowViewLocations.push(bottomItem);

        if (bottomItem.isSelected) {
            pluginContext.selectedLocation = bottomItem;
        }
        else {
            topItem.isSelected = true;
            pluginContext.selectedLocation = topItem;
        }

        pluginContext.actionCommand = this.formContent.actionCommand ? this.formContent.actionCommand : 'showmessage';

        this.setAfterSubmitRule(pluginContext);

        if (this.formContent.actionCommand === 'callarule') {
            pluginContext.afterSubmitRule = this.formContent.afterSubmitRule;
            for (const rule of pluginContext.afterSubmitRules) {
                if ((rule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule).id === this.formContent.afterSubmitRule.id) {
                    rule.isSelected = true;
                    pluginContext.afterSubmitRule = rule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule;
                }
                else {
                    rule.isSelected = false;
                }
            }
        }
        else if (this.formContent.actionCommand === 'navigateto') {
            if (this.formContent.afterSubmitNavigateToForm.id) {
                pluginContext.navigateUrl = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    smartFields: [
                        sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartForm.createInstance({
                            id: this.formContent.afterSubmitNavigateToForm.id,
                            title: this.formContent.afterSubmitNavigateToForm.name,
                            name: this.formContent.afterSubmitNavigateToForm.name
                        })
                    ]
                });

                pluginContext.toggleToUrl = false;
            }
            else if (this.formContent.afterSubmitNavigateToURL) {
                pluginContext.navigateUrl.smartFields.clear();

                pluginContext.navigateUrl.smartFields.push(
                    sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                        text: this.formContent.afterSubmitNavigateToURL
                    })
                );
                pluginContext.toggleToUrl = true;
            }
        }
        else {
            if (!this.formContent.afterSubmitMessage) {
                pluginContext.submitMessage = this.resourceService.getString('wizard_Task_smartform_default_submit_message');
            }
            else {
                pluginContext.submitMessage = this.formContent.afterSubmitMessage;
            }
        }

        pluginContext.allocateTaskToUser = this.formContent.workflowAllocate;
    }

    /**
     * Set the default value for the after submit rule on the context
     * */
    private setAfterSubmitRule(pluginContext: IProcessPluginDefinitionContext) {
        pluginContext.afterSubmitRule = pluginContext.afterSubmitRules.length > 0
            ? pluginContext.afterSubmitRules[0].metaData
            : null;

        //Ensure dropdown items is configured to display correct selected item
        for (const rule of pluginContext.afterSubmitRules) {
            if (rule.metaData.id !== pluginContext.afterSubmitRule.id) {
                rule.isSelected = false;
            } else {
                rule.isSelected = true;
            }
        }
    }

    /**
     * Method to get the error message.
     */
    private getMessageFromResult(result: string): string {
        let exception = null;
        try {
            exception = JSON.parse(result);
        } catch { }

        let messageTitle = '';

        if (exception?.ExceptionDetail) {
            if (exception.ExceptionDetail.InnerException) {
                messageTitle = exception.ExceptionDetail.InnerException.Message;
            }
            else {
                messageTitle = exception.ExceptionDetail.Message;
            }
        }
        else {
            if (exception?.InnerException) {
                messageTitle = exception.InnerException.ExceptionMessage;
            }
            else if (exception.Message) {
                messageTitle = exception.Message;
            }
            else if (exception?.ExceptionMessage) {
                messageTitle = exception.ExceptionMessage;
            }
        }

        return messageTitle;
    }

    /**
     * Method to detect changes
     */
    private detectChanges(pluginContext: IProcessPluginDefinitionContext): boolean {
        //Check if form is going to be checked-out
        if (this.formContent.checkedOutBy === '') {
            pluginContext.checkOutForm = true;
        }

        //Check if a new action is added to a rule
        if (pluginContext.selectedRule) {
            if (((pluginContext.selectedRule.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule).id !== this.formContent.workflowActionRule.id) ||
                (!pluginContext.isUserForm && pluginContext.makeStateDefault !== this.formContent.isWorkflowActionStateDefault) ||
                (!pluginContext.createNewState && pluginContext.selectedState.key !== this.formContent.workflowActionStateId)) {
                pluginContext.addActionToRule = true;
            }
        }

        //Check if something changed on the usertask
        if (pluginContext.isUserForm
            && (
                (pluginContext.submitMessage !== this.formContent.afterSubmitMessage && this.formContent.actionCommand === 'showmessage')
                || (
                    pluginContext.afterSubmitRule
                    && pluginContext.afterSubmitRule.id !== this.formContent.afterSubmitRule.id
                    && this.formContent.actionCommand === 'callarule'
                )
                || this.formContent.actionCommand === 'navigateto'
                || this.formContent.actionCommand !== pluginContext.actionCommand
                || this.formContent.workflowStripLocation !== pluginContext.selectedLocation.key
                || this.formContent.workflowAllocate !== pluginContext.allocateTaskToUser
            )
        ) {
            pluginContext.addActionToRule = true;
        }

        //Check if item references exists on the process.
        pluginContext.addItemReferences = false;
        if (pluginContext.containsNewReferences) {
            for (const newReference of pluginContext.newItemReferences.collection) {
                if (!newReference.disabled && !(this.processService.root as
                    sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessExtended)
                    .findWorkflowItemReference(newReference.smartObjectName,
                        newReference.smartObjectMethodName, this.processService as any,
                        newReference.primary, newReference.startMode,
                        newReference.primaryReference)) {
                    pluginContext.addItemReferences = true;
                    break;
                }
            }
        }

        return pluginContext.checkOutForm
            || pluginContext.createNewState
            || pluginContext.addItemReferences
            || pluginContext.addActionToRule;
    }

    //Reset config values when new form is selected.
    private resetToDefaults(pluginContext: IProcessPluginDefinitionContext): void {
        pluginContext.pageIndex = 0;
        pluginContext.formLoading = true;
        pluginContext.containsDefaultRule = false;
    }

    /**
     * Method  to setup state model
     */
    private configureStateModel(pluginContext: IProcessPluginDefinitionContext): void {
        pluginContext.states = [];
        pluginContext.selectedState = null;
        pluginContext.selectedRule = null;

        pluginContext.foundWorkflowActionOnForm = (this.formContent.workflowAction !== null &&
            this.formContent.workflowAction?.id !== null && this.formContent.workflowAction?.id !== undefined);

        //If the startworkflow action was not found on form, create new state with workflow name.
        if (!pluginContext.foundWorkflowActionOnForm) {
            pluginContext.createNewState = true;

            if (pluginContext.isUserForm) {
                pluginContext.newStateName = this.resourceService.getString('smartforms_integration_user_task_state_workflow_task');
            }
            else {
                pluginContext.newStateName = this.processService.root.title;
            }
        }
        else {
            pluginContext.actionId = this.formContent.workflowAction?.id;
            pluginContext.makeStateDefault = (this.formContent.isWorkflowActionStateDefault && this.formContent.isWorkflowActionStateDefault === true);
            pluginContext.createNewState = false;
        }

        for (const state of this.formContent.states) {
            const item: IDropDownMenuItem = {
                key: state.id,
                title: state.name,
                isSelected: this.formContent.workflowActionStateId === state.id.toString()
            };
            pluginContext.states.push(item);

            if (item.isSelected) {
                pluginContext.selectedState = item;
            }
        }

        if (!pluginContext.selectedState && this.formContent.states.length > 0) {
            pluginContext.selectedState = pluginContext.states[0];
        }
    }
   
}
