import { Component, Inject, Input, OnDestroy, OnInit, QueryList, ViewChildren } from '@angular/core';
import { ListItemComponent } from 'src/app/components/common/list-item/list-item.component';
import { WorkflowService } from 'src/app/services/workflow/workflow.service';
import { listItemIcons } from 'src/app/shared/constants/list-item';
import { ValidationTypes } from 'src/app/shared/enums/model/validation-types';
import { DecisionOptionType } from 'src/app/shared/enums/model/workflow/process/decision-option-types';
import { isUndefinedOrNull } from 'src/app/shared/utils/general';
import { findUniqueName } from 'src/app/shared/utils/name-helper';
import { IConfigPanelTab } from 'src/types/models/config-panel/i-config-panel-tab';
import { ISplitActivityConfiguration } from 'src/types/models/process/activities/configuration/i-split-activity-configuration';
import { IActivity } from 'src/types/models/process/activities/i-activity';
import { IOutcome } from 'src/types/models/process/outcomes/i-outcome';
import { IValidationService } from 'src/types/services/validation/ivalidation.service';
import { IValidatorItem } from 'src/types/validation/i-validator-item';
import { IConfigPanelService } from '../../../../../../../types/services/config-panel/i-config-panel-service';
import { IEnvironmentService } from '../../../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../../../types/services/plugin/i-plugin-service';
import { ConfigPanelServiceToken } from '../../../../../../services/config-panel/config-panel-service-factory';
import { EnvironmentServiceToken } from '../../../../../../services/environment/environment-service-factory';
import { PluginServiceToken } from '../../../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../../../services/ruleplugin/rule-plugin-service';
import { UndoRedoServiceToken } from '../../../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../../../services/validation/validation-service-factory';
import { ChooseControlTypes } from '../../../../../../shared/enums/choose-control/choose-control-types';
import { RuleContextHandlerNames } from '../../../../../../shared/enums/rules/rule-context-handler-names';
import { RulePluginBaseComponent } from '../../../base/rule-plugin-base/rule-plugin-base.component';

@Component({
    selector: 'k2-split-step',
    templateUrl: './split-step.component.html',
    styleUrls: ['./split-step.component.less']
})
export class SplitStepComponent extends RulePluginBaseComponent implements OnInit, OnDestroy {

    // Input properties
    @Input() activity: IActivity;

    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    // Public properties
    hoverClass = '';
    isToggleBtnHover = false;
    tabIndexSplitLogicRadioButtons: number;
    outcomeIcon = listItemIcons.outcomeIcon;

    get hasPortsAvailable(): boolean {
        let usedPorts = 0;
        const incomingPortCount = this.workflowService.countAllIncomingPorts(this.activity as unknown as IActivity);

        usedPorts = incomingPortCount + this.configuration.outcomes.length;

        return usedPorts < 12;
    }

    /**
     * Property that is set to the selected outcome.
     */
    selectedOutcome: IOutcome;

    get configuration(): ISplitActivityConfiguration {
        return this.activity?.configuration as unknown as ISplitActivityConfiguration;
    }

    // Private properties
    private forceFocus: boolean;
    private isInEditMode: boolean;

    // Enums
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    decisionOptionType: typeof DecisionOptionType = DecisionOptionType;

    @ViewChildren(ListItemComponent) outcomes: QueryList<ListItemComponent>;

    constructor(@Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        protected rulePluginService: RulePluginService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken) protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        protected resourceService: ResourceService,
        private workflowService: WorkflowService
    ) {
        super(validationService, configPanelService, rulePluginService, undoRedoService, pluginService, environmentService, resourceService, RuleContextHandlerNames.SplitRule);
    }

    ngOnInit(): void {
        //Set activity configuration as tab validation object (this cannot be done in the 'super', because inputs are not accessible yet)
        this.setTabValidationObject(this.activity.configuration, this.configPanelTab);

        this.refreshTabIndexes();
        this.loadPersist();
        this.validatePathList();
    }

    ngOnDestroy() {
        super.ngOnDestroy();
    }

    // Public Methods
    shouldForceFocus(outcome: IOutcome): boolean {
        return outcome === this.selectedOutcome && (this.isInEditMode === true || this.forceFocus === true);
    }

    isSelected(outcome: IOutcome): boolean {
        return outcome === this.selectedOutcome && this.isInEditMode === false;
    }

    selectRuleType(ruleType: DecisionOptionType): void {
        this.undoRedoService.addUndoMarker('--change split step configuration select rule type--');

        //This custom callback is to ensure that the validation of the rules do not linger agter you undo and to ensure that the validation is kept up to date
        //the scope can be undefined hence why an internal object is used splitActivityConfiguration
        this.undoRedoService.addCustomUndoEvent((): boolean => {
            this.validationService.forceAllPassValidation(this.configuration.outcomeRule);
            return true;
        }, null, '-- remove validation from split rule --');

        const wasCustomRuleType = this.configuration.doCustomLogic;

        switch (ruleType) {
            case DecisionOptionType.auto:
                this.configuration.doCustomLogic = false;
                if (wasCustomRuleType) {
                    this.rulePluginService.setCustomRuleStatements(
                        this.ruleContextHandlerName + this.configuration.getId(),
                        this.configuration.outcomeRule.statements
                    );
                }
                //Recreate the auto-rule
                this.workflowService.createAutoRuleForDecisionOrSplit(
                    this.activity as IActivity
                );

                this.loadEmptyRuleDesigner();
                break;
            case DecisionOptionType.custom:
                this.configuration.doCustomLogic = true;
                this.loadAndPopulateRuleDesigner();
                break;
        }

        this.onRuleTypeSelected();
    }

    /**
     * Adds a new outcome path.
     */
    addNewPath(): void {
        if (this.hasPortsAvailable) {
            this.undoRedoService.addUndoMarker('--split step: add path--');

            const newName = findUniqueName(this.configuration.outcomes, 'title', this.resourceService.getString('wizard_SplitStep_Path'), null, true);

            const outcome: IOutcome = {
                title: newName,
                linkedActionReference: null,
                inferredTitle: newName,
                isCustomTitle: true,
                linkedParent: null,
                getParentActivity: () => sourceCode.k2Designer.objectModel.workflow.process.k2Process.Activity
            };

            this.configuration.outcomes.push(outcome as IOutcome);

            this.selectOutcomeByIndex(this.configuration.outcomes.length - 1);
            this.validatePathList();

        } else {
            console.error('No ports available for adding new path to split step.');
        }
    }

    /**
     * Remove selected outcome
     */
    removeSelectedOutcome(): void {

        if (!this.selectedOutcome) {
            //no outcome selected to delete
            return;
        }

        this.undoRedoService.addUndoMarker('--split step: delete path--');

        let index = this.configuration.outcomes.indexOf(this.selectedOutcome);

        this.selectedOutcome.removeFromModelArray();

        //if there is a non-custom rule, recreate it to match changes
        if (this.configuration.decisionOptionType === DecisionOptionType.auto) {
            this.workflowService.createAutoRuleForDecisionOrSplit(this.activity, false);
        }

        this.selectedOutcome = null;

        if (index >= this.configuration.outcomes.length) {
            index = this.configuration.outcomes.length - 1;
        }

        this.validatePathList();
        this.selectOutcomeByIndex(index);
    }

    /**
     * Select the outcome
     *
     * @param target
     * @param outcome
     */
    outcomeSelected = (target: EventTarget, outcome: IOutcome): void => {

        this.selectedOutcome = outcome;

        //Highlight the selected outcome text input
        if ((target as HTMLElement)?.nodeName?.toUpperCase() === 'INPUT') {
            const textInput = target as HTMLInputElement;
            textInput.focus();
            textInput.select();
            this.isInEditMode = true;
        }
        else {
            this.outcomeSelected(this.outcomes.last.noDropInput.nativeElement, this.configuration.outcomes[this.configuration.outcomes.length - 1]);
        }

    };

    // Protected members

    protected loadAndPopulateRuleDesigner(): void {
        this.setSavedCustomStatements(this.configuration, this.configuration.outcomeRule);

        if (!this.activity.configuration.outcomeRule ||
            !this.activity.configuration.outcomeRule.statements ||
            this.activity.configuration.outcomeRule.statements.length === 0) {
            this.activity.configuration.outcomeRule = this.rulePluginService.createDefaultRule(this.ruleContextHandlerName);
        }

        this.rulePluginService.setDesignerContext(this.ruleContextHandlerName, this.activity.configuration.outcomeRule);
        this.rulePluginService.renderCanvas();

    }

    protected refreshTabIndexes(): void {
        this.tabIndexSplitLogicRadioButtons = this.configuration.doCustomLogic === true ? 0 : -1;
    }

    protected renderRuleDesigner(): void {
        if (this.configuration.doCustomLogic) {
            this.loadAndPopulateRuleDesigner();
        }
        else {
            this.loadEmptyRuleDesigner();
        }
    }

    // Private methods

    private selectOutcomeByIndex(index: number) {

        const isIndexDefined = !isUndefinedOrNull(index);
        if (this.configuration.outcomes.length > 0 && isIndexDefined) {
            if (index < 0) {
                index = 0;
            }

            //Check if list element for outcome exists (new item not always immediately visible)
            const isOutcomeCreated = index < this.outcomes.length;
            if (!isOutcomeCreated) {
                // Reason for timeout: when a new outcome is added, the outcome list element may not be available yet, so focus can't be set.
                setTimeout(() => {
                    this.selectOutcomeByIndex(index);
                }, 100);
            } else {
                //Select list item
                this.outcomeSelected(this.outcomes.get(index).noDropInput.nativeElement, this.configuration.outcomes[index]);
            }
        }
    }

    private loadPersist(): void {
        if (this.configuration.outcomes.length >= 1 &&
            this.configuration.outcomesEventReference?.length > 0) {

            const linkedAct = this.activity.getRoot<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>()
                .findItem<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IEvent>(this.configuration.outcomesEventReference)
                .getLogicalParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>();
            if (linkedAct.configuration.outcomes.length === this.configuration.outcomes.length &&
                !this.configuration.doCustomLogic) {

                this.configuration.outcomeRule = sourceCode.k2Designer.objectModel.workflow.configuredRules.ConfiguredDecisionRule.createInstance({
                    statements: [
                        this.workflowService.createTaskExpression(
                            linkedAct as unknown as IActivity,
                            0,
                            this.activity as unknown as IActivity
                        )
                    ]
                });
            }
        } else {
            const baseName = this.resourceService.getString('wizard_SplitStep_Path');

            while (this.configuration.outcomes.length < 2) {
                const newName = findUniqueName(this.configuration.outcomes, 'title', baseName, null, true);

                this.configuration.outcomes.push({
                    title: newName
                } as IOutcome);
            }
        }
    }

    private validatePathList(): void {
        this.validationService.evaluate([ValidationTypes.Required],
            <IValidatorItem>{
                modelObject: this.configuration,
                propertyName: 'outcomes',
                validationFailedMessage: 'resource_service_empty_list',
                validationResourceTokens: ['wizard_SplitStep_Path', 'configpanel_tabs_tooltips_title_LogicalSplitShapeTab'],
            });
    }
}
