import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockPluginService } from '../../../testing/mock-services/plugin-service';
import { IPluginService } from '../../../types/services/plugin/i-plugin-service';

const pluginServiceFactory = () => environment.devHarness === true
    ? new MockPluginService()
    : sourceCode.k2Designer.core.environmentService.pluginService;

export const PluginServiceToken = new InjectionToken<IPluginService>('IPluginService');

export const PluginServiceFactory = {
    provide: PluginServiceToken,
    useFactory: pluginServiceFactory,
    deps: [

    ]
};
