import { Component, ElementRef, EventEmitter, Inject, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { IListItemIconData } from '../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { ClipboardServiceToken } from '../../../services/clipboard/clipboard-service-factory';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { DragDropService } from '../../../services/drag-drop/drag-drop.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { listItemCommands } from '../../../shared/constants/list-item';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { ListItemDropDownType } from '../../../shared/enums/list-item/list-item-drop-down-type';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';
import { isUndefinedOrNull, propertyMatchesInObjectArray } from '../../../shared/utils/general';
import { findUniqueNameForListArray } from '../../../shared/utils/name-helper';
import { BaseComponent } from '../../base/base.component';
import { InputComponent } from '../input/input.component';
import { TextboxComponent } from '../text-box/text-box.component';

@Component({
    selector: 'k2-list-item',
    templateUrl: './list-item.component.html',
    styleUrls: ['./list-item.component.less']
})
export class ListItemComponent extends BaseComponent implements OnInit, OnChanges {

    /**
     * INPUT FLAGS
     * -----------
     */
    /**
     * Custom CSS class for List-Item component main element
     */
    @Input() customClass = '';
    /**
     * Property to determine if item is disabled
     */
    @Input() isDisabled = false;
    /**
     * Determines whether the List Item kan be edited (typing into a text box)
     */
    @Input() isEditable = false;
    @Input() isEditing = false;
    /**
     * If true, a required indicator will be added to left of item
     */
    @Input() isRequired = false;
    /**
     * If true, item will add a blue selection indicator around it if "selected" property is true
     */
    @Input() isSelectable = false;
    /**
     * Indicates if the item should display a selected state or not
     */
    @Input() isSelected = false;
    @Input() isTextBoxFocused = false;
    /**
     * If true, the item will display a sortable drag handle on the right
     */
    @Input() isSortable = false;
    /**
     * If true, the toggle button will be displayed on right of textbox (switch between read-only and edit mode)
     */
    @Input() shouldShowToggleTextbox = false;
    /**
     * If true, the add/remove toggle button that enables/disables is displayed
     */
    @Input() shouldShowToggleEnabled = false;
    @Input() isDraggable = false;
    /**
     * If true, a K2 Textbox will be rendered if editable true
     * If false, an input will be rendered if editable true
     */
    @Input() shouldAllowDrop = false;
    @Input() shouldAlwaysHideBadge = false;
    @Input() shouldFocusOnCreate = false;
    @Input() shouldForceFocus = false;
    @Input() shouldHideBrowseButton = false;
    /**
     * If true, a dropdown arrow will be rendered on right of item
     */
    @Input() shouldShowDropDown = false;
    /**
     * Setting to display text below the list item.
     */
    @Input() shouldShowView = false;

    /**
     * Dropdown menu item template
     */
    @Input() dropDownMenuItemTemplate: DropdownMenuItemTemplates = DropdownMenuItemTemplates.DefaultListItem;
    /**
     * Watermark for dropdown or textbox placeholder
     */
    @Input() watermark = '';
    @Input() emptyDropDownTemplate = '';
    @Input() footerItems: IDropDownMenuItem[] = [];
    @Input() leftIconData: IListItemIconData = null;
    /**
     * Object model associated with this list item - either an expression or string
     */
    @Input() listItemModel: IExpression | string;
    @Input() listItemDropDownType: ListItemDropDownType = ListItemDropDownType.custom;
    @Input() menuItems: IDropDownMenuItem[] = [];
    @Input() rightIconData: IListItemIconData = null;
    /**
     * Sub text that must be displayed underneath text box
     */
    @Input() subText: string;
    @Input() subTextTooltip = '';
    @Input() tabIndexValue = 0;
    @Input() textBoxConstraint: TextBoxConstraint = TextBoxConstraint.None;
    /**
     * Name of property on the model that returns the text field for this list item
     */
    @Input() textProperty = '';
    /**
     * Before the item text is set, it will be validated against this model if specified
     */
    @Input() validationArray: sourceCode.k2Designer.objectModel.core.model.IModelArray<any>;
    /**
     * Name of a view to display below the list item.
     */
    @Input() viewName = '';

    /**
     * Types of validation to perform on model by BaseComponent parent
     */
    @Input() validation: IValidationOptions[] = [];

    /**
     * Is the label/input with no dropdown read-only
     */
    @Input() isReadonly = false;

    /**
     * EVENTS / OUTPUT
     * ---------------
     */

    /**
     * Bind to the drop down item selected event
     */
    @Output() dropDownItemSelected: EventEmitter<IDropDownMenuItem> = new EventEmitter<IDropDownMenuItem>();
    /**
     * Bind to the content toggled enabled/disabled state changed event
     */
    @Output() enabledStateToggled: EventEmitter<void> = new EventEmitter<void>();
    /**
     * Bind to list item control clicked event (selectListItem)
     */
    @Output() listItemSelected: EventEmitter<EventTarget> = new EventEmitter<EventTarget>();
    /**
     * Bind to the content texbox button clicked event
     */
    @Output() textBoxButtonClicked: EventEmitter<void> = new EventEmitter<void>();
    /**
     * Bind to the content right icon button clicked event
     */
    @Output() rightIconClicked: EventEmitter<EventTarget> = new EventEmitter<EventTarget>();
    /**
     * Bind to text box blur event
     */
    @Output() textBoxBlurred: EventEmitter<EventTarget> = new EventEmitter<EventTarget>();
    /**
     * Bind to text box focus event
     */
    @Output() textBoxFocused: EventEmitter<EventTarget> = new EventEmitter<EventTarget>();
    /**
     * Bind to the toggle button clicked event (toggleTextBox)
     */
    @Output() toggleTextBoxButtonClicked: EventEmitter<void> = new EventEmitter<void>();

    @Output() itemTextChange: EventEmitter<any> = new EventEmitter<any>();
    @Output() inputClicked: EventEmitter<void> = new EventEmitter<void>();
    @Output() inputKeyPressed: EventEmitter<number> = new EventEmitter<number>();

    @ViewChild('input') simpleTextInput: InputComponent;
    @ViewChild('noDropInput') noDropInput: ElementRef<HTMLInputElement>;
    @ViewChild('textbox') textbox: TextboxComponent;

    /**
     * State of the toggling (only relevant if shouldToggleEnabled is true)
     */
    isToggleEnabled = false;
    listItemHover = false;
    buttonHover = false;
    menuActive = false;
    isDraggableOnHover = false;

    dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;
    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    get shouldShowLeftIcon(): boolean {
        const shouldShow = this.leftIconData
            && !(this.shouldShowDropDown && ((this.isToggleEnabled && !this.shouldAllowDrop) || (!this.isToggleEnabled && !this.isEditable)));
        return shouldShow;
    }

    constructor(private _dragDropService: DragDropService,
        private _resourceService: ResourceService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService,
        @Inject(ClipboardServiceToken) private _clipBoardService: IClipboardService,
        @Inject(CommandServiceToken) private _commandService: ICommandService,
        private _rootElement: ElementRef) {
        super(_validationService);
    }

    ngOnInit(): void {

        this.setApplicableValidationTypes(this.validation);
        if (this.validation?.length) super.validateArrayOnChange(this.listItemModel as IExpression, 'smartFields', null);

        if (this.shouldFocusOnCreate) {
            $(this._rootElement.nativeElement).focus();
        }

        this._ensureWatermark();

        this._createDropdownCommand();
    }

    ngOnChanges(change: SimpleChanges) {
        if (change?.isSelected) {
            if (change.isSelected.previousValue && !change.isSelected.currentValue && this.isTextBoxFocused) {
                this.isTextBoxFocused = false;
            }
        }
    }

    /**
     * Indicates whether a drag operation is in progress
     */
    get isDragging(): boolean {
        return this._dragDropService.isBusyDragging;
    }

    /**
     * Gets the text value of the model, from its text property
     */
    get itemText(): string {
        if (this.listItemModel && this.textProperty) {
            return this.listItemModel[this.textProperty];
        }
        return '';
    }

    /**
     * Sets the text value of the model's text property
     */
    set itemText(val: string) {
        //TODO: Test undo/redo with this, the old k2ListItem directive had an elaborate workaround with timeouts for undo/redo here
        if (this.textProperty !== '' && !isUndefinedOrNull(this.listItemModel[this.textProperty])) {
            this.listItemModel[this.textProperty] = val;
        }
    }

    modelChanged(inputChanged: string, val: string): void {
        if (inputChanged === 'itemText') {
            //set item text
            this.itemText = val;
            this.itemTextChange.emit(val);
        }
    }

    get itemTitle(): string {
        let result = '';
        if (this.listItemModel) {
            //Use model with text property if specified
            if (this.textProperty) {
                result = this.listItemModel[this.textProperty];
            } else {
                const expression = this.listItemModel as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpressionExtended;
                if (expression?.smartFields?.length > 0) {
                    if (expression.smartFields[0].customTitle) {
                        result = expression.smartFields[0].customTitle;
                    }
                    else {
                        result = expression.smartFields[0].title;
                    }
                }
            }
        }
        return result;
    }

    /**
     * Gets list item model as type IExpression
     * */
    get expressionModel(): sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpression {
        return (this.listItemModel as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpression) || null;
    }

    /**
     * List item selected event
     *
     * @param event
     */
    selectListItem(event: MouseEvent): void {
        this.listItemSelected.emit(event?.target);
    }

    /**
     * Textbox toggled event
     * */
    toggleTextbox = (): void => {
        const expression = this.listItemModel as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpression;
        if (!isUndefinedOrNull(expression)) {
            this._undoRedoService.addUndoMarker('--k2ListItem: toggle expression isDynamic group --');

            expression.isDynamic = !expression.isDynamic;
        }

        this._ensureWatermark();

        this.toggleTextBoxButtonClicked.emit();
    };

    /**
     * Toggle between enable/disable state.
     */
    toggleEnableState(): void {

        this.isDisabled = !this.isDisabled;

        this.enabledStateToggled.emit();
    }

    /**
     * Textbox focused event
     * */
    onTextboxFocus(event: FocusEvent): void {

        this._clipBoardService.setCurrentContext();
        this._commandService.setCurrentContext();

        if (this.shouldAllowDrop) {
            const rootEl = $(this._rootElement.nativeElement);
            rootEl.find('.k2-text-box-focused').removeClass('k2-text-box-focused');
            rootEl.addClass('k2-text-box-focused');
        }

        this.isTextBoxFocused = true;

        this.textBoxFocused.emit(event.target);
    }

    /**
     * Textbox blurred event
     * */
    onTextboxBlur(event: FocusEvent): void {
        const shouldApplyValidation = this.validationArray?.length > 0;
        let isValidationPassed = true;

        if (shouldApplyValidation) {
            if (this.textProperty) {
                if (shouldApplyValidation) {
                    //If title is empty, set back to original title
                    const originalTitle = (this.listItemModel as any).originalTitle;
                    if (this.itemText === '' && originalTitle) {
                        this.itemText = originalTitle;
                    }

                    //Because the list item model is bound, the validation array values will be updated as textbox value is updated
                    //The item matching the itemText will always exist in the array, validate for more than 1 match:
                    if (propertyMatchesInObjectArray(this.validationArray, this.textProperty, this.itemText) > 1) {
                        this.itemText = findUniqueNameForListArray(this.validationArray,
                            this.textProperty,
                            this.itemText,
                            this.listItemModel, false);
                        isValidationPassed = false;
                    }
                }
            }
        }

        if (isValidationPassed) {
            this.isTextBoxFocused = false;
            this.textBoxBlurred.emit(event.target);
        } else {
            //don't blur if validation failed
            (event.target as HTMLElement).focus();
        }
    }

    /**
     * Event was emitted from child ListItemContent to ListItem to indicate that
     * its textbox button was clicked.
     * Need to emit to the List Item's parent
     * */
    onRightIconClicked = (target: EventTarget): void => {
        this.rightIconClicked.emit(target);
    };

    /**
     * Event was emitted from child ListItemContent to ListItem to indicate that
     * its textbox button was clicked.
     * Need to emit to the List Item's parent
     * */
    onTextBoxButtonClicked = (): void => {
        this.textBoxButtonClicked.emit();
    };

    /**
     * Event was emitted from child ListItemContent dropdown to ListItem to indicate that
     * its dropdown item was selected.
     * Need to emit to the List Item's parent
     * */
    onDropDownItemSelected = (item: IDropDownMenuItem) => {
        this.dropDownItemSelected.emit(item);
    };

    /**
     * Show tooltip for textbox if text overflows
     *
     * @param event
     */
    applyOverflowTooltip(event: MouseEvent): void {
        const input = event.target as HTMLInputElement;
        if (this._isOverflown(input)) {
            input.setAttribute('title', input.value);
        }
    }

    /**
     * Removes textbox tooltip
     *
     * @param event
     */
    removeOverflowTooltip(event: MouseEvent): void {
        const input = $(event.target);
        input.removeAttr('title');
    }

    /**
     * Input label clicked event
     * */
    onInputLabelClicked(): void {
        this.inputClicked.emit();
    }

    onInputKeyPressed(event: KeyboardEvent): void {
        this.inputKeyPressed.emit(event.keyCode);
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    private _isOverflown(element: HTMLElement): boolean {
        return element.offsetWidth < element.scrollWidth;
    }

    /**
     * Ensures that a constraint without a default watermark is handled appropriately
     */
    private _ensureWatermark(): void {
        if (this.watermark.length > 0) {
            //use custom specified watermark
            return;
        }

        //Default dropdown watermark
        if (this.shouldShowDropDown && !this.shouldAllowDrop && this.watermark === '') {
            this.watermark = this._resourceService.getString('general_choose');
        }
        else if (!this.shouldShowDropDown || this.shouldAllowDrop) {
            //Textbox watermark, set to default type drop or according to constraint
            if (this.textBoxConstraint === TextBoxConstraint.None) {
                this.watermark = this.shouldAllowDrop === false ?
                    this._resourceService.getString('textbox_watermark_type') :
                    this._resourceService.getString('textbox_watermark_droptype');
            } else {

                if (this.shouldAllowDrop) {
                    switch (this.textBoxConstraint) {
                        case TextBoxConstraint.DropOnly: {
                            this.watermark = this._resourceService.getString('textbox_watermark_droponly');
                        } break;
                        case TextBoxConstraint.DropSelect: {
                            this.watermark = this._resourceService.getString('textbox_watermark_dropselect');
                        } break;
                        default: {
                            this.watermark = this._resourceService.getString('textbox_watermark_droptype');
                        }
                    }
                } else {
                    switch (this.textBoxConstraint) {
                        case TextBoxConstraint.DropSelect: {
                            this.watermark = this._resourceService.getString('textbox_watermark_select');
                        } break;
                        default: {
                            this.watermark = this._resourceService.getString('textbox_watermark_type');
                        }
                    }
                }
            }
        }
    }

    /**
     * This the a factory method. It creates a predefined implementation of dropdown with command type of list item.
     *
     * @param listItemControlType
     */
    private _createDropdownCommand() {
        switch (this.listItemDropDownType) {
            case ListItemDropDownType.toggle:
                this.shouldShowDropDown = true;
                this.isToggleEnabled = true;
                this.shouldHideBrowseButton = true;

                this.footerItems.push({
                    title: this._resourceService.getString('control_PickerControl_Item_TypeConstructMyOwn'),
                    key: listItemCommands.toggle.key,
                    icon: listItemCommands.toggle.icon,
                    innerItemComponent: DropdownMenuItemTemplates.Command,
                    onSelected: this.toggleTextbox
                });

                break;
            case ListItemDropDownType.browse:
                this.shouldShowDropDown = true;
                this.isToggleEnabled = false;
                this.shouldHideBrowseButton = false;

                this.footerItems.push({
                    title: this._resourceService.getString('control_PickerControl_Item_Browse'),
                    key: listItemCommands.browse.key,
                    icon: listItemCommands.browse.icon,
                    innerItemComponent: DropdownMenuItemTemplates.Command,
                    onSelected: this.onTextBoxButtonClicked
                });

                break;
            case ListItemDropDownType.picker:
                this.shouldShowDropDown = true;
                this.isToggleEnabled = true;
                this.shouldHideBrowseButton = false;

                this.footerItems.push({
                    title: this._resourceService.getString('control_PickerControl_Item_Browse'),
                    key: listItemCommands.browse.key,
                    icon: listItemCommands.browse.icon,
                    innerItemComponent: DropdownMenuItemTemplates.Command,
                    onSelected: this.onTextBoxButtonClicked
                });

                this.footerItems.push({
                    title: this._resourceService.getString('control_PickerControl_Item_TypeConstructMyOwn'),
                    key: listItemCommands.toggle.key,
                    icon: listItemCommands.toggle.icon,
                    innerItemComponent: DropdownMenuItemTemplates.Command,
                    onSelected: this.toggleTextbox
                });

                break;
            default:
            //Custom type: footer or command items must be specified by component using list-item menuItems or footerItems inputs
                break;
        }
    }
}
