import { Directive, ElementRef, Injector, Input } from '@angular/core';
import { UpgradeComponent } from '@angular/upgrade/static';
import IDropDownMenuItem from '../../shared/enums/drop-down/drop-down-menu-item';
//Extending old upgraded directive (directives/k2DataFieldMapper/k2DataFieldMapper)

@Directive({
    selector: 'k2-data-field-mapper'
})
export class DataFieldMapperComponent extends UpgradeComponent {

    /**
     *  Datafields collection (Left drop down)
     */
    @Input() datafields: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDataField>;

    /**
     *  Rows to be mapped
     */
    @Input() rows: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.setDataFields.ISetDataFieldMapping>;

    /**
     *  Title that will display on control
     */
    @Input() title: string;

    /**
     *  Icon that will display on control
     */
    @Input() icon: string;

    /**
     *  Collection of items that is available to select from (Right Dropdown)
     */
    @Input() values: IDropDownMenuItem[];

    constructor(elementRef: ElementRef, injector: Injector) {
        super('k2DataFieldMapper', elementRef, injector);
    }

}
