import { Directive, ElementRef, Injector, Input } from '@angular/core';
import { UpgradeComponent } from '@angular/upgrade/static';

@Directive({
    selector: 'k2-toggle-switch'
    //Extending old upgraded directive (directives/K2ToggleSwitch/k2ToggleSwitch.ts)
})
export class ToggleSwitchComponent extends UpgradeComponent {
    @Input() public k2Checked: boolean;
    @Input() public k2Model: any;

    @Input() public k2ToggleButtonClicked: (checked: boolean, model: any) => void;

    constructor(elementRef: ElementRef, injector: Injector) {
        super('k2ToggleSwitch', elementRef, injector);
    }

}
