import { Component, Inject, Input, OnInit } from '@angular/core';
import { IPluginService } from '../../../../../types/services/plugin/i-plugin-service';
import { PluginServiceToken } from '../../../../services/plugin/plugin-service-factory';

@Component({
    selector: 'k2-panel-gutter',
    templateUrl: './panel-gutter.component.html',
    styleUrls: ['./panel-gutter.component.less']
})
export class PanelGutterComponent implements OnInit {
    @Input() public item: any;

    constructor(@Inject(PluginServiceToken) private _pluginService: IPluginService) { }

    ngOnInit(): void {
    }

    public closePanel(name?: string) {
        const panelName = name || this.item.name;

        //Hide the 4th panel (for example filter panel) once the associated panel (3rd panel) gets hidden from config panel.
        const plugin = this._pluginService.getPlugin(panelName);

        if (plugin?.plgnDef?.collapseAssociatedPanel) {
            this._pluginService.toggleCollapsed(plugin.plgnDef.collapseAssociatedPanel);
        }

        this._pluginService.toggleCollapsed(panelName);
    }

}
