import { Injectable } from '@angular/core';
import { DropDownMenuComponent } from 'src/app/components/common/drop-down/drop-down-menu/drop-down-menu.component';
import { IDropDownService } from 'src/types/services/drop-down/i-drop-down.service';

@Injectable({
    providedIn: 'root'
})
export class DropDownService implements IDropDownService {
    private _menus: DropDownMenuComponent[] = [];

    constructor() {
    }

    /**
     * Registers a Drop Down Menu with the service
     * Returns the index of the registered menu
     */
    registerMenu = (menu: DropDownMenuComponent): number =>  {
        this._menus.push(menu);
        return this._menus.length - 1;
    };

    /**
     * Registers a Drop Down Menu with the service
     * This should be called from the ngOnDestroy in the dropdownmenu component
     */
    deRegisterMenu = (menu: DropDownMenuComponent) => {
        this._menus.splice(this._menus.indexOf(menu), 1);
    };

    /**
     * Collapse all dropdown menus, except for the one passed in.
     *
     * @param menu Dropdown menu component
     */
    collapseAllExcept = (menu: DropDownMenuComponent) => {
        this._menus.forEach( x => {
            if( x !== menu){
                x.collapseMenu();
            }
        });
    };

}
