import { DatePipe } from '@angular/common';
import { Component, ComponentFactoryResolver, ComponentRef, Inject, Input, OnInit, ViewChild } from '@angular/core';
import { Subscription } from 'rxjs';
import { IDataService } from '../../../../types/services/data/i-data-service';
import { IEnvironmentService } from '../../../../types/services/environment/i-environment-service';
import { IRedirectService } from '../../../../types/services/redirect/i-redirect-service';
import { IRibbonService } from '../../../../types/services/ribbon-bar/i-ribbon-bar-service';
import { ICloseWFData } from '../../../../types/uiComponents/ribbon/ICloseWFData';
import { IFileInfoModel } from '../../../../types/uiComponents/ribbon/IFileInfoModel';
import { IRibbonConfiguration } from '../../../../types/uiComponents/ribbon/IRibbonConfiguration';
import { ISubTabComponent } from '../../../../types/uiComponents/ribbon/ISubTabComponent';
import { ITabComponent } from '../../../../types/uiComponents/ribbon/ITabComponent';
import { TabHostDirective } from '../../../directives/tab-host/tab-host.directive';
import { DataServiceToken } from '../../../services/data/data-service-factory';
import { EnvironmentServiceToken } from '../../../services/environment/environment-service-factory';
import { ProcessService } from '../../../services/process/process.service';
import { RedirectServiceToken } from '../../../services/redirect/redirect-service-factory';
import { RibbonBarServiceToken } from '../../../services/ribbon-bar/ribbon-bar-service-factory';
import { TabItemInfoComponent } from '../tab-file/tab-item-info/tab-item-info.component';
import { TabItemOptionsComponent } from '../tab-file/tab-item-options/tab-item-options.component';

export interface IFileInfoModelResult {
    categoryId: number;
    categoryName: string;
    path: string;
    owner: string;
    modifiedOwner: string;
    createdDate: string;
    modifiedDate: string;
    title: string;
    clientIdentifier: string;
}

@Component({
    selector: 'app-tab-file',
    templateUrl: './tab-file.component.html',
    styleUrls: ['./tab-file.component.less'],
    providers: [DatePipe]
})
export class TabFileComponent implements OnInit {

    @Input() uiComp: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    @Input() ribbonCnf: IRibbonConfiguration;
    @Input() public getString: (resourceId: string, collectionId?: string) => string;
    @ViewChild(TabHostDirective, { static: true }) tabHost: TabHostDirective;

    public showInfoOption: boolean;
    public currentItem: ISubTabComponent;
    public isStandalone: boolean;
    public fileInfo: IFileInfoModel;

    private _config: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    private _rbbnCnf: IRibbonConfiguration;
    private _busyClosing = false;
    private fileMenuItems: sourceCode.k2Designer.objectModel.ribbon.IFileMenuItem[];
    private isDeployingProcess: boolean;
    private saveErrorMessage: string;
    private ribbonTabChangedCallback: sourceCode.k2Designer.services.studio.IRibbonTabChangedCallback;
    private _busyClosingEventSubscription: Subscription;

    private _userOptionsService: sourceCode.k2Designer.services.studio.UserOptionsService;

    constructor(@Inject(EnvironmentServiceToken) private _envService: IEnvironmentService,
        @Inject(RedirectServiceToken) private _redirectService: IRedirectService,
        @Inject(RibbonBarServiceToken) private _ribbonBarService: IRibbonService,
        @Inject(DataServiceToken) private _dataService: IDataService,
        private _processService: ProcessService,
        private datePipe: DatePipe,
        private componentFactoryResolver: ComponentFactoryResolver) {
    }

    ngOnInit(): void {

        this._config = this.uiComp;
        this._rbbnCnf = <IRibbonConfiguration>this._config.cnf;

        this.showInfoOption = true; //No Apps context

        // opening from legacy hostId will be 'smartforms'
        this.isStandalone = this._envService.currentEnvironment.hostId.toUpperCase() === 'STANDALONE';

        this.populateItems(this._rbbnCnf.tabs);
        this.isDeployingProcess = false;

        this.saveErrorMessage = this.getString('workflow_save_error_message').format(this._envService.workflowFullname);

        this.initOptionsData();
    }

    ngOnDestroy(): void {
        this._ribbonBarService.removeRibbonTabChanged(this.ribbonTabChangedCallback);
        this._busyClosingEventSubscription.unsubscribe();
    }

    public selectMenuItem(itemName: string): void {
        for (let i = 0; i < this.fileMenuItems.length; i++) {
            if (this.fileMenuItems[i].name === itemName) {
                this.currentItem = <ISubTabComponent>this.fileMenuItems[i];
                break;
            }
        }

        this.loadFileMenuComponent(this.currentItem);
    }

    public isCloseEnabled(): boolean {
        //Process service might not be done loading
        //TODO: ideally ngOnInit should await the service loaded callback before completing

        if (!this._envService || !this._processService) return false;

        if (this.isDeployingProcess === true
            || this._dataService.getOpenRequestCount() > 0
            || this._processService.getInstructionCount() > 0
            || this._processService.allowProcessing() === false) {
            return false;
        }
        return true;
    }

    public createNewStudioItem(): void {
        if (this._busyClosing === false) {
            this._busyClosing = true;

            const that = this;
            const exit = function() {
                that.exit.call(that, false, null);
                $(document).trigger('DesignerStateChange', ['Normal']);
            };
            this.processRedirect(exit, exit);
        }
    }

    public getClosingStatus(): string {
        let result = '';

        //Process service might not be done loading
        //TODO: ideally ngOnInit should await the service loaded callback before completing
        if (!this._envService || !this._processService) return result;

        if (this.isDeployingProcess) {
            result = this.getString('workflow_deployment_in_progress');
        }
        else if (this._dataService.getOpenRequestCount() > 0
            || this._processService.getInstructionCount() > 0
            || this._processService.allowProcessing() === false) {
            result = this.getString('workflow_deployment_disabled_saving_workflow_to_server');
        }
        return result;
    }

    //Close
    public selectItem(item?: sourceCode.k2Designer.objectModel.ribbon.IFileMenuItem): void {

        if (this._busyClosing === false) {
            this._busyClosing = true;

            const that = this;
            const complete = () => {
                const closingData: ICloseWFData = {
                    close: true,
                    item,
                    params: {},
                    categoryId: this.fileInfo?.category?.id
                };
                that.exit.call(that, closingData);
                //TODO: Change to emit event with EventEmitter which can be subscribed to in upgraded components
                $(document).trigger('DesignerStateChange', ['Normal']);
            };
            const fail = () => {
                const closingData: ICloseWFData = {
                    close: true,
                    item,
                    params: {},
                    categoryId: this.fileInfo?.category?.id
                };
                that.onFlushCompleteWithFailure.call(that, function() { that.exit.call(that, closingData); });
                //TODO: Change to emit event with EventEmitter which can be subscribed to in upgraded components
                $(document).trigger('DesignerStateChange', ['Error']);
            };

            this.processRedirect(complete, fail);
        }
    }

    private onFlushCompleteWithFailure(callback: () => void) {
        this._busyClosing = false;
        callback();
    }

    private populateItems(tabs: ITabComponent[]): void {
        let _menuItems: ISubTabComponent[];

        for (let d = 0; d < tabs.length; d++) {
            if (tabs[d].drpDn) {
                _menuItems = <ISubTabComponent[]>tabs[d].navItms;
                break;
            }
        }

        if (_menuItems != null && _menuItems.length > 0) {
            this.fileMenuItems = _menuItems;
            this.currentItem = _menuItems[0];
            this.selectMenuItem(this.currentItem.name);
        }
    }

    /**
     * Will use redirect service to exit designer
     *
     * @param close boolean indication whether closing the page or creating new workflow
     * @param item optional - current item
     * @param params optional - additional parameters to append to exit url
     */
    private exit(closingData: ICloseWFData) {
        this._busyClosing = false;
        if (closingData.item === null) {
            this._redirectService.exit(closingData.categoryId, closingData.close, closingData.params);
        }
        else {
            this.currentItem = <any>closingData.item;
        }
    }

    //Close or New
    private processRedirect(onFlushCompleted?: () => void, onFlushFail?: () => void): void {
        $(document).trigger('DesignerStateChange', ['Busy']);
        const onEventRaisedCallBack = () => {
            try {
                this._processService.flush(onFlushCompleted, onFlushFail, true);
            }
            catch (e) {
                $(document).trigger('DesignerStateChange', ['Error']);
                this._busyClosing = false;
                console.error('processRedirect error: ' + e.toString());
                this.exit({close: true, categoryId: this.fileInfo?.category?.id, item: null, params: null });
            }
        };
        this._processService.requestProcessingCompleteNotification(true, true, onEventRaisedCallBack);
    }

    private initOptionsData() {

        if (!this.showInfoOption) {
            this.selectMenuItem('Options');
        }
    }

    private initFileInfo(componentRef: ComponentRef<TabItemInfoComponent>): void {

        //Get File Information if it was not already fetched:
        if (!this.fileInfo) {
            const processId = this._envService.query.guid || this._envService.query.id;

            if (processId) {
                this._dataService.httpGet('api/process/getfileinfomodelbyprocessid',
                    {
                        processId
                    }, this.onFileInfoFetched.bind(this, componentRef), null, false);
            }
            else {
                if (this._processService) {

                    const that = this;
                    var updateProcessInfoSubscriber = {
                        subscriberId: 'rootIdChangedSubscriberFromFileController',
                        notify(data) {

                            that._dataService.httpGet('api/process/getfileinfomodelbyjsonid', //TODO: Apps: implement provider method
                                {
                                    jsonId: data
                                }, that.onFileInfoFetched.bind(that, componentRef), null, false);
                            that._processService.unRegisterObjectModelServiceEventListner('rootId', updateProcessInfoSubscriber.subscriberId);
                        }
                    };
                    if (!this._processService.rootId) {
                        //the process service is not always available,
                        //create a subscription to the services property we need
                        //so we can execute when the value is available
                        this._processService.registerObjectModelServiceEventListner('rootId', updateProcessInfoSubscriber);
                    }
                    else {
                        //if the value is available
                        updateProcessInfoSubscriber.notify(this._processService.rootId);
                    }
                }
            }
        } else {
            this.loadFileInfoComponent(componentRef);
        }
    }

    private loadFileMenuComponent(menuItem: ISubTabComponent): void {

        switch (menuItem.componentName) {
            case 'TabItemInfoComponent': menuItem.component = TabItemInfoComponent; break;
            case 'TabItemOptionsComponent': menuItem.component = TabItemOptionsComponent; break;
        }

        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(menuItem.component);

        const viewContainerRef = this.tabHost.viewContainerRef;
        if (viewContainerRef) {
            viewContainerRef.clear();
        }

        const componentRef = viewContainerRef.createComponent(componentFactory);
        componentRef.instance.getString = this.getString.bind(this);

        if (componentFactory) {
            if (menuItem.componentName === 'TabItemInfoComponent') {
                this.initFileInfo(componentRef);
                componentRef.instance.isBusyDeploying.subscribe(isBusy => this.isDeployingProcess = isBusy);
                componentRef.instance.fileItemConfig = menuItem.cnf;
                this._busyClosingEventSubscription = componentRef.instance.isBusyClosing.subscribe(closingData => this.exit(closingData));
            }
        }
    }

    private loadFileInfoComponent(componentRef: ComponentRef<TabItemInfoComponent>): void {
        componentRef.instance.fileItemInfo = this.fileInfo;
    }

    private onFileInfoFetched(componentRef: ComponentRef<TabItemInfoComponent>, result: IFileInfoModelResult, error: boolean): void {
        if (!error && result) {
            this.fileInfo = {
                owner: (result.owner) ? result.owner.substring(result.owner.indexOf('\\') + 1) : this.getString('concierge_workflow_no_results'),
                createdDate: (result.createdDate) ?
                    this.datePipe.transform(result.createdDate, 'MM/dd/yyyy HH:mm a') :
                    this.getString('concierge_workflow_no_results'),
                modifiedOwner: (result.modifiedOwner) ?
                    result.modifiedOwner.substring(result.modifiedOwner.indexOf('\\') + 1) :
                    this.getString('concierge_workflow_no_results'),
                modifiedDate: (result.modifiedDate) ?
                    this.datePipe.transform(result.modifiedDate, 'MM/dd/yyyy HH:mm a') :
                    this.getString('concierge_workflow_no_results'),
                category: {
                    name: (result.path) ? sourceCode.k2Designer.utilities.ellipsePath(result.path, 'start') : this.getString('concierge_workflow_no_results'),
                    url: '/designer/#app=AppStudio&action=list&filter=category&catid=' + result.categoryId,
                    id: result.categoryId,
                    path: result.path
                },
                title: result.title,
                clientIdentifier: result.clientIdentifier
            };

            if (result.path && result.path.length) {
                const pieces: string[] = result.path.split('/');

                this._envService.workFlowCategoryName = (pieces[pieces.length - 1]);
            }

            if (componentRef) {
                this.loadFileInfoComponent(componentRef);
            }
        } else {
            console.error('Could not render FileInfo ' + (<any>result).toString());
        }
    }

}
