import { Component, ElementRef, EventEmitter, Inject, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { ClipboardServiceToken } from '../../../services/clipboard/clipboard-service-factory';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { BaseComponent } from '../../base/base.component';

@Component({
    selector: 'k2-input',
    templateUrl: './input.component.html',
    styleUrls: ['./input.component.less']
})
export class InputComponent extends BaseComponent implements OnInit, OnDestroy {
    @Input() public value: any;             // previously textValue
    @Input() public validationTypes: any;
    @Input() public validationModel: any;
    @Input() public validationProperty: string;
    @Input() public parentObject: sourceCode.k2Designer.objectModel.core.model.IModelObject;
    @Input() public showCustomBadge: boolean;
    @Input() public alwaysHideBadge = false;
    @Input() public customBadgeModel: sourceCode.k2Designer.objectModel.core.model.IModelObject;

    @Input() public placeholder: string;
    @Input() public maxLength: number;
    @Input() public isDisabled = false;
    @Input() public shouldIgnoreFocus = false;
    @Input() public isReadOnly = false;

    @Output() public k2Change = new EventEmitter<string>();
    @Output() public k2Focus = new EventEmitter<any>();
    @Output() public k2Blur = new EventEmitter<any>();
    //The two-way binding output for the input 'value'. This needs to be emitted on change for two-way binding to work.
    @Output() public valueChange = new EventEmitter<string>();
    //Event to indicate key pressed
    @Output() public keyPressed = new EventEmitter<KeyboardEvent>();

    @ViewChild('input') inputElement: ElementRef<HTMLInputElement>;

    constructor(@Inject(ValidationServiceToken) validationService: IValidationService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        @Inject(CommandServiceToken) private commandService: ICommandService,
        @Inject(ClipboardServiceToken) private clipboardService: IClipboardService,
        private resourceService: ResourceService) {
        super(validationService);
    }

    ngOnInit(): void {
        if (!this.validationTypes) {
            this.validationTypes = [];
        }
        if (this.validationModel && this.validationProperty) {
            this.validatePropertyOnChange(this.validationModel, this.validationProperty);
        }
        this.setApplicableValidationTypes(this.validationTypes);

        //Default watermark
        this.placeholder = this.placeholder || this.getString('textbox_watermark_type');
    }

    ngOnDestroy(): void {

    }

    public onFocus(ev?: FocusEvent): void {
        if (!this.shouldIgnoreFocus) {
            this.clipboardService.setCurrentContext('input');
            this.commandService.setCurrentContext();
            this.k2Focus.emit(ev);
        }
    }

    public onBlur(ev): void {
        if (!this.shouldIgnoreFocus) {
            this.k2Blur.emit(ev);
        }
    }

    public onEditorDrop(event: DragEvent): void {
        if (event.stopPropagation) event.stopPropagation();
        if (event.preventDefault) event.preventDefault();
    }

    public onModelChanged(val: string): void {
        if (this.undoRedoService.isEnabled()) {
            this.undoRedoService.addUndoMarker('--k2Input: changed text--');
        }

        this.k2Change.emit(val);
        this.valueChange.emit(val);
    }

    /**
     * Focus this textbox
     */
    public doFocus() {
        this.inputElement.nativeElement.focus();
        //cast to any because we only really need the target property of FocusEvent
        this.onFocus({ target: this.inputElement.nativeElement } as any);
    }

    /**
     * key press event handler
     * key press only gets triggered by input that changes text
     *
     * @param event
     */
    onKeyPress(event: KeyboardEvent): boolean {
        this.keyPressed.emit(event);
        return true;
    }

    /**
     * Returns resource string from JSON
     *
     * @param key
     */
    getString = (key: string) => this.resourceService.getString(key);
}
