import { Component, ElementRef, Inject, Input, OnInit } from '@angular/core';
import { IConfiguredExpression } from '../../../../../types/models/rules/i-configured-expression';
import { IConfiguredGroupExpression } from '../../../../../types/models/rules/i-configured-group-expression';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { DragDropService } from '../../../../services/drag-drop/drag-drop.service';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';

@Component({
    selector: 'k2-group-expression',
    templateUrl: './group-expression.component.html',
    styleUrls: ['./group-expression.component.less']
})
export class GroupExpressionComponent extends ExpressionBaseComponent implements OnInit {
    @Input() public expressionModel: IConfiguredGroupExpression;
    @Input() public index: number;
    @Input() public leftItems: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem[];
    @Input() public externalDefinitionParent: sourceCode.k2Designer.objectModel.core.model.IComponent;
    @Input() nested: boolean;
    @Input() alternateColour: boolean;
    @Input() hideSimpleExpressionDragGrip: boolean;
    @Input() hideSimpleExpressionOperatorCommands: boolean;

    @Input() public selectedK2Model: {
        item: any;
        index: number;
    };

    public isSorting: boolean;
    public sortable: boolean;

    constructor(private _dragDropService: DragDropService,
        @Inject(SelectionServiceToken) _selService: ISelectionService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        rulePluginService: RulePluginService,
        public resourceService: ResourceService,
        eventService: EventNotifierService,
        _el: ElementRef) {
        super(_selService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, _el, resourceService);
    }

    ngOnInit(): void {
        super.ngOnInit();
    }

    public expressionSelected(i: number, item: IConfiguredExpression) {
        this.selectedK2Model.index = i;
        this.selectedK2Model.item = item;
    }

    /**
     * Insert expression at the index
     *
     * @param index - destination index for moving expression
     * @param droppedData - expression that is being dragged/dropped
     */
    public insertExpressionAt(index: number, droppedData: any): void {
        const configuredOperator: IConfiguredExpression = this.rulePluginService.createOperator('GroupBooleanOperator');

        //clone of moving expression to apply as destination expression (the original one will be removed eventually)
        const droppedDataClone = droppedData.clone();

        // end result
        //[expression]   //existing item
        //[operator]     <== inserted
        //[expression]   <== inserted (this is the one you are drag/dropping)

        //insert the dropping item to specified index
        this.expressionModel.expressions.splice(index, 0, droppedDataClone);
        //insert the subsequent operator ('AND' 'OR') below the dropping item row
        this.expressionModel.expressions.splice(index, 0, configuredOperator);
    }

    /**
     *
     * @param index - index of where the expression is dropped (i.e. destination)
     * @param dropppedData - expression that is being drag/dropped
     */
    public moveExpression(index: number, droppedData: any): void {
        //need this check to ensure dropped data is an expression and not IF/ELSE block or any other drappable items
        if (!this.rulePluginService.isConfiguredExpression(droppedData)) {
            return;
        }

        this.insertExpressionAt(index, droppedData);

        //remove the originally dragged item because we don't need it anymore. we got a clone that was dropped to the target index
        this.deleteExpression(droppedData);
    }

    /**
     *  called when an expression is dropped onto the very top empty space.
        this function moves the expression to new location (first row)
     */
    public onDropTop(): void {
        if (this._dragDropService.hasItem('ruleDesignerItem')) {

            const droppedData = <any>this._dragDropService.getItem('ruleDesignerItem').getSource();

            if (this.rulePluginService.isExpressionPlugin(droppedData)) {
                //it gets here when dropping an expression from the bottom toolbox area
                const newExpression = this.rulePluginService.createExpression(droppedData.name);
                this.insertExpressionAt(0, newExpression);
            }
            else {
                const index = 0; //we know when an expression is dropped on top space, insertion index is 0
                this.moveExpression(index, droppedData);
            }
        }
    }

    /**
     *  called when an expression is dropped onto the very bottom empty space
        this function moves the expression to new location (last row)
     */
    public onDropBottom(): void {
        if (this._dragDropService.hasItem('ruleDesignerItem')) {
            const droppedData: any = <any>this._dragDropService.getItem('ruleDesignerItem').getSource();

            const index = this.expressionModel.expressions.length; //this gets the destination index of target block (group)

            if (this.rulePluginService.isExpressionPlugin(droppedData)) {
                //it gets here when dropping an expression from the bottom toolbox area
                const newExpression = this.rulePluginService.createExpression(droppedData.name);
                this.insertExpressionAt(index, newExpression);
            } else {
                this.moveExpression(index, droppedData);
            }
        }
    }

    public onDrop(index?: number): void {
        // when an expression is dropped on empty space there is no index and we don't deal with it.
        if (!index) {
            return;
        }

        if (this._dragDropService.hasItem('ruleDesignerItem')) {

            const droppedData: any = <any>this._dragDropService.getItem('ruleDesignerItem').getSource();

            if (this.rulePluginService.isExpressionPlugin(droppedData)) {
                //it gets here when dropping an expression from the bottom toolbox area

                //create a new expression and insert
                const newExpression = this.rulePluginService.createExpression(droppedData.name);
                this.insertExpressionAt(index, newExpression);
            } else {
                //it gets here when an existing expression is dragged/dropped onto an operator area between expressions
                this.moveExpression(index, droppedData);
            }
        }
    }

    /**
     * This function gets called when the Drop event of a k2TaskExpression or k2SimpleExpression fires (something is dropped on an expression).
     * It determines if dropped expression should form a group with dropped-on expression or be added to existing group.
     *
     * @param index - index of the dropped-on expression
     */
    public droppedGroupConditions(index: number): void {
        if (!this._dragDropService.hasItem('ruleDesignerItem')) {
            return; //Non-RuleDesigner item was dropped, invalid drop.
        }

        const dragDropServiceData: any = this._dragDropService.getItem('ruleDesignerItem').getSource();
        let droppedData = <IConfiguredExpression>{};
        let isCanvasItem = false;

        if (this.rulePluginService.isConfiguredExpression(dragDropServiceData)) {
            //Expression that was dropped, was dragged from somewhere else on RuleDesigner canvas
            isCanvasItem = true;
            droppedData = dragDropServiceData;
        } else if (this.rulePluginService.isExpressionPlugin(dragDropServiceData)) {
            //Expression that was dropped, was dragged from toolbar and did not exist on canvas before (new).
            droppedData = this.rulePluginService.createExpression(dragDropServiceData.name);
        } else {
            return; //Invalid item dropped, do nothing.
        }

        if (isCanvasItem && this.isDroppedOnItself(droppedData, index)) return;

        //Add dropped expression to the group
        const shouldDeleteOriginal = this.addExpressionToGroup(droppedData, index, isCanvasItem);

        //Remove dropped expression from where it was
        if (shouldDeleteOriginal) {
            this.deleteExpression(droppedData);
        }
    }

    /**
     * Returns true if index that expression was dropped on was the same as index of expression it was dropped on
     *
     * @param expression expression dropped
     * @param index index of dropped-on expression
     */
    private isDroppedOnItself(expression: IConfiguredExpression,
        index: number): boolean {
        const droppedOnExpression = this.expressionModel.expressions[index];
        return droppedOnExpression.getReference() === expression.getReference();
    }

    /**
     * Moving existing expression and dropping on another expression to form a group
     *
     * @param expression
     * @param index
     * @param isCanvasItem
     */
    private addExpressionToGroup(expression: IConfiguredExpression,
        index: number, isCanvasItem: boolean = true): boolean {

        const group = this.getExpressionParentGroup();
        let shouldDeleteOriginal = isCanvasItem;

        //Always create a new group if expression was dragged from toolbar (not from canvas)
        //Always create a new group if expression was dropped on an expression from another group/level.
        //If expression was dropped on an expression from within the same group/level,
        //only create a new group if there will be enough expressions left in this group (current group should contain more than 2 expressions or 1 group)
        const shouldCreateNewGroup = !isCanvasItem ||
            (!this.isDroppedWithinSameGroup(expression) ||
                (this.isDroppedWithinSameGroup(expression) &&
                    (this.getConfiguredExpressionsInGroup(group).length > 2 || this.getGroupsInGroup(group).length > 0)));

        if (shouldCreateNewGroup) {
            //Clone expression so it doesn't trigger unexpected events:
            const expressionClone = expression.clone<IConfiguredExpression>();
            //Reset clone internal id to ensure it is assigned a unique id when added to group:
            expressionClone.internalId = null;
            //Dropped expression will always be joined to dropped-on expression by a default (AND) operator
            const newOperator = this.rulePluginService.createOperator('GroupBooleanOperator');

            //Expression that was dropped on
            const droppedOnExpression = this.expressionModel.expressions[index];
            //Clone expression so it doesn't trigger unexpected events:
            const droppedOnExpressionClone = droppedOnExpression.clone<IConfiguredExpression>();
            droppedOnExpressionClone.internalId = null;

            //Add the new group to the parent group at the same position the dropped-on expression was
            const newGroup = this.createNewExpressionGroup();
            group.expressions.splice(index + 1, 0, newGroup as any);

            //Populate the new group
            newGroup.expressions.push(droppedOnExpressionClone);
            newGroup.expressions.push(newOperator);
            newGroup.expressions.push(expressionClone);

            //Remove original dropped-on expression, because it is now cloned into a new group
            this.deleteExpression(droppedOnExpression);

        } else {
            shouldDeleteOriginal = false;
        }

        return shouldDeleteOriginal;
    }

    /**
     * Returns the Group in which expression is contained (IConfiguredComplexLogicalGroupExpression)
     *
     * @param expression
     */
    protected /*override*/ getExpressionParentGroup(expression?: IConfiguredExpression): IConfiguredGroupExpression {
        if (!expression) {
            expression = this.expressionModel.expressions as any;
        }
        return super.getExpressionParentGroup(expression) as any;
    }

    /**
     * Returns all configured expressions in the group (will exclude other groups contained in group)
     *
     * @param group
     */
    private getConfiguredExpressionsInGroup(group: IConfiguredGroupExpression):
        IConfiguredExpression[] {
        const expressions = group.expressions.filter((item) => item.componentId === sourceCode.k2Designer.objectModel.core.deployment.LookupTypes.ConfiguredSimpleExpression);
        return expressions;
    }

    /**
     * Returns all groups contained in the group (will exclude other expressions in group)
     *
     * @param group
     */
    private getGroupsInGroup(group: IConfiguredGroupExpression):
        IConfiguredExpression[] {
        const groups =
            group.expressions.filter((item) => this.isGroupType(item));
        return groups;
    }

    /**
     * Returns true if dropped expression's group is the same as dropped-on expression's group
     *
     * @param expression
     */
    private isDroppedWithinSameGroup(expression: IConfiguredExpression): boolean {
        const droppedOnGroup = this.getExpressionParentGroup(this.expressionModel.expressions as any);
        const droppedFromGroup = this.getExpressionParentGroup(expression);
        return droppedOnGroup.getReference() === droppedFromGroup.getReference();
    }

    private createNewExpressionGroup(): sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression {
        const newGroup = <sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>this.rulePluginService.createExpression('GroupExpression');
        return newGroup;
    }

    private groupConditions(index: number): void {
        const operator: IConfiguredExpression = this.expressionModel.expressions[index];
        const aboveExpression: IConfiguredExpression = this.expressionModel.expressions[index - 1];
        const belowExpression: IConfiguredExpression = this.expressionModel.expressions[index + 1];
        const clonedAboveExpression: IConfiguredExpression = aboveExpression.clone<IConfiguredExpression>();
        const clonedOperator: IConfiguredExpression = operator.clone<IConfiguredExpression>();
        const clonedBelowExpression = belowExpression.clone<IConfiguredExpression>();

        let containerSmallerThanThreeItems = false;

        //This check is used to prevent empty groups being created
        if (this.expressionModel.expressions.length <= 3) {
            containerSmallerThanThreeItems = true;
        }

        //Create a group (Array) and replace the current item with this array, pushing the items into the new array
        if (!containerSmallerThanThreeItems) {

            const newValue = this.createNewExpressionGroup() as any;
            this.expressionModel.expressions.splice(index, 1, newValue);

            aboveExpression.removeFromModelArray();
            belowExpression.removeFromModelArray();
            operator.removeFromModelArray();

            (<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>newValue).expressions.push(clonedAboveExpression);
            (<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>newValue).expressions.push(clonedOperator);
            (<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>newValue).expressions.push(clonedBelowExpression);
        }
    }

    protected /*override*/ getDragImage(): HTMLElement {
        const result: HTMLDivElement = document.createElement('div');
        result.innerHTML = this.rootElement.nativeElement.innerHTML.toString();
        return result;
    }

}
