import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockToolBoxService } from '../../../testing/mock-services/tool-box-service';
import { IToolBoxService } from '../../../types/services/tool-box/i-toolbox-service';

const toolBoxServiceFactory = () => environment.devHarness === true
    ? new MockToolBoxService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.ToolboxService);

export const ToolBoxServiceToken = new InjectionToken<IToolBoxService>('IToolBoxService');

export const ToolBoxServiceFactory = {
    provide: ToolBoxServiceToken,
    useFactory: toolBoxServiceFactory,
    deps: [

    ]
};
