import { Component, ElementRef, Input, OnInit } from '@angular/core';

@Component({
    selector: 'k2-bread-crumb-item',
    templateUrl: './bread-crumb-item.component.html',
    styleUrls: ['./bread-crumb-item.component.less']
})
export class BreadCrumbItemComponent implements OnInit {
    @Input() isDropDownItem: boolean;
    @Input() index: number;
    @Input() crumb: IBreadCrumbItem;

    constructor(private el: ElementRef<HTMLElement>) {
    }

    ngOnInit(): void {
        if (this.crumb) {
            this.crumb.isOverflowed = this.isOverFlowed.bind(this);
        }
    }

    public isOverFlowed(): boolean {
        if (!this.crumb.isCrumbItem) {
            return false;
        }
        const crumbItem = this.el.nativeElement;
        const container = crumbItem.parentElement;
        const childHeight = $(crumbItem.children).height();
        return (container.scrollHeight !== 0) && container.scrollHeight > childHeight;
    }
}
