import { Component, Input, OnInit } from '@angular/core';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { isUndefinedOrNull } from 'src/app/shared/utils/general';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IEventPlatformRemovePluginContext } from 'src/types/uiComponents/pluginWizards/IEventPlatformRemovePluginContext';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { PopupService } from '../../../../services/popup/popup.service';
import { ProcessService } from '../../../../services/process/process.service';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-event-platform-result',
    templateUrl: './event-platform-result.component.html',
    styleUrls: ['./event-platform-result.component.less'],
    providers: [{provide: PageComponent, useExisting: EventPlatformResultComponent}]
})
export class EventPlatformResultComponent extends PageComponent implements OnInit {
    buttonConfig: IPopupButtonsConfig = {
        finish: {
            buttonText: this.getString('workflow_contextPopup_okButton')
        }
    };
    @Input() public config: IPopupConfiguration;

    public context: IEventPlatformRemovePluginContext;
    public resources: any;

    constructor(protected processService: ProcessService,
        resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.context = this.config.context as IEventPlatformRemovePluginContext;
        this.resources = this.context.resourceObject;
    }

    beforePageDisplayed() {
        this.removeWorkflowIntegration();
    }

    beforeNavigateNext() {}
    beforeNavigatePrevious() {}

    /**
     * Remove workflow integration from form
     * */
    private removeWorkflowIntegration(): void {

        this.context.inProgress = true;

        this.notifyPageStateChanged(PopupState.Busy);

        const plugins = this.processService.definitionPlugins;
        for (let i = 0; i < plugins.length; i++) {
            const subscriptions = this.processService.root.configuration.eventPlatformConfiguration.subscriptions;

            let processed = false;

            for (let j = 0; j < subscriptions.length; j++) {

                const subscription = subscriptions[j];
                if (this.context.selectedSubscriptionId === subscription.identifier) {

                    subscription.mapping.data.fields.splice(0, subscription.mapping.data.fields.length);
                    subscription.state = sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.State.Deleted;
                    processed = true;
                    break;
                }
            }

            if (processed) {
                break;
            }
        }

        if (isUndefinedOrNull(this.processService.root.configuration.eventPlatformConfiguration.subscriptions) ||
            this.processService.root.configuration.eventPlatformConfiguration.subscriptions.length === 0) {
            this.processService.root.configuration.eventPlatformConfiguration = null;
        }

        this.removeWorkflowIntegrationCompleted(null, null);
    }

    /**
     * Callback method when form is updated
     */
    private removeWorkflowIntegrationCompleted(result: string, error: boolean): void {

        // Stop loading animation if it failed or not.
        this.notifyPageStateChanged(PopupState.Normal);

        if (!error) {
            this.context.success = true;
        }
        else {
            const exception = JSON.parse(result);

            // Use the Inner Exception Message first, then fall back on Exception Message, else Message
            if (exception.InnerException) {
                this.context.errorMessage = exception.InnerException.ExceptionMessage || exception.InnerException.Message;
            }
            else {
                this.context.errorMessage = exception.ExceptionMessage || exception.Message;
            }

            this.context.success = false;

            this.notifyPageStateChanged(PopupState.Error);
        }
        this.context.inProgress = false;
    }

}
