import { Component, ElementRef, Inject, OnDestroy, OnInit } from '@angular/core';
import { DropDownComponent } from 'src/app/components/common/drop-down/drop-down.component';
import { IConfiguredOutcomeStatement } from '../../../../../types/models/rules/i-configured-outcome-statement';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { IValidationService } from '../../../../../types/services/validation/ivalidation.service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../services/validation/validation-service-factory';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { OutcomeItemNames } from '../../../../shared/enums/rules/outcome-item-names';
import { OutcomeType } from '../../../../shared/enums/rules/outcome-type';
import { RuleContextHandlerNames } from '../../../../shared/enums/rules/rule-context-handler-names';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { isUndefinedOrNull } from '../../../../shared/utils/general';
import { StatementBaseComponent } from '../../base/statement-base/statement-base.component';

@Component({
    selector: 'k2-outcome-statement',
    templateUrl: './outcome-statement.component.html',
    styleUrls: ['./outcome-statement.component.less']
})
export class OutcomeStatementComponent extends StatementBaseComponent implements OnInit, OnDestroy {
    public /*override*/ statementModel: IConfiguredOutcomeStatement;

    public dropDownHover: boolean;
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplates: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;
    public getDragItemFn;
    public getDragImageFn;
    public outcomeType: OutcomeType;
    public multipleOutcome: boolean;
    public dropdownButtonSelected: boolean;
    public outcomeArray: IDropDownMenuItem[];

    private _rootElement: HTMLElement;
    private _isVotingCustomRule: boolean;
    private _isSelecting: boolean;
    private _currentActivityConfig: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration;
    private _outcomesChangedRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover;

    constructor(@Inject(SelectionServiceToken) _selService: ISelectionService,
        private _rulePluginSvc: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoSvc: IUndoRedoService,
        private _resourceService: ResourceService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        eventService: EventNotifierService,
        private _el: ElementRef) {

        super(_selService, _rulePluginSvc, commandService, configPanelService,
            undoRedoSvc, eventService, _el);

        this._rootElement = _el.nativeElement;
    }

    ngOnInit(): void {
        super.ngOnInit();

        this.undoRedoService.forceDisable();

        this.dropDownHover = false;
        this.getDragImageFn = super.getDragImage.bind(this);

        this._isVotingCustomRule = false;

        this.outcomeArray = [];

        switch (this._rulePluginSvc.contextHandler.name) {
            case RuleContextHandlerNames.DecisionRule:
                this.outcomeType = OutcomeType.Decision;
                this.multipleOutcome = false;
                break;
            case RuleContextHandlerNames.VotingCustomRule:
                this.outcomeType = OutcomeType.Voting;
                this._isVotingCustomRule = true;
                this.multipleOutcome = false;
                break;
            case RuleContextHandlerNames.MergeRule:
                this.outcomeType = OutcomeType.Merge;
                this.multipleOutcome = false;
                break;
            case RuleContextHandlerNames.SplitRule:
                this.outcomeType = OutcomeType.Split;
                this.multipleOutcome = true;
                break;
            case RuleContextHandlerNames.StartRule:
                this.outcomeType = OutcomeType.Start;
                this.multipleOutcome = false;
                break;
        }

        this.dropdownButtonSelected = false;

        this.loadOutcomeConfiguration();

        this.setupOutcomes();

        this._rulePluginSvc.executeDroppedCallbacks();

        this.listenToEvents();

        if (this.statementModel.createdFromIfDrop !== true && this.statementModel.isSelectable) {
            this._rootElement.focus();
        }

        this.undoRedoService.resetForceDisable();
    }

    ngOnChanges(changes): void {
        if (changes.statementModel && changes.statementModel.selectedItems) {
            this.onSelectedItemsChanged();
        }
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        if (this._outcomesChangedRemover) {
            this._outcomesChangedRemover();
        }
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    private setupMergeRuleOutcomes(): void {
        const mergeStatement: sourceCode.k2Designer.objectModel.workflow.configuredRules.IConfiguredMergeStatement = this.statementModel as any;
        const isMergeAction: boolean = isUndefinedOrNull(mergeStatement.isMergeActionResult) ? false : mergeStatement.isMergeActionResult;

        this.outcomeArray.push({
            title: this._resourceService.getString('ruledesigner_outcome_expression_merge'),
            key: OutcomeItemNames.Merge,
            isSelected: isMergeAction === true,
            additionalValueProperty: {
                displayIcon: 'ic-outcomemerge'
            },
        });
        this.outcomeArray.push(DropDownComponent.separator);
        this.outcomeArray.push({
            title: this._resourceService.getString('ruledesigner_outcome_expression_wait'),
            key: 'wait',
            isSelected: isMergeAction === false,
            additionalValueProperty: {
                displayIcon: 'ic-outcomewait'
            },
        });
    }

    // Sometimes when changing contexts and using undo redo, the selectedItems are cleared. This will repopulate
    private onSelectedItemsChanged() {
        if (!this._isSelecting) {
            // if it is not selecting, and it changed, we can assume stuff's out of sync.
            // no need to compare values etc.

            // selectedItems is the source of truth, as that is what is updated by undo redo etc
            for (let i = 0; i < this.outcomeArray.length; i++) {
                let isSelected = false;
                for (let j = 0; j < this.statementModel.selectedItems.length; j++) {
                    if (this.statementModel.selectedItems[j].key === this.outcomeArray[i].key) {
                        isSelected = true;
                        break;
                    }
                }

                this.outcomeArray[i].isSelected = isSelected;
            }
        }
    }

    private loadOutcomeConfiguration() {
        if (this._rulePluginSvc.contextModel) {
            const actConfiguration = this._rulePluginSvc.contextModel.getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration>();
            if (actConfiguration.instanceOf(sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.ClientEventConfiguration.implementor)) {
                this._currentActivityConfig = actConfiguration.getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>()
                    .getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>()
                    .getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>()
                    .configuration;
            }
            else {
                this._currentActivityConfig = actConfiguration;
            }
        }
    }

    private setupOutcomes(): void {
        this.outcomeArray.length = 0;

        if (this.outcomeType === OutcomeType.Merge) {
            this.setupMergeRuleOutcomes();
        }
        else {
            const actCnf: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration = this._currentActivityConfig;

            if (actCnf && actCnf.outcomes) {
                for (var i = 0; i < actCnf.outcomes.length; i++) {
                    let isSelected = false;
                    isSelected = this.statementModel.linkedOutcomeReferences.filter(function(item) {
                        return item === actCnf.outcomes[i].getReference();
                    }).length > 0;
                    if (isSelected) {
                        this.statementModel.waitForResponse = false;
                    }

                    let dispIcon: string;

                    switch (this.outcomeType) {
                        case OutcomeType.Decision:
                        //Split uses the same icon as decision
                        case OutcomeType.Split:
                            dispIcon = 'ic-outcomedecision';
                            break;
                        case OutcomeType.Start:
                            dispIcon = i == 0 ? 'ic-startprocess' : 'ic-dontstartprocess';
                            break;
                        case OutcomeType.Voting:
                        default:
                            dispIcon = 'ic-outcomevoting';
                            break;
                    }

                    this.outcomeArray.push(<any>{
                        title: (<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IOutcomeExtended>actCnf.outcomes[i]).inferredTitle,
                        key: actCnf.outcomes[i].getReference(),
                        isSelected,
                        titleBinding: {
                            objectReference: actCnf.outcomes[i],
                            propertyName: 'inferredTitle'
                        }, additionalValueProperty: {
                            displayIcon: dispIcon
                        }
                    });
                }
            }

            if (this._isVotingCustomRule) {
                this.outcomeArray.push({
                    title: this._resourceService.getString('ruledesigner_outcome_expression_waitForResponses'),
                    key: 'wait_for_responses',
                    isSelected: this.statementModel.waitForResponse || false,
                    titleBinding: {
                        objectReference: {
                            inferredTitle: this._resourceService.getString('ruledesigner_outcome_expression_waitForResponses')
                        },
                        propertyName: 'inferredTitle'
                    },
                    additionalValueProperty: {
                        displayIcon: 'ic-outcomewait'
                    }
                });
            }
        }

        this.refreshSelectedItems();
    }

    private listenToEvents(): void {
        const actCnf: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivityConfiguration = this._currentActivityConfig;
        if (actCnf && actCnf.outcomes) {
            this._outcomesChangedRemover = actCnf.outcomes.listen(sourceCode.k2Designer.objectModel.core.model.EventTypes.anythingChanged, this.onOutcomesChanged.bind(this));
        }
    }

    private onOutcomesChanged(): boolean {
        this.setupOutcomes();
        return true;
    }

    public onMenuClose(): void {
        this.dropdownButtonSelected = false;
    }

    public setDropDownActive(): void {
        this.dropdownButtonSelected = true;
    }

    public dropDownActive(): boolean {
        return this.dropdownButtonSelected;
    }

    /***
     * This is used to populate the current displayed item.
     * */
    public getSelectedItems(): sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredStatement[] {
        return this.statementModel.selectedItems;
    }

    public onSelected(item: IDropDownMenuItem): void {
        this._isSelecting = true;
        let itemAdded = false;

        if (item.key === OutcomeItemNames.WaitForResponse) {
            const totalOutcomes = this.statementModel.linkedOutcomeReferences.length;
            this.statementModel.linkedOutcomeReferences.splice(0, totalOutcomes);
            this.statementModel.waitForResponse = item.isSelected;

            //Deselected every dropdown Items except WaitingForResponse
            const selectedOutcomesDropdownItems =
                this.outcomeArray.filter(function(items) {
                    return (items.isSelected === true && items.key !== OutcomeItemNames.WaitForResponse);
                });

            for (let i = 0; i < selectedOutcomesDropdownItems.length; i++) {
                selectedOutcomesDropdownItems[i].isSelected = false;
            }
        }
        else if (this.outcomeType === OutcomeType.Merge) {
            this.statementModel.isMergeActionResult = (item.key.toLowerCase() === OutcomeItemNames.Merge);
        }
        else {
            if (item.isSelected === true) {
                if (!this.multipleOutcome) {
                    this.statementModel.linkedOutcomeReferences.splice(0, this.statementModel.linkedOutcomeReferences.length); //ensure only one item can be selected

                    for (let i = 0; i < this.outcomeArray.length; i++) {
                        this.outcomeArray[i].isSelected = this.outcomeArray[i] === item; //not selected item = false.
                    }
                }

                itemAdded = !!this.statementModel.linkedOutcomeReferences.find(r => r === item.key);

                // ensure item is added
                if (!itemAdded) {
                    this.statementModel.waitForResponse = false;
                    this.statementModel.linkedOutcomeReferences.push(item.key);
                }
            } else {//if not selected, remove item from linkedOutcomeReferences
                const indexToRemove: number = this.statementModel.linkedOutcomeReferences.indexOf(item.key);
                if (indexToRemove > -1)
                {this.statementModel.linkedOutcomeReferences.splice(indexToRemove, 1);}
            }

            //Uncheck Wait For response item
            const waitForResponseDropdownItem: IDropDownMenuItem = this.outcomeArray.find(i => i.key === OutcomeItemNames.WaitForResponse);
            if (waitForResponseDropdownItem) {
                waitForResponseDropdownItem.isSelected = false;
            }
        }

        this.refreshSelectedItems();

        this.setFocus();
        this._isSelecting = false;
    }

    private refreshSelectedItems(): void {
        this.statementModel.selectedItems.splice(0, this.statementModel.selectedItems.length);

        for (let i = 0; i < this.outcomeArray.length; i++) {
            if (this.outcomeArray[i].isSelected) {
                this.statementModel.selectedItems.push({
                    title: this.outcomeArray[i].title,
                    icon: this.outcomeArray[i].additionalValueProperty.displayIcon,
                    key: this.outcomeArray[i].key
                });
            }
        }
    }

    // Sets the focus (for unit-testability)
    private setFocus(): void {
        this._rootElement.focus();
    }

    public getOutcomeItems(): IDropDownMenuItem[] {
        return this.outcomeArray;
    }

    public addDraggingState() {
        this.states.add(RuleControlStates.dragging);
    }

    public removeDraggingState() {
        this.states.remove(RuleControlStates.dragging);
    }

    public changeDropDownHover() {
        this.dropDownHover = !this.dropDownHover;
    }
}
