import { Component, ElementRef, EventEmitter, Inject, Input, OnChanges, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { ILegacyResourceService } from '../../../types/services/resources/i-legacy-resource.service';
import { LegacyResourceServiceToken } from '../../legacy-service-factories/resource-service-factory';
import { defaultDebounceTime } from '../../shared/constants/general';
import { isUndefinedOrNull } from '../../shared/utils/general';

@Component({
    selector: 'k2-bread-crumb',
    templateUrl: './bread-crumb.component.html',
    styleUrls: ['./bread-crumb.component.less']
})
export class BreadCrumbComponent implements OnInit, OnChanges, OnDestroy {
    @Input() public searchText: string;
    @Input() public hideSearch = false;
    @Input() public placeholderText: string;
    @Input() public crumbItems: IBreadCrumbItem[];
    @Input() public selectedItem: IBreadCrumbItem;
    @Input() public rootText: string;
    @Input() public rootIcon = 'icon icon-set-toolbox icon-size16 ic-category';
    @Input() public searchInputDelay: number;
    @Input() public autofocusOnInput: boolean;
    @Output() public onCrumbItemSelected = new EventEmitter<IBreadCrumbItemEvent>();
    @Output() public searchTextChanged = new EventEmitter<string>();

    public dropDownCrumbs: IBreadCrumbItem[] = [];
    public visibleCrumbs: IBreadCrumbItem[] = [];

    private _window = window;
    public isSearchShowing = true;
    private _placeholderText: string;
    private _isSmartObjectBrowser: boolean;
    private searchTextSubject = new Subject<string>();
    private searchTextSub: Subscription;

    constructor(@Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        private el: ElementRef) {
        this.searchTextSub = this.searchTextSubject.pipe(
            debounceTime(1000),
            distinctUntilChanged()
        ).subscribe(((value: string) => {
            this.searchTextChanged.emit(value);
        }));
    }

    ngOnInit() {
        this.visibleCrumbs = [];
        this.dropDownCrumbs = [];
        if (this.rootText === '') this.rootText = this._resourceService.getString('breadcrumb_default_root_text');
        if (this._placeholderText === '') this._placeholderText = this._resourceService.getString('breadcrumb_items_placeholder_text');
        if (this.crumbItems === undefined || this.crumbItems === null) this.crumbItems = [];
        if (this.crumbItems.length > 0) this.refreshCrumbs(this.crumbItems, []);

        $(this.el.nativeElement).find('.bc-child-el').bind('leavedone', (): void => {
            this.recalculateMaxWidth(this.visibleCrumbs.length);
        });

        $(this._window).on('resize', $.proxy(this.recalculateMaxHeight, this));

        if (this.autofocusOnInput) this.focusInput();
    }

    ngOnDestroy() {
        this.searchTextSub.unsubscribe();
    }

    ngOnChanges(changes) {
        if (changes.crumbItems) {
            this.refreshCrumbs(changes.crumbItems.currentValue, changes.crumbItems.previousValue);
        }

        if (changes.searchText) {
            this.searchTextChanged.emit(this.searchText);
        }
    }

    get isSmartObjectBrowser(): boolean {
        return this._isSmartObjectBrowser;
    }

    set isSmartObjectBrowser(value: boolean) {
        this._isSmartObjectBrowser = value;
    }

    public onSearchTextChanged(value): void {
        this.searchTextSubject.next(value);
    }

    //if there is space left for last item to expand => recalculates it's maximum width
    private recalculateMaxWidth(index: number): void {
        let ret = '';

        if (index > 0) {

            const breadCrumbsElements = $(this.el.nativeElement).find('.bc-child-el').find('.bc-content');

            if ($(breadCrumbsElements[breadCrumbsElements.length]).parent().attr('maxwidthmodified') === '1') {
                return;
            }

            const maxWidth = $(this.el.nativeElement).find('.bc-child-el').prop('offsetWidth');

            let totalChildrenLength = 0; // [ET] - we need to include 12px for each node, not just once off
            for (var i = 0; i < breadCrumbsElements.length; i++) {
                totalChildrenLength += breadCrumbsElements[i].offsetWidth + 15; // [ET] - 15px should be counted on each node (12 + 3), where 12px is the arrow infront
            }

            if (maxWidth > totalChildrenLength) {
                const dif = maxWidth - totalChildrenLength - 27;
                if (dif > 0) {
                    ret = 'calc(50% + ' + dif + 'px)';
                } else {
                    ret = 'calc(50% - ' + Math.abs(dif) + 'px)';
                }

                $(breadCrumbsElements[i - 1]).parent().css('max-width', ret).attr('maxwidthmodified', '1');
            }

        }
    }

    public onMouseDown(evt: MouseEvent): void {
        document.removeEventListener('mousedown', this.onMouseDown.bind(this), true);
        if ($(evt.target).closest('.breadcrumb-node').length === 0) {
            $(this.el.nativeElement).find('.breadcrumb-node').hide();
        }
    }

    public toggleNode(): void {
        $(this.el.nativeElement).find('.breadcrumb-node').toggle();

        if ($(this.el.nativeElement).find('.breadcrumb-node').is(':visible')) {
            document.addEventListener('mousedown', this.onMouseDown.bind(this), true);
            this.recalculateMaxHeight();
        }
    }

    public setIconSetName(): string {
        return (this.searchText === undefined || this.searchText.length === 0) ? 'ribbon' : 'steps';
    }

    public setIconName(): string {
        return (this.searchText === undefined || this.searchText.length === 0) ? 'search' : 'headerclose';
    }

    private recalculateMaxHeight(): void {
        const menu = $(this.el.nativeElement).find(`ul[role='menu']`);
        const scrollContainer = $(this.el.nativeElement).find('.breadcrumb-scroll-container');

        if (menu.length > 0 && scrollContainer.length > 0) {
            scrollContainer.height(menu.outerHeight());
            scrollContainer.css('max-height', window.innerHeight - menu.offset().top);
        }
    }

    private refreshCrumbs(newItems: IBreadCrumbItem[], oldItems: IBreadCrumbItem[]): void {
        const oldLength = (oldItems ? oldItems.length : 0);
        this.isSearchShowing = !(newItems.length > 0);
        if (!newItems || newItems.length === 0) { // clicked on root
            this.visibleCrumbs = [];
            this.dropDownCrumbs = [];
            return;
        }
        if (newItems.length === oldLength) // clicked on current crumb
        {
            return;
        }
        if (newItems.length < oldLength) { // clicked on a parent crumb
            this.visibleCrumbs = [];
            this.dropDownCrumbs = [];
            this.addVisible(newItems.slice(0), true);
        }
        else if (newItems && newItems.length > 0) { // new item
            const index = newItems.length - 1;
            this.addVisible([newItems[index]], true); // Angular will not trigger addcrumb for items already in the list
        }
    }

    private addVisible(items: IBreadCrumbItem[], addToEnd: boolean) {
        if (items?.length === 0) {
            return;
        }
        const item = items.pop();
        item.index = this.crumbItems.indexOf(item);
        item.isCrumbItem = true;
        item.icon = null;
        item.supressIcon = true;
        if (!this._isSmartObjectBrowser) {
            if (item.title === 'SmartObjects') {
                item.icon = 'icon icon-set-steps icon-size16 ic-executesmartobject';
                item.supressIcon = false;
            }
        }
        if (addToEnd) {
            this.visibleCrumbs.push(item);
        } else {
            this.visibleCrumbs.unshift(item);
        }
        setTimeout(() => {
            if (item.isOverflowed()) {
                this.unshiftOverflow(item);
            } else {
                this.addVisible(items, false);
            }
        }, 0);
    }

    private unshiftOverflow(newCrumb: IBreadCrumbItem) {
        const crumb = this.visibleCrumbs.shift();
        crumb.isCrumbItem = false;
        if (crumb.title !== 'SmartObjects') {
            crumb.icon = 'icon icon-set-toolbox icon-size16 ic-category';
        }
        this.dropDownCrumbs.unshift(crumb);
        setTimeout(() => { // Check if the element is still overflowed after an angular cycle
            let isOverflow: boolean;
            if (this.visibleCrumbs.length > 0) {
                isOverflow = this.visibleCrumbs[0].isOverflowed();
            } else {
                return;
            }

            if (isOverflow) {
                this.unshiftOverflow(newCrumb);
            } else {
                const remainder = this.crumbItems.filter(value => !this.dropDownCrumbs.includes(value) && !this.visibleCrumbs.includes(value));
                for (const item of remainder) {
                    this.dropDownCrumbs.unshift(item);
                }
            }
        }, 0);
    }

    onBlur(forced: boolean): void {
        if (this.searchText) {
            if (forced) {
                if (this.searchText.length === 0 && this.crumbItems.length > 0) {
                    this.clearSearch();
                }
                else {
                    this.showSearch();
                }
            }
        }
    }

    private showSearch(): void {
        this.searchText = '';
        this.isSearchShowing = true;
        this.focusInput(defaultDebounceTime);
    }

    clearSearchAndFocusInput() {
        this.clearSearch();
        this.focusInput();
        this.searchTextChanged.emit(this.searchText);
    }

    private clearSearch(): void {
        this.searchText = '';
        this.isSearchShowing = this.crumbItems.length > 0 ? false : true;
    }

    initiateSearch(): void {
        if (!isUndefinedOrNull(this.searchText)) {
            if (this.searchText.length > 0) {
                this.clearSearch();
            }
            else {
                this.showSearch();
            }
        }
        else {
            this.showSearch();
        }

        this.focusInput();
    }

    crmbItmClicked(itm: IBreadCrumbItem) {
        this.updateBreadCrumb(itm);

    }

    private updateBreadCrumb(item: IBreadCrumbItem) {
        if (item === null) {
            this.crumbItems = [];
        } else {
            this.crumbItems = this.crumbItems.slice(0, item.index + 1);
        }
        this.onCrumbItemSelected.emit({ item, newCrumbs: this.crumbItems });
    }

    ddItmClicked(itm: IBreadCrumbItem) {
        this.updateBreadCrumb(itm);
    }

    private focusInput(delay: number = 0) {
        setTimeout((): void => {
            const inputBox = $(this.el.nativeElement).find(`input[type='text']`)[0];
            $(inputBox).focus();
        }, delay);
    }
}
