import { Component, ElementRef, HostListener, Inject, OnInit } from '@angular/core';
import { CommandServiceToken } from 'src/app/services/command/command-service-factory';
import { ConfigPanelServiceToken } from 'src/app/services/config-panel/config-panel-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { RulePluginService } from 'src/app/services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from 'src/app/services/selection/selection-service-factory';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { StatementDirectives } from 'src/app/shared/enums/rules/statement-directives';
import { isUndefinedOrNull } from 'src/app/shared/utils/general';
import { IConfiguredComplexLogicalGroupExpression } from 'src/types/models/rules/i-configured-complex-logical-group-expression';
import { IConfiguredExpression } from 'src/types/models/rules/i-configured-expression';
import { IConfiguredIfThenElseStatement } from 'src/types/models/rules/i-configured-if-then-else-statement';
import { IConfiguredSimpleExpression } from 'src/types/models/rules/i-configured-simple-expression';
import { IConfiguredStatement } from 'src/types/models/rules/i-configured-statement';
import { IItemConfiguration } from 'src/types/models/rules/i-item-configuration';
import { IQITConfiguration } from 'src/types/models/rules/i-qit-configuration';
import { IStatementConfiguration } from 'src/types/models/rules/i-statement-configuration';
import { IConfigPanelService } from 'src/types/services/config-panel/i-config-panel-service';
import { ISelectionService } from 'src/types/services/selection/i-selection-service';
import { DragDropService } from '../../../../services/drag-drop/drag-drop.service';
import { StatementBaseComponent } from '../../base/statement-base/statement-base.component';

@Component({
    selector: 'k2-if-then-else-statement',
    templateUrl: './if-then-else-statement.component.html',
    styleUrls: ['./if-then-else-statement.component.less']
})
export class IfThenElseStatementComponent extends StatementBaseComponent implements OnInit {
    public ifBlockHover = false;
    public thenBlockHover = false;
    public elseBlockHover = false;

    public qitConfiguration: IQITConfiguration;
    public hoverIfQITLeft = false;
    public hoverIfQITRight = false;
    public hoverThenQITLeft = false;
    public hoverThenQITRight = false;
    public hoverElseQITLeft = false;
    public hoverElseQITRight = false;

    public static ALLOW_MULTIPLE_STATEMENTS = false;

    constructor(@Inject(SelectionServiceToken) protected selService: ISelectionService,
        rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        private dragDropService: DragDropService,
        private resourceService: ResourceService,
        eventService: EventNotifierService,
        rootElement: ElementRef<HTMLElement>) {
        super(selService, rulePluginService, commandService, configPanelService,
            undoRedoService, eventService, rootElement);
    }

    public get configuredIfThenElseStatement(): IConfiguredIfThenElseStatement {
        return this.statementModel as IConfiguredIfThenElseStatement;
    }

    ngOnInit(): void {
        super.ngOnInit();

        if (this.configuredIfThenElseStatement?.IfExpressions.length > 0) {
            this.configuredIfThenElseStatement.IfExpressions[0].isSelectable = this.configuredIfThenElseStatement.isSelectable;

            if (this.configuredIfThenElseStatement.createdFromIfDrop === true) {
                this.configuredIfThenElseStatement.IfExpressions[0].createdFromIfDrop = true;
            }
        }

        if (this.configuredIfThenElseStatement?.elseStatements.length > 0) {
            this.configuredIfThenElseStatement.elseStatements[0].isSelectable = this.configuredIfThenElseStatement.isSelectable;

            if (this.configuredIfThenElseStatement.createdFromIfDrop === true) {
                this.configuredIfThenElseStatement.elseStatements[0].createdFromIfDrop = true;
            }
        }

        if (this.configuredIfThenElseStatement?.thenStatements.length > 0) {
            this.configuredIfThenElseStatement.thenStatements[0].isSelectable = this.configuredIfThenElseStatement.isSelectable;

            if (this.configuredIfThenElseStatement.createdFromIfDrop === true) {
                this.configuredIfThenElseStatement.thenStatements[0].createdFromIfDrop = true;
            }
        }

        if (this.configuredIfThenElseStatement?.createdFromIfDrop !== true && this.configuredIfThenElseStatement?.isSelectable) {
            (this.rootElement.nativeElement.querySelector('.if-then-else-container') as HTMLElement)?.focus();
        }

        this.qitConfiguration = (this.rulePluginService.getStatementConfiguration(this.configuredIfThenElseStatement) as IStatementConfiguration).qitConfig;
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    unfocusAll(event: Event): boolean {
        if ((event.target as HTMLElement).parentElement.parentElement.classList.contains('pointer-events-none')) {
            event.stopPropagation();
            event.preventDefault();
            event.cancelBubble = true;

            this.selService.setSelection([], 'rule');

            //tab out from selected element.
            //.rules-heading has tabindex = '-1' --> important
            (document.querySelector('.rules-container .rules-heading') as HTMLElement)?.focus();

            event.stopImmediatePropagation();
            return false;
        }
    }

    onDragHighlightCheck(data: { area: string; direction: string }): boolean {
        if (this.dragDropService.hasItem('ruleDesignerItem')) {
            const dragDropSource = (<IConfiguredStatement | IItemConfiguration>this.dragDropService.peekItem('ruleDesignerItem').getSource());
            let dragDropItem = {} as IConfiguredStatement;

            if (this.rulePluginService.isStatementPlugin(dragDropSource)) {
                dragDropItem = this.rulePluginService.createStatement((<IItemConfiguration>dragDropSource).name);
            }
            else if (this.rulePluginService.isConfiguredStatement(dragDropSource)) {
                dragDropItem = <IConfiguredStatement>dragDropSource;
            }

            const ifThenElseStatement = <IConfiguredIfThenElseStatement>dragDropItem;
            const isStatementExist = !isUndefinedOrNull(ifThenElseStatement.elseStatements) && ifThenElseStatement.elseStatements.length < 1;
            const isDirectiveExist = (!isUndefinedOrNull(ifThenElseStatement.directive)
                && ifThenElseStatement.directive.indexOf('statement') > -1
                && !this.checkIfDroppedItemNotParent(this.configuredIfThenElseStatement, dragDropItem));

            switch (data.area) {
                case 'if':
                    switch (data.direction) {
                        case 'in':
                            if (this.rulePluginService.isExpressionPlugin(dragDropSource)
                                || this.rulePluginService.isConfiguredExpression(dragDropSource))
                            {this.ifBlockHover = true;}
                            break;
                        case 'out': this.ifBlockHover = false;
                            break;
                    }
                    break;
                case 'then':
                    switch (data.direction) {
                        case 'in':
                            if ((this.configuredIfThenElseStatement.thenStatements.length < 1 || isStatementExist) && isDirectiveExist)
                            {this.thenBlockHover = true;}
                            break;
                        case 'out': this.thenBlockHover = false;
                            break;
                    }
                    break;
                case 'else':
                    switch (data.direction) {
                        case 'in':
                            if ((this.configuredIfThenElseStatement.elseStatements.length < 1 || isStatementExist) && isDirectiveExist)
                            {this.elseBlockHover = true;}

                            break;
                        case 'out': this.elseBlockHover = false;
                            break;
                    }
                    break;
            }
        }

        return false;
    }

    /**
     * Drop onto the if element
     */
    onIfDrop(): void {
        this.ifBlockHover = false;
        this.addDroppedExpressionItem();
    }

    /**
     * Drop onto the then element
     */
    onThenDrop(): void {
        this.thenBlockHover = false;
        this.addDroppedStatementItem('then');
    }

    /**
     * Drop onto the else element
     */
    onElseDrop(): void {
        this.elseBlockHover = false;
        this.addDroppedStatementItem('else');
    }

    private checkIfDroppedItemNotParent(currentItem: IConfiguredStatement, droppedItem: IConfiguredStatement): boolean {
        const currentItemParent: IConfiguredStatement = currentItem.getLogicalParentModel<IConfiguredStatement>();

        if (droppedItem?.getReference) {
            if (currentItemParent && currentItemParent.instanceOf && currentItemParent.instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor)) {
                const droppedItemReference = droppedItem.getReference();
                if (currentItemParent.getReference() === droppedItemReference || currentItem.getReference() === droppedItemReference) {
                    return true;
                }

                if (this.checkIfDroppedItemNotParent(currentItemParent, droppedItem)) {
                    return true;
                }
            }
        }
        return false;
    }

    private addDroppedStatementItem(block: string) {
        if (this.dragDropService.hasItem('ruleDesignerItem')) {
            let droppedData: IItemConfiguration | IConfiguredStatement = {};
            let configuredBlockData: IConfiguredStatement = null;
            droppedData = (<IItemConfiguration>this.dragDropService.getItem('ruleDesignerItem').getSource());

            if ((<IConfiguredStatement>droppedData).getReference) {
                if (this.configuredIfThenElseStatement.getReference() === (<IConfiguredStatement>droppedData).getReference()) {
                    return;
                }
            }

            if (this.checkIfDroppedItemNotParent(this.configuredIfThenElseStatement, <IConfiguredStatement>droppedData)) {
                return;
            }

            if (this.rulePluginService.isConfiguredStatement(droppedData)) {
                configuredBlockData = (<IConfiguredStatement>droppedData).clone();
            } else if (this.rulePluginService.isStatementPlugin(droppedData)) {
                const itemName: string = (<IItemConfiguration>droppedData).name;
                configuredBlockData = <IConfiguredStatement>this.rulePluginService.createStatement(itemName);
            }

            if (!isUndefinedOrNull(configuredBlockData)) {
                if (block === 'then') {
                    if (IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS ||
                        (!IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS &&
                            (!(this.configuredIfThenElseStatement.thenStatements.length > 0) ||
                            (!isUndefinedOrNull((<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements) &&
                            (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length < 1)))) {

                        if (!IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS &&
                            this.configuredIfThenElseStatement.thenStatements.length > 0 &&
                            (!isUndefinedOrNull((<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements) &&
                            (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length < 1)) {

                            let clonedExpression: any = this.configuredIfThenElseStatement.thenStatements[0].clone();
                            clonedExpression.createdFromIfDrop = true;

                            if (!!(<IConfiguredStatement>droppedData).getReference) {
                                if (this.checkIfIfThenElseStatementIntheSameBranch(this.configuredIfThenElseStatement, <IConfiguredStatement>droppedData)) {
                                    if (clonedExpression.directive === StatementDirectives.ifThenElse) {
                                        if (clonedExpression.thenStatements.length > 0) {
                                            (<any>this.configuredIfThenElseStatement.getRoot()).findItem((<IConfiguredStatement>droppedData).getReference()).removeFromModelArray();
                                            if (this.configuredIfThenElseStatement.thenStatements.length > 0) {
                                                clonedExpression = this.configuredIfThenElseStatement.thenStatements[0].clone();
                                            } else {
                                                clonedExpression = null;
                                            }
                                        } else {
                                            clonedExpression = null;
                                        }
                                    }
                                }
                            }

                            if (clonedExpression !== null) {
                                (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.push(clonedExpression);
                            }

                            this.configuredIfThenElseStatement.thenStatements.splice(0, this.configuredIfThenElseStatement.thenStatements.length);
                            this.configuredIfThenElseStatement.thenStatements.push(configuredBlockData);
                            if ((<IConfiguredStatement>droppedData).instanceOf && (<IConfiguredStatement>droppedData).instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor)) {
                                (<IConfiguredStatement>droppedData).removeFromModelArray();
                            }
                        } else {
                            if (!!(<any>configuredBlockData).elseStatements && (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements[0]).createdFromIfDrop = true;
                            }
                            if (!!(<any>configuredBlockData).thenStatements && (<IConfiguredIfThenElseStatement>configuredBlockData).thenStatements.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).thenStatements[0]).createdFromIfDrop = true;
                            }
                            if (!!(<any>configuredBlockData).IfExpressions && (<IConfiguredIfThenElseStatement>configuredBlockData).IfExpressions.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).IfExpressions[0]).createdFromIfDrop = true;
                            }
                            this.configuredIfThenElseStatement.thenStatements.push(configuredBlockData);
                            if ((<IConfiguredStatement>droppedData).instanceOf && (<IConfiguredStatement>droppedData).instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor)) {
                                (<IConfiguredStatement>droppedData).removeFromModelArray();
                            }
                        }
                    }
                } else if (block === 'else') {
                    if (IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS ||
                        (!IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS &&
                            (!(this.configuredIfThenElseStatement.elseStatements.length > 0) ||
                            (!isUndefinedOrNull((<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements) &&
                            (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length < 1)))) {

                        if (!IfThenElseStatementComponent.ALLOW_MULTIPLE_STATEMENTS &&
                            this.configuredIfThenElseStatement.elseStatements.length > 0 &&
                            (!isUndefinedOrNull((<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements) &&
                            (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length < 1)) {

                            let clonedExpression: any = this.configuredIfThenElseStatement.elseStatements[0].clone();
                            clonedExpression.createdFromIfDrop = true;

                            if (!!(<IConfiguredStatement>droppedData).getReference) {
                                if (this.checkIfIfThenElseStatementIntheSameBranch(this.configuredIfThenElseStatement, <IConfiguredStatement>droppedData)) {
                                    if (clonedExpression.directive === StatementDirectives.ifThenElse) {
                                        if (clonedExpression.elseStatements.length > 0) {
                                            (<any>this.configuredIfThenElseStatement.getRoot()).findItem((<IConfiguredStatement>droppedData).getReference()).removeFromModelArray();
                                            clonedExpression = this.configuredIfThenElseStatement.elseStatements[0].clone();
                                        } else {
                                            clonedExpression = null;
                                        }
                                    }
                                }
                            }

                            if (clonedExpression !== null) {
                                (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.push(clonedExpression);
                            }

                            this.configuredIfThenElseStatement.elseStatements.splice(0, this.configuredIfThenElseStatement.elseStatements.length);

                            this.configuredIfThenElseStatement.elseStatements.push(configuredBlockData);
                            if ((<IConfiguredStatement>droppedData).instanceOf && (<IConfiguredStatement>droppedData).instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor)) {
                                (<IConfiguredStatement>droppedData).removeFromModelArray();
                            }
                        } else {
                            if (!!(<any>configuredBlockData).elseStatements && (<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).elseStatements[0]).createdFromIfDrop = true;
                            }
                            if (!!(<any>configuredBlockData).thenStatements && (<IConfiguredIfThenElseStatement>configuredBlockData).thenStatements.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).thenStatements[0]).createdFromIfDrop = true;
                            }
                            if (!!(<any>configuredBlockData).IfExpressions && (<IConfiguredIfThenElseStatement>configuredBlockData).IfExpressions.length > 0) {
                                (<any>(<IConfiguredIfThenElseStatement>configuredBlockData).IfExpressions[0]).createdFromIfDrop = true;
                            }

                            this.configuredIfThenElseStatement.elseStatements.push(configuredBlockData);
                            if ((<IConfiguredStatement>droppedData).instanceOf && (<IConfiguredStatement>droppedData).instanceOf(sourceCode.k2Designer.objectModel.core.configuredRules.ConfiguredStatement.implementor)) {
                                (<IConfiguredStatement>droppedData).removeFromModelArray();
                            }
                        }
                    }
                }
            }
        }
    }

    private checkIfIfThenElseStatementIntheSameBranch(addedTo: IConfiguredIfThenElseStatement, addedExpression: IConfiguredStatement) {
        let continueCheck = true;

        while (continueCheck) {
            if (addedExpression.getReference() === addedTo.getReference()) {
                return true;
            }

            continueCheck = addedExpression.directive === StatementDirectives.ifThenElse;

            addedExpression = addedExpression.getLogicalParentModel();
        }

        return false;
    }

    private addDroppedExpressionItem() {
        if (this.dragDropService.hasItem('ruleDesignerItem')) {
            const droppedData = this.dragDropService.getItem('ruleDesignerItem').getSource() as IConfiguredExpression;

            //if expression item
            if (this.rulePluginService.isConfiguredExpression(droppedData)) {
                const droppedExpression: IConfiguredExpression = droppedData;
                const droppedExpressionParent = droppedExpression.getParentModel<sourceCode.k2Designer.objectModel.core.model.IModelArray<IConfiguredExpression>>();
                const droppedExpressionIndex = droppedExpressionParent.indexOf(droppedExpression);
                const clonedDroppedExpression = droppedExpression.clone();

                //when if block is currently empty
                if (this.configuredIfThenElseStatement.IfExpressions && this.configuredIfThenElseStatement.IfExpressions.length === 0) {
                    //create group
                    const configuredBlockData = this.rulePluginService.createExpression('GroupExpression') as IConfiguredComplexLogicalGroupExpression;
                    configuredBlockData.expressions.push(clonedDroppedExpression);
                    //add to if expressions
                    this.configuredIfThenElseStatement.IfExpressions.push(configuredBlockData);
                } else {//I am in a group
                    //do nothing if was just dropped on its same parent
                    if ((<IConfiguredComplexLogicalGroupExpression>this.configuredIfThenElseStatement.IfExpressions[0]).expressions.indexOf(droppedExpression) > -1) {
                        return;
                    }
                    //add operator
                    (<IConfiguredComplexLogicalGroupExpression>this.configuredIfThenElseStatement.IfExpressions[0]).expressions.push(
                        this.rulePluginService.createOperator('GroupBooleanOperator'));
                    //add clone of block
                    (<IConfiguredComplexLogicalGroupExpression>this.configuredIfThenElseStatement.IfExpressions[0]).expressions.push(clonedDroppedExpression);
                }
                //remove original block
                this.deleteExpression(droppedExpression);
            }
            //if toolbar dropped item / expression plugin
            else if (this.rulePluginService.isExpressionPlugin(droppedData)) {
                const itemName: string = (droppedData as any).name;

                const configuredBlockData = <IConfiguredExpression>this.rulePluginService.createExpression(itemName);

                if (this.configuredIfThenElseStatement.IfExpressions && this.configuredIfThenElseStatement.IfExpressions.length === 0) {
                    //We create a new group expression and then add the relevant new expression to its expression array
                    const configuredBlockData = this.rulePluginService.createExpression('GroupExpression') as IConfiguredComplexLogicalGroupExpression;
                    configuredBlockData.expressions.push(<IConfiguredExpression>this.rulePluginService.createExpression(itemName));

                    this.configuredIfThenElseStatement.IfExpressions.push(configuredBlockData);
                }
                else {
                    //We create a new operator
                    const configuredOperator = this.rulePluginService.createOperator('GroupBooleanOperator') as IConfiguredExpression;
                    //We create a new expression, depending on the name of the component
                    const configuredBlockData = <IConfiguredExpression>this.rulePluginService.createExpression(itemName) as IConfiguredExpression;

                    (<IConfiguredComplexLogicalGroupExpression>this.configuredIfThenElseStatement.IfExpressions[0]).expressions.push(configuredOperator);
                    (<IConfiguredComplexLogicalGroupExpression>this.configuredIfThenElseStatement.IfExpressions[0]).expressions.push(configuredBlockData);
                }
            }
        }
    }

    private deleteExpression(k2ObjectModel: IConfiguredExpression) {
        const expressionParent = k2ObjectModel.getParentModel<sourceCode.k2Designer.objectModel.core.model.IModelArray<IConfiguredExpression>>();
        const index = expressionParent.indexOf(<any>k2ObjectModel);

        if (expressionParent.length < 2) {
            (<any>k2ObjectModel.getLogicalParentModel()).removeFromModelArray();
        }
        else {
            k2ObjectModel.removeFromModelArray();
            const groupOpertorOf = function(index: number, expressions: sourceCode.k2Designer.objectModel.core.model.IModelArray<IConfiguredExpression>) {
                if (index > expressions.length - 1) {
                    index -= 1;
                }
                return expressions[index].directive === 'k2-group-boolean-operator-expression' ? expressions[index] : null;
            };

            const operatorToBeRemove: any = groupOpertorOf(index, expressionParent);
            if (operatorToBeRemove) {
                operatorToBeRemove.removeFromModelArray();
            }

            const grpExpression: any = <any>expressionParent.getLogicalParentModel();
            if (grpExpression.directive === grpExpression.getLogicalParentModel().directive && grpExpression.expressions.length === 1) {
                const k2GroupExpressionModel = grpExpression.getParentModel();
                const index1 = k2GroupExpressionModel.indexOf(grpExpression);
                const expression = expressionParent[0].clone();
                k2GroupExpressionModel.getLogicalParentModel().expressions.splice(index1, 1, expression);
            }
        }
    }

    getDragImage = (): HTMLElement => {
        const result = super.getDragImage();
        result.classList.add('ifthen');
        return result;
    };

    onDragStart(): void {  //hide block to give appearance of dragging
        const element = this.rootElement.nativeElement.querySelector('> div') as HTMLElement;
        if (!!element) {
            element.style.opacity = '0';
            element.style.transition = 'opacity 0s';
        }
    }

    onDragStop(): void {
        const element = this.rootElement.nativeElement.querySelector('> div') as HTMLElement;
        if (!!element) {
            element.style.opacity = '';
            element.style.transition = '';
        }
    }



    addQitExpressionItem(componentName: string) {
        if (this.configuredIfThenElseStatement.IfExpressions && this.configuredIfThenElseStatement.IfExpressions.length === 0) {
            this.undoRedoService.addUndoMarker('--add rules expression item--');

            //We create a new group expression and then add the relevant new expression to its expression array
            const configuredBlockData = this.rulePluginService.createExpression('GroupExpression') as IConfiguredComplexLogicalGroupExpression;
            const configuredExpression = this.rulePluginService.createExpression(componentName) as IConfiguredExpression;

            configuredBlockData.expressions.push(configuredExpression);
            this.configuredIfThenElseStatement.IfExpressions.push(configuredBlockData);
        }
    }

    addQitStatementItem(componentName: string, block: string) {
        this.undoRedoService.addUndoMarker('--add rules statement item--');

        //We create a new statement, depending on the name of the component
        const configuredBlockData = this.rulePluginService.createStatement(componentName) as IConfiguredStatement;

        if (block === 'then') {
            this.configuredIfThenElseStatement.thenStatements.push(configuredBlockData);
        }
        else if (block === 'else') {
            this.configuredIfThenElseStatement.elseStatements.push(configuredBlockData);
        }
    }

    /**
     * Keypress handling
     *
     * @param event
     * @returns
     */
    @HostListener('keydown', ['$event'])
    public onKeyPress(event: KeyboardEvent): void {  // tab loop and keyboard accessibility
        const $currentFocus = event.target as HTMLElement; //get current focused item

        event.stopPropagation(); // prevent k2 text box from stealing focus
        event.preventDefault();  // prevent tab from getting overridden

        switch (event.keyCode) {
        // enter or right
            case 13:
            case 39:
            //if on an if then else block entire focus
                if ($currentFocus.classList.contains('if-then-else-container')) {
                //first tabindex within the if block.
                    ($currentFocus.querySelector('[tabindex]:first') as HTMLElement)?.focus();

                //if a qit icon has focus
                } else if ($currentFocus.classList.contains('qit-icon')) {
                //mimic clicking icon to create new element
                    $currentFocus.querySelector('span').click();
                } else {
                //otherwise just try to focus the next inside element.
                    ($currentFocus.nextElementSibling.querySelector('[tabindex]:first') as HTMLElement)?.focus();  //finds first tab index in child
                }
                break;

            //space bar
            case 32:
                $currentFocus?.click(); //select by mocking a click
                $currentFocus?.focus();  //refocus
                break;

            // backspace, delete
            case 8:
            case 46:
                if ($currentFocus.classList.contains('if-then-else-container') || $currentFocus.classList.contains('rules-ifBlock')) {
                //if container then remove / If block / then block / else block
                    this.undoRedoService.addUndoMarker('-- delete ifthenelse statement --');
                    this.delete();
                    //focus up to closest parent
                    ($currentFocus.parentElement.closest('[tabindex]') as HTMLElement)?.focus();
                }
                break;

            //escape
            case 27:
            //focus up to closest parent
                ($currentFocus.parentElement.closest('[tabindex]') as HTMLElement)?.focus();
                break;

            //tab
            case 9:
            //if main block then focus toolbox
                if ($currentFocus.classList.contains('rules-ifBlock')) {
                    if (event.shiftKey) {
                        if (!!$currentFocus.parentElement.previousElementSibling.querySelector('.rules-ifBlock:first')) {  //if there is a next sibling
                            ($currentFocus.parentElement.previousElementSibling.querySelector('.rules-ifBlock:first') as HTMLElement)?.focus();
                        } else {//loop back to parent
                            ($currentFocus.parentElement.parentElement.querySelector('.rules-ifBlock:last') as HTMLElement)?.focus();
                        }
                    } else {
                        if (!!$currentFocus.parentElement.nextElementSibling.querySelector('.rules-ifBlock:first')) {  //if there is a next sibling
                            ($currentFocus.parentElement.nextElementSibling.querySelector('.rules-ifBlock:first') as HTMLElement)?.focus();
                        } else {//loop back to parent
                            ($currentFocus.parentElement.parentElement.querySelector('.rules-ifBlock:first') as HTMLElement)?.focus();
                        }
                    }
                } else if ($currentFocus.classList.contains('qit-icon')) { //tab across qit icons
                    if (!!$currentFocus.nextElementSibling) {
                        ($currentFocus.nextElementSibling as HTMLElement)?.focus();
                    } else {
                        ($currentFocus.previousElementSibling as HTMLElement)?.focus();
                    }
                }
                break;

            default:
            // handle undo/redo and others
                super.onKeyPress(event);
                break;
        }
    }
}
