import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { extend } from 'angular';
import { IGridCell } from '../../../../types/models/grid/i-grid-cell';
import { IGridRow } from '../../../../types/models/grid/i-grid-row';
import { ResourceService } from '../../../services/resource/resource.service';
import { ColumnTypes } from '../../../shared/enums/grid/column-types';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-grid',
    templateUrl: './grid.component.html',
    styleUrls: ['./grid.component.less']
})
export class GridComponent extends BaseResourceComponent implements OnInit {

    @Input() public fixedRows: number;
    @Input() public valuesAreReverted: boolean;
    @Input() public parameterNameEditable: boolean;
    @Input() public headerText: string;
    @Input() public rows: any[];
    @Input() public cells: string[];
    @Input() public selectedRow: IGridRow;

    @Output() public setSelectedRow = new EventEmitter<IGridRow>();

    public typeDropPlaceholder: string;

    private _toArrayMapping: any;

    constructor(private _resourceService: ResourceService) {
        super(_resourceService);
    }

    ngOnInit(): void {
        this.typeDropPlaceholder = this._resourceService.getString('general_type_drop');

        if (this.cells && this.cells.length) {

            this._toArrayMapping = {};

            for (let i = 0; i < this.cells.length; i++) {

                this._toArrayMapping[this.cells[i]] = undefined;
            }
        }
    }

    public selectRow(row: IGridRow, rowIndex: number): void {

        if (this.fixedRows - 1 < rowIndex) {
            this.selectedRow = row;
            this.setSelectedRow.emit(this.selectedRow);
        }
        else {
            this.selectedRow = null;
        }
    }

    public isTextbox(cell: IGridCell): boolean {
        if (!cell.type || (cell.type === ColumnTypes.complexText || cell.type === ColumnTypes.simpleText)) {
            return true;
        }
        return false;
    }

    public getCells(row: IGridRow): IGridCell[] {
        if (Array.isArray(row)) {
            return <IGridCell[]>(<any>row);
        }

        if (row instanceof sourceCode.k2Designer.objectModel.core.model.ModelBase) {
            return (<sourceCode.k2Designer.objectModel.core.model.ModelBase>(<any>row)).toArray(this._toArrayMapping);
        }

        return [];
    }

    get columnCount(): number {
        if (this.rows && this.rows.length) {
            return this.getCells(this.rows[0]).length;
        } else {
            return 1;
        }
    }

    public onBlur(): void {
        this.selectedRow = null;
    }

}
