import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { DataServiceToken } from 'src/app/services/data/data-service-factory';
import { EnvironmentServiceToken } from 'src/app/services/environment/environment-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { globalVariableNames } from 'src/app/shared/constants/general';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';
import { SharePointIntegrationDeletionStatus } from 'src/app/shared/enums/integration/sharepoint-integration-delete-status';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IDataService } from 'src/types/services/data/i-data-service';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IRemoveSharePointPluginContext } from 'src/types/uiComponents/pluginWizards/IRemoveSharePointPluginContext';
import { IItemReference } from '../../../../../types/models/process/item-references/i-item-reference';
import { IContextWizardPopupConfiguration } from '../../../../../types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { ProcessService } from '../../../../services/process/process.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-sp-remove-integration',
    templateUrl: './remove-integration.component.html',
    styleUrls: ['./remove-integration.component.less'],
    providers: [{ provide: PageComponent, useExisting: SharepointRemoveIntegrationComponent }]
})
export class SharepointRemoveIntegrationComponent extends PageComponent implements OnInit {

    @Input() config: IContextWizardPopupConfiguration;
    @Input() isResultPage: boolean;

    @Output() dialogClosed = new EventEmitter<any>();

    /**
     * Context of the plugin wizard.
     */
    pluginContext: IRemoveSharePointPluginContext;

    /**
     * Choose control types enumerator
     */
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;

    buttonConfig: IPopupButtonsConfig;

    /**
     * Sharepoint deletion status enumerator
     */
    public deletionStatus: typeof SharePointIntegrationDeletionStatus = SharePointIntegrationDeletionStatus;

    constructor(@Inject(EnvironmentServiceToken) private envService: IEnvironmentService,
        protected resourceService: ResourceService,
        @Inject(DataServiceToken) private dataService: IDataService,
        private processService: ProcessService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.pluginContext = this.config.context as IRemoveSharePointPluginContext;
        this.pluginContext.status = SharePointIntegrationDeletionStatus.inProgress;

        this.pluginContext.deleteListHeading = this.resourceService.getString('sharepoint_integration_delete_from_list_heading')
            .replace('{0}', this.pluginContext.selectedPlugin.title);
        this.pluginContext.deleteListDescription = this.resourceService.getString('sharePoint_integration_summary_changes_heading')
            .replace('{0}',
                this.pluginContext.selectedPlugin.metaData.DocumentLibrary ?
                    this.resourceService.getString('sharePoint_integration_found_references_library') :
                    this.resourceService.getString('sharePoint_integration_found_references_list'));

        if (!this.isResultPage) {
            this.pluginContext.deleteItemReferences = true;
        }

        this.buttonConfig = {
            previous: {
                isVisible: !this.isResultPage,
                buttonText: this.getString('workflow_contextPopup_cancelButton')
            },
            next: {
                buttonText: this.isResultPage ? this.getString('workflow_contextPopup_okButton') :
                    this.getString('workflow_contextPopup_delete_event'),
                classOverride: 'modal-button alert large'
            }
        };
    }

    beforePageDisplayed() {
        if (this.isResultPage) {
            this.deleteIntegration();
        }
    }

    beforeNavigateNext() { }

    beforeNavigatePrevious() { }

    /**
     * Method to toggle checkbox.
     */
    public toggleCheckbox(): void {
        this.pluginContext.deleteItemReferences = !this.pluginContext.deleteItemReferences;
    }


    /**
     * Method to remove the integration.
     */
    private deleteIntegration() {
        this.notifyPageStateChanged(PopupState.Busy);

        //Build event receiver string.
        let eventReceiverString = '';

        for (let i = 0; i < this.pluginContext.selectedPlugin.metaData.EventData.length; i++) {
            eventReceiverString += this.pluginContext.selectedPlugin.metaData.EventData[i].Id;
            if (i < this.pluginContext.selectedPlugin.metaData.EventData.length - 1) {
                eventReceiverString += ';';
            }
        }

        //Remove event receivers
        this.dataService.httpGet('api/sharePoint/removeWorkFlowEventReceiverForSpecificWorkflow', {
            host: this.envService.currentEnvironment.hostId,
            workflowId: this.pluginContext.selectedPlugin.name,
            settingId: this.pluginContext.selectedPlugin.id,
            eventReceivers: eventReceiverString,
            siteUrl: this.pluginContext.selectedPlugin.metaData.SiteURL,
            listId: this.pluginContext.selectedPlugin.metaData.ListId,
            workflowName: this.envService.workflowFullname
        }, this.manageEventReceiversCompleted, this.envService, false, null, false);
    }

    /**
     * Callback method when events was removed.
     *
     * @param result
     * @param error
     */
    private manageEventReceiversCompleted = (result: any, error: boolean): void => {
        this.notifyPageStateChanged(PopupState.Normal);

        if (!error) {
            this.deleteReferences();
            this.pluginContext.status = SharePointIntegrationDeletionStatus.success;

            //Remove SharePoint integration global environment value which validation is done against because that does not get saved
            //in the model.
            this.envService.removeGlobalAttribute(globalVariableNames.sharepointIntegratonWorkflowData);
        } else {
            this.pluginContext.status = SharePointIntegrationDeletionStatus.failed;

            const exception = JSON.parse(result);
            if (exception.ExceptionMessage) {
                this.pluginContext.errorMessage = exception.ExceptionMessage;
            } else {
                this.pluginContext.errorMessage = exception.Message;
            }

            this.buttonConfig = {
                next: {
                    isVisible: false
                },
                previous: {
                    isVisible: false
                },
                finish: {
                    isVisible: false
                }
            };

            this.notifyConfigChanged();
        }
    };

    /**
     * Method to delete the references if selected.
     */
    private deleteReferences(): void {
        if (this.pluginContext.deleteItemReferences) {
            const primarySPItemReference: IItemReference =
                (this.processService.root as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessExtended)
                    .findWorkflowItemReference(this.pluginContext.selectedPlugin.metaData.SmartObjectName, '',
                        this.processService as any, true, globalVariableNames.sharepointEvent, '');

            if (primarySPItemReference) {
                //Remove the secondary item references.
                for (let i: number = this.processService.root.configuration.itemReferences.length - 1; i > -1; i--) {
                    if (this.processService.root.configuration.itemReferences[i].primaryReference &&
                        this.processService.root.configuration.itemReferences[i].primaryReference === primarySPItemReference.systemName) {
                        //Remove item reference
                        this.processService.root.configuration.itemReferences[i].removeFromModelArray();
                    }
                }

                //Remove primary item reference.
                primarySPItemReference.removeFromModelArray();
            }
        }
    }
}
