import { IStates } from '../../../types/models/rules/i-states';

/**
 * A wrapper class for the string array of control states
 * */
export class States implements IStates {

    private _states: string[] = [];

    public States() {
        this._states = [];
    }

    /**
     * Add a new state to the rule component
     *
     * @param newState
     */
    public add(newState: string): boolean {

        let result = false;
        if (this._states.indexOf(newState) < 0) {
            this._states.push(newState);
            result = true;
        }
        return result;
    }

    /**
     * Remove state from the rule component
     *
     * @param state
     */
    public remove(state: string): boolean {
        let result = false;
        const index = this._states.indexOf(state);
        if (index > -1) {
            this._states.splice(index, 1);
            result = true;
        }

        return result;
    }

    /**
     * Clear all states on the rule component
     * */
    public clear(): boolean {
        this._states.splice(0);
        return this._states.length < 1;
    }

    /**
     * Check if state exists
     *
     * @param state
     */
    public isExist(state: string): boolean {
        return this._states.indexOf(state) > -1;
    }

    /**
     * Return all states seperated by a space
     * */
    public toString() {
        return this._states.join(' ');
    }

}
