import { Inject, Injectable } from '@angular/core';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { IProcessDefinitionPluginConfiguration } from '../../../types/services/process/i-process-definition-plugin-configuration';
import { IProcessService } from '../../../types/services/process/i-process-service';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';
import { environment } from 'src/environments/environment';
import { MockProcessService } from 'src/testing/mock-legacy-services/process/mock-legacy-process-service';
import { ILegacyProcessService } from 'src/types/services/process/i-legacy-process-service';
import { IProcess } from '../../../types/models/process/i-process';
import { IActivity } from '../../../types/models/process/activities/i-activity';
import { ErrorSeverity } from '../../shared/enums/model/error-severity';
import { IConfiguredSmartField } from '../../../types/models/core/configured-smart-fields/i-configured-smart-field';
import { IValidatedComponent } from '../../../types/validation/i-validated-component';

@Injectable({
    providedIn: 'root'
})
export class ProcessService implements IProcessService {
    private processService: ILegacyProcessService;

    get legacyProcessService() {
        if (!this.processService) {
            if(environment.devHarness === true ){
                this.processService = new MockProcessService(this.environmentService) as any;
            } else {
                this.processService = this.environmentService
                    .activeStudioItem
                    .getService(sourceCode.k2Designer.services.studioItem.workflow.ProcessService) as any;
            }
        }
        return this.processService;
    }

    get definitionPlugins(): IProcessDefinitionPluginConfiguration[] {
        return this.legacyProcessService.definitionPlugins;
    }

    get root(): sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process {
        return this.legacyProcessService.root;
    }

    get rootId(): string {
        return this.legacyProcessService.rootId;
    }

    get beginCloningOperation(): () => void {
        return this.legacyProcessService.beginCloningOperation;
    }

    get finishCloningOperation(): () => void {
        return this.legacyProcessService.finishCloningOperation;
    }

    get isCloning(): boolean {
        return this.legacyProcessService.isCloning;
    }

    get itemInstance(): sourceCode.k2Designer.objectModel.environment.IItemInstance {
        return this.legacyProcessService.itemInstance;
    }

    get id(): string {
        return this.legacyProcessService.id;
    }

    get listenReady(): (callback: sourceCode.k2Designer.services.core.IServiceReadyCallback) => void {
        return this.legacyProcessService.listenReady;
    }

    get notifyReady(): () => void {
        return this.legacyProcessService.notifyReady;
    }

    constructor(@Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService) { }

    isInitialized(): boolean {
        return this.legacyProcessService.isInitialized();
    }
    unRegisterEventListener(event: any, subscriberId: any): void {
        this.legacyProcessService.unRegisterEventListener(event, subscriberId);
    }

    createNewActivity(initialize: boolean, wizardId?: number, template?: string): IActivity {
        return this.legacyProcessService.createNewActivity(initialize, wizardId, template);
    }

    registerObjectModelServiceEventListner(event: any, subscriber: any) {
        return this.legacyProcessService.registerObjectModelServiceEventListner(event, subscriber);
    }

    unRegisterObjectModelServiceEventListner(event: any, subscriberId: any) {
        return this.legacyProcessService.unRegisterObjectModelServiceEventListner(event, subscriberId);
    }

    listenForErrorList(cb: () => void, errorSeverity?: ErrorSeverity): () => void {
        return this.legacyProcessService.listenForErrorList(cb, errorSeverity);
    }

    cacheExternalReferences(configuredFields: IConfiguredSmartField[]): void {
        return this.legacyProcessService.cacheExternalReferences(configuredFields);
    }

    deployProcess(onCompleted: () => void): void {
        return this.legacyProcessService.deployProcess(onCompleted);
    }

    raiseReferenceError(modelObject: IValidatedComponent, errorProperty: string, errorReference: string): void {
        return this.legacyProcessService.raiseReferenceError(modelObject, errorProperty, errorReference);
    }

    removeTrackedItem(definitionReference: string, useReference: string): void {
        return this.legacyProcessService.removeTrackedItem(definitionReference, useReference);
    }

    listenForSRErrorList(cb: () => void, errorSeverity?: ErrorSeverity): () => void {
        return this.legacyProcessService.listenForSRErrorList(cb, errorSeverity);
    }

    getErrorList(): sourceCode.k2Designer.objectModel.core.model.IErrorInformation[] {
        return this.legacyProcessService.getErrorList();
    }

    registerEventListener(event: any, subscriber: any): void {
        return this.legacyProcessService.registerEventListener(event, subscriber);
    }

    busyProcessingInstructions(): boolean {
        return this.legacyProcessService.busyProcessingInstructions();
    }

    flush(callbackSuccess?: () => void, callbackFail?: () => void, closeProject?: boolean, isAutoFlush?: boolean): void {
        return this.legacyProcessService.flush(callbackSuccess, callbackFail, closeProject, isAutoFlush);
    }

    getInstructionCount(): number {
        return this.legacyProcessService.getInstructionCount();
    }

    allowProcessing(): boolean {
        return this.legacyProcessService.allowProcessing();
    }
    enableProcessing(): void {
        return this.legacyProcessService.enableProcessing();
    }

    requestProcessingCompleteNotification(stopProcessing: boolean, cleanup: boolean, onEventRaisedCallback: () => void): void {
        return this.legacyProcessService.requestProcessingCompleteNotification(stopProcessing, cleanup, onEventRaisedCallback);
    }

    setTitle(newValue: string): Promise<string> {

        return new Promise<string>((resolve, reject) => {
            this.legacyProcessService.setTitle(newValue).fail((originalTitle: string)=>{
                reject(originalTitle);
            });
        });
    }

    /**
     * Returns a list of reference strings from tracked references that are linked to the specified node
     *
     * @param nodePath - reference path of node
     */
    getTrackedReferencesForNode(nodePath: string): string[] {
        return this.legacyProcessService.getTrackedReferencesForNode(nodePath);
    }

    /**
     * Returns whether or not the reference has children that are used
     */
    public trackedReferenceHasChildren(definitionPath: string, excludedIndex?: number): boolean {
        return this.legacyProcessService.trackedReferenceHasChildren(definitionPath, excludedIndex);
    }

    getActivityDefaultName(wizardId: number, template: string): string {
        return this.legacyProcessService.getActivityDefaultName(wizardId, template);
    }

    getActivityDefaultNameFromActivity(activity: IActivity): string {
        return this.legacyProcessService.getActivityDefaultNameFromActivity(activity);
    }
}
