import { Component, Input, OnInit } from '@angular/core';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';

@Component({
    selector: 'drop-down-item-base',
    templateUrl: './drop-down-item-base.component.html',
    styleUrls: ['./drop-down-item-base.component.less']
})
export class DropDownItemBaseComponent implements OnInit {
    @Input() isFirst: boolean;
    @Input() isLast: boolean;
    @Input() item: IDropDownMenuItem;
    @Input() isMultiSelect = false;

    public dropdownHover: boolean;

    constructor() { }

    ngOnInit(): void {
    }

    getItemTitle(item: IDropDownMenuItem): string {
        if (item?.titleBinding?.objectReference && item.titleBinding.propertyName?.length) {
            return item.titleBinding.objectReference[item.titleBinding.propertyName];
        } else {
            return '';
        }
    }
}
