import { Directive, ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';

@Directive({
    selector: '[k2ContextMenu]'
})
export class ContextMenuDirective {
    @Input() contextMenuContext: any;
    @Output() rightClickEvent = new EventEmitter<any>();

    constructor() {
    }

    /* Handles the right click event and retrieves an array of context menu objects from the context menu service*/
    @HostListener('contextmenu', ['$event'])
    public onRightClick(e: MouseEvent): boolean {
        e.preventDefault();
        e.stopPropagation();

        this.rightClickEvent.emit({ e, context: this.contextMenuContext });
        return false;
    }
}
