import { Inject, Injectable } from '@angular/core';
import { IExpression } from '../../../types/models/core/configured-smart-fields/i-expression';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';

@Injectable({
    providedIn: 'root'
})
export class RichEditService {
    private _legacyRichEditService;
    private get legacyRichEditService(): sourceCode.k2Designer.services.studio.RichEditPanelService {
        if (!this._legacyRichEditService) this._legacyRichEditService = this._envService.getService(sourceCode.k2Designer.services.studio.RichEditPanelService);
        return this._legacyRichEditService;
    }

    constructor(@Inject(EnvironmentServiceToken) private _envService) { }

    /**
     * Set the context for all rich text service operations
     *
     * @param context Explicit context description
     * @param expression New context expression for rich text service operations
     */
    public setContext(context: string, expression: IExpression): void {
        this.legacyRichEditService?.setContext(context, expression);
    }
}
