/**
 * Enum to define what action should be taken on an escalation
 */
export enum DeadlineActionType {

    /**
     * Represents a escalation of none
     */
    None = 0,

    /**
     * Represents a default escalation action
     */
    DefaultActivityEscalation = 1,

    /**
     * Represents an email escalationaction
     */
    EmailEscalationAction = 2,

    /**
     * Represents an expire acticity escalation
     */
    ExpireActivityEscalationAction = 3,

    /**
     * Represents a goto activity escalation
     */
    GotoActivityEscalationAction = 4,

    /**
     * Represents a redirect escalation
     */
    RedirectEscalationAction = 5,

    /**
     * Represents a SmartObject escalation
     */
    SmartObjectEscalationAction = 6,

}
