import { ElementRef, EventEmitter, HostListener, Input, Output } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { IProcessPluginDefinitionContext } from 'src/types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { IGeneralItem } from 'src/types/uiComponents/pluginWizards/IGeneralItem';
import { GeneralItemTypes } from 'src/app/shared/enums/plugin-wizard/general-item-types';

@Component({
    selector: 'k2-smart-forms-rules',
    templateUrl: './smart-forms-rules.component.html',
    styleUrls: ['./smart-forms-rules.component.less']
})
export class SmartFormsRulesComponent implements OnInit {

    /**
     * Index in list used for item id
     */
    @Input() public id = '';
    private items: IGeneralItem[];
    @Input('items')
    set Items(items: IGeneralItem[]) {
        this.items = items || [];
    }
    get Items() {
        return this.items;
    }
    @Input() parentContext: IProcessPluginDefinitionContext;
    @Output() public onRulePlacementChanged = new EventEmitter<boolean>();

    /**
     * The sortable widget does not provide position info
     * on its 'start' event, need to record two different
     * sets and handle accordingly
     */
    private _anchorPosition: IPosition = {
        x: 0,
        y: 0
    };

    /**
     * if set to true, the _anchorPosition will be reset
     */
    private _refreshPosition: boolean;

    /**
     * A flag indicating that a drag operation is occurring
     */
    private _isDraggingItem = false;

    /**
     * Create a copy of the original action when we need to revert any changes
     */
    private _dragModelItem: IGeneralItem;

    private _startDragIndex = -1;
    private _nonWorkflowActionItems: IGeneralItem[];

    /**
     * Record initial index of the action to check if position has changed
     */
    private _originalActionIndex = -1;

    /**
     * Create a copy of the original action when we need to revert any changes
     */
    private _originalAction: IGeneralItem;
    private _sortableWidget: JQuery;
    private _rootElement: any;

    constructor(private _el: ElementRef) {
    }

    ngOnInit(): void {

        if (!this.id || this.id === '') {
            this.id = sourceCode.k2Designer.utilities.shortUid();
        }

        this._rootElement = $(this._el.nativeElement);

        setTimeout(() => {
            const options: JQueryUI.SortableOptions = {
                opacity: 0.6,
                axis: 'y',
                placeholder: `k2-rule-item-place-holder-${this.parentContext.isUserForm ? 'user-task' : 'start-event'}`,
                cancel: '.not-draggable:not(.workflow-sub-item)',
                revert: 150,
                tolerance: 'pointer',

                start: this.start.bind(this),
                stop: this.stop.bind(this),
                change: this.change.bind(this),
                sort: this.sort.bind(this)
            };

            const parentById = this._rootElement.parent().find('#' + this.id);
            if (parentById && typeof parentById.sortable === 'function') {
                this._sortableWidget = (<JQuery>parentById).sortable(options);
            }
        }, 0);
    }

    @HostListener('mousedown', ['$event'])
    onMouseDown(event: MouseEvent) {
        const element = document.elementFromPoint(event.clientX, event.clientY);
        this.doFocus(element);

        this._anchorPosition = {
            x: event.clientX,
            y: event.clientY
        };
        this._refreshPosition = false;
    }

    @HostListener('mouseup', ['$event'])
    onMouseUp(event: MouseEvent) {
        const workflowItem = this.getWorkflowItem();
        if (workflowItem) {
            workflowItem.focused = true;
        }
    }


    @HostListener('mousemove', ['$event'])
    onMouseMove(event: MouseEvent) {

        if (this._refreshPosition) {
            this._anchorPosition =
            {
                x: event.clientX,
                y: event.clientY
            };
            this._refreshPosition = false;
        }

        if (this._isDraggingItem) {

            let allowDocking = false;

            // determine if we should scroll the viewport
            const helper = this._rootElement[0].querySelector('.ui-sortable-helper');

            helper.classList.add('grabbed');

            // first determine if the item is in between handlers with different indentation
            const insertIndex = this.getPlaceHolderIndex();

            let curItem = this._dragModelItem;
            let prevItem = this.items[insertIndex - 1] as IGeneralItem;

            if (insertIndex === this.items.length - 1) {

                if (prevItem.itemType === GeneralItemTypes.action
                    && prevItem.level !== 0) {
                    allowDocking = true;
                }

            }
            else if (insertIndex > 1) {

                curItem = this._dragModelItem;
                prevItem = this.items[insertIndex - 1] as IGeneralItem;
                const nextItem = this.items[insertIndex + 1] as IGeneralItem;

                if (prevItem.itemType === GeneralItemTypes.action
                    && curItem.level !== prevItem.level) {
                    allowDocking = true;
                }
                else if (nextItem.itemType === GeneralItemTypes.action
                    && curItem.level !== nextItem.level
                    && (nextItem.itemType.toString() === GeneralItemTypes.condition.toString() ||
                        nextItem.itemType == GeneralItemTypes.action)) {
                    allowDocking = true;
                }
            }

            if (allowDocking) {
                const helperRuleText = helper.querySelector('.rule-text');

                const horizontalDiff = this._anchorPosition.x - event.clientX;
                const absDiff = Math.abs(horizontalDiff);

                if (absDiff > 40) {
                    // moving to the right
                    if (horizontalDiff < 0) {
                        // determine level of indentation
                        const prevItem = this.items[insertIndex - 1] as IGeneralItem;
                        this._dragModelItem.level = prevItem.level;
                        this.toggleRuleTextIndent(helperRuleText, prevItem.level);
                    }
                    else {
                        this._dragModelItem.level = 0;
                        this.toggleRuleTextIndent(helperRuleText, 0);
                    }
                }
            }

            event.preventDefault();
        }
    }

    @HostListener('blur', ['$event'])
    doBlur() {
        const workflowItem = this.getWorkflowItem();
        workflowItem.focused = false;
    }

    @HostListener('focus', ['$event'])
    Focus() {
        const element = this._rootElement[0].querySelector('.workflow-item');
        this.doFocus(element);
    }

    private toggleRuleTextIndent(ruleText: Element, level: number) {
        if (level === 0) {
            ruleText.classList.remove('indent');
            ruleText.classList.remove('double-indent');
        }
        else if (level === 1) {
            ruleText.classList.remove('double-indent');
            ruleText.classList.add('indent');
        }
        else if (level === 2) {
            ruleText.classList.remove('indent');
            ruleText.classList.add('double-indent');
        }
    }

    private doFocus(element: Element) {
        // get model item
        const workflowItem = this.getWorkflowItem();
        workflowItem.focused = true;

        // determine if the workflow action was clicked
        if (element !== null) {
            // ensure its the correct html element
            let targetElement = element;
            while (!targetElement.classList.contains('workflow-item')) {
                targetElement = targetElement.parentElement;
                // fail-safe
                if (!targetElement || targetElement.classList.contains('k2-wizard-box')) {
                    targetElement = null;
                    event.preventDefault();
                    break;
                }
            }

            if (targetElement !== null) {
                workflowItem.selected = true;
            }
        }
    }

    private getWorkflowItem(): IGeneralItem {
        const ruleItems = this.items;
        const workflowItem: IGeneralItem = null;
        for (let i = 0, len = ruleItems.length; i < len; i++) {

            const item = ruleItems[i] as IGeneralItem;
            if (item.isWorkflowAction || (this.parentContext.isUserForm && item.isOpenTaskCondition) || item.isLoadProcessAction) {

                return item;
            }
        }
        return null;
    }

    private getPlaceHolderIndex(): number {
        let className = '';
        if (this.parentContext.isUserForm) {
            className = '.k2-rule-item-place-holder-user-task';
        }
        else {
            className = '.k2-rule-item-place-holder-start-event';
        }
        const placeHolder = this._rootElement[0].querySelector(className);
        const uiElements = this._rootElement[0].querySelector('.ui-sortable').querySelectorAll(`${className}, .k2-smartform-rule-item:not(.ui-sortable-helper)`);
        return this.getIndex(placeHolder, uiElements);
    }

    private getIndex(element: Element, elements: NodeListOf<Element>): number {
        for (let i = 0, len = elements.length; i < len; i++) {

            if (element === elements[i]) {
                return i;
            }
        }
        return -1;
    }

    private start(evt, ui) {
        this._isDraggingItem = true;

        //TODO: add index property to UI instead of using getElementIndex function
        this._startDragIndex = sourceCode.k2Designer.utilities.ui.getElementIndex(ui.item[0]);
        this._dragModelItem = this.items[this._startDragIndex] as IGeneralItem;

        this._nonWorkflowActionItems = this.getNonWorkflowActionItems(this.items);

        if (this._originalActionIndex === -1) {
            this._originalActionIndex = this._startDragIndex;
            this._originalAction = JSON.parse(JSON.stringify(this._dragModelItem)) as
                IGeneralItem;
        }
    }

    private stop(evt, ui) {
        this._isDraggingItem = false;

        this.updateObjectModel(ui.item[0]);

        if (!sourceCode.k2Designer.utilities.isUndefinedOrNull(this.onRulePlacementChanged)) {
            let changed = false;
            for (let i = 0; i < this.items.length; i++) {
                const item = this.items[i] as IGeneralItem;
                if (item.isWorkflowAction) {
                    changed = item.handlerId !== this._originalAction.handlerId
                        || item.previousActionId !== this._originalAction.previousActionId
                        || item.level !== this._originalAction.level;
                    break;
                }
                else if (item.isOpenTaskCondition) {
                    changed = item.previousHandlerId !== this._originalAction.previousHandlerId;
                    break;
                }
            }

            this.onRulePlacementChanged.emit(changed);
        }
    }

    private change(evt, ui) {
        // make sure we reset the anchor position for the docking logic of the
        // workflow action
        this._refreshPosition = true;

        const insertIndex = this.getPlaceHolderIndex();
        if (insertIndex > -1) {
            const helper = this._rootElement[0].querySelector('.ui-sortable-helper');
            const ruleText = helper.querySelector('.rule-text');

            if (insertIndex === 0 || insertIndex === (this.items.length - 1)) {
                this._dragModelItem.level = 0;
                this.toggleRuleTextIndent(ruleText, 0);
            }
            else {
                const handled = false;
                const item =
                    <IGeneralItem>this._nonWorkflowActionItems[insertIndex - 1];

                let level = -1;
                if (item.itemType === GeneralItemTypes.else) {
                    // check if else has conditions
                    if (item.subItems.length > 0) {
                        level = item.subItems[0].level + 1;
                    }
                    else {
                        level = 1;
                    }
                }
                else if (item.itemType === GeneralItemTypes.condition) {
                    level = item.level + 1;
                }
                else if (item.itemType === GeneralItemTypes.forEach) {
                    level = 1;
                }
                else if (item.itemType === GeneralItemTypes.action) {
                    level = item.level;
                }

                this._dragModelItem.level = level;
                this.toggleRuleTextIndent(ruleText, level);
            }
        }
    }

    private sort(evt, ui) {
        const scrollContainer = this._rootElement[0].querySelector('.k2-scrollbar-container');

        const item = this._rootElement[0].querySelector('.workflow-item').parentElement;

        let top = this.getPixels(item.style.top);
        top += scrollContainer.scrollTop;

        // prevent from dragging out of control
        if (top < 0) {
            item.style.top = '0px';
        }
        else {
            item.style.top = `${top}px`;
        }

    }

    private getNonWorkflowActionItems(items: IGeneralItem[]): IGeneralItem[] {
        const currentItemsList: IGeneralItem[] = [];

        for (let i = 0; i < items.length; i++) {
            const item = items[i] as IGeneralItem;

            if (item && !item.isWorkflowAction && !item.isOpenTaskCondition) {
                // add cloned item
                currentItemsList.push({ ...item });
            }
        }
        return currentItemsList;
    }

    /**
     * Updates the whole object model
     *
     * @param draggedElement
     */
    private updateObjectModel(draggedElement: HTMLElement) {
        const items = this.items;

        const index = this.getUIWorkflowItemIndex();

        items.splice(this._startDragIndex, 1);
        items.splice(index, 0, this._dragModelItem);

        const ruleText = draggedElement.querySelector('.rule-text');

        let level = 0;
        if (ruleText.classList.contains('indent')) {
            level = 1;
        }
        else if (ruleText.classList.contains('double-indent')) {
            level = 2;
        }
        this._dragModelItem.level = level;

        this.updateWorkflowItem();
    }

    private getUIWorkflowItemIndex(): number {
        const uiSortable = this._rootElement[0].querySelector('.ui-sortable');

        const uiElements = uiSortable.querySelectorAll('.k2-smartform-rule-item ');
        const actionElementParent = uiSortable.querySelector('.workflow-item').parentElement;

        return this.getIndex(actionElementParent, uiElements);
    }

    private updateWorkflowItem() {
        if (this.parentContext.isUserForm) {
            this.updateWorkflowCondition();
        }
        else {
            this.updateWorkflowAction();
        }
    }

    private updateWorkflowCondition() {
        const ruleModelItems = this.items;
        const workflowItem = this.getWorkflowItem();
        const workflowItemIndex = ruleModelItems.indexOf(workflowItem);

        if (sourceCode.k2Designer.utilities.isUndefinedOrNull(workflowItem)) {
            return;
        }

        let previousHandler: IGeneralItem = null;

        if (workflowItemIndex > 0) {
            previousHandler = ruleModelItems[workflowItemIndex - 1] as IGeneralItem;
        }

        if (previousHandler && previousHandler.subItems.length > 0) {
            const elseBlock: IGeneralItem[] =
                previousHandler.subItems.filter((item) => item.itemType === GeneralItemTypes.else);

            if (elseBlock.length > 0) {
                previousHandler = elseBlock[0];
            }
        }

        workflowItem.previousHandlerId = previousHandler ? previousHandler.id : null;
    }

    // Update the current workflow action
    private updateWorkflowAction() {
        const ruleModelItems = this.items;

        const workflowAction = this.parentContext.workflowAction;

        for (let i = 0, len = ruleModelItems.length; i < len; i++) {

            const item = ruleModelItems[i] as IGeneralItem;

            if (item.isWorkflowAction) {

                if (i === this._originalActionIndex
                    && item.level === this._originalAction.level) {
                    // make sure properties are reset
                    item.handlerId = this._originalAction.handlerId;
                    item.previousActionId = this._originalAction.previousActionId;
                    return;
                }

                // handle edge cases first, first and last item
                if (i === 0) {
                    const nextItem = ruleModelItems[i + 1] as IGeneralItem;

                    // top-most action, no previous action exists
                    item.previousActionId = null;

                    if (nextItem.itemType === GeneralItemTypes.action) {
                        item.handlerId = nextItem.handlerId;
                    }
                    else {
                        item.handlerId = null;
                    }
                }
                else if (i === ruleModelItems.length - 1) {

                    const prevItem = ruleModelItems[i - 1] as IGeneralItem;

                    item.previousActionId = prevItem.id;
                    if (item.level === prevItem.level) {
                        item.handlerId = prevItem.handlerId;
                    }
                    else {
                        item.handlerId = null;
                    }
                }
                else {
                    // SmartForms will make sure that rules are well formed, so
                    // if the action is at the bottom, there will always be a previous action
                    // available, we just need to know if its on the same level or in different handler
                    const prevItem = ruleModelItems[i - 1] as IGeneralItem;
                    const nextItem = ruleModelItems[i + 1] as IGeneralItem;

                    if (sourceCode.k2Designer.utilities.isUndefinedOrNull(prevItem.handlerId)) {
                        item.previousActionId = null;

                        if (item.level === nextItem.level) {
                            item.handlerId = nextItem.handlerId;
                        }
                    }
                    else if (item.level === prevItem.level) {
                        item.handlerId = prevItem.handlerId;
                        item.previousActionId = prevItem.id;
                    }
                    else {
                        if (nextItem.itemType === GeneralItemTypes.action
                            && nextItem.level === item.level) {
                            item.handlerId = nextItem.handlerId;
                        }
                        else {
                            item.handlerId = null;
                            item.previousActionId = null;
                        }
                    }
                }

                workflowAction.handlerId = item.handlerId;
                workflowAction.previousActionId = item.previousActionId;
                break;
            }
        }
    }

    private getPixels(unit: string): number {
        if (unit.indexOf('px') > -1) {
            return parseInt(unit.substring(0, unit.length - 2));
        }
        else if (unit.indexOf('%') > -1) {
            return -1;
        }
        return parseInt(unit);
    }
}

export interface IPosition {
    x: number;
    y: number;
}
