import { Component, Inject, OnInit, Input, Output, EventEmitter, ElementRef } from '@angular/core';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../types/services/plugin/i-plugin-service';
import { ISelectionService } from '../../../types/services/selection/i-selection-service';
import { EnvironmentServiceToken } from '../../services/environment/environment-service-factory';
import { EventNotifierService } from '../../services/event-notifier/event-notifier.service';
import { PluginServiceToken } from '../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../services/resource/resource.service';
import { RulePluginService } from '../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../services/selection/selection-service-factory';
import { plugins } from '../../shared/constants/plugins';
import { RuleDesignerSelectionBaseComponent } from './base/rule-designer-selection-base/rule-designer-selection-base.component';

@Component({
    selector: 'k2-rules-designer',
    templateUrl: './rules-designer.component.html',
    styleUrls: ['./rules-designer.component.less']
})
export class RulesDesignerComponent extends RuleDesignerSelectionBaseComponent implements OnInit {
    @Input() uiComp: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    @Output() closePanel = new EventEmitter<void>();
    @Output() checkPanelActive = new EventEmitter<string>();

    rulesHandler: string;
    emptyRuleString: string;

    constructor(@Inject(EnvironmentServiceToken) private env: IEnvironmentService,
        @Inject(SelectionServiceToken) private selectionService: ISelectionService,
        protected rulePluginService: RulePluginService,
        @Inject(PluginServiceToken) private pluginService: IPluginService,
        protected resourceService: ResourceService,
        protected eventService: EventNotifierService,
        protected element: ElementRef<HTMLElement>) {
        super(selectionService, eventService, element);
    }

    ngOnInit(): void {
        super.ngOnInit();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    private get pluginStates() {
        return this.env.activeStudioItem.pluginStates;
    }

    isEmpty(): boolean {
        return !this.rulePluginService.contextModel;
    }

    isRightMostPanel(): boolean {
        return this.pluginService.isRightMostPanel(plugins.panels.rulesDesigner);
    }

    isRulesActive(): boolean {
        if (this.pluginStates[plugins.panels.rulesDesigner] === 'expanded') {
            this.rulesHandler = this.resourceService.getString('rulesDesigner_rulesContext_' + this.getRulesHandler().toLocaleLowerCase());

            const emptyRuleToken: string = 'rulesDesigner_rulesEmptyContext_' + this.getRulesHandler().toLocaleLowerCase();
            const emptyRuleString: string = this.resourceService.getString(emptyRuleToken);
            this.emptyRuleString = emptyRuleToken !== emptyRuleString ? emptyRuleString : this.resourceService.getString('rulesDesigner_rulesEmptyContext_default');
        }

        return this.pluginStates[plugins.panels.rulesDesigner] === 'expanded';
    }

    isNotValid(): boolean {
        return this.rulePluginService.isNewTab;
    }

    isCtxActive(): boolean {
        return this.pluginStates[plugins.panels.contextBrowser] === 'expanded';
    }

    toggleContextBrowser(): void {
        this.pluginService.toggleState(plugins.panels.contextBrowser);
    }

    isPanelActive(name: string): void {
        this.checkPanelActive.emit(name);
    }

    private getRulesHandler(): string {
        const contextHandlerName: string = this.rulePluginService?.contextHandler?.name || 'empty';
        return contextHandlerName;
    }

    /***
     * Toggles the state of the rules designer
     * */
    toggleRulesDesigner(): void {
        this.pluginService.toggleState(plugins.panels.rulesDesigner);
    }

    setSelection(event: Event): void {
        if (this.isSelected) {
            if ($(event.target).hasClass('rules-flexChild')) {
                this.isSelected = false;
            }
        }

        if (!this.isSelected) {
            this.selectionService.setSelection([], 'rule');
        }
    }

    hasAssociatePanels(): boolean {
        return this.pluginService.hasAssociatePanels();
    }

    onClosePanel(): void {
        this.closePanel.emit();
    }

    showContextBrowserToggleButton(): boolean {
        const panel: sourceCode.k2Designer.objectModel.plugin.IInstance = this.pluginService.getRightMostPanel();
        if (!panel || panel.plgnDef.name === this.uiComp.name) {
            return true;
        }
        return false;
    }
}
