import { Component, EventEmitter, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { IPopupConfiguration } from '../../../../types/models/popup/i-popup-configuration';
import { ResourceService } from '../../../services/resource/resource.service';
import { ActionPopupTemplate } from '../../../shared/enums/popup/action-popup-template';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-action-popup',
    templateUrl: './action-popup.component.html',
    styleUrls: ['./action-popup.component.less']
})
export class ActionPopupComponent extends BaseResourceComponent implements OnInit {
    @Input() config: IPopupConfiguration;
    @Input() template: ActionPopupTemplate;

    @Output() buttonClicked = new EventEmitter<any>();

    @ViewChild('OAuthError') oAuthErrorTemplate: TemplateRef<any>;
    @ViewChild('customError') customError: TemplateRef<any>;

    selectedTemplate: TemplateRef<any>;

    constructor(resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (this.template) {
            switch (this.template) {
                case ActionPopupTemplate.oAuth:
                    this.selectedTemplate = this.oAuthErrorTemplate;
                    break;
                default: this.selectedTemplate = this.customError;
            }
        }
    }

    onButtonClicked(button) {
        this.buttonClicked.emit(button);
    }
}
