import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { keyCodes } from 'src/app/shared/constants/general';
import { isUndefinedOrNull } from 'src/app/shared/utils/general';
import { ISelectableTile } from 'src/types/models/selectable-tile/i-selectable-tile';

@Component({
    selector: 'k2-tile-select',
    templateUrl: './tile-select.component.html',
    styleUrls: ['./tile-select.component.less']
})
export class TileSelectComponent implements OnInit {

    @Input() tiles: ISelectableTile[] = [];

    @Output() selectionChanged = new EventEmitter<{previous: ISelectableTile; current: ISelectableTile; element: HTMLElement}>();

    @ViewChild('tileSelectContainer') tilesContainer: ElementRef<HTMLInputElement>;

    private _isEnabled = true;
    private _selectedTile: ISelectableTile = null;
    private _isBusyHandlingSelectionEvent = false;

    constructor() { }

    /**
     * Gets the enabled state of the component
     */
    get isEnabled(): boolean {
        return this._isEnabled;
    }

    /**
     * Sets the enabled state of the component
     */
    set isEnabled(value: boolean) {
        if (this._isEnabled === value) {
            return;
        }

        this._isEnabled = value;

        this.tiles.forEach((tile: ISelectableTile) => tile.enabled = value);
    }

    /**
     * Set the selected value and automatically select the tile associated with that value if it exists
     */
    @Input()
    set selectedValue(value: any) {
        if (isUndefinedOrNull(value)) {
            return;
        }

        for (const tile of this.tiles) {
            if (tile.value === value) {
                this.onTileSelected(tile);
                break;
            }
        }
    }

    /**
     * Gets the value of the selected tile
     */
    get selectedValue(): any {
        if (this._selectedTile) {
            return this._selectedTile.value;
        }

        return null;
    }

    ngOnInit(): void {
        if (!this._selectedTile) {
            this._selectedTile = this.tiles.find((tile: ISelectableTile) => tile.selected);
        }
    }

    /**
     * Called when the component gains focus - will make all tiles focusable by giving them a tab index
     */
    onFocus(event: Event): void {
        if (!this.isEnabled) {
            return;
        }

        //enable tab index on all tiles
        const focusableElements = this.getFocusableTiles();

        if (focusableElements.length > 0
            && this.tilesContainer.nativeElement.getAttribute('tabindex') === '0') {

            const selectedElement = focusableElements.filter((el: HTMLElement) => el.classList.contains('selected'));
            if (selectedElement.length > 0) {
                selectedElement[0].focus();
            } else {
                focusableElements[0].focus();
            }
        }
    }

    /**
     * KeyDown handler to allow for arrow navigating around tiles
     *
     * @param event
     */
    onKeyDown(event: KeyboardEvent, tile?: ISelectableTile): void {
        if (!this.isEnabled) {
            return;
        }

        const focusedElement = document.activeElement;

        switch (event.keyCode) {
            case keyCodes.LEFT_ARROW:
            case keyCodes.RIGHT_ARROW:
            case keyCodes.DOWN_ARROW:
            case keyCodes.UP_ARROW:

                const nextTile = this.getNextNavigateTile(<HTMLElement>focusedElement, event.keyCode);
                if (nextTile !== null) {
                    nextTile.focus();
                }
                break;
            case keyCodes.ENTER:
            case keyCodes.SPACE:
                this.onTileSelected(tile);
                event.preventDefault();
                event.stopPropagation();
                break;
        }

    }

    /**
     * Handler to prevent mouse events from interrupting focus of child tiles
     *  if the user clicks on blank space in the component itself instead of a tile
     *
     * @param event
     */
    onMouseDown(event: MouseEvent): void {
        if (event.target !== this.tilesContainer.nativeElement) {
            return;
        }

        event.preventDefault();
        event.stopPropagation();
    }

    /**
     * Triggers when this tile has been clicked
     */
    onTileClicked(tile: ISelectableTile) {
        if (tile.enabled === true && tile.selected === false) {
            this.onTileSelected(tile);
        }
    }

    /**
     * Called when a child tile gets focus
     */
    onTileFocus(tile: ISelectableTile) {
        tile.focused = true;
        this.tilesContainer.nativeElement.removeAttribute('tabindex');
    }

    /**
     * Called when a child tile loses focus - need to ensure tab indexing is correct to allow tab navigation
     */
    onTileBlur(tile: ISelectableTile) {
        tile.focused = false;
        // we need to know where the focus is going to to see if we should reset 'focusability' of the component
        // unfortunately setTimeout is the best option so we can see what the next active element is
        setTimeout(() => {
            const focusedElement = document.activeElement;
            if (this.tilesContainer.nativeElement.contains(document.activeElement)) {
                this.tilesContainer.nativeElement.removeAttribute('tabindex');
            } else {
                this.tilesContainer.nativeElement.setAttribute('tabindex', '0');
            }
        }, 1);
    }

    /**
     * Determines the next navigatable tile based on the keyCode used
     *
     * @param focusedElement
     * @param keyCode
     */
    private getNextNavigateTile(focusedElement: HTMLElement, keyCode: number): HTMLElement {

        let focusedContainer = focusedElement;
        //IE11 will report the activeElement as a non-focusable sub element of the actual tile element, need to find the correct element to find the next tile.
        if (!focusedElement.classList.contains('selectable-tile-container')) {
            focusedContainer = focusedElement.closest('.selectable-tile-container');
        }

        if (isUndefinedOrNull(focusedContainer)) {
            return null;
        }

        const allTiles = this.getFocusableTiles(true);
        const tilesPerRow = this.getTilesPerRow(allTiles);
        const focusedIndex = allTiles.indexOf(focusedContainer);

        let jumpValue = 0;

        switch (keyCode) {
            case keyCodes.LEFT_ARROW:
                jumpValue = -1;
                break;
            case keyCodes.RIGHT_ARROW:
                jumpValue = 1;
                break;
            case keyCodes.DOWN_ARROW:
                jumpValue = tilesPerRow;
                break;
            case keyCodes.UP_ARROW:
                jumpValue = -tilesPerRow;
                break;
        }

        let goalIndex = focusedIndex + jumpValue;
        if (goalIndex > allTiles.length || goalIndex < 0) {
            return null;
        }

        const maxAttempts = allTiles.length;
        let attempts = 0;

        while (goalIndex >= 0
            && goalIndex < allTiles.length
            && allTiles[goalIndex].classList.contains('disabled')
            && attempts < maxAttempts) {

            goalIndex += jumpValue;
            attempts += 1;
        }

        if (goalIndex >= 0 && goalIndex < allTiles.length && allTiles[goalIndex] !== focusedContainer) {
            return allTiles[goalIndex];
        }

        return null;
    }

    /**
     * Calculates the current number of tiles per row - dynamic to allow for responsive calculations
     *
     * @param tileElements
     */
    private getTilesPerRow(tileElements: HTMLElement[]): number {
        let currentCount = 0;
        let highestCount = 0;

        let lastOffsetTop = -1;

        for (let i = 0; i < tileElements.length; i++) {
            const offsetTop = tileElements[i].offsetTop;

            if (lastOffsetTop !== offsetTop) {
                lastOffsetTop = offsetTop;
                currentCount = 1;
            } else {
                currentCount += 1;
            }

            highestCount = highestCount > currentCount ? highestCount : currentCount;
        }

        return highestCount;
    }

    /**
     * Gets all tiles in thee component
     *
     * @param includeDisabled - include disabled tiles in returned collection
     */
    private getFocusableTiles(includeDisabled: boolean = false): HTMLElement[] {
        let selector = '.selectable-tile-container';
        if (!includeDisabled) {
            selector += ':not(.disabled)';
        }
        return Array.from(this.tilesContainer.nativeElement.querySelectorAll(selector));
    }

    /**
     * Callback invoked by a tile when it's getting selected, will call selectionChanged callback if one is registered
     *
     * @param tile the tile that was just selected
     */
    private onTileSelected(tile: ISelectableTile): void {

        if (this._isBusyHandlingSelectionEvent) {
            return;
        }

        this._isBusyHandlingSelectionEvent = true;

        try {

            const previousSelection: ISelectableTile = this._selectedTile;

            if (previousSelection === tile) {
                return;
            }

            if (!!previousSelection) {
                previousSelection.selected = false;
            }
            tile.selected = true;

            this._selectedTile = tile;

            this.selectionChanged.emit({
                previous: previousSelection,
                current: tile,
                element: <HTMLElement>this.tilesContainer.nativeElement.querySelector('#' + tile.id)
            });
        } finally {
            this._isBusyHandlingSelectionEvent = false;
        }
    }
}
