import { Component, OnInit, Inject, Input } from '@angular/core';
import { IEnvironmentService } from '../../../../../types/services/environment/i-environment-service';
import { IUserOptions } from '../../../../../types/services/user-options/i-user-options-service';
import { EnvironmentServiceToken } from '../../../../services/environment/environment-service-factory';
import { UserOptionsServiceToken } from '../../../../services/user-options/user-options-service-factory';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';

export interface ITip {
    val: boolean;
    title: string;
}

@Component({
    selector: 'app-tab-item-options',
    templateUrl: './tab-item-options.component.html',
    styleUrls: ['./tab-item-options.component.less']
})
export class TabItemOptionsComponent implements OnInit {

    @Input() public getString: (resourceId: string, collectionId?: string) => string;

    public tips: { [key: number]: ITip };
    public objectKeys: (o: object) => string[];
    public k2ToggleButtonClicked: (checked: boolean, model: any) => void;
    public chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    constructor(@Inject(UserOptionsServiceToken) private _userOptionsService: IUserOptions) {

        this.tips = {};
        for (const key in this._userOptionsService.userOptions.tips) {
            const keyNum: number = parseInt(key, 10);
            this.tips[key] = {
                val: this._userOptionsService.userOptions.tips[key],
                title: sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypesImplementor(keyNum)
            };
        }
        this.objectKeys = Object.keys;
        this.k2ToggleButtonClicked = this._k2ToggleButtonClicked.bind(this);
    }

    ngOnInit(): void {
    }

    public k2SetAlltipsClicked(): void {
        this._userOptionsService.setAllTipsValue();

        for (const key in this._userOptionsService.userOptions.tips) {
            this._userOptionsService.userOptions.tips[key] = true;
            this.tips[key].val = true;
        }
    }

    private _k2ToggleButtonClicked(checked: boolean, model: any): void {
        this._userOptionsService.setTipValue(<sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypes>(<number>model), checked);
        this.tips[model].val = checked;
    };

    public getUserTipTitle(key: any): string {
        return sourceCode.k2Designer.objectModel.workflow.itemProvider.UserOptionsTipTypesImplementor(<number>key);
    }

    get isCanvasSelectionInverted(): boolean {
        return this._userOptionsService?.userOptions?.canvasSelectionInverted;
    }

    set isCanvasSelectionInverted(val: boolean) {
        if (this.isCanvasSelectionInverted !== val) {
            this._userOptionsService?.changeCanvasSelectionInverted();
        }
    }
}
