import { Component, Inject, OnDestroy, OnInit} from '@angular/core';
import { debounceTime, distinctUntilChanged, Subject, Subscription } from 'rxjs';
import { BaseResourceComponent } from 'src/app/components/base-resource/base-resource.component';
import { EnvironmentServiceToken } from 'src/app/services/environment/environment-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { ProcessService } from 'src/app/services/process/process.service';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { WorkflowNameChangedEvent } from 'src/app/shared/constants/events';
import { defaultDebounceTime } from 'src/app/shared/constants/general';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from 'src/app/shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { ProcessPriorityTypes } from 'src/types/objectModel/workflow/process/k2Process/process-priority-types';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IProcess } from '../../../../../../../../types/models/process/i-process';

@Component({
    selector: 'k2-process-settings-tab',
    templateUrl: './process-settings-tab.component.html',
    styleUrls: ['./process-settings-tab.component.less']
})
export class ProcessSettingsTabComponent extends BaseResourceComponent implements OnInit, OnDestroy {

    process: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process;
    priorityItems: IDropDownMenuItem[];
    selectedItem: IDropDownMenuItem;
    selectedImplementor: string;
    currentProcessTitle: string;
    watermark: string;
    isNameReadOnly = true;

    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    private titleSubject = new Subject<string>();
    private subscriptions = new Subscription();

    constructor(@Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        private processService: ProcessService,
        private eventNotifier: EventNotifierService,
        resourceService: ResourceService) {
        super(resourceService);

        this.createPriorityItems();

        this.subscriptions.add(
            this.titleSubject.pipe(
                debounceTime(defaultDebounceTime),
                distinctUntilChanged()
            ).subscribe((value: string) => {
                this.validateTitle();
            })
        );
    }

    ngOnInit(): void {
        this.process = this.processService.root;

        if (this.currentProcessTitle !== this.process.title){
            this.currentProcessTitle = this.process.title;
        }

        //Configuration is not always available, as the process Json still needs to be applied to the model
        if (this.process.configuration) {
            if (!this.process.configuration.processPriority) {
                for (const priorityItem of this.priorityItems) {
                    if (priorityItem.key === ProcessPriorityTypes.high.toString()) {
                        this.selectedItem = priorityItem;
                        this.selectedImplementor = priorityItem.key.toString();
                        priorityItem.isSelected = true;
                        break;
                    }
                }
            }
            else {
                const priorityKey = this.process.configuration.processPriority.toString();

                for (const priorityItem of this.priorityItems) {
                    if (priorityKey === priorityItem.key.toString()) {
                        this.selectedItem = priorityItem;
                        this.selectedImplementor = priorityItem.key.toString();
                        priorityItem.isSelected = true;
                        break;
                    }
                }
            }
        }

        this.isNameReadOnly = this.environmentService.query.namereadonly === 'true' ? true : false;

        this.subscriptions.add(
            this.eventNotifier.subscribe(WorkflowNameChangedEvent, (newValue: string)=>{
                if (this.currentProcessTitle !== this.process.title){
                    this.currentProcessTitle = this.process.title;
                }
            })
        );
    }

    ngOnDestroy(){
        this.subscriptions.unsubscribe();
    }

    validateTitle(): void {
        if (this.process.title !== this.currentProcessTitle) {

            // If it passed, the process name will be updated by the processService and should be the same.
            // If it failed, originalProcessTitle from the validationcallback would be the original and current process name.
            this.processService.setTitle(this.currentProcessTitle.slice()).catch((originalProcessTitle: string) => {
                this.currentProcessTitle = originalProcessTitle;
            });
        }
    }

    /**
     * Update the process descrion when the text changed
     *
     * @param text
     */
    onDescriptionChanged(text: string): void {
        this.process.description = text;
    }

    /**
     * When a dropdown item is selected
     *
     * @param item
     */
    itemSelected(item: IDropDownMenuItem): void {
        this.selectedItem = item;
        this.selectedImplementor = item.key;

        this.process.configuration.processPriority = parseInt(item.key, 10) as ProcessPriorityTypes;
    }

    /**
     * this function gets called on focus out of the title textbox and reset to previou value if empty value is detected
     */
    checkForEmptyTitle(): void {
        if (!this.currentProcessTitle) {
            this.currentProcessTitle = this.process.title.slice();
        }
    }

    /**
     * Populate the priority dropdown
     *
     * @returns Array of IDropDownMenuItems
     */
    createPriorityItems(): IDropDownMenuItem[] {
        const priorityItems: IDropDownMenuItem[] = [];

        priorityItems.push({
            key: ProcessPriorityTypes.low.toString(),
            icon: 'icon-set-dropdownset ic-priority_low',
            isSeparator: false,
            title: this.resourceService.getString('priority_low')
        });

        priorityItems.push({
            key: ProcessPriorityTypes.med.toString(),
            icon: 'icon-set-dropdownset ic-priority_medium',
            isSeparator: false,
            title: this.resourceService.getString('priority_med')
        });

        priorityItems.push({
            key: ProcessPriorityTypes.high.toString(),
            icon: 'icon-set-dropdownset ic-priority_high',
            isSeparator: false,
            title: this.resourceService.getString('priority_high')
        });

        this.priorityItems = priorityItems;

        return priorityItems;
    }

    /**
     * Do validation when the model changes
     *
     * @param val
     */
    ngModelChange(val: string){
        this.titleSubject.next(val);
    }
}
