import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { rules } from 'src/app/shared/constants/rules';
import { IConfiguredSimpleExpression } from '../../../../../types/models/rules/i-configured-simple-expression';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';

@Component({
    selector: 'k2-simple-expression',
    templateUrl: './simple-expression.component.html',
    styleUrls: ['./simple-expression.component.less']
})
export class SimpleExpressionComponent extends ExpressionBaseComponent implements OnInit {

    @Input() public index: number;
    @Input() public hideDragGrip = false;
    @Input() public hideOperatorCommands = false;

    @Output() public droppedGroupConditions: EventEmitter<number> = new EventEmitter<number>();

    public simpleExpressionOperatorDropdownItems: IDropDownMenuItem[] = [];

    public selectedOperatorItem: IDropDownMenuItem;
    public selectedLeftItem: IDropDownMenuItem;

    public selectedKey: string;
    public getDragImageFn;
    public operatorHover = false;

    // Enum imports for use in templates
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    constructor(@Inject(SelectionServiceToken) selectionService: ISelectionService,
        rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        eventService: EventNotifierService,
        resourceService: ResourceService,
        private _rootElement: ElementRef) {
        super(selectionService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, _rootElement, resourceService);
    }

    public get simpleExpression(): IConfiguredSimpleExpression {
        return this.expressionModel as IConfiguredSimpleExpression;
    }

    ngOnInit(): void {
        super.ngOnInit();

        this.undoRedoService.forceDisable();

        this.getDragImageFn = super.getDragImage.bind(this);

        if (!this.simpleExpression.logicalOperator) {
            this.simpleExpression.logicalOperator = rules.defaultOperator.operatorName;
            this.simpleExpression.operatorIcon = rules.defaultOperator.operatorIcon;
        }
        else if (!this.simpleExpression.operatorIcon) {
            this.simpleExpression.operatorIcon = rules.icons.operatorIconBase + this.simpleExpression.logicalOperator.toLowerCase();
        }

        this.setupOperators();

        if (this.expressionModel.createdFromIfDrop !== true && this.expressionModel.isSelectable) {
            this._rootElement.nativeElement.focus();
        }

        this.undoRedoService.resetForceDisable();
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        this.rulePluginService.removeRuleDesignerItem(this);
    }

    public onDrop(): void {
        this.droppedGroupConditions.emit(this.index);
    }

    /**
     * Populates the operators dropdown menu items collection
     */
    private setupOperators(): void {
        const config = this.rulePluginService.getExpressionConfiguration(this.expressionModel) as
            sourceCode.k2Designer.objectModel.rulePlugins.IExpressionConfiguration;

        super.populateLogicalOperators(config.operators, this.simpleExpressionOperatorDropdownItems, this.simpleExpression.logicalOperator);

        if (!this.hideOperatorCommands) {
            super.populateOperatorCommands(config.commands, this.simpleExpressionOperatorDropdownItems);
        }
    }

    public onItemSelected(item: IDropDownMenuItem) {
        this.undoRedoService.addUndoMarker('-- group boolean operator selected --');

        this.simpleExpression.logicalOperator = item.key;
        this.simpleExpression.operatorIcon = item.icon;

        this.selectedOperatorItem = item;

        this.updateMenuState();

        this._rootElement.nativeElement.getElementsByClassName('condition-operator')[0].focus();
    }

    //Operator dropdown items
    public getSimpleExpressionOperatorDropdownItems(): IDropDownMenuItem[] {
        return this.simpleExpressionOperatorDropdownItems;
    }

    public getOperatorIcon() {
        let iconClass = this.simpleExpression.operatorIcon;

        let iconSet = this.states.isExist(RuleControlStates.operatorHover) ? RuleControlStates.operatorHover : '';
        iconSet = this.states.isExist(RuleControlStates.menuActive) ? RuleControlStates.menuActive : iconSet;

        const operandSelected = this.states.isExist(RuleControlStates.selected) ? ' selected' : '';
        const selected = this.states.isExist(RuleControlStates.selected) ? ' colour_dark' : '';

        switch (iconSet) {
            case RuleControlStates.operatorHover:
            case RuleControlStates.menuActive:
                iconClass = 'icon icon-set-dropdownset icon-size16 ic-dropdown' + selected;
                break;
            default:
                iconClass += operandSelected;
                break;
        }

        return iconClass;
    }

    public updateMenuState() {
        if (this.states.isExist(RuleControlStates.menuActive)) {
            this.states.remove(RuleControlStates.menuActive);
        } else {
            this.states.add(RuleControlStates.menuActive);
        }
    }

    public addDraggingState = super.addDraggingState.bind(this);

    public removeDraggingState = super.removeDraggingState.bind(this);

    public operatorOnMouseEnter() {
        this.operatorHover = true;
        this.states.add(RuleControlStates.hover);
        this.states.add(RuleControlStates.operatorHover);
    }

    public operatorOnMouseLeave() {
        this.operatorHover = false;
        this.states.remove(RuleControlStates.hover);
        this.states.remove(RuleControlStates.operatorHover);
    }

    public operatorOnFocus() {
        this.setSelected(true);
        this.states.add(RuleControlStates.operatorSelected);
    }

    public operatorOnBlur() {
        this.states.remove(RuleControlStates.operatorSelected);
    }

    public gripOnFocus() {
        this.setSelected(true);
    }

    public addState(state: string) {
        this.states.add(state);
    }

    public removeState(state: string) {
        this.states.remove(state);
    }

}
