import { Component, Inject, Input, OnInit } from '@angular/core';
import { IConfigPanelTab } from '../../../../../../../types/models/config-panel/i-config-panel-tab';
import { IMergeActivityConfiguration } from '../../../../../../../types/models/process/activities/configuration/i-merge-activity-configuration';
import { IActivity } from '../../../../../../../types/models/process/activities/i-activity';
import { IConfiguredMergeStatement } from '../../../../../../../types/models/rules/i-configured-merge-statement';
import { IConfigPanelService } from '../../../../../../../types/services/config-panel/i-config-panel-service';
import { IEnvironmentService } from '../../../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../../../types/services/plugin/i-plugin-service';
import { IValidationService } from '../../../../../../../types/services/validation/ivalidation.service';
import { IListItemIconData } from '../../../../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { IValidatorItem } from '../../../../../../../types/validation/i-validator-item';
import { ConfigPanelServiceToken } from '../../../../../../services/config-panel/config-panel-service-factory';
import { EnvironmentServiceToken } from '../../../../../../services/environment/environment-service-factory';
import { PluginServiceToken } from '../../../../../../services/plugin/plugin-service-factory';
import { ProcessService } from '../../../../../../services/process/process.service';
import { ResourceService } from '../../../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../../../services/ruleplugin/rule-plugin-service';
import { UndoRedoServiceToken } from '../../../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../../../services/validation/validation-service-factory';
import { MergeLogicTypes } from '../../../../../../shared/enums/activities/merge-logic-types';
import { StepTemplates } from '../../../../../../shared/enums/activities/step-template-names';
import { ChooseControlTypes } from '../../../../../../shared/enums/choose-control/choose-control-types';
import IDropDownMenuItem from '../../../../../../shared/enums/drop-down/drop-down-menu-item';
import { ValidationTypes } from '../../../../../../shared/enums/model/validation-types';
import { RuleContextHandlerNames } from '../../../../../../shared/enums/rules/rule-context-handler-names';
import { RulePluginBaseComponent } from '../../../base/rule-plugin-base/rule-plugin-base.component';

@Component({
    selector: 'k2-merge-step',
    templateUrl: './merge-step.component.html',
    styleUrls: ['./merge-step.component.less']
})
export class MergeStepComponent extends RulePluginBaseComponent implements OnInit {

    @Input() activity: IActivity;
    /**
     * NOTE/TODO: When config panel feature is worked on, the config panel tab should be set from parent component
     * (not from child as is currently)
     */
    @Input() configPanelTab: IConfigPanelTab;

    noSplitStepSelectedTemplate = '';
    availableSplitStepsDropdownItems: IDropDownMenuItem[] = [];
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    mergeLogicTypes: typeof MergeLogicTypes = MergeLogicTypes;
    /**
     * True if any activity steps between merge and selected split step should expire
     */
    canExpireActivitySteps = false;

    tabIndexMergeStepLogicRadioButtons = 0;

    /**
     * Remember the logic selection to retain custom rules
     */
    private previousLogicSelection: MergeLogicTypes;

    constructor(protected resourceService: ResourceService,
        @Inject(ValidationServiceToken) protected validationService: IValidationService,
        private processService: ProcessService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        protected rulePluginService: RulePluginService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken) protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService) {

        super(validationService, configPanelService, rulePluginService, undoRedoService,
            pluginService, environmentService, resourceService, RuleContextHandlerNames.MergeRule);
    }

    isToggleBtnHover = false;

    leftIconData: IListItemIconData = {
        isWrappedInContentDiv: true,
        customClassFlexRow: 'list-item-icons',
        iconClass: 'icon icon-size24 icon-set-picker ic-splittask',
        isVertical: true
    };

    ngOnInit(): void {
        this.noSplitStepSelectedTemplate = this.getNoSplitStepSelectedTemplate();
        this.availableSplitStepsDropdownItems = this.getSplitSteps();
        this.previousLogicSelection = this.mergeLogicType;

        if (!this.mergeActivityConfig.selectedSplitStep) {
            //Create empty selected split step with default logic (wait for all)
            this.mergeActivityConfig.selectedSplitStep = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                smartFields: []
            });
            this.mergeActivityConfig.checkWaitForAll = true;
        }
        //Validate (this has to be done after creating empty selected split step if not set)
        this.validateSelectedSplitStep();

        //Set activity configuration as tab validation object (this cannot be done in the 'super', because inputs are not accessible yet)
        this.setTabValidationObject(this.activity.configuration, this.configPanelTab);
    }

    /**
     * Set selected split step
     *
     * @param selectedStep
     */
    selectSplitItem(selectedStep: IDropDownMenuItem): void {
        this.undoRedoService.addUndoMarker('--select split step--');

        this.mergeActivityConfig.selectedSplitStepReference = selectedStep.key;

        this.mergeActivityConfig.selectedSplitStep.smartFields.push(
            sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                text: this.mergeActivityConfig.selectedSplitStepTitle,
                title: this.mergeActivityConfig.selectedSplitStepTitle
            })
        );
        this.validateSelectedSplitStep();
    }

    get mergeActivityConfig(): IMergeActivityConfiguration {
        return this.activity.configuration as unknown as IMergeActivityConfiguration;
    }

    expireActivePath(shouldExpire: boolean): void {
        this.mergeActivityConfig.expireActivePath = shouldExpire;
    }

    selectRuleType(type: MergeLogicTypes): void {
        this.undoRedoService.addUndoMarker('--change merge step configuration--');

        this.undoRedoService.addCustomUndoEvent((): boolean => {

            this.validationService.forceAllPassValidation(this.mergeActivityConfig.outcomeRule);

            return true;
        }, null, '-- remove validation from merge rule --');

        //Only custom logic is allowed to choose if activity steps should expire (toggle the expireActivePath setting)
        this.canExpireActivitySteps = (type === MergeLogicTypes.custom);
        //Only 'Wait for First' logic expires activity steps by default
        this.mergeActivityConfig.expireActivePath = (type === MergeLogicTypes.waitForFirst);

        // Only custom should display the rule designer
        if (type !== MergeLogicTypes.custom) {
            this.loadEmptyRuleDesigner();

            //set the outcome rule statements to be undefined.
            if (this.mergeActivityConfig.outcomeRule) {
                if (this.previousLogicSelection === MergeLogicTypes.custom) {
                    this.rulePluginService.setCustomRuleStatements(this.ruleContextHandlerName + this.mergeActivityConfig.getId(), this.mergeActivityConfig.outcomeRule.statements);
                }
                this.mergeActivityConfig.outcomeRule.statements = null;
            }

            this.validationService.forceAllPassValidation(this.mergeActivityConfig.outcomeRule);

        }

        this.onRuleTypeSelected();

        this.previousLogicSelection = type;
    }

    get mergeLogicType(): MergeLogicTypes {
        if (this.mergeActivityConfig.checkWaitForAll) {
            return MergeLogicTypes.waitForAll;
        }
        if (this.mergeActivityConfig.checkWaitForFirst) {
            return MergeLogicTypes.waitForFirst;
        }
        if (this.mergeActivityConfig.checkNoLogic) {
            return MergeLogicTypes.noLogic;
        }
        if (this.mergeActivityConfig.checkCustom) {
            return MergeLogicTypes.custom;
        }
        return MergeLogicTypes.waitForAll; //default
    }

    protected refreshTabIndexes(): void {
        this.tabIndexMergeStepLogicRadioButtons = this.mergeActivityConfig.checkCustom === true ? 0 : -1;
    }

    public toggleRulesDesigner(): void {
        super.toggleRulesDesigner();
    }

    /**
     * Populate the merge rules in the rule designer
     * */
    protected loadAndPopulateRuleDesigner(): void {

        this.setSavedCustomStatements(this.mergeActivityConfig, this.mergeActivityConfig.outcomeRule);

        if (!this.mergeActivityConfig?.outcomeRule?.statements || this.mergeActivityConfig.outcomeRule.statements.length === 0) {

            const defaultRule = this.rulePluginService.createDefaultRule(this.ruleContextHandlerName);
            this.mergeActivityConfig.outcomeRule = defaultRule;

            //then statement default to merge
            const thenStatement = (defaultRule.statements[0] as IConfiguredMergeStatement).thenStatements[0];
            thenStatement.isMergeActionResult = true;

            //else statement default to wait
            const elseStatement = (defaultRule.statements[0] as IConfiguredMergeStatement).elseStatements[0];
            elseStatement.isMergeActionResult = false;
        }

        this.rulePluginService.setDesignerContext(this.ruleContextHandlerName, this.mergeActivityConfig.outcomeRule);
        this.rulePluginService.renderCanvas();
    }

    /**
     * Depending on the merge logic configured, display populated or empty rules designer
     * */
    protected renderRuleDesigner(): void {
        if (this.mergeActivityConfig.checkCustom) {
            this.loadAndPopulateRuleDesigner();
        }
        else {
            this.loadEmptyRuleDesigner();
        }
    }

    private validateSelectedSplitStep(): void {
        this.validationService.evaluate([ValidationTypes.Required],
            {
                modelObject: this.mergeActivityConfig.selectedSplitStep,
                propertyName: 'smartFields',
                validationFailedMessage: 'resource_service_empty_field',
                validationResourceTokens: ['resource_service_field_name_link_to', 'configpanel_tabs_tooltips_title_LogicalMergeShapeTab'],
            } as IValidatorItem);
        this.validationService.reevaluateErrors(this.mergeActivityConfig.errorInformation);
    }

    private getNoSplitStepSelectedTemplate(): string {
        const message = this.getString('merge_step_empty_dropDown');
        return '<div class=\'empty-state-icon\'><span class=\'icon icon-set-steps icon-size24 ic-split\'></span></div><div class=\'empty-state-message\'>' + message + '</div>';
    }

    /**
     * Return list of dropdown items containing all split steps on the process
     * */
    private getSplitSteps(): IDropDownMenuItem[] {
        const items: IDropDownMenuItem[]= [];
        const proc = this.processService.root;
        const splitSteps = proc.nodes.filter(n => n.ui?.template === StepTemplates.SplitStep.toString());
        splitSteps.forEach((splitStep) => {
            const splitStepDropdownItem: IDropDownMenuItem = {
                icon: 'icon-size24 icon-set-picker ic-splittask',
                title: splitStep.title,
                key: splitStep.getReference(),
                isSelected: this.mergeActivityConfig.selectedSplitStepReference === splitStep.getReference()
            };

            items.push(splitStepDropdownItem);
        });

        return items;
    }
}
