import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockCanvasService } from '../../../testing/mock-services/canvas-service';
import { ICanvasService } from '../../../types/services/canvas/i-canvas.service';

const canvasServiceFactory = () => environment.devHarness === true
    ? new MockCanvasService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.CanvasService);

export const CanvasServiceToken = new InjectionToken<ICanvasService>('ICanvasService');

export const CanvasServiceFactory = {
    provide: CanvasServiceToken,
    useFactory: canvasServiceFactory,
    deps: [

    ]
};
