import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockUndoRedoService } from '../../../testing/mock-services/undo-redo-service';


const undoRedoServiceFactory = () => environment.devHarness === true
    ? new MockUndoRedoService()
    : sourceCode.k2Designer.core.environmentService.undoRedoService;

export const UndoRedoServiceToken = new InjectionToken<IUndoRedoService>('IUndoRedoService');

export const UndoRedoServiceFactory = {
    provide: UndoRedoServiceToken,
    useFactory: undoRedoServiceFactory,
    deps: []
};
