import { Component, Input, OnInit, Output, EventEmitter, HostBinding, HostListener } from '@angular/core';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';

@Component({
    selector: '[k2DropDownMenuItem]',
    templateUrl: './drop-down-menu-item.component.html',
})
export class DropDownMenuItemComponent implements OnInit {

    @Input() isFirst: boolean;
    @Input() isLast: boolean;
    @Input() isMultiSelect: boolean;
    @Input() item: IDropDownMenuItem;
    @Input() innerItemComponent: DropdownMenuItemTemplates;

    @Output()
    public onItemSelected: EventEmitter<IDropDownMenuItem>
                        = new EventEmitter<IDropDownMenuItem>();

    @HostBinding('class.separator')
    private isSeparator = false;

    @HostBinding('class.disabled')
    private isDisabled = false;

    @HostBinding('class.hover')
    public dropdownHover: boolean;

    public dropdownMenuItemTemplates: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    constructor() { }

    ngOnInit(): void {
        this.isSeparator = this.item.isSeparator;
        this.isDisabled = this.item.isDisabled;
    }

    @HostListener('mouseup', ['$event'])
    selectItem(evt): void{
        evt.stopPropagation();

        if(typeof this.item.onSelected === 'function'){
            this.item.onSelected(this.item);
        }

        this.onItemSelected.emit(this.item);
    }

    @HostListener('mouseenter')
    onMouseEnter() {
        this.dropdownHover = true;
    }

    @HostListener('mouseleave')
    onMouseLeave() {
        this.dropdownHover = false;
    }

}
