import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ISmartFormItemReference } from 'src/types/models/smart-forms/i-smart-form-item-reference';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IRemoteEventReceiver } from 'src/types/uiComponents/pluginWizards/IRemoteEventReceiver';
import { IRemoteEventReceivers } from 'src/types/uiComponents/pluginWizards/IRemoteEventReceivers';
import { ISharePointSettingResult } from 'src/types/uiComponents/pluginWizards/ISharePointSettingsResult';
import { ISharePointContext } from '../../../../types/uiComponents/pluginWizards/ISharePointContext';
import { EnvironmentServiceToken } from '../../environment/environment-service-factory';

@Injectable({
    providedIn: 'root'
})
export class SharePointService {

    constructor(private http: HttpClient, @Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService) { }

    /**
     * Helper method to call to determine pages, buttons and page indicators.
     *
     * @param pluginContext
     */
    detectChanges(scopePluginContext: ISharePointContext): void {

        //Build event string for back-end ; seperated.
        scopePluginContext.eventReceiverString = '';
        for (let i = 0; i < scopePluginContext.remoteEventReceivers.EventDataCollection.length; i++) {
            if (scopePluginContext.remoteEventReceivers.EventDataCollection[i].Selected) {
                scopePluginContext.eventReceiverString = scopePluginContext.eventReceiverString + scopePluginContext.remoteEventReceivers.EventDataCollection[i].Id + ';';
            }
        }

        //Remove the last ;
        if (scopePluginContext.eventReceiverString.length > 0) {
            scopePluginContext.eventReceiverString = scopePluginContext.eventReceiverString.substring(0, scopePluginContext.eventReceiverString.length - 1);
        }
    }

    /**
     * Save sharepoint settings
     *
     * @param serviceInstance
     * @param listId
     * @param subSiteRelativeUrl
     * @returns
     */
    saveSharePointSettings(serviceInstance: string, listId: string, subSiteRelativeUrl: string): Observable<ISharePointSettingResult> {

        return new Observable<ISharePointSettingResult>(subscriber => {

            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService?.currentEnvironment.hostId)
                    .set('serviceInstance', serviceInstance)
                    .set('listId', listId)
                    .set('relativeUrl', subSiteRelativeUrl)
            };

            this.http
                .get<string>('api/sharePoint/SaveSharePointSettings', options)
                .subscribe({
                    next: (settingResult: string) => {
                        subscriber.next(JSON.parse(settingResult));
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Save a SharePoint workflow setting, returns the workflow ID
     *
     * @param workflowName
     * @param listId
     * @param siteUrl
     * @param settingsId
     * @param smartObjectName
     * @param isDocumentLibrary
     * @param eventReceivers
     */
    saveSharePointWorkflowSetting(workflowName: string, listId: string, siteUrl: string,
        settingsId: string, smartObjectName: string, isDocumentLibrary: boolean,
        eventReceivers: string): Observable<string> {

        return new Observable<string>(subscriber => {

            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
                    .set('workflowName', workflowName)
                    .set('listId', listId)
                    .set('siteUrl', siteUrl)
                    .set('settingsId', settingsId)
                    .set('smartObjectName', smartObjectName)
                    .set('isDocumentLibrary', isDocumentLibrary)
                    .set('eventReceivers', eventReceivers)
            };

            this.http
                .get<string>('api/sharePoint/saveSharePointWorkflowSetting', options)
                .subscribe({
                    next: (workflowId: string) => {
                        subscriber.next(workflowId);
                        subscriber.complete();
                    },
                    error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Gets item references for a specific smartobject
     *
     * @param smartObjectName
     * @param isDocLib
     * @returns
     */
    getItemReferences(smartObjectName: string, isDocLib: boolean): Observable<ISmartFormItemReference[]> {
        return new Observable<ISmartFormItemReference[]>(subscriber => {
            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
                    .set('smartObjectSystemName', smartObjectName)
                    .set('isDocLib', isDocLib)
            };

            this.http
                .get<string>('api/sharePoint/getItemReferences', options)
                .subscribe({
                    next: (itemReferences: string) => {
                        subscriber.next(JSON.parse(itemReferences));
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Gets sharepoint available events
     *
     * @returns
     */
    getSharePointAvailableEvents(): Observable<IRemoteEventReceivers> {
        return new Observable<IRemoteEventReceivers>(subscriber => {
            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
            };

            this.http
                .get<string>('api/sharePoint/GetSharePointAvailableEvents', options)
                .subscribe({
                    next: (events: string) => {
                        subscriber.next(JSON.parse(events));
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Get sharepoint setting events
     *
     * @param settingId
     * @returns
     */
    getSharePointSettingEvents(settingId: string): Observable<IRemoteEventReceivers> {
        return new Observable<IRemoteEventReceivers>(subscriber => {
            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
                    .set('settingId', settingId)
            };

            this.http
                .get<string>('api/sharePoint/getSharePointSettingEvents', options)
                .subscribe({
                    next: (eventReceivers: string) => {
                        subscriber.next(JSON.parse(eventReceivers));
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Configuration of SharePoint events
     *
     * @param workflowId
     * @param settingId
     * @param eventReceivers
     * @param siteUrl
     * @param listId
     */
    manageEventReceivers(workflowId: string, settingId: string, eventReceivers: string,
        siteUrl: string, listId: string): Observable<void> {
        return new Observable<void>(subscriber => {

            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
                    .set('workflowId', workflowId)
                    .set('settingId', settingId)
                    .set('eventReceivers', eventReceivers)
                    .set('siteUrl', siteUrl)
                    .set('listId', listId)
            };

            this.http
                .get<void>('api/sharePoint/manageEventReceivers', options)
                .subscribe({
                    next: () => {
                        subscriber.next();
                        subscriber.complete();
                    },
                    error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }

    /**
     * Gets events for workflow setting
     *
     * @param settingId
     * @param workflowName
     * @returns
     */
    getEventsForWorkflowSetting(settingId: string, workflowName: string): Observable<IRemoteEventReceiver[]> {
        return new Observable<IRemoteEventReceiver[]>(subscriber => {
            const options = {
                params: new HttpParams()
                    .set('host', this.environmentService.currentEnvironment.hostId)
                    .set('settingId', settingId)
                    .set('workflowName', workflowName)
            };

            this.http
                .get<string>('api/sharePoint/getEventsForWorkflowSetting', options)
                .subscribe({
                    next: (eventReceivers: string) => {
                        subscriber.next(JSON.parse(eventReceivers));
                        subscriber.complete();
                    }, error: (err: string) => {
                        console.error(err);
                        subscriber.error(err);
                    }
                });
        });
    }
}
