import { Component, ElementRef, EventEmitter, HostListener, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { ComponentIdMapping } from '../../../shared/constants/components';
import { TextboxComponent } from '../../common/text-box/text-box.component';
import { SmartFieldComposerComponent } from '../smart-field-composer/smart-field-composer.component';

@Component({
    selector: 'k2-smart-field-container',
    templateUrl: './smart-field-container.component.html',
    styleUrls: ['./smart-field-container.component.less']
})
export class SmartFieldContainerComponent implements OnInit, OnDestroy {
    @Input() public dataModel: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectField;
    @Input() public k2DragItem: any;
    @Input() public k2TextBox: TextboxComponent;
    @Input() public pluginConfig: any;
    @Input() public isPopOverActive: boolean;
    @Input() public resources: any = {};
    @Input() public isSelected: boolean;
    @Input() public isConfiguring: boolean;
    @Input() public enableDragging = true;
    @Output() public smartFieldClick = new EventEmitter<any>();
    @Output() public openSmartFieldComposer = new EventEmitter<any>();

    @ViewChild('k2AttachmentField') public k2AttachmentField: TemplateRef<any>;
    @ViewChild('k2DataField') public k2DataField: TemplateRef<any>;
    @ViewChild('k2DateTimeField') public k2DateTimeField: TemplateRef<any>;
    @ViewChild('k2EnvironmentField') public k2EnvironmentField: TemplateRef<any>;
    @ViewChild('k2ExpressionField') public k2ExpressionField: TemplateRef<any>;
    @ViewChild('k2InlineFunctionField') public k2InlineFunctionField: TemplateRef<any>;
    @ViewChild('k2ItemReferenceField') public k2ItemReferenceField: TemplateRef<any>;
    @ViewChild('k2SmartFormsField') public k2SmartFormsField: TemplateRef<any>;
    @ViewChild('k2SmartObjectField') public k2SmartObjectField: TemplateRef<any>;
    @ViewChild('k2SmartWizardField') public k2SmartWizardField: TemplateRef<any>;
    @ViewChild('k2UserField') public k2UserField: TemplateRef<any>;
    @ViewChild('k2WorkflowContextField') public k2WorkflowContextField: TemplateRef<any>;

    public isMulti: boolean;
    // For smart objects
    public objectName: string;
    public targetName: string;
    public smartFieldComposer: SmartFieldComposerComponent;

    public selectedTemplate: TemplateRef<any>;
    constructor(public element: ElementRef<HTMLElement>) { }

    public onDrop(data: DragEvent) {
        data.preventDefault();
        return false;
    }

    ngOnInit() {
        this.isMulti = (this.dataModel as any)?.methodReturnType?.indexOf('[]') > -1;

        if (this.dataModel.componentId === ComponentIdMapping.k2SmartObjectField) { // only do this for smart objects
            const rootObj: sourceCode.k2Designer.objectModel.core.model.IModelObject =
                this.dataModel.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelObject>();

            // if root is same as current scope data model then it means there is no parent
            if (rootObj === this.dataModel) {
                return;
            }

            const methodRef: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField =
                rootObj.findItem<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField>(this.dataModel.methodReference);

            // if the method reference cannot be resolved then object isn't part of the main model anymore and we can just exit
            if (!methodRef) {
                return;
            }

            this.isMulti = (methodRef.type.toLowerCase() === 'list');
            this.objectName = methodRef.displayName;

            if (this.dataModel.propertyReference) {
                const propRef: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField =
                    rootObj.findItem<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField>(this.dataModel.propertyReference);
                this.targetName = propRef.displayName;
            } else if (this.dataModel.methodReference) {
                this.targetName = methodRef.displayName;
            }
        }

        setTimeout(() => {
            const template = this[ComponentIdMapping[this.dataModel.componentId]];
            this.selectedTemplate = template || this.k2InlineFunctionField; // display inline function field template if component id doesnt have it's own template
        }, 0);
    }

    ngOnDestroy() {
        if (this.smartFieldComposer) {
            this.smartFieldComposer.closeForSmartField(this);
        }
    }

    @HostListener('click')
    public onClick() {
        this.smartFieldClick.emit({ dataModel: this.dataModel, element: this.element.nativeElement });
    }

    @HostListener('dblclick')
    public onDoubleClick() {
        this.openSmartFieldComposer.emit(this);
    }
}
