import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IDataField } from '../../../../../types/models/data-field/i-data-field';
import { INewDataFieldContext } from '../../../../../types/models/popup/i-new-datafield-context';
import { IContextPopupConfiguration } from '../../../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { DataTypeResolverService } from '../../../../services/plugins/data-type-resolver/data-type-resolver.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { BaseResourceComponent } from '../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-data-field-popup-header',
    templateUrl: './data-field-popup-header.component.html',
    styleUrls: ['./data-field-popup-header.component.less']
})
export class DataFieldPopupHeaderComponent extends BaseResourceComponent implements OnInit {

    @Output() onDialogClosed = new EventEmitter<void>();
    @Input() config: IContextPopupConfiguration;
    dataField: IDataField;

    public dataFieldIcon: string;

    constructor(protected resourceService: ResourceService,
        private dataTypeResolver: DataTypeResolverService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.dataField = (this.config.context as INewDataFieldContext)
            .existingDataField;
        this.dataFieldIcon = this.dataTypeResolver.getSpriteClassFromDataType(this.dataField.type.toString());
    }

    /**Emits the dialogClosed event so that
     * components using this header can call the host's dialogClosed functionality when triggered.
     * */
    public emitDialogClosed(): void {
        this.onDialogClosed.emit();
    }
}
