import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { DeadlineActionType } from '../../../../types/models/reminder-control/deadline-action-type';
import { ReminderActionType } from '../../../../types/models/reminder-control/reminder-action-type';
import { IListItemIconData } from '../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { ConfigurationService } from '../../../services/configuration/configuration-service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ChooseControlTemplates } from '../../../shared/enums/choose-control/choose-control-templates';
import { ChooseControlTypes } from '../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';
import { IEnumLookupPair } from '../../../shared/models/i-enum-lookup-pair';
import { showDatePicker } from '../../../shared/utils/date-picker';
import { isUndefinedOrNull } from '../../../shared/utils/general';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-reminder-control',
    templateUrl: './reminder-control.component.html',
    styleUrls: ['./reminder-control.component.less']
})
export class ReminderControlComponent extends BaseResourceComponent implements OnInit {
    @Input() headingText: string;
    @Input() headingIcon: string;
    @Input() contentDescription: string;
    @Input() actionTypeKey: ReminderActionType;
    @Input() onDateInterval: IExpression;
    @Input() beforeOrAfterIntervalDays: IExpression;
    @Input() beforeOrAfterIntervalHours: IExpression;
    @Input() beforeOrAfterIntervalMins: IExpression;
    @Input() beforeOrAfterIntervalSecs: IExpression;
    @Input() repeatReminder: boolean;
    @Input() repeatingIntervalDays: IExpression;
    @Input() repeatingIntervalHours: IExpression;
    @Input() repeatingIntervalMins: IExpression;
    @Input() repeatingIntervalSecs: IExpression;
    @Input() repeatingAmmount: IExpression;;
    @Input() isSelected: boolean;
    @Input() reminderMode: DeadlineActionType;
    @Input() reminderButtonText: string;
    @Input() repeatInfinitely: boolean;
    @Input() deadlineObject: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDeadline;
    @Input() hasTaskDeadline: boolean;

    @Output() toggleRulesDesigner: EventEmitter<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDeadline> = new EventEmitter<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDeadline>();
    @Output() onActionTypeChanged: EventEmitter<IDropDownMenuItem> = new EventEmitter<IDropDownMenuItem>();

    //Enumerator types
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    chooseControlTemplates: typeof ChooseControlTemplates = ChooseControlTemplates;
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    textBoxConstraint: typeof TextBoxConstraint = TextBoxConstraint;
    deadlineActionType: typeof DeadlineActionType = DeadlineActionType;
    reminderActionType: typeof ReminderActionType = ReminderActionType;

    //Public variables
    calenderRightIconData: IListItemIconData = {
        customClassFlexRow: 'list-item-icons',
        iconClass: 'icon icon-set-datatypes icon-size16 ic-datetime fdatepicker',
        isVertical: true,
        isWrappedInContentDiv: true
    };

    intervalRightIconData: IListItemIconData = {
        customClassFlexRow: 'list-item-icons',
        iconClass: 'icon icon-set-contextbrowser icon-size16 ic-infinite',
        isVertical: true,
        isWrappedInContentDiv: true
    };

    workingHoursDropdownItems: IDropDownMenuItem[];
    workingHoursDisabled: boolean;
    workingHoursFooterItems: IDropDownMenuItem[];
    actionTypeTitle: string;
    selectedRowIndex: number;
    intervalDropdownFields: IDropDownMenuItem[];

    constructor(resourceService: ResourceService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        private configurationService: ConfigurationService) {
        super(resourceService);
    }

    ngOnInit(): void {
        //Backwards compatibility, field did not exists, create new instance
        if (!this.deadlineObject?.dynamicWorkingHours) {
            this.deadlineObject.dynamicWorkingHours = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({ smartFields: [], isDynamic: false });
        }

        //Ensure repeating list item is shown on reload
        if (this.repeatingAmmount?.smartFields?.length > 0) {
            this.repeatReminder = true;
        }

        this.actionTypeTitle = sourceCode.k2Designer.objectModel.workflow.process.k2Process.ReminderActionTypeImplementor(this.actionTypeKey);

        this.setupIntervalDropdownFields();

        this.workingHoursDisabled = true;
        this.getWorkingHoursFromManagement();
    }

    private getWorkingHoursFromManagement(): void {
        this.configurationService.getWorkingHours()
            .subscribe((result: string[] | string) => {
                this.createDropdownForWorkingHours(result);
            });

        this.configurationService.getWorkingHours();
    }

    private createDropdownForWorkingHours(workingHours: string[] | string): void {
        this.workingHoursDropdownItems = [];

        if (workingHours === 'false') {
            this.workingHoursDisabled = true;
            return;
        }

        for (let i = 0; i < workingHours.length; i++) {
            let isSelected = false;

            if (!this.deadlineObject.isDefaultWorkingHours && workingHours[i] === this.deadlineObject.workingHours) {
                isSelected = true;
            }
            this.workingHoursDropdownItems.push({
                key: i.toString(),
                title: workingHours[i],
                isSelected,
                onSelected: this.selectWorkingHours.bind(this)
            });
        }

        this.workingHoursDisabled = false;

        this.workingHoursDropdownItems.push({
            key: this.resourceService.getString('general_default_working_hours'),
            title: this.resourceService.getString('general_default_working_hours'),
            isSelected: this.deadlineObject?.isDefaultWorkingHours && !this.deadlineObject.dynamicWorkingHours.isDynamic || false,
            onSelected: this.selectDefaultWorkingHours.bind(this)
        });

        this.workingHoursFooterItems = [];
        this.workingHoursFooterItems.push({
            title: this.resourceService.getString('control_PickerControl_Item_TypeConstructMyOwn'),
            key: 'typeConstructMyOwn',
            icon: 'icon-set-toolbar ic-edit',
            innerItemComponent: DropdownMenuItemTemplates.Command,
            onSelected: () => {
                this.deadlineObject.dynamicWorkingHours.isDynamic = true;
            }
        });
    }

    private selectWorkingHours(item: IDropDownMenuItem): void {
        this.deadlineObject.workingHours = item.title;
        this.deadlineObject.isDefaultWorkingHours = false;
        this.deadlineObject.dynamicWorkingHours.isDynamic = false;
        this.deadlineObject.dynamicWorkingHours.smartFields.clear();
    }

    private selectDefaultWorkingHours(item: IDropDownMenuItem): void {
        this.deadlineObject.workingHours = item.title;
        this.deadlineObject.isDefaultWorkingHours = true;
        this.deadlineObject.dynamicWorkingHours.isDynamic = false;
        this.deadlineObject.dynamicWorkingHours.smartFields.clear();
    }

    /**
     * Method when item in the drop down was selected.
     */
    private intervalItemClicked(index: number, item: IEnumLookupPair): void {

        this.actionTypeKey = item.key as number;
        this.actionTypeTitle = sourceCode.k2Designer.objectModel.workflow.process.k2Process.ReminderActionTypeImplementor(this.actionTypeKey);

        switch (item.key) {
            case ReminderActionType.After:
                this.onDateInterval.smartFields.clear();
                break;
            case ReminderActionType.Before:
                this.onDateInterval.smartFields.clear();
                if (!isUndefinedOrNull(this.deadlineObject)) {
                    this.deadlineObject.withinWorkingHoursOnly = false;
                }
                break;
            case ReminderActionType.On:
                this.beforeOrAfterIntervalDays.smartFields.clear();
                this.beforeOrAfterIntervalHours.smartFields.clear();
                this.beforeOrAfterIntervalMins.smartFields.clear();
                this.beforeOrAfterIntervalSecs.smartFields.clear();
                break;
        }
    }

    /**
     * This method creates the single dropdown list. It is used to returns the appropriate dropdown interface needed to display in the Reminder Control
     */
    private setupIntervalDropdownFields(): void {
        this.intervalDropdownFields = [];

        const opts = sourceCode.k2Designer.objectModel.workflow.process.k2Process.GetReminderActionTypeList();
        for (let i = 0; i < opts.length; i++) {
            this.intervalDropdownFields.push({
                key: opts[i].key.toString(),
                title: opts[i].title,
                onSelected: this.intervalItemClicked.bind(this, i, opts[i]),
                isSelected: this.actionTypeKey === opts[i].key
            });
        }
    }

    getSelectedWorkingHoursText(): string {
        if (this.deadlineObject?.dynamicWorkingHours?.isDynamic) {
            return this.resourceService.getString('choose_time_zone');
        }

        if (!isUndefinedOrNull(this.deadlineObject)) {
            return this.deadlineObject.workingHours || this.resourceService.getString('general_default_working_hours');
        } else {
            return this.resourceService.getString('general_default_working_hours');
        }
    }

    openCalendarControl(eventTarget: EventTarget): void {
        showDatePicker(eventTarget, (ev) => {
            this.undoRedoService.enabledAndBeginUndoGroupWithMarkerAndFinish('--reminder control: change timer date--');
            this.onDateInterval.smartFields.clear();
            this.onDateInterval.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.DateTimeField.createInstance({
                title: ev.formatted,
                dateTime: ev.formatted
            }));
        });
    }

    toggleInfiniteInterval(): void {
        this.undoRedoService.enabledAndBeginUndoGroupWithMarkerAndFinish('--reminder control: change to infinite interval--');

        if (this.repeatInfinitely === true) {
            this.repeatingAmmount.smartFields.clear();
            this.repeatInfinitely = false;
        }
        else {
            this.repeatingAmmount.smartFields.clear();
            this.repeatingAmmount.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                text: this.resourceService.getString('wizard_Task_Reminders_Task_Reminder_Infinite')
            }));
            this.repeatInfinitely = true;
        }
    }

    showBadge(): boolean {
        if (this.actionTypeKey !== 3) {
            return false;
        }

        if (isUndefinedOrNull(this.deadlineObject)) {
            return false;
        }

        return this.hasTaskDeadline === true;
    }

    reminderButtonClicked(): void {
        this.toggleRulesDesigner.emit(this.deadlineObject);
    }

    intervalDropDownItemSelected(dropdownItem: IDropDownMenuItem): void {
        this.onActionTypeChanged.emit(dropdownItem);
    }

    getModelForBadge(): sourceCode.k2Designer.objectModel.core.model.IValidatedComponent {
        switch (this.deadlineObject.actionType) {
            case DeadlineActionType.EmailEscalationAction:
                return this.deadlineObject.deadlineEmailAction;
            case DeadlineActionType.RedirectEscalationAction:
                return this.deadlineObject.deadlineRedirectAction;
            case DeadlineActionType.SmartObjectEscalationAction:
                return this.deadlineObject.deadlineSmoAction;
            default:
                console.error('Unknown deadline action type while trying to find out if we should badge the button. deadlineObject.actionType: ', this.deadlineObject.actionType);
                throw 'Unknown deadline action type while trying to find out if we should badge the button. deadlineObject.actionType: ' + this.deadlineObject.actionType;
        }
    }

    checkedChanged(checked: boolean): void {
        this.deadlineObject.withinWorkingHoursOnly = checked;
    }
}
