import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { ILegacyResourceService } from '../../../../../types/services/resources/i-legacy-resource.service';
import { LegacyResourceServiceToken } from '../../../../legacy-service-factories/resource-service-factory';
import { ValidationServiceToken } from '../../../../services/validation/validation-service-factory';

@Component({
    selector: 'k2-mini-badge',
    templateUrl: './mini-badge.component.html',
    styleUrls: ['./mini-badge.component.less']
})
export class MiniBadgeComponent implements OnInit, OnDestroy {
    @Input() public k2Model?: any;
    @Input() public forceShow: boolean;
    @Input() public alwaysHideBadge: boolean;
    @Input() public customClass?: string;
    @Input() public toolTipText?: string;
    public customBadgeClass?: string;
    private modelChangedListener;

    constructor(@Inject(ValidationServiceToken) private _validationService,
        @Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService) { }

    ngOnInit(): void {
        //Check if the model contains errors
        if (this.k2Model?.childErrorInformation || this.k2Model?.errorInformation) {
            this.setupBadge();
            this.modelChangedListener = this.k2Model.listen(sourceCode.k2Designer.objectModel.core.model.EventTypes.anythingChanged, this.setupBadge.bind(this));
        }
    }
    ngOnDestroy(): void {
        if (this.modelChangedListener) this.modelChangedListener();
    }

    private setupBadge(): void {
        this.customBadgeClass = this.customClass + (this._validationService.getErrorSeverity(this.k2Model) === 1 ? ' mini-warning-badge' : '');
        this.toolTipText = this.toolTipText ? this.toolTipText : this.getErrorMessage();
    }

    private getErrorMessage() {
        let message = '';
        if (this.k2Model && this.k2Model.errorInformation &&
            this.k2Model.errorInformation.length > 0 &&
            !!this.k2Model.errorInformation[0].errorMessage) {
            message = this._resourceService.resolveTokenResourceString(this.k2Model.errorInformation[0].errorMessage, this.k2Model.errorInformation[0].errorResourceTokens, this.k2Model);
        } else if (this.k2Model && this.k2Model.childErrorInformation &&
            this.k2Model.childErrorInformation.length > 0 &&
            !!this.k2Model.childErrorInformation[0].errorMessage) {
            message = this._resourceService.resolveTokenResourceString(this.k2Model.childErrorInformation[0].errorMessage, this.k2Model.childErrorInformation[0].errorResourceTokens, this.k2Model);
        }

        return message;
    }
}
