import { Directive, ElementRef, EventEmitter, HostListener, Inject, Input } from '@angular/core';
import { isUndefinedOrNull } from '../../shared/utils/general';

@Directive({
    selector: '[k2TooltipSimple]'
})
export class TooltipSimpleDirective {

    //TODO: This is only used in ribbon tooltips ever, consider replacing completely with tooltip basic

    @Input() public k2TooltipDescription: string;
    @Input() public k2TooltipSection: string;
    @Input() public k2TooltipSubSection: string;
    @Input() public k2TooltipTitle: string;
    @Input() public k2TooltipPosition: string;
    @Input() public k2TooltipVisible: boolean;
    @Input() public showTimer: number;

    private _element: HTMLElement;
    private _scope: sourceCode.k2Designer.directives.k2TooltipSimple.IK2TooltipSimpleScope;

    constructor(@Inject('$scope') $scope: sourceCode.k2Designer.directives.k2TooltipSimple.IK2TooltipSimpleScope,
        el: ElementRef<HTMLElement>)
    {
        this._element = el.nativeElement;
        this._scope = $scope;
    }

    @HostListener('mouseenter', ['$event'])
    onMouseEnter(e: MouseEvent) {
        const toolTipContainer = this.renderTemplate();

        this.checkIfAnySectionIsMoreThenMaxHeight(toolTipContainer);

        this.positionTootip(toolTipContainer, e);

        this.showTimer = window.setTimeout(() => { toolTipContainer.classList.add('show'); }, 500);
    }

    @HostListener('mouseout', ['$event'])
    onMouseOut(e: MouseEvent): void {
        document.getElementById('simpleToolTipParent').classList.remove('show');
        window.clearTimeout(this._scope.showTimer);
    }

    @HostListener('mousedown', ['$event'])
    onMouseDown(e: MouseEvent): void {
        document.getElementById('simpleToolTipParent').classList.remove('show');
        window.clearTimeout(this._scope.showTimer);
    }

    private positionTootip(toolTipView: HTMLElement, e: MouseEvent): void {
        let coordinates = this.calculateCoordinates(10, this.k2TooltipPosition, toolTipView);


        toolTipView.style.left = coordinates.x + 'px';
        toolTipView.style.top = coordinates.y + 'px';

        if (this.isOutOfPage(toolTipView)) {
            coordinates = this.calculateCoordinates(10, coordinates.alternatePosition, toolTipView);
            toolTipView.style.left = coordinates.x + 'px';
            toolTipView.style.top = coordinates.y + 'px';
        }

    }

    private calculateCoordinates(tooltipOffset: number, toolTipPosition: string, tooTipView: HTMLElement): any {
        const ret: any = {};

        let x = 0;
        let y = 0;
        let alternatePosition: string = toolTipPosition;

        let elementMargin = 0;
        let elementPadding = 0;

        const boundingRect = this._element.getBoundingClientRect();

        switch (toolTipPosition) {
            case 'top': {
                const viewHeight = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(tooTipView, 'height').replace('px', '')));
                x = boundingRect.left;
                y = boundingRect.top - tooltipOffset - viewHeight;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-left').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-left').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x += elementMargin + elementPadding;
                alternatePosition = 'bottom';
            } break;
            case 'left': {
                x = boundingRect.left - tooltipOffset - 200;
                y = boundingRect.top;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-left').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-left').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x += elementMargin + elementPadding;
                alternatePosition = 'right';
            } break;
            case 'right': {

                x = boundingRect.right + tooltipOffset;
                y = boundingRect.top;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-right').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-right').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x -= (elementMargin + elementPadding);
                alternatePosition = 'left';
            } break;
            case 'bottomleft': {
                x = boundingRect.left - 200;
                y = boundingRect.bottom + tooltipOffset;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-left').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-left').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x += elementMargin + elementPadding;
                alternatePosition = 'bottomright';
            } break;
            case 'bottomright': {
                x = boundingRect.right;
                y = boundingRect.bottom + tooltipOffset;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-right').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-right').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x -= (elementMargin + elementPadding);
                alternatePosition = 'bottomleft';
            } break;
            case 'topleft': {
                const viewHeight = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(tooTipView, 'height').replace('px', '')));
                x = boundingRect.left - 200;
                y = boundingRect.top - tooltipOffset - viewHeight;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-left').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-left').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x += elementMargin + elementPadding;
                alternatePosition = 'topright';
            } break;
            case 'topright': {
                const viewHeight = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(tooTipView, 'height').replace('px', '')));
                x = boundingRect.right;
                y = boundingRect.top - tooltipOffset - viewHeight;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-right').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-right').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x -= (elementMargin + elementPadding);
                alternatePosition = 'topleft';
            } break;
            default: {
                x = boundingRect.left;
                y = boundingRect.bottom + tooltipOffset;
                elementMargin = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'margin-left').replace('px', '')));
                elementPadding = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(this._element, 'padding-left').replace('px', '')));
                if (isNaN(elementMargin)) {
                    elementMargin = 0;
                }
                if (isNaN(elementPadding)) {
                    elementPadding = 0;
                }
                x += elementMargin + elementPadding;
                alternatePosition = 'top';
            }; break;
        }

        return {
            x,
            y,
            alternatePosition
        };
    }

    private isOutOfPage(toolTipContainer: HTMLElement): boolean {
        const offsetWidth: number = window.document.body.offsetWidth;
        const offsetHeight: number = window.document.body.offsetHeight;
        const tooltipContainerRect: ClientRect = toolTipContainer.getBoundingClientRect();

        if (tooltipContainerRect.top < 0 || tooltipContainerRect.top > offsetHeight ||
            tooltipContainerRect.left < 0 || tooltipContainerRect.left > offsetWidth ||
            tooltipContainerRect.bottom < 0 || tooltipContainerRect.bottom > offsetHeight ||
            tooltipContainerRect.right < 0 || tooltipContainerRect.right > offsetWidth) {

            return true;
        }

        return false;
    }

    private renderTemplate(): HTMLElement {
        const theToolTipParent = document.getElementById('simpleToolTipParent');
        theToolTipParent.innerHTML = this.getTemplateString();

        return document.getElementById('simpleToolTipParent');
    }

    private checkIfAnySectionIsMoreThenMaxHeight(toolTipContainer: HTMLElement): void {
        const sections = toolTipContainer.querySelectorAll('.sections label');
        for (let i = 0; i < sections.length; i++) {
            const actualHeight = sections[i].scrollHeight;
            const maxHeight = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(<HTMLElement>sections[i], 'max-height').replace('px', '')));

            if (actualHeight > maxHeight) {
                sections[i].classList.add('multiline');
            }
        }

        const description = toolTipContainer.querySelectorAll('.description label')[0];
        if (!isUndefinedOrNull(description)) {
            const actualHeight = description.scrollHeight;
            const maxHeight = Math.round(Number(sourceCode.k2Designer.utilities.getActualElementStyleValue(<HTMLElement>description, 'max-height').replace('px', '')));

            if (actualHeight > maxHeight) {
                description.classList.add('multiline');
            }
        }
    }

    /**
     * In AngularJS, the tooltip html was built up including the angular scope values and compiled by the angular compile service.
     * This approach below for Angular10, does not compile the html as angular, which means that the input strings will not dynamically update
     * (it will only update once with every mouseover)
     */
    private getTemplateString(): string {
        let templateStr = '';
        if (this.k2TooltipVisible) {
            let toolTipTitleStr = '';
            if (this.k2TooltipTitle && this.k2TooltipTitle != '') {
                toolTipTitleStr =
                    `<div class='name'>\
                        <label>${this.k2TooltipTitle}</label>\
                     </div>`;
            }
            let toolTipDescStr = '';
            if (this.k2TooltipDescription && this.k2TooltipDescription != '') {
                toolTipDescStr =
                    `<div class=\'description\'>\
                        <label>${this.k2TooltipDescription}</label>\
                     </div>`;
            }
            let toolTipSectionStr = '';
            if (this.k2TooltipSection && this.k2TooltipSection != '') {
                toolTipSectionStr =
                    `<div class=\'sections\'>\
                        <label>${this.k2TooltipSection}</label>\
                     </div>`;
            }
            let toolTipSubSectionStr = '';
            if (this.k2TooltipSubSection && this.k2TooltipSubSection != '') {
                toolTipSubSectionStr =
                    `<div class=\'sections\'>\
                        <label>${this.k2TooltipSubSection}</label>\
                     </div>`;
            }

            templateStr =
                `<div class='simpleToolTip'>
                    ${toolTipTitleStr}
                    ${toolTipDescStr}
                    ${toolTipSectionStr}
                    ${toolTipSubSectionStr}
                 </div>`;
        }
        return templateStr;
    }

}
