import { DOCUMENT } from '@angular/common';
import { Component, ElementRef, EventEmitter, HostListener, Inject, Input, OnChanges, OnDestroy, OnInit, Output, QueryList, SimpleChanges, ViewChild, ViewChildren } from '@angular/core';
import { asyncScheduler, Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, throttleTime } from 'rxjs/operators';
import { EventTypes } from 'src/app/shared/enums/model/event-types';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { ISmartFieldComposerOptions } from '../../../../types/uiComponents/smartFieldComposer/ISmartFieldComposerOptions';
import { ClipboardServiceToken } from '../../../services/clipboard/clipboard-service-factory';
import { CommandServiceToken } from '../../../services/command/command-service-factory';
import { DragDropService } from '../../../services/drag-drop/drag-drop.service';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { ProcessService } from '../../../services/process/process.service';
import { SmartFieldComposerServiceToken } from '../../../services/smart-field-composer/smart-field-composer-service-factory';
import { SmartFieldPluginServiceToken } from '../../../services/smart-field-plugin/smart-field-plugin-service-factory';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { ComponentIdMapping } from '../../../shared/constants/components';
import { OpenSmartFieldComposerEvent } from '../../../shared/constants/events';
import { inputDebounceTime, keyCodes } from '../../../shared/constants/general';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';
import { BaseComponent } from '../../base/base.component';
import { SmartFieldContainerComponent } from '../../smart-fields/smart-field-container/smart-field-container.component';

@Component({
    selector: 'k2-text-box',
    templateUrl: './text-box.component.html',
    styleUrls: ['./text-box.component.less']
})
export class TextboxComponent extends BaseComponent implements OnInit, OnDestroy, OnChanges {
    /*
     * The expression associated with this textbox
     */
    @Input() public k2Expression: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpressionExtended;
    /**
     * Indicates the a drop can be applied to the textbox
     */
    @Input() public k2CanDrop: boolean;

    @Input() public k2Validation: IValidationOptions[];

    @Input() public isMultiLine = true;
    @Input() public k2ShowBrowseButton = false;
    @Input() public k2Watermark = '';
    @Input() public k2Watermarkicon: string;
    @Input() public alwaysHideBadge = false;

    /**
     * Indicates the type of constraint that is applied to the textbox when the user is typing
     */
    @Input() public textConstraint: TextBoxConstraint = TextBoxConstraint.None;

    @Output() public k2TextboxOnDrop = new EventEmitter<any>();
    @Output() public k2TextboxOnDragEnter = new EventEmitter<any>();
    @Output() public k2TextboxOnDragLeave = new EventEmitter<any>();
    @Output() public k2ButtonClicked = new EventEmitter<any>();
    @Output() public k2TextboxOnFocus = new EventEmitter<any>();
    @Output() public k2TextboxOnBlur = new EventEmitter<any>();

    public isFocused = false;
    public componentIdMapping = ComponentIdMapping;
    public textLength;
    public composerEnabled = false;
    public enabled = true;
    public selectedSmartFields: any[] = [];
    public hideEditor: boolean;
    public displayedFields: any[] = [];
    public codeView = false;
    public showCharCount = false;
    public showWaterMark = true;
    public isOwnedByComposer = false;

    @ViewChild('editorDiv') public editorDiv: ElementRef<HTMLElement>;
    @ViewChildren('smartFieldContainer') public smartFieldContainers: QueryList<SmartFieldContainerComponent>;

    private mouseMoveSubject: Subject<MouseEvent> = new Subject();
    private mouseMoveSubcription: Subscription;

    private textChangedSubject: Subject<InputEvent> = new Subject();
    private textChangedSubscription: Subscription;

    private externalChangedSubject: Subject<any> = new Subject();
    private externalChangedSubscription: Subscription;

    private _forceCopyOnDrag = false;
    private _composerEnabledForDragging: boolean;
    private fieldsChangedExternallyRemover: () => void;

    public htmlElement: HTMLElement;

    private openSmartFieldComposerEvent: EventEmitter<ISmartFieldComposerOptions>;

    constructor(
        private el: ElementRef,
        private _dragDropService: DragDropService,
        @Inject(UndoRedoServiceToken) public _undoRedoService: IUndoRedoService,
        @Inject(SmartFieldPluginServiceToken) public _smartFieldService: ISmartFieldPluginService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        @Inject(SmartFieldComposerServiceToken) private _smartFieldComposerService: ISmartFieldComposerService,
        @Inject(ClipboardServiceToken) public _clipboardService: IClipboardService,
        @Inject(CommandServiceToken) private _commandService: ICommandService,
        private _processService: ProcessService,
        private _notificationService: EventNotifierService,
        @Inject(DOCUMENT) private $document: Document) {
        super(_validationService);
        this.enabled = true;
        this.openSmartFieldComposerEvent = this._notificationService.addEvent(OpenSmartFieldComposerEvent);
    }

    ngOnInit() {
        this.isMultiLine =
            this.k2Expression?.multiLine === true ||
            this.k2Expression?.richText === true ||
            this.k2Expression?.html === true;

        this.textLength = 0;
        if (this.k2Expression?.smartFields) {
            this.k2Expression.smartFields.forEach((val: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField) => {
                this.textLength += val.text?.length || 0;
            });
            this.displayedFields = [];
            this.reRender();
        }

        this.mouseMoveSubcription = this.mouseMoveSubject
            .pipe(throttleTime(100, asyncScheduler, { leading: true, trailing: true }))
            .subscribe((e) => {
                this.mouseMoveDebounced();
            });

        this.textChangedSubscription = this.textChangedSubject
            .pipe(debounceTime(inputDebounceTime),
                distinctUntilChanged())
            .subscribe((e: InputEvent) => {
                this.textChangedDebounced(e);
            });

        this.externalChangedSubscription = this.externalChangedSubject
            .pipe(debounceTime(500))
            .subscribe(this.onFieldsChangedExternally);

        this.fieldsChangedExternallyRemover = this.k2Expression?.smartFields.listen(EventTypes.anythingChanged, this.fieldsChangedExternally.bind(this));
        this.htmlElement = this.el.nativeElement;
        this.setApplicableValidationTypes(this.k2Validation);
        super.validateArrayOnChange(this.k2Expression, `smartFields`, null);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (!this.isFocused && changes.k2Expression) { // update coming from outside and it's on the expression
            this.reRender();

            if (this.fieldsChangedExternallyRemover) {
                this.fieldsChangedExternallyRemover();
            }
            this.fieldsChangedExternallyRemover = this.k2Expression?.smartFields.listen(EventTypes.anythingChanged, this.fieldsChangedExternally.bind(this));
        }
    }

    ngOnDestroy() {
        super.ngOnDestroy();
        this.mouseMoveSubcription?.unsubscribe();
        this.textChangedSubscription?.unsubscribe();
        this.externalChangedSubscription?.unsubscribe();
        if (typeof this.fieldsChangedExternallyRemover === 'function') {
            this.fieldsChangedExternallyRemover();
        }
    }

    private mouseDown = false;
    private dragging = false;
    /**
     * Functionality for mousedown event
     *
     * @param event the mouse event associated with the mousedown
     */
    public onMouseDown(event) {
        this.mouseDown = true;
    }

    /**
     * Functionality for the mouse up event
     */
    @HostListener('document:mouseup')
    public onMouseUp() {
        if (this.mouseDown && !this.dragging) {
            this.setSelectedSmartFields();
        }
        if (this.dragging) {
            this.dragging = false;
        }
        this.mouseDown = false;
    }

    private mouseMoveDebounced() {
        if (this.mouseDown && !this.dragging) {
            this.setSelectedSmartFields();
        }
    }

    /**
     * Functionality for mouse move event inside the component
     *
     * @param e mouse event associated with the mouse move
     */
    public onMouseMove(e: MouseEvent) {
        this.mouseMoveSubject.next(e);
    }

    fieldsChangedExternally(object: sourceCode.k2Designer.objectModel.core.model.IModelBase) {
        if (!this.isFocused) {
            this.externalChangedSubject.next(object);
        }
    }

    onFieldsChangedExternally = (object: sourceCode.k2Designer.objectModel.core.model.IModelBase) => {
        //exit out of method if from is null

        const isModelArray: boolean = object instanceof sourceCode.k2Designer.objectModel.core.model.ModelArray;

        // if it is not a text value, we are not interested
        if (!isModelArray && !(object as any)?.text) { // if it is not a text value, we are not interested
            return;
        }

        const isErrorInfo: boolean = (<any>object).instanceOf(sourceCode.k2Designer.objectModel.core.model.ErrorInformation.implementor);
        const logicalParentModel: sourceCode.k2Designer.objectModel.core.model.IModelBase = object.getLogicalParentModel();

        // if object is an errorinfo object that was modified in validation, causing onExpressionChangedExternally to fire, do not continue
        if (isErrorInfo
            || (isModelArray && (<any>object).length > 0 && (<any>object)[0].instanceOf(sourceCode.k2Designer.objectModel.core.model.ErrorInformation.implementor))
            || (logicalParentModel && logicalParentModel.instanceOf(sourceCode.k2Designer.objectModel.core.model.ErrorInformation.implementor))) {
            return;
        }

        if (logicalParentModel && !this.k2Expression.isParentOf(logicalParentModel)) {
            this.reRender();
        }
    };

    private setSelectedSmartFields(range?: Range) {
        if (!range) {
            const selection = this.$document.getSelection();
            if (!selection.isCollapsed && selection.rangeCount > 0) {
                for (const container of this.smartFieldContainers) {
                    const smartField = this.k2Expression.smartFields[parseInt(container.element.nativeElement.id)];
                    if (selection.containsNode(container.element.nativeElement, true)) {
                        if (!this.selectedSmartFields.includes(smartField)) {
                            this.selectedSmartFields.push(smartField);
                        }
                    } else {
                        if (this.selectedSmartFields.includes(smartField)) {
                            this.selectedSmartFields.splice(this.selectedSmartFields.indexOf(smartField), 1);
                        }
                    }
                };
            } else {
                this.selectedSmartFields = [];
            }
        } else {
            for (const container of this.smartFieldContainers) {
                const smartField = this.k2Expression.smartFields[parseInt(container.element.nativeElement.id)];
                if (range.intersectsNode(container.element.nativeElement)) {
                    if (!this.selectedSmartFields.includes(smartField)) {
                        this.selectedSmartFields.push(smartField);
                    }
                } else {
                    if (this.selectedSmartFields.includes(smartField)) {
                        this.selectedSmartFields.splice(this.selectedSmartFields.indexOf(smartField), 1);
                    }
                }
            };
        }
    }

    /**
     * Functionality for the on click event
     *
     * @param event event associated with the mouse click
     */
    public onClick(event: MouseEvent) {
        const target = event.target as HTMLElement;
        if ($(target).parents('k2-smart-field-container').length === 0) {
            setTimeout(() => {
                const selection = this.$document.getSelection();
                if (selection.isCollapsed) {
                    this.selectedSmartFields = [];
                }
            }, 0);
        } else {
            this.doFocus();
        }
    }

    /**
     * The K2 button was clicked
     */
    public k2ButtonClick() {
        this.k2ButtonClicked.emit();
    }

    private rendering = false;
    /**
     * This is to bypass the outerHTML bug because angular has no way to inject pure HTML without a container
     * https://github.com/angular/angular/issues/41131
     */
    public reRender(composerFieldToOpen: any = null) {
        if (!this.rendering && this.k2Expression?.smartFields) {
            this.rendering = true;
            this.displayedFields = [];
            this.hideEditor = true;
            setTimeout(() => {
                for (const field of (this.k2Expression.smartFields as sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField>)) {
                    if (field.componentId === ComponentIdMapping.TextSmartField) {
                        this.displayedFields.push(field.clone());
                    } else {
                        this.displayedFields.push(field);
                    }
                } // Shouldnt be a reference to the original
                this.textLength = 0;
                this.k2Expression.smartFields.forEach((val: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField) => {
                    this.textLength += val.text?.length || 0;
                });
                this.hideEditor = false;
                if (this.textLength || this.k2Expression.smartFields.find((f) => f.componentId !== ComponentIdMapping.TextSmartField)) {
                    this.showWaterMark = false;
                } else {
                    this.showWaterMark = true;
                }
                setTimeout(() => {
                    this.rendering = false;
                    if (composerFieldToOpen) {
                        setTimeout(() => {
                            this.openSmartFieldComposer(composerFieldToOpen.field, composerFieldToOpen.config, composerFieldToOpen.fromUserDrop, composerFieldToOpen.fromContextBrowser, composerFieldToOpen.droppedOnNew);
                        }, 0);
                    }
                }, 0);
            }, 0);
        }
    }

    /**
     * Functionality for handling text changing, after it has been debounced
     *
     * @param event the text change event
     */
    public textChangedDebounced(event: InputEvent, textRange: Range = null) {
        const selection = this.$document.getSelection();
        let caretNode = selection.anchorNode;
        let caretOffset = selection.anchorOffset;
        if (textRange) {
            caretNode = textRange.startContainer;
            caretOffset = textRange.startOffset;
        }
        this._undoRedoService.addUndoMarker(`-- textbox text changed --`);
        if (this.isFocused && (this.editorDiv.nativeElement.contains(caretNode) ||
            (caretNode === this.editorDiv.nativeElement && caretNode?.nodeType === Node.TEXT_NODE) // TEXT
        )) {
            if (caretNode === this.editorDiv.nativeElement) {
                caretNode = caretNode.childNodes[caretOffset];
                if (!caretNode) {
                    this.k2Expression.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                        text: ''
                    }));
                    return;
                }
            }
            const prevField = this.getPreviousSmartField(caretNode); //if previous field is null, text node is at first text field
            const index: number = prevField ? parseInt((prevField as HTMLElement).id) + 1 : 0; // a text field should never follow another
            const val = this.sanitizeText(this.getTextValue(caretNode));
            if (!this.k2Expression.smartFields[index] && (this.isCharacterKeyPress(event) || val)) {
                this.k2Expression.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: val
                }));
            } else if (this.k2Expression.smartFields[index] && this.k2Expression.smartFields[index].componentId !== ComponentIdMapping.TextSmartField) {
                this.k2Expression.smartFields.splice(index, 0, sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: val
                }));
            } else {
                if (!val && !this.k2Expression.smartFields?.length) {
                    return;
                } else if (!val) {
                    this.k2Expression.smartFields.splice(index, 1);
                } else {
                    (this.k2Expression.smartFields[index] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text = val;
                }
            }
        } else { // we dont know which text changed, update ALL of them
            this.mergeTextFields();
            let currentNode = this.editorDiv.nativeElement.firstChild;
            if (currentNode) {
                let index = 0;
                while (currentNode) {
                    if ((currentNode as HTMLElement)?.tagName?.toLowerCase() === 'k2-smart-field-container') {
                        currentNode = currentNode.nextSibling;
                        index++;
                        continue;
                    }
                    const value = this.sanitizeText(this.getTextValue(currentNode));
                    if (this.k2Expression.smartFields[index]?.componentId === ComponentIdMapping.TextSmartField) {
                        (this.k2Expression.smartFields[index] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text = value;
                        index++;
                    } else if (value) {
                        this.k2Expression.smartFields.splice(index, 0, sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                            text: value
                        }));
                        index++;
                    }
                    currentNode = this.getNextSmartField(currentNode) as any;
                }
            } else { // no text or otherwise
                this.removeSmartFields(this.k2Expression.smartFields);
            }
        }
        this.textLength = 0;
        this.k2Expression.smartFields.forEach((val: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField) => {
            this.textLength += val.text?.length || 0;
        });
    }

    /**
     * Functionality that handles text changing inside the editordiv
     *
     * @param event
     */
    public onTextChanged(event: InputEvent) {
        this.textChangedSubject.next(event);
        if (this.sanitizeText(this.editorDiv?.nativeElement?.textContent)) {
            this.showWaterMark = false;
        } else {
            this.showWaterMark = true;
        }
    }

    /**
     * On focus event handler
     *
     * @param event
     */
    public onFocus(event) {
        this._commandService.setCurrentContext(`K2Textbox`);
        this._clipboardService.setCurrentContext(`K2Textbox`);
        //this._richEditService.setCurrentTextbox(this);

        //if (!this._composerEnabled) {
        //    this._richEditService.setFocus(this._scope.k2Expression);
        //}

        this.k2TextboxOnFocus.emit(event);
        this.isFocused = true;
    }

    /**
     * On blur event handler
     *
     * @param event
     */
    public onBlur(event) {
        this.textChangedDebounced(null);
        this.isFocused = false;
        this.selectedSmartFields = [];
        this.k2TextboxOnBlur.emit(event);
    }

    /**
     * Smart field clicked event handler
     *
     * @param event
     */
    public onSmartFieldClick(event) {
        if (!this._ownsActiveComposer) {
            this.selectedSmartFields = [];
            this.selectedSmartFields.push(event.dataModel);

            const selection = this.$document.getSelection();
            selection.removeAllRanges();
        }
    }

    private getNextNode(node: Node): Node {
        if (node.firstChild)
        {return node.firstChild;}
        while (node) {
            if (node.nextSibling)
            {return node.nextSibling;}
            node = node.parentNode;
        }
    }

    private getPreviousNode(node: Node): Node {
        if (node.lastChild)
        {return node.lastChild;}
        while (node) {
            if (node.previousSibling)
            {return node.previousSibling;}
            node = node.parentNode;
        }
    }

    private getNextEditable(sourceNode: Node): Node {
        let nextNode: Node = sourceNode;
        if (nextNode.nodeType === Node.ELEMENT_NODE &&
            (nextNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') {
            if (!nextNode.nextSibling) {
                return null;
            }
            nextNode = nextNode.nextSibling;
        }
        while (nextNode) {
            nextNode = this.getNextNode(nextNode);
            if (nextNode.nodeType === Node.ELEMENT_NODE &&
                (nextNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') {
                if (!nextNode.nextSibling) {
                    return null;
                }
                nextNode = nextNode.nextSibling;
            }
            if ($(this.el.nativeElement).has(nextNode as any).length === 0) { // Next node is outside of the textbox
                return null;
            } else if (nextNode.nodeType === Node.TEXT_NODE) {
                return nextNode;
            }
        }
    }

    private getPreviousEditable(sourceNode: Node): Node {
        let prevNode: Node = sourceNode;
        if (prevNode.nodeType === Node.ELEMENT_NODE &&
            (prevNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') {
            if (!prevNode.previousSibling) {
                return null;
            }
            prevNode = prevNode.previousSibling;
        }
        while (prevNode) {
            prevNode = this.getPreviousNode(prevNode);
            if (prevNode.nodeType === Node.ELEMENT_NODE &&
                (prevNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') {
                if (!prevNode.previousSibling) {
                    return null;
                }
                prevNode = prevNode.previousSibling;
            }
            if ($(this.el.nativeElement).has(prevNode as any).length === 0) { // Previous node is outside of the textbox
                return null;
            } else if (prevNode.nodeType === Node.TEXT_NODE) {
                return prevNode;
            }
        }
    }

    private getNextSmartField(sourceNode: Node): Node {
        let nextNode: Node = sourceNode;
        while (nextNode) {
            nextNode = this.getNextNode(nextNode);
            if ($(this.el.nativeElement).has(nextNode as any).length === 0) { // Next node is outside of the textbox
                return null;
            } else if (nextNode.nodeType === Node.ELEMENT_NODE &&
                (nextNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container' &&
                !(nextNode as HTMLElement).classList?.contains('deleted')) {
                return nextNode;
            }
        }
    }

    private getPreviousSmartField(sourceNode: Node): Node { // Since text fields wont follow each other, search for a smartfield,
        let prevNode: Node = sourceNode;
        while (prevNode) {
            prevNode = this.getPreviousNode(prevNode);
            if ($(this.el.nativeElement).has(prevNode as any).length === 0) { // Previous node is outside of the textbox
                return null;
            } else if (prevNode.nodeType === Node.ELEMENT_NODE &&
                (prevNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container' &&
                !(prevNode as HTMLElement).classList?.contains('deleted')) {
                return prevNode;
            }
        }
    }

    private moveCaretToNextEditable(source: Node) {
        const caretRange = this.$document.createRange();
        const nextEditableNode = this.getNextEditable(source);
        if (nextEditableNode) {
            caretRange.setStart(nextEditableNode, 0);
            const selection = this.$document.getSelection();
            selection.removeAllRanges();
            selection.addRange(caretRange);
        }
    }

    private moveCaretToPrevEditable(sourceElement: HTMLElement) {
        const caretRange = this.$document.createRange();
        const previousEditableNode = this.getPreviousEditable(sourceElement);
        if (previousEditableNode) {
            const offset = (previousEditableNode as Text).length;
            caretRange.setStart(previousEditableNode, offset);
            const selection = this.$document.getSelection();
            selection.removeAllRanges();
            selection.addRange(caretRange);
        }
    }

    /**
     * select a smartfield
     *
     * @param field field to select
     * @param append should be appended to the current selection
     */
    public selectSmartField(field: any, append: boolean = false) {
        if (!append || !this.selectedSmartFields) {
            this.selectedSmartFields = [];
        }
        this.selectedSmartFields.push(field);
    }

    /**
     * drag enter event handler
     *
     * @param event
     */
    public onDragEnter(event: DragEvent): boolean {
        this.k2TextboxOnDragEnter.emit();
        this._forceCopyOnDrag = this.isSmartFieldFromParentExpression();
        return false;
    }

    /**
     * drag leave event handler
     *
     * @param event
     */
    public onDragLeave(event: DragEvent): boolean {
        this.k2TextboxOnDragLeave.emit();
        if (this._composerEnabledForDragging) {
            this._composerEnabledForDragging = false;
            this.toggleStateForComposer(true);
        }
        return false;
    }

    /**
     * drag over event handler
     *
     * @param event
     */
    public onDragOver(event: DragEvent): boolean {
        //if a composer is active and the item is a smartfield,
        //allow the drop by temporarily enabling the element for dropping
        if (this.composerEnabled &&
            (this._dragDropService.hasItem('configuredSmartField') ||
                this._dragDropService.hasItem('smartField'))) {
            this._composerEnabledForDragging = true;
            this.toggleStateForComposer(false);
        }
        event.dataTransfer.dropEffect = (event.ctrlKey === true || this._forceCopyOnDrag) ? 'copy' : 'move';
        if ($(event.target).parents('k2-smart-field-container').length > 0) {
            event.dataTransfer.dropEffect = 'none';
        }
        const sel = this.$document.getSelection();
        if (!sel.isCollapsed && sel.containsNode($(event.target)[0] as any)) {
            event.dataTransfer.dropEffect = 'none';
        }
        return false;
    }

    /**
     * drag start event handler
     *
     * @param event
     */
    public onDragStart(event: DragEvent): boolean {
        this.dragging = true;
        let src: SmartFieldContainerComponent;
        const srcParent = $(event.srcElement).parents('k2-smart-field-container');
        if (srcParent.length === 1) {
            src = this.smartFieldContainers.find((sfc) => sfc.element.nativeElement === srcParent[0]);
        }
        if (src && (this.selectedSmartFields?.length === 0 || !this.selectedSmartFields.find((sf) => sf === src?.dataModel))) {
            this.selectedSmartFields = [src.dataModel];
        }
        const configuredFields = this.getFieldsFromSelection();
        this._dragDropService.setItem('TextBoxItems', { source: this, data: configuredFields } as any);
        event.dataTransfer.setData(`text/plain`, `content`);
        event.dataTransfer.effectAllowed = `copyMove`;
        return true;
    }

    /**
     * drop event handler
     *
     * @param event
     */
    public onDrop(event: DragEvent): boolean {
        if (this._dragDropService.hasItem('canvasItem') ||
            this._dragDropService.hasItem('ruleDesignerItem') ||
            (this.k2CanDrop === false) ||
            $(event.target).parents('k2-smart-field-container').length > 0) {
            event.preventDefault();
            return;
        }

        let smartFieldPlugin: sourceCode.k2Designer.objectModel.smartFieldPlugins.ISmartFieldPlugin;
        let configuredFields: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[];
        let originalFields: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[];
        let originalSource: sourceCode.k2Designer.objectModel.smartFieldComposer.IComposerContainer;
        let fromContextBrowser = false;
        const droppedOnNewField = false;
        const fromParentExpression = false;
        const onlyText = false;
        let removeSelection = false;

        this._undoRedoService.addUndoMarker('-- textbox ondrop --');

        if (this._dragDropService.hasItem('smartField')) {
            const dragItem = this._dragDropService.getItem('smartField');

            if (this._dragDropService.hasItem('configuredSmartField')) {
                this._dragDropService.getItem('configuredSmartField');
            }

            const dropData = dragItem.getDropData<any, sourceCode.k2Designer.objectModel.core.itemProvider.IItemBase<any, any>>(dragItem.getDragData<any>());
            if (dragItem.getSource<sourceCode.k2Designer.objectModel.core.itemProvider.IItemBase<any, any>>().resolverType) {
                dropData.resolverType = dragItem.getSource<sourceCode.k2Designer.objectModel.core.itemProvider.IItemBase<any, any>>().resolverType;
            }
            smartFieldPlugin = this._smartFieldService.getSmartFieldPlugin(dropData.componentId);
            configuredFields = [smartFieldPlugin.getConfiguredData(dropData)];
            fromContextBrowser = true;

            this._processService.cacheExternalReferences(configuredFields);
        }
        else if (this._dragDropService.hasItem('TextBoxItems')) { // Coming from a textbox
            const dragData = this._dragDropService.getItem('TextBoxItems') as any;
            originalFields = dragData.data;
            originalSource = dragData.source;
            removeSelection = event.ctrlKey !== true && !this._forceCopyOnDrag;
            configuredFields = [];
            for (const [i, field] of originalFields.entries()) {
                configuredFields.push(field.clone());
                if (field.componentId === ComponentIdMapping.TextSmartField) {
                    if (!this.isMultiLine) {
                        (configuredFields[i] as any).text = this.sanitizeText((field as any).text);
                    }
                }
            }
        } else { // Plain text
            const dragData = event.dataTransfer.getData('text/html');
            if (dragData) {
                const newField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: dragData
                });
                configuredFields = [newField];
            }
        }

        if (configuredFields && configuredFields.length > 0) {
            // associate the configured field with the expression so that it can reference other objects (data fields/env fields/SMOs)
            if (!onlyText) {
                configuredFields.forEach((f) => f.associateWithParent ? f.associateWithParent(this.k2Expression) : null);
            }

            // set the cursor position
            let range: Range = null;
            const selection: Selection = this.$document.getSelection();
            let x: Number = event.x;
            let y: Number = event.y;
            const currentSelection = selection.rangeCount ? selection.getRangeAt(0) : null;

            // For those browsers that not yet support .x & .y
            if (!x) x = event.clientX;
            if (y) y = event.clientY;
            if ((<any>document).caretPositionFromPoint) {
                const pos = (<any>document).caretPositionFromPoint(x, y);
                range = this.$document.createRange();
                range.setStart(pos.offsetNode, pos.offset);
                selection.removeAllRanges();
                selection.addRange(range);
            } else if ((<any>document).caretRangeFromPoint) {
                range = (<any>document).caretRangeFromPoint(x, y);
                selection.removeAllRanges();
                selection.addRange(range);
            } else if ((<any>document).body.createTextRange) {
                // IE
                // put inside try-catch because IE throws an error if trying to drop
                // on something that is not content-editable, eg. the outer part of the
                // k2 textbox (which we specifically want to include to make drag/drop
                // easier.
                try {
                    range = (<any>document).body.createTextRange();
                    (<any>range).moveToPoint(event.pageX, event.pageY);
                } catch (e) {
                }
            }

            if ((range.startContainer as HTMLElement).tagName?.toLowerCase() === 'k2-smart-field-container' ||// dropped the field in a bad place, do nothing
                $(range.startContainer).parents('k2-smart-field-container').length > 0 || (
                !currentSelection?.collapsed && currentSelection?.isPointInRange(range.startContainer, range.startOffset))) { // dropped field on current selection, do nothing
                return false;
            }

            if (removeSelection && currentSelection && !currentSelection.collapsed) {
                const el = this.$document.createElement('caret');
                range.insertNode(el);
                (originalSource as any).removeRangeFromTextbox(currentSelection);
                range.setStartBefore($('caret')[0]);
                $('caret').remove();
                this.insertSmartFieldsAtCaret(configuredFields);
            } else if (originalSource && originalFields.length === 1 && originalFields[0].componentId !== this.componentIdMapping.TextSmartField) {
                this.insertSmartFieldsAtCaret(configuredFields);
                const el = this.$document.createElement('caret');
                range.insertNode(el);
                (originalSource as any).removeSmartFields(originalFields, true, true);
                range.setStartBefore($('caret')[0]);
                $('caret').remove();
            } else {
                this.insertSmartFieldsAtCaret(configuredFields);
            }

            this.mergeTextFields();
        }

        if (configuredFields?.length === 1 && smartFieldPlugin) {
            event.preventDefault();
        }

        //if (this.k2Expression.richText === true) {
        //    this.notifyChanges();
        //}

        if (this.k2TextboxOnDrop !== null && this.k2TextboxOnDrop !== undefined) {
            this.k2TextboxOnDrop.emit();
        }

        if (this._composerEnabledForDragging) {
            this.toggleStateForComposer(true);
            this._composerEnabledForDragging = false;
        }

        this.reRender(configuredFields?.length === 1 && smartFieldPlugin ?
            { field: configuredFields[0], config: smartFieldPlugin.configuration, userDrop: true, fromContextBrowser, droppedOnNew: droppedOnNewField } :
            null
        );
        if (!(configuredFields?.length === 1 && smartFieldPlugin)) {
            setTimeout(() => {
                if (this.editorDiv?.nativeElement.lastChild) {
                    const lastChild = this.editorDiv.nativeElement.lastChild;
                    const sel = this.$document.getSelection();
                    const range = this.$document.createRange();
                    range.selectNodeContents(lastChild.nodeType === Node.TEXT_NODE ? lastChild : this.getPreviousEditable(lastChild));
                    range.collapse(false);
                    sel.removeAllRanges();
                    sel.addRange(range);
                } else {
                    this.editorDiv?.nativeElement.focus();
                }
            }, 5);
        } else {
            this.isFocused = false;
        }
        return false;
    }

    /**
     * copy event handler
     *
     * @param event
     */
    @HostListener('document:copy', ['$event'])
    public onCopy(event: Event): boolean {
        const configuredFields = this.getFieldsFromSelection();
        if (!configuredFields) {
            return true;
        }

        const selection = this.$document.getSelection();
        let rawText = '';
        if (selection.rangeCount > 0) {
            const range = selection.getRangeAt(0);
            const frag = range.cloneContents();
            rawText = frag.textContent;
        }

        if (configuredFields.length > 0) {
            // cache the  external reference before removing the field from the array;
            // In updateObjectModel we will add it from cache if necessary.
            // We need it for copy as well as paste, as you can copy, delete, and then paste.
            if (this.selectedSmartFields.length > 0) {
                this._processService.cacheExternalReferences(this.selectedSmartFields);
            }
            this._clipboardService.setClipboard(configuredFields, rawText, null);
            event.preventDefault();
        }
        return false;
    }

    /**
     * Cut event handler
     *
     * @param event
     */
    @HostListener('document:cut', ['$event'])
    public onCut(event: Event): boolean {
        const configuredFields = this.getFieldsFromSelection();
        if (!configuredFields || configuredFields.length === 0) {
            return true;
        }
        this._undoRedoService.addUndoMarker('-- textbox update model - cut --');
        if (configuredFields.length > 0) {
            // cache the  external reference before removing the field from the array;
            // In updateObjectModel we will add it from cache if necessary.
            // We need it for copy as well as paste, as you can copy, delete, and then paste.
            if (this.selectedSmartFields.length > 0) {
                this._processService.cacheExternalReferences(this.selectedSmartFields);
            }
            this._clipboardService.setClipboard(configuredFields, null, null);
            event.preventDefault();
        }

        // Get selection and range
        const selection: Selection = window.getSelection();
        let range: Range = selection.rangeCount > 0 ? selection.getRangeAt(0) : null;
        if (range && !selection.isCollapsed) {
            const endContainer = range?.endContainer;
            this.removeRangeFromTextbox(range);
            if (range) {
                range = this.$document.createRange();
                range.setStart(endContainer, 0);
                selection.removeAllRanges();
                selection.addRange(range);
            }
        } else {
            if (configuredFields?.length === 1) {
                this.removeSmartFields(this.selectedSmartFields);
                this.selectedSmartFields = [];
            }
        }
        return false;
    }

    private _keyDownForPasting;
    private _fromKeyDown;

    /**
     * Paste event handler
     *
     * @param event
     */
    @HostListener('document:paste', ['$event'])
    public onPaste(event: Event): boolean {
        if (!this.isFocused && !(this.selectedSmartFields.length > 0)) {
            return true;
        }
        //keyDownForPasting will determine if the key is held down for pasting, if it is
        //then onPaste will not continue and will preventDefault.
        if (this._keyDownForPasting) {
            event.preventDefault();
            return;
        }

        if (this._fromKeyDown) {
            this._keyDownForPasting = true;
        }

        this._clipboardService.setCurrentContext('K2Textbox');
        const selection: Selection = this.$document.getSelection();
        const range: Range = selection.rangeCount ? selection.getRangeAt(0) : null;
        const doc = this._clipboardService.getSystemClipboard(event, this.isRichTextBox && this.isMultiLine);
        if (this._clipboardService.hasData() || doc) {
            this._undoRedoService.addUndoMarker('-- textbox update model - paste --');
            if (!selection.isCollapsed && range) {
                // Remove the selection's content (will be replaced)
                this.removeRangeFromTextbox(range);
            }
            let sfIndex = null;
            if (!range) {
                sfIndex = (this.k2Expression.smartFields as any[]).indexOf(this.selectedSmartFields[0]); // there can only be one smartfield in selection if the selection doesnt exist
                this.removeSmartFields(this.selectedSmartFields, undefined, false);
            }
            if (this._clipboardService.hasData()) {
                const items: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[] = this._clipboardService.getClipboard();
                const clonedFields: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[] = [];
                for (let i = 0; i < items.length; i++) {
                    const clonedField = items[i].clone<sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField>();
                    clonedFields.push(clonedField);

                    // reset internalId (ensure that new unique IDs will be created)
                    clonedField.internalId = undefined;
                    // ensure that a SmartObject and Inline Function is parented so that they
                    // can do externalDefinitions lookups
                    if (clonedField.componentId === ComponentIdMapping.k2SmartObjectField
                        || clonedField.componentId === ComponentIdMapping.k2InlineFunctionField
                    ) {
                        clonedField.associateWithParent(this.k2Expression);
                    }
                }
                if (!sfIndex) {
                    this.insertSmartFieldsAtCaret(clonedFields);
                } else {
                    if (sfIndex === this.k2Expression.smartFields.length) {
                        this.k2Expression.smartFields.push(...clonedFields);
                    } else {
                        clonedFields.reverse().forEach((f) => {
                            (this.k2Expression.smartFields as any[]).splice(sfIndex, 0, f);
                        });
                    }
                }
            }
            else if (doc) {
                const newField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: this.codeView ? this.decodeHtml(doc.innerHTML) : doc.innerHTML
                });
                this.insertSmartFieldsAtCaret([newField]);
            }

            this.mergeTextFields();
            event.stopPropagation();
            event.preventDefault();
            this.reRender();
            setTimeout(() => {
                if (this.editorDiv?.nativeElement.lastChild) {
                    const lastChild = this.editorDiv.nativeElement.lastChild;
                    const sel = this.$document.getSelection();
                    const range = this.$document.createRange();
                    range.selectNodeContents(lastChild.nodeType === Node.TEXT_NODE ? lastChild : this.getPreviousEditable(lastChild));
                    range.collapse(false);
                    sel.removeAllRanges();
                    sel.addRange(range);
                } else {
                    this.editorDiv?.nativeElement.focus();
                }
            }, 5);
        } else {
            return true;
        }
        return false;
    }

    /**
     * Insert the fields into the location of the caret
     *
     * @param configuredFields fields to insert
     * @returns index at which fields are inserted
     */
    private insertSmartFieldsAtCaret(configuredFields: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[]): number {
        const selection = this.$document.getSelection();
        let node = selection.anchorNode;
        let offset = selection.anchorOffset;

        if (node as HTMLElement === this.editorDiv.nativeElement) {
            node = node.childNodes[offset];
            offset = 0;
        }

        const prevSmartField = this.getPreviousSmartField(node) as HTMLElement;
        let index: number;
        if (prevSmartField) {
            index = parseInt(prevSmartField.id) + 1;
        } else {
            index = 0;
        }

        if (!node) { // editordiv is empty
            (this.k2Expression.smartFields as any[]).push(...configuredFields);
            return;
        }

        if (this.isStartOfText(node, offset)) { // If caret is at a placeholder or start of text, simply insert at the index
            configuredFields.reverse().forEach((f) => {
                (this.k2Expression.smartFields as any[]).splice(index, 0, f);
            });
        } else if (this.isEndOfText(node, offset)) { // If caret is at the end of a text entry, insert after
            if (index === this.k2Expression.smartFields.length - 1) {
                (this.k2Expression.smartFields as any[]).push(...configuredFields);
            } else {
                configuredFields.reverse().forEach((f) => {
                    (this.k2Expression.smartFields as any[]).splice(index + 1, 0, f);
                });
            }
        } else if ((node as HTMLElement).tagName?.toLowerCase() === 'k2-smart-field-container') {
            (this.k2Expression.smartFields as any[]).splice(index, 0, ...configuredFields);
        } else { // In the middle of a text entry
            let beforeContent = this.getTextValue(node, offset, false, true);
            let afterContent = this.getTextValue(node, offset, true, false);
            beforeContent = this.sanitizeText(beforeContent);
            afterContent = this.sanitizeText(afterContent);
            const newTextField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                text: afterContent,
            });

            if (!this.k2Expression.smartFields[index]) { // dropped on a placeholder at the end
                this.k2Expression.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: beforeContent
                }));
                (this.k2Expression.smartFields as any[]).push(...configuredFields);
                this.k2Expression.smartFields.push(newTextField);
            } else {
                (this.k2Expression.smartFields[index] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text = beforeContent;
                this.k2Expression.smartFields.splice(index + 1, 0, newTextField);
                configuredFields.reverse().forEach((f) => {
                    (this.k2Expression.smartFields as any[]).splice(index + 1, 0, f);
                });
            }
        }
        return index;
    }

    /**
     * Get a valid source expression
     *
     * @param draggedSmartField
     */
    public getValidSourceExpression(draggedSmartField: SmartFieldContainerComponent): any {
        let sourceExpression: any = draggedSmartField.dataModel.getLogicalParentModel();
        if (sourceExpression) {
            let cnt = 0;
            while (!sourceExpression.isParentOf && cnt < 10) {
                sourceExpression = sourceExpression.getLogicalParentModel();
                cnt++;
            }
        }
        else {
            draggedSmartField.k2TextBox.k2Expression;
        }
        return sourceExpression;
    }

    public isSmartFieldFromParentExpression() {
        //always allow new smartfields
        if (this._dragDropService.hasItem('smartField')) {
            return false;
        }

        if (this._dragDropService.hasItem('configuredSmartField')) {
            const originalSmartFieldMoved: SmartFieldContainerComponent = <any>this._dragDropService.peekItem('configuredSmartField');
            const sourceExpression = this.getValidSourceExpression(originalSmartFieldMoved);
            const myExpression = this.k2Expression;

            if (sourceExpression?.isParentOf(myExpression) &&
                originalSmartFieldMoved.isConfiguring) {
                return true;
            }
        }
        return false;
    }

    /**
     * open the smartfield composer
     *
     * @param data smartfield data
     * @param config smart field config
     * @param fromUserDrop is it from drop
     * @param fromContextBrowser is it from context browser
     * @param droppedOnNewTextBox
     */
    public openSmartFieldComposer(data: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField, config: sourceCode.k2Designer.objectModel.smartFieldPlugins.ISmartFieldPluginConfig, fromUserDrop: boolean = false, fromContextBrowser: boolean = false, droppedOnNewTextBox: boolean = false): void {
        //A fix for IE11 and Edge console error.
        if (!config) {
            return;
        }
        let showComposer = true; //cannot be shown
        let showComposerOnDrop = true; //will not be shown on drop

        if (data.instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.InlineFunctionField.implementor)) { //inline function has to have more than one parameter to open on drop
            showComposerOnDrop = (<sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.IInlineFunctionField>data).parameters.length > 0;
        } else if (data.instanceOf(sourceCode.k2Designer.objectModel.workflow.itemProvider.ItemReferenceItem.implementor)) {//Don't show composer for item reference field on drop or double click.
            showComposer = false;
        }

        if (((fromUserDrop === true && showComposerOnDrop === true && config.aspxLocation && config.aspxLocation.length && fromContextBrowser === true)
            || fromUserDrop !== true || droppedOnNewTextBox === true) && showComposer === true) {
            const container = this.smartFieldContainers.find((container) => container.dataModel === data);
            container.isConfiguring = true;
            if (config.aspxLocation) {
                const composerOptions = {
                    container: this,
                    smartField: container,
                    pluginConfig: config,
                    data,
                    eventHandlers: null,
                    arrow: {
                        left: 0,
                        top: 0,
                        container: {
                            top: 0,
                        },
                    },
                    commandDrawerOpen: false,
                    commandDrawerHeight: 0,
                    index: 0
                } as ISmartFieldComposerOptions;

                //Add support to open legacy composer in new textbox for non-migrated steps
                const legacyComposerScope: sourceCode.k2Designer.directives.k2SmartFieldComposer.ISmartFieldComposer = (<any>$(container.element.nativeElement).closest('.k2-smartfield-composer')).scope();

                if (legacyComposerScope?.open) {
                    (composerOptions as any).item = $(container.element.nativeElement);
                    legacyComposerScope.open(composerOptions as any);
                }

                this.openSmartFieldComposerEvent.emit(composerOptions);
            }
        } else {
            // This cant be fixed in the UI project unless the smart field composer is migrated
            // K2Designerhost should be tweakable to fix the issue
            this._smartFieldComposerService.updateArrowPositionFor(this);
        }
    }

    public onOpenSmartFieldComposer(container: SmartFieldContainerComponent) {
        if (sourceCode.k2Designer.utilities.constants.EXCLUDEDSMARTFIELDS.indexOf(container.dataModel.componentId) !== -1) {
            return;
        }
        const smartFieldPlugin = this._smartFieldService.getSmartFieldPlugin(container.dataModel.componentId);
        this.openSmartFieldComposer(container.dataModel, smartFieldPlugin.configuration);
    }


    /**
     * Key down event handler
     *
     * @param event
     */
    public onKeyDown(event: KeyboardEvent): boolean {
        switch (event.keyCode) {
            case keyCodes.BACKSPACE:
            case keyCodes.DELETE:
                this.processBackspaceDelete(event);
                break;
            case keyCodes.LEFT_ARROW:
                this.processLeftArrow(event);
                break;
            case keyCodes.UP_ARROW:
                this.processUpArrow(event);
                break;
            case keyCodes.RIGHT_ARROW:
                this.processRightArrow(event);
                break;
            case keyCodes.DOWN_ARROW:
                this.processDownArrow(event);
                break;
            case keyCodes.KEY_A:
                if (event.ctrlKey) {
                    this.selectedSmartFields = this.k2Expression.smartFields.filter((d) => d.componentId !== ComponentIdMapping.TextSmartField);
                }
                break;
            case keyCodes.KEY_Z: // ctrl-z for undo
            //Only allow one undo/redo operation at a time.
                if (event.ctrlKey && this._commandService.keyHaveBeenReleased()) {
                    this._undoRedoService.undo();
                    setTimeout(() => {
                        if (this.editorDiv?.nativeElement.lastChild) {
                            const lastChild = this.editorDiv.nativeElement.lastChild;
                            const sel = this.$document.getSelection();
                            const range = this.$document.createRange();
                            range.selectNodeContents(lastChild.nodeType === Node.TEXT_NODE ? lastChild : this.getPreviousEditable(lastChild));
                            range.collapse(false);
                            sel.removeAllRanges();
                            sel.addRange(range);
                        } else {
                            this.editorDiv?.nativeElement.focus();
                        }
                    }, 5);
                }
                break;
            case keyCodes.KEY_Y: // ctrl-y for redo
            //Only allow one undo/redo operation at a time.
                if (event.ctrlKey && this._commandService.keyHaveBeenReleased()) {
                    this._undoRedoService.redo();
                    setTimeout(() => {
                        if (this.editorDiv?.nativeElement.lastChild) {
                            const lastChild = this.editorDiv.nativeElement.lastChild;
                            const sel = this.$document.getSelection();
                            const range = this.$document.createRange();
                            range.selectNodeContents(lastChild.nodeType === Node.TEXT_NODE ? lastChild : this.getPreviousEditable(lastChild));
                            range.collapse(false);
                            sel.removeAllRanges();
                            sel.addRange(range);
                        } else {
                            this.editorDiv?.nativeElement.focus();
                        }
                    }, 5);
                }
                break;
            default:
                break;
        }
        event.stopPropagation();
        return true;
    }

    /**
     * key press event handler
     * key press only gets triggered by input that changes text
     *
     * @param event
     */
    public onKeyPress(event: KeyboardEvent): boolean {
        if (this.isCharacterKeyPress(event)) {
            const sel = this.$document.getSelection();
            this._undoRedoService.addUndoMarker(`-- textbox selection remove --`);
            if (!sel || sel.isCollapsed &&
                (this.selectedSmartFields.length === 1)) {
                const index = (this.k2Expression.smartFields as any[]).indexOf(this.selectedSmartFields[0]);
                if (this.k2Expression.smartFields[index - 1]?.componentId === this.componentIdMapping.TextSmartField) {
                    for (const el of this.smartFieldContainers) {
                        if (parseInt(el.element.nativeElement.id) === index) {
                            this.moveCaretToPrevEditable(el.element.nativeElement);
                            break;
                        }
                    }
                } else if (this.k2Expression.smartFields[index + 1]?.componentId === this.componentIdMapping.TextSmartField) {
                    for (const el of this.smartFieldContainers) {
                        if (parseInt(el.element.nativeElement.id) === index) {
                            this.moveCaretToNextEditable(el.element.nativeElement);
                            break;
                        }
                    }
                } else {
                    (this.k2Expression.smartFields as any[]).splice(index + 1, 0, sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                        text: '',
                    }));
                    for (const el of this.smartFieldContainers) {
                        if (parseInt(el.element.nativeElement.id) === index) {
                            this.moveCaretToNextEditable(el.element.nativeElement);
                            break;
                        }
                    }
                }
                this.removeSmartFields(this.selectedSmartFields, false, true);
                this.selectedSmartFields = [];
            } else {
                this.removeRangeFromTextbox(sel.getRangeAt(0), true);
            }
        }

        switch (event.keyCode) {
            case keyCodes.ENTER:
                this.processEnterKey(event);
                break;
            default:
                this.processValidKeyPress(event);
                break;
        }
        return true;
    }

    private processValidKeyPress(evt): void {
        if (this.isCharacterKeyPress(evt)) {
            //TODO: Also validate max length here

            // Constraint validation
            switch (this.textConstraint) {
                case TextBoxConstraint.Number: {
                    this.allowOnlyWholeNumbers(evt);
                    break;
                }
                case TextBoxConstraint.DropSelect:
                case TextBoxConstraint.DropOnly: {
                    evt.preventDefault();
                    break;
                }
                default: {
                    break;
                }
            }
        }
    }

    /**
     * Only numeric input accepted
     *
     * @param evt
     */
    private allowOnlyWholeNumbers(evt): void {
        if (evt.charCode < 48 || evt.charCode > 57) {
            evt.preventDefault();
        }
    }

    private isCharacterKeyPress(evt) {
        if (!evt) return false;
        if (typeof evt.which == `undefined`) {
            // This is IE, which only fires keypress events for printable keys
            return true;
        } else if (typeof evt.which == `number` && evt.which > 0) {
            // In other browsers except old versions of WebKit, evt.which is
            // only greater than zero if the keypress is a printable key.
            // We need to filter out backspace and ctrl/alt/meta key combinations
            return !evt.ctrlKey && !evt.metaKey && !evt.altKey && evt.which != 8;
        }
        return false;
    }

    private processLeftArrow(event: KeyboardEvent): void {
        const selection = this.$document.getSelection();
        if (selection.rangeCount === 0 && this.selectedSmartFields.length > 0) {
            event.preventDefault();
            const smartField = this.selectedSmartFields.pop();
            const index = this.k2Expression.smartFields.indexOf(smartField);

            for (const el of this.smartFieldContainers) {
                if (parseInt(el.element.nativeElement.id) === index) {
                    this.moveCaretToPrevEditable(el.element.nativeElement);
                    break;
                }
            }
            return;
        }
        if (!event.shiftKey) {
            if (selection.isCollapsed) {
                let caretNode = selection.anchorNode;
                let caretOffset = selection.anchorOffset;
                if (caretNode === this.editorDiv.nativeElement) {
                    caretNode = caretNode.childNodes[caretOffset];
                    caretOffset = 0;
                    if (!caretNode) { // editordiv is empty
                        return;
                    }
                }
                if (this.isStartOfText(caretNode, caretOffset)) // start of text node
                {
                    while (caretNode.parentElement && caretNode.parentElement !== this.editorDiv.nativeElement) { // make sure there is no br in between
                        caretNode = caretNode.parentElement;
                    }
                    if ((caretNode.previousSibling as HTMLElement)?.tagName?.toLowerCase() === 'br') {
                        return;
                    }
                    const prevField = this.getPreviousSmartField(caretNode); // Previous field will always be smartfield or nothing
                    const index = parseInt((prevField as HTMLElement)?.id ?? '0');
                    if (prevField) {
                        this.selectedSmartFields.push(this.k2Expression.smartFields[index]);
                        selection.removeAllRanges();
                        event.preventDefault();
                    }
                }
            } else if (selection.rangeCount > 0) {
                event.preventDefault();
                this.selectedSmartFields = [];
                selection.collapseToStart();
            }
        } else {
            if (selection.rangeCount > 0) {
                this.setSelectedSmartFields();
            }
        }
    }

    private processUpArrow(event: KeyboardEvent): void {
        const selection = this.$document.getSelection();
        if (selection.rangeCount > 0 && event.shiftKey) {
            setTimeout(() => {
                this.setSelectedSmartFields();
            }, 0);
        } else if (selection.rangeCount > 0 && this.selectedSmartFields.length) {
            this.selectedSmartFields = [];
        }
    }

    private processRightArrow(event: KeyboardEvent): void {
        const selection = this.$document.getSelection();
        if (selection.rangeCount === 0 && this.selectedSmartFields.length > 0) {
            event.preventDefault();
            const smartField = this.selectedSmartFields.pop();
            const index = this.k2Expression.smartFields.indexOf(smartField);

            for (const el of this.smartFieldContainers) {
                if (parseInt(el.element.nativeElement.id) === index) {
                    this.moveCaretToNextEditable(el.element.nativeElement);
                    break;
                }
            }
            return;
        }
        if (!event.shiftKey) {
            if (selection.isCollapsed) {
                let caretNode = selection.anchorNode;
                let caretOffset = selection.anchorOffset;
                if (caretNode === this.editorDiv.nativeElement) {
                    caretNode = caretNode.childNodes[caretOffset];
                    caretOffset = 0;
                    if (!caretNode) { // editordiv is empty
                        return;
                    }
                }
                if (this.isEndOfText(caretNode, caretOffset)) // end of text value
                {
                    while (caretNode.parentElement && caretNode.parentElement !== this.editorDiv.nativeElement) { // make sure there is no br in between
                        caretNode = caretNode.parentElement;
                    }
                    if ((caretNode.nextSibling as HTMLElement)?.tagName?.toLowerCase() === 'br') {
                        const placeholder = this.$document.createTextNode('\u200b');
                        caretNode.nextSibling.after(placeholder);
                        selection.setPosition(placeholder);
                        event.preventDefault();
                        return;
                    }
                    const nextField = this.getNextSmartField(caretNode); // Next field will always be smartfield or nothing
                    const index = parseInt((nextField as HTMLElement)?.id ?? '0');
                    if (nextField) {
                        this.selectedSmartFields.push(this.k2Expression.smartFields[index]);
                        selection.removeAllRanges();
                        event.preventDefault();
                    }
                }
            } else if (selection.rangeCount > 0 && !event.shiftKey) {
                this.selectedSmartFields = [];
                selection.collapseToEnd();
                event.preventDefault();
            }
        } else {
            if (selection.rangeCount > 0) {
                this.setSelectedSmartFields();
            }
        }
    }

    private processDownArrow(event: KeyboardEvent): void {
        const selection = this.$document.getSelection();
        if (selection.rangeCount > 0 && event.shiftKey) {
            setTimeout(() => {
                this.setSelectedSmartFields();
            }, 0);
        } else if (selection.rangeCount > 0 && this.selectedSmartFields.length) {
            this.selectedSmartFields = [];
        }
    }

    private processEnterKey(event: KeyboardEvent): void {
        event.preventDefault();
        if (!this.isMultiLine) {
            return;
        }
        const selection = window.getSelection();
        const range = selection.getRangeAt(0);
        const br = this.$document.createElement('br');
        const textNode = this.$document.createTextNode('\u200b'); //Passing ' ' directly will not end up being shown correctly
        range.insertNode(br);
        range.collapse(false);
        range.insertNode(textNode);
        range.selectNodeContents(textNode);
        range.collapse(false);

        selection.removeAllRanges();
        selection.addRange(range);
    }

    private processBackspaceDelete(event: KeyboardEvent): void {
        this._undoRedoService.addUndoMarker(`-- textbox backspace delete --`);
        const isBackSpace = event.keyCode === keyCodes.BACKSPACE;
        const selection = this.$document.getSelection();

        if (this.selectedSmartFields?.length) {
            if (selection.rangeCount === 0) {
                this.removeSmartFields(this.selectedSmartFields, true);
            } else {
                this.removeSmartFields(this.selectedSmartFields);
                this.onTextChanged(event as any);
            }
            this.selectedSmartFields = [];
        }

        if (this.k2Expression.smartFields.length === 0) { // Everything was deleted
            this.reRender();
            setTimeout(() => {
                this.editorDiv.nativeElement.focus();
            }, 5);
            return;
        }

        if (selection.isCollapsed && selection.anchorNode) { // is a caret position, not a selection
            let caretNode = selection.anchorNode;
            if (isBackSpace && this.isStartOfText(selection.anchorNode, selection.anchorOffset)) {
                while (caretNode.parentElement && caretNode.parentElement !== this.editorDiv.nativeElement) { // make sure there is no br in between
                    caretNode = caretNode.parentElement;
                }
                if ((caretNode.previousSibling as HTMLElement)?.tagName?.toLowerCase() === 'br') {
                    return;
                }
                const prevField = this.getPreviousSmartField(selection.anchorNode) as HTMLElement;
                if (prevField) {
                    event.preventDefault();
                    this.removeSmartFields([this.k2Expression.smartFields[parseInt(prevField.id)]]);
                    setTimeout(() => { this.onTextChanged(event as any); }, 0);
                }
                return;
            }
            if (!isBackSpace && this.isEndOfText(selection.anchorNode, selection.anchorOffset)) {
                while (caretNode.parentElement && caretNode.parentElement !== this.editorDiv.nativeElement) { // make sure there is no br in between
                    caretNode = caretNode.parentElement;
                }
                if ((caretNode.nextSibling as HTMLElement)?.tagName?.toLowerCase() === 'br') {
                    return;
                }
                const nextField = this.getNextSmartField(selection.anchorNode) as HTMLElement;
                if (nextField) {
                    event.preventDefault();
                    this.removeSmartFields([this.k2Expression.smartFields[parseInt(nextField.id)]]);
                    setTimeout(() => { this.onTextChanged(event as any); }, 0);
                }
                return;
            }
        }
    }

    /**
     * sanitize the text for model update
     *
     * @param text
     */
    public sanitizeText(text: string): string {
        if (this.codeView) {
            text = this.decodeHtml(text);
        }

        let cleanedText: string = text;
        const replacements = [
            { regex: /[\u200b]/g, value: '' }, // remove nbsp
            { regex: /<\!--.*?-->/g, value: '' } // remove html comments
        ];

        if (!this.isMultiLine) {
            replacements.push({ regex: /\r?\n|\r/g, value: '' }); // remove any newlines
            replacements.push({ regex: /\<br\>/g, value: '' }); // and any <br>
        }

        if (cleanedText && cleanedText.length > 0) {
            for (const toReplace of replacements) {
                cleanedText = cleanedText.replace(toReplace.regex, toReplace.value);
            }
        }

        return cleanedText;
    }

    /**
     * decode given html to raw text
     *
     * @param html
     */
    private decodeHtml(html) {
        const txt = this.$document.createElement('textarea');
        txt.innerHTML = html;
        const decoded = txt.value;
        txt.remove();
        return decoded;
    }

    /**
     * Format the text for displaying
     *
     * @param text
     */
    public formatText(text: string): string {
        if (!(text?.trim())) {
            return '\u200b';
        }
        let formattedText: string = text;
        const replacements = [
            { regex: /\<br\>/g, value: '<br>\u200b' }
        ];

        if (formattedText && formattedText.length > 0) {
            for (const toReplace of replacements) {
                formattedText = formattedText.replace(toReplace.regex, toReplace.value);
            }
        }

        return formattedText;
    }

    private isEndOfText(node: Node, offset: number): boolean {
        if (node.nodeType === Node.TEXT_NODE) {
            if (offset < (node as Text).length && node.nodeValue !== '\u200b') { // not the end of the text node
                return false;
            }
        }
        if (node === this.editorDiv.nativeElement) {
            node = node.childNodes[offset];
            if (!node) { // editordiv is empty
                return true;
            }
        }

        let nextNode: Node = node;
        do {
            nextNode = this.getNextNode(nextNode);
            if (nextNode.nodeType === Node.TEXT_NODE) {
                return false;
            }
        } while (nextNode &&
        this.editorDiv.nativeElement.contains(nextNode) &&
            !((nextNode as HTMLElement).tagName?.toLowerCase() === 'k2-smart-field-container')
        );
        return true;
    }

    private isStartOfText(node: Node, offset: number): boolean {
        if (node.nodeType === Node.TEXT_NODE) {
            if (offset !== 0 && node.nodeValue !== '\u200b') { // not the start of the text node
                return false;
            }
        }
        if (node === this.editorDiv.nativeElement) {
            node = node.childNodes[offset];
            if (!node) { // editordiv is empty
                return true;
            }
        }
        let prevNode: Node = node;
        do {
            prevNode = this.getPreviousNode(prevNode);
            if (prevNode.nodeType === Node.TEXT_NODE) {
                return false;
            }
        } while (prevNode &&
        this.editorDiv.nativeElement.contains(prevNode) &&
            !((prevNode as HTMLElement).tagName?.toLowerCase() === 'k2-smart-field-container')
        );
        return true;
    }

    /**
     * Remove smartfields from the current textbox
     *
     * @param fields fields to remove
     * @param shouldMergeText should any text be merged when a field is deleted
     */
    public removeSmartFields(fields: sourceCode.k2Designer.objectModel.core.configuredSmartFields.IConfiguredSmartField[], shouldMergeText = false, shouldRemoveText = true) {
        for (const field of fields) {
            const index = this.k2Expression.smartFields.indexOf(field);
            if (index === -1) {
                throw new Error('tried to remove smartfield that was not in this textbox');
            }
            if (this.k2Expression.smartFields[index] !== field) {
                throw new Error('tried to remove smartfield that was not at the index in this textbox');
            }

            if (shouldRemoveText &&
                this.k2Expression.smartFields[index - 1]?.componentId === ComponentIdMapping.TextSmartField &&
                this.k2Expression.smartFields[index + 1]?.componentId === ComponentIdMapping.TextSmartField &&
                !fields.includes(this.k2Expression.smartFields[index + 1])) {
                if (shouldMergeText) {
                    (this.k2Expression.smartFields[index - 1] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text =
                        ((this.k2Expression.smartFields[index - 1] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text as string)
                            .concat((this.k2Expression.smartFields[index + 1] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text);
                }
                this.k2Expression.smartFields.splice(index + 1, 1);
            }
            field.removeFromModelArray();
            const existingEl = this.smartFieldContainers.find((sfc) => sfc.dataModel === field);
            if (existingEl) {
                existingEl.element.nativeElement.classList.add('deleted');
            }
            this.displayedFields.splice(index, 1);
        }
    }

    /**
     * set the states for the composer
     *
     * @param composerActive
     */
    public toggleStateForComposer(composerActive: boolean) {
        this.composerEnabled = composerActive;
        this.enabled = !composerActive;
    }

    /**
     * Retrieve Text value of a field
     *
     * @param Node Node inside the field
     * @param offset the offset of the start node
     * @param forwards take all text to end of field
     * @param backwards take all text from the start of the field
     */
    private getTextValue(node: Node, offset: number = null, forwards: boolean = null, backwards: boolean = null): string {
        let fieldValue: string;
        let fieldValueForward = '';
        let fieldValueBackward = '';

        if (node !== this.editorDiv.nativeElement && !this.editorDiv.nativeElement.contains(node)) {
            return null;
        }

        let currentNode = node;
        while (currentNode.parentElement && currentNode.parentElement != this.editorDiv.nativeElement) {
            currentNode = currentNode.parentElement;
        }

        if (currentNode.nodeType === Node.COMMENT_NODE) {
            fieldValue = '';
        } else {
            const range = this.$document.createRange();
            if ((offset !== undefined || offset !== null) && forwards) {
                range.setStart(node, offset);
                range.setEndAfter(currentNode);
            } else if ((offset !== undefined || offset !== null) && backwards) {
                range.setStartBefore(currentNode);
                range.setEnd(node, offset);
            } else {
                range.selectNode(currentNode);
            }
            const textFrag = range.cloneContents();
            const container: HTMLElement = this.$document.createElement('div');
            container.appendChild(textFrag.cloneNode(true));
            fieldValue = container.innerHTML;
            container.remove();
        }

        let backwardsNode: Node = backwards || offset === undefined || offset === null ? currentNode.previousSibling : null;
        let forwardsNode: Node = forwards || offset === undefined || offset === null ? currentNode.nextSibling : null;

        while (backwardsNode || forwardsNode) {
            switch (backwardsNode?.nodeType) {
                case Node.ELEMENT_NODE:
                    if ((backwardsNode as HTMLElement).classList?.contains('deleted')) { // disregard deleted smartfields
                        break;
                    } else if ($(this.editorDiv.nativeElement).has(backwardsNode as HTMLElement).length === 0 ||
                        (backwardsNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') { // reached the start
                        backwardsNode = null;
                    } else {
                        fieldValueBackward = (backwardsNode as HTMLElement).outerHTML.concat(fieldValueBackward);
                    }
                    break;
                case Node.TEXT_NODE:
                    fieldValueBackward = (backwardsNode as Text).textContent.concat(fieldValueBackward);
                    break;
                case Node.COMMENT_NODE:
                    break;
                default: // Do nothing because its most likely undefined/null
                    break;
            }

            switch (forwardsNode?.nodeType) {
                case Node.ELEMENT_NODE:
                    if ((forwardsNode as HTMLElement).classList.contains('deleted')) { //disregard deleted smart fields
                        break;
                    } else if ($(this.editorDiv.nativeElement).has(forwardsNode as HTMLElement).length === 0 ||
                        (forwardsNode as HTMLElement).tagName.toLowerCase() === 'k2-smart-field-container') { // reached the end
                        forwardsNode = null;
                    } else {
                        fieldValueForward = fieldValueForward.concat((forwardsNode as HTMLElement).outerHTML);
                    }
                    break;
                case Node.TEXT_NODE:
                    fieldValueForward = fieldValueForward.concat((forwardsNode as Text).textContent);
                    break;
                case Node.COMMENT_NODE: // Need special logic here to filter angular comments before comments can be saved
                    break;
                default: // Do nothing because its most likely undefined/null
                    break;
            }

            if (backwardsNode) {
                backwardsNode = backwardsNode.previousSibling;
            }
            if (forwardsNode) {
                forwardsNode = forwardsNode.nextSibling;
            }
        }

        return fieldValueBackward.concat(fieldValue, fieldValueForward);
    }

    /**
     * Indicates whether or not the control is a rich text editor control or not (read-only)
     */
    public get isRichTextBox(): boolean {
        return (typeof this.k2Expression.richText !== 'undefined' && this.k2Expression.richText === true);
    }

    private mergeTextFields() {
        for (let i = 0; i < this.k2Expression.smartFields.length; i++) {
            let field = this.k2Expression.smartFields[i] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField;
            while (field &&
                field.componentId === ComponentIdMapping.TextSmartField
                && (!field.text || !field.text.trim())) {
                this.k2Expression.smartFields.splice(i, 1);
                field = this.k2Expression.smartFields[i] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField;
            }
            while (field &&
                field.componentId === ComponentIdMapping.TextSmartField &&
                this.k2Expression.smartFields[i + 1]?.componentId === ComponentIdMapping.TextSmartField) {
                (this.k2Expression.smartFields[i] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text =
                    ((this.k2Expression.smartFields[i] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text as string)
                        .concat((this.k2Expression.smartFields[i + 1] as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField).text);
                this.k2Expression.smartFields.splice(i + 1, 1);
            }
        }
    }

    private getFieldsFromSelection(): any[] {
        if (!this.isFocused && !(this.selectedSmartFields.length > 0)) {
            return null;
        }
        const configuredFields = [];
        const selection = this.$document.getSelection();
        if (selection && selection.rangeCount > 0 && !selection.isCollapsed && (selection.anchorNode === this.editorDiv.nativeElement || $(selection.anchorNode).parents('.k2-text-editor').length > 0)) { // ensure that the selection is inside the editor and that there is a selection
            const range = selection.getRangeAt(0);
            this._clipboardService.setCurrentContext('K2Textbox');

            const startOffset = range.startContainer === this.editorDiv.nativeElement ? 0 : range.startOffset;
            const startNode = range.startContainer === this.editorDiv.nativeElement ? range.startContainer.childNodes[range.startOffset] : range.startContainer;
            const fieldBeforeStart = this.getPreviousSmartField(startNode);
            const startIndex = fieldBeforeStart ? parseInt((fieldBeforeStart as HTMLElement).id) + 1 : 0; // caret start index

            const endOffset = range.endContainer === this.editorDiv.nativeElement ? 0 : range.endOffset;
            const endNode = range.endContainer === this.editorDiv.nativeElement ? range.endContainer.childNodes[range.endOffset] : range.endContainer;
            const fieldAfterEnd = this.getNextSmartField(endNode);
            const endIndex = fieldAfterEnd ? parseInt((fieldAfterEnd as HTMLElement).id) - 1 : this.k2Expression.smartFields.length - 1; // caret end index

            if (startIndex === endIndex) {
                const textFrag = range.cloneContents();
                const container: HTMLElement = this.$document.createElement('div');
                container.appendChild(textFrag.cloneNode(true));
                configuredFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                    text: this.codeView ? this.decodeHtml(container.innerHTML) : container.innerHTML,
                }));
                container.remove();
            } else {
                for (let i = startIndex; i <= endIndex; i++) {
                    if (this.selectedSmartFields.includes(this.k2Expression.smartFields[i])) {
                        configuredFields.push(this.k2Expression.smartFields[i]);
                    } else {
                        switch (i) {
                            case startIndex:
                                configuredFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                                    text: this.sanitizeText(this.getTextValue(startNode, startOffset, true)),
                                }));
                                break;
                            case endIndex:
                                configuredFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                                    text: this.sanitizeText(this.getTextValue(endNode, endOffset, false, true))
                                }));
                                break;
                            default:
                                let field = this.k2Expression.smartFields[i];
                                if (field.componentId === ComponentIdMapping.TextSmartField) {
                                    field = field.clone();
                                }
                                configuredFields.push(field);
                        }
                    }
                }
            }
        } else if (this.selectedSmartFields) {
            this.selectedSmartFields.forEach((field) => {
                configuredFields.push(field);
            });
        }
        return configuredFields;
    }

    /**
     * Remove the selected contents from the textbox
     */
    public removeRangeFromTextbox(range: Range, shouldRemoveSmartFields: boolean = true): void {
        this.setSelectedSmartFields(range);

        // Remove the content of the selection (cutting)
        if (range && !range.collapsed) {
            range.deleteContents();
        }

        if (this.selectedSmartFields && shouldRemoveSmartFields) {
            this.removeSmartFields(this.selectedSmartFields);
            this.selectedSmartFields = [];
        }

        if (range) {
            this.textChangedDebounced(null, range);
        }
    }

    /**
     * Focus this textbox
     */
    public doFocus() {
        this.editorDiv.nativeElement.focus();
    }

    private _ownsActiveComposer: boolean;
    public disableForComposer() {
        this._ownsActiveComposer = true;
        this.toggleStateForComposer(this._ownsActiveComposer);
    }

    public enableForComposer() {
        this._ownsActiveComposer = false;
        this.toggleStateForComposer(this._ownsActiveComposer);
    }

    public composerClosed() {
        for (const container of this.smartFieldContainers) {
            container.isConfiguring = false;
        }
    }
}
