import { Component, ElementRef, Inject, Input, OnInit } from '@angular/core';
import { PopupConfigStates } from 'src/app/shared/enums/popup/popup-config-states';
import { IPopupButton } from '../../../../types/models/popup/i-popup-button';
import { IRecentsService } from '../../../../types/services/recents/i-recents-service';
import { IContextPopupConfiguration } from '../../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { IHierarchyDialogContext } from '../../../../types/uiComponents/pluginWizards/IHierarchyDialogContext';
import { PopupService } from '../../../services/popup/popup.service';
import { RecentsServiceToken } from '../../../services/recents/recents-service-factory';
import { ResourceService } from '../../../services/resource/resource.service';
import { popup } from '../../../shared/constants/wizard-popup';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

type IAssetItem = sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItem;

@Component({
    selector: 'k2-hierarchy-dialog',
    templateUrl: './hierarchy-dialog.component.html',
    styleUrls: ['./hierarchy-dialog.component.less']
})
export class HierarchyDialogComponent extends BaseResourceComponent implements OnInit {
    @Input() public config: IContextPopupConfiguration;
    @Input() public buttonClicked: (button: IPopupButton) => void;

    //Resource string for recents header
    public recentsHeader = '';

    public context: IHierarchyDialogContext;

    /**
     * Selected item when not drilled downed.
     */
    public selectedItem: IAssetItem;

    /**
     * PopupConfigState enum
     */
    public state = PopupConfigStates;

    /**
     * Add item to the breadcrumb.
     */
    crumbToAdd: IBreadCrumbItem;

    /**
     * String that will display in the breadcrumb search box.
     */
    public searchText: string;

    public hierarchySectionWidth: string;

    //Private members
    private _recentItemSelected = false;

    constructor(protected resourceService: ResourceService,
        private popupService: PopupService,
        @Inject(RecentsServiceToken) private recentsService: IRecentsService,
        private element: ElementRef) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.context = this.config.context as IHierarchyDialogContext;

        //Initalize recents and state
        if (!this.recentsService.hasContext(this.context.recentContextString)) {
            this.recentsService.registerContext(this.context.recentContextString, 25);
        }
        this.context.recentItems = this.getRecentItems();
        this.context.stateConfiguration ??= { state: PopupConfigStates.normal };

        this.getPersistedAssetItem();
    }

    /**
     * Method called when clicked on a recent item.
     */
    public recentItemClicked(selectedRecentItem: IAssetItem): void {
        this.config.validationModel = [selectedRecentItem];
        this.selectedItem = selectedRecentItem;
        this._recentItemSelected = true;
        this.config.buttons.okButton.enabled = true;
        this.setReturnData();
    }

    /**
     * Method called when clicked on a list item.
     */
    public listItemClicked(item: IAssetItem): void {
        //if drilldown is allowed then add it to the breadcrumb
        if (item.allowDrillDown || item.allowAlternateDrillDown) {
            if (item.path) {
                item.path.length = 0;

                if (this.context.crumbItems &&
                    this.context.crumbItems.indexOf(item as IBreadCrumbItem) === -1) {
                    item.path.push(...this.context.crumbItems);
                }
            }

            if (this.context.crumbItems &&
                this.context.crumbItems.indexOf(item as IBreadCrumbItem) === -1) {
                const newBreadCrumbItems = this.context.crumbItems.concat();
                newBreadCrumbItems.push(item as any);

                this.context.crumbItems = newBreadCrumbItems;
                this.crumbToAdd = item as IBreadCrumbItem;

                this.context.onBreadCrumbAdded(item as IBreadCrumbItem);
            }

            if (this.config.validationModel) {
                this.config.validationModel.length = 0;
            }
            this.config.buttons.okButton.enabled = false;
        }
        else {
            this.config.validationModel = [item];
            this.selectedItem = item;
            this.setReturnData();

            this.config.buttons.okButton.enabled = true;
        }
    }


    /**
     * Method called when clicked on a list item.
     */
    public listItemDblClicked(selectedItem: IAssetItem): void {
        if (!selectedItem.allowDrillDown) {
            this.config.validationModel = [selectedItem];
            this.selectedItem = selectedItem;

            this.setReturnData();
            this.popupService.hide(popup.buttons.okResult, this.config);
        }
    }

    /***
     * Breadcrumb search text changed
     * */
    public onSearch() {
        if (this.searchText && this.searchText.length >= 2 ||
            (this.searchText && this.searchText.length === 0)) {

            //Callback method to invoke on implementation side.
            this.context.onSearch(this.searchText);
        }
    }

    /**
     * Window object (wrapped for testing purposes)
     */
    public get window(): any {
        return window;
    }

    /**
     * Event that fires when an crumb item is selected
     */
    public onCrumbItemSelected(event: IBreadCrumbItemEvent): void {
        this.context.crumbItems = event.newCrumbs;
        this.context.onBreadCrumbClicked(event.item);
    }

    /**
     * Get the recent items that is added by the hierarchy control.
     */
    private getRecentItems(): IAssetItem[] {
        const recentContext: string = this.context.recentContextString;
        if (this.recentsService.itemCount(recentContext) > 0) {
            const _mapping = {
                name: 'name',
                key: 'key',
                title: 'title',
                allowDrillDown: 'allowDrillDown',
                spriteClass: 'spriteClass',
                metaData: 'metaData',
                path: 'path'
            };
            const items = this.recentsService.getItems(this.context.recentContextString)
                .map<IAssetItem>(
                    (item: any): IAssetItem => {
                        const result: IAssetItem = <any>{};
                        for (const p in _mapping) {
                            result[p] = item[_mapping[p]];
                        }
                        (<any>result).__originalItem = item;
                        return result;
                    });
            return items;
        }
    }

    //Loop through all list items to get last selected asset item.
    private getPersistedAssetItem(): void {
        if (this.context.persistedAssetItemKey && this.context.persistedAssetItemKey.length > 0) {
            for (let i = 0; i < this.context.listItems.length; i++) {
                if (this.context.listItems[i].key === this.context.persistedAssetItemKey) {
                    this.listItemClicked(this.context.listItems[i]);
                    break;
                }
            }
        }
    }

    /**
     * Method that gets called when this dialog closes
     */
    private setReturnData(): void {
        if (this.selectedItem.path && this._recentItemSelected !== true) {
            this.selectedItem.path.length = 0;

            if (this.context.crumbItems) {
                this.selectedItem.path.push(...this.context.crumbItems);
            }
        }

        this.config.returnData = {
            selectedItem: this.selectedItem,
            selectedFromRecent: this._recentItemSelected,
            recentContextString: this.context.recentContextString
        };
    }
}
