import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockObjectModelService } from '../../../testing/mock-services/object-model-service';
import { IObjectModelService } from '../../../types/services/object-model/i-object-model-service';

const objectModelServiceFactory = () => environment.devHarness === true
    ? new MockObjectModelService()
    : sourceCode.k2Designer.core.environmentService.activeStudioItem.getService(sourceCode.k2Designer.services.studioItem.ObjectModelService);

export const ObjectModelServiceToken =
    new InjectionToken<IObjectModelService<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2Process>>('IObjectModelService');

export const ObjectModelServiceFactory = {
    provide: ObjectModelServiceToken,
    useFactory: objectModelServiceFactory,
    deps: [

    ]
};
