import { Component, Inject, Input, OnInit } from '@angular/core';
import { DataServiceToken } from 'src/app/services/data/data-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { PopupState } from 'src/app/shared/enums/plugin-wizard/popup-state';
import { shortUid } from 'src/app/shared/utils/general';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IDataService } from 'src/types/services/data/i-data-service';
import { IEnvironmentService } from 'src/types/services/environment/i-environment-service';
import { IContextWizardPopupConfiguration } from 'src/types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { IRemovePluginContext } from 'src/types/uiComponents/pluginWizards/IRemovePluginContext';
import { EnvironmentServiceToken } from '../../../../services/environment/environment-service-factory';
import { ProcessService } from '../../../../services/process/process.service';
import { ResourceService } from '../../../../services/resource/resource.service';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';
import { PageComponent } from '../../page/page.component';

@Component({
    selector: 'k2-sf-remove-integration',
    templateUrl: './remove-integration.component.html',
    styleUrls: ['./remove-integration.component.less'],
    providers: [{ provide: PageComponent, useExisting: SmartFormsRemoveIntegrationComponent }]
})
export class SmartFormsRemoveIntegrationComponent extends PageComponent implements OnInit {

    @Input() config: IContextWizardPopupConfiguration;

    @Input() showResult = false;

    @Input() isResultPage: boolean;

    /**
     * Unique identifier for the check-out check box.
     */
    checkOutCheckBoxId: string;

    /**
     * Unique identifier for the state check box.
     */
    stateCheckBoxId: string;

    /**
     * Unique identifier for the check-out check box.
     */
    actionCheckBoxId: string;

    /**
     * Unique identifier for the check-out check box.
     */
    itemReferenceCheckboxId: string;

    /**
     * Context provided for the popup
     */
    get pluginContext(): IRemovePluginContext {
        return this.config?.context as any;
    }

    /**
     *  Message of the error if occur.
     */
    errorMessage: string;

    /**
     * Resource string to localize UI
     */
    public resources: {
        [resourceId: string]: string;
    };

    // Enum imports for use in templates
    chooseControlTypes = ChooseControlTypes;

    buttonConfig: IPopupButtonsConfig = {
        previous: {
            buttonText: 'workflow_contextPopup_cancelButton'
        },
        next: {
            classOverride: 'modal-button alert large',
            buttonText: 'workflow_contextPopup_delete_event'
        },
        finish: {
            buttonText: 'workflow_contextPopup_okButton'
        }
    };

    constructor(@Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        private eventService: EventNotifierService,
        private processService: ProcessService,
        protected resourceService: ResourceService,
        @Inject(DataServiceToken) private dataService: IDataService) {
        super(resourceService);
        this.checkOutCheckBoxId = shortUid();
        this.stateCheckBoxId = shortUid();
        this.actionCheckBoxId = shortUid();
        this.itemReferenceCheckboxId = shortUid();
    }

    ngOnInit(): void {
        if (this.pluginContext.state?.isBase) this.pluginContext.deleteState = false;

        this.buttonConfig.previous.isVisible = !this.isResultPage;
    }

    beforePageDisplayed() {
        if(this.isResultPage){
            this.removeWorkflowIntegration();
        }
    }

    beforeNavigateNext() {}

    beforeNavigatePrevious() {}

    public toggleCheckboxValue(controlId: string): void {
        if (this.showResult) return;
        if (controlId == this.actionCheckBoxId) {
            this.pluginContext.deleteAction = !this.pluginContext.deleteAction;
        }
        else if (controlId == this.stateCheckBoxId) {
            this.pluginContext.deleteState = !this.pluginContext.deleteState;
        }
        else if (controlId == this.itemReferenceCheckboxId) {
            this.pluginContext.deleteItemReferences = !this.pluginContext.deleteItemReferences;
        }

        //Checkout should also be unchecked if all items are unchecked.
        if (!this.pluginContext.deleteAction && !this.pluginContext.deleteState && !this.pluginContext.deleteItemReferences) {
            this.pluginContext.checkOut = false;
        }
        else if (!this.pluginContext.checkOut) {
            this.pluginContext.checkOut = true;
        }
    }

    /**
     * Remove workflow integration from form.
     * */
    removeWorkflowIntegration(): void {
        this.pluginContext.inProgress = true;
        this.notifyPageStateChanged(PopupState.Busy);

        if (this.pluginContext.actionId) {
            this.dataService.httpGet('api/smartforms/removeworkflowintegration', {
                host: this.environmentService.currentEnvironment.hostId,
                hashes: [],
                formId: this.pluginContext.formSystemName,
                actionId: this.pluginContext.actionId,
                stateId: this.pluginContext.state.id,
                deleteAction: this.pluginContext.deleteAction,
                deleteState: this.pluginContext.deleteState,
                deleteItemReferenceMappings: this.pluginContext.deleteItemReferences,
                workflowname: this.environmentService.workflowFullname
            }, this.removeWorkflowIntegrationCompleted.bind(this), this.environmentService, false, null, false);
        }
        else {
            //Remove the form if it is added in the workflow definition, this will be added when the user select to add the rule later or an inteded plugin from Smartforms context.
            for (let i = 0; i < this.processService.root.configuration.processDefinitions.length; i++) {
                const formProcessDefinition = this.processService.root.configuration.processDefinitions[i] as
                    sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.IFormProcessDefinition;

                if (formProcessDefinition.formSystemName === this.pluginContext.formSystemName) {
                    //Mark the name, don't remove the entry.  We need something to know that the intended plugin is already configured or removed otherwise it will keep adding the inteded plugin
                    //each time it hits the constructor.
                    const removedFormSystemName = formProcessDefinition.formSystemName + '_removed';
                    (this.processService.root.configuration.processDefinitions[i] as
                        sourceCode.k2Designer.objectModel.workflow.process.k2Process.processDefinitions.formsPD.IFormProcessDefinition)
                        .formSystemName = removedFormSystemName;
                }
            }

            this.removeWorkflowIntegrationCompleted(null, null);
        }
    }

    /**
     * Callback method when form is updated
     */
    public removeWorkflowIntegrationCompleted(result: string, error: boolean): void {
        this.notifyPageStateChanged(PopupState.Normal);

        if (!error) {
            this.removeItemReferences();
            this.pluginContext.success = true;
            this.showResult = true;
        }
        else {
            const exception = JSON.parse(result);

            // Use the Inner Exception Message first, then fall back on Exception Message, else Message
            if (exception.InnerException) {
                this.errorMessage = exception.InnerException.ExceptionMessage || exception.InnerException.Message;
            }
            else {
                this.errorMessage = exception.ExceptionMessage || exception.Message;
            }

            this.pluginContext.success = false;
        }
        this.pluginContext.inProgress = false;
    }

    /**
     * Method to remove the item references that was created with the forms integration.
     */
    private removeItemReferences(): void {
        if (this.pluginContext.deleteItemReferences) {
            for (let i = 0; i < this.pluginContext.itemReferences.length; i++) {
                for (let j = 0; j < this.processService.root.configuration.itemReferences.length; j++) {
                    if (this.processService.root.configuration.itemReferences[j].systemName == this.pluginContext.itemReferences[i].name) {
                        //Remove item reference
                        this.processService.root.configuration.itemReferences[j].removeFromModelArray();
                    }
                }
            }
        }
    }

}
