import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockPopupService } from '../../../testing/mock-services/popup-service';
import { IPopupService } from '../../../types/services/popup/i-popup-service';

const legacyPopupServiceFactory = () => environment.devHarness === true
    ? new MockPopupService()
    : sourceCode.k2Designer.core.environmentService.popupService;

export const LegacyPopupServiceToken = new InjectionToken<IPopupService>('IPopupService');

export const LegacyPopupServiceFactory = {
    provide: LegacyPopupServiceToken,
    useFactory: legacyPopupServiceFactory,
    deps: [

    ]
};
