/// <reference path="../../../types/global.d.ts" />
export class HttpInterceptor implements IHttpInterceptor {

    constructor(public headers: { [name: string]: string }) {

    }

    /**
     * Overrides all xmlhttprequest open calls to define the headers passed in. Can only be called once in the app
     *
     * @param headers headers to set per request
     */
    public Intercept() {
        // This overrides the xml httprequest open function to set the request headers defined above on every open call
        // This defines the headers when the request is first created
        const that = this;
        (function(open) {
            XMLHttpRequest.prototype.open = function(method: string, url: any, async?: boolean, user?: string, password?: string) {
                open.call(this, method, url, async, user, password);
                if (that.headers) {
                    for (const i in that.headers) {
                        this.setRequestHeader(i, that.headers[i]);
                    }
                }
            };
        })(XMLHttpRequest.prototype.open);
    }
}
