import { Injectable } from '@angular/core';
import { IGetResourceFileCompletedCallback } from 'src/types/services/resources/i-resource-file-completed-callback';
import { IResourceInfo } from 'src/types/services/resources/i-resource-info';
import { IValidatedComponent } from '../../../types/validation/i-validated-component';
import { ILegacyResourceService } from '../../../types/services/resources/i-legacy-resource.service';

@Injectable({
    providedIn: 'root'
})
export class MockLegacyResourceService implements ILegacyResourceService {

    private _jsonStrings: {
        [resourceId: string]: string;
    } = {
        umUser_popup_user_managers: 'User Managers',
        wizard_workflow_integration_update_form: 'wizard_workflow_integration_update_form - {0}'
    };

    constructor() { }

    info: IResourceInfo;

    onGetResourceFileCompletedCallbacks: IGetResourceFileCompletedCallback[] = [];

    getString(resourceId: string, collectionId?: string): string {
        return this._jsonStrings[resourceId] ?? resourceId;
    }

    getResxFile(resourceName: string): string {
        //Set Mock resources
        this.strings[resourceName] = {...this._resx[resourceName]};

        //Do callback in timeout to simulate async order of resource file loaded
        setTimeout(() => {
            if (this.onGetResourceFileCompletedCallbacks && this.onGetResourceFileCompletedCallbacks.length)
            {for (const cb of this.onGetResourceFileCompletedCallbacks) {
                if (typeof cb === 'function') {
                    cb();
                }
            }}
        });
        return resourceName;
    }

    // used for resx
    strings: { [collectionId: string]: { [resourceId: string]: string } } = {};

    resolveTokenResourceString: (resourceStr: string,
        resourceTokens: sourceCode.k2Designer.objectModel.core.model.IModelArray<string>,
        modelObj: sourceCode.k2Designer.objectModel.core.model.IModelObject) => string =
        (resourceStr: string,
            resourceTokens: sourceCode.k2Designer.objectModel.core.model.IModelArray<string>,
            modelObj: sourceCode.k2Designer.objectModel.core.model.IModelObject) => {
            //Fake response that can be tested
            let errorMsg = '';
            if ((modelObj as IValidatedComponent)?.errorInformation?.length > 0) {
                errorMsg = (modelObj as IValidatedComponent).errorInformation[0]?.errorMessage;
            }
            return resourceStr + '|' + resourceTokens?.toString() + '|' + errorMsg;
        };

    private _resx: {
        [resourceName: string]: {
            [keyName: string]: string;
        };
    } = {
        Wizards: {
            wizard_workflow_integration_smartforms_usertask_open_task_rule: 'wizard_workflow_integration_smartforms_usertask_open_task_rule',
            smartforms_integration_start_rule_heading: 'smartforms_integration_start_rule_heading',
            wizard_workflow_integration_smartforms_usertask_open_task_rule_description: 'wizard_workflow_integration_smartforms_usertask_open_task_rule_description',
            smartforms_integration_start_rule_sub_heading: 'smartforms_integration_start_rule_sub_heading',
            wizard_workflow_integration_smartforms_usertask_open_task_rule_actions: 'wizard_workflow_integration_smartforms_usertask_open_task_rule_actions',
            wizard_workflow_integration_smartforms_form_rules: 'wizard_workflow_integration_smartforms_form_rules',
            breadcrumb_users_placeholder_text: 'Search Users',
            popup_UserDialog_Label_UserGroup:'User Group',
            general_RECENT: 'RECENT',
            general_No_Recents: 'NO RECENTS TO DISPLAY'
        },
        RulesDesigner: {
            ruleDesigner_ThisResponse_ChooseAction:'Choose Action...'
        },
        Controls:{
            filterExpression_choose_property: 'Choose Property'
        }
    };
}
