import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockItemProviderService } from '../../../testing/mock-services/item-provider-service';
import { IItemProviderService } from '../../../types/services/item-provider/i-item-provider-service';

const itemProviderServiceFactory = () => environment.devHarness === true
    ? new MockItemProviderService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.ItemProviderService);

export const ItemProviderServiceToken = new InjectionToken<IItemProviderService>('IItemProviderService');

export const ItemProviderServiceFactory = {
    provide: ItemProviderServiceToken,
    useFactory: itemProviderServiceFactory,
    deps: [

    ]
};
