import { EventEmitter, Inject, Injectable } from '@angular/core';
import { Subscription } from 'rxjs';
import { IModelArray } from '../../../types/models/base/i-model-array';
import { IRemoveEventPluginContext } from '../../../types/models/event-platform/i-remove-event-plugin-context';
import { ISubscription } from '../../../types/models/event-platform/i-subscription';
import { IConfiguredPluginDefinition } from '../../../types/models/integration/i-configured-plugin-definition';
import { IWorkflowData } from '../../../types/models/integration/i-workflow-data';
import { IPage } from '../../../types/models/popup/i-page';
import { IPageButton } from '../../../types/models/popup/i-page-button';
import { IPopupButton } from '../../../types/models/popup/i-popup-button';
import { IPopupStateConfiguration } from '../../../types/models/popup/i-popup-state-configuration';
import { IItemReference } from '../../../types/models/process/item-references/i-item-reference';
import { IDataService } from '../../../types/services/data/i-data-service';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { IProcessDefinitionPluginConfiguration } from '../../../types/services/process/i-process-definition-plugin-configuration';
import { IContextWizardPopupConfiguration } from '../../../types/uiComponents/pluginWizards/i-context-wizard-popup-configuration';
import { IDataMapperContext } from '../../../types/uiComponents/pluginWizards/IDataMapperContext';
import { IHierarchyDialogContext } from '../../../types/uiComponents/pluginWizards/IHierarchyDialogContext';
import { IntegrationType } from '../../shared/enums/integration/integration-type';
import { SharePointAssetType } from '../../shared/enums/integration/sharepoint-asset-type';
import { PopupConfigStates } from '../../shared/enums/popup/popup-config-states';
import { DataServiceToken } from '../data/data-service-factory';
import { EnvironmentServiceToken } from '../environment/environment-service-factory';
import { PopupService } from '../popup/popup.service';
import { ProcessService } from '../process/process.service';
import { ResourceService } from '../resource/resource.service';

type IAssetItem = sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItem;

@Injectable({
    providedIn: 'root'
})
export class WorkflowIntegrationService {

    constructor(@Inject(EnvironmentServiceToken) private _environmentService: IEnvironmentService,
        private resourceService: ResourceService,
        private popupService: PopupService,
        private _processService: ProcessService,
        @Inject(DataServiceToken) private _dataService: IDataService) { }

    /**
     * Method to remove the selected item from the configuration plugins.
     * Resolves promise on closing
     */
    public removeConfiguredPluginDefinition(pluginDef: IConfiguredPluginDefinition): Promise<void> {
        const promise = new Promise<void>((resolve, reject) => {
            if (pluginDef.integrationType === IntegrationType.WorkflowError) {
                const resourceObject = {
                    pageTitle: this.resourceService.getString('event_platform_when_any_workflow_errors'),
                    deleteEventHeading: this.resourceService.getString('event_platform_delete_event_heading'),
                    deleteEventUpdateHeading: this.resourceService.getString('event_platform_delete_event_update_heading_workflow_error_event'),
                    processing: this.resourceService.getString('event_platform_delete_event_processing'),
                };

                this.showPopupForDeletePlatformEvent(resourceObject, pluginDef).then(() => { resolve(); });
            } else {
                console.error(`Removing ${pluginDef.integrationType} type integrations not supported with removeConfiguredPluginDefinition.`);
            }
        });
        return promise;
    }

    /**
     * Method to get item references with the smartobject name.
     */
    public getSharePointItemReferencesBySmartObject(primarySmartObjectName: string): IModelArray<IItemReference> {
        const itemReferences: IModelArray<IItemReference> = [];

        //This will get the item references with primary, type and smartObject name.
        const primaryItemReference: IItemReference = (this._processService.root as sourceCode.k2Designer.objectModel.workflow.process.k2Process.IK2ProcessExtended)
            .findWorkflowItemReference(primarySmartObjectName, '', this._processService as any,
            true, 'sharePointEvent', '');
        if (primaryItemReference) {
            itemReferences.push(primaryItemReference);

            for (let i = 0; i < this._processService.root.configuration.itemReferences.length; i++) {

                if (this._processService.root.configuration.itemReferences[i].primaryReference === primaryItemReference.systemName) {
                    itemReferences.push(this._processService.root.configuration.itemReferences[i]);
                }
            }
        }

        return itemReferences;
    }

    /**
     * Method to edit the selected configured plugin.
     */
    public editPlugin(pluginDef: IConfiguredPluginDefinition): Promise<void> {
        return new Promise((resolve, reject) => {
            let pages: IPage[];
            for (const defPlugin of this._processService.definitionPlugins) {
                //Check for the integration type and the selected plugin to determine which popup to show and pages to populate.
                if (pluginDef.integrationType === IntegrationType.WorkflowError &&
                    defPlugin.name === 'WorkflowErrorEventProcessDefinition') {

                    pages = defPlugin.pages;
                    pages[0].finishButton.titleToken = 'workflow_contextPopup_okButton';
                    this.showWorkflowErrorHandlerPopup(pluginDef, pages, true).then(() => { resolve(); });
                }
            }
        });
    }

    private showNoUserEventsIntegrationErrorMessage(): Promise<void> {
        return new Promise((resolve, reject) => {
            this.popupService.showActionErrorDialog(this.resourceService.getString('smartforms_integration_duplicate_error_heading'),
                this.resourceService.getString('smartforms_integration_duplicate_error_body'),
                () => { resolve(); });
        });
    }

    /**
     * Method to show the wizard popup for deleting any event platform event
     *
     * @param resourceObject An object that contains all the resources for the specific event's aspx page.
     */
    private showPopupForDeletePlatformEvent(resourceObject, pluginDef: IConfiguredPluginDefinition): Promise<void> {
        return new Promise<void>((resolve, reject) => {
            const pages: IPage[] = [];
            pages.push(<IPage>{
                pageTitleToken: resourceObject.pageTitle,
                url: 'uiComponents/configPanel/plugins/pluginWizards/processWizards/eventPlatform/pages/eventPlatformRemovePage.html',
                previousButton: <IPageButton>{
                    titleToken: 'workflow_contextPopup_cancelButton',
                    visible: true
                },
                nextButton: <IPageButton>{
                    titleToken: 'workflow_contextPopup_delete_event',
                    visible: true,
                    class: 'modal-button alert large'
                },
                finishButton: <IPageButton>{
                    titleToken: 'workflow_contextPopup_okButton',
                    visible: false,
                    class: 'modal-button alert large'
                },
                hidePageIndicator: true
            },
                <IPage>{
                    pageTitleToken: resourceObject.pageTitle,
                    url: 'uiComponents/configPanel/plugins/pluginWizards/processWizards/eventPlatform/pages/eventPlatformRemoveResultPage.html',
                    previousButton: <IPageButton>{
                        titleToken: 'workflow_contextPopup_cancelButton',
                        visible: false
                    },
                    nextButton: <IPageButton>{
                        titleToken: 'workflow_contextPopup_nextButton',
                        visible: false
                    },
                    finishButton: <IPageButton>{
                        titleToken: 'workflow_contextPopup_okButton',
                        visible: true
                    },
                    hidePageIndicator: true
                });

            const popupConfig: IContextWizardPopupConfiguration = {
                pages,
                buttons: {},
                hideInfoButton: true,
                heading: '',
                modal: true,
                width: 400,
                context: {
                    selectedSubscriptionId: pluginDef.id,
                    resourceObject,
                } as IRemoveEventPluginContext,
                onClosed: (key: string, dismissResult: string) => {
                    resolve();
                },
                validationModel: []
            } as IContextWizardPopupConfiguration;

            this.popupService.showContextWizardPopup(popupConfig);
        });
    }

    private showWorkflowErrorHandlerPopup(pluginDef: IConfiguredPluginDefinition, pages: IPage[], editMode: boolean = false): Promise<string> {
        return new Promise<string>((resolve, reject) => {
            let tempOriginalConfiguration = null;
            let subscriptionModel: ISubscription = null;
            let subscriptionIndex = 0;

            if (!this._processService.root.configuration.eventPlatformConfiguration) {
                subscriptionModel = this.createNewSubscription(0);

                this._processService.root.configuration.eventPlatformConfiguration = sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.EventPlatformConfiguration.createInstance({
                    subscriptions: [subscriptionModel]
                });
            }
            else if (editMode === false) {

                tempOriginalConfiguration = this._processService.root.configuration.eventPlatformConfiguration.clone();

                const subscriptions = this._processService.root.configuration.eventPlatformConfiguration.subscriptions;

                // We will need to revisit this once we implement support for more events
                if (subscriptions.length === 0) {
                    subscriptionModel = this.createNewSubscription(subscriptions.length);
                    subscriptions.push(subscriptionModel);
                }
                else {
                    subscriptionModel = subscriptions[0];
                    subscriptionModel.state = sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.State.Enabled;
                }

            } else {
                const subscriptions = this._processService.root.configuration.eventPlatformConfiguration.subscriptions;

                for (let i = 0; i < subscriptions.length; i++) {
                    if (pluginDef.id === subscriptions[i].identifier) {
                        subscriptionIndex = i;
                        subscriptionModel = this._processService.root.configuration.eventPlatformConfiguration.subscriptions[subscriptionIndex];
                        tempOriginalConfiguration = this._processService.root.configuration.eventPlatformConfiguration.clone();
                    }
                }
            }

            const popupConfig: IContextWizardPopupConfiguration = {
                pages,
                buttons: {},
                hideInfoButton: true,
                heading: '',
                headingIcon: 'workflowerrorstartevent',
                modal: true,
                width: 400,
                customClass: 'event-platform',
                context: <IDataMapperContext>{
                    pageTitle: this.resourceService.getString('event_platform_on_any_error_heading').replace('{0}', this._environmentService.contextProviderService.itemInstance.title),
                    mapperTitle: this._environmentService.contextProviderService.itemInstance.title,
                    title: this._environmentService.contextProviderService.itemInstance.title,
                    icon: 'ic-callsubworkflow',
                    model: subscriptionModel,
                    hideIndicators: true,
                    pages
                },
                onClosed: (key: string, dismissResult: string) => {
                    resolve(dismissResult);
                },
                validationModel: []
            } as IContextWizardPopupConfiguration;

            this.popupService.showContextWizardPopup(popupConfig);
        });
    }

    /**
     * Creates a new subscription instance with the index appended to the identifier
     */
    public createNewSubscription(index: number): ISubscription {
        // creatorObjectIdentifier, displayName and data.id will be set at deployment
        const identifier: string = '_onanyworkflowerror_subscription_' + index;
        const subscriptionModel = sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.Subscription.createInstance({
            eventObjectIdentifier: 'workflow',
            eventIdentifier: 'com.k2.event.runtime.workflow.error.v1',
            creatorSystem: 'Workflow',
            creatorObjectIdentifier: null,
            displayName: null,
            version: '1.0',
            identifier,
            state: sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.State.Enabled,
            description: 'Start when any Workflow Errors',
            type: 'com.k2.actions.workflow.start.v1',
            mapping: sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.Mapping.createInstance({
                data: sourceCode.k2Designer.objectModel.workflow.process.k2Process.eventPlatform.Data.createInstance({
                    id: null,
                    subject: '',
                    version: 0,
                })
            })
        });

        return subscriptionModel;
    }

    /**
     * Create a new instance of the process definition and show a dialog with the plugin details.
     */
    showProcessDefinitionPlugin(item: IProcessDefinitionPluginConfiguration): Promise<string> {
        return new Promise<string>((resolve, reject) => {
            let newProcessDefinition;
            //Create a new instance of the plugin and add it to the process definition.
            if (item.cls.length > 0) {
                const lookup = sourceCode.k2Designer.objectModel.core.model.getImplementorExporter(item.cls);
                newProcessDefinition = sourceCode.k2Designer.objectModel.core.model.ModelObject.createFrom(lookup.implementor, lookup.exporter, null, null);
            }

            if (item.name === 'WorkflowErrorEventProcessDefinition') {
                // Skip past the hierarchy control to the next popup page
                this.showWorkflowErrorHandlerPopup(newProcessDefinition, item.pages).then(() => { resolve('success'); }).catch(() => { reject(); });
            } else {
                throw 'Only WorkflowErrorEventProcessDefinition popups are supported with this method.';
            }
        });
    }

    /**
     * Method to open the hierarchy control.
     */
    showFormsHierarchyControl(): Promise<IAssetItem> {

        return new Promise<IAssetItem>((resolve, reject) => {
            //Open the hierarchy control.
            const hierarchyDialogContext = {
                listItems: [],
                crumbItems: [],
                recentItems: [],
                hideRecents: false,
                onBreadCrumbAdded: (item: IBreadCrumbItem) => {
                    this.loadHierarchyItems(hierarchyDialogContext, item);
                },
                onBreadCrumbClicked: (item: IBreadCrumbItem) => {
                    this.loadHierarchyItems(hierarchyDialogContext, item);
                },
                onSearch: (value: string) => {

                },
                recentContextString: 'form',
                stateConfiguration: <IPopupStateConfiguration>{
                    message: '',
                    state: 'normal'
                }
            } as IHierarchyDialogContext;

            //load items
            this.loadHierarchyItems(hierarchyDialogContext);

            this.popupService.showHierarchyDialog(this.resourceService.getString('wizard_workflow_integration_select_smartforms'),
                false,
                hierarchyDialogContext,
                (item: IAssetItem): void => {
                    if (item) {
                        this._dataService.httpGet('api/smartforms/canIntegrateWithForm', {
                            host: this._environmentService.currentEnvironment.hostId,
                            hashes: [],
                            formGuid: item.key,
                            workflowName: this._environmentService.workflowFullname
                        },
                        (result: string, error: boolean) => {
                            if (!error) {
                                if (result.toUpperCase() === 'TRUE') {
                                    resolve(item);
                                }
                                else if (result.toUpperCase() === 'FALSE') {
                                    //Show not allowed error message.
                                    this.showNoUserEventsIntegrationErrorMessage().then(() => { reject('Hierarchy failed'); });
                                }
                            }
                        }, this._environmentService, false, null, false);
                    }
                });
        });
    }

    private refreshSub: Subscription;
    /**
     * Method to load bread crumbs from rest service.
     */
    private loadHierarchyItems(context: IHierarchyDialogContext, selectedItem?: IAssetItem): void {
        //Check if breadCrumbItem item is null.
        let assetItem: IAssetItem = null;
        if (selectedItem) {
            assetItem = <IAssetItem>{
                title: '',
                key: selectedItem.key,
                type: selectedItem.type
            };
        }

        context.stateConfiguration.message = '';
        context.stateConfiguration.state = PopupConfigStates.loading;
        if (this.refreshSub) {
            this.refreshSub.unsubscribe();
            this.refreshSub = null;
        }
        if (context.stateConfiguration.actionButtons) context.stateConfiguration.actionButtons[0].visible = false;

        //Call rest service.
        this._dataService.httpGet('api/smartforms/getcategoryforms',
            {
                host: this._environmentService.currentEnvironment.hostId,
                hashes: [],
                sourceItem: assetItem
            },
            (result: any, error: boolean) => {
                if (!error) {
                    const sourceItem: IAssetItem = result;
                    if (sourceItem !== null && sourceItem !== undefined) {
                        if (sourceItem.items.length > 0) {
                            //Populate the list items with the source items child items.
                            context.listItems = sourceItem.items;
                            context.stateConfiguration.state = PopupConfigStates.normal;
                        }
                        else {
                            //Clear items if no children on return item.
                            context.listItems = [];
                            context.stateConfiguration.message = this.resourceService.getString('hierachy_dialog_no_forms');
                            context.stateConfiguration.state = PopupConfigStates.empty;
                        }
                    }
                }
                else {
                    context.stateConfiguration.state = PopupConfigStates.error;
                    context.stateConfiguration.message = result ? JSON.parse(result).Message : '';
                    const refreshButton = <IPopupButton>{
                        key: 'btnRefresh',
                        text: this.resourceService.getString('refresh'),
                        onClick: new EventEmitter<IPopupButton>(),
                        visible: true
                    };
                    context.stateConfiguration.actionButtons = [refreshButton];
                    this.refreshSub = refreshButton.onClick.subscribe(() => { this.loadHierarchyItems(context); });
                }
            }, this._environmentService, false, null, false);
    }

    public showSharePointHierarchyControl(): Promise<IAssetItem> {

        return new Promise<IAssetItem>((resolve, reject) => {
            //Open the hierarchy control.
            const hierarchyDialogContext = {
                listItems: [],
                crumbItems: [],
                recentItems: [],
                hideRecents: false,
                onBreadCrumbAdded: (item: IBreadCrumbItem) => {
                    this.onSharePointBreadCrumbAdded(hierarchyDialogContext, item);
                },
                onBreadCrumbClicked: (item: IBreadCrumbItem) => {
                    this.onSharePointBreadCrumbClicked(hierarchyDialogContext, item);
                },
                onSearch(value: string) {},
                recentContextString: 'sharepoint',
                stateConfiguration: <IPopupStateConfiguration>{
                    message: '',
                    state: 'normal'
                }
            };

            this.loadServiceInstances(hierarchyDialogContext);

            //First check if there is not already an event-based or forms-based SharePoint integration done with the
            //same workflow name.
            this._dataService.httpGet('api/smartforms/checkIfSharePointIntegrationExists', {
                host: this._environmentService.currentEnvironment.hostId,
                hashes: [],
                workflowName: this._environmentService.workflowFullname
            }, (result: string, error: boolean) => {
                if (!error) {
                    if (result.toUpperCase() === 'FALSE') {

                        //No integrations exists with process name, continue as normal.
                        this.popupService.showHierarchyDialog(this.resourceService.getString('wizard_workflow_integration_select_sharePoint'),
                            false,
                            hierarchyDialogContext,
                            (item: IAssetItem) => {
                                resolve(item);
                            });
                    }
                    else if (result.toUpperCase() === 'TRUE') {
                        //Integration found (either SharePoint forms, or SharePoint event based with specific workflowname
                        //Show duplicate message.
                        this.popupService.showActionErrorDialog(this.resourceService.getString('sharepoint_integration_duplicate_error_heading'),
                            this.resourceService.getString('sharepoint_integration_duplicate_error_body'),
                            () => { reject(); });
                    }
                }
            }, this._environmentService, false, null, false);
        });
    }

    private loadServiceInstances(hierarchyDialogContext: IHierarchyDialogContext): void {
        const serviceInstances: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem = <any>{
            parentName: 'SmartObject_Service_Functions',
            sysName: 'GetServiceInstances',
            inputs: ['ServiceTypeName|SourceCode.SmartObjects.Services.SharePoint.SharePointService'],
            returns: ['ServiceInstanceSystemName|undefined|Key', 'ServiceInstanceName|undefined|Value', 'ServiceInstanceSystemName|undefined|AllowDrilldown'],
            type: 9
        };

        hierarchyDialogContext.stateConfiguration.message = '';
        hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.loading;
        if (hierarchyDialogContext.stateConfiguration.actionButtons) hierarchyDialogContext.stateConfiguration.actionButtons[0].visible = false;

        //load items
        this.loadSharePointHierarchyItems(hierarchyDialogContext, SharePointAssetType.site, '', '', serviceInstances);
    }

    private _asyncMethodsLoaded: number;
    /**
     * When a breadcrumb item is added to the breadcrumb, refresh the list.
     */
    private onSharePointBreadCrumbAdded(hierarchyDialogContext: IHierarchyDialogContext, item: IAssetItem): void {
        hierarchyDialogContext.stateConfiguration.message = '';
        hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.loading;
        if (hierarchyDialogContext.stateConfiguration.actionButtons) hierarchyDialogContext.stateConfiguration.actionButtons[0].visible = false;

        let serviceInstance: string;
        //The metaData is split with the asset type|service instance|sub site relative url
        if (item.metaData.split('|')[0] === SharePointAssetType.site.toString()) {
            serviceInstance = item.key;
        } else {
            serviceInstance = '';
        }

        let relativeUrl: string;
        //The metaData is split with the asset type|service instance|sub site relative url
        if (item.metaData.split('|')[0] === SharePointAssetType.subSite.toString()) {
            relativeUrl = item.metaData.split('|')[2] + item.key;
        } else {
            relativeUrl = '';
        }

        const subSites: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem = <any>{
            parentName: serviceInstance + '_Management_Site',
            sysName: 'GetSubSites',
            inputs: ['K2_Int_SubSiteRelativeUrl|' + relativeUrl, 'ReturnActivatedSitesOnly|true'],
            returns: ['RelativeUrl||Key', 'Title||Value', 'RelativeUrl||AllowDrilldown', 'K2_Int_SubSiteRelativeUrl||SubSiteRelativeUrl'],
            type: 9
        };

        const lists: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem = <any>{
            parentName: serviceInstance + '_Management_List',
            sysName: 'GetLists',
            inputs: ['K2_Int_SubSiteRelativeUrl|' + relativeUrl, 'Hidden|false', 'IsListSupported|true'],
            returns: ['Id||Key', 'Title||Value'],
            type: 9
        };

        const libraries: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem = <any>{
            parentName: serviceInstance + '_Management_Library',
            sysName: 'GetLibraries',
            inputs: ['K2_Int_SubSiteRelativeUrl|' + relativeUrl, 'Hidden|false', 'IsListSupported|true'],
            returns: ['Id||Key', 'Title||Value'],
            type: 9
        };
        this._asyncMethodsLoaded = 0;
        this.loadSharePointHierarchyItems(hierarchyDialogContext, SharePointAssetType.subSite, relativeUrl, serviceInstance, subSites);
        this.loadSharePointHierarchyItems(hierarchyDialogContext, SharePointAssetType.list, relativeUrl, serviceInstance, lists);
        this.loadSharePointHierarchyItems(hierarchyDialogContext, SharePointAssetType.library, relativeUrl, serviceInstance, libraries);
    }

    /**
     * Method when clicked on breadcrumb item.
     */
    private onSharePointBreadCrumbClicked(hierarchyDialogContext: IHierarchyDialogContext, item: IBreadCrumbItem): void {
        //Reset variables so that loading screen shows.
        hierarchyDialogContext.stateConfiguration.message = '';
        hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.loading;
        if (hierarchyDialogContext.stateConfiguration.actionButtons) hierarchyDialogContext.stateConfiguration.actionButtons[0].visible = false;

        if (!item) {
            this.loadServiceInstances(hierarchyDialogContext);
        }
        else {
            this.onSharePointBreadCrumbAdded(hierarchyDialogContext, item);
        }
    }

    /**
     * Method to load bread crumbs from rest service.
     */
    private loadSharePointHierarchyItems(hierarchyContext: IHierarchyDialogContext, spAssetType?: SharePointAssetType, relativeUrl?: string, serviceInstance?: string, selectedItem?: any): void {
        //Check if breadCrumbItem item is null.
        let assetItem: IAssetItem = null;
        if (selectedItem) {
            assetItem = selectedItem;
        }

        if (this.refreshSub) {
            this.refreshSub.unsubscribe();
            this.refreshSub = null;
        }
        //Call rest service.
        this._dataService.httpGet('api/contextplugins/getsmartobjectitemproviderdata',
            {
                host: this._environmentService.currentEnvironment.hostId,
                hashes: [],
                sourceItem: assetItem
            },
            (result: any, error: boolean) => {
                this.assetsSharePointLoaded(hierarchyContext, spAssetType, serviceInstance, relativeUrl, result, error);
            }
            , this._environmentService, false);
    }

    /**
     * Callback method when asset items are received from server.
     */
    private assetsSharePointLoaded(hierarchyDialogContext: IHierarchyDialogContext, spAssetType: SharePointAssetType, serviceInstance: string, relativeUrl: string, result: any, error: boolean): void {
        const assetItems = [];
        if (!error) {
            const sourceItem: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem = result.update.items[0];

            for (let i = 0; i < sourceItem.result.resultItems.length; i++) {

                //Need to save more info in metaData, so that recents work without additional server calls.
                //The metaData is split with the asset type|service instance|sub site relative url
                const assetItem = sourceCode.k2Designer.objectModel.core.itemProvider.AssetItem.createInstance({
                    key: '',
                    title: '',
                    allowDrillDown: false,
                    selectable: false,
                    metaData: spAssetType.toString() + '|' + serviceInstance + '|' + relativeUrl
                });

                if (spAssetType === SharePointAssetType.site || spAssetType === SharePointAssetType.subSite) {
                    assetItem.spriteClass = 'items-container-item-icon sprite-no-icon16 icon icon-size16 icon-set-steps ic-sharepointsitesandworkspaces';
                }
                else if (spAssetType === SharePointAssetType.list) {
                    assetItem.spriteClass = 'items-container-item-icon sprite-no-icon16 icon icon-size16 icon-set-steps ic-sharepointlistitem';
                }
                else if (spAssetType === SharePointAssetType.library) {
                    assetItem.spriteClass = 'items-container-item-icon sprite-no-icon16 icon icon-size16 icon-set-steps ic-sharepointdocuments';
                }

                for (let j = 0; j < sourceItem.result.resultItems[i].resultItems.length; j++) {

                    const resultItem: sourceCode.k2Designer.objectModel.workflow.configuredSmartObjectResult.ISmartObjectResultItem = (<any>sourceItem.result.resultItems[i]).resultItems[j];

                    if (resultItem.name.toLowerCase() === 'key' &&
                        assetItem.key === '') {
                        assetItem.key = resultItem.value;
                    } else if (resultItem.name.toLowerCase() === 'value' &&
                        (assetItem.title === '' || assetItem.title === assetItem.key)) {
                        assetItem.title = resultItem.value;
                    } else if (resultItem.name.toLowerCase() === 'allowdrilldown') {
                        if (resultItem.value && resultItem.value.length > 0) {
                            if (resultItem.value.toLowerCase() === 'true' || resultItem.value.toLowerCase() === 'false') {
                                assetItem.allowDrillDown = resultItem.value.toLowerCase() === 'true' ? true : false;
                            } else {
                                assetItem.allowDrillDown = true;
                            }
                        }
                    } else if (resultItem.name.toLowerCase() === 'alternatedrilldown') {
                        if (resultItem.value && resultItem.value.length > 0) {
                            if (resultItem.value.toLowerCase() === 'true' || resultItem.value.toLowerCase() === 'false') {
                                assetItem.allowAlternateDrillDown = resultItem.value.toLowerCase() === 'true' ? true : false;
                            } else {
                                assetItem.allowAlternateDrillDown = true;
                            }
                        }
                    }
                    else if (resultItem.name.toLowerCase() === 'selectable') {
                        if (resultItem.value && resultItem.value.length > 0) {
                            if (resultItem.value.toLowerCase() === 'true' || resultItem.value.toLowerCase() === 'false') {
                                assetItem.selectable = resultItem.value.toLowerCase() === 'true' ? true : false;
                            } else {
                                assetItem.selectable = true;
                            }
                        }
                    } else if (resultItem.name.toUpperCase() === 'SUBSITERELATIVEURL') {
                        if (resultItem.value && resultItem.value.length > 0) {
                            assetItem.metaData = spAssetType.toString() + '|' + serviceInstance + '|' + resultItem.value;
                        }
                    }

                    if (assetItem.title === '') {
                        assetItem.title = assetItem.key;
                    }
                }
                assetItems.push(assetItem);
            }

            //All async methods, to return Sites, List and libraries callback method is this.  The asyncMethodsLoaded gets
            //incremented with one, if it gets to 3, we know all have been called and we can update the loading screen.
            this._asyncMethodsLoaded += 1;

            if (this._asyncMethodsLoaded === 3 || spAssetType === SharePointAssetType.site) {
                if (assetItems.length > 0) {
                    hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.normal;
                }
                else {
                    //Clear items if no children on return item.
                    hierarchyDialogContext.stateConfiguration.message = this.resourceService.getString('sharePoint_integration_no_list_libraries_sites');
                    hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.empty;
                }
            }
        }
        else {
            hierarchyDialogContext.stateConfiguration.state = PopupConfigStates.error;
            hierarchyDialogContext.stateConfiguration.message = result ? JSON.parse(result).Message : '';
            const refreshButton = <IPopupButton>{
                key: 'btnRefresh',
                text: this.resourceService.getString('refresh'),
                onClick: new EventEmitter<IPopupButton>(),
                visible: true
            };
            hierarchyDialogContext.stateConfiguration.actionButtons = [refreshButton];
            this.refreshSub = refreshButton.onClick.subscribe(() => { this.loadSharePointHierarchyItems(hierarchyDialogContext); });
        }
        //Populate the list items with the source items child items.
        hierarchyDialogContext.listItems = assetItems;
    }

    /**
     * Retrieve the Sharepoint List Smart Object Name
     */
    GetSharepointListSmartObjectName(): Promise<string> {
        return new Promise((resolve, reject) => {
            this._dataService.httpGet('api/sharePoint/getListSmartObjectName', {
                host: this._environmentService.currentEnvironment.hostId,
                settingId: this._environmentService.referrer.settingid
            }, (smartObjectSystemName: string) => {
                resolve(smartObjectSystemName);
            });
        });
    }

    /**
     * Get the events that was saved with workflow name
     */
    GetSharepointIntegration(): Promise<IWorkflowData> {
        return new Promise<IWorkflowData>((resolve, reject) => {

            this._dataService.httpGet('api/sharePoint/getSharePointPluginDetails',
                {
                    host: this._environmentService.currentEnvironment.hostId,
                    workflowName: this._environmentService.workflowFullname
                }, (result, error) => {
                    if (error) reject();
                    else if (result === 'null') {
                        resolve(null);
                    } else {
                        resolve(result);
                    }
                });
        });
    }

    /**
     * When it is a new process and the required properties is in querystring, auto configure the item references and
     * start event on the process.
     */
    integrateWithSharePoint(smartObjectSystemName: string): Promise<string> {
        return new Promise<string>((resolve, reject) => {
            this._dataService.httpGet('api/sharePoint/autoConfingureSharePointEvents', {
                host: this._environmentService.currentEnvironment.hostId,
                workflowName: this._environmentService.workflowFullname,
                listId: this._environmentService.referrer.splistid,
                siteUrl: this._environmentService.referrer.spsiteurl,
                settingId: this._environmentService.referrer.settingid,
                smartObjectName: smartObjectSystemName,
                isDocumentLibrary: this._environmentService.referrer.isdoclib
            },
            (result, error) => {
                if (error) reject();
                else resolve(result);
            });
        });
    }

    GetStartWorkflowForms(intendedForms: string[]): Promise<sourceCode.k2Designer.objectModel.smartForms.IStartWorkflow[]> {
        return new Promise<sourceCode.k2Designer.objectModel.smartForms.IStartWorkflow[]>((resolve, reject) => {
            this._dataService.httpGet('api/smartforms/getstartworkflowforms', {
                host: this._environmentService.currentEnvironment.hostId,
                hashes: [],
                processName: this._environmentService.workflowFullname,
                intendedForms: JSON.stringify(intendedForms)
            }, (result, error) => {
                if (error) reject();
                else resolve(result);
            });
        });
    }

    GetFormCategoryAndName(): Promise<sourceCode.k2Designer.objectModel.smartForms.IFormContent> {
        return new Promise((resolve, reject) => {
            this._dataService.httpGet('api/smartforms/getformcategoryandname', {
                host: this._environmentService.currentEnvironment.hostId,
                hashes: [],
                form: this._environmentService.query.objectid
            }, (result, error) => {
                if (error) reject();
                else resolve(result);
            });
        });
    }
}
