import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { IPopupButton } from '../../../types/models/popup/i-popup-button';
import { INotificationService } from '../../../types/services/notification/i-notification.service';
import { IValidationService } from '../../../types/services/validation/ivalidation.service';
import { IContextPopupConfiguration } from '../../../types/uiComponents/pluginWizards/i-context-popup-configuration';
import { NotificationServiceToken } from '../../services/notification/notification-service-factory';
import { PopupService } from '../../services/popup/popup.service';
import { ResourceService } from '../../services/resource/resource.service';
import { ValidationServiceToken } from '../../services/validation/validation-service-factory';
import { keyCodes } from '../../shared/constants/general';
import { popup } from '../../shared/constants/wizard-popup';
import { ToastrTypes } from '../../shared/enums/model/toastr-types';
import { PopupHeaderTemplate } from '../../shared/enums/popup/popup-header-template';
import { PopupTemplate } from '../../shared/enums/popup/popup-template';
import { BaseResourceComponent } from '../base-resource/base-resource.component';

@Component({
    selector: 'k2-popup',
    templateUrl: './popup.component.html',
    styleUrls: ['./popup.component.less']
})
export class PopupComponent extends BaseResourceComponent implements OnInit {
    @Input() config: IContextPopupConfiguration;

    selectedHeaderTemplate: TemplateRef<any>;
    selectedFooterTemplate: TemplateRef<any>;

    popupTemplates = PopupTemplate;

    @ViewChild('dataFieldHeader') dataFieldHeaderTemplate: TemplateRef<any>;
    @ViewChild('wiz') wizard: ElementRef<HTMLElement>;
    @ViewChild('header') header: ElementRef<HTMLElement>;
    @ViewChild('footer') footer: ElementRef<HTMLElement>;

    constructor(private _popupService: PopupService,
        @Inject(ValidationServiceToken) private validationService: IValidationService,
        @Inject(NotificationServiceToken) private notificationService: INotificationService,
        resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (this.config?.headerTemplate) {
            switch (this.config.headerTemplate) {
                case PopupHeaderTemplate.dataField:
                    this.selectedHeaderTemplate = this.dataFieldHeaderTemplate;
                    break;
            }
        }

        if (this.config?.buttons) {
            for (const button in this.config.buttons) {
                if (!(this.config.buttons[button].onClick instanceof EventEmitter)) {
                    this.config.buttons[button].onClick = new EventEmitter<IPopupButton>();
                }
            }
        }
        this.config.closeEvent = new EventEmitter<IPopupButton>();
    }

    onKeyDown(e: KeyboardEvent): void {
        if (e.keyCode === keyCodes.ENTER) {
            const okButton: IPopupButton = this.config.buttons.okButton;

            if (okButton && okButton.enabled === true) {
                this.buttonClicked(okButton);
            }
        } else if (e.keyCode === keyCodes.ESCAPE) {
            this.dialogClosed();
        }
    }

    dialogClosed() {
        this.config.closeEvent.emit({ key: popup.buttons.topRightClose, dismissResult: popup.buttons.cancelResult });
        this._popupService.hide('', this.config);
    }

    buttonClicked(button: IPopupButton) {
        const res = true;
        let validationFailed = false;
        let validationResults: sourceCode.k2Designer.objectModel.core.model.IErrorInformation[];

        if (button.invokeValidation === true) {
            this.validationService.overrideEnabled = true;
            validationResults = this.validationService.evaluate([this.config.validationContext], this.config.validationModel);
            validationFailed = validationResults.length > 0;
        }
        this.validationService.overrideEnabled = false;

        if (validationFailed === false) {
            button.onClick.emit(button);

            if (button.dismissResult != null && button.dismissResult.length > 0 && res) {
                this.config.closeEvent.emit(button);
                this._popupService.hide(button.dismissResult, this.config);
            }
        } else {
            //validation failed upon clicking OK button. Display the error messages.
            for (let i = 0, len = validationResults.length; i < len; i++) {
                const validationResult: any = validationResults[i].getJson();
                const errorMessageKey: string = validationResult.errorMessage;

                this.notificationService.toastr('', this.getString(errorMessageKey), ToastrTypes.error, 2000);
            }
        }
    }
}
