import { Component, Inject, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { ErrorSeverity } from '../../../shared/enums/model/error-severity';
import { isUndefinedOrNull } from '../../../shared/utils/general';

@Component({
    selector: 'k2-badges',
    templateUrl: './badges.component.html'
})
export class BadgesComponent implements OnInit, OnChanges {
    @Input() public k2Model: IValidatedComponent;
    @Input() public toolTipText: string;
    @Input() public forceShow: boolean;
    @Input() public alwaysHideBadge: boolean;
    @Input() public errorSeverity: ErrorSeverity;

    public severity: typeof ErrorSeverity = ErrorSeverity;

    constructor(private resourceService: ResourceService,
        private processService: ProcessService,
        @Inject(ValidationServiceToken) private validationService: IValidationService) {
    }

    ngOnInit(): void {
        //Check if the model contains errors
        if (this.k2Model?.childErrorInformation?.length > 0 ||
            this.k2Model?.errorInformation?.length) {

            this.setupBadge();
        }
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.k2Model) {
            this.setupBadge();
        }
    }

    private setupBadge(): void {

        this.errorSeverity = this.validationService.getErrorSeverity(this.k2Model);

        const firstError = this.validationService.getFirstError(this.k2Model);

        if (firstError) {

            //attempt to use model object from first error to avoid [unresolved token]
            //if the model cannot be found, resort to this.k2Model
            let model = this.processService.root.findItem<IValidatedComponent>(firstError.reference);
            if (isUndefinedOrNull(model)) {
                model = this.k2Model;
            }

            this.toolTipText = this.resourceService.resolveTokenResourceString(firstError.errorMessage,
                firstError.errorResourceTokens,
                model);
        }
    }
}
