import { Component, ElementRef, EventEmitter, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IConfigPanelService } from '../../../../../types/services/config-panel/i-config-panel-service';
import { IRulePluginService } from '../../../../../types/services/rules/i-rule-plugin.service';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { CommandServiceToken } from '../../../../services/command/command-service-factory';
import { ConfigPanelServiceToken } from '../../../../services/config-panel/config-panel-service-factory';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../../services/undo-redo/undo-redo-service-factory';
import { rules } from '../../../../shared/constants/rules';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { RuleDesignerItemBaseComponent } from '../rule-designer-item-base/rule-designer-item-base.component';
import { IExpression } from '../../../../../types/models/rules/i-expression';
import IDropDownMenuItem from 'src/app/shared/enums/drop-down/drop-down-menu-item';
import { command } from 'src/types/models/rules/i-expression-command';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { operator } from 'src/types/models/rules/i-expression-operator';
import { IConfiguredExpression } from 'src/types/models/rules/i-configured-expression';
import { DropDownComponent } from 'src/app/components/common/drop-down/drop-down.component';
import { DragDropData } from 'src/app/shared/models/drag-drop-data';

@Component({
    selector: 'k2-expression-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export class ExpressionBaseComponent extends RuleDesignerItemBaseComponent implements OnInit, OnDestroy, IExpression {

    protected composerActiveEvent: EventEmitter<void>;
    private _composerActiveSubscription: Subscription;

    @Input() public index = 0;

    constructor(@Inject(SelectionServiceToken) selService: ISelectionService,
        rulePluginService: RulePluginService,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
        eventService: EventNotifierService,
        protected rootElement: ElementRef<HTMLElement>,
        protected resourceService: ResourceService) {

        super(selService, rulePluginService, commandService, configPanelService,
            undoRedoService, eventService, rootElement);
    }

    ngOnInit(): void {
        super.ngOnInit();

        //Add and subscribe to the textbox composer active event:
        this.composerActiveEvent = this.eventService.addEvent(rules.events.textboxComposerActive);
        this._composerActiveSubscription = this.eventService.subscribe(rules.events.textboxComposerActive,
            this.onTextboxActiveComposer);

    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
        this._composerActiveSubscription.unsubscribe();
    }

    textBoxOnBlur = () => {
        this.states.remove(RuleControlStates.textboxFocus);
    };

    textBoxOnFocus = () => {
        this.states.add(RuleControlStates.textboxFocus);
    };

    getDragItem(item: IConfiguredExpression, context: any): IDragDropData {
        item.index = this.index;
        return new DragDropData<any, any, any>(item, context, this.getDragData, this.getDropData, null);
    }

    getDragData = (source: any): any => source;

    getDropData = (source: any, dragData: any): any => source || dragData;

    /*override*/ delete = (): Promise<boolean> => {

        let isDeleted = false;

        if (this.expressionModel && this.deleteExpression(this.expressionModel)) {
            isDeleted = true;
        }

        const promise = new Promise<boolean>((resolve) => {
            resolve(isDeleted);
        });

        return promise;
    };

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    /**
     * Populate the supplied dropdown menu item list with the supplied operators
     *
     * @param operators
     * @param dropDownMenuItems
     * @param selectedOperator
     */
    protected populateLogicalOperators(operators: operator[], dropDownMenuItems: IDropDownMenuItem[], selectedOperator: string) {

        for (let i = 0; i < operators.length; i++) {

            i > 0 && dropDownMenuItems.push(DropDownComponent.separator);

            dropDownMenuItems.push({
                key: operators[i].name,
                title: this.resourceService.getString(operators[i].titleToken),
                icon: operators[i].icon,
                innerItemComponent: DropdownMenuItemTemplates.SimpleExpressionOperator,
                isSelected: selectedOperator?.toString() === operators[i].name
            });
        }
    }

    protected deleteExpression(expressionToDelete: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression): boolean {
        if (!expressionToDelete) return false;

        this.undoRedoService.addUndoMarker('-- delete expression--');

        const parentGroup = this.getExpressionParentGroup(expressionToDelete);
        if (!parentGroup || !parentGroup.expressions || !parentGroup.expressions.length) {
            expressionToDelete.removeFromModelArray();
            return true;
        }

        if (parentGroup.expressions.length === 1) {
            //If parent of object to delete only contains 1 object to delete, remove parent too
            parentGroup.removeFromModelArray();
        } else {
            const expressionToRemoveIndex = parentGroup.expressions.indexOf(expressionToDelete);
            //If expression to delete is contained between 2 operators, remove 1 operator (to prevent double operators remaining)
            if (parentGroup.expressions.length > 2 && expressionToRemoveIndex > 0 &&
                expressionToRemoveIndex < parentGroup.expressions.length - 1 &&
                this.isOperatorType(parentGroup.expressions[expressionToRemoveIndex - 1]) &&
                this.isOperatorType(parentGroup.expressions[expressionToRemoveIndex + 1])) {
                parentGroup.expressions[expressionToRemoveIndex - 1].removeFromModelArray();
            }

            expressionToDelete.removeFromModelArray();
        }

        this.cleanupOperators(parentGroup);
        this.updateGroupExpressionOf(parentGroup);
        return true;
    }

    /**
     * Returns logical parent group of expression
     *
     * @param expression
     */
    protected getExpressionParentGroup(expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression): sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression {
        return expression.getLogicalParentModel<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>();
    }

    /**
     * Returns true if expression is of type ConfiguredGroupOperator
     *
     * @param expression
     */
    protected isOperatorType(expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression): boolean {
        return expression.componentId === sourceCode.k2Designer.objectModel.core.deployment.LookupTypes.ConfiguredGroupOperator;
    }

    /**
     * Returns true if expression is of type ConfiguredComplexLogicalGroupExpression
     *
     * @param expression
     */
    protected isGroupType(expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression): boolean {
        return expression.componentId === sourceCode.k2Designer.objectModel.core.deployment.LookupTypes.ConfiguredComplexLogicalGroupExpression;
    }

    /**
     * Returns true if expression is of type ConfiguredSimpleExpression
     *
     * @param expression
     */
    protected isSimpleExpressionType(expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression): boolean {
        return expression.componentId === sourceCode.k2Designer.objectModel.core.deployment.LookupTypes.ConfiguredSimpleExpression;
    }

    /**
     * Ensures there will never be trailing / leading operators in a group
     *
     * @param droppedDataParent - logical parent of the dragged/dropped expression
     */
    protected cleanupOperators(parentGroup: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression): void {
        //Cleanup operators:
        //If collection of expressions now ends with an operator, remove trailing operator:
        if (parentGroup.expressions.length > 0 && this.isOperatorType(parentGroup.expressions[parentGroup.expressions.length - 1])) {
            parentGroup.expressions[parentGroup.expressions.length - 1].removeFromModelArray();
        }
        //If collection of expressions now starts with an operator, remove leading operator:
        if (parentGroup.expressions.length > 0 && this.isOperatorType(parentGroup.expressions[0])) {
            parentGroup.expressions[0].removeFromModelArray();
        }
    }

    /**
     * Populate the supplied dropdown menu item list with the supplied commands
     *
     * @param commands
     * @param dropDownMenuItems
     */
    protected populateOperatorCommands(commands: command[], dropDownMenuItems: IDropDownMenuItem[]) {
        commands?.forEach(c => {
            dropDownMenuItems.push(DropDownComponent.separator);

            dropDownMenuItems.push({
                key: c.name,
                icon: c.icon,
                title: this.resourceService.getString(c.titleToken),
                innerItemComponent: DropdownMenuItemTemplates.Command,
                onSelected: this.onCommandSelected
            });
        });
    }

    /**
     * Handler for when a command is selected
     *
     * @param item
     */
    protected /*virtual*/ onCommandSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {
        item.key ===  rules.commands.delete && this.delete();
    };

    /**
     * Consolidates groups and ungroup where needed.
     * If group only contains 1 item, it should no longer be its own group
     *
     * @param parentGroup
     */
    protected updateGroupExpressionOf(parentGroup: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression) {
        //The expression's original parent group, now only contains 1 child
        if (parentGroup && parentGroup.expressions.length === 1) {
            if (this.isGroupType(parentGroup.expressions[0])) {
                //The child is also a group. Move group one level up (to avoid groups only containing groups).
                const innerGroup = <sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredComplexLogicalGroupExpression>parentGroup.expressions[0];
                //Transfer inner group's expressions to outer group
                for (let i = 0; i < innerGroup.expressions.length; i++) {
                    const expressionClone = innerGroup.expressions[i].clone<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression>();
                    expressionClone.internalId = null; //reset internal id to force a new one and references to update
                    parentGroup.expressions.push(expressionClone);
                }
                //Remove original inner goup
                innerGroup.removeFromModelArray();
                this.cleanupOperators(parentGroup);
            } else if (this.isSimpleExpressionType(parentGroup.expressions[0])) {
                //The child expression is not in another group, move one level up if possible and remove group (to avoid single expression in own group)
                const parentOfParentGroup = this.getExpressionParentGroup(parentGroup);
                if (parentOfParentGroup && parentOfParentGroup.expressions) {
                    const parentGroupIndex = parentOfParentGroup.expressions.indexOf(parentGroup);
                    const expressionClone = parentGroup.expressions[0].clone<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression>();
                    expressionClone.internalId = null; //reset internal id to force a new one and references to update
                    parentOfParentGroup.expressions.splice(parentGroupIndex, 0, expressionClone);
                    parentGroup.removeFromModelArray();
                    this.cleanupOperators(parentOfParentGroup);
                }
            }
        }
    }

    private onTextboxActiveComposer(event: ng.IAngularEvent) {

        const expressionComposerActiveEvent = this.eventService.addEvent(rules.events.expressionComponserActive);
        expressionComposerActiveEvent.emit(this);

        event.stopPropagation();
    }

}
