/**
 * validate undefined or null of an object.
 *
 * @param object
 */
export function isUndefinedOrNull(object: any): boolean {
    const result = (typeof object === 'undefined' || object === null);
    return result;
}

/** Optionally delays a function or immediately executes it only once when called multiple times in a very short period.
 *
 * @param func The function to be executed
 * @param wait The interval between calls waited before the function is actually executed
 * @param immediate Indicates whether the function should be executed immediately on the first call and not executed again until the interval is reached and the cycle starts again.
 */
export function debounce(func: () => any, wait: number, immediate?: boolean): () => boolean {

    //Used to store a setTimeout value
    let timeout: number;
    //The arguments passed into this function
    let args: IArguments;
    //The context of this function
    let context: (args: IArguments) => boolean;
    // Used to store the current dateTime at certain intervals
    let timestamp: number;
    // The boolean value of the result of executing the function passed in
    let result: boolean;

    var later: () => void = function() {

        // Check and store the time difference between the last function call and now
        const last = new Date().getTime() - timestamp;

        // Check if the time difference is greater less than the specified wait time
        if (last < wait && last >= 0) {
            timeout = window.setTimeout(later, wait - last);
        } else {

            //If the time difference is greater, we now execute our function
            timeout = null;
            if (!immediate) {
                result = func.apply(context, args);
                if (!timeout) context = args = null;
            }
        }
    };

    return function() {

        context = this;
        args = arguments;
        timestamp = new Date().getTime();

        // Before we set a timeout (If there isn't already one set), we create a boolean value to determine if immediate execution should occur.
        const callNow: boolean = immediate && !timeout;

        // If there is no timeout, we set a timeout to check for the last successive call within the interval provided
        if (!timeout) {
            timeout = window.setTimeout(later, wait);
        }

        // If immediate is set in the arguments, then we execute the function provided
        if (callNow) {
            result = func.apply(context, args);
            context = args = null;
        }

        return result;
    };
}

/**
 * Returns a 4-character alpha-numeric identifier, no more than 1.6 million
 */
export function shortUid(): string {
    return ('0000' + (Math.random() * Math.pow(36, 4) << 0).toString(36)).slice(-4);
}

/**
 *  Will return a true boolean value if there is a duplicate item in the array.
 *
 * @param model This is the model property that will contain the value and validationProperty that will be compared against
 * @param array  This is the validation array that must not contain the duplicate
 * @param validationProperty This is a the name of the property on the model that will be validated against
 */
export function isDuplicateItemInArray(model: any, array: Array<any>, validationProperty: string): boolean {

    if (model && array && validationProperty && model[validationProperty]) {
        //There wil always be one value in the array that is the same as the model because it is already in the array.  If there is more than one it is a duplicate
        return propertyMatchesInObjectArray(array, validationProperty, model[validationProperty]) > 1;
    }

    return false;
}

/**
 * Returns the number of items in the array that matches with the property
 * This function will cater for any array as well as model array type
 *
 * @param model This is the model property that will contain the value and validationProperty that will be compared against
 * @param array This is the validation array that must not contain the duplicate
 * @param propertyName This is a the name of the property on the model that will be compared against
 * @param propertyValue This is a the value of the property on the model that will be compared against
 * @param isCaseSensitive By default property is not case sensitive
 */
export function propertyMatchesInObjectArray(array: Array<any>, propertyName: string,
    propertyValue: string, isCaseSensitive?: boolean): number {
    propertyValue = propertyValue.trim();

    let foundCount = 0;
    for (let i = 0; i < array.length; i++) {
        const property = (array[i][propertyName] ?? array[i][propertyName].trim()) || '';
        if (!isCaseSensitive) {
            if (property === propertyValue) {
                foundCount++;
            }
        } else {
            if (property.toUpperCase() === propertyValue.toUpperCase()) {
                foundCount++;
            }
        }
    }
    return foundCount;
}

/**
 * Returns the first enum key that corresponds with a certain string enum value
 *
 * @param myEnum The enum containing the string value
 * @param enumValue the string value you are searching for.
 */
export function getEnumKeyByEnumValue<T extends { [index: string]: string }>(myEnum: T, enumValue: string): keyof T | null {
    const keys = Object.keys(myEnum).filter(x => myEnum[x] == enumValue);
    return keys.length > 0 ? keys[0] : null;
}
