import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { IExpression } from '../../../../../../types/models/core/configured-smart-fields/i-expression';
import { IEnvironmentService, IPluginStateChangedCallback } from '../../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../../types/services/plugin/i-plugin-service';
import { EnvironmentServiceToken } from '../../../../../services/environment/environment-service-factory';
import { PluginServiceToken } from '../../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { RichEditService } from '../../../../../services/rich-edit/rich-edit.service';
import { SmartFieldComposerServiceToken } from '../../../../../services/smart-field-composer/smart-field-composer-service-factory';
import { keyCodes } from '../../../../../shared/constants/general';
import { plugins } from '../../../../../shared/constants/plugins';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-body-template',
    templateUrl: './body-template.component.html',
    styleUrls: ['./body-template.component.less']
})
export class BodyTemplateComponent extends BaseResourceComponent implements OnInit, OnDestroy {

    @Input() body: IExpression;
    @Input() isEnabled: boolean;

    isRichEdit: boolean;
    keyCodes = keyCodes;
    btnInsertFieldClass: string = '';

    private panelStateChangeRemover: IPluginStateChangedCallback;

    constructor(resourceService: ResourceService,
        @Inject(PluginServiceToken) private pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        @Inject(SmartFieldComposerServiceToken) private smoComposerService: ISmartFieldComposerService,
        private richEditService: RichEditService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.isRichEdit = this.environmentService.activeStudioItem.pluginStates[plugins.panels.emailBody] === plugins.panels.states.expanded;
        this.panelStateChangeRemover = this.environmentService.listenActiveStudioItemPluginStateChanged(this.panelStateChanged.bind(this));
        if (this.body?.smartFields?.length) {
            this.richEditService.setContext(`EmailEventMailBody`, this.body);
        }
    }

    ngOnDestroy(): void {
        this.environmentService.removeActiveStudioItemPluginStateChangedListener(this.panelStateChangeRemover);
    }

    toggleRichTextEditor(): void {
        this.pluginService.toggleState(plugins.panels.emailBody);

        if (!this.isRichEdit) {
            this.smoComposerService.closeAll();
        }
    }

    toggleContextBrowser(): void {
        this.pluginService.toggleState(plugins.panels.contextBrowser);
    }

    clearBody(): void {
        this.body.smartFields.clear();

        (this.body as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpressionExtended)
            .notifyChange(null);
    }

    /**
     * Update the display of rich text editor depending on if the email body panel is expanded or collapsed
     * 
     * @param pluginName
     */
    private panelStateChanged(pluginName?: string): void {
        if (pluginName === plugins.panels.emailBody) {
            this.isRichEdit = this.environmentService.activeStudioItem.pluginStates[plugins.panels.emailBody] === plugins.panels.states.expanded;
        }
    }
}
