import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { DropDownMenuAlignment } from '../../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../../shared/enums/drop-down/drop-down-menu-item';

@Component({
    selector: 'k2-email-part-selector',
    templateUrl: './email-part-selector.component.html',
    styleUrls: ['./email-part-selector.component.less']
})
export class EmailPartSelectorComponent implements OnInit {

    @Input() dropdownItems: IDropDownMenuItem[] = [];

    @Output() emailPartSelected = new EventEmitter<IDropDownMenuItem>();

    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;

    constructor() { }

    ngOnInit(): void {
    }

    onEmailPartSelected(item: IDropDownMenuItem): void {
        this.emailPartSelected.emit(item);
    }
}
