import { Component, EventEmitter, Inject, OnDestroy, OnInit, ElementRef } from '@angular/core';
import { Subscription } from 'rxjs';
import { ISelectionService } from '../../../../../types/services/selection/i-selection-service';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { SelectionServiceToken } from '../../../../services/selection/selection-service-factory';
import { rules } from '../../../../shared/constants/rules';
import { RuleControlStates } from '../../../../shared/enums/rules/rule-control-states';
import { RuleBaseComponent } from '../rule-base/rule-base.component';
import { IRuleDesignerSelection } from '../../../../../types/models/rules/i-rule-designer-selection';
import { IConfiguredExpression } from '../../../../../types/models/rules/i-configured-expression';
import { IConfiguredStatement } from '../../../../../types/models/rules/i-configured-statement';

@Component({
    selector: 'k2-rule-designer-selection-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export class RuleDesignerSelectionBaseComponent extends RuleBaseComponent implements OnInit, OnDestroy, IRuleDesignerSelection {

    //Properties implemented from interface IRuleDesignerSelection:
    isSelected: boolean;
    isParentSelected: boolean;

    protected expressionModel: IConfiguredExpression;
    protected statementModel: IConfiguredStatement;
    protected isParent = false;

    protected ruleDesignerSelection: IRuleDesignerSelection;

    private _ruleItemParentSelectedSubscription: Subscription;
    private _ruleItemParentSelectedEvent: EventEmitter<boolean>;

    constructor(@Inject(SelectionServiceToken) private _selService: ISelectionService,
        protected eventService: EventNotifierService,
        protected rootElement: ElementRef<HTMLElement>) {
        super();
    }

    ngOnInit(): void {
        this.isSelected = false;
        this.isParentSelected = false;

        if (this.isParent) {
            this._selService.listen(this.onSelectServiceParentListener, rules.context.rule);
            this.setSelected = this.setParentSelected;
        } else {
            this._selService.listen(this.onSelectServiceListener, rules.context.rule);
        }

        this._ruleItemParentSelectedEvent = this.eventService.addEvent(rules.events.ruleItemParentSelected);
        this._ruleItemParentSelectedSubscription = this.eventService.subscribe(rules.events.ruleItemParentSelected, (event, value) => {
            this.isParentSelected = value;
            this.isSelected = value;

            if (value) {
                this.states.add(RuleControlStates.selected.toString());
            } else {
                this.states.remove(RuleControlStates.selected.toString());
            }
        });

        //TODO: user story to consolidate expressionModel and statementModel
        if (!!this.expressionModel && this.expressionModel.isSelectable) {
            this.setSelected(true, this.expressionModel.createdFromIfDrop);
        } else if (!!this.statementModel && this.statementModel.isSelectable) {
            this.setSelected(true, this.statementModel.createdFromIfDrop);
        }
    }

    ngOnDestroy(): void {
        this._ruleItemParentSelectedSubscription.unsubscribe();
    }

    /**
     * Triggered when rules context selection is changed and component is parent
     * */
    onSelectServiceParentListener = () => {
        const selectedItems = this._selService.getSelection(rules.context.rule);

        //if it's me and I'm selected
        //TODO: user story to consolidate expressionModel and statementModel
        if (selectedItems.indexOf(this.expressionModel) > -1 ||
            selectedItems.indexOf(this.statementModel) > -1) {
            this.isParentSelected = false;
            this.isSelected = true;
        } else if ((!this.isParentSelected || selectedItems.length === 0) && this.isSelected) {
            this.isSelected = false;
            //notify child scopes, set to false
            this._ruleItemParentSelectedEvent.emit(this.isSelected);
        } else {
            this.isSelected = false;
        }
    };

    /**
     * Override for setSelected method if component is parent
     *
     * @param value
     */
    setParentSelected = (value: boolean) => {
        this.setSelected(value);

        //notify child scopes
        this._ruleItemParentSelectedEvent.emit(this.isSelected);
    };

    /**
     * Triggered when rules context selection is changed
     * */
    onSelectServiceListener = () => {
        const selectedItems = this._selService.getSelection(rules.context.rule);

        //if it's me and I'm selected
        //TODO: user story to consolidate expressionModel and statementModel
        if (selectedItems.indexOf(this.expressionModel) > -1 ||
            selectedItems.indexOf(this.statementModel) > -1) {
            this.isParentSelected = false;
            this.isSelected = true;
            this.states.add(RuleControlStates.selected.toString());
            return;
        } else if ((!this.isParentSelected || selectedItems.length === 0) && this.isSelected) {
            this.isSelected = false;
            this.states.remove(RuleControlStates.selected.toString());
        } else {
            this.isSelected = false;
            this.states.remove(RuleControlStates.selected.toString());
        }
    };

    /**
     * Set selected state
     *
     * @param value
     * @param skipService
     */
    setSelected = (value: boolean, skipService?: boolean) => {
        if (value) {
            this.states.add(RuleControlStates.selected.toString());
        } else {
            this.states.remove(RuleControlStates.selected.toString());
        }

        if (skipService !== true) {
            this.isSelected = value;
            //set selection in rule context of selection service
            //TODO: user story to consolidate expressionModel and statementModel
            this._selService.setSelection([this.expressionModel || this.statementModel], rules.context.rule);
        }
    };
}
