import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockCommandService } from '../../../testing/mock-services/command-service';

const commandServiceFactory = () => environment.devHarness === true
    ? new MockCommandService()
    : sourceCode.k2Designer.core.environmentService.commandService;

export const CommandServiceToken = new InjectionToken<ICommandService>('ICommandService');

export const CommandServiceFactory = {
    provide: CommandServiceToken,
    useFactory: commandServiceFactory,
    deps: [

    ]
};
