import { Component, Inject, OnInit, OnDestroy, Input, ComponentFactoryResolver, ViewChild, HostListener, ElementRef } from '@angular/core';
import { ITabComponent } from '../../../types/uiComponents/ribbon/ITabComponent';
import { TabHomeComponent } from '../ribbon/tab-home/tab-home.component';
import { TabHostDirective } from '../../directives/tab-host/tab-host.directive';
import { IRibbonConfiguration } from '../../../types/uiComponents/ribbon/IRibbonConfiguration';
import { TabFileComponent } from './tab-file/tab-file.component';
import { TabViewComponent } from './tab-view/tab-view.component';
import { EnvironmentServiceToken } from '../../services/environment/environment-service-factory';
import { IEnvironmentService } from '../../../types/services/environment/i-environment-service';
import { LegacyResourceServiceToken } from '../../legacy-service-factories/resource-service-factory';
import { ILegacyResourceService } from '../../../types/services/resources/i-legacy-resource.service';
import { ObjectModelServiceToken } from '../../services/object-model/object-model-service-factory';
import { IObjectModelService } from '../../../types/services/object-model/i-object-model-service';
import { UndoRedoServiceToken } from '../../services/undo-redo/undo-redo-service-factory';
import { SelectionServiceToken } from '../../services/selection/selection-service-factory';
import { ISelectionService } from '../../../types/services/selection/i-selection-service';
import { ContextProviderServiceToken } from '../../services/context-provider/context-provider-service-factory';
import { IContextProviderService } from '../../../types/services/context-provider/i-context-provider-service';
import { ProgressBarServiceToken } from '../../services/progress-bar/progress-bar-service-factory';
import { IProgressBarService } from '../../../types/services/progress-bar/i-progress-bar-service';
import { RibbonBarServiceToken } from '../../services/ribbon-bar/ribbon-bar-service-factory';
import { IRibbonService } from '../../../types/services/ribbon-bar/i-ribbon-bar-service';
import { DesignerServiceToken } from '../../services/designer/designer-service-factory';
import { IDesignerService } from '../../../types/services/designer/i-designer-service';
import { ItemProviderServiceToken } from '../../services/item-provider/item-provider-service-factory';
import { IItemProviderService } from '../../../types/services/item-provider/i-item-provider-service';

@Component({
    selector: 'k2-ribbon',
    templateUrl: './ribbon.component.html',
    styleUrls: ['./ribbon.component.less']
})

export class RibbonComponent implements OnInit, OnDestroy {

    @Input() public uiComp: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    @ViewChild(TabHostDirective, { static: true }) tabHost: TabHostDirective;

    // #region - private properties -
    private _timeout: ng.ITimeoutService;
    private _element: ng.IAugmentedJQuery;
    private _config: sourceCode.k2Designer.objectModel.plugin.IDefinition;
    private _rbbnCnf: IRibbonConfiguration;
    private _contextProvider: sourceCode.k2Designer.objectModel.contextProvider.WorkflowContextProvider;
    /**
     * Seting the path of the ProgressBarService to the local variable
     */
    private static SAVE_BUTTON_PULSE_TIMEOUT = 3000;
    private _resourceHash: string;
    // #endregion - private properties -

    //#region -scope interface properties
    public tabs: ITabComponent[];
    public contextTabs: ITabComponent[];
    public currentTab: ITabComponent;
    public getString: (resourceId: string, collectionId?: string) => string;
    public designerSvc: sourceCode.k2Designer.services.studio.DesignerService;
    public hasContext: boolean;
    public currentContextTabs: ITabComponent[];
    public finishing: boolean;
    public finished: boolean;
    public saving: boolean;
    public manual: boolean;
    public canRedo: boolean;
    public canUndo: boolean;
    public canSave: boolean;
    public loaderHasLoaded: boolean;
    public hideStudioNavTop: boolean;
    public undoRedoEnabled: boolean;
    public showFinishButton: boolean;
    public saveErrorMessage: string;
    public selectTab: (tab: ITabComponent) => void;
    public togglePin: () => void;
    public unpin: () => void;
    public getCurrentActivity: () => void;
    public loadContextTabs: (onCompleted: () => void) => void;
    public isContextTab: (tab: ITabComponent) => boolean;
    public invokeUndo: () => void;
    public invokeRedo: () => void;
    public invokeSave: (closeproject: boolean) => void;
    public onFinishButtonClicked: () => void;
    public isHosted: boolean;
    //Resource object containing localized resources
    public resources: {
        [resourceId: string]: string;
    };
    // #endregion - scope interface properties -

    constructor(@Inject('$rootElement') element,
        @Inject(EnvironmentServiceToken) private _envService: IEnvironmentService,
        @Inject(ProgressBarServiceToken) private _progressBarService: IProgressBarService,
        @Inject(RibbonBarServiceToken) private _ribbonBarService: IRibbonService,
        @Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        @Inject(ObjectModelServiceToken) private _objectModelService: IObjectModelService<sourceCode.k2Designer.objectModel.workflow.process.k2Process.K2Process>,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService,
        @Inject(SelectionServiceToken) private _selectionService: ISelectionService,
        @Inject(ContextProviderServiceToken) private _contextProviderService: IContextProviderService,
        @Inject(ItemProviderServiceToken) private _itemProviderService: IItemProviderService,
        private componentFactoryResolver: ComponentFactoryResolver,
        private elementRef: ElementRef) {
        this._element = element;

        this._resourceService.onGetResourceFileCompletedCallbacks.push(this.onGetResourceFileCompleted.bind(this));
        this._resourceHash = this._resourceService.getResxFile('Ribbon');

        this.getString = this._resourceService.getString.bind(this._resourceService);

        this.saveErrorMessage = this.getString('workflow_save_error_message').format(this._envService.workflowFullname);

        this.invokeRedo = this._invokeRedo.bind(this);
        this.invokeUndo = this._invokeUndo.bind(this);
        this.invokeSave = this._invokeSave.bind(this);
        this.onFinishButtonClicked = this._onFinishButtonClicked.bind(this);

        this._envService.notifyOnReady(this.onEnvironmentServiceReady.bind(this));
        this.isHosted = this._isHosted.bind(this);

        this.finishing = false;
        this.finished = false;
        this.manual = false;
        this.saving = false;
        this.showFinishButton = false;

        this._objectModelService.registerEventListener('onStartSaving', {
            notify: this.onStartSaving.bind(this),
            id: 'ribbonContller.onStartSaving'
        });

        this._objectModelService.registerEventListener('onFinishSaving', {
            notify: this.onFinishSaving.bind(this),
            id: 'ribbonContller.onFinishSaving'
        });

        this.undoRedoEnabled = this._undoRedoService.undoRedoEnabled;

        if (this.undoRedoEnabled !== false) {
            this._undoRedoService.listen(this.onUndoRedo.bind(this));
        }

        // Call the method that registers a callback function to listen for the loader to complete
        this.listenForLoadCompletion();
    }

    ngOnInit(): void {
        this._config = this.uiComp;
        this._rbbnCnf = <IRibbonConfiguration>this._config.cnf;

        this.currentTab = null;
        this.selectTab = this._selectTab.bind(this);
        this.togglePin = this._togglePin.bind(this);
        this.unpin = this._unpin.bind(this);
        this._selectionService.listen(this.onCanvasSelectionChanged.bind(this));
        this.getCurrentActivity = this._getCurrentActivity.bind(this);
        this.hasContext = false;
        this.loadContextTabs = this._loadContextTabs.bind(this);
        this.currentContextTabs = [];
        this.isContextTab = this._isContextTab.bind(this);
        this.isHosted = this._isHosted();

        this._contextProvider = this._contextProviderService.getContextProviderFromType<sourceCode.k2Designer.objectModel.contextProvider.WorkflowContextProvider>(sourceCode.k2Designer.objectModel.contextProvider.WorkflowContextProvider);

        // load all tab-plugins
        this.onTabsLoaded();
        this._loadContextTabs(this.onContextTabsLoaded.bind(this));
    }

    ngOnDestroy() {
        //clear timeouts etc.
    }

    /**
     * Registers a callback function to listen for the loader to complete
     */
    public listenForLoadCompletion(): void {
        this._progressBarService.listenLoadedComplete(() => {
            this.loaderHasLoaded = true;
        });
    }

    /**
     * Close tab if clicked outside
     *
     * @param data
     */
    @HostListener('document:click', ['$event'])
    clickout(event) {
        if (this.currentTab == null) return;

        if (this.elementRef.nativeElement == event.target) return;

        //Did we click on the current tab menu item?
        if (event.target.href != null && event.target.href.indexOf(this.currentTab.id) > -1) return;

        //Did we click inside currently active tab?
        const currentTabId = `current-tab-${this.currentTab.id}`;

        if (event.target.id === currentTabId) return;

        //Clicking on current tab to close or on 'Show Me' button
        if ((event.target.closest(`#${currentTabId}`) === null || event.target.id === 'btnShowStartStep')
            && event.target.closest('.delete-process') === null) {
            this._deselectTab();
        }
    }

    //called from objeceModelService upon auto save start
    private onStartSaving(data: any) {
        this.saving = true;
    }

    //called from objeceModelService upon auto save finish
    private onFinishSaving(data: any) {
        //timeout needed for animation to pulse in regular pattern
        setTimeout(() => {
            if (this.manual === false) {
                this.saving = false;
                //TODO: find out if we are still supporting IE 11?
                if (sourceCode.k2Designer.core.isIE11) {
                    //IE11 only - need to force reflow the element to stop animation
                    this._element.find('.qat.sav').height();
                }
            }
        }, RibbonComponent.SAVE_BUTTON_PULSE_TIMEOUT);
    }

    private _isHosted(): boolean {
        this.hideStudioNavTop = this._envService.query.hosted;
        if (this.hideStudioNavTop || this.currentTab?.id === 'File') {
            return true;
        }
        else {
            return false;
        }
    }

    private onEnvironmentServiceReady(): void {
        this.undoRedoEnabled = this._undoRedoService.undoRedoEnabled;

        if (this.undoRedoEnabled) {
            this._undoRedoService.listenStateChanged(this.onUndoRedoServiceCallBack.bind(this));
        }

        this.canRedo = false;
        this.canUndo = false;
        this.canSave = true;
    }

    private _invokeRedo(): void {
        if (this.canRedo) this._undoRedoService.redo();
    }

    private _invokeUndo(): void {
        if (this.canUndo) this._undoRedoService.undo();
    }

    private onUndoRedo() {
        //Do nothing for now
    }

    private _invokeSave(closeproject: boolean = false): void {
        //begin saving indicator pulse
        this.saving = true;
        this.manual = true;

        //before attempt to save, make sure saving is not currently in progress
        if (this._objectModelService.busyProcessingInstructions() == false) {
            this._objectModelService.flush(null, null, closeproject);
        }
        else {
            const that = this;
            var saveProcessCallBack = {
                subscriberId: 'invokeSave_saveProcessCallBack',
                notify(data) {
                    this._objectModelService.unRegisterEventListener(sourceCode.k2Designer.services.studioItem.ObjectModelService.ProcessingCompleteEvent, saveProcessCallBack.subscriberId);
                    this._objectModelService.flush(null, null, closeproject);
                }
            };
            //Will invoke a processBulk and call back to this when complete
            this._objectModelService.registerEventListener(sourceCode.k2Designer.services.studioItem.ObjectModelService.ProcessingCompleteEvent, saveProcessCallBack);
        }

        // pulse save indicator for approx 3 times even though auto save may have taken care of the business but we still need to show users
        setTimeout(() => {
            //end saving indicator pulse
            this.saving = false;
            this.manual = false;
        }, RibbonComponent.SAVE_BUTTON_PULSE_TIMEOUT);
    }

    private _onFinishButtonClicked(): void {
        this.finished = false;
        this.finishing = true;
        this.invokeSave(true);
    }

    private _updateUndoRedoUi: number;
    private onUndoRedoServiceCallBack(): void {
        if (!this._updateUndoRedoUi) {
            this._updateUndoRedoUi = window.setTimeout(this.doUndoRedoStateUpdate.bind(this), 500);
        }
    }

    private doUndoRedoStateUpdate(): void {
        this._updateUndoRedoUi = null;
        this.canRedo = this._undoRedoService.canRedo();
        this.canUndo = this._undoRedoService.canUndo();
    }

    // #region - private methods -

    private _pinned = false;
    private _togglePin(): void {
        this._pinned = !this._pinned;
        if (this._pinned) {
            this._envService.changeSectionLayout(132);
        } else {
            this._envService.changeSectionLayout(30);
        }
    }

    private _unpin(): void {
        if (this._pinned) {
            this._envService.changeSectionLayout(30);
            this._pinned = false;
        }
    }

    private _isChangingSelection = false;
    private onCanvasSelectionChanged(context: string, previous: any[], current: any[]): void {
        if (this._isChangingSelection === true) return;
        this._isChangingSelection = true;
        if (current !== null && current !== undefined && current.length > 0) {
            this.getCurrentActivity();
        }
        else {
            this.hasContext = false;
            this._updateRibbon(null);
            this.unpin();
        }
        this._isChangingSelection = false;
    }

    private _getCurrentActivity(): void {
        if (this._contextProvider.getCntx() != null) {
            const activityData = (<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IActivity>this._contextProvider.getCntx()[0]);
            if (activityData != null) {
                if (activityData.children != null) {
                    for (let i = 0; i < activityData.children.length; i++) {
                        const cnf: any = activityData.children[i].wizardDefinition || // changed to any because these properties dont exist on any objects (hascontext and contextribbontabs)
                            (<sourceCode.k2Designer.uiComponents.toolbox.plugins.WizardsController>this._itemProviderService.getItemProvider(sourceCode.k2Designer.uiComponents.toolbox.plugins.WizardsController)).getWizardDefinition(activityData.children[i].wizardId);
                        if (cnf != null || cnf != undefined) {
                            if (cnf.hasContext != null || cnf.hasContext != undefined) {
                                this.hasContext = <boolean>cnf.hasContext;
                                if (this.hasContext) {
                                    if (cnf.contextRibbonTabs.length > 0) {
                                        this._updateRibbon(cnf.contextRibbonTabs[0]);
                                    }
                                }
                            }
                            else {
                                this.hasContext = false;
                                this._updateRibbon(null);
                                this.unpin();
                            }
                        }
                        else {
                            this.hasContext = false;
                            this._updateRibbon(null);
                            this.unpin();
                        }
                    }
                }
                else {
                    this.hasContext = false;
                    this._updateRibbon(null);
                    this.unpin();
                }
            }
            else {
                this.hasContext = false;
                this._updateRibbon(null);
                this.unpin();
            }
        }
        else {
            this.hasContext = false;
            this._updateRibbon(null);
            this.unpin();
        }
    }

    private _updateRibbon(ctxTab: string): void {
        if (this.hasContext) {
            if (ctxTab != null) {
                if (this.currentContextTabs.length > 0) {
                    for (var d = 0; d < this.currentContextTabs.length; d++) {
                        for (var k = 0; k < this.tabs.length; k++) {
                            if (this.tabs[k].id === this.currentContextTabs[d].id) {
                                var idx: number = this.tabs.indexOf(this.currentContextTabs[d]);
                                this.tabs.splice(idx, 1);
                                break;
                            }
                        }
                    }

                    this.currentContextTabs = [];
                    this.unpin();
                }

                for (var d = 0; d < this.contextTabs.length; d++) {
                    if (this.contextTabs[d].id === ctxTab) {
                        for (var k = 0; k < this.tabs.length; k++) {
                            if (this.tabs[k].id !== this.contextTabs[d].id) {
                                this.onScriptsLoaded(this.contextTabs[d]);
                                break;
                            }
                        }
                    }
                }
            }
        }
        else {
            if (this.currentContextTabs.length > 0) {
                for (var d = 0; d < this.currentContextTabs.length; d++) {
                    for (var k = 0; k < this.tabs.length; k++) {
                        if (this.tabs[k].id === this.currentContextTabs[d].id) {
                            var idx: number = this.tabs.indexOf(this.currentContextTabs[d]);
                            this.tabs.splice(idx, 1);
                            break;
                        }
                    }
                }
                this.currentContextTabs = [];
                this.unpin();
            }
        }
    }

    private onScriptsLoaded(tab: ITabComponent): void {
        this.tabs.push(tab);
        this.currentContextTabs.push(tab);
    }

    /**
     * Callback function when loading of tabs completed
     */
    private onTabsLoaded(): void {
        this.tabs = this._rbbnCnf.tabs;
        this._progressBarService.incrementGlobalProgressBar();
    }

    /**
     * Callback function when loading of context tabs completed
     */
    private onContextTabsLoaded(): void {
        this._progressBarService.incrementGlobalProgressBar();
        this.contextTabs = this._rbbnCnf.contextTabs;
    }

    private _selectTab(tab: ITabComponent): void {
        if (this.currentTab && this.currentTab.id == tab.id) {
            this._deselectTab();
        }
        else {
            this.currentTab = tab;
            this._ribbonBarService.ribbonTabChanged();
            this.loadTabComponent(tab);
        }
    }

    private _deselectTab(): void {
        const viewContainerRef = this.tabHost.viewContainerRef;
        viewContainerRef.clear();
        this.currentTab = null;
        this._element.find('.ribbon-nav-top .items-list li.active').removeClass('active');
    }

    private _isContextTab(tab: ITabComponent): boolean {
        let retValue = false;
        if (this.hasContext) {
            if (this.currentContextTabs.length > 0) {
                if (this.currentContextTabs.indexOf(tab) > -1) {
                    retValue = true;
                }
            }
        }

        return retValue;
    }

    private loadTabComponent(tab: ITabComponent): void {
        switch (tab.componentName) {
            case 'TabHomeComponent': tab.component = TabHomeComponent; break;
            case 'TabFileComponent': tab.component = TabFileComponent; break;
            case 'TabViewComponent': tab.component = TabViewComponent; break;
        }

        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(tab.component);

        const viewContainerRef = this.tabHost.viewContainerRef;
        if (viewContainerRef) {
            viewContainerRef.clear();
        }

        if (componentFactory) {
            const componentRef = viewContainerRef.createComponent<ITabComponent>(componentFactory);
            componentRef.instance.descTk = tab.descTk;
            componentRef.instance.drpDn = tab.drpDn;
            componentRef.instance.id = tab.id;
            componentRef.instance.nameTk = tab.nameTk;
            componentRef.instance.navItms = tab.navItms;
            componentRef.instance.rsc = tab.rsc;
            componentRef.instance.uiComp = this.uiComp;
            componentRef.instance.togglePin = this.togglePin;
            componentRef.instance.getString = this.getString.bind(this);
        }
    }

    private loadTabs(onCompleted: () => void): void {
        sourceCode.k2Designer.utilities.forEachAsync(
            this._rbbnCnf.tabs,
            (tab: ITabComponent, idx: number, tabLoaded: () => void) => {
                tabLoaded();
            },
            () => {
                onCompleted();
            });
    }

    private _loadContextTabs(onCompleted: () => void): void {
        sourceCode.k2Designer.utilities.forEachAsync(
            this._rbbnCnf.contextTabs,
            (tab: ITabComponent, idx: number, tabLoaded: () => void) => {
                tabLoaded();
            },
            () => {
                onCompleted();
            });
    }

    private onGetResourceFileCompleted(): void {
        this.resources = this._resourceService.strings[this._resourceHash];
    }
}
