import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockRedirectService } from '../../../testing/mock-services/redirect-service';
import { IRedirectService } from '../../../types/services/redirect/i-redirect-service';

const redirectServiceFactory = () => environment.devHarness === true
    ? new MockRedirectService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.RedirectService);

export const RedirectServiceToken = new InjectionToken<IRedirectService>('IRedirectService');

export const RedirectServiceFactory = {
    provide: RedirectServiceToken,
    useFactory: redirectServiceFactory,
    deps: [

    ]
};
