import { Component, ElementRef, Inject, OnInit } from '@angular/core';
import { DragDropService } from '../../../services/drag-drop/drag-drop.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { RulePluginService } from '../../../services/ruleplugin/rule-plugin-service';

@Component({
    selector: 'k2-rule-toolbox',
    templateUrl: './rule-toolbox.component.html',
    styleUrls: ['./rule-toolbox.component.less']
})
export class RuleToolboxComponent implements OnInit {
    public up: boolean;
    public rulesItemDragged: boolean;

    private _eventRemovers: sourceCode.k2Designer.objectModel.core.model.IEventRemover[];

    constructor(private _rulesPluginService: RulePluginService,
        private _resourceService: ResourceService,
        private _dragDropService: DragDropService) {
    }

    ngOnInit(): void {
        this.up = false;
        this.rulesItemDragged = false;

        this._eventRemovers = [];
        this._eventRemovers.push(this._dragDropService.listenDragStarted(this.onStartedDragging.bind(this)));
        this._eventRemovers.push(this._dragDropService.listenDragStopped(this.onStoppedDragging.bind(this)));
    }

    ngOnDestroy(): void {
        for (let i = 0; i < this._eventRemovers.length; i++) {
            this._eventRemovers[i]();
        }
    }

    getString = (token: string) => this._resourceService.getString(token);

    public changeUpValue(): void {
        this.up = !this.up;
    }

    private onStartedDragging(type: string, data: sourceCode.k2Designer.objectModel.dragDropService.IDragDropData): void {
        if (type === 'ruleDesignerItem' && typeof (data.getSource<any>().componentId) !== 'undefined') {
            this.rulesItemDragged = true;
        }
    }

    private onStoppedDragging(): void {
        this.rulesItemDragged = false;
    }

    public getToolboxItems(): sourceCode.k2Designer.objectModel.rulePlugins.IItemConfiguration[] {
        return this._rulesPluginService.getToolboxItems();
    }

    public getDraggedItem(item: sourceCode.k2Designer.objectModel.rulePlugins.IItemConfiguration, context: any): sourceCode.k2Designer.objectModel.dragDropService.IDragDropData {
        return new sourceCode.k2Designer.objectModel.dragDropService.DragDropData<any, any, any>(item, context, null, null);
    }

    public getDragImage(item: sourceCode.k2Designer.objectModel.rulePlugins.IItemConfiguration): HTMLElement {
        const result: HTMLElement = document.createElement('div');

        result.innerHTML = '<div class=\'icon icon-set-blocks icon-size32 ' + item.icon + '\'> </div>';

        return result;
    }

}
