import { Component, EventEmitter, Inject, Input, OnInit, Output, TemplateRef, ViewChild } from '@angular/core';
import { IModelArray } from '../../../../types/models/base/i-model-array';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { IRecentsService } from '../../../../types/services/recents/i-recents-service';
import { IRecipientValidationOptions } from '../../../../types/validation/i-recipient-validation-options';
import { ProcessService } from '../../../services/process/process.service';
import { RecentsServiceToken } from '../../../services/recents/recents-service-factory';
import { ResourceService } from '../../../services/resource/resource.service';
import { RichEditService } from '../../../services/rich-edit/rich-edit.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ComponentIdMapping } from '../../../shared/constants/components';
import { EmailConstants } from '../../../shared/constants/email';
import { keyCodes } from '../../../shared/constants/general';
import { ChooseControlTypes } from '../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { EmailParts } from '../../../shared/enums/email/email-parts';
import { PriorityTypes } from '../../../shared/enums/email/priority-types';
import { ListItemDropDownType } from '../../../shared/enums/list-item/list-item-drop-down-type';
import { TextBoxConstraint } from '../../../shared/enums/text-box/text-box-constraint';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';
import workflowConfiguredSmartField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields; // can only use this for interfaces
import coreConfiguredSmartField = sourceCode.k2Designer.objectModel.core.configuredSmartFields; // can only use this for interfaces

@Component({
    selector: 'k2-email',
    templateUrl: './email.component.html',
    styleUrls: ['./email.component.less']
})
export class EmailComponent extends BaseResourceComponent implements OnInit {
    @ViewChild('userTemplate') userDropDownTemplate: TemplateRef<any>;

    @Input() emailPartDisableList: EmailParts[] = [];
    @Input() emailPartHideList: EmailParts[] = [];
    @Input() emailPartReadOnlyList: EmailParts[] = [];

    @Input() from: IExpression;
    @Input() to: IModelArray<IExpression>;
    @Input() cc: IModelArray<IExpression>;
    @Input() bcc: IModelArray<IExpression>;
    @Input() attachments: IModelArray<IExpression>;
    @Input() subject: IExpression;
    @Input() body: IExpression;
    @Input() priority: PriorityTypes;
    @Input() shouldAllowCustomTemplate: boolean = true;
    /**
     * Validation for recipient fields
     */
    @Input() validation: IRecipientValidationOptions = {};

    /**
     * Should list of users in recipient picker contain Originator/Originator's Manager?
     */
    @Input() shouldIncludeOriginatorUsers: boolean = false;

    @Output() fromChange = new EventEmitter<IExpression>();
    @Output() toChange = new EventEmitter<IModelArray<IExpression>>();
    @Output() ccChange = new EventEmitter<IModelArray<IExpression>>();
    @Output() bccChange = new EventEmitter<IModelArray<IExpression>>();
    @Output() attachmentsChange = new EventEmitter<IModelArray<IExpression>>();
    @Output() priorityChange = new EventEmitter<PriorityTypes>();
    @Output() emailPartHideListChange = new EventEmitter<EmailParts[]>();

    mailPriorityItems: IDropDownMenuItem[];
    get selectedPriority() {
        return this.mailPriorityItems?.find((item: IDropDownMenuItem) => item.isSelected);
    }

    emailParts = EmailParts;
    listItemDropDownType = ListItemDropDownType;
    dropdownMenuItemTemplates = DropdownMenuItemTemplates;
    originatorEmail: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField;
    originatorManagerEmail: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField;
    emailPartDropdownItems: IDropDownMenuItem[] = [];
    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    selectedField: IExpression;
    chooseControlTypes = ChooseControlTypes;
    textBoxConstraints = TextBoxConstraint;
    keyCodes = keyCodes;

    get shouldShowFrom(): boolean {
        return !this.emailPartDisableList.includes(EmailParts.From)
            && !this.emailPartHideList.includes(EmailParts.From)
            && !!this.from;
    }
    set shouldShowFrom(val: boolean) {
        if (val && this.emailPartHideList.includes(EmailParts.From)) {
            this.emailPartHideList.splice(this.emailPartHideList.indexOf(EmailParts.From), 1);
            this.emailPartHideListChange.emit(this.emailPartHideList);
        } else if (val === false && !this.emailPartHideList.includes(EmailParts.From)) {
            this.emailPartHideList.push(EmailParts.From);
            this.emailPartHideListChange.emit(this.emailPartHideList);
        }
    }

    get shouldShowCc() {
        return !this.emailPartHideList.includes(EmailParts.Cc);
    }
    set shouldShowCc(val: boolean) {
        if (val && this.emailPartHideList.includes(EmailParts.Cc)) {
            this.emailPartHideList.splice(this.emailPartHideList.indexOf(EmailParts.Cc), 1);
            if (!this.cc.length) {
                this.cc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
            }
            this.emailPartHideListChange.emit(this.emailPartHideList);
        } else if (val === false && !this.emailPartHideList.includes(EmailParts.Cc)) {
            this.emailPartHideList.push(EmailParts.Cc);
            this.cc.clear();
            this.emailPartHideListChange.emit(this.emailPartHideList);
        }
    }

    get shouldShowBcc() {
        return !this.emailPartHideList.includes(EmailParts.Bcc);
    }
    set shouldShowBcc(val: boolean) {
        if (val && this.emailPartHideList.includes(EmailParts.Bcc)) {
            this.emailPartHideList.splice(this.emailPartHideList.indexOf(EmailParts.Bcc), 1);
            if (!this.bcc.length) {
                this.bcc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
            }
            this.emailPartHideListChange.emit(this.emailPartHideList);
        } else if (val === false && !this.emailPartHideList.includes(EmailParts.Bcc)) {
            this.emailPartHideList.push(EmailParts.Bcc);
            this.bcc.clear();
            this.emailPartHideListChange.emit(this.emailPartHideList);
        }
    }

    /**
     * Returns true if any recipient (to/cc/bcc) / from field is displayed
     */
    get shouldShowAnyUserField() {
        return this.shouldShowFrom || !this.emailPartDisableList.includes(EmailParts.To) || this.shouldShowCc ||
            this.shouldShowBcc;
    }

    constructor(resourceService: ResourceService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        private processService: ProcessService,
        @Inject(RecentsServiceToken) private recentsService: IRecentsService) {
        super(resourceService);
        undoRedoService.listen(() => {
            this.selectedField = undefined;
        });
    }

    ngOnInit(): void {
        this.originatorEmail = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.createInstance({
            fieldName: sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATOREMAILWORKFLOWCONTEXTFIELDNAME,
            title: this.resourceService.getString(`user_task_step_recipient_originator_context_field_email`),
            customTitle: this.resourceService.getString(`user_task_step_recipient_originator`),
            parentName: this.resourceService.getString(`workflow_originator_field_parent_name`),
            internalId: 1,
            componentId: ComponentIdMapping.k2WorkflowContextField,
        });

        this.originatorManagerEmail = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.createInstance({
            fieldName: sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATORMANAGEREMAILWORKFLOWCONTEXTFIELDNAME,
            title: this.resourceService.getString(`user_task_step_recipient_originator_context_field_email`),
            customTitle: this.resourceService.getString(`user_task_step_recipient_originator_manager`),
            parentName: this.resourceService.getString(`workflow_originator_manager_field_parent_name`),
            internalId: 1,
            componentId: ComponentIdMapping.k2WorkflowContextField,
        });

        this.populatePriorityList();
        this.recentsService.registerContext(`RecentlySelectedUsers`, 10);
        this.setEmailPartDropdownItems();
    }

    populatePriorityList() {
        this.mailPriorityItems = [];
        this.mailPriorityItems.push({
            key: PriorityTypes.low.toString(),
            icon: `icon-set-dropdownset ic-priority_low`,
            isSeparator: false,
            title: this.resourceService.getString(`priority_low`),
            isSelected: this.priority === PriorityTypes.low
        } as IDropDownMenuItem);

        this.mailPriorityItems.push({
            key: PriorityTypes.med.toString(),
            icon: `icon-set-dropdownset ic-priority_medium`,
            isSeparator: false,
            title: this.resourceService.getString(`priority_med`),
            isSelected: this.priority === PriorityTypes.med || !this.priority
        } as IDropDownMenuItem);

        this.mailPriorityItems.push({
            key: PriorityTypes.high.toString(),
            icon: `icon-set-dropdownset ic-priority_high`,
            isSeparator: false,
            title: this.resourceService.getString(`priority_high`),
            isSelected: this.priority === PriorityTypes.high
        } as IDropDownMenuItem);
    }

    addAttachment(): void {
        this.undoRedoService.addUndoMarker(`--emailController: add attachment--`);
        this.attachments.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
            smartFields: []
        }));
    }

    deleteAttachment() {
        if (this.selectedField && this.attachments.includes(this.selectedField)) {
            this.undoRedoService.addUndoMarker(`--emailController: delete attachment--`);
            this.selectedField.removeFromModelArray();
            this.selectedField = undefined;
        }
    }

    addTo(): void {
        this.undoRedoService.addUndoMarker(`--emailController: add to--`);
        this.to.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
    }

    addCC(): void {
        this.undoRedoService.addUndoMarker(`--emailController: add cc--`);
        this.cc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
    }

    addBCC(): void {
        this.undoRedoService.addUndoMarker(`--emailController: add bcc--`);
        this.bcc.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
    }

    deleteRecipient() {
        this.undoRedoService.addUndoMarker(`--emailController: delete recipient--`);
        this.selectedField.removeFromModelArray();
        if (!this.bcc?.length) {
            this.emailPartHideList.push(EmailParts.Bcc);
        }
        if (!this.cc?.length) {
            this.emailPartHideList.push(EmailParts.Cc);
        }
        if (!this.to?.length) {
            this.to.push(sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance());
        }
        this.selectedField = undefined;
    }

    toggle(userExpression: IExpression): void {
        if (userExpression.isDynamic !== true && userExpression.smartFields.length > 1) {
            userExpression.smartFields.clear();
        }
        else if (userExpression.isDynamic &&
            userExpression.smartFields?.length === 1 &&
            userExpression.smartFields[0].instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.UserField.implementor)
        ) {
            const text: workflowConfiguredSmartField.IUserField = userExpression.smartFields[0];
            userExpression.smartFields.clear();
            userExpression.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                text: text.email || text.systemName
            }));
        }
        else if (!userExpression.isDynamic &&
            userExpression.smartFields?.length === 1 &&
            userExpression.smartFields[0].instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.implementor)
        ) {
            const recents =
                this.recentsService.getItems(`RecentlySelectedUsers`) as workflowConfiguredSmartField.IUserField[];
            const field = userExpression.smartFields[0] as workflowConfiguredSmartField.ITextField;
            userExpression.smartFields.clear();
            for (const recentUser of recents) {
                if (field.text === recentUser.email
                    || field.text === recentUser.systemName) {
                    userExpression.smartFields.push(
                        sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.UserField.createInstance({
                            title: recentUser.displayName,
                            email: recentUser.email,
                            displayName: recentUser.displayName,
                            systemName: recentUser.systemName,
                            identityType: (recentUser as any).type.toString(),
                            returnType: sourceCode.k2Designer.objectModel.core.configuredSmartFields.ConfiguredSmartFieldTypes.userEmail
                        })
                    );
                }
            }
        }
        else if (userExpression.smartFields?.length === 1) {
            const smartfield = userExpression.smartFields[0];
            /*For the Originator's Email and Originator's Manager's Email fields,
             * we want them to resolve to `Originator` and `Originator's Manager` when dynamic is toggled off */
            if (smartfield.instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.implementor)
                && (((smartfield as workflowConfiguredSmartField.IWorkflowContextField).fieldName === EmailConstants.OriginatorEmail) ||
                    ((smartfield as workflowConfiguredSmartField.IWorkflowContextField).fieldName === EmailConstants.OriginatorManagerEmail)
                )) {
                this.ensureBuiltInRecipientEmailCustomTitle((smartfield as workflowConfiguredSmartField.IWorkflowContextField));
            }
            /*For all other Workflow SmartFields, we want them to resolve to the `Choose Recipient...`
             * watermark and badge the dropdown when dynamic is toggled off */
            // eslint-disable-next-line max-len
            else if (smartfield.instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.implementor)
            ) {
                userExpression.smartFields.clear();
            }
            //From address is a special case that we want to resolve, so we don't clear the SmartFields
            else if (smartfield.instanceOf(sourceCode.k2Designer.objectModel.core.configuredSmartFields.ConfiguredSmartField.implementor)
                && ((smartfield as coreConfiguredSmartField.IConfiguredSmartField).title === EmailConstants.FromAddress)) { }
            //Do not clear if this is a Group Email SmartField.
            else if (sourceCode.k2Designer.utilities.smartObjects.isEmailForGroupUsersField(this.processService as any, smartfield)) {
                smartfield.customTitle =
                    sourceCode.k2Designer.utilities.smartObjects.getGroupDisplayName(this.processService as any, smartfield);
            }
            //Clear all other SmartFields
            else if (smartfield.instanceOf(sourceCode.k2Designer.objectModel.core.configuredSmartFields.ConfiguredSmartField.implementor)
            ) {
                userExpression.smartFields.clear();
            }
        }
    }

    prioritySelected(item: IDropDownMenuItem) {
        this.priority = parseInt(item.key, 10);
        this.priorityChange.emit(this.priority);
    }

    emailPartSelected(item: IDropDownMenuItem) {
        switch (item.key) {
            case EmailParts.From.toString(): this.shouldShowFrom = item.isSelected;
                break;
            case EmailParts.Bcc.toString(): this.shouldShowBcc = item.isSelected;
                break;
            case EmailParts.Cc.toString(): this.shouldShowCc = item.isSelected;
                break;
        }
    }

    private ensureBuiltInRecipientEmailCustomTitle(workflowContextField: workflowConfiguredSmartField.IWorkflowContextField): void {
        if (workflowContextField.fieldName ===
            sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATOREMAILWORKFLOWCONTEXTFIELDNAME) {
            workflowContextField.customTitle = this.resourceService.getString(`user_task_step_recipient_originator`);
        }
        else if (workflowContextField.fieldName ===
            sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATORMANAGEREMAILWORKFLOWCONTEXTFIELDNAME) {
            workflowContextField.customTitle = this.resourceService.getString(`user_task_step_recipient_originator_manager`);
        }
    }

    private setEmailPartDropdownItems(): void {
        this.emailPartDropdownItems = [];
        if (!this.emailPartDisableList.includes(this.emailParts.From)) {
            //From
            this.emailPartDropdownItems.push({
                title: this.getFromResx('Wizards', 'wizard_Task_Notification_Label_From_sm').value,
                key: this.emailParts.From.toString(),
                isSelected: this.shouldShowFrom
            });
        }
        if (!this.emailPartDisableList.includes(this.emailParts.Cc)) {
            //CC
            this.emailPartDropdownItems.push({
                title: this.getFromResx('Wizards', 'wizard_Task_Notification_Label_Cc_sm').value,
                key: this.emailParts.Cc.toString(),
                isSelected: this.shouldShowCc
            });
        }
        if (!this.emailPartDisableList.includes(this.emailParts.Bcc)) {
            //BCC
            this.emailPartDropdownItems.push({
                title: this.getFromResx('Wizards', 'wizard_Task_Notification_Label_Bcc_sm').value,
                key: this.emailParts.Bcc.toString(),
                isSelected: this.shouldShowBcc
            });
        }
    }
}
