import { Injectable } from '@angular/core';
import { SmartObjectItemTypes } from '../../../shared/enums/model/workflow/item-provider/smart-object-item-types';
import { DataFieldTypes } from '../../../shared/enums/model/workflow/process/data-field-types';
import { IEnumLookupPair } from '../../../shared/models/i-enum-lookup-pair';
import { ResourceService } from '../../resource/resource.service';

@Injectable({
    providedIn: 'root'
})
export class DataTypeResolverService {

    constructor(private resourceService: ResourceService) { }

    public getSpriteClassFromDataType(type: string): string {
        let retClass = '';

        if (type != null) {
            switch (type.toString()) {
                case '0':
                    retClass = 'yesno';
                    break;
                case '1':
                    retClass = 'datetime';
                    break;
                case '2':
                    retClass = 'decimal';
                    break;
                case '4':
                    retClass = 'number';
                    break;
                case '6':
                    retClass = 'text';
                    break;

                default:
                    retClass = 'text';
            }
        }

        return retClass;
    }


    public getSpriteClassFromServerEnumDataType(type: number): string {
        let retClass = '';

        //The entry for the datatype in the db is incremeneted with 1 when you deploy.  It does not correspond directly to
        //the enum in authoring datafieldtypes.
        if (type !== null) {
            switch (type.toString()) {
                case '1':
                    retClass = 'yesno';
                    break;
                case '2':
                    retClass = 'datetime';
                    break;
                case '3':
                    retClass = 'decimal';
                    break;
                case '4':
                case '5':
                case '6':
                    retClass = 'number';
                    break;
                case '7':
                    retClass = 'text';
                    break;
                default:
                    retClass = 'text';
            }
        }

        return retClass;
    }

    public getSmartObjectItemType(type: string): SmartObjectItemTypes {
        if (type && type.length > 0) {
            switch (type.toLowerCase()) {
                case 'text':
                    return SmartObjectItemTypes.text;
                case 'number':
                    return SmartObjectItemTypes.number;
                case 'decimal':
                    return SmartObjectItemTypes.decimal;
                case 'yesno':
                    return SmartObjectItemTypes.yesNo;
                case 'datetime':
                    return SmartObjectItemTypes.dateTime;
                case 'date':
                    return SmartObjectItemTypes.date;
                default:
                    return SmartObjectItemTypes.text;
            }
        }

        return SmartObjectItemTypes.text;
    }

    /**
     * Method to return the class name for the data type
     * NOTE:  This will only work if the browser is in english, 'ic-jaNein' is not a valid class name for YesNo ext
     *
     * @param type Type id for smart object
     */
    public getSmartObjectSpriteClassFromDataType(type: number): string {
        let retClass = sourceCode.k2Designer.objectModel.workflow.itemProvider.SmartObjectItemTypesImplementor(<SmartObjectItemTypes>(<number>type));

        retClass = retClass.replace(/[\\\/&-]/g, '');

        return retClass.toLowerCase();
    }

    public getSmartObjectSpriteClassFromDataTypeByType(itemType: number): string {
        let icon = '';
        switch (itemType) {
            case SmartObjectItemTypes.smartObject:
                break;
            case SmartObjectItemTypes.smartObjectCategory:
                break;
            case SmartObjectItemTypes.smartObjectMethod:
                break;
            case SmartObjectItemTypes.smartObjectExecution:
                break;
            case SmartObjectItemTypes.smartObjectTypes:
                break;
            case SmartObjectItemTypes.smartObjectServiceObjectNames:
                break;
            case SmartObjectItemTypes.autoGuid:
                icon = 'autoguid';
                break;
            case SmartObjectItemTypes.autoNumber:
                icon = 'autonumber';
                break;
            case SmartObjectItemTypes.date:
                icon = 'date';
                break;
            case SmartObjectItemTypes.file:
                icon = 'file';
                break;
            case SmartObjectItemTypes.guid:
                icon = 'guid';
                break;
            case SmartObjectItemTypes.hyperlink:
                icon = 'hyperlink';
                break;
            case SmartObjectItemTypes.image:
                icon = 'image';
                break;
            case SmartObjectItemTypes.memo:
                icon = 'memo';
                break;
            case SmartObjectItemTypes.multivalue:
                icon = 'multivalue';
                break;
            case SmartObjectItemTypes.time:
                icon = 'time';
                break;
            case SmartObjectItemTypes.xml:
                icon = 'xml';
                break;
            case SmartObjectItemTypes.text:
                icon = 'text';
                break;
            case SmartObjectItemTypes.number:
                icon = 'number';
                break;
            case SmartObjectItemTypes.decimal:
                icon = 'decimal';
                break;
            case SmartObjectItemTypes.yesNo:
                icon = 'yesno';
                break;
            case SmartObjectItemTypes.dateTime:
                icon = 'datetime';
                break;
            default:
                break;
        }
        return icon;
    }

    public getInlineFunctionSpriteClassFromDataType(itemType: number): string {
        let icon = '';
        switch (itemType) {
            case SmartObjectItemTypes.smartObject:
                break;
            case SmartObjectItemTypes.smartObjectCategory:
                break;
            case SmartObjectItemTypes.smartObjectMethod:
                break;
            case SmartObjectItemTypes.smartObjectExecution:
                break;
            case SmartObjectItemTypes.smartObjectTypes:
                break;
            case SmartObjectItemTypes.smartObjectServiceObjectNames:
                break;
            case SmartObjectItemTypes.autoGuid:
                icon = 'autoguid';
                break;
            case SmartObjectItemTypes.autoNumber:
                icon = 'autonumber';
                break;
            case SmartObjectItemTypes.date:
                icon = 'date';
                break;
            case SmartObjectItemTypes.file:
                icon = 'file';
                break;
            case SmartObjectItemTypes.guid:
                icon = 'guid';
                break;
            case SmartObjectItemTypes.hyperlink:
                icon = 'hyperlink';
                break;
            case SmartObjectItemTypes.image:
                icon = 'image';
                break;
            case SmartObjectItemTypes.memo:
                icon = 'memo';
                break;
            case SmartObjectItemTypes.multivalue:
                icon = 'multivalue';
                break;
            case SmartObjectItemTypes.time:
                icon = 'time';
                break;
            case SmartObjectItemTypes.xml:
                icon = 'xml';
                break;
            case SmartObjectItemTypes.text:
                icon = 'text';
                break;
            case SmartObjectItemTypes.number:
                icon = 'number';
                break;
            case SmartObjectItemTypes.yesNo:
                icon = 'yesno';
                break;
            case SmartObjectItemTypes.dateTime:
                icon = 'datetime';
                break;
            default:
                break;
        }
        return icon;
    }

    public getSpriteClassFromDotNetType(type: string): string {
        let retClass = '';

        if (type != null) {
            switch (type.toLocaleLowerCase()) {
                case 'integer':
                    retClass = 'number';
                    break;
                case 'datetime':
                    retClass = 'datetime';
                    break;
                case 'decimal':
                    retClass = 'decimal';
                    break;
                case 'boolean':
                    retClass = 'yesno';
                    break;

                default:
                    retClass = 'text';
            }
            return retClass;
        }
    }

    public getTypeFromDotNetType(type: string): number {

        let returnType = 6;

        if (type != null) {
            switch (type.toLowerCase()) {
                case 'boolean':
                    returnType = 0;
                    break;
                case 'datetime':
                    returnType = 1;
                    break;
                case 'decimal':
                    returnType = 2;
                    break;
                case 'integer':
                    returnType = 4;
                    break;
            }
        }
        return returnType;
    }


    public getIconBySmartFieldType(dataType: number): string {
        let icon = '';

        switch (dataType) {
            case 2: {
                icon = 'ic-number';
                break;
            }
            case 3: {
                icon = 'ic-yesno';
                break;
            }
            case 4: {
                icon = 'ic-datetime';
                break;
            }
            case 5: {
                icon = 'ic-any';
                break;
            }
            case 9: {
                icon = 'ic-decimal';
                break;
            }
            default: {
                icon = 'ic-text';
                break;
            }
        }

        return icon;
    }

    /**
     * Dictionary of of resource tokens for data fields by key type
     */
    lookupTokensDataFieldTypes: {
            [value: number]: {
                token: string;
            };
        } = {
            0: { token: 'workflow_dataFieldType_yesNo' },
            1: { token: 'workflow_dataFieldType_dateTime' },
            2: { token: 'workflow_dataFieldType_decimal' },
            4: { token: 'workflow_dataFieldType_number' },
            6: { token: 'workflow_dataFieldType_string' },
        };

    /**
     * Previously named dataFieldTypesImplementor
     * Returns resource string representing title of data type by key
     *
     * @param value
     */
    dataFieldTypesTitle(value: number): string {
        const lookup = this.lookupTokensDataFieldTypes[value];
        if (lookup !== null && lookup !== undefined) {
            return this.resourceService.getString(lookup.token);
        } else {
            return '--could not resolve enum-lookup(' + value + ' in ' + this.lookupTokensDataFieldTypes + ')--';
        }
    }

    getDataFieldTypesList(): IEnumLookupPair[] {
        return [
            {
                key: 0,
                title: this.dataFieldTypesTitle(0)
            },
            {
                key: 1,
                title: this.dataFieldTypesTitle(1)
            },
            {
                key: 2,
                title: this.dataFieldTypesTitle(2)
            },
            {
                key: 4,
                title: this.dataFieldTypesTitle(4)
            },
            {
                key: 6,
                title: this.dataFieldTypesTitle(6)
            },

        ];
    }
}
