import { IValidationService } from '../../../types/services/validation/ivalidation.service';
import { ValidationTypes } from '../enums/model/validation-types';
import { propertyMatchesInObjectArray } from './general';

/**
 * Returns a unique name
 * This function will take the name passed in, if name already exists in collection passed in (array), will append
 * a number to name.
 *
 * e.g. myName (where item with name myName already exists in array), will return myName 1
 *
 * @param array Collection of items that will be used to check if name already exists
 * @param propertyName The property to use for assigning the object's new name
 * @param originalName Original name to use for object
 * @param mainModel Model that contains the original name that will be the same as the Property name in this object
 * @param isCaseSensitive This will ensure that no case sensitivity will be applied
 */
export function findUniqueNameForListArray(array: sourceCode.k2Designer.objectModel.core.model.IModelArray<any>, propertyName: string,
    originalName: string, mainModel: any, isCaseSensitive?: boolean): string {

    let ct = 1;
    originalName = originalName.trim();
    let newName: string = originalName + ' ' + ct;

    do {
        const found = propertyMatchesInObjectArray(array, propertyName, newName, isCaseSensitive) > 0;

        if (found) {
            ct++;
            newName = originalName + ' ' + ct;

        } else {
            break;
        }
    } while (ct < 1000);

    return newName;
}

export const disallowedWorkflowSpecialChar = '\\/:*?"<>,|()\';';

/**
 * Returns a unique name
 * This function will take the name passed in, if name already exists in collection passed in (array), will append
 * a number to name.
 *
 * If forceNumber is true, it will always append number to new name, whether it exists in array or not.
 * e.g. myName (where item with name myName already exists in array), will return myName 1
 *
 * @param array Collection of items that will be used to check if name already exists
 * @param propertyName The property to use for assigning the object's new name
 * @param defaultName Default name to use for object
 * @param mainItem
 * @param forceNumber Whether to always append number to name, or only if name already exists in array
 * @param seperator To use between the name and number appended. Will default to underscore for systemname propertyname and space for all others
 * @param limit Optional parameter to ensure that the resulting name does not exceed the specified length
 */
export function findUniqueName(array: any[],
    propertyName: string, defaultName: string, mainItem: any = null,
    forceNumber: boolean = false, seperator: string = ' ', limit?: number): string {

    let ct: number = forceNumber ? 1 : 0;
    let suffix: string = (forceNumber ? ' ' + ct : '');
    let newName: string = defaultName + suffix;

    do {
        const item = array.find(x => x[propertyName] === newName);

        if (!!item && item !== mainItem) {

            ct++;

            suffix = seperator + ct;
            newName = defaultName + suffix;

            if (!!limit && newName.length > limit) {
                newName = defaultName.substr(0, limit - suffix.length) + suffix;
            }

        } else {
            break;
        }
    } while (ct < 1000);

    return newName;
}

/**
 * Returns true if value contains any of the following special characters: \/:.*?"<>,|()
 * This function will use the validation service and validation service will return array of validation errors and messages
 *
 * @value string Text value to be validated for special characters
 */
export function containsSpecialCharacters(value: string, validationService: IValidationService): boolean {

    validationService.overrideEnabled = true;

    const validationResult: sourceCode.k2Designer.objectModel.core.model.IErrorInformation[] =
        validationService.evaluate_Legacy(
           [ValidationTypes.LegacyPropertyValidator],
            {
                evaluationValue: value,
                array: [],
                evaluationProperty: 'title',
                evaluateEmptyValue: true, //evaluate empty value
                emptyPropertyMessage: '', //empty title resource message
                duplicatePropertyValueMessage: '', //duplicate title resource message
                evaluateSpecialCharacters: true, //evaluate special characters
                specialCharacters: '\\/:.*?"<>,|()', //list of special chars
            } as sourceCode.k2Designer.objectModel.validationService.plugins.IPropertyValidatorItem);

    validationService.overrideEnabled = false;

    return (validationResult.length > 0);
}
