export enum MergeLogicTypes {
    /**
     * Wait for all followed paths to be completed
     * */
    waitForAll = 1,

    /**
     * Wait for the first  path to be completed
     * */
    waitForFirst = 2,

    /**
     * Allow all completed paths to follow through
     * */
    noLogic = 3,

    /***
     * Custom logic
     * */
    custom = 4
}
