import { Component, Input, OnInit } from '@angular/core';
import { ResourceService } from '../../../services/resource/resource.service';
import { PopupConfigStates } from '../../../shared/enums/popup/popup-config-states';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-popup-states',
    templateUrl: './popup-states.component.html',
    styleUrls: ['./popup-states.component.less']
})
export class PopupStatesComponent extends BaseResourceComponent implements OnInit {
    @Input() stateConfiguration: any;

    get misconfigured(): boolean {
        return this.stateConfiguration?.state == PopupConfigStates.misconfigured;
    }
    get loading(): boolean {
        return this.stateConfiguration?.state == PopupConfigStates.loading;
    }
    get oAuthError(): boolean {
        return this.stateConfiguration?.state == PopupConfigStates.oautherror;
    }
    get error(): boolean {
        return this.stateConfiguration?.state == PopupConfigStates.error;
    }
    get normal(): boolean {
        return this.stateConfiguration?.state == PopupConfigStates.normal;
    }

    constructor(resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
    }
}
