/**
 * Item type represent an action, condition or handler.
 */
export enum GeneralItemTypes {
    /**
     * Indicates the item is an handler
     */
    handler = 1,

    /**
     * Indicates the item is an condition
     */
    condition = 2,

    /**
     * Indicates the item is an action
     */
    action = 3,

    /**
     * Indicates the item is an else
     */
    else = 4,

    /**
     * Indicates the item is a for each
     */
    forEach
}
