import { Component, ElementRef, Inject, Input, OnChanges, OnInit, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { IFilterCallbackData } from '../../../../types/models/smart-field/i-filter-callback-data';
import { IFilterPanelService } from '../../../../types/services/filter-panel/i-filter-panel';
import { IItemProviderService } from '../../../../types/services/item-provider/i-item-provider-service';
import { IPluginService } from '../../../../types/services/plugin/i-plugin-service';
import { ISelectionService } from '../../../../types/services/selection/i-selection-service';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { ISmartFieldComposerOptions } from '../../../../types/uiComponents/smartFieldComposer/ISmartFieldComposerOptions';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { FilterPanelServiceToken } from '../../../services/filter-panel/filter-panel-service-factory';
import { ItemProviderServiceToken } from '../../../services/item-provider/item-provider-service-factory';
import { PluginServiceToken } from '../../../services/plugin/plugin-service-factory';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { SelectionServiceToken } from '../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { plugins } from '../../../shared/constants/plugins';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { ExternalReferenceState } from '../../../shared/enums/external-references/external-references';
import { SmartFieldFilters } from '../../../shared/enums/smart-field-composer/smart-field-composer';
import { getEnumKeyByEnumValue } from '../../../shared/utils/general';
import { RuleDesignerSelectionBaseComponent } from '../../rules-designer/base/rule-designer-selection-base/rule-designer-selection-base.component';

@Component({
    selector: 'k2-smart-field-composer-filter',
    templateUrl: './smart-field-composer-filter.component.html',
    styleUrls: ['./smart-field-composer-filter.component.less']
})
export class SmartFieldComposerFilterComponent extends RuleDesignerSelectionBaseComponent implements OnInit, OnChanges {
    @Input() public model: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectFieldMethod;
    @Input() public composer: ISmartFieldComposerOptions;
    @Input() public template: string;

    @ViewChild('FirstItemWhere') public FirstItemWhere: TemplateRef<any>;
    @ViewChild('AllItemsAsCollection') public AllItemsAsCollection: TemplateRef<any>;
    @ViewChild('Custom') public Custom: TemplateRef<any>;

    public selectedTemplate: TemplateRef<any>;
    public isToggleBtnHover: boolean;
    public smoMethodExternalDefinition: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField;
    private _filterPropList: IDropDownMenuItem[];
    filterPropSelectedKey: string;
    filterPropSelectedIcon: string;
    learnMoreLinkUrl = sourceCode.k2Designer.utilities.buildHelpLinkUrl(501); // Not sure where this 501 is defined

    constructor(@Inject(FilterPanelServiceToken) private _filterPanelService: IFilterPanelService,
        private _resourceService: ResourceService,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService,
        private _processService: ProcessService,
        @Inject(ValidationServiceToken) private _validationService: IValidationService,
        @Inject(SelectionServiceToken) _selService: ISelectionService,
        protected eventService: EventNotifierService,
        protected rootElement: ElementRef<HTMLElement>,
        @Inject(PluginServiceToken) private _pluginService: IPluginService,
        @Inject(ItemProviderServiceToken) private _itemProviderService: IItemProviderService) {
        super(_selService, eventService, rootElement);
    }

    ngOnInit(): void {
        super.ngOnInit();
        const smoField = this.composer?.data as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectField;
        if (smoField) {
            this.smoMethodExternalDefinition = (smoField.getRoot() as any).findItem(smoField.method.externalReference);
        }
        this.updateTemplate();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.template) {
            switch (this.template) {
                case SmartFieldFilters.AllItemsAsCollection:
                //default to 'all'
                    if (this.model?.filter?.configuredListOptions) {
                        this.model.filter.configuredListOptions.filterOption = sourceCode.k2Designer.utilities.FILTEROPTION_ALL;
                    }
                    break;
                case SmartFieldFilters.FirstItemWhere:
                case SmartFieldFilters.Custom:
                    break;
            }
            this.updateTemplate();
        }
    }

    private updateTemplate() {
        setTimeout(() => {
            const key = getEnumKeyByEnumValue(SmartFieldFilters, this.template);
            if (this[key]) {
                this.selectedTemplate = this[key];
            }
        }, 0);
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    getFilterButtonText(): string {
        let buttonText = '';

        if (this._filterPanelService.isPanelActive()
            && this._filterPanelService.isFilterPanelOriginatedFrom(this.composer.data)) {
            buttonText = this._resourceService.getString('workflow_smartFieldComposer_SMO_filter_panel_close');
        }
        else {
            buttonText = this._resourceService.getString('workflow_smartFieldComposer_SMO_filter_panel_open');
        }
        return buttonText;
    }

    get filterPropList() {
        if (!this._filterPropList) {
            this._filterPropList = this.getFilterPropList();
        }
        return this._filterPropList;
    }

    isFilterActive(): boolean {
        return this._filterPanelService.isPanelActive();
    }

    /**
     * Returns true if filter open and populated with data from a different smartfield than the one currently in scope.
     * Used to disable filter panel button if the filter panel is currently open and it is open from SmartField other than the one in scope
     */
    public shouldDisableFilterPanelButton(): boolean {
        let context = this._filterPanelService.getContext();

        if (!this._filterPanelService.isPanelActive() && context) {
            //filter panel is not open, but has a context, reset the panel origin to enable all custom buttons
            this._filterPanelService.setContext(null);
            context = this._filterPanelService.getContext();
        }

        //if filter panel is active and is not originated from this composer, disable the button (this is for when two composers with two buttons)
        if (context && !this._filterPanelService.isFilterPanelOriginatedFrom(this.composer.data)) {
            return true;
        }
        return false;
    }

    getFilterPropList(): IDropDownMenuItem[] {
        const result: IDropDownMenuItem[] = [];
        let selectedItem: IDropDownMenuItem;

        const firstItemExpression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilterExpression =
            <sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilterExpression>this.model.filter.configuredFilter.expressions[0];

        //If any .XML properties was used to filter against, flag them so they can be replaced by the extension
        const selectedProp: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField =
            this._processService.root.findItem<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField>(firstItemExpression.property);

        let extensionPropName = '';

        if (selectedProp.systemName.match(/(.XML)$/) && (selectedProp.type === 'file' || selectedProp.type === 'image')) {
            extensionPropName = selectedProp.systemName.substr(0, selectedProp.systemName.lastIndexOf('.XML'));
        }

        for (let i = 0; i < this.smoMethodExternalDefinition.returns.length; i++) {
            const prop: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField = this.smoMethodExternalDefinition.returns[i];

            //Only exclude .XML properties of type file and image
            const includeProp: boolean = !prop.systemName.match(/(.XML)$/) ? true : prop.type !== 'file' && prop.type !== 'image';

            //We need to exclude properties with .XML from properties to configure against
            if (includeProp) {
                const newItem = {
                    key: prop.getReference(),
                    title: prop.displayName,
                    icon: 'icon icon-sprite icon-set-datatypes icon-size24 selected ' + this.getPropIconOf(prop.type),
                    isSelected: (firstItemExpression.property === prop.getReference()),
                    innerItemComponent: DropdownMenuItemTemplates.SmartFieldSmoFilter
                };

                if (newItem.isSelected) {
                    selectedItem = newItem;
                } else {
                    //Check if the property had an extension and if the current item added needs to be used
                    if (extensionPropName === prop.systemName) {
                        selectedItem = newItem;
                    }
                }

                //Exclude missing property from dropdown items after setting it as selected
                if (prop.state === ExternalReferenceState.Missing) {
                    continue;
                }

                if (i > 0) {
                    result.push({
                        key: 'separator-' + i,
                        isSeparator: true
                    });
                }

                result.push(newItem);
            }
        }

        if (!selectedItem) {
            let itemToSelect = result[0];

            if (this.smoMethodExternalDefinition.inputs.length > 0) {
                const firstRequiredPropReference = this.smoMethodExternalDefinition.inputs[0].getReference();
                for (let j = 0; j < result.length; j++) {
                    if (result[j].key === firstRequiredPropReference) {
                        itemToSelect = result[j];
                    }
                }
            }

            itemToSelect.isSelected = true;
            selectedItem = itemToSelect;
        }

        this.onFilterPropSelected(selectedItem);
        return result;
    };

    private onFilterPropSelected(item: IDropDownMenuItem) {
        this._undoRedoService.addUndoMarker('-- undo SMO option --');
        this._undoRedoService.addCustomObjectUndoEvent(this, ['filterPropSelectedKey'], '-- undo SMO filter dropdown --');
        this._undoRedoService.addCustomUndoEvent(this.undoDropdownItemSelection.bind(this), '-- custom undo event to revert the SMO filter dropdown --');

        const firstItemExpression = this.model.filter.configuredFilter
            .expressions[0] as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilterExpression;

        const referencedProperty = this.model.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelBase>()
            .findItem(item.key) as sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectReferenceDefinition;

        firstItemExpression.property = item.key;
        firstItemExpression.propertyType = referencedProperty.type;

        this.filterPropSelectedKey = item.key;

        this.validateSelectedProperty();

        this.filterPropSelectedIcon = item.icon.replace('selected', '');
    }

    /**
     * Custom undo event that fires when the filter selection choice is undone
     *
     * @param isUndo
     */
    private undoDropdownItemSelection(isUndo: boolean): boolean {
        //If undo was performed
        if (isUndo) {
            const firstItemExpression = this.model.filter.configuredFilter
                .expressions[0] as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilterExpression;

            for (let f = 0; f < this._filterPropList.length; f++) {
                this._filterPropList[f].isSelected = firstItemExpression.property === this._filterPropList[f].key;
            }
        } else {
            //If redo was performed
            for (let f = 0; f < this._filterPropList.length; f++) {
                this._filterPropList[f].isSelected = this.filterPropSelectedKey === this._filterPropList[f].key;
            }
        }

        return true;
    }

    /**
     * If selected property is in missing state, raise reference error.
     * If selected property is valid, re-evaluate errors.
     * */
    private validateSelectedProperty() {
        if (!this.model.filter.configuredFilter.expressions?.length) {
            return;
        }

        const selectedItem = this.model.filter.configuredFilter
            .expressions[0] as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredFilterExpression;

        if (!selectedItem?.property) {
            return;
        }

        //Get property:
        const referencedProperty = this.model.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelBase>()
            .findItem(selectedItem.property) as sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectReferenceDefinition;

        if (!referencedProperty) {
            return;
        }

        if (referencedProperty.state === sourceCode.k2Designer.objectModel.core.model.ExternalReferenceState.Missing) {
            this._processService.raiseReferenceError(selectedItem, 'property', selectedItem.property);
        } else {
            this._validationService.reevaluateErrors(selectedItem.errorInformation, true);
        }
    }

    getPropIconOf(type: string) {
        if (type) {
            return 'ic-' + type.replace('/', '').toLowerCase();
        }
        return 'ic-text';
    }

    onFocus() {
        this.setSelected(true, true);
    }

    onBlur() {
        this.setSelected(false, true);
    }

    getDisplayName(externalRef: string): string {
        let title = '';
        const propRef: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField = (this.composer.data.getRoot() as any)
            .findItem(externalRef);

        if (propRef) {
            title = propRef.displayName;
        }

        return title;
    }

    toggleFilterPanel(): void {
        //Because this custom filter tab only exists for list methods, no need to hide/show filter. This additional functionality to hide/show filter tab
        //was implemented so that the user can still supply input properties on non list methods. Here it needs to reset incase a previous field was one
        //of those.
        this._filterPanelService.canConfigureFilter = true;
        this._filterPanelService.canConfigureProperties = true;

        this._pluginService.toggleState(plugins.panels.filterPanel);

        if (this._filterPanelService.isPanelActive()) {
            //panel is opened, save where the panel was opened
            this.setFilterContext();
        }
        else {
            //panel is closed, discard the origin
            this._filterPanelService.setContext(null);
        }

        //close other empty associated panels
        this._filterPanelService.closeEmptyAssociatedPanel();
    }

    private _inputProperties: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem;
    private _returnProperties: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem;

    private setFilterContext(): void {
        const filterSmartFieldMethod = (this.composer.data as sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ISmartObjectField).method;
        const root = filterSmartFieldMethod.getRoot<sourceCode.k2Designer.objectModel.core.model.ModelBase>();

        const exRefMethod = root.findItem<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField>(filterSmartFieldMethod.externalReference);

        const def = (<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectReferenceDefinition>this.composer.data.externalReferenceDefinition);

        const returnsFilterCallBackData: IFilterCallbackData = {
            exRefMethod,
            filterSmartFieldMethod,
            returnInputProperties: false
        };

        //get returns properties for filter tab
        const returnPropertiesSmoItemConfiguration: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem =
            sourceCode.k2Designer.objectModel.workflow.itemProvider.SmartObjectItem.createInstance({
                parentName: def.systemName,
                sysName: exRefMethod.systemName,
                title: '',
                parentGroupings: [],
                groupings: [],
                returnInputProperties: returnsFilterCallBackData.returnInputProperties,
            });

        this._itemProviderService.getItemProvider(sourceCode.k2Designer.uiComponents.contextBrowser.plugins.SmoPlugin).loadDefinitionProperties(
            'api/contextplugins/getsmartobjectdefinitionproperties', <any>returnPropertiesSmoItemConfiguration,
            this.onDataLoaded.bind(this, returnsFilterCallBackData));

        //get input properties for properties tab
        const inputsFilterCallBackData: IFilterCallbackData = {
            exRefMethod,
            filterSmartFieldMethod,
            returnInputProperties: true
        };
        const inputPropertiesSmoItemConfiguration: sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem =
            sourceCode.k2Designer.objectModel.workflow.itemProvider.SmartObjectItem.createInstance({
                parentName: def.systemName,
                sysName: exRefMethod.systemName,
                title: '',
                parentGroupings: [],
                groupings: [],
                returnInputProperties: inputsFilterCallBackData.returnInputProperties,
            });

        this._itemProviderService.getItemProvider(sourceCode.k2Designer.uiComponents.contextBrowser.plugins.SmoPlugin).loadDefinitionProperties(
            'api/contextplugins/getsmartobjectdefinitionproperties', <any>inputPropertiesSmoItemConfiguration,
            this.onDataLoaded.bind(this, inputsFilterCallBackData));
    }

    /**
     * This function gets called from 2 ajax calls. (inputProperties and returnProperties).
     * When both properties are set then activates the filter panel.
     *
     * @param params - array of custom parameters: [externalReferenceDefinition, smartObjectFieldMethod, returnInputProperties]
     * @param callBackData - data obtained from the ajax call that contains SmartObject info
     */
    private onDataLoaded(filterCallbackData: IFilterCallbackData, callBackData: sourceCode.k2Designer.objectModel.core.itemProvider.IDataLoadedData) {
        const externalReferenceDefinition = filterCallbackData.exRefMethod;
        const method = filterCallbackData.filterSmartFieldMethod;
        const isInputProperties = filterCallbackData.returnInputProperties;

        if (isInputProperties) {
            this._inputProperties = <sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem>(<any>callBackData.sourceItem);
        }
        else {
            this._returnProperties = <sourceCode.k2Designer.objectModel.workflow.itemProvider.ISmartObjectItem>(<any>callBackData.sourceItem);
        }

        //when both ajax call is completed, then we can activate the filter panel
        if (this._inputProperties && this._returnProperties) {

            const filterContext = <sourceCode.k2Designer.services.studio.IFilterPanelContext>{
                field: externalReferenceDefinition,
                configuredListOptions: method.filter.configuredListOptions,
                filterConfig: method.filter.configuredFilter,
                savedUIValues: method.filter.savedUIValues,
                inputMappings: method.filter.inputMappings,
                smartField: this.composer.data,
                inputProperties: this._inputProperties,
                returnProperties: this._returnProperties
            };

            this._filterPanelService.setContext(filterContext);
        }
    }
}
