import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockDragDropService } from '../../testing/mock-services/drag-drop-service';
import { IDragDropService } from '../../types/services/drag-drop/i-drag-drop.service';

const legacyDragDropServiceFactory = () => environment.devHarness === true
    ? new MockDragDropService()
    : sourceCode.k2Designer.core.environmentService.dragDropService;

export const LegacyDragDropServiceToken = new InjectionToken<IDragDropService>('IDragDropService');

export const LegacyDragDropServiceFactory = {
    provide: LegacyDragDropServiceToken,
    useFactory: legacyDragDropServiceFactory,
    deps: []
};
