import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IRecentsService } from 'src/types/services/recents/i-recents-service';
import { MockRecentsService } from '../../../testing/mock-services/recents-service';


const recentsServiceFactory = () => environment.devHarness === true
    ? new MockRecentsService()
    : sourceCode.k2Designer.core.environmentService.getService
            <sourceCode.k2Designer.services.studio.RecentsService>(sourceCode.k2Designer.services.studio.RecentsService);

export const RecentsServiceToken = new InjectionToken<IRecentsService>('IRecentsService');

export const RecentsServiceFactory = {
    provide: RecentsServiceToken,
    useFactory: recentsServiceFactory,
    deps: [

    ]
};
