import { Component, Inject, Input, OnInit } from '@angular/core';
import { IdentityService } from 'src/app/services/identity/identity.service';
import { RecentsServiceToken } from 'src/app/services/recents/recents-service-factory';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { WorkflowContextBrowserItemTypes } from 'src/app/shared/enums/model/workflow/item-provider/workflow-context-browser-item-types';
import { PopupConfigStates } from 'src/app/shared/enums/popup/popup-config-states';
import { CommonContexts } from 'src/app/shared/enums/services/recents/common-contexts';
import { IRecentsService } from 'src/types/services/recents/i-recents-service';
import { IPopupConfiguration } from '../../../../types/models/popup/i-popup-configuration';
import { IUserDialogContext } from '../../../../types/models/popup/i-user-dialog-context';
import { PopupService } from '../../../services/popup/popup.service';
import { popup } from '../../../shared/constants/wizard-popup';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

@Component({
    selector: 'k2-user-dialog',
    templateUrl: './user-dialog.component.html',
    styleUrls: ['./user-dialog.component.less'],
    providers: [
        IdentityService
    ]
})
export class UserDialogComponent extends BaseResourceComponent implements OnInit {
    @Input() config: IPopupConfiguration;

    get context(): IUserDialogContext {
        if ((this.config?.context as any)?.umUserDialogSettings) { // legacy has a dialog settings in the context
            return (this.config.context as any).umUserDialogSettings;
        }
        return this.config?.context as IUserDialogContext;
    }

    public stateConfiguration: sourceCode.k2Designer.objectModel.popup.IPopupStateConfiguration;
    public breadCrumbItems: sourceCode.k2Designer.components.IBreadCrumbItem[];
    public selectedUmFilter: string;
    public searchText = '';
    public isSearching: boolean;
    public selectedUserData: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem;
    public isGroupOrRole: boolean;
    public umFilterDropdownItems: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem[] = [];
    public recentSelectedUsers: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem[];

    /**
     * This is the entire Controls Object Data
     */
    public umUserDialogObjectData: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem[];

    /*
     * Enums
     */
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public popupConfigStates: typeof PopupConfigStates = PopupConfigStates;

    get hasRecents(): boolean {
        return !!this.recentSelectedUsers?.length;
    }
    private _mapping: any = JSON.parse('{ \'displayName\': \'displayName\', \'email\': \'email\', \'type\': \'type\', \'systemName\' : \'systemName\', \'description\' : \'description\', \'label\' : \'label\' }'.replace(/'/g, '"'));
    private _umFilterOptions: string[] = [];

    constructor(
        private popupService: PopupService,
        @Inject(RecentsServiceToken) private recentsService: IRecentsService,
        protected resourceService: ResourceService,
        private identityService: IdentityService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.identityService
            .getLabels()
            .subscribe(this.onLabelsLoaded);

        this.breadCrumbItems = [];

        this.stateConfiguration = {
            message: '',
            state: PopupConfigStates.normal,
            actionButtons: [{
                key: 'btnRefresh',
                text: this.resourceService.getString('refresh'),
                onClick: this.refreshSearch.bind(this),
                visible: false
            } as sourceCode.k2Designer.objectModel.popup.IPopupButton]
        } as sourceCode.k2Designer.objectModel.popup.IPopupStateConfiguration;

        // get first 5
        this.recentSelectedUsers = this.getRecentUsers()?.slice(0, 5);
    }

    /**
     * When an item is selected
     *
     * @param item
     * @param newBreadCrumbItems
     */
    itemSelected(event: IBreadCrumbItemEvent) {
        this.breadCrumbItems = event.newCrumbs;
        if (!event.item) {
            this.breadCrumbItems.length = 0;
            this.getUsersForSelection(this.selectedUmFilter, this.searchText);
        }
    }

    /**
     * When the search string changes
     *
     * @param value
     */
    refreshSearch(value: string): void {
        this.searchText = value;
        if (this.searchText != null && this.searchText != undefined && this.searchText.length > 0) {
            this.isSearching = true;
            this.getUsersForSelection(this.selectedUmFilter, this.searchText);
        }
        else {
            this.isSearching = false;
            this.searchText = '';
            this.stateConfiguration.state = PopupConfigStates.normal;
            this.identityService.abortGetIdentities();
        }
    }

    /**
     * Double click selects a user and closes the popup
     *
     * @param selectedUser
     */
    closeOnDoubleClick(selectedUser: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem): void {
        this.selectUser(selectedUser);
        this.popupService.hide(popup.buttons.okResult, this.config);
    }

    /**
     * Handle user selection
     *
     * @param selectedUser
     */
    selectUser(selectedUser: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem): void {
        if (this.selectedUserData !== selectedUser) {
            this.selectedUserData = selectedUser;
            this.config.returnData = this.selectedUserData;
        }
        this.config.buttons.okButton.enabled = true;
    }

    /**
     * Get the applicable icon for the identity type
     *
     * @param item
     * @returns
     */
    getIcon(item: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem): string {
        switch (item.type) {
            case WorkflowContextBrowserItemTypes.identityRole:
                this.isGroupOrRole = true;
                return 'icon icon-size24 icon-set-steps ic-roles';
            case WorkflowContextBrowserItemTypes.identityGroup:
                this.isGroupOrRole = true;
                return 'icon icon-size24 icon-set-steps ic-group';
            case WorkflowContextBrowserItemTypes.identityUser:
                this.isGroupOrRole = false;
                return 'icon icon-size24 icon-set-steps ic-user';
            default:
                this.isGroupOrRole = false;
                return 'icon icon-size24 icon-set-steps ic-disableuser';
        }
    }

    /**
     * This is the call for the users
     */
    getUsersForGroup(label: string, identity: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem): void {
        this.breadCrumbItems = [];
        this.breadCrumbItems = <sourceCode.k2Designer.components.IBreadCrumbItem[]>
            [
                {
                    allowDrillDown: false,
                    title: identity.displayName,
                    key: identity.displayName,
                    isCrumbItem: true
                }
            ];

        this.identityService
            .getIdentitiesByGroup(label, identity.systemName)
            .subscribe((result: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem[]) => {
                if (result) {
                    this.umUserDialogObjectData = result;
                }
            });
    }

    /**
     * When the call to retrieve labels returns
     *
     * @param labels The result of request for users
     */
    private onLabelsLoaded = (labels: any): void => {

        this._umFilterOptions = labels.valueList;
        this.selectedUmFilter = labels.defaultLabel;

        if (this._umFilterOptions) {
            let itemIndex = -1;

            for (let i = 0; i < this._umFilterOptions.length; i++) {
                const isSelectedDefault: boolean = this.selectedUmFilter === this._umFilterOptions[i];

                // Item
                this.umFilterDropdownItems.push({
                    key: (itemIndex++).toString(),
                    title: this._umFilterOptions[i],
                    onSelected: this.onUmFilterSelected,
                    isSelected: isSelectedDefault
                } as sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem);

                // Separator
                this.umFilterDropdownItems.push({
                    key: (itemIndex++).toString(),
                    isSeparator: true
                } as sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem);
            }
        }

        this.umFilterDropdownItems.push({
            key: 'userManagers',
            icon: 'icon icon-set-buttons icon-size24 ic-help_button',
            isSeparator: false,
            title: this.resourceService.getString('umUser_popup_user_managers')
        });
    };

    private onUmFilterSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {
        this.selectedUmFilter = item.title;

        if (this.searchText.length > 0) {
            this.getUsersForSelection(this.selectedUmFilter, this.searchText);
        }
    };

    private getUsersForSelection(label: string, filter: string): void {
        this.umUserDialogObjectData = [];
        this.stateConfiguration.message = '';
        this.stateConfiguration.state = PopupConfigStates.loading;
        this.stateConfiguration.actionButtons[0].visible = false;

        this.identityService
            .getIdentities(label, filter, this.context.assetType)
            .subscribe({
                next: (result: any) => {
                    this.stateConfiguration.state = PopupConfigStates.normal;
                    this.umUserDialogObjectData = result;
                },
                error: (result: any) => {
                    const exception = <any>JSON.parse(result);
                    this.stateConfiguration.message = exception.ExceptionDetail ? exception.ExceptionDetail.Message : exception.Message;
                    this.stateConfiguration.state = PopupConfigStates.error;
                    this.stateConfiguration.actionButtons[0].visible = true;
                }
            });
    }

    private getRecentUsers(): sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem[] {
        if (this.recentsService.itemCount(CommonContexts.RecentlySelectedUsers) > 0) {
            let items = [];
            items = items
                .concat(this.recentsService
                    .getItems(CommonContexts.RecentlySelectedUsers)
                    .map((item: any) => {
                        let result: sourceCode.k2Designer.objectModel.workflow.itemProvider.IUserItem;

                        // This whole mapping thing seems questionable...
                        if (this._mapping) {
                            const userObject = {} as any;

                            for (const p in this._mapping) {
                                userObject[p] = item[this._mapping[p]];
                            }

                            for (let typeCount = 0; typeCount < this.context.assetType.length; typeCount++) {
                                if (this.getAssetTypeID(this.context.assetType[typeCount]) === userObject.type) {
                                    result = userObject as any;
                                    break;
                                }
                            }
                        } else {
                            result = <any>item;
                        }
                        return result;
                    }))
                .filter(x => !!x);
            return items;
        }
    }

    private getAssetTypeID(assetType: string): number {
        switch (assetType) {
            case 'Roles':
                return WorkflowContextBrowserItemTypes.identityRole;
            case 'Groups':
                return WorkflowContextBrowserItemTypes.identityGroup;
            case 'Users':
                return WorkflowContextBrowserItemTypes.identityUser;
            default:
                return WorkflowContextBrowserItemTypes.identityUser;
        }
    }
}
