import { Component, ElementRef, Inject, Input, OnInit, QueryList, Renderer2, ViewChildren } from '@angular/core';
import { Subscription } from 'rxjs';
import { CommandServiceToken } from 'src/app/services/command/command-service-factory';
import { ConfigPanelServiceToken } from 'src/app/services/config-panel/config-panel-service-factory';
import { EventNotifierService } from 'src/app/services/event-notifier/event-notifier.service';
import { ResourceService } from 'src/app/services/resource/resource.service';
import { RulePluginService } from 'src/app/services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from 'src/app/services/selection/selection-service-factory';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { rules } from 'src/app/shared/constants/rules';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from 'src/app/shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { RuleControlStates } from 'src/app/shared/enums/rules/rule-control-states';
import { VotingLeftOperandTypes } from 'src/app/shared/enums/rules/voting-left-operand-types';
import IVotingRuleExpressionConfiguration from 'src/types/models/rules/i-voting-rule-expression-configuration';
import { IConfigPanelService } from 'src/types/services/config-panel/i-config-panel-service';
import { ISelectionService } from 'src/types/services/selection/i-selection-service';
import { TextboxComponent } from '../../../common/text-box/text-box.component';
import { ExpressionBaseComponent } from '../../base/expression-base/expression-base.component';
import { IConfiguredThisResponseExpression } from '../../../../../types/models/rules/i-configured-this-response-expression';
import { DropDownComponent } from 'src/app/components/common/drop-down/drop-down.component';

@Component({
    selector: 'k2-this-response-expression',
    templateUrl: './this-response-expression.component.html',
    styleUrls: ['./this-response-expression.component.less']
})
export class ThisResponseExpressionComponent extends ExpressionBaseComponent implements OnInit {

    // Used to get the textbox
    @ViewChildren('txtNumberOfResponses') textBoxes: QueryList<TextboxComponent>;
    private txtNumberOfResponses: TextboxComponent;

    @Input() expression: IConfiguredThisResponseExpression;

    @Input() index: number;

    selectedAction: sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfigurationAction;
    typePickerOperators: IDropDownMenuItem[] = [];
    typePickerTypes: IDropDownMenuItem[] = [];
    logicalOperators: IDropDownMenuItem[] = [];
    actionItems: IDropDownMenuItem[] = [];
    rulesIcons = rules.icons;

    get isConditionSelected(): boolean {
        return this.states.isExist(RuleControlStates.selected);
    };

    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    votingOperandTypes: typeof VotingLeftOperandTypes = VotingLeftOperandTypes;

    // We need a separate template and message, as the text is fetched asynchronously. We can't just reference the observable from the template, as it is consumed by the dropdown and it doesn't like it.
    getEmptyDropdownTemplate(): string {
        return `<span class="icon icon-set-picker icon-size24 ic-thisresponse"></span><div class="empty-state-message">${this._emptyDropdownMessage}</div>`;
    };

    private _outsideClickListenerRemover: any;
    private _configuredActions: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfigurationAction>;
    private _emptyDropdownMessage = '';
    private _getEmptyDDSubscription: Subscription;
    private _expressionConfiguration: IVotingRuleExpressionConfiguration;

    constructor(@Inject(SelectionServiceToken) selService: ISelectionService,
        rulePluginService: RulePluginService,
        eventService: EventNotifierService,
        rootElement: ElementRef<HTMLElement>,
        resourceService: ResourceService,
        private _renderer: Renderer2,
        @Inject(CommandServiceToken) commandService: ICommandService,
        @Inject(ConfigPanelServiceToken) configPanelService: IConfigPanelService,
        @Inject(UndoRedoServiceToken) undoRedoService: IUndoRedoService,
    ) {
        super(selService, rulePluginService, commandService, configPanelService, undoRedoService, eventService, rootElement, resourceService);

        // TODO: Test that the empty dropdown thing still works for this response.
    }

    ngOnInit(): void {

        super.ngOnInit();

        this.undoRedoService.forceDisable();

        this.expressionModel = this.expression as IConfiguredThisResponseExpression;
        this._expressionConfiguration = this.rulePluginService.getExpressionConfiguration(this.expression) as IVotingRuleExpressionConfiguration;

        this._configuredActions = this.rulePluginService
            .contextModel
            .getParentModel<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.IClientEventConfiguration>()
            .actions;

        this.setInitialValues();

        this.populateTypePickerOperationDropDown(this._expressionConfiguration);
        this.populateTypePickerTypesDropDown(this._expressionConfiguration);
        this.populateLogicalOperatorDropDown(this._expressionConfiguration);
        this.populateActionsDropDown(this._expressionConfiguration);

        this.undoRedoService.resetForceDisable();
    }

    ngAfterViewInit(): void {

        // The textbox won't render when this control loads, so we have to create a query, and subscribe to result changes
        // Then we can get the first one to focus on when it is rendered.
        this.textBoxes.changes.subscribe((components: QueryList<TextboxComponent>) => {
            this.txtNumberOfResponses = components.first;
        });

        // Handle deselecting the condition
        this._outsideClickListenerRemover = this._renderer.listen('document', 'mouseup', (evt) => {
            if (this.rootElement.nativeElement !== evt?.target) {
                this.setSelected(false);
            }
        });
    }

    ngOnDestroy(): void {
        typeof this._outsideClickListenerRemover === 'function' && this._outsideClickListenerRemover();

        this._getEmptyDDSubscription?.unsubscribe();

        super.ngOnDestroy();
    }

    getDragImageFn = super.getDragImage.bind(this);

    /**
     * Generic click handler
     *
     * @param evt
     */
    onClick(evt) {
        this.setSelected(true);
        evt.stopPropagation();
    }

    addDraggingState = super.addDraggingState;

    removeDraggingState = super.removeDraggingState;

    /**
     * Set default or existing values for the control
     */
    setInitialValues() {
        if (!this.expression.leftOperandType) {
            this.expression.leftOperandType = VotingLeftOperandTypes.everyResponse.toString();
        }

        if (!this.expression.specialOperator) {
            this.expression.specialOperator = rules.operators.equals;
        }

        if (!this.expression.logicalOperator) {
            this.expression.logicalOperator = rules.operators.equals;
        }

        this.expression.specialOperatorIcon = this._expressionConfiguration
            .specialOperators
            .find(s => s.name === this.expression.specialOperator)
            ?.icon;

        this.expression.logicalOperatorIcon = this._expressionConfiguration
            .operators
            .find(o => o.name === this.expression.logicalOperator)
            ?.icon;

        if (!!this.expression.taskActionTitle) {
            this.selectedAction = this._configuredActions?.find(a => a.actionTitle === this.expression.taskActionTitle);
        } else {
            this.expression.taskActionTitle = this._configuredActions[0]?.actionTitle;
            this.selectedAction = this._configuredActions[0]; // if nothing is configured, setting it to undefined is correct.
        }
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    isHovering(controlName: string) {
        return this.states.isExist(`${controlName}-hover`);
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    hoverOn(controlName: string) {
        this.states.add(`${controlName}-hover`);
    }

    /**
     * Handles state for hovering
     *
     * @param controlName
     * @returns
     */
    hoverOff(controlName: string) {
        this.states.remove(`${controlName}-hover`);
    }

    /**
     * Handler for when a type picker type is selected.
     *
     * @param item
     */
    onTypePickerTypeSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {

        this.expression.leftOperandType = item.key;
        this.expression.leftOperandTypeIcon = item.icon;

        if (item.key === 'numberOfResponses') {
            setTimeout(() => {
                this.txtNumberOfResponses?.doFocus();
            }, 100);
        }
    };

    /**
     * Handler for the Type Picker Operator
     *
     * @param item
     */
    onTypePickerOperatorSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {
        this.expression.specialOperator = item.key;
        this.expression.specialOperatorIcon = item.key === 'equals' ? 'icon icon-set-conditionsdropdown icon-size16 ic-popoverarrow selected' : item.icon;
    };

    /**
     * Handler for the Logical Operator
     *
     * @param item
     */
    onLogicalOperatorSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {

        this.expression.logicalOperator = item.key;
        this.expression.logicalOperatorIcon = item.icon;
    };

    /**
     * Handler for the Action
     *
     * @param item
     */
    onActionSelected = (item: sourceCode.k2Designer.objectModel.dropDownMenu.IDropDowntMenuItem) => {
        this.expression.taskActionReference = item.key;
        this.expression.taskActionTitle = item.title;
    };

    private populateTypePickerOperationDropDown(configuration: IVotingRuleExpressionConfiguration) {
        this.populateLogicalOperators(configuration.specialOperators, this.typePickerOperators, this.expression.specialOperator);
        this.populateOperatorCommands(configuration.commands, this.typePickerOperators);
    }

    private populateTypePickerTypesDropDown(configuration: IVotingRuleExpressionConfiguration) {
        this.populateLogicalOperators(configuration.leftOperandType, this.typePickerTypes, this.expression.leftOperandType);
    }

    private populateLogicalOperatorDropDown(configuration: IVotingRuleExpressionConfiguration) {
        this.populateLogicalOperators(configuration.operators, this.logicalOperators, this.expression.logicalOperator);
        this.populateOperatorCommands(configuration.commands, this.logicalOperators);
    }

    private populateActionsDropDown(configuration: IVotingRuleExpressionConfiguration) {

        if (!this.selectedAction && !!this.expression.taskActionTitle) {
            this.actionItems.push({
                title: this.expression.taskActionTitle,
                key: '',
                innerItemComponent: DropdownMenuItemTemplates.Disabled,
                isSelected: true
            });
        }

        this._configuredActions?.forEach(a => {
            this.actionItems.length > 0 && this.actionItems.push(DropDownComponent.separator);

            this.actionItems.push({
                key: a.getReference(),
                title: a.actionTitle,
                innerItemComponent: DropdownMenuItemTemplates.SimpleExpressionOperator,
                isSelected: a === this.selectedAction // It should always be the same instance.
            });
        });
    }

}
