import { Directive, ElementRef, Injector, Input } from '@angular/core';
import { UpgradeComponent } from '@angular/upgrade/static';

@Directive({
    selector: 'k2-help-link'
    //Extending old upgraded directive (directives/k2HelpLink/k2HelpLink)
})
export class HelpLinkComponent extends UpgradeComponent {

    @Input() helpLinkText: string;
    @Input() k2ContextId: string;
    @Input() k2ExactLocation: string;
    @Input() helpLinkUrl: string;

    constructor(elementRef: ElementRef, injector: Injector) {
        super('k2HelpLink', elementRef, injector);
    }

}
