import { Inject, Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { share } from 'rxjs/operators';
import { LegacyResourceServiceToken } from 'src/app/legacy-service-factories/resource-service-factory';
import { IDataService } from 'src/types/services/data/i-data-service';
import { ILegacyResourceService } from 'src/types/services/resources/i-legacy-resource.service';
import { DataServiceToken } from '../data/data-service-factory';

@Injectable({
    providedIn: 'root'
})
export class ResourceService {

    private _resources: {
        [resourceName: string]: {
            [key: string]: string;
        };
    } = {};

    private _resourceSubjects: {
        [resourceName: string]: {
            [key: string]: BehaviorSubject<string>;
        };
    } = {};

    private _loadResxFileObservables: {
        [resourceName: string]: Observable<{ [key: string]: string }>;
    } = {};

    constructor(@Inject(LegacyResourceServiceToken) private _legacyResourceService: ILegacyResourceService,
        @Inject(DataServiceToken) private _dataService: IDataService) {
    }

    getString = (key: string) => this._legacyResourceService.getString(key);

    /**
     * Resolves a string based on the token provided
     *
     * @param resourceStr
     * @param resourceTokens
     * @param modelObj
     */
    resolveTokenResourceString = (resourceStr: string,
        resourceTokens: sourceCode.k2Designer.objectModel.core.model.IModelArray<string>,
        modelObj: sourceCode.k2Designer.objectModel.core.model.IModelObject
    ) => this._legacyResourceService.resolveTokenResourceString(resourceStr, resourceTokens, modelObj);

    /**
     *
     * @param resourceName The resx file you want to load
     * @param key The key in the file for which you want the value
     * @returns
     */
    getFromResx(resourceName: string, key: string): BehaviorSubject<string> {

        this._resourceSubjects[resourceName] ??= {
            key: null
        };

        const resSubj = this._resourceSubjects[resourceName][key] ??= new BehaviorSubject(null);

        if (this._resources[resourceName]) {
            resSubj.next(this._resources[resourceName][key]);
        } else {

            // We will just load it from the legacy resource service. It handles caching the data if the fileHash matches
            const subscription = this.loadResxFile(resourceName).subscribe((resource) => {

                this._resources[resourceName] = resource;

                if (!this._resources[resourceName]) {

                    console.error(`Resource with name ${resourceName} not found`);
                    resSubj.next(`${resourceName}_${key}`);

                } else if (!this._resources[resourceName][key]) {

                    console.error(`Resource with Name ${resourceName} and Key ${key} not found`);
                    resSubj.next(`${resourceName}_${key}`);
                } else {
                    resSubj.next(this._resources[resourceName][key]);
                }
                subscription.unsubscribe();
            });
        }

        return this._resourceSubjects[resourceName][key];
    }

    private loadResxFile(resourceName: string): Observable<{ [key: string]: string }> {

        this._loadResxFileObservables[resourceName] ??= new Observable<{ [key: string]: string }>(subscriber => {

            this._dataService.httpGet('api/core/getresxfile', { resourceName }, resource => {
                subscriber.next(resource);
            });

        })
            .pipe(share());

        return this._loadResxFileObservables[resourceName];
    };

}
