import { Input } from '@angular/core';
import { Component, Inject, OnInit } from '@angular/core';
import { forEach } from 'angular';
import { IConfigPanelTab } from '../../../../../../types/models/config-panel/i-config-panel-tab';
import { IActivity } from '../../../../../../types/models/process/activities/i-activity';
import { ICanvasService } from '../../../../../../types/services/canvas/i-canvas.service';
import { IValidationService } from '../../../../../../types/services/validation/ivalidation.service';
import { CanvasServiceToken } from '../../../../../services/canvas/canvas-service-factory';
import { ProcessService } from '../../../../../services/process/process.service';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { ValidationServiceToken } from '../../../../../services/validation/validation-service-factory';
import { ActivityPriorityTypes } from '../../../../../shared/enums/activities/activity-priority-types';
import { StepTemplates } from '../../../../../shared/enums/activities/step-template-names';
import { ChooseControlTypes } from '../../../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../../../shared/enums/drop-down/drop-down-triangle-position';
import { containsSpecialCharacters } from '../../../../../shared/utils/name-helper';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-activity-settings',
    templateUrl: './activity-settings.component.html',
    styleUrls: ['./activity-settings.component.less']
})
export class ActivitySettingsComponent extends BaseResourceComponent implements OnInit {
    @Input() activity: IActivity;
    @Input() items: IConfigPanelTab[];

    public wizardName: string;
    public wizardIcon: string;
    public wizardDescription: string;
    public priorityItems: IDropDownMenuItem[];
    public selectedItem: IDropDownMenuItem;
    public mustShowPriority: boolean;

    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    dropDownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;

    private wizardConfiguration: sourceCode.k2Designer.objectModel.workflow.smartWizards.ISmartWizardDefinition;

    constructor(protected resourceService: ResourceService,
        @Inject(CanvasServiceToken) private canvasService: ICanvasService,
        protected processService: ProcessService,
        @Inject(ValidationServiceToken) private validationService: IValidationService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.priorityItems = [];

        //Priority dropdown
        this.priorityItems.push({
            key: ActivityPriorityTypes.low.toString(),
            icon: 'icon-set-dropdownset ic-priority_low',
            isSeparator: false,
            title: this.resourceService.getString('priority_low'),
            isSelected: this.activity.configuration.priority === ActivityPriorityTypes.low
        } as IDropDownMenuItem);

        this.priorityItems.push({
            key: ActivityPriorityTypes.med.toString(),
            icon: 'icon-set-dropdownset ic-priority_medium',
            isSeparator: false,
            title: this.resourceService.getString('priority_med'),
            isSelected: this.activity.configuration.priority === ActivityPriorityTypes.med
        } as IDropDownMenuItem);

        this.priorityItems.push({
            key: ActivityPriorityTypes.high.toString(),
            icon: 'icon-set-dropdownset ic-priority_high',
            isSeparator: false,
            title: this.resourceService.getString('priority_high'),
            isSelected: this.activity.configuration.priority === ActivityPriorityTypes.high
        } as IDropDownMenuItem);

        for (const priorityItem of this.priorityItems) {
            if (priorityItem.isSelected) {
                this.selectedItem = priorityItem;
            }
        }

        if (this.activity?.children?.length <= 1) {
            if (this.activity.ui.template.toLowerCase() !== StepTemplates.PlaceHolderStep.toLocaleLowerCase()) {
                const wizard: IConfigPanelTab = this.items[0];

                this.wizardName = this.processService.getActivityDefaultNameFromActivity(this.activity);
                this.wizardIcon = wizard.spriteClass;
                this.wizardDescription = this.getDescriptionOf(wizard);
                this.wizardConfiguration = (wizard.objectModel as any).wizardConfiguration;
            }
            else {
                this.wizardName = this.resourceService.getString('workflow_step_placeholder');
                this.wizardIcon = 'ic-placeholderevent';
                this.wizardDescription = this.resourceService.getString('wizarddescription_placeholder_step');
            }
        }

        this.mustShowPriority = false;

        for (const child of this.activity.children) {
            if (child?.instanceOf(sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.client.ClientEvent.implementor)) {
                this.mustShowPriority = true;
                break;
            }
        }
    }

    getSvgIcon(): string {
        if (this.wizardConfiguration &&
            this.canvasService.getIcons()[this.wizardConfiguration.icon]) {
            return this.wizardConfiguration.icon;
        } else {
            return 'fallbackIcon';
        }
    }

    resetTitle(): void {
        this.activity.customTitle = false;
        if (this.activity.children?.length > 0 && this.activity.children[0].wizardId > 0) {
            this.activity.title = this.processService.getActivityDefaultName(this.activity.children[0].wizardId, null);
            //Sets the Event Title same as Activity Title
            if (this.activity.children.length === 1) {
                this.activity.children[0].title = this.activity.title;
            }
        } else {
            this.activity.title = this.processService.getActivityDefaultName(null, this.activity.ui.template);
        }
    }

    validateTitle(): void {
        if (!this.activity.title || this.activity.title === '') {
            this.resetTitle();
        }
    }

    validateAndSetTitle(newValue: string): void {
        //if title is empty, keep it empty until the user focus out
        if (newValue === '') {
            this.activity.title = newValue;
            this.activity.customTitle = true;
        } else if (this.activity.title !== newValue) {
            this.activity.ui.showLabel = true;

            if (!containsSpecialCharacters(newValue, this.validationService)) {
                this.activity.title = newValue;
                this.activity.customTitle = true;
                //Ensure that event child's title equal to activity's title
                sourceCode.k2Designer.objectModel.workflow.process.k2Process.ensureTitle(this.activity as any);
            }
        }
    }

    checkForSpecialCharacters(keyBoardEvent: KeyboardEvent): void {
        if (containsSpecialCharacters(keyBoardEvent.key, this.validationService)) {
            keyBoardEvent.preventDefault();
        }
    }

    showLabelChanged(): void {
        this.activity.ui.showLabel = !this.activity.ui.showLabel;
    }

    onDescriptionChanged(text: string): void {
        this.activity.description = text;
    }

    itemSelected(item: IDropDownMenuItem): void {
        this.selectedItem = item;

        this.activity.configuration.priority = Number(item.key);

        for (const priorityItem of this.priorityItems) {
            if (priorityItem.title !== item.title) {
                priorityItem.isSelected = false;
            }
        }
    }

    onNotesChanged(notes: string): void {
        this.activity.notes = notes;
    }

    private getDescriptionOf(wizard: IConfigPanelTab): string {
        let description = '';

        if (wizard.descriptionToken !== '') {
            description = this.resourceService.getString(wizard.descriptionToken);
            if (description === wizard.descriptionToken) {
                description = '';
            }
        }

        return description;
    }
}
