import { Injectable } from '@angular/core';

/**
 * NOTE: This is a service class with stub methods currently only used in the dev environment
 * (implementation of the interface is determined by the factory),
 * but the stubs will be replaced with true implementation when services are ported.
 * */

@Injectable({
    providedIn: 'root'
})
export class MockClipboardService implements IClipboardService {
    constructor() { }
    private clipboard: any[] = [];

    /**
     * Gets the appropriate source that contains clipboardData.  This is needed because IE11 has clipboardData on window instead of event
     *
     * @param event the event to determine the source from
     */
    public getClipboardSource(event: Event): any {
        return (<any>event).clipboardData ? event : window;
    }

    public setCurrentContext = (clipboardContext: string) => {

    };
    public hasData = () => this.clipboard.length > 0;
    clipboardEnabled = true;
    canCopy(): boolean { return true; };
    canCut(): boolean { return true; };
    canDelete(): boolean { return true; };
    canPaste(): boolean { return true; };
    copy: (options?: any) => boolean;
    cut: (options?: any) => boolean;
    delete: (options?: any) => boolean;
    paste: (options?: any) => boolean;
    listen: (operation: sourceCode.k2Designer.objectModel.clipboard.ClipboardOperations, callback: sourceCode.k2Designer.objectModel.clipboard.IClipboardOperationCompletedCallback<any>, clipboardContext?: string) => void;
    public setClipboard = (data: any[], raw: string, event?: Event) => {
        this.clipboard = data;
    };
    public getClipboard = () => this.clipboard;
    getSystemClipboard = (event: Event, asRichText: boolean): Element => {
        let results: Element = null;
        const clipboardSource: any = this.getClipboardSource(event);
        let clipboardContents: string = null;

        if (asRichText) {
            // External programs may have style elements that affect body/div/p elements etc
            // However, it may need to be sanitized for security reasons

            try {
                clipboardContents = clipboardSource.clipboardData.getData('text/html');
                if (!clipboardContents) {
                    const getDataSourceType: string = true ? 'text/plain' : 'Text';
                    clipboardContents = clipboardSource.clipboardData.getData(getDataSourceType);
                }

                if (/class="?Mso|style="[^"]*\bmso-|style='[^'']*\bmso-|w:WordDocument/i.test(clipboardContents)) {
                    // Office content detected
                    //clipboardContents = this.cleanMsOfficeContent(clipboardContents);
                }

            }
            catch (e) {
                // Some error has occured, leaving it to the browser's native clipboard handling instead
                return null;
            }
        }
        else {
            //IE and Edge support "Text" in getData, everybody else does text/plain.
            const getDataSourceType: string = true ? 'text/plain' : 'Text';
            clipboardContents = clipboardSource.clipboardData.getData(getDataSourceType);
        }

        if (clipboardContents !== null && clipboardContents.length > 0) {
            // Sanitize the clipboard content
            //clipboardContents = RichEditPanelService.sanitizeHtml(clipboardContents);

            results = <Element>$.parseHTML('<root>' + (asRichText ? clipboardContents : '') + '</root>')[0];

            if (!asRichText) {
                results.appendChild(document.createTextNode(clipboardContents));
            }
        }

        return results;
    };
    clearClipboard: () => void;

}
