import { Component, EventEmitter, HostListener, Inject, Input, OnChanges, OnDestroy, OnInit, Output } from '@angular/core';
import { Subscription } from 'rxjs';
import { IPropertyMapperDataFieldChangedEvent } from '../../../../types/events/property-mapper/i-data-field-changed-event';
import { IModelDictionary } from '../../../../types/models/base/i-model-dictionary';
import { IExpression } from '../../../../types/models/core/configured-smart-fields/i-expression';
import { ISmartObjectMethodField } from '../../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-method-field';
import { ISmartObjectPropertyField } from '../../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-property-field';
import { IProcess } from '../../../../types/models/process/i-process';
import { IValidationService } from '../../../../types/services/validation/ivalidation.service';
import { IPropertyMapperHeading } from '../../../../types/uiComponents/property-mapper/i-property-mapper-heading';
import { IPropertyMapperRow } from '../../../../types/uiComponents/property-mapper/i-property-mapper-row';
import { IPropertyMapperRowValueCollection } from '../../../../types/uiComponents/property-mapper/i-property-mapper-row-value-collection';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import { IValidationTypeItem } from '../../../../types/validation/i-validation-type-item';
import { IValidatorItem } from '../../../../types/validation/i-validator-item';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { ExternalReferenceService } from '../../../services/external-reference/external-reference.service';
import { ProcessService } from '../../../services/process/process.service';
import { ResourceService } from '../../../services/resource/resource.service';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../services/validation/validation-service-factory';
import { ComponentIdMapping } from '../../../shared/constants/components';
import { dataTypes } from '../../../shared/constants/data-types';
import { PropertyMapperDataFieldChangedEvent } from '../../../shared/constants/events';
import { globalVariableNames } from '../../../shared/constants/general';
import { DropDownMenuAlignment } from '../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';
import { DropDownMenuTrianglePosition } from '../../../shared/enums/drop-down/drop-down-triangle-position';
import { ExternalReferenceState } from '../../../shared/enums/external-references/external-references';
import { ErrorSeverity } from '../../../shared/enums/model/error-severity';
import { EventTypes } from '../../../shared/enums/model/event-types';
import { ValidationTypes } from '../../../shared/enums/model/validation-types';
import { DisplayModes } from '../../../shared/enums/property-mapper/display-modes';
import { ReferenceTypes } from '../../../shared/enums/references/reference-types';
import { ExternalReferenceUtilities } from '../../../shared/utils/external-references';
import { isUndefinedOrNull } from '../../../shared/utils/general';
import { BaseResourceComponent } from '../../base-resource/base-resource.component';

type ITextField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.ITextField;

@Component({
    //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
    //Tag will remain k2-property-mapper-v2 until old propertymapper completely deprecated.
    selector: 'k2-property-mapper-v2',
    templateUrl: './property-mapper.component.html',
    styleUrls: ['./property-mapper.component.less']
})
export class PropertyMapperComponent extends BaseResourceComponent implements OnInit, OnChanges, OnDestroy {

    /**
     * Property that can be set on the property mapper to show that there is multiple mappings (used in call subw workflow - multiple)
     */
    @Input() shouldShowRepeatingHeader = false;

    /*
     *  The property mapper is being hosted inside a popup. When set to true, necessary css styles will be applied
     */
    @Input() isInsidePopup = false;

    /**
     * Used to describe the icon and heading that will display on the property mapper
     */
    @Input() headingData: IPropertyMapperHeading;

    /*
    * Property that indicate what type of data is used in property mapper (ex workflow, smo)
    */
    @Input() displayMode: DisplayModes;

    //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
    /**
     * This is a list of the PropertyMapper Rows that represent the groupings that will display in the Property Mapper Component
     */
    @Input() groupings: IPropertyMapperRow[];

    /*
    *   Flag to indicate when the data obtained from ajax call is applied to the property mapper
    */
    @Input() isDataLoaded = true;

    /**
     * Collection of rows that will be saved and persisted
     */
    @Input() savedProperties: IModelDictionary<IPropertyMapperRowValueCollection>;

    /**
     * This is a list of the PropertyMapperRows that represents the rows that can be displayed in the Property Mapper Control. This is the base of the
     * dropdowns and the altered properties are pushed into the selected properties array and saved properties collection
     */
    @Input() availableProperties: IPropertyMapperRow[];

    /**
     * Method that will return the items for the right-hand mapping dropdown in the property mapper row
     */
    @Input() getValueItems: (isDataFieldRow?: boolean) => IDropDownMenuItem[];

    /**
     * The footer items for the right-hand mapping dropdown in the property mapper row (datafields)
     */
    @Input() valueFooterItems: IDropDownMenuItem[];

    //TODO: This input is only set by SmartWizards property mapper (Feature 936055: [Angular Migration] - SmartWizards)
    /**
     * External definition parent that will save all the properties provided
     */
    @Input() externalDefinitionParent: ISmartObjectMethodField;

    /**
     * Bind to list item control clicked event (selectListItem)
     */
    @Output() mapperRowSelected: EventEmitter<IPropertyMapperRow> = new EventEmitter<IPropertyMapperRow>();

    /**
     * Variables to expose enums
     */
    displayModes: typeof DisplayModes = DisplayModes;
    dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;

    /**
     * Key of the selected row
     */
    selectedRowKey: string;

    /**
     * Boolean value that evaluates if the row Dropdown has focus
     */
    isRowDropdownFocused: boolean;

    /**
     * Boolean value that evaluates if the row Dropdown has focus
     */
    isRowValueControlFocused: boolean;

    /**
     * This is a list of the PropertyMapperRows that represent the rows that is in an altered state and displays in the property mapper
     */
    selectedProperties: IPropertyMapperRow[];

    /**
     * To determine class on hover
     */
    isTrashCanHovered: boolean;

    /**
     * This is the collection of items used in the left dropdown of the property mapper row
     */
    propertyMapperLeftItems: {
        [key: string]: {
            items: IDropDownMenuItem[];
        };
    } = {};

    /**
     * This is the collection of items used in the right dropdown of the property mapper row,
     * populated by the getValueItems input function
     * Items and footer items are generated depending if this dropdown displays datafields or not
     * and depending on the row key, for performance purposes, a dictionary instead of a function is used for items
     */
    propertyMapperRightItems: {
        [key: string]: {
            items: IDropDownMenuItem[];
            footerItems: IDropDownMenuItem[];
            selectedItemTitle: string;
            selectedIcon: string;
        };
    } = {};

    /**
     * The available items to add/remove from the property mapper rows (used in multiselect at the footer of the property mapper)
     */
    addRemoveRowItems: IDropDownMenuItem[] = [];

    //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
    /**
     * This is the array that the grouping / content-type dropdown in property mapper heading uses
     */
    groupingItems: IDropDownMenuItem[] = [];

    /**
     * Indicates if the datafield dropdown must show
     */
    shouldMapToDataFields = false;

    /**
     * Currently selected row
     */
    selectedRow: IPropertyMapperRow;

    /*
    *  Collection of property keys and validator items
    */
    private validatorItems: { [key: string]: IValidatorItem } = {};

    /**
     * To remove listener that listens for datafields updated
     */
    private dataFieldEventRemover: sourceCode.k2Designer.objectModel.core.model.IEventRemover = null;

    /**
     * To listen for datafield changed event being emited from legacy parent
     */
    private dataFieldChangedSubscription: Subscription;


    constructor(public resourceService: ResourceService,
        private externalReferenceService: ExternalReferenceService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        @Inject(ValidationServiceToken) private validationService: IValidationService,
        private processService: ProcessService,
        private eventNotifierService: EventNotifierService) {
        super(resourceService);
    }

    ngOnInit(): void {
        if (typeof this.getValueItems === 'function') {
            this.setPropertyMapperRightItems();
        }

        this.selectedProperties = [];

        this.dataFieldChangedSubscription = this.eventNotifierService.subscribe(PropertyMapperDataFieldChangedEvent, this.dataFieldChanged.bind(this));
    }

    ngOnChanges(changes) {
        if (changes.availableProperties || changes.savedProperties) {
            this.onAvailablePropertiesChanged();
        }
    }

    ngOnDestroy(): void {
        this.dataFieldEventRemover();
        this.dataFieldChangedSubscription?.unsubscribe();
    }

    onMapperRowSelected(row: IPropertyMapperRow): void {
        this.selectedRowKey = row.key;
        this.selectedRow = row;
        this.mapperRowSelected.emit(row);
    }

    //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
    /*
     * Key of the selected content type
     */
    get selectedGroupingKey(): string {
        const selectedItem = this.groupingItems.find(g => g.isSelected);
        return selectedItem ? selectedItem.key : '';
    }

    /**
     * Title of the selected content type
     */
    get selectedGroupingTitle(): string {
        const selectedItem = this.groupingItems.find(g => g.isSelected);
        return selectedItem ? selectedItem.title : '';
    }

    groupingSelected(item: IDropDownMenuItem): void {
        //TODO: Groupings not yet implemented
    }

    /**
     * Executes when the left-side dropdown list item is selected from the single dropdown in the Property Mapper Row.
     * The selected item's altered state is set to true and the Property Mapper Row item's altered state is set to false and its value is cleared
     */
    leftItemSelected(item: IDropDownMenuItem, oldKey: string): void {
        this.undoRedoService.addUndoMarker('--k2PropertyMapper: single dropdown changing property--');
        const key = item.key;

        const newProperty = this.availableProperties.find(a => a.key === item.key);
        if (newProperty) {
            this.addProperty(newProperty);
        }

        const oldSelectedProperty = this.selectedProperties.find(p => p.key === oldKey);
        if (oldSelectedProperty) {
            this.removeProperty(oldSelectedProperty);
        }

        this.setPropertyMapperLeftItems();
        this.setPropertyMapperRightItems();
        this.selectedRowKey = key;

    }

    /**
     * Right-hand dropdown item selected (datafield or item reference)
     */
    rightItemSelected(item: IDropDownMenuItem, selectedRowKey: string): void {
        if (!item.key) {
            return;
        }

        this.undoRedoService.addUndoMarker('--k2PropertyMapper: item selected--');

        //Left-side key
        this.selectedRowKey = selectedRowKey;
        //Right-side key
        const referenceKey = item.key;

        let selectedRowValue: IPropertyMapperRowValueCollection = null;
        const selectedProperty = this.selectedProperties.find(p => p.key === selectedRowKey);
        const savedProperty: IPropertyMapperRowValueCollection = this.savedProperties[this.selectedRowKey];

        if (this.shouldMapToDataFields || selectedProperty?.mapToDataFields) {
            const tokenReference = savedProperty?.tokenReference || referenceKey;

            if (!this.shouldMapToDataFields) {
                //Row level

                let value: IExpression = null;

                if (referenceKey === globalVariableNames.activityInstanceDestInstanceData) {
                    value = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                        smartFields:
                            [
                                sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.createInstance({
                                    fieldName: globalVariableNames.activityInstanceDestInstanceData,
                                    title: this.resourceService.getString('instance_data'),
                                    customTitle: this.resourceService.getString('list_item')
                                })
                            ],
                        isDynamic: savedProperty.value?.isDynamic
                    });

                    selectedRowValue =
                        sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.smartWizard.PropertyMapperRowValueCollection.createInstance({
                            value,
                            tokenReference: item.additionalValueProperty,
                            rowTitleForViewModel: selectedProperty?.rowTitle
                        });

                    selectedRowValue.value.isDynamic = false;
                }
                else {
                    selectedRowValue = this.createDataFieldMapperRow(referenceKey, tokenReference, item.title, savedProperty?.value?.isDynamic);
                }
            }
            else {
                selectedRowValue = this.createDataFieldMapperRow(referenceKey, item.title, tokenReference);
            }
        } else {

            selectedRowValue =
                sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.smartWizard.PropertyMapperRowValueCollection.createInstance({
                    value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                        type: ReferenceTypes.ItemReference,
                        smartFields:
                            [
                                sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                                    text: referenceKey,
                                    customTitle: item.title,
                                })
                            ]
                    }),
                    tokenReference: item.additionalValueProperty,
                    rowTitleForViewModel: selectedProperty?.rowTitle
                });

            selectedRowValue.value.isDynamic = false;
        }

        this.updateSavedRowWithDatafieldTitle(selectedRowValue);

        item.isSelected = true;
    }

    addOrRemoveRowItemsClicked(item: IDropDownMenuItem): void {
        const property = this.availableProperties.find(a => a.key === item.key);
        if (typeof property.actionDropdownItemMethod === 'function') {
            //TODO: do not think actionDropdownItemMethod is ever set, do we really need this?
            property.actionDropdownItemMethod();
        } else {
            this.addOrRemoveRowItems(property, false);
        }
    }

    getSelectedDataFieldIcon(key: string): string {

        if (typeof this.getValueItems === 'function' &&
            this.savedProperties[key]?.value?.smartFields?.length === 1) {

            const dataFieldItems: IDropDownMenuItem[] = this.getValueItems(true);

            //Only if one data field then we can try to resolve back to the dropped data field.
            for (const dataFieldItem of dataFieldItems) {
                if (dataFieldItem.key === this.savedProperties[key].value.smartFields[0].dataFieldReference
                    || dataFieldItem.key === this.savedProperties[key].value.smartFields[0].fieldName) {
                    return dataFieldItem.icon;
                }
            }
        }
        return '';
    }

    /**
     * Deletes a row from property mapper (when row selected and trashcan clicked)
     * */
    deleteMapperRow(): void {
        this.undoRedoService.addUndoMarker('--k2PropertyMapper: delete mapper row--');

        if (!this.selectedRowKey) return; //No row selected

        this.removeProperty(this.selectedProperties.find(p => p.key === this.selectedRowKey));
        this.selectedRowKey = '';
        this.selectedRow = null;

        this.onAvailablePropertiesChanged();
    }

    /**
     * Toggle button to change right value between dynamic (textbox) and static (dropdown)
     */
    toggleDropDown(expression: IExpression): void {

        this.undoRedoService.addUndoMarker('--datafield toggled--');
        if (expression.smartFields.length > 0) {
            expression.smartFields.clear();
            this.setPropertyMapperRightItems();
        }
        expression.isDynamic = !expression.isDynamic;
    }

    /**
     * Called when the properties that is available to be added is changed
     */
    private onAvailablePropertiesChanged(): void {

        this.selectedProperties = [];

        if (!this.availableProperties || !this.savedProperties) return;

        for (const availableProperty of this.availableProperties) {
            //Ensure available property is set up correctly:
            availableProperty.typeIcon = availableProperty.typeIcon ? availableProperty.typeIcon.toLocaleLowerCase() : dataTypes.icons.default;

            const savedProperty = this.savedProperties[availableProperty.key] as IPropertyMapperRowValueCollection;
            if (savedProperty) {
                availableProperty.value = savedProperty;
                //If value was incorrectly set to textfield, change to datafield
                if (this.shouldMapToDataFields && savedProperty.value.smartFields?.length === 1) {
                    const savedExpression = savedProperty.value.smartFields[0];
                    if (savedExpression.instanceOf(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.implementor)) {
                        const textField = savedExpression as ITextField;
                        availableProperty.value = this.createDataFieldMapperRow(textField.text, availableProperty.key,
                            textField.customTitle || textField.title);
                    }
                }
                availableProperty.altered = true;
                availableProperty.value.tokenReference = this.getExternalDefinitionToken(availableProperty.key);

                if (this.savedProperties[availableProperty.key].rowTitleForViewModel) {
                    availableProperty.rowTitle = this.savedProperties[availableProperty.key].rowTitleForViewModel;
                }

                this.selectedProperties.push(availableProperty);
            } else if (availableProperty.locked || availableProperty.required) {
                //This property must always be listed in selected properties (e.g. Folio)
                let shouldDisableUndoRedo = false;
                if (availableProperty.required === true) {
                    shouldDisableUndoRedo = true;
                }

                this.undoRedoService.forceDisableWhile(() => {
                    if (!availableProperty.value) {
                        availableProperty.value = this.createExpressionMapperRow(availableProperty.key);
                        
                    } else {
                        availableProperty.value.tokenReference = this.getExternalDefinitionToken(availableProperty.key);
                    }
                    this.savedProperties.add(availableProperty.key, availableProperty.value);
                    this.selectedProperties.push(availableProperty);
                }, shouldDisableUndoRedo, 'k2PropertyMapper.onAvailablePropertiesChanged');
            }
        }

        this.setAddRemoveRowItems();
        this.setPropertyMapperLeftItems();
        this.setPropertyMapperRightItems();

        //only apply validation logic for property mapper in call sub workflow step.
        //we will need to move validation code to controller level later
        if (this.displayMode === this.displayModes.workflow) {
            this.registerMappingValidation();
            this.validateMapping();
        }
    }

    /**
     * Return an external reference token based on the type and key
     */
    private getExternalDefinitionToken(key: string): string {
        return ExternalReferenceUtilities.getPropertyMapperExternalDefinitionToken(this.externalDefinitionParent, this.availableProperties, key);
    }

    /**
     * Updates values used to determine the title value for the selected dropdown item
     */
    private updateSavedRowWithDatafieldTitle(savedValue: IPropertyMapperRowValueCollection) {
        if (this.savedProperties[this.selectedRowKey]) {
            this.savedProperties[this.selectedRowKey] = savedValue;
        } else {
            this.savedProperties.add(this.selectedRowKey, savedValue);
        }

        this.setPropertyMapperRightItems();

        this.validationService.reevaluateErrors
            (this.savedProperties.getLogicalParentModel<IValidatedComponent>().childErrorInformation, true);
    }

    /**
     * Setup properties available to be chosen on the left mapping (dropdown) of a property mapper row
     */
    private setPropertyMapperLeftItems(): void {
        this.propertyMapperLeftItems = {};

        if (!this.selectedProperties) return;

        for (const row of this.selectedProperties) {
            let includeRow = true;

            if (this.selectedGroupingKey || row.actionDropdownItemMethod) {
                //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
                if (row.parentGroupings &&
                    row.parentGroupings.indexOf(this.selectedGroupingKey) === -1) {
                    includeRow = false;
                }
                if (row.actionDropdownItemMethod) {
                    includeRow = false;
                }
            }

            if (includeRow) {
                const key = row.key;

                this.propertyMapperLeftItems[key] = {
                    items: []
                };

                for (const availableProperty of this.availableProperties) {
                    const isSelected = availableProperty.key === key;
                    //Do not include property that is selected by another row
                    if (!isSelected && this.selectedProperties.find(p => p.key === availableProperty.key)) {
                        continue;
                    }
                    //Do not include system fields
                    if (availableProperty.systemField) {
                        continue;
                    }
                    //Do not include missing fields that are not selected by current row
                    if (availableProperty.state === ExternalReferenceState.Missing && !isSelected) {
                        continue;
                    }

                    this.propertyMapperLeftItems[key].items.push({
                        key: availableProperty.key,
                        icon: 'icon icon-set-datatypes icon-size16 ic-' + availableProperty.typeIcon?.toLowerCase(),
                        title: availableProperty.rowTitle,
                        isSelected
                    });
                }
            }
        }
    }

    /**
     * Returns the title of the datafield selected and saved in the saved properties
     *
     * @param key
     * @param rowIsDataField
     */
    private getSelectedItemTitleDropdown(row: IPropertyMapperRow, currentDropdownItems: IDropDownMenuItem[]): string {
        const key = row.key;

        if (!isUndefinedOrNull(this.savedProperties[key]) &&
            this.savedProperties[key].value &&
            this.savedProperties[key].value.smartFields[0]) {

            if (row.isReturnProperty || row.mapToDataFields) {
                return this.setAndReturnDataFieldTitle(currentDropdownItems, key);
            } else {
                //Need to keep this here for backwards-compatibility, this was not implemented correclty, should have used a datafield smartfield
                //and not a text field with the text property set to externalRef.  This gets used in SmartWizards (SMO - Map to data fields)
                for (const currentDropdownItem of currentDropdownItems) {
                    if (currentDropdownItem.key === this.savedProperties[key].value.smartFields[0].text) {
                        if (currentDropdownItem.title !== this.savedProperties[key].value.smartFields[0].customTitle) {
                            this.savedProperties[key].value.smartFields[0].customTitle = currentDropdownItem.title;
                        }

                        return currentDropdownItem.title;
                    }
                }

                //If no item was found, use the customTitle of the saved field. We also need to ensure the custom title is set correctly
                if ((isUndefinedOrNull(this.savedProperties[key].value.smartFields[0].customTitle) ||
                    this.savedProperties[key].value.smartFields[0].customTitle === '') &&
                    !isUndefinedOrNull(this.savedProperties[key].value.smartFields[0].title)) {
                    this.savedProperties[key].value.smartFields[0].customTitle = this.savedProperties[key].value.smartFields[0].title;
                }

                return this.savedProperties[key].value.smartFields[0].customTitle;
            }
        }

        return '';
    }

    /**
     * Returns the icon for the selected right dropdown item
     *
     * @param key
     * @param isDataFields
     */
    private getSelectedItemIconDropdown(key: string, isDataFields: boolean): string {
        if (isDataFields) {
            return this.getSelectedDataFieldIcon(key);
        }
        const property = this.availableProperties.find(a => a.key === key);
        return property?.typeIcon || '';
    }

    private setAndReturnDataFieldTitle(dataFieldMenuItems: IDropDownMenuItem[], key: string): string {

        if (!isUndefinedOrNull(this.savedProperties[key]) &&
            !isUndefinedOrNull(this.savedProperties[key].value) &&
            !isUndefinedOrNull(this.savedProperties[key].value.smartFields) &&
            this.savedProperties[key].value.smartFields.length === 1) {

            for (const dataFieldMenuItem of dataFieldMenuItems) {

                // Only if one data field then we can try to resolve back to the dropped data field.
                if (dataFieldMenuItem.key === this.savedProperties[key].value.smartFields[0].dataFieldReference ||

                    // Special case where user can map a data field to the ActivityInstanceDestInstanceData on the
                    // Call Sub Workflow step from a dropdown that contains
                    // the 'List Item' (ActivityInstanceDestInstanceData) and datafields.
                    dataFieldMenuItem.key === this.savedProperties[key].value.smartFields[0].fieldName) {
                    this.savedProperties[key].value.smartFields[0].customTitle = dataFieldMenuItem.title;
                    return dataFieldMenuItem.title;
                }
            }

            //If no item was found, use the customTitle of the saved field. We also need to ensure the custom title is set correctly
            if ((isUndefinedOrNull(this.savedProperties[key].value.smartFields[0].customTitle) ||
                this.savedProperties[key].value.smartFields[0].customTitle === '') &&
                !isUndefinedOrNull(this.savedProperties[key].value.smartFields[0].title)) {
                this.savedProperties[key].value.smartFields[0].customTitle = this.savedProperties[key].value.smartFields[0].title;
            }

            return this.savedProperties[key].value.smartFields[0].customTitle;
        }

        return '';
    }

    private registerMappingValidation(): void {
        const validationConfigs: {
            [validationType: number]: IValidationTypeItem;
        } = {};
        const errorMessage = 'property_mapper_property_missing';

        const keys: string[] = this.savedProperties.getKeys();

        for (const propertyKey of keys) {
            validationConfigs[ValidationTypes.AlwaysFalseValidator] = {
                validationType: ValidationTypes.AlwaysFalseValidator,
                validationFailedMessage: errorMessage,
                validationResourceTokens: [propertyKey]
            };

            const validatedModel = this.savedProperties[propertyKey] as IValidatedComponent;

            const validatorItem = {
                modelObject: validatedModel,
                propertyName: 'tokenReference',
                errorSeverity: ErrorSeverity.Error,
                validationFailedMessage: errorMessage,
                validationResourceTokens: [propertyKey],
                validationConfigs,
                validationValue: '',
                hideShowMeLink: false,
            } as IValidatorItem;

            this.validatorItems[propertyKey] = validatorItem;

            this.undoRedoService.addCustomUndoEvent((isUndo): boolean => {
                this.validateMapping();

                return true;
            }, '-- validate mapping on undo redo --');
        }
    }

    private validateMapping(): void {
        if (!this.availableProperties || this.availableProperties.length === 0) return;

        for (const key in this.validatorItems) {
            if (this.validatorItems.hasOwnProperty(key)) {

                const property = this.selectedProperties.find(p => p.key === key);
                if (isUndefinedOrNull(property) && !isUndefinedOrNull(this.savedProperties[key])) {
                    //Property is missing from selectedProperties but defined in savedProperties
                    this.validationService.evaluate([ValidationTypes.AlwaysFalseValidator], this.validatorItems[key]);
                    //Add back to selected properties in error
                    let savedProperty = this.savedProperties[key] as IPropertyMapperRowValueCollection;
                    let type = ReferenceTypes.Other;
                    if (savedProperty.value?.smartFields.length > 0) {
                        switch (savedProperty.value.smartFields[0].componentId) {
                            case ComponentIdMapping.k2DataField: type = ReferenceTypes.DataField; break;
                            case ComponentIdMapping.k2ItemReferenceField: type = ReferenceTypes.ItemReference; break;
                        }
                    }
                    this.selectedProperties.push({
                        key,
                        rowTitle: savedProperty.rowTitleForViewModel || key,
                        type: type,
                        typeIcon: dataTypes.icons.default,
                        value: savedProperty,
                        state: ExternalReferenceState.Missing
                    });
                    this.setAddRemoveRowItems();
                    this.setPropertyMapperLeftItems();
                    this.setPropertyMapperRightItems();

                } else if ((this.validatorItems[key].modelObject as IValidatedComponent)?.errorInformation?.length > 0) {
                    //Clear error
                    this.validationService.forcePassValidation(this.validatorItems[key]);
                }
            }
        }
    }

    private setPropertyMapperRightItems(): boolean {
        this.propertyMapperRightItems = {};

        if (!this.selectedProperties) return;
        let shouldListenForDataFields = false;
        const canGetValueItems = typeof this.getValueItems === 'function';
        const valueItems: IDropDownMenuItem[][] = [[], []];

        for (const row of this.selectedProperties) {
            const isDataFields = this.shouldMapToDataFields || row.mapToDataFields || false;
            if (canGetValueItems) {
                if (valueItems[+isDataFields].length === 0) {
                    valueItems[+isDataFields] = this.getValueItems(isDataFields);
                }
            }
            const dropdownItems = valueItems[+isDataFields] || [];
            const key = row.key;

            this.propertyMapperRightItems[key] = {
                items: dropdownItems,
                //Only datafield/variable dropdowns have a footer (Create New)
                footerItems: isDataFields ? this.valueFooterItems : [],
                selectedItemTitle: this.getSelectedItemTitleDropdown(row, dropdownItems),
                //Dynamic icon depending on datafield or item reference type
                selectedIcon: this.getSelectedItemIconDropdown(key, isDataFields)
            };

            if (!shouldListenForDataFields && isDataFields) {
                //If right hand items contain any datafields, items need to be updated if datafields are updated
                shouldListenForDataFields = true;
            }
        }

        if (shouldListenForDataFields) {
            if (typeof this.dataFieldEventRemover === 'function') {
                this.dataFieldEventRemover();
            }
            //Update datafields collection when datafields are changed (datafield could also be removed and validation needs to happen)
            this.dataFieldEventRemover =
                this.processService.root.configuration.dataFields.listen(EventTypes.anythingChanged, this.onAvailablePropertiesChanged.bind(this));
        }
        return true;
    }

    /**
     * Populates the left items available (in the bottom multiselect footer) for adding/removing rows to/from the mapper
     */
    private setAddRemoveRowItems(): IDropDownMenuItem[] {
        this.addRemoveRowItems = [];
        if (!this.availableProperties) return;


        for (const property of this.availableProperties) {
            let selected = false;
            let isDisabled = false;

            const selectedProperty = this.selectedProperties.find(p => p.key === property.key);
            if (!property.locked) {
                property.altered = !isUndefinedOrNull(selectedProperty);

                if (property.altered) {
                    selected = true;
                }
                else {
                    property.altered = false;
                }
            } else if (selectedProperty) {
                // if the property has locked state make it disable state (ex Load method is selected and
                // ID property should not be able to removed by clicking add/remove checkbox)
                selected = true;
                isDisabled = true;
            }

            let addItem = true;

            if (this.selectedGroupingKey?.length > 0 && property.parentGroupings?.length > 0) {
                //TODO: Groupings not yet implemented(SharePoint list item content-types) (Feature 936055: [Angular Migration] - SmartWizards)
                addItem = property.parentGroupings.indexOf(this.selectedGroupingKey) > -1;
            }

            //Do not include system fields or fields in error
            if (property.systemField || property.state === ExternalReferenceState.Missing) {
                addItem = false;
            }

            if (addItem === true) {
                this.addRemoveRowItems.push({
                    key: property.key.toString(),
                    icon: 'icon icon-set-datatypes icon-size16 ic-' + property.typeIcon.toLowerCase(),
                    isDisabled,
                    isSelected: selected,
                    title: property.rowTitle
                });
            }

        }
    }

    /**
     * This method is called to execute when a Dropdown Item is selected to determine the selected item's key to change its altered state to the opposite
     *
     * @param propertyMapperRow
     * @param multiAddEvent
     */
    private addOrRemoveRowItems(propertyMapperRow: IPropertyMapperRow, multiAddEvent: boolean = false): any {
        //Do not add a undo redo marker for multi add events, when performing undo, we want all added properties to be removed
        let shouldDisableUndoRedo = false;
        if (!multiAddEvent) {
            this.undoRedoService.addUndoMarker('--k2PropertyMapper: set selected item--');
        } else {
            this.undoRedoService.addUndoInfo('--k2PropertyMapper: set selected item multiAddEvent - disable undo--');
            shouldDisableUndoRedo = true;
        }

        this.undoRedoService.forceDisableWhile(() => {

            if (!propertyMapperRow.value) {
                propertyMapperRow.value = this.createExpressionMapperRow(propertyMapperRow.key);
            }

            if (!this.savedProperties[propertyMapperRow.key] || !this.selectedProperties.find(p => p.key === propertyMapperRow.key)) {
                this.addProperty(propertyMapperRow);
            } else {
                this.removeProperty(propertyMapperRow);
            }

            this.setPropertyMapperLeftItems();
            if (typeof this.getValueItems === 'function') {
                this.setPropertyMapperRightItems();
            }

            if (multiAddEvent !== false) {
                this.undoRedoService.addUndoInfo('--k2PropertyMapper: set selected item multiAddEvent - enable undo--');
            }
        }, shouldDisableUndoRedo, 'k2PropetyMapper.setSelectedItem');
    }


    /**
     * Called when a property is added from the list of available properties
     *
     * @param propertyMapperRow
     */
    private addProperty(propertyMapperRow: IPropertyMapperRow): void {
        propertyMapperRow.altered = true;

        if (!this.savedProperties[propertyMapperRow.key]) {
            this.savedProperties.add(propertyMapperRow.key, propertyMapperRow.value);
        } else {
            this.savedProperties[propertyMapperRow.key] = propertyMapperRow.value;
        }

        let selectedProperty = this.selectedProperties.find(p => p.key === propertyMapperRow.key);
        if (!selectedProperty) {
            this.selectedProperties.push(propertyMapperRow);
        }
        else {
            selectedProperty = propertyMapperRow;
        }

        this.onMapperRowSelected(propertyMapperRow);
    }

    /**
     * Remove property from selected and saved properties.
     * If property removed was in error, also remove it from available properties.
     * Re-evaluate errors, to allow for validation to bubble up and remove errors where necessary.
     *
     * @param propertyMapperRow
     */
    private removeProperty(propertyMapperRow: IPropertyMapperRow): void {
        propertyMapperRow.altered = false;

        const keyToRemove = propertyMapperRow.key;

        let propertyToCleanup: ISmartObjectPropertyField = null;
        const externalReferencePropertyQP = this.savedProperties[keyToRemove].tokenReference;

        if (!isUndefinedOrNull(externalReferencePropertyQP)) {
            propertyToCleanup = this.savedProperties.getRoot<IProcess>()
                .findItem<ISmartObjectPropertyField>
                (externalReferencePropertyQP);
        }

        this.savedProperties.remove(keyToRemove);

        //Remove from selectedProperties
        const removeIndex = this.selectedProperties?.indexOf(this.selectedProperties?.find(s => s.key === keyToRemove));
        if (removeIndex >= 0) {
            this.selectedProperties.splice(removeIndex, 1);
        }

        this.validationService.reevaluateErrors(
            this.savedProperties
                .getLogicalParentModel<IValidatedComponent>()
                .childErrorInformation,
            true
        );

        //Remove the missing property from external references if nothing else is using it
        //(There seems to be a syncing issue with the definition on cleanup and the definition that gets deployed. Interim solution:
        //The references has to be removed here as well as on deploy, else the correct definition won't be deployed to KPRX - See Defect 883188)
        if (!isUndefinedOrNull(propertyToCleanup)) {
            if (propertyToCleanup.state === ExternalReferenceState.Missing) {
                this.externalReferenceService.removeUnusedExternalReferences();
            }
        }

        this.selectedRowKey = '';
    }

    /**
     * Returns the index of the row based on the key provided
     *
     * @param groupingKey
     */
    private getPropertyIndex(propertyKey: string): number {
        for (let p = 0; p < this.availableProperties.length; p++) {
            if (this.availableProperties[p].key === propertyKey) {
                return p;
            }
        }
    }

    private createDataFieldMapperRow(dataFieldReference: string, tokenReference: string, dataFieldTitle: string,
        isDynamic: boolean = false): IPropertyMapperRowValueCollection {
        const row = sourceCode.k2Designer.objectModel.workflow.process.k2Process.events
            .smartWizard
            .PropertyMapperRowValueCollection
            .createInstance({
                value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    type: ReferenceTypes.DataField,
                    smartFields:
                        [
                            sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.DataField.createInstance({
                                dataFieldReference,
                                customTitle: dataFieldTitle
                            })
                        ],
                    isDynamic
                }),
                tokenReference: this.getExternalDefinitionToken(tokenReference)
            });
        return row;
    }

    private createExpressionMapperRow(tokenReference: string): IPropertyMapperRowValueCollection {
        const row =
            sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.smartWizard.PropertyMapperRowValueCollection.createInstance({
                value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    smartFields: []
                }),
                tokenReference: this.getExternalDefinitionToken(tokenReference)
            });

        return row;
    }

    private dataFieldChanged = (event: IPropertyMapperDataFieldChangedEvent) => {
        if (event.dataField) {
            const item = this.propertyMapperRightItems[event.key].items.find(i => i.key === event.dataField.dataFieldReference);
            if (item) {
                this.rightItemSelected(item, event.key);
            }
        }
    };
}
