import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockSelectionService } from '../../../testing/mock-services/selection-service';
import { ISelectionService } from '../../../types/services/selection/i-selection-service';

const selectionServiceFactory = () => environment.devHarness === true
    ? new MockSelectionService()
    : sourceCode.k2Designer.core.environmentService.selectionService;

export const SelectionServiceToken = new InjectionToken<ISelectionService>('ISelectionService');

export const SelectionServiceFactory = {
    provide: SelectionServiceToken,
    useFactory: selectionServiceFactory,
    deps: [

    ]
};
