/**
 * Module to assist with unit testing, ensures namespace objects are defined.
 * */
export namespace Mocks {
    export function ensureNamespace(ns: string, config: any): any {
        const parts: string[] = ns.split('.');
        let obj: object = window;
        while (parts.length) {
            const pt = parts.shift();
            obj[pt] = obj[pt] || {};
            obj = obj[pt];
        }

        if (config) {
            for (const p in config) {
                obj[p] = config[p];
            }
        }
        return obj;
    }

    export function destroyNamespace(ns: string, whole: boolean = false): void {
        const parts: string[] = ns.split('.');
        let obj = window;
        while (parts.length) {
            const pt = parts.shift();
            obj = getObj(obj, pt);
        }

        if (obj && obj !== window) {
            if (whole)
            {
                nullify(obj);
            }
            else {
                obj = undefined;
            }
        }
    }

    function getObj(obj: any, prop: string): any {
        return obj[prop];
    }

    function nullify(obj) {
        if (isNode(obj)) {
            obj = undefined;
            return;
        }
        for (const key in obj) {
            if (typeof obj[key] == 'object' && !Date.parse(obj[key])) {
                obj[key] = nullify(obj[key]);
            }
            obj[key] = undefined;
        }
        return obj;
    }

    function isNode(o) {
        return (
            typeof Node === 'object' ? o instanceof Node :
                o && typeof o === 'object' && typeof o.nodeType === 'number' && typeof o.nodeName === 'string'
        );
    }
}
