import { OnDestroy } from '@angular/core';
import { NgZone } from '@angular/core';
import { Component, ElementRef, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';
import { IItemProviderService } from '../../../types/services/item-provider/i-item-provider-service';
import { ILegacyResourceService } from '../../../types/services/resources/i-legacy-resource.service';
import { IToolBoxService } from '../../../types/services/tool-box/i-toolbox-service';
import { LegacyResourceServiceToken } from '../../legacy-service-factories/resource-service-factory';
import { DragDropService } from '../../services/drag-drop/drag-drop.service';
import { ItemProviderServiceToken } from '../../services/item-provider/item-provider-service-factory';
import { ToolBoxServiceToken } from '../../services/tool-box/tool-box-service-factory';
import { isUndefinedOrNull } from '../../shared/utils/general';

@Component({
    selector: 'k2-stack-panel',
    templateUrl: './stack-panel.component.html',
    styleUrls: ['./stack-panel.component.less']
})
export class StackPanelComponent implements OnInit, OnDestroy {
    @Input() k2Items: sourceCode.k2Designer.objectModel.dragDropService.IDragDropData[];
    @Input() k2TempEdit: boolean;
    @Input() k2Itemheight: number;

    @Output() k2OnDragEnd = new EventEmitter<any>();

    private currentIndex: number;
    private _slctPrv: sourceCode.k2Designer.objectModel.core.itemProvider.IItemProvider<any>;
    private _dropIndex: number;

    private dragOverSub: Subscription;

    constructor(
        private _element: ElementRef,
        @Inject(ToolBoxServiceToken) private _toolboxService: IToolBoxService,
        private _dragDropService: DragDropService,
        @Inject(ItemProviderServiceToken) private _itemProviderService: IItemProviderService,
        @Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        private zone: NgZone) { }

    ngOnInit(): void {
        this.zone.runOutsideAngular(() => {
            this.dragOverSub = fromEvent(this._element.nativeElement, 'dragover').subscribe((event: DragEvent) => this.onDragOver(event));
        });
    }

    ngOnDestroy() {
        this.dragOverSub?.unsubscribe();
    }

    private getDescriptionFrom(item: any): string {

        if (!isUndefinedOrNull(item.description)) {
            if (item.description.length > 0) {
                return item.description;
            }
        }

        let description = sourceCode.k2Designer.utilities.getDescriptionFrom(item.name);

        //Favorites bar doesn't include _step in name, try it with _step for the right resource string
        if (description.length === 0) {
            description = sourceCode.k2Designer.utilities.getDescriptionFrom(item.name + '_step');
        }

        return description;
    }

    private getSvgIcon(iconId): string {
        return iconId ? iconId : 'fallbackIcon';
    }

    private getSectionFrom(item: any): string {
        if (sourceCode.k2Designer.utilities.isObjectaSharePointObject(item)) {
            return 'SharePoint';
        }

        return item.section;
    }
    private getSubSectionFrom(item: any): string {
        if (sourceCode.k2Designer.utilities.isObjectaSharePointObject(item)) {
            return item.section;
        }

        return item.subsection;
    }
    /**
     * Returns the current state of the favourites (Editable or not editable)
     */
    private getFavouritesEditableState(): boolean {
        if (this._toolboxService) return this._toolboxService.favouritesEditableState;
    }

    private favouritesItemContextMenu(event: MouseEvent, context?: any): void {

        const toolboxService = this._toolboxService;
        let favouriteIcon;

        const configuration: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuConfiguration = <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuConfiguration>{
            e: event,
            menuItems: <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem[]>[
                <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem>{
                    title: this._resourceService.getString('context_menu_favourites_favourite'),
                    enabled: true,
                    icon: 'icon icon-set-edittoolbox_popover icon-size16 ic-favourites',
                    onSelected(item: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem) {
                        toolboxService.removeFavouriteItem(context);
                    }
                },
                <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem>{
                    title: this._resourceService.getString('context_menu_favourites_edit'),
                    enabled: true,
                    icon: 'icon icon-set-toolbar icon-size16 ic-edit',
                    onSelected(item: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem) {
                        toolboxService.favouritesEditableState = !toolboxService.favouritesEditableState;
                    }
                },
                <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem>{
                    title: this._resourceService.getString('context_menu_favourites_help'),
                    enabled: true,
                    icon: 'icon icon-set-edittoolbox_popover icon-size16 ic-help',
                    onSelected(item: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem) {

                        // TODO: complete help
                    }
                }],
            width: 140
        };

        sourceCode.k2Designer.uiComponents.contextMenu.showContextMenu(configuration);
    }

    public onDrop(e: DragEvent): boolean {

        const currentIndex = this.currentIndex;
        this.currentIndex = null;

        if (this._toolboxService.favouritesEditableState) {

            const y = this.getRelativeYCoord(e.pageY);
            this._dropIndex = this.getDropIndex(y);

            //If moving existing fav. item, if index to drop is higher than previous index, indexes will shift up because item is removed from top:
            const addAtIndex = currentIndex !== null && this._dropIndex > currentIndex ? this._dropIndex - 1 : this._dropIndex;
            //Add/move item to specified index:
            this._toolboxService.addFvrt(addAtIndex, this._dragDropService.getItem('canvasItem'));
        }

        return false;
    }

    private getDragImage(item: sourceCode.k2Designer.objectModel.core.itemProvider.IItemBase<any, any>): HTMLElement {
        const result: HTMLDivElement = document.createElement('div');
        result.innerHTML = item.title;
        sourceCode.k2Designer.directives.k2DragDrop.K2Draggable.dragOffset = { x: 0, y: 0 };
        return result;
    }

    public getDragImageFn = this.getDragImage.bind(this);

    private getFavoritesDragData(source: any, context: any, extraData: any): any {
        return source || extraData || context;
    }

    public getFavoritesDropData(source: any, context: any, dragData: any, extraData: any): any {
        return source || extraData || dragData || context;
    }

    /**
     * Return an object of data that is associated with the favourite item
     */
    private getDragItem(item: sourceCode.k2Designer.objectModel.core.itemProvider.IItemBase<any, any>, context: any): sourceCode.k2Designer.objectModel.dragDropService.IDragDropData {
        this._slctPrv = this._itemProviderService.getItemProviderByName((<any>item).__dataProviderName);
        return this._slctPrv.getDragItem(item, null);
    }

    public getDragItemFn = this.getDragItem.bind(this);

    private _dragHeightAdd = 0;
    private getTotalHeight(): string {
        return (this.k2Items.length * this.k2Itemheight) + this._dragHeightAdd + 'px';
    }

    private getTop(idx: number): string {
        return idx * this.k2Itemheight + 'px';
    }

    private getLeft(idx: number): string {
        return '5px';
    }

    public onDragEnd(e: DragEvent): boolean {

        const el: HTMLElement = <HTMLElement>e.srcElement;
        $(el).parent().removeClass('ng-leave');
        $(el).parent().removeClass('ng-leave-active');

        this.k2OnDragEnd.emit();

        return false;
    }

    public onDragLeave(e: DragEvent): boolean {
        this.dragOverInProgress = false;

        if (this._toolboxService.favouritesEditableState) {

            if (this.k2TempEdit === true && e.type === 'dragend') {

                this._toolboxService.favouritesEditableState = false;
                this.k2TempEdit = false;

                return;
            }
        }

        //TODO: Add animation to shift favorites items back in place when dragged out
        return false;
    }

    public onDragEnter(e: DragEvent): boolean {

        if (this._toolboxService.favouritesEditableState) {
            //var x: HTMLElement = <HTMLElement>e.srcElement.cloneNode(true);
            //x.style.backgroundColor = "red";

            //(<any>e.dataTransfer).setDragImage(x, 0, 0);

            this._dragHeightAdd = this.k2Itemheight / 2;
        }
        return false;
    }

    public onDragStart(e: DragEvent, k2DragIndex?: number): boolean {
        $('tip.repatriate-tooltip').remove();

        this.currentIndex = k2DragIndex;

        const x: HTMLElement = <HTMLElement>e.srcElement;
        $(x).parent().addClass('ng-leave');
        $(x).parent().addClass('ng-leave-active');

        if (e.altKey && !this._toolboxService.favouritesEditableState) {
            this._toolboxService.favouritesEditableState = true;
            this.k2TempEdit = true;
        }
        return false;
    }

    private dragOverInProgress = false;
    public onDragOver(e: DragEvent): boolean {
        e.preventDefault();

        if (this._toolboxService.favouritesEditableState) {

            if (!this.dragOverInProgress) {
                this.dragOverInProgress = true;
                var isNewItem = this.currentIndex == null;

                //TODO: Add animation to shift favorite items out of the way to make space for dragged-over item
            }

            e.dataTransfer.dropEffect = isNewItem ? 'copy' : 'move';

        } else {
            e.dataTransfer.dropEffect = 'none';
        }
        return false;
    }

    //Gets the y location relative to the top of the stackpanel root element
    private getRelativeYCoord(pageY: number): number {
        return pageY - $(this._element.nativeElement).offset().top;
    }

    //Gets the x location relative to the left of the stackpanel root element
    private getRelativeXCoord(pageX: number): number {
        return pageX - $(this._element.nativeElement).offset().left;
    }

    //Calculates the index for dropping item in stackpanel (0 based)
    private getDropIndex(y: number): number {
        return Math.floor((y + (this.k2Itemheight / 2)) / this.k2Itemheight);
    }
}
