import { Component, Inject, Input, OnDestroy, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IPopupService } from 'src/types/services/popup/i-popup-service';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { IDataService } from '../../../../../types/services/data/i-data-service';
import { IEnvironmentService } from '../../../../../types/services/environment/i-environment-service';
import { IRecentsService } from '../../../../../types/services/recents/i-recents-service';
import { IListItemIconData } from '../../../../../types/uiComponents/list-item/i-list-item-icon-data';
import { IHierarchyDialogContext } from '../../../../../types/uiComponents/pluginWizards/IHierarchyDialogContext';
import { IProcessPluginDefinitionContext } from '../../../../../types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { DataServiceToken } from '../../../../services/data/data-service-factory';
import { EnvironmentServiceToken } from '../../../../services/environment/environment-service-factory';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { PopupService } from '../../../../services/popup/popup.service';
import { RecentsServiceToken } from '../../../../services/recents/recents-service-factory';
import { ResourceService } from '../../../../services/resource/resource.service';
import { listItemCommands, listItemIcons } from '../../../../shared/constants/list-item';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';
import { DropDownMenuAlignment } from '../../../../shared/enums/drop-down/drop-down-menu-alignment';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { DropdownMenuItemTemplates } from '../../../../shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from '../../../../shared/enums/drop-down/drop-down-triangle-position';
import { IntegrationActionCommands } from '../../../../shared/enums/integration/integration-action-commands';
import { isUndefinedOrNull, shortUid } from '../../../../shared/utils/general';
import { DropDownComponent } from '../../../common/drop-down/drop-down.component';
import { PageComponent } from '../../page/page.component';

type IAssetItem = sourceCode.k2Designer.objectModel.core.itemProvider.IAssetItem;

@Component({
    selector: 'k2-submit-task-action-settings',
    templateUrl: './submit-task-action-settings.component.html',
    styleUrls: ['./submit-task-action-settings.component.less'],
    providers: [{ provide: PageComponent, useExisting: SubmitTaskActionSettingsComponent }]
})
export class SubmitTaskActionSettingsComponent extends PageComponent implements OnInit, OnDestroy {
    buttonConfig: IPopupButtonsConfig;

    @Input() public config: IPopupConfiguration;

    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    public actionCommands: typeof IntegrationActionCommands = IntegrationActionCommands;
    public formIcon: IListItemIconData = listItemIcons.formIcon;

    /**
     * Unique identifier form the show message radio button
     */
    public showMessageUid: string;

    /**
     * Unique identifier form the call a rule radio button
     */
    public callARuleUid: string;

    /**
     * Unique identifier form the navigate radio button
     */
    public navigateToUid: string;

    /**
     * Form items
     */
    public formsDropdownItems: IDropDownMenuItem[] = [];

    /**
     * Rules items
     */
    public rulesDropdownItems: IDropDownMenuItem[] = [];

    /**
     * Recent form items
     */
    public recentForms: IAssetItem[] = [];

    formLoaded = false;

    /**
     * This context get send through to the hierarchy control
     */
    private hierarchyDialogContext: IHierarchyDialogContext;
    private formContentSub: Subscription;

    constructor(protected resourceService: ResourceService,
        private smartFormsIntegrationService: SmartFormsIntegrationService,
        @Inject(RecentsServiceToken) private recentsService: IRecentsService,
        @Inject(DataServiceToken) private dataService: IDataService,
        @Inject(EnvironmentServiceToken) private environmentService: IEnvironmentService,
        private popupService: PopupService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.showMessageUid = shortUid();
        this.callARuleUid = shortUid();
        this.navigateToUid = shortUid();

        //Check recent service if service exists
        this.refreshFormsDropDown();

        this.formContentSub = this.smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formRulesLoaded();
                this.formLoaded = true;
            });
    }

    ngOnDestroy(): void {
        this.formContentSub?.unsubscribe();
    }

    beforePageDisplayed() {
    }

    beforeNavigateNext() {}

    beforeNavigatePrevious() {}

    /**
     * Context of the plugin wizard.
     */
    get pluginContext(): IProcessPluginDefinitionContext {
        return this.config.context as IProcessPluginDefinitionContext;
    }

    getFromResx = (resourceName: string, key: string) => this.resourceService.getFromResx(resourceName, key);

    /**
     * Method that gets called when the action command selection changed.
     */
    selectionChanged(actionCommand: string, checked: boolean): void {
        this.pluginContext.actionCommand = actionCommand;
        this.smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    /**
     * Method that gets called when the submit message changed.
     */
    submitMessageChanged(): void {

        this.smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    /**
     * Method when item is selected from the dropdown.
     */
    itemSelected(item: IDropDownMenuItem): void {

        for (let i = 0; i < this.pluginContext.afterSubmitRules.length; i++) {
            if (this.pluginContext.afterSubmitRules[i].key !== item.key) {
                this.pluginContext.afterSubmitRules[i].isSelected = false;
                this.pluginContext.afterSubmitRule = item.metaData as sourceCode.k2Designer.objectModel.smartForms.IRule;
            }
        }
        this.smartFormsIntegrationService.determinePages(this.pluginContext);

    }

    /**
     * Method that executes when you toggle between text to type for URL and selection of a form.
     */
    toggleToText(): void {

        this.pluginContext.toggleToUrl = !this.pluginContext.toggleToUrl;

        if (this.pluginContext.toggleToUrl) {
            this.pluginContext.navigateUrl.smartFields.clear();
            this.pluginContext.navigateUrl.smartFields.push(sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({
                text: ''
            }));
            (this.pluginContext.navigateUrl as sourceCode.k2Designer.objectModel.core.configuredSmartFields.IExpressionExtended).notifyChange(null);
        }
    }

    /**
     * Method when dropdown is selected.
     */
    formSelected = (item: IDropDownMenuItem) => {

        if (item) {
            if (item.key === listItemCommands.browse.key) {
                this.openHierarchyControl();
            }
            else {

                this.pluginContext.navigateUrl = sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                    smartFields: [
                        sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartForm.createInstance({
                            id: item.key,
                            name: item.title || item.metaData,
                            title: item.title
                        })
                    ]
                });
                this.smartFormsIntegrationService.determinePages( this.pluginContext);
            }
        }
    };

    /**
     * Method to refresh the items in the navigate to rule dropdown.
     */
    refreshFormsDropDown(): void {

        this.formsDropdownItems = [];

        //Refresh recentItems by looking in the recent service for recently added forms.
        if (this.recentsService.hasContext('form')) {
            this.recentForms = this.recentsService.getItems('form');
        }

        let navToFormRef: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartForm;
        if (this.pluginContext.navigateUrl?.smartFields?.length > 0) {
            navToFormRef = (this.pluginContext.navigateUrl.smartFields[0] as sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartForm);
        }

        let dropDownItem: IDropDownMenuItem;
        for (let i = 0; i < this.recentForms.length; i++) {

            dropDownItem = {
                title: this.recentForms[i].title,
                key: this.recentForms[i].key,
                metaData: this.recentForms[i].name,
                innerItemComponent: DropdownMenuItemTemplates.Default,
                icon: 'icon-set-steps ic-form',
                isSelected: navToFormRef?.id === this.recentForms[i].key
            };

            this.formsDropdownItems.push(dropDownItem);
        }

        this.formsDropdownItems.push(DropDownComponent.separator);

        this.formsDropdownItems.push({
            title: this.resourceService.getString('control_PickerControl_Item_Browse'),
            key: listItemCommands.browse.key,
            icon: listItemCommands.browse.icon,
            innerItemComponent: DropdownMenuItemTemplates.Command
        });
    }

    /**
     * Method to open the hierarchy control.
     */
    private openHierarchyControl(): void {

        this.hierarchyDialogContext = {
            listItems: [],
            crumbItems: [],
            recentItems: [],
            hideRecents: true,
            onBreadCrumbAdded: (item: IBreadCrumbItem) => {
                this._loadHierarchyItems(item);
            },
            onBreadCrumbClicked: (item: IBreadCrumbItem) => {
                this._loadHierarchyItems(item);
            },
            onSearch(value: string) {

            },
            recentContextString: 'form'
        } as IHierarchyDialogContext;

        //load items
        this._loadHierarchyItems();

        //TODO:
        //uiComponents.popup.hierarchyDialog.showHierarcyDialog method not migrated.
        //This is a shared method that lived in the hierarchDialogController, but was called by
        //callSubWorkflowController, workflowIntegrationController, recentSmartFormsController,
        //submitTaskActionSettings, taskStepSmartFormController, smartWizardSmartObjectPickerControlController,
        //smartObjectReminderAddInPanelController
        //It will need to be added in a common service when one of these components are migrated, not as a specific component's exported function

        (this.popupService as any).showHierarchyDialog(
            this.resourceService.getString('wizard_workflow_integration_smartforms'),
            false,
            this.hierarchyDialogContext,
            this.formSelectionClosed.bind(this)
        );
    }

    private formSelectionClosed(item: IAssetItem): void {
        this.recentsService.addItem('form', item, 'key');
        this.formSelected(item);
        this.refreshFormsDropDown();
        this.smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    private formRulesLoaded = (): void => {
        this.rulesDropdownItems = this.pluginContext.afterSubmitRules;
    };

    /**
     * Method to load bread crumbs from rest service.
     */
    private _loadHierarchyItems(selectedItem?: IBreadCrumbItem): void {

        //Check if breadCrumbItem item is null.
        let assetItem: IAssetItem = null;
        if (selectedItem) {
            assetItem = {
                title: '',
                key: selectedItem.key,
                type: selectedItem.assetItem?.type ?? (selectedItem as any).type
            };
        }

        //Call rest service.
        this.dataService.httpGet('api/smartforms/getcategoryforms',
            {
                host: this.environmentService.currentEnvironment.hostId,
                hashes: [],
                sourceItem: assetItem
            }, this.assetsLoaded.bind(this));
    }

    /**
     * Callback method when asset items are received from server.
     */
    private assetsLoaded(result: IAssetItem, error: boolean): void {

        if (!error) {
            const sourceItem = result;
            if (!isUndefinedOrNull(sourceItem)) {
                if (sourceItem.items.length > 0) {
                    //Populate the list items with the source items' child items.
                    this.hierarchyDialogContext.listItems = sourceItem.items;
                }
                else {
                    //Clear items if no children on return item.
                    this.hierarchyDialogContext.listItems = [];
                }
            }
        }
    }
}
