import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockDataService } from '../../../testing/mock-services/data-service';
import { IDataService } from '../../../types/services/data/i-data-service';

const dataServiceFactory = () => environment.devHarness === true
    ? new MockDataService()
    : sourceCode.k2Designer.core.environmentService.dataService;

export const DataServiceToken = new InjectionToken<IDataService>('IDataService');

export const DataServiceFactory = {
    provide: DataServiceToken,
    useFactory: dataServiceFactory,
    deps: [

    ]
};
