import { AfterViewInit, Component, EventEmitter, Inject, Input, OnChanges, OnInit, Output, SimpleChanges, TemplateRef, ViewChild } from '@angular/core';
import { IConfiguredSmartField } from '../../../../../../types/models/core/configured-smart-fields/i-configured-smart-field';
import { IExpression } from '../../../../../../types/models/core/configured-smart-fields/i-expression';
import { IEnvironmentService } from '../../../../../../types/services/environment/i-environment-service';
import { IRecentsService } from '../../../../../../types/services/recents/i-recents-service';
import { EnvironmentServiceToken } from '../../../../../services/environment/environment-service-factory';
import { PopupService } from '../../../../../services/popup/popup.service';
import { RecentsServiceToken } from '../../../../../services/recents/recents-service-factory';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { SmartObjectService } from '../../../../../services/smart-object/smart-object.service';
import { UndoRedoServiceToken } from '../../../../../services/undo-redo/undo-redo-service-factory';
import { ComponentIdMapping } from '../../../../../shared/constants/components';
import IDropDownMenuItem from '../../../../../shared/enums/drop-down/drop-down-menu-item';
import { EmailParts } from '../../../../../shared/enums/email/email-parts';
import { ListItemDropDownType } from '../../../../../shared/enums/list-item/list-item-drop-down-type';
import { ConfiguredSmartFieldTypes } from '../../../../../shared/enums/model/workflow/core/configured-smart-fields/configured-smart-field-types';
import { WorkflowContextBrowserItemTypes } from '../../../../../shared/enums/model/workflow/item-provider/workflow-context-browser-item-types';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';
import workflowConfiguredSmartField = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields; // can only use this for interfaces
import coreConfiguredSmartField = sourceCode.k2Designer.objectModel.core.configuredSmartFields; // can only use this for interfaces

@Component({
    selector: 'k2-recipient-picker',
    templateUrl: './recipient-picker.component.html',
    styleUrls: ['./recipient-picker.component.less']
})
export class RecipientPickerComponent extends BaseResourceComponent implements OnInit, OnChanges, AfterViewInit {
    @ViewChild('userTemplate') userDropDownTemplate: TemplateRef<any>;

    @Input() validationOptions: IValidationOptions[];
    @Input() classRepeatSpace: string = 'k2-list-item-repeat-label-spacing';
    @Input() recipientModel: IExpression;
    @Input() emailPart: EmailParts = EmailParts.To;
    /**
     * To indicate if users dropdown should include Originator and Orignator's Manager users by default
     */
    @Input() shouldIncludeOriginatorUsers: boolean = false;
    @Output() onNotificationToggled: EventEmitter<IExpression> = new EventEmitter<IExpression>();
    @Output() onRecipientSelected: EventEmitter<IExpression> = new EventEmitter<IExpression>();

    listItemDropDownType: typeof ListItemDropDownType = ListItemDropDownType;
    originatorEmail: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField;
    originatorManagerEmail: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField;
    userDropDownItems: IDropDownMenuItem[] = [];
    pickerDropDownItems: IDropDownMenuItem[] = [];
    selectedField: IExpression;
    /**
     * Return types per email type
     */
    emailPartReturnTypes: string[];
    chooseWatermark: string;

    constructor(resourceService: ResourceService,
        private smartObjectService: SmartObjectService,
        @Inject(EnvironmentServiceToken) private envService: IEnvironmentService,
        @Inject(RecentsServiceToken) private recentsService: IRecentsService,
        @Inject(UndoRedoServiceToken) private undoRedoService: IUndoRedoService,
        private popupService: PopupService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.originatorEmail = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.createInstance({
            fieldName: sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATOREMAILWORKFLOWCONTEXTFIELDNAME,
            title: this.resourceService.getString(`user_task_step_recipient_originator_context_field_email`),
            customTitle: this.resourceService.getString(`user_task_step_recipient_originator`),
            parentName: this.resourceService.getString(`workflow_originator_field_parent_name`),
            internalId: 1,
            componentId: ComponentIdMapping.k2WorkflowContextField,
        });

        this.originatorManagerEmail = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.WorkflowContextField.createInstance({
            fieldName: sourceCode.k2Designer.services.core.EnvironmentService.ORIGINATORMANAGEREMAILWORKFLOWCONTEXTFIELDNAME,
            title: this.resourceService.getString(`user_task_step_recipient_originator_context_field_email`),
            customTitle: this.resourceService.getString(`user_task_step_recipient_originator_manager`),
            parentName: this.resourceService.getString(`workflow_originator_manager_field_parent_name`),
            internalId: 1,
            componentId: ComponentIdMapping.k2WorkflowContextField,
        });
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.recipientModel?.currentValue) {
            this.pickerDropDownItems = this.getUserListFor(this.recipientModel);
        }
    }

    ngAfterViewInit() {
        this.userDropDownItems = this.getUserDropDownList();
        this.pickerDropDownItems = this.getUserListFor(this.recipientModel);
        if (this.emailPart === EmailParts.From) {
            this.emailPartReturnTypes = ['Users'];
            this.chooseWatermark = 'wizard_task_destination_sets_choose_sender';
        } else {
            this.emailPartReturnTypes = ['Users', 'Groups'];
            this.chooseWatermark = 'wizard_task_destination_sets_choose';
        }
    }

    getIcon(destinationExpression: IExpression): string {
        return this.smartObjectService.getDestinationIcon(destinationExpression);
    }

    getUserListFor(expression: IExpression): IDropDownMenuItem[] {
        if (!expression) {
            return []
        }

        const shouldShowGroups = this.emailPart !== EmailParts.From
        const ddList: IDropDownMenuItem[] = this.userDropDownItems.filter((item) => !item.additionalValueProperty
            || item.additionalValueProperty.type === WorkflowContextBrowserItemTypes.identityUser
            || (item.additionalValueProperty.type === WorkflowContextBrowserItemTypes.identityGroup && shouldShowGroups))
            .map(x => ({ ...x }));

        const smartField: workflowConfiguredSmartField.IUserField =
            expression.smartFields?.length ? expression.smartFields[0] : null;
        for (const item of ddList) {
            item.templateRef = this.userDropDownTemplate;
            if (!item.icon) {
                switch (item.additionalValueProperty.type) {
                    case (WorkflowContextBrowserItemTypes.identityUser):
                        item.icon = 'ic-user';
                        break;
                    case (WorkflowContextBrowserItemTypes.identityGroup):
                        item.icon = 'ic-group';
                        break;
                    case (WorkflowContextBrowserItemTypes.identityRole):
                        item.icon = 'ic-role';
                        break;
                }
            }
            item.isSelected = (smartField?.customTitle || smartField?.displayName) === (item.title);
            item.onSelected = (ddItem: IDropDownMenuItem) => {
                let sfItem: IConfiguredSmartField;
                switch (item.additionalValueProperty?.type) {
                    case (WorkflowContextBrowserItemTypes.identityUser):
                        sfItem = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.UserField.createInstance({
                            title: item.key,
                            displayName: item.title,
                            email: item.additionalValueProperty.email,
                            systemName: item.additionalValueProperty.systemName,
                            returnType: ConfiguredSmartFieldTypes.userEmail
                        });
                        break;
                    case (WorkflowContextBrowserItemTypes.identityGroup):
                        sfItem = this.smartObjectService.wrapEmailForGroupUsersField(
                            item.additionalValueProperty.displayName,
                            item.additionalValueProperty.systemName,
                            item.additionalValueProperty.label);
                        break;
                    default:
                        if (item.key === 'Originator') {
                            sfItem = this.originatorEmail;
                        } else if (item.key === 'ProcessOriginatorManager') {
                            sfItem = this.originatorManagerEmail;
                        }
                        break;
                }

                this.selectUser(expression, sfItem);
            };
        }
        return ddList;
    }

    preventPropagation(e: MouseEvent) {
        e.stopPropagation();
        return false;
    }

    getUserDropDownList() {
        const items: IDropDownMenuItem[] = [];

        if (this.shouldIncludeOriginatorUsers) {
            items.push(<IDropDownMenuItem>{
                title: this.resourceService.getString(`user_task_step_recipient_originator`),
                key: `Originator`,
                icon: `icon-set-picker ic-recipientnondynamic`,
            });

            items.push(<IDropDownMenuItem>{
                title: this.resourceService.getString(`user_task_step_recipient_originator_manager`),
                key: `ProcessOriginatorManager`,
                icon: `icon-set-picker ic-recipientnondynamic`,
            });
        }

        const listOfRecentUser: sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.IUserField[] = this.getRecentlySelectedUsers();

        for (const recentUser of listOfRecentUser) {
            items.push(<IDropDownMenuItem>{
                title: recentUser.displayName,
                key: recentUser.displayName,
                additionalValueProperty: recentUser
            });
        }

        return items;
    }

    browseUser(expression: IExpression, returnTypes: string[]): void {
        this.popupService.showUserDialog((item, cancelled) => {
            if (!cancelled) {
                let itemToAdd = null;

                if (item.type === WorkflowContextBrowserItemTypes.identityGroup) {
                    itemToAdd = this.smartObjectService
                        .wrapEmailForGroupUsersField(item.displayName,
                            item.systemName,
                            item.label);
                }
                else {
                    itemToAdd = sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.UserField
                        .createInstance({
                            title: item.displayName,
                            email: item.email,
                            displayName: item.displayName,
                            systemName: item.systemName,
                            identityType: item.type.toString(),
                            returnType: sourceCode.k2Designer.objectModel.core.configuredSmartFields.ConfiguredSmartFieldTypes.userEmail
                        });
                }

                this.selectUser(expression, itemToAdd);
                if (!this.userDropDownItems.find(
                    (existingItem) => existingItem.additionalValueProperty &&
                        (existingItem.additionalValueProperty?.systemName === item.systemName))) {
                    this.recentsService.addItem(
                        sourceCode.k2Designer.services.studio.RecentsService.COMMONCONTEXTS.RecentlySelectedUsers,
                        item, `systemName`);
                    this.userDropDownItems.push({
                        title: item.displayName,
                        key: item.displayName,
                        additionalValueProperty: item
                    } as IDropDownMenuItem);
                }
            }
        }, returnTypes);
    }

    toggle(userExpression: IExpression): void {
        this.onNotificationToggled.emit(userExpression);
    }

    recipientSelected(selectedRecipient: IExpression): void {
        this.selectedField = selectedRecipient;
        this.onRecipientSelected.emit(selectedRecipient);
    }

    private getRecentlySelectedUsers() {
        let items = [];
        if (this.recentsService.itemCount(`RecentlySelectedUsers`) > 0) {
            items = this.recentsService.getItems(`RecentlySelectedUsers`, 10);
        }
        return items;
    }


    private selectUser(expression: IExpression, item: IConfiguredSmartField): void {
        if (expression.smartFields) {
            this.undoRedoService.addUndoMarker(`--emailController: select user--`);

            expression.isDynamic = false;
            expression.smartFields.clear();
            expression.smartFields.push(item);
            (expression as coreConfiguredSmartField.IExpressionExtended)
                .notifyChange(this);
        }

    }
}
