import { Component, Inject, Input, OnDestroy } from '@angular/core';
import { IConfigPanelTab } from '../../../../../../types/models/config-panel/i-config-panel-tab';
import { IConfigTabValidatedComponent } from '../../../../../../types/models/config-panel/i-config-tab-validated-component';
import { IObjectPropertiesToListenTo } from '../../../../../../types/models/config-panel/i-objects-properties-to-listen-to';
import { IConfigPanelService } from '../../../../../../types/services/config-panel/i-config-panel-service';
import { IValidationService } from '../../../../../../types/services/validation/ivalidation.service';
import { IValidatedComponent } from '../../../../../../types/validation/i-validated-component';
import { ConfigPanelServiceToken } from '../../../../../services/config-panel/config-panel-service-factory';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { ValidationServiceToken } from '../../../../../services/validation/validation-service-factory';
import { ErrorSeverity } from '../../../../../shared/enums/model/error-severity';
import { EventTypes } from '../../../../../shared/enums/model/event-types';
import { ValidationTypes } from '../../../../../shared/enums/model/validation-types';
import { isUndefinedOrNull } from '../../../../../shared/utils/general';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-plugin-controller',
    template: '<p> Empty Template since it wont be used </p>'
})
export class PluginBaseComponent extends BaseResourceComponent implements OnDestroy {
    @Input() item: IConfigPanelTab;
    @Input() eventConfiguration: sourceCode.k2Designer.objectModel.workflow.process.k2Process.IEventConfiguration;

    /**
     * Override in child component if default tab validation should be overridden
     */
    protected overrideDefaultTabValidation = false;

    constructor(@Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        protected resourceService: ResourceService) {

        super(resourceService);
    }

    ngOnDestroy(): void {
        // make sure no listeners or watches stay live after the scope is destroyed
        if (!isUndefinedOrNull(this.validationListeners)) {
            const listenerKeys = Object.keys(this.validationListeners);
            for (let i = 0; i < listenerKeys.length; i++) {
                this.destroyListener(listenerKeys[i]);
            }
        }
    }

    /**
     * Set the tab validation object to validate and the config panel tab.
     * NOTE/TODO: When config panel feature is worked on, the configpanel tab should be set from parent component
     * (not from child as is currently)
     *
     * @param tabObjectToValidate
     * @param tabItem - config panel tab
     */
    public setTabValidationObject(tabObjectToValidate?: IValidatedComponent, tabItem?: IConfigPanelTab): void {
        if (!this.overrideDefaultTabValidation) {
            //If no specific tab validation specified by child config panel controller,
            //we assume that default validation applies that will badge the tab using error info of
            //the event configuration and its children.

            if (!tabObjectToValidate) {
                //Use default eventconfiguration
                tabObjectToValidate = this.eventConfiguration;
            }

            const objToValidateConfigTab: IConfigTabValidatedComponent = {
                componentObj: tabObjectToValidate,
                compareFullReference: false
            };
            this.addObjectsToValidateConfigTab([objToValidateConfigTab], tabItem);
        }
    }

    private validationTypes: {
        [field: string]: Array<ValidationTypes>;
    } = {};

    private validationConfigs: {
        [field: string]: {
            [validationType: number]: sourceCode.k2Designer.objectModel.validationService.plugins.IValidationTypeItem;
        };
    } = {};

    private modelsPropertiesErrorMessages: {
        [field: string]: sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem;
    } = {};

    //Dictionary of listeners with key of fieldNamepropertyName -
    //The field to validate and the property name of object to listen to
    private validationListeners: {
        [fieldProperty: string]: Function;
    } = {};

    protected get ConfigPanelService(): IConfigPanelService {
        return this.configPanelService;
    }

    /**
     * Binds the validation types to a field.
     *
     * @param fieldName - The string name for this field. It can be anything as long as it is unique in the controller.
     * @param validationData  - Configuration for validation to be done on field (obtain this object by calling helper function createValidationData)
     */
    protected bindValidationToField(fieldName: string, validationData: sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem): void {
        if (!isUndefinedOrNull(validationData.validationConfigs)) {
            const validationTypes: Array<any> = Object.keys(validationData.validationConfigs);
            for (let i = 0; i < validationTypes.length; i++) { // convert from string keys to number. The validation configs uses an enum, so it will be a number
                validationTypes[i] = parseInt(validationTypes[i]);
            }
            this.validationTypes[fieldName] = validationTypes;
        } else {
            throw 'validationConfigs must be specified';
        }

        this.validationConfigs[fieldName] = validationData.validationConfigs;
        this.bindModelForValidation(fieldName, validationData);
    }

    /**
     * Helper function that will create a validationItem object used to validate when listeners are triggered
     * (to pass to bindValidationToFieldAndListen method).
     * Currently, it only does one type of validation on object's property (only one validation config). This could
     * be modified in future to cater for multiple validation configurations.
     *
     * @param errorMessage - Message to display when validation failed
     * @param validationType - Type of validation to perform on object's property
     * @param objectToValidate - Object on which the property to validate lies
     * @param propertyToValidate - Property to perform validation on
     * @param errorSeverity - Type of Error (Warning, Error)
     * @param validationResourceTokens - List of tokens to replace in error message
     * @returns validationData object of type validationItem used by bindValidationToFieldAndListen method
     */
    protected createValidationData(errorMessage: string, validationType: ValidationTypes,
        objectToValidate: sourceCode.k2Designer.objectModel.core.model.IModelObject, propertyToValidate: string, errorSeverity: ErrorSeverity,
        validationResourceTokens: string[] = []): sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem {
        const requiredType = validationType;
        const validationConfigs = {};
        validationConfigs[requiredType] = {
            validationType: requiredType,
            validationFailedMessage: errorMessage
        };

        const validationData = new sourceCode.k2Designer.objectModel.validationService.plugins.ValidatorItem({
            modelObject: objectToValidate,
            propertyName: propertyToValidate,
            validationConfigs,
            validationFailedMessage: errorMessage,
            errorSeverity,
            validationResourceTokens
        });

        return validationData;
    }

    /**
     * Binds the validation types to a field. Adds listeners to specified properties that will trigger validation on field.
     *
     * @param fieldName - The string name for this field. It can be anything as long as it is unique in the controller.
     * @param validationData - Configuration for validation to be done on field (obtain this object by calling helper function createValidationData)
     * @param propertiesToListenTo - Collection of objects and their properties on which listeners will be bound that triggers the validation
     * @param condition - (Optional) Function that will return true or false. Default is true, if not specified. If function evaluates to true, validation
     * on field will be done. If function evaluates to false, no validation will be done on field and previous errors will be cleared.
     */
    protected bindValidationToFieldAndListen(fieldName: string, validationData: sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem,
        propertiesToListenTo: IObjectPropertiesToListenTo[], condition?: Function
    ): void {

        if (typeof condition !== 'function') {
            condition = () => true;
        }

        //Bind validation to field with specified validation data
        this.bindValidationToField(fieldName, validationData);

        //Add listener to all of the properties it should listen to
        for (let p = 0; p < propertiesToListenTo.length; p++) {
            const model = propertiesToListenTo[p].object;
            const properties = propertiesToListenTo[p].properties;
            for (let q = 0; q < properties.length; q++) {
                const propertyName = properties[q];
                this.applyListener(fieldName, model, propertyName, condition);
            }
        }

    }

    private destroyListener(key: string): void {
        if (typeof this.validationListeners[key] === 'function') {
            this.validationListeners[key]();
            this.validationListeners[key] = null;
        }
    }


    /**
     * Applies correct listener for either array or property
     *
     * @param fieldName - field on which validation will be performed when listener triggered
     * @param model - object on which to listen to
     * @param propertyName - property of object on which to listen to
     * @param condition - function that will evaluate if validation should be performed
     */
    private applyListener(fieldName: string, model: sourceCode.k2Designer.objectModel.core.model.IModelObject, propertyName: string, condition: Function) {
        if (!isUndefinedOrNull(model) && Array.isArray(model[propertyName])) {
            this.validateArrayOnChange(fieldName, model, propertyName, condition);
        } else {
            this.validatePropertyOnChange(fieldName, model, propertyName, condition);
        }
    }

    /**
     * Applies listener for array
     *
     * @param fieldName - field on which validation will be performed when listener triggered
     * @param model - object on which to listen to
     * @param arrayProperty - array property of object on which to listen to
     * @param condition - function that will evaluate if validation should be performed
     */
    protected validateArrayOnChange(fieldName: string, model: sourceCode.k2Designer.objectModel.core.model.IModelObject, arrayProperty: string, condition: Function) {
        if (model && model[arrayProperty] && model[arrayProperty].listen) {
            const key = fieldName + '_' + arrayProperty;
            this.destroyListener(key);
            this.validationListeners[key] = model[arrayProperty].listen(EventTypes.arrayChanged,
                this.onArrayListenerTriggered.bind(this, fieldName, condition));
        }
    }


    /**
     * Applies listener for property validation
     *
     * @param fieldName - field on which validation will be performed when listener triggered
     * @param model - object on which to listen to
     * @param property - property of object on which to listen to
     * @param condition - function that will evaluate if validation should be performed
     */
    protected validatePropertyOnChange(fieldName: string, model: sourceCode.k2Designer.objectModel.core.model.IModelObject, property: string, condition: Function) {
        if (model && model.listen) {
            const key = fieldName + '_' + property;
            this.destroyListener(key);
            this.validationListeners[key] = model.listen(EventTypes.propertyChanged,
                this.onPropertyListenerTriggered.bind(this, fieldName, property, condition));
        }
    }

    /**
     * Triggered on change of array. Accepts the standard IArrayChangedEvent parameters.
     * If condition function evaluates to true (default), array will validated. If not, validation on field will be forced to pass.
     *
     * @param fieldName - field on which validation will be performed when listener triggered
     * @param condition - function that will evaluate if validation should be performed
     * @param arrayObj - array that changed, triggering listener
     * @param name
     * @param oldVal
     * @param newVal
     */
    private onArrayListenerTriggered(fieldName: string, condition: Function,
        arrayObj: sourceCode.k2Designer.objectModel.core.model.IModelObject, name: string, oldVal: any, newVal: any): boolean {
        // since we listened to the array, no need to compare the property to the name
        if (condition() === true) {
            this.validateField(fieldName);
        } else {
            this.forcePassValidateField(fieldName);
        }
        return true;
    }

    /**
     * Triggered on change of property. Accepts the standard IPropertyChangedEvent parameters.
     * Checks if the correct property has changed, and then validates.
     * If condition function evaluates to true (default), property will validated. If not, validation on field will be forced to pass.
     *
     * @param fieldName - field on which validation will be performed when listener triggered
     * @param property - property that should be validated
     * @param condition - function that will evaluate if validation should be performed
     * @param obj - Model object where property changed exists
     * @param name - name of property changed
     * @param oldVal
     * @param newVal
     */
    private onPropertyListenerTriggered(fieldName: string, property: string, condition: Function,
        obj: sourceCode.k2Designer.objectModel.core.model.IModelObject, name: string, oldVal: any, newVal: any): boolean {
        if (property === name) {
            if (condition() === true) {
                this.validateField(fieldName);
            } else {
                this.forcePassValidateField(fieldName);
            }
        }
        return true;
    }

    /**
     * Does validation for specified field
     *
     * @param fieldName
     */
    protected validateField(fieldName: string): boolean {
        this.validate(fieldName);
        return true;
    }

    /**
     * Pass validation for specified field, using the validation data configured
     *
     * @param fieldName field to pass validation on
     */
    protected forcePassValidateField(fieldName: string): boolean {
        const validationData = this.modelsPropertiesErrorMessages[fieldName];
        if (isUndefinedOrNull(validationData)) return false;
        this.validationService.forcePassValidation(<sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem>{
            modelObject: validationData.modelObject,
            propertyName: validationData.propertyName
        });
        return true;
    }

    /**
     * Returns true if the validation for a specific field exists
     *
     * @param fieldName
     */
    protected isModelBoundForField(fieldName: string): boolean {
        return !isUndefinedOrNull(this.modelsPropertiesErrorMessages[fieldName]);
    }

    /**
     * Binds a model to a valdation field. The validation field should already exist when you call this.
     *
     * @param fieldName
     * @param validationData
     */
    private bindModelForValidation(fieldName: string, validationData: sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem): void {
        if (isUndefinedOrNull(this.validationTypes[fieldName])) {
            throw 'No validations bound to this field yet. Use bindValidationtoField first';
        }

        if (!isUndefinedOrNull(validationData.modelObject) &&
            !isUndefinedOrNull(validationData.propertyName) && validationData.propertyName !== '') {
            this.modelsPropertiesErrorMessages[fieldName] = validationData;
        }
    }

    /**
     * Validate a field.
     * bindValidationToField must have been called before calling this
     *
     * @param fieldName
     * @param validationData
     */
    protected validate(fieldName: string, validationData?: sourceCode.k2Designer.objectModel.validationService.plugins.IValidatorItem): sourceCode.k2Designer.objectModel.core.model.IErrorInformation[] {

        let validationResult: sourceCode.k2Designer.objectModel.core.model.IErrorInformation[];

        if (isUndefinedOrNull(validationData)) {
            validationData = this.modelsPropertiesErrorMessages[fieldName];
        }

        validationResult = this.validationService.evaluate(this.validationTypes[fieldName], validationData);

        return validationResult;
    }

    /**
     * Add objects that should be used to validate tab. If one of these objects is in error, the tab will be in error.
     *
     * @param objToValidate collection of objects to watch for tab validation
     */
    protected addObjectsToValidateConfigTab(objsToValidate: IConfigTabValidatedComponent[], currentTab: IConfigPanelTab): void {
        if (isUndefinedOrNull(currentTab)) return;
        if (isUndefinedOrNull(currentTab.componentsToWatch)) {
            currentTab.componentsToWatch = [];
        }

        for (const objToValidate of objsToValidate) {
            if (!isUndefinedOrNull(objToValidate.componentObj)) {
                const count = currentTab.componentsToWatch.length === 0 ? 0 :
                    currentTab.componentsToWatch.filter(
                        (comp: IConfigTabValidatedComponent) =>
                            comp.componentObj === objToValidate.componentObj ||
                            comp.componentObj.getReference() === objToValidate.componentObj.getReference()).length;
                if (count === 0) {
                    currentTab.componentsToWatch.push(objToValidate);
                }
            }
        }
        this.configPanelService.notifyConfigTabErrorDataSet(this.item);
    }
}
