import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { IRulePluginService } from 'src/types/services/rules/i-rule-plugin.service';
import { MockRulePluginService } from '../../testing/mock-services/rule-plugin-service';

const legacyRulePluginServiceFactory = () => environment.devHarness === true
    ? new MockRulePluginService()
    : sourceCode.k2Designer.core.environmentService.rulePluginService;

export const LegacyRulePluginServiceToken = new InjectionToken<IRulePluginService>('IRulePluginService');

export const LegacyRulePluginServiceFactory = {
    provide: LegacyRulePluginServiceToken,
    useFactory: legacyRulePluginServiceFactory,
    deps: []
};
