import { Component, Inject, Input, OnDestroy } from '@angular/core';
import { IActivityConfiguration } from '../../../../../../types/models/process/activities/configuration/i-activity-configuration';
import { IConfiguredRule } from '../../../../../../types/models/rules/i-configured-rule';
import { IConfigPanelService } from '../../../../../../types/services/config-panel/i-config-panel-service';
import { IEnvironmentService } from '../../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../../types/services/plugin/i-plugin-service';
import { IValidationService } from '../../../../../../types/services/validation/ivalidation.service';
import { ConfigPanelServiceToken } from '../../../../../services/config-panel/config-panel-service-factory';
import { EnvironmentServiceToken } from '../../../../../services/environment/environment-service-factory';
import { PluginServiceToken } from '../../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { RulePluginService } from '../../../../../services/ruleplugin/rule-plugin-service';
import { UndoRedoServiceToken } from '../../../../../services/undo-redo/undo-redo-service-factory';
import { ValidationServiceToken } from '../../../../../services/validation/validation-service-factory';
import { plugins } from '../../../../../shared/constants/plugins';
import { RuleContextHandlerNames } from '../../../../../shared/enums/rules/rule-context-handler-names';
import { isUndefinedOrNull } from '../../../../../shared/utils/general';
import { PluginBaseComponent } from '../plugin-base/plugin-base.component';

@Component({
    selector: 'k2-rule-plugin-base',
    template: '<p> Empty Template since it wont be used </p>'
})
export abstract class RulePluginBaseComponent extends PluginBaseComponent implements OnDestroy {

    constructor(@Inject(ValidationServiceToken) protected validationService: IValidationService,
        @Inject(ConfigPanelServiceToken) protected configPanelService: IConfigPanelService,
        protected rulePluginService: RulePluginService,
        @Inject(UndoRedoServiceToken) protected undoRedoService: IUndoRedoService,
        @Inject(PluginServiceToken) protected pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) protected environmentService: IEnvironmentService,
        protected resourceService: ResourceService,
        @Inject('ruleContextHandlerName') protected ruleContextHandlerName: RuleContextHandlerNames) {
        super(validationService, configPanelService, resourceService);
    }

    ngOnDestroy(): void {
        super.ngOnDestroy();
    }

    /**
     * Abstract methods to be implemented by implementor classes
     * */
    protected abstract loadAndPopulateRuleDesigner(): void;
    protected abstract refreshTabIndexes(): void;
    protected abstract selectRuleType(ruleType: number): void;
    protected abstract renderRuleDesigner(): void;


    /*
     * Load an empty rule designer, and ensure it's collapsed
     */
    protected loadEmptyRuleDesigner(): void {
        this.rulePluginService.renderEmptyDesigner(this.ruleContextHandlerName);
    }

    /*
     * Toggle the rule designer, expanded or collapsed.
     */
    public toggleRulesDesigner(): void {
        this.undoRedoService.addCustomUndoEvent((): boolean => {
            this.toggleRulesDesigner();
            return true;
        }, null, '-- toggle rule designer --');

        this.loadAndPopulateRuleDesigner();
        this.rulePluginService.renderCanvas();
        this.pluginService.toggleState(plugins.panels.rulesDesigner);
    }

    /*
     * Call this whenever the rule type has changed. It will update tab indexes and update it when undo redo is invoked.
     */
    protected onRuleTypeSelected(): void {
        this.refreshTabIndexes();
        this.renderRuleDesigner();

        this.undoRedoService.addCustomUndoEvent((): boolean => {
            this.renderRuleDesigner();
            // use setTimeout, because when redoing it will execute this before making the model changes.
            // This only updates the tab index, so should have no effect on logic. No-one should read the tab index, should they?
            setTimeout(() => {
                this.refreshTabIndexes();
            });
            // Just in case the edit rules button is focussed. It should not be focussed when it is disabled
            $(document.activeElement).blur();
            return true;
        }, null, '-- calculate tab index for edit rule --');
    }

    /*
     * Used to apply the highlighting class on the expand button
     */
    public isRulesActive(): boolean {
        return this.environmentService.activeStudioItem.pluginStates.rulesDesigner === 'expanded';
    }

    protected setSavedCustomStatementsById(id: string, rule: IConfiguredRule): void {
        const savedStatements = this.rulePluginService.getCustomRuleStatements(id);
        if (!isUndefinedOrNull(savedStatements) && !isUndefinedOrNull(rule)) {
            rule.statements = savedStatements;
        }
    }

    protected setSavedCustomStatements(activityConfig: IActivityConfiguration, rule: IConfiguredRule): void {
        this.setSavedCustomStatementsById(this.ruleContextHandlerName + activityConfig.getId(), rule);
    }
}
