import { Component, Input, OnChanges, OnInit, SimpleChanges } from '@angular/core';
import { IConfiguredStatement } from '../../../../../types/models/rules/i-configured-statement';
import { IItemConfiguration } from '../../../../../types/models/rules/i-item-configuration';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';
import { StatementDirectives } from '../../../../shared/enums/rules/statement-directives';

@Component({
    selector: 'k2-rule-statement-block',
    templateUrl: './rule-statement-block.component.html',
    styleUrls: ['./rule-statement-block.component.less']
})
export class RuleStatementBlockComponent {
    @Input() public statementModel: IConfiguredStatement[];

    public statementDirectives = StatementDirectives;

    constructor(private _rulePluginService: RulePluginService) { }

    public getConfig(statementModel): IItemConfiguration {
        return this._rulePluginService.getStatementConfiguration(statementModel);
    }
}
