import { Component, Input, Output, ViewChild, EventEmitter, OnInit, OnDestroy } from '@angular/core';
import { ChooseControlTypes } from 'src/app/shared/enums/choose-control/choose-control-types';
import { ChooseControlTemplates } from 'src/app/shared/enums/choose-control/choose-control-templates';
import { ChooseControlDefaultLabelComponent } from './templates/choose-control-default-label/choose-control-default-label.component';
import { IStates } from '../../../../types/models/rules/i-states';
import { States } from '../../../shared/control-helpers/states';
import { EventNotifierService } from '../../../services/event-notifier/event-notifier.service';
import { Subscription } from 'rxjs';
import { RadioButtonChangedEvent } from '../../../shared/constants/events';
import { IRadioEvent } from '../../../../types/events/choose-control/i-radio.event';
import { IValidatedComponent } from '../../../../types/validation/i-validated-component';
import IDropDownMenuItem from '../../../shared/enums/drop-down/drop-down-menu-item';

@Component({
    selector: 'k2-choose-control-v2', //TODO: rename once the AngularJS directive is deprecated
    templateUrl: './choose-control.component.html',
    styleUrls: ['./choose-control.component.less']
})
export class ChooseControlComponent implements OnInit, OnDestroy {
    //The disabled state for the input element
    @Input() disabled: boolean;

    //The type of selection control to use
    @Input() type: ChooseControlTypes;

    //The checked state for the radio button
    @Input() set checked(val: boolean) {
        if (val !== this._checked) {
            if (this._checked !== null) { // should not emit change if it is initial value set
                if (val) { // should only emit radio event if the radio value is true and not initial
                    this._radioEvent?.emit({ source: this, name: this.name });
                }
                this.checkedChange.emit(val); // this should only fire after the radio emit
            }
            this._checked = val;

            if (this.labelTemplate === ChooseControlTemplates.chooseControlDefaultLabel) {
                this.defaultLabel?.onToggle();
            }
        }
    }

    //The group name for the input element, used to set radio group behaviour
    @Input() name: string;

    //Identify which label template to use
    //Defaults to chooseControlDefaultLabel template if not specified
    @Input() labelTemplate: ChooseControlTemplates = ChooseControlTemplates.chooseControlDefaultLabel;

    //The id to use for the input tag
    @Input() id: string;

    //The text to display in the label
    @Input() displayText: string;

    //custom class to attach to the label element
    @Input() customLabelClass: string;

    //Should add the dynamic button
    @Input() isDynamicMode: boolean;

    //Called when toggle button is clicked
    @Input() onToggle: () => void;

    //States that determine if the control is toggled
    @Input() isToggled: boolean;

    //Can be set as a callback when the dynamic mode changes
    @Input() dynamicModeChanged: (isToggled: boolean) => void;

    //Value used for dynamic mode textbox and custom validation
    @Input() modelValue: IValidatedComponent;

    //Control states for styling
    @Input() states: IStates = new States();

    @Input() selectedValue: string;

    @Input() additionalDropdownItems: IDropDownMenuItem[];

    @Input() footerItems: IDropDownMenuItem[];

    @Input() isDarkStyle: boolean;

    //When the control is blurred
    @Output() onBlurred = new EventEmitter<any>();

    //When the dynamic mode is toggled
    @Output() onModeToggled = new EventEmitter<any>();

    //When the textbox in dynamic mode is blurred
    @Output() onTextBoxBlurred = new EventEmitter<any>();

    // Two way binding checked emitter
    @Output() checkedChange = new EventEmitter<boolean>();

    @ViewChild(ChooseControlDefaultLabelComponent) defaultLabel: ChooseControlDefaultLabelComponent;

    //Enumerator types
    chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;
    chooseControlTemplates: typeof ChooseControlTemplates = ChooseControlTemplates;

    private _checked = null;
    private _radioEventSub: Subscription;
    private _radioEvent: EventEmitter<IRadioEvent>;

    get checked(): boolean {
        return this._checked;
    }

    constructor(private _eventNotifierService: EventNotifierService) {
    }

    ngOnInit() {
        if (this.type === ChooseControlTypes.radiobutton && this.name) {
            this._radioEvent = this._eventNotifierService.addEvent(RadioButtonChangedEvent);
            this._radioEventSub = this._eventNotifierService.subscribe(RadioButtonChangedEvent, (event: IRadioEvent) => {
                if (event.source !== this && event.name === this.name) { // changes value if another item in the radio group is selected
                    this.checked = false;
                }
            });
        }
    }

    ngOnDestroy() {
        this._radioEventSub?.unsubscribe();
    }

    //Unused by any components at the moment (dynamic mode)
    onModeToggle() {
        this.onModeToggled.emit();
    }

    onTextBoxBlur() {
        this.onTextBoxBlurred.emit();
    }

    onClick() {
        if (!this.disabled) {
            switch (this.type) {
                case ChooseControlTypes.checkbox:
                    this.checked = !this.checked;
                    break;
                case ChooseControlTypes.radiobutton:
                    this.checked = true;
                    break;
            }
        }
    }
}

