import { IExternalReferenceDefinition } from '../../../types/models/core/external-reference-definitions/i-external-reference-definition';
import { ISmartObjectMethodField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-method-field';
import { ProcessService } from '../../services/process/process.service';
import { ISmartObjectPropertyField } from '../../../types/models/core/external-reference-definitions/smart-objects/i-smart-object-property-field';
import { IPropertyMapperRow } from '../../../types/uiComponents/property-mapper/i-property-mapper-row';
import { isUndefinedOrNull } from './general';
import { IModelArray } from '../../../types/models/base/i-model-array';

export namespace ExternalReferenceUtilities {
    /**
     * Returns an external reference token for an external reference definition's property based on the type and key.
     *
     * @param externalDefinitionParent parent external reference method
     * @param key property system name
     * @param isReturn true if return property (false if input property)
     * @param addIfNotExists adds property if it doesn't exist (default true)
     */
    export function getExternalDefinitionToken(
        externalDefinitionParent: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectMethodField | ISmartObjectMethodField,
        key: string, isReturn: boolean, addIfNotExists: boolean = true): string {
        if (externalDefinitionParent) {
            let exRefParentProperties = externalDefinitionParent.returns as IModelArray<IExternalReferenceDefinition>;
            if (!isReturn) {
                exRefParentProperties = externalDefinitionParent.inputs;
            }
            let smartObjectMethodProperty: sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField =
                exRefParentProperties.single<sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.ISmartObjectPropertyField>({
                    systemName: key
                });

            if (!smartObjectMethodProperty && addIfNotExists) {
                //It doesn't exist, add to returns / inputs collection
                smartObjectMethodProperty = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                    systemName: key
                }); 

                exRefParentProperties.push(smartObjectMethodProperty);
            }
            if (smartObjectMethodProperty) {
                return smartObjectMethodProperty.getReference();
            }
        }

        return key;
    }

    /**
     * Evaluates if the specified property exists on the external definition parent and returns the reference to that property, or adds the property and returns the reference
     * @param externalDefinitionParent
     * @param properties
     * @param key
     */
    export function getPropertyMapperExternalDefinitionToken(externalDefinitionParent: ISmartObjectMethodField,
        properties: IPropertyMapperRow[], key: string): string {

        if (externalDefinitionParent) {
            let property: IPropertyMapperRow = null;

            //Find the property in available properties, we need information of this property for the external reference
            for (let p: number = 0; p < properties.length; p++) {
                if (properties[p].key === key) {
                    property = properties[p];
                    break;
                }
            }

            if (isUndefinedOrNull(property)) {
                return key;
            }

            let smartObjectMethodProperty: ISmartObjectPropertyField = property.isReturnProperty === true ?
                externalDefinitionParent.returns.single<ISmartObjectPropertyField>({
                    systemName: key
                }) :
                externalDefinitionParent.inputs.single<ISmartObjectPropertyField>({
                    systemName: key
                });

            // if it is an older workflow where a parameter was wrongly added to the inputs, remove it.
            if (smartObjectMethodProperty && property.isParameter === true) {
                smartObjectMethodProperty.removeFromModelArray();
                smartObjectMethodProperty = null;
            }

            // if the isParameter flag is not explicitly false, look in the parameters array for it
            if (!smartObjectMethodProperty && property.isParameter !== false) {
                smartObjectMethodProperty =
                    externalDefinitionParent.parameters.single<ISmartObjectPropertyField>({
                        systemName: key
                    });
            }

            if (!smartObjectMethodProperty) {
                smartObjectMethodProperty = sourceCode.k2Designer.objectModel.core.externalReferenceDefinitions.SmartObjectPropertyField.createInstance({
                    systemName: key,
                    type: property.type.toLowerCase(),
                    displayName: property.rowTitle,
                    isRequired: property.required
                }) as ISmartObjectPropertyField;

                if (property.isParameter === true) {
                    externalDefinitionParent.parameters.push(smartObjectMethodProperty);
                } else if (property.isReturnProperty === true) {
                    externalDefinitionParent.returns.push(smartObjectMethodProperty);
                } else {
                    externalDefinitionParent.inputs.push(smartObjectMethodProperty);
                }

                return smartObjectMethodProperty.getReference();
            }

            //Now ensure the information of the property is set
            smartObjectMethodProperty.type = property.type.toLowerCase();
            smartObjectMethodProperty.displayName = property.rowTitle;
            smartObjectMethodProperty.isRequired = property.required;

            return smartObjectMethodProperty.getReference();
        }

        return key;
    }

    /**
     * Evaluates a string to find a external definition on the process and return the resolved value based on the property.
     * By default the property is systemName
     *
     * @param token
     * @param processService
     * @param property
     */
    export function resolveExternalReferenceToken(token: string, processService: ProcessService, property: string = ''): string {
        let resolvedToken: string = token;

        if (property === '') {
            property = 'systemName';
        }

        const externalDef: IExternalReferenceDefinition = processService.root.findItem<IExternalReferenceDefinition>(token);

        if (externalDef) {
            resolvedToken = externalDef[property] ? externalDef[property] : token;
        }

        return resolvedToken;
    }

}
