        this.loadPopOverTmpl();

        this.isRecentProvider = false;

        this.isLoading = false;

        if (this.getPrvsRootCats() && this.getPrvsRootCats()[0]) {
            this._toolBoxService.ensureRecentsDefinition(this.getPrvsRootCats()[0].items);
        }

        if (!this.rootText) {
            this.rootText = this._resourceService.getString('toolbox_breadcrumb_root_text');
        }

