    ngOnInit(): void {
        const providers: sourceCode.k2Designer.objectModel.core.itemProvider.IInstanceDefinition[] =
            this._pluginService.getItemProviderInstances(
                this.uiComp.name,
                this._envService.activeStudioItem.studioItemManagementService.itemInstanceConfiguration.name
            );
        if (providers) {
            this.prvLoadedCount = providers.length;
            for (const provider of providers) {
                this._itemProviderService.getItemProviderByName(provider.name)
                    .listenInitialDataLoaded(this.checkProviderInitialDataLoaded.bind(this));
            }
        }
    }

