    public favouritesToolboxItemContextMenu(event: MouseEvent, context?: any): void {
        const _toolBoxService = this._toolBoxService;
        const favouriteIcon = 'icon icon-set-edittoolbox_popover icon-size16 ic-favourites';
        let pinTitle;

        if (_toolBoxService.checkIfFavouritesContains(context)) {
            pinTitle = this._resourceService.getString('context_menu_favourites_unpin');
        }
        else {
            pinTitle = this._resourceService.getString('context_menu_favourites_pin');
        }

        const configuration: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuConfiguration =
            <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuConfiguration>{
                e: event,
                menuItems: <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem[]>[
                    <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem>{
                        title: pinTitle,
                        enabled: true,
                        icon: favouriteIcon,
                        onSelected: (item: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem) => {
                            if (_toolBoxService.checkIfFavouritesContains(context)) {
                                _toolBoxService.removeFavouriteItem(context);
                            } else {
                                _toolBoxService.addFavouriteItem(0, context);
                            }

                        }
                    },
                    <sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem>{
                        title: this._resourceService.getString('context_menu_favourites_help'),
                        enabled: true,
                        icon: 'icon icon-set-edittoolbox_popover icon-size16 ic-help',
                        onSelected: (item: sourceCode.k2Designer.objectModel.contextMenu.IContextMenuItem) => {
                            window.open(sourceCode.k2Designer.utilities.buildHelpLinkUrl(3), '_blank');
                        }
                    }],
                width: 140
            };

        sourceCode.k2Designer.uiComponents.contextMenu.showContextMenu(configuration);
    }

