import { Component, Input, OnInit } from '@angular/core';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { IDataMapperContext } from 'src/types/uiComponents/pluginWizards/IDataMapperContext';
import { ResourceService } from '../../../../services/resource/resource.service';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { ProcessService } from '../../../../services/process/process.service';
import { PageComponent } from '../../page/page.component';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';
import { IModelArray } from 'src/types/models/base/i-model-array';

/**
 * Parent component to be extended by Event Platform components
 * */

@Component({
    selector: 'k2-data-mapper',
    templateUrl: './data-mapper.component.html',
    styleUrls: ['./data-mapper.component.less']
})
export class DataMapperComponent extends PageComponent implements OnInit {

    @Input() config: IPopupConfiguration;
    @Input() values: IDropDownMenuItem[] = [];

    buttonConfig: IPopupButtonsConfig;

    context: IDataMapperContext;
    rows: IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.setDataFields.ISetDataFieldMapping> = [];
    datafields: IModelArray<sourceCode.k2Designer.objectModel.workflow.process.k2Process.IDataField> = [];
    existingDataFields: string[] = [];
    resources: any = {};
    showMapper: boolean;
    addedDataFieldsReferences: string[] = [];

    constructor(protected resourceService: ResourceService,
        protected processService: ProcessService,
        protected _eventService: EventNotifierService) {
        super(resourceService);
    }

    ngOnInit(): void {
        this.context = this.config.context as IDataMapperContext;
        this.existingDataFields = [];
        this.datafields = this.processService.root.configuration.dataFields || [];
        this.existingDataFields = this.getExistingDataFields();
        this.rows = this.context.model?.mapping?.data?.fields || [];

        this.resources = {
            createVariablesHeading: this.resourceService.getString('event_platform_on_error_event_create_variables_heading'),
            createVariablesButtonLabel: this.resourceService.getString('event_platform_create_variables_button_label'),
            createVariablesManuallyLabelPart1: this.resourceService.getString('event_platform_create_variables_manually_label_part_1'),
            createVariablesManuallyLabelPart2: this.resourceService.getString('event_platform_create_variables_manually_label_part_2'),
            clearMappingsText: this.resourceService.getString('event_platform_clear_mappings_text'),
            clearMappingsTooltip: this.resourceService.getString('event_platform_clear_mappings_tooltip'),
            startRightsHeading: this.resourceService.getString('event_platform_start_rights_heading'),
            startRightsMessage: this.resourceService.getString('event_platform_start_rights_message')
        };

        //Needs this for edit to get to second state without clicking buttons.
        if (this.rows.length > 0) {
            this.showMapper = true;
        }
    }
    beforePageDisplayed() {}
    beforeNavigateNext() {}
    beforeNavigatePrevious() {
        if(this.datafields.length > 0) {
            this.removeCreatedVariables();
        }
    }

    public clearMappings(): void {
        this.showMapper = false;
        for (let i: number = this.rows.length - 1; i > -1; i--) {
            this.rows[i].removeFromModelArray();
        }
        this.removeCreatedVariables();
    }

    /**
     * Protected methods to inherit from
     * */

    protected createVariables(): void {
        this.populateAutoGeneratedDataFields();
        this.setMapperProperty();
        this.showMapper = true;
    }

    protected createManually(): void {
        this.showMapper = true;
    }

    private getExistingDataFields(): string[] {
        this.existingDataFields = [];

        if (this.datafields.length > 0) {

            for (const datafield of this.datafields) {
                this.existingDataFields.push(
                    datafield.title
                );
            }
        }
        return this.existingDataFields;
    }

    private removeCreatedVariables(): void {
        for (let i = this.datafields.length - 1; i > -1; i--) {
            if (this.existingDataFields.indexOf(this.datafields[i].title) < 0) {
                // First remove the row, so that it clears the error and doesn't badge/change the wrong row.
                const reference = this.datafields[i].getReference();
                this.rows.find(x => x.dataFieldReference === reference)?.removeFromModelArray();

                this.datafields[i].removeFromModelArray();
            }
        }
    }

    private populateAutoGeneratedDataFields(): void {
        for (const value of this.values) {
            if (!this.dataFieldExist(value.title)) {
                this.datafields.push(sourceCode.k2Designer.objectModel.workflow.process.k2Process.DataField.createInstance({
                    title: value.title,
                    type: value.metaData
                }));
            }
        }
    }

    private dataFieldExist(title: string): boolean {
        for (const datafield of this.datafields) {
            if (datafield.title === title) {
                return true;
            }
        }
        return false;
    }

    private setMapperProperty(): void {

        const trackedReferences = this.processService.root.trackedReferences;

        for (const datafield of this.datafields) {
            if (!this.rowMappingExists(datafield.getReference())) {

                //This will also check if part of values.
                const defaultValue: string = this.getDefaultValue(datafield.title, datafield.type);
                if (defaultValue && !this.rowMappingExists(datafield.dataFieldControlReference)) {
                    const referenceDefinitionPath = datafield.getReference();

                    for (const trackedReference of trackedReferences) {
                        if (trackedReference.trackedObjectDefinitionPath === referenceDefinitionPath) {
                            for (let k = trackedReference.references.length; k >= 0; k--) {
                                this.processService.removeTrackedItem(null, trackedReference.references[k]);
                            }
                        }
                    }

                    // eslint-disable-next-line max-len
                    const row = sourceCode.k2Designer.objectModel.workflow.process.k2Process.events.setDataFields.SetDataFieldMapping.createInstance({
                        dataFieldReference: datafield.getReference(),
                        value: sourceCode.k2Designer.objectModel.core.configuredSmartFields.Expression.createInstance({
                            // eslint-disable-next-line max-len
                            smartFields: [sourceCode.k2Designer.objectModel.workflow.configuredSmartFields.TextField.createInstance({ text: defaultValue })],
                            isDynamic: false
                        })
                    });
                    this.rows.push(row);
                }
            }
        }
    }

    private rowMappingExists(dataFieldReference: string): boolean {
        return this.rows.some((i) => i.dataFieldReference === dataFieldReference);
    }

    private getDefaultValue(title: string, type: sourceCode.k2Designer.objectModel.workflow.process.k2Process.DataFieldTypes): string {
        for (const value of this.values) {
            if (value.title === title && value.metaData === type) {
                return value.key;
            }
        }
        return null;
    }
}
