import { InjectionToken } from '@angular/core';
import { environment } from 'src/environments/environment';
import { MockSmartFieldPluginService } from '../../../testing/mock-services/smart-field-plugin-service';

const smartFieldPluginServiceFactory = () => environment.devHarness === true
    ? new MockSmartFieldPluginService()
    : sourceCode.k2Designer.core.environmentService.getService(sourceCode.k2Designer.services.studio.SmartFieldPluginService);

export const SmartFieldPluginServiceToken = new InjectionToken<ISmartFieldPluginService>('ISmartFieldPluginService');

export const SmartFieldPluginServiceFactory = {
    provide: SmartFieldPluginServiceToken,
    useFactory: smartFieldPluginServiceFactory,
    deps: [

    ]
};
