import { Component, Inject, Input, OnInit, EventEmitter, OnDestroy } from '@angular/core';
import { ResourceService } from '../../../../services/resource/resource.service';
import { ChooseControlTypes } from '../../../../shared/enums/choose-control/choose-control-types';
import { shortUid } from '../../../../shared/utils/general';
import { SmartFormsIntegrationService } from '../../../../services/plugins/smart-forms-integration/smart-forms-integration.service';
import { IProcessPluginDefinitionContext } from '../../../../../types/uiComponents/pluginWizards/IProcessPluginDefinitionContext';
import { EventNotifierService } from '../../../../services/event-notifier/event-notifier.service';
import { Subscription } from 'rxjs';
import { IPopupConfiguration } from '../../../../../types/models/popup/i-popup-configuration';
import { PageComponent } from '../../page/page.component';
import { IPopupButtonsConfig } from 'src/types/config/i-popup-buttons-config';

@Component({
    selector: 'k2-configure-form-rule',
    templateUrl: './configure-form-rule.component.html',
    styleUrls: ['./configure-form-rule.component.less'],
    providers: [{ provide: PageComponent, useExisting: ConfigureFormRuleComponent }]
})
export class ConfigureFormRuleComponent extends PageComponent implements OnInit, OnDestroy {
    buttonConfig: IPopupButtonsConfig = {
        previous: {
            isVisible: true
        },
        next: {},
        finish: {}
    };

    @Input() public config: IPopupConfiguration;

    /**
     * Unique identifier for configure later selection on UI
     */
    public configureLaterUid: string;

    /**
     * Unique identifier for configure now selection on UI
     */
    public configureNowUid: string;

    /**
     * Unique identifier for configure now selection on UI
     */
    public doItForMeUid: string;

    /**
     * Context of the plugin wizard.
     */
    public pluginContext: IProcessPluginDefinitionContext;

    public chooseControlTypes: typeof ChooseControlTypes = ChooseControlTypes;

    formLoaded = false;

    private _getFormSub: Subscription;

    constructor(resourceService: ResourceService,
        private _smartFormsIntegrationService: SmartFormsIntegrationService,
        private _eventService: EventNotifierService) {

        super(resourceService);

        this.configureLaterUid = shortUid();
        this.configureNowUid = shortUid();
        this.doItForMeUid = shortUid();
    }

    ngOnInit(): void {
        this.pluginContext = this.config.context as IProcessPluginDefinitionContext;

        this._getFormSub = this._smartFormsIntegrationService
            .getFormContent(this.pluginContext)
            .subscribe(() => {
                this.formLoaded = true;
            });
    }

    ngOnDestroy() {
        this._getFormSub.unsubscribe();
    }

    beforePageDisplayed() {
        if(!this.pluginContext.configureRuleNow && !this.pluginContext.configureRuleLater){
            this.pluginContext.configureRuleForMe = true;
        }
    }

    beforeNavigateNext() {
        this._smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    beforeNavigatePrevious() {}

    /**
     * Method that gets called when the user select to configure the rule later
     */
    public configureLaterChecked(val: boolean): void {
        if (!val) return;
        //Deployment specifically check for this text, this can't be placed in resource, then runtime will fail.
        if (this.pluginContext.isUserForm) {
            this.pluginContext.newStateName = '(Base State)';
        }

        this._smartFormsIntegrationService.determinePages(this.pluginContext);
    }

    /**
     * Method that gets called when the user select to configure the rule now
     */
    public configureNowChecked(val: boolean): void {
        if (!val) return;
        if (this.pluginContext.isUserForm) {
            this.pluginContext.newStateName = this.getString('smartforms_integration_user_task_state_workflow_task');
        }

        this._smartFormsIntegrationService.determinePages(this.pluginContext);

        this.buttonConfig.next.isVisible = true;
        this.buttonConfig.finish.isVisible = false;

        this.notifyConfigChanged();
    }

    /**
     * Method that gets called when the user select to do it for me option
     */
    public configureForMe(val: boolean): void {
        if (!val) return;
        if (this.pluginContext.isUserForm) {
            this.pluginContext.newStateName = this.getString('smartforms_integration_user_task_state_workflow_task');
        }

        this.buttonConfig.next.isVisible = false;
        this.buttonConfig.finish.isVisible = true;

        this._smartFormsIntegrationService.determinePages(this.pluginContext);
    }
}
