import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IStates } from '../../../../../../types/models/rules/i-states';
import { IValidatedComponent } from '../../../../../../types/validation/i-validated-component';
import { ResourceService } from '../../../../../services/resource/resource.service';
import { States } from '../../../../../shared/control-helpers/states';
import { ChooseControlTypes } from '../../../../../shared/enums/choose-control/choose-control-types';
import { BaseResourceComponent } from '../../../../base-resource/base-resource.component';

@Component({
    selector: 'k2-choose-control-outcome-label',
    templateUrl: './choose-control-outcome-label.component.html',
    styleUrls: ['./choose-control-outcome-label.component.less']
})
export class ChooseControlOutcomeLabelComponent extends BaseResourceComponent implements OnInit {

    @Input() id: string;
    @Input() displayText: string;
    @Input() isChecked: boolean;
    @Input() isDisabled: boolean;
    @Input() isToggled: boolean;
    @Input() modelValue: IValidatedComponent;
    @Input() customLabelClass: string;
    @Input() type: ChooseControlTypes;
    //Control states for styling
    @Input() states: IStates = new States();

    @Output() onClick = new EventEmitter<void>();

    widgetClass = '';
    nonWidgetClass = '';
    hovered = false;
    types: typeof ChooseControlTypes = ChooseControlTypes;

    constructor(protected resourceService: ResourceService) {
        super(resourceService);
    }

    ngOnInit(): void {
    }

    onClicked(): void {
        this.onClick.emit();
    }

}
