import { Component, EventEmitter, HostListener, Inject, Input, OnInit, Output } from '@angular/core';
import { LegacyResourceServiceToken } from 'src/app/legacy-service-factories/resource-service-factory';
import { UndoRedoServiceToken } from 'src/app/services/undo-redo/undo-redo-service-factory';
import { DropDownMenuAlignment } from 'src/app/shared/enums/drop-down/drop-down-menu-alignment';
import { DropdownMenuItemTemplates } from 'src/app/shared/enums/drop-down/drop-down-menu-item-templates';
import { DropDownMenuTrianglePosition } from 'src/app/shared/enums/drop-down/drop-down-triangle-position';
import { EventTypes } from 'src/app/shared/enums/model/event-types';
import { ComplexOperator } from 'src/app/shared/enums/rules/complex-operator';
import { ILegacyResourceService } from 'src/types/services/resources/i-legacy-resource.service';
import IDropDownMenuItem from '../../../../shared/enums/drop-down/drop-down-menu-item';
import { isUndefinedOrNull } from '../../../../shared/utils/general';
import { RulePluginService } from '../../../../services/ruleplugin/rule-plugin-service';

@Component({
    selector: 'k2-group-boolean-operator-expression',
    templateUrl: './group-boolean-operator-expression.component.html',
    styleUrls: ['./group-boolean-operator-expression.component.less']
})
export class GroupBooleanOperatorExpressionComponent implements OnInit {

    @Input() configuredGroupOperator: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredGroupOperator;
    @Input() index: number;

    @Output() onDrop: EventEmitter<number> = new EventEmitter<number>();
    @Output() onGroupOptionSelected: EventEmitter<number> = new EventEmitter<number>();

    public multipleConditionAndOr: IDropDownMenuItem[] = [];
    public title: string;

    private _onModelPropertyChangedRemover: () => void;
    private _isSelecting: boolean;
    private _previousOperatorValue: sourceCode.k2Designer.objectModel.core.configuredRules.ComplexOperator;

    // Enum imports for use in templates
    public dropdownMenuAlignment: typeof DropDownMenuAlignment = DropDownMenuAlignment;
    public dropDownMenuTrianglePosition: typeof DropDownMenuTrianglePosition = DropDownMenuTrianglePosition;
    public dropdownMenuItemTemplate: typeof DropdownMenuItemTemplates = DropdownMenuItemTemplates;

    constructor(
        @Inject(LegacyResourceServiceToken) private _resourceService: ILegacyResourceService,
        private _rulePluginService: RulePluginService,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService) {
    }

    ngOnInit(): void {
        this.setupMultipleConditionDropdownItems();

        this.setTitle();

        this._onModelPropertyChangedRemover = this.configuredGroupOperator
            .listen(EventTypes.propertyChanged, this.onModelPropertyChanged.bind(this));
    }

    ngOnDestroy(): void{
        if(this._onModelPropertyChangedRemover) {
            this._onModelPropertyChangedRemover();
        }
    }

    @HostListener('drop', ['$event'])
    public dropped(evt: Event): boolean {
        this.onDrop.emit(this.index);
        return false;
    }

    @HostListener('dragleave', ['$event'])
    private dragleft(evt: Event): boolean {
        evt.preventDefault();
        evt.stopPropagation();
        return false;
    }

    @HostListener('dragenter', ['$event'])
    private dragentered(evt: Event): boolean {
        evt.preventDefault();
        evt.stopPropagation();
        return false;
    }

    private onModelPropertyChanged = ()=> {

        if(!this._isSelecting) {
            this.setSelectedItem();
            this.setTitle();

            this._previousOperatorValue = this.configuredGroupOperator.expressionOperator;
        }
    };

    private setTitle = (): void => {
        // By the time this is reached, the dropdown should have been set up, and the default item selected.
        // If not, show that nothing is selected.
        this.title = this.multipleConditionAndOr.find(x => x.isSelected)?.title ?? 'ERROR - NO OPTION SELECTED';
    };

    private setSelectedItem = (): void => {
        this.multipleConditionAndOr.forEach(
            x => {
                x.isSelected = x.key === this.configuredGroupOperator.expressionOperator.toString()
                    ? true
                    : false;
            });
    };

    private addCondition = (): void => {
        const expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression =
            this._rulePluginService.createExpression('SimpleExpression') as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression;

        this.addExpressionToModel(expression);
    };

    private addTaskCondition = (): void => {
        const expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression =
            this._rulePluginService.createExpression('TaskExpression') as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression;

        this.addExpressionToModel(expression);
    };

    private addFilterExpression = (): void => {
        const expression: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression =
            this._rulePluginService.createExpression('FilterExpression') as sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression;

        this.addExpressionToModel(expression, true);
    };

    private addExpressionToModel(expression?: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression, isForFilterExpression: boolean = false): void {

        const operator: sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredGroupOperator = this._rulePluginService.createOperator('GroupBooleanOperator');
        operator.configureForFilterControl = isForFilterExpression;

        const parent: sourceCode.k2Designer.objectModel.core.model.IModelArray<sourceCode.k2Designer.objectModel.core.configuredRules.IConfiguredExpression> =
            this.configuredGroupOperator.getParentModel();

        this._undoRedoService.addUndoMarker('-- group boolean operator - addExpression selected --');

        //Insert after current expression.
        const currentExpression_index = parent.indexOf(this.configuredGroupOperator);
        parent.splice(currentExpression_index + 1, 0, expression);
        parent.splice(currentExpression_index + 2, 0, operator);
    }

    private onItemSelected = (dropdownItem: IDropDownMenuItem): void => {
        this._undoRedoService.addUndoMarker('-- group boolean operator selected --');

        this._isSelecting = true;

        this.title = dropdownItem.title;

        switch (dropdownItem.key) {
            case ComplexOperator.And.toString():
                this.configuredGroupOperator.expressionOperator = ComplexOperator.And;
                break;
            case ComplexOperator.Or.toString():
            default:
                this.configuredGroupOperator.expressionOperator = ComplexOperator.Or;
                break;
        }

        this._previousOperatorValue = this.configuredGroupOperator.expressionOperator;
        this._isSelecting = false;
    };

    private setupMultipleConditionDropdownItems(): void {

        this.multipleConditionAndOr = [{
            key: ComplexOperator.And.toString(),
            isSeparator: false,
            title: this._resourceService.getString('ruledesigner_logical_and_operator_cap'),
            isSelected: this.configuredGroupOperator.expressionOperator === ComplexOperator.And,
            onSelected: this.onItemSelected
        }, {
            key: 'seperator-1',
            isSeparator: true
        }, {
            key: ComplexOperator.Or.toString(),
            isSeparator: false,
            title: this._resourceService.getString('ruledesigner_logical_or_operator_cap'),
            isSelected:  isUndefinedOrNull(this.configuredGroupOperator.expressionOperator) // if it is "Or", the value is removed from the model
                || this.configuredGroupOperator.expressionOperator === ComplexOperator.Or,
            onSelected: this.onItemSelected
        }, {
            key: 'seperator-2',
            isSeparator: true
        }];

        if (!this.configuredGroupOperator.configureForFilterControl) {
            this.multipleConditionAndOr.push({
                key: 'g',
                icon: 'icon icon-set-conditionsdropdown icon-size16 ic-groupconditions',
                isSeparator: false,
                title: this._resourceService.getString('ruledesigner_group_conditions_cap'),
                ignoreUpdateSelection: true,
                onSelected: () => {
                    this.onGroupOptionSelected.emit(this.index);
                }
            });
        }

        this.multipleConditionAndOr.push({
            key: 'c',
            icon: 'icon icon-set-rulestoolbar icon-size16 ic-condition',
            isSeparator: false,
            title: this._resourceService.getString('ruledesigner_add_condition_cap'),
            ignoreUpdateSelection: true,
            onSelected: this.configuredGroupOperator.configureForFilterControl
                ? this.addFilterExpression
                : this.addCondition
        });

        if (!this.configuredGroupOperator.configureForFilterControl &&
            this._rulePluginService.contextHandler.configuration.expressions.indexOf('TaskExpression') !== -1) {
            this.multipleConditionAndOr.push({
                key: 't',
                icon: 'icon icon-set-rulestoolbar icon-size16 ic-taskcondition',
                isSeparator: false,
                title: this._resourceService.getString('ruledesigner_add_task_condition_cap'),
                ignoreUpdateSelection: true,
                onSelected: this.addTaskCondition
            });
        }
    }
}
