import { Component, Inject, OnInit } from '@angular/core';
import { ISelectionService } from '../../../../types/services/selection/i-selection-service';
import { DraggableDirective } from '../../../directives/draggable/draggable.directive';
import { DragDropService } from '../../../services/drag-drop/drag-drop.service';
import { RulePluginService } from '../../../services/ruleplugin/rule-plugin-service';
import { SelectionServiceToken } from '../../../services/selection/selection-service-factory';
import { UndoRedoServiceToken } from '../../../services/undo-redo/undo-redo-service-factory';

@Component({
    selector: 'k2-toolbox-trash',
    templateUrl: './toolbox-trash.component.html',
    styleUrls: ['./toolbox-trash.component.less']
})
export class ToolboxTrashComponent implements OnInit {
    public hover: boolean;

    constructor(@Inject(SelectionServiceToken) private _selectionService: ISelectionService,
        private _dragDropService: DragDropService,
        private _rulePluginService: RulePluginService,
        @Inject(UndoRedoServiceToken) private _undoRedoService: IUndoRedoService) { }

    ngOnInit(): void {
        this.hover = false;
    }

    public changeTrashHoverState() {
        this.hover = !this.hover;
    }

    public trashClick(): void {
        //get rule from selection service
        const selectedRuleItem: any = <any>this._selectionService.getSelection('rule');

        if (selectedRuleItem.length > 0) {
            this.delete(selectedRuleItem[0]);
        }
    }

    private animateLeave() {
        DraggableDirective.keepDragGhost();
        const dragGhost = DraggableDirective.currentGhost;
        dragGhost.addEventListener('transitionend', this.removeDragGhost.bind(this), false);
        dragGhost.className += ' drag-leave';
    }

    private removeDragGhost() {
        DraggableDirective.currentGhost.removeEventListener('transitionend', this.removeDragGhost.bind(this), false);

        DraggableDirective.dontKeepDragGhost();
        DraggableDirective.removeDragGhost();
    }

    public onItemDroppedOnBin(dragDropData: IDragDropData): boolean {
        this.hover = false;

        const droppedRuleItem: any = <any>(dragDropData.getSource());
        if (this._rulePluginService.isExpressionPlugin(droppedRuleItem) || this._rulePluginService.isStatementPlugin(droppedRuleItem)) {
            return;
        }

        this.animateLeave();
        this.delete(droppedRuleItem);

        return false;
    }

    private delete(item: any) {

        const ruleDesignerItem = this._rulePluginService.getRuleDesignerItem(item);

        if (ruleDesignerItem.length > 0) {
            this._undoRedoService.addUndoMarker('-- delete rules items -- ');
        }

        for (let i = 0; i < ruleDesignerItem.length; i++) {
            ruleDesignerItem[i].delete();
        }
    }

}
