import { Component, Inject, Input, OnInit } from '@angular/core';
import { IEnvironmentService } from '../../../../../types/services/environment/i-environment-service';
import { IPluginService } from '../../../../../types/services/plugin/i-plugin-service';
import { EnvironmentServiceToken } from '../../../../services/environment/environment-service-factory';
import { PluginServiceToken } from '../../../../services/plugin/plugin-service-factory';
import { ResourceService } from '../../../../services/resource/resource.service';
import { isUndefinedOrNull } from '../../../../shared/utils/general';

@Component({
    selector: 'k2-no-context-panel',
    templateUrl: './no-context-panel.component.html',
    styleUrls: ['./no-context-panel.component.less']
})
export class NoContextPanelComponent implements OnInit {
    @Input() public item: sourceCode.k2Designer.objectModel.plugin.IDefinition;

    constructor(private _resourceService: ResourceService,
        @Inject(PluginServiceToken) private _pluginService: IPluginService,
        @Inject(EnvironmentServiceToken) private _envService: IEnvironmentService) { }

    ngOnInit(): void {
    }

    getFromResx = (resourceName: string, key: string) => this._resourceService.getFromResx(resourceName, key);

    public showContextBrowserToggleButton(): boolean {
        const panel: sourceCode.k2Designer.objectModel.plugin.IInstance = this._pluginService.getRightMostPanel();

        if (isUndefinedOrNull(panel) || panel.plgnDef.name === this.item.name) {
            return true;
        }

        return false;
    }

    public togglePanel(name?: string) {
        const panelName = name || this.item.name;
        this._pluginService.toggleState(panelName);
    }

    public closePanel(name?: string) {
        const panelName = name || this.item.name;

        //Hide the 4th panel (for example filter panel) once the associated panel (3rd panel) gets hidden from config panel.
        const plugin = this._pluginService.getPlugin(panelName);

        if (plugin?.plgnDef?.collapseAssociatedPanel) {
            this._pluginService.toggleCollapsed(plugin.plgnDef.collapseAssociatedPanel);
        }

        this._pluginService.toggleCollapsed(panelName);
    }

    public isPanelActive(name?: string): boolean {
        let result = false;
        const panelName = name || this.item.name;

        result = this._envService.activeStudioItem.pluginStates[panelName] === 'expanded';

        return result;
    }
}
