/**
 * Types of validation that can be performed on a field
 */
export enum ValidationTypes {

    /**
     * Field is required and should be populated
     */
    Required = 1,

    /**
     * Validate that there are no special characters in the text
     */
    SpecialCharactersForbidden = 2,

    /**
     * Validate that there aren't any duplicates in the array
     */
    Unique = 3,

    /**
     * Custom regex validation
     */
    Regex = 4,

    /**
     * Max length
     */
    MaxLength = 5,

    /**
     * Validates that the reference exists
     */
    Reference = 6,

    /**
     * Validates data fields
     */
    DataField = 7,

    /**
     * Validates using a validation service.
     */
    ValidationByService = 8,

    /**
     * Smart Wizard validator
     */
    SmartWizard = 9,

    /**
     * The existing property validator
     */
    LegacyPropertyValidator = 10,

    /**
     * The pseudovalidator that always returns false
     * Is used in case if I want to provide the validation function from controller instead of creating a whole new validator
     * validation function in controlller is provided as condition property
     */
    AlwaysFalseValidator = 11,

    /**
     * Validates if there is a deadline configured
     */
    RemindersValidator = 12,

    /**
     * Validates outgoing lines from an action and
     * shows a badge if amount of lines is incorrect
     */
    ActionLinksValidator = 13,

    /**
     * Validates rules for multi-step
     */
    MultiStepValidator = 14,

    /**
     * Validates if the mathematical expression is valid.
     */
    MathematicalValidator = 15,

    /**
     * Validates decision rule
     * All outcomes mapped in decision rule must exist for it to be valid.
     */
    OutcomesRule = 16,

    /**
     * Validates if all outcomes are linked to actions
     */
    OutcomeLink = 17,

    /**
     * Validates if a step's links will create an infinite loop scenario
     */
    InfiniteLoop = 18,

    /**
     * Validates if a workflow may go into a recursive loop
     */
    WorkflowRecursion = 19,

    /**
     * Validates that there is at most 1 list field in an expression and that no list/non-list fields are mixed
     */
    RestrictListFields = 20,

    /**
     * Validates that the item specified exists in the collection
     */
    CollectionValidator = 21,

    /**
     * Validates that deadline has outcomes when configuration requires it
     */
    DeadlineOutcomes = 22,

}
